/**
 * Security PolicyHeaderConstants is used to load the Security Policy screens header information
 * @author: Loganathan R P
 */

/**
 * headerInformation is used to load the Security Policy screens header information
 * @param  key
 */

export default function headerInformation(key) {
  switch (key) {
    case 'registerSecurityPolicy':
      return {
        title: ['security_policy_register_header'],
        description: ['security_policy_register_desc'],
      };
    case 'securityPolicyGroup':
      return {
        title: 'security_policy_group_header',
        description: ['security_policy_group_desc'],
      };
    case 'registerSuccess':
      return {
        title: ['security_policy_register_header'],
        description: ['security_policy_register_desc_success'],
      };
    case 'securityPolicylist':
      return {
        title: ['security_policy_list_header'],
        description: ['security_policy_list_desc'],
      };

    case 'securityPolicyDetail':
      return {
        title: ['security_policy_detail_header'],
        description: ['security_policy_detail_desc'],
      };

    case 'securityPolicydelete':
      return {
        title: ['security_policy_delete_header'],
        description: ['security_policy_delete_desc'],
      };
    case 'deletesuccess':
      return {
        title: ['security_policy_delete_header'],
        description: ['security_policy_delete_desc_success'],
      };
    case 'updatesecurityPolicy':
      return {
        title: ['security_policy_change_header'],
        description: ['security_policy_change_desc'],
      };
    case 'updatesuccess':
      return {
        title: ['security_policy_change_header'],
        description: ['security_policy_change_desc_success'],
      };
    case 'securityPolicyAddFinish':
      return {
        title: ['security_policy_register_check'],
        description: ['security_policy_add_finish_desc'],
      };
    case 'securityPolicyAddCheck':
      return {
        title: ['security_policy_register_check'],
        description: ['security_policy_register_description'],
      };
    case 'securityPolicychange':
      return {
        title: ['security_policy_change'],
        description: ['security_policy_change_description'],
      };
    case 'securityPolicyChangeFinish':
      return {
        title: ['security_policy_change_finish'],
        description: ['security_policy_change_finish_desc'],
      };
    case 'securityPolicyUpdateCheck':
      return {
        title: ['security_policy_change_check'],
        description: ['security_policy_change_check_desc'],
      };
    default:
      return {};
  }
}
