/**
 * getFile is to get fileSource
 * @param key
 */

const getWallpaperFile = (groupId, fileName) => {
  return postWithBlobData(`${APIENDPOINTS.WALLPAPERDOWNLOAD}/${groupId}/${fileName}`)
    .then((response) => response.blob())
    .then((blobData) => {
      const fileData = new Blob([blobData]);
      return new File([fileData], fileName);
    });
};

/**
 * getGroupInformation is to fetch group information for the selected group id
 * @param sscreenState
 * @param screenProp
 * @param isCopy
 * @return groupRecord
 */
export const getGroupInformation = async (
  { dataSource: groupRecord, dynamicData: dynamicRecord },
  { makeGroupData, defaultPolicyData, currentLanguage },
  isCopy,
) => {
  const response = await post(APIENDPOINTS.GETGROUPDATA, {
    groupId: groupRecord.groupId,
  });
  const result = await response.json();
  const { status, message, data: dataSource } = result;
  let groupData = {
    message,
    openErrorDialog: true,
    dialogWidth: '400px',
    pageLoading: false,
  };
  if (status === 'OK') {
    const scheduleResponse = await post(APIENDPOINTS.SCHEDULEDATA, {
      groupId: groupRecord.groupId,
    });
    const scheduleResult = await scheduleResponse.json();
    const { status: scheduleStatus, data: scheduleData } = scheduleResult;
    dataSource.groupName = `${dataSource.groupName}${isCopy || ''}`;
    dataSource.deviceList = isCopy ? [] : dataSource.deviceList;
    dataSource.mainPolicyMapper.alwaysOnVpnPackage =
      dataSource.mainPolicyMapper.alwaysOnVpnPackageObject ||
      defaultPolicyData.alwaysOnVpnPackageObject;
    dataSource.mainPolicyMapper.permittedInputMethods =
      dataSource.mainPolicyMapper.permittedInputMethodsObject ||
      defaultPolicyData.permittedInputMethodsObject;
    dataSource.mainPolicyMapper.permittedAccessibilityServices =
      dataSource.mainPolicyMapper.permittedAccessibilityServicesObject ||
      defaultPolicyData.permittedAccessibilityServicesObject;
    dataSource.mainPolicyMapper.deviceOwnerLockScreenInfo =
      dataSource.mainPolicyMapper.deviceOwnerLockScreenInfoObject ||
      defaultPolicyData.deviceOwnerLockScreenInfoObject;
    dataSource.mainPolicyMapper.kioskCustomization =
      dataSource.mainPolicyMapper.kioskCustomizationObject || defaultPolicyData.kioskCustomization;
    dataSource.mainPolicyMapper.systemUpdate =
      dataSource.mainPolicyMapper.systemUpdateObject || defaultPolicyData.systemUpdateObject;
    dataSource.mainPolicyMapper.systemUpdate.freezePeriods =
      defaultPolicyData.systemUpdateObject.freezePeriods;
    dataSource.mainPolicyMapper.shortSupportMessage =
      dataSource.mainPolicyMapper.shortSupportMessageObject ||
      defaultPolicyData.shortSupportMessageObject;
    dataSource.mainPolicyMapper.longSupportMessage =
      dataSource.mainPolicyMapper.longSupportMessageObject ||
      defaultPolicyData.longSupportMessageObject;
    dataSource.kioskAppSelect = [];
    dataSource.kioskBgApps = [];
    dataSource.kioskMultiApps = [];
    dataSource.homeSingleSelect = [];
    dataSource.homeBgSelect = [];
    dataSource.homePackageName = 'jp.co.axseed.mdmhome';
    if (dataSource.kioskEnable) {
      dataSource.applicationListMapper = [];
      if (dataSource.mainPolicyMapper.kioskCustomLauncherEnabled) {
        dataSource.kioskMultiApps = dataSource.mainPolicyMapper.applications;
      } else if (dataSource.appSetting) {
        dataSource.homeBgSelect = dataSource.mainPolicyMapper.applications.filter(
          (data) => data.installType === 'FORCE_INSTALLED',
        );
        if (dataSource.wallpaperHomeImageName) {
          dataSource.wallpaperHomeImage = await getWallpaperFile(
            groupRecord.groupId,
            dataSource.wallpaperHomeImageName,
          );
        }
      } else {
        dataSource.kioskAppSelect = dataSource.mainPolicyMapper.applications.filter(
          (data) => data.installType === 'KIOSK',
        );
        dataSource.kioskBgApps = dataSource.mainPolicyMapper.applications.filter(
          (data) => data.installType === 'FORCE_INSTALLED',
        );
      }
    } else {
      const packageMapper = cloneDeep(dataSource.applicationListMapper).reduce(
        (accumulator, record) => {
          accumulator[record.packageName] = record.appName;
          return accumulator;
        },
        {},
      );
      dataSource.mainPolicyMapper.applications.forEach((appData) => {
        appData.appName = packageMapper[appData.packageName] || '';
      });
      dataSource.applicationListMapper = dataSource.mainPolicyMapper.applications;
      dataSource.imeApps = dataSource.mainPolicyMapper.applications.filter(
        (app) =>
          dataSource.mainPolicyMapper.permittedInputMethods.packageNames.indexOf(app.packageName) >
          -1,
      );
      dataSource.asmApps = dataSource.mainPolicyMapper.applications.filter(
        (app) =>
          dataSource.mainPolicyMapper.permittedAccessibilityServices.packageNames.indexOf(
            app.packageName,
          ) > -1,
      );
    }

    if (scheduleStatus === 'OK' && scheduleData) {
      dataSource.isScheduledGroup = true;
      dataSource.wifiCertFileDetails = scheduleData.groupMapper.wifiCertFileDetails;
      dataSource.wifiCSVFileName = scheduleData.groupMapper.wifiCSVFileName;
      dataSource.sessionFolderName = scheduleData.groupMapper.sessionFolderName;
      dataSource.scheduleProgress = scheduleData.scheduleProgress;
      dataSource.applicationSchedule = dateTimeBasedOnLocale(
        scheduleData.scheduledTime,
        currentLanguage,
      );
    } else {
      dataSource.applicationSchedule =
        dataSource.applicationScheduleType === 1 ? dataSource.applicationSchedule : '';
    }
    dataSource.applicationScheduleType = scheduleData ? 1 : 0;
    if (makeGroupData) makeGroupData(dataSource);
    groupData = {
      dataSource,
      pageLoading: false,
      dialogWidth: '400px',
      openErrorDialog: false,
      dynamicData: {
        ...dynamicRecord,
        hideApplicationTimeText: isEmpty(dataSource.applicationSchedule),
        policyIsExist: !isEmpty(dataSource.mainPolicyMapper),
        isScheduleOn: scheduleData,
        isSelectButtonOn: scheduleData,
        currentLanguageLocale: dataSource.currentLanguageLocale,
        deviceIndicator: !isEmpty(dataSource.deviceList) && (
          <DeviceIndicator deviceList={dataSource.deviceList} />
        ),
      },
    };
  }
  return groupData;
};

/**
 * getProbableDate is to get next probable time
 * @return resultValue
 */
export const getProbableDate = async () => {
  const response = await get(APIENDPOINTS.GETPROBABLEDATE);
  const result = await response.json();
  const { status, data: resultValue } = result;
  if (status === 'OK') {
    return resultValue;
  }
  return resultValue;
};

/**
 * comboSwitch is to set policy component hide/show condition
 * @param option
 */
const comboSwitch = (option) => {
  return {
    COMPLEX: [13, []],
    NUMERIC: [4, [10, 11, 12]],
    SOMETHING: [4, [10, 11, 12]],
    ALPHABETIC: [4, [10, 11, 12]],
    BIOMETRIC_WEAK: [1, [10, 11, 12]],
    NUMERIC_COMPLEX: [4, [10, 11, 12]],
    ALPHANUMERIC: [4, [10, 11, 12]],
    PASSWORD_QUALITY_UNSPECIFIED: [1, []],
  }[option];
};

/**
 * validateTableData is to hide/show policy components
 * @param tableList
 * @param dataSource
 * @param panelKey
 * @return tableList
 */
export const validateTableData = (tableList, dataSource, panelKey, currentIndex) => {
  let tableDataList = tableList;
  if (panelKey === 'passcode_setting' || panelKey === 'setting_for_passcode_policy') {
    const passcodeChange = comboSwitch(dataSource.passwordPolicies[0].passwordQuality);
    tableDataList = tableDataList.filter(
      (data, index) => index < passcodeChange[0] || passcodeChange[1].indexOf(index) > -1,
    );
  } else if (panelKey === 'wifi_settings') {
    const securityType =
      !isEmpty(dataSource.openNetworkConfiguration.NetworkConfigurations[currentIndex]) &&
      dataSource.openNetworkConfiguration.NetworkConfigurations[currentIndex].WiFi.Security ===
        'None';
    tableDataList = securityType ? tableDataList.slice(0, 5) : tableDataList;
  }
  return tableDataList;
};

