/**
 * allowedPolicyPanelKeys is find allowed policy Panles
 * @param policyPanelKey
 * @param existingPanelKeys
 * @return allowedPolicyPanelKeys
 */
export function allowedPolicyPanelKeys(policyPanelKey, existingPanelKeys) {
  if (policyPanelKey === 'wifi_settings') {
    return [...existingPanelKeys, 'wifi_settings'];
  }
  if (policyPanelKey === 'wifi_settings_certificate') {
    return [...existingPanelKeys, 'wifi_settings_certificate'];
  }
  if (policyPanelKey === '') {
    return existingPanelKeys;
  }
  return [...existingPanelKeys, ...otherPolicyPanelKeys];
}

