/**
 * getNewAppliedPolicyKeys is build new applied policy panel keys
 * @param location
 * @param dataSource
 * @return appliedPolicyKeys
 */
export function getNewAppliedPolicyKeys(location, dataSource) {
  if (location && location.state && location.state.appliedPolicyKeysFromConfirm) {
    return location.state.appliedPolicyKeysFromConfirm;
  }
  return dataSource.appliedPolicyKeys ? dataSource.appliedPolicyKeys : '';
}

/**
 * getCertFilesFromLocation is used to get Cert Files from location
 * @param location
 * @param props
 * @return certFiles
 */
export function getCertFilesFromLocation(location, props) {
  if (
    location &&
    location.state &&
    location.state.certFiles &&
    (location.state.certFiles.certFile1 || location.state.certFiles.certFile2)
  ) {
    return { ...location.state.certFiles };
  }
  if (props.certFiles.certFile1 || props.certFiles.certFile2) {
    return { ...props.certFiles };
  }
  return { certFile1: null, certFile2: null };
}

/**
 * generateFileUploadBottomComponent is build components below File Updload
 * @param key
 * @param dynamicData
 * @param languageLabelCheck
 * @param downloadErrorText
 * @param dataSource
 * @param wifiCertificateConfiguration
 * @param downloadPreviousFile
 * @param showCsvErrorDialog
 * @return component
 */
export function generateFileUploadBottomComponent(
  key,
  dynamicData,
  languageLabelCheck,
  downloadErrorText,
  dataSource,
  wifiCertificateConfiguration,
  downloadPreviousFile,
  showCsvErrorDialog,
) {
  return (
    <>
      {dynamicData.wificert[`${key}ErrorText`] && (
        <div>
          {parseInt(dynamicData.wificert.errorListLength, 10) <= 5 ? (
            <SBButton
              type="button"
              title="view_error"
              name="view_error"
              id="view_error"
              onClick={() => showCsvErrorDialog(dynamicData.wificert[`${key}ErrorText`])}
            />
          ) : (
            <>
              <span className="red bold">{languageLabelCheck('csv_error')}</span>
              <span>
                <a
                  href
                  tabIndex="0"
                  style={CommonConstants.href_underline_style}
                  onKeyDown={(event) => {
                    if (event.keyCode === 13) {
                      event.preventDefault();
                      downloadErrorText(dynamicData.wificert[`${key}ErrorText`]);
                    }
                  }}
                  onClick={() => downloadErrorText(dynamicData.wificert[`${key}ErrorText`])}
                >
                  {languageLabelCheck('download_wifi_csv_error')}
                </a>
              </span>
            </>
          )}
        </div>
      )}
      {wifiCertificateConfiguration &&
        wifiCertificateConfiguration.updatedCertFileNames &&
        wifiCertificateConfiguration.updatedCertFileNames[key] && (
          <div className="mb10">
            <span>{wifiCertificateConfiguration.updatedCertFileNames[key]}</span>
          </div>
        )}
      {dataSource &&
        dataSource.showOldwifiCertFiles &&
        dataSource.wifiCertFileDetails &&
        dataSource.wifiCertFileDetails[key] && (
          <div className="mt10">
            <span>{`${languageLabelCheck('previously_uploaded_file')} `}</span>
            <span>
              <a
                href
                tabIndex="0"
                style={CommonConstants.href_underline_style}
                onKeyDown={(event) => {
                  if (event.keyCode === 13) {
                    event.preventDefault();
                    downloadPreviousFile(key);
                  }
                }}
                onClick={() => downloadPreviousFile(key)}
              >
                {dataSource.wifiCertFileDetails[key]}
              </a>
            </span>
          </div>
        )}
    </>
  );
}

