/**
 * getCertificateFiles is is get Valid certificate Files
 * @param wifiCertificateConfiguration
 * @param errorMap
 * @param isTls
 */
export function getCertificateFiles(wifiCertificateConfiguration, errorMap, isTls) {
  const certFiles = {};
  const updatedCertFileNames = {};
  const uploadedCertFiles = wifiCertificateConfiguration.certFiles;
  if (errorMap.caCertificateFileError) {
    wifiCertificateConfiguration.caCertificateFile = null;
    certFiles.certFile1 = null;
    updatedCertFileNames.CACertificate = '';
  } else {
    updatedCertFileNames.CACertificate = uploadedCertFiles.certFile1
      ? uploadedCertFiles.certFile1.name
      : '';
    certFiles.certFile1 = uploadedCertFiles.certFile1;
  }

  if ((errorMap.UserCertificateErrorText || errorMap.certPlusCsvError) && isTls) {
    wifiCertificateConfiguration.certpluscsv = null;
    certFiles.certFile2 = null;
    updatedCertFileNames.UserCertificate = '';
  } else if (isTls) {
    wifiCertificateConfiguration.individualCSVFile = null;
    updatedCertFileNames.UserCertificate = uploadedCertFiles.certFile2
      ? uploadedCertFiles.certFile2.name
      : '';
  }

  if ((errorMap.IndividualCSVErrorText || errorMap.individualCsvError) && !isTls) {
    wifiCertificateConfiguration.individualCSVFile = null;
    certFiles.certFile2 = null;
    updatedCertFileNames.IndividualCSV = '';
  } else if (!isTls) {
    wifiCertificateConfiguration.certpluscsv = null;
    updatedCertFileNames.IndividualCSV = uploadedCertFiles.certFile2
      ? uploadedCertFiles.certFile2.name
      : '';
  }
  certFiles.certFile2 = uploadedCertFiles.certFile2;
  wifiCertificateConfiguration.updatedCertFileNames = updatedCertFileNames;
  wifiCertificateConfiguration.certFiles = certFiles;
}

/**
 * maintainFilesOnTabSwitch is to save File Names on Tab switch
 */
export function maintainFilesOnTabSwitch(wifiCertificateConfiguration) {
  const updatedCertFileNames = {};
  updatedCertFileNames.CACertificate = wifiCertificateConfiguration.caCertificateFile
    ? wifiCertificateConfiguration.caCertificateFile.name
    : '';
  updatedCertFileNames.UserCertificate = wifiCertificateConfiguration.certpluscsv
    ? wifiCertificateConfiguration.certpluscsv.name
    : '';
  updatedCertFileNames.IndividualCSV = wifiCertificateConfiguration.individualCSVFile
    ? wifiCertificateConfiguration.individualCSVFile.name
    : '';
  wifiCertificateConfiguration.updatedCertFileNames = updatedCertFileNames;
}

/**
 * wifiCertCSVHeaders is to hold WIFI Setting certificate CSV Headers
 * @return csvHeaders
 */
export const wifiCertCSVHeaders = 'serial,cert_file,cert_pass,eap_id,eap_anonymous_id,eap_password';

/**
 * wifiCSVHeaders is to hold WIFI Setting CSV Headers
 * @return csvHeaders
 */
export const wifiCSVHeaders = 'ssid,auto_connection,security_type,password';

/**
 * downloadCertFileStoredInSession is to download cert files temporarky stored
 * @param fileKey
 * @param sessionFolderName
 * @param fileName
 * @return File
 */
export function downloadCertFileStoredInSession(fileKey, sessionFolderName, fileName) {
  return postWithBlobData(APIENDPOINTS.SESSIONCERTFILEDOWNLOAD, {
    fileKey,
    sessionFolderName,
    fileName,
  })
    .then((response) => response.blob())
    .then((blobData) => {
      return new File([new Blob([blobData])], fileName);
    });
}
