/**
 * IDDelete is used to peform delete oepration
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { isUndefined } from 'util';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import IDCRUDFooter from './IDCRUDFooter';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import { post, performDelete } from '../../../actions/commonActions';
import { tableListView, setReference } from '../Constants/IDCRUDconstants';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import headerInformation from '../Constants/IDHeaderConstants';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import checkLanguage from '../../../language/checkLanguage';

class IDDelete extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location } = props;
    let param = {};
    if (!isUndefined(location.state)) {
      param = location.state.param;
    }
    this.state = {
      pageLoading: false,
      headerData: headerInformation('iddelete'),
      dataSource: param,
      errorMessage: '',
      reference: setReference(),
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

   /**
   * componentDidMount is used to set initial focus to the screen
   */

  componentDidMount() {
    const { history } = this.props;
    const { reference, dataSource } = this.state;
    const getUserDetails = async () => {
      this.setState({ pageLoading: true });
      const data = await post(APIENDPOINTS.IDUSERDETAILS,dataSource);
      const userDetails = await data.json();
      const { idListVo } = userDetails;
      if (userDetails.status && idListVo.authorityId !== 6) {
        this.setState({
          pageLoading: false,
          dataSource: userDetails.idListVo,
        });
      } else {
        history.push(ROUTEENDPOINTS.ERROR);
      }
      if(reference.okRef.current) reference.okRef.current.focus();
    };
    getUserDetails();
  }



  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onOkDialog perform save event on clicking ok button in dialog
   */

  onOkDialog = () => {
    const { footer, reference , dataSource } = this.state;
    this.setState({ pageLoading: true });
    const performSave = async () => {
      const response = await performDelete(`${APIENDPOINTS.DELETEADMIN}/${dataSource.userId}`);
      const responseData = await response.json();
      if (responseData.status) {
        footer.primaryVisibile = false;
        this.setState({
          headerData: headerInformation('deletesuccess'),
          modalIsOpen: false,
          footer,
          pageLoading: false,
          dataSource: responseData.idListVo,
        });
        if (reference.backRef.current) reference.backRef.current.focus();
      } else {
        footer.okDisable = true;
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
          footer,
          errorMessage: responseData.message,
        });
        if (reference.cancelRef.current) reference.cancelRef.current.focus();
      }
    };
    performSave();
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { history } = this.props;
    switch (key) {
      case 'save':
        this.setState({
          modalIsOpen: true,
        });
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.IDLIST,JSON.parse(localStorage.getItem('idListParam')));
        break;
      case 'back':
        history.push(ROUTEENDPOINTS.IDLIST);
        break;
      default:
        break;
    }
  };

  /**
   * render is used to render the entire delete screen
   * @return component
   */

  render() {
    const {
      headerData,
      dataSource,
      pageLoading,
      footer,
      reference,
      errorMessage,
      modalIsOpen,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame
                  tableList={tableListView(false)}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <IDCRUDFooter
                  reference={reference}
                  footer={footer}
                  onHandleClick={key => this.onHandleClick(key)}
                />
              </div>
              <br />
              <span className="red bold">{this.languageLabelCheck(errorMessage)}</span>
              <SBDialog
                open={modalIsOpen}
                header="delete_confirmation"
                type="confirmation"
                content="confirm_delete_data"
                onCancel={() => {
                  this.setState({ modalIsOpen: false });
                }}
                onOk={() => {
                  this.onOkDialog();
                }}
                width="400px"
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

IDDelete.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(PropTypes.string),
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps)(withRouter(IDDelete));
