/**
 * Dashboard screen is used to load the dashboard 
 @author Vijayavel Mohan
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import countBy from 'lodash/countBy';
import isNull from 'lodash/isNull';
import isEmpty from 'lodash/isEmpty';
import { Link } from 'react-router-dom';
import headerInformation from '../../SBAdmin/ApplicationManagement/Constants/AppManageHeaderConstants';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableHeader from '../../common/component/tableheader/SBTableHeader';
import DashboardGuageCircle from './component/DashboardGuageCircle';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import DashboardTableHeader from './component/DashboardTableHeader';
import DashboardDonutChart from './component/DashboardDonutChart';
import SBTable from '../../common/component/sbtable/SBTable';
import { dateBasedOnLocale } from '../../common/utils/dateUtils';
import { CommonConstants } from '../../constants/CommonConstants';
import { get, post } from '../../actions/commonActions';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import checkLanguage from '../../language/checkLanguage';
import APIENDPOINTS from '../../constants/APIEndPoints';
import {
  deviceStatusColsInfo,
  deviceListDefaultParam,
  CHARTCOLORS,
  CONSTANTS,
  deviceListLocalStorage,
} from './constants/dashboardConstants';
import SBBarChart from '../../common/component/charts/SBBarChart';
import DashboardAdmin from './DashboardAdmin';
import DashboardStackedBar from './component/DashboardStackedBar';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

const styles = {
  signedDevice: {
    width: '125px',
    height: '225px',
    padding: '25px 20px 20px 20px',
    left: '35%',
    top: '-30px',
    position: 'relative',
    marginBottom: '-105px',
    transform: 'translate(60,60)',
  },
  leftColStyle: {
    width: '100%',
  },
  rightColStyle: {
    display: 'inline-table',
    width: '100%',
  },
  icon: {
    width: '18px',
    height: '16px',
    marginRight: '5px',
    verticalAlign: 'middle',
    marginBottom: '2px',
  },
};

const profileStatusCols = [
  {
    title: 'group_name',
    dataIndex: 'groupName',
    textAlign: 'center',
    render: (record) => {
      return (
        <Link to={{ pathname: ROUTEENDPOINTS.DEVICELIST, state: record }}>
          <u>{record.groupName}</u>
        </Link>
      );
    },
  },
  {
    title: 'deployment_request_date',
    dataIndex: 'date',
    textAlign: 'center',
  },
  {
    title: 'application_status',
    dataIndex: 'chartData',
    styles: { width: '350px' },
    render: (record) => {
      return <DashboardStackedBar showTextIn data={record.chartData} />;
    },
  },
];

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      headerData: headerInformation('dashboard'),
      registeredDevices: 0,
      totalDeviceCount: 0,
      deviceList: [],
      deviceStatusChartData: [
        {
          name: this.languageLabelCheck(CONSTANTS.SAFETYNET_DETECTION),
          color: CHARTCOLORS.SAFETYNET_DETECTION,
          count: 0,
        },
        {
          name: this.languageLabelCheck(CONSTANTS.UPDATING),
          color: CHARTCOLORS.UPDATING,
          count: 0,
        },
        {
          name: this.languageLabelCheck(CONSTANTS.SUCCESSFULL_UPDATE),
          color: CHARTCOLORS.SUCCESSFULL,
          count: 0,
        },
        {
          name: this.languageLabelCheck(CONSTANTS.POLICY_VIOLATION),
          colors: CHARTCOLORS.POLICY_VIOLATION,
          count: 0,
        },
      ],
      noticeList: [],
      policyVoilation: [],
      profileApplicationStatus: [],
      pageLoading: true,
    };
  }

  /**
   * load to get the initial data
   */
  componentDidMount = () => {
    const buildComponent = async () => {
      await this.getPolicyStatus();
      await this.getDeviceCount();
      await this.getNoticeList();
      await this.getDeviceList();
      this.setState({ pageLoading: false });
    };
    buildComponent();
  };

  /**
   *  getDeviceCount is used to get the registered device count
   */
  getDeviceCount = async () => {
    const { history, userVo } = this.props;
    try {
      const userDetailResponse = await get(`${APIENDPOINTS.TENANTDETAILS}/${userVo.dmCode}`);
      const userDetailData = await userDetailResponse.json();
      if (userDetailData.status) {
        const { applicationVo } = userDetailData;
        this.setState({
          totalDeviceCount: applicationVo.androidDeviceCount,
          registeredDevices: applicationVo.androidDeviceRegistered,
        });
      }
    } catch (exception) {
      history.push('/error');
    }
  };

  /**
   * getNoticeList is used to get the noticelist from softbank
   */
  getPolicyStatus = async () => {
    const { history } = this.props;
    try {
      const response = await get(APIENDPOINTS.GROUPDEVICESYNCSTATUS);
      const policyList = await response.json();
      this.setState({ profileApplicationStatus: policyList });
    } catch (exception) {
      history.push('/error');
    }
  };

  /**
   * getNoticeList is used to get the noticelist from softbank
   */
  getNoticeList = async () => {
    const { history } = this.props;
    try {
      const response = await get(`${APIENDPOINTS.NOTICELOOKLIST}?limit=5
      &page=1`);
      const responseData = await response.json();
      this.setState({
        noticeList: responseData.customerNoticeList
      });
    } catch (exception) {
      history.push('/error');
    }
  };

  /**
   * getDeviceList is used to get the devices from the table
   */
  getDeviceList = async () => {
    const { history } = this.props;
    try {
      const devicelist = await post(APIENDPOINTS.GETDEVICELIST, deviceListDefaultParam);
      const result = await devicelist.json();
      const tableData = result.data;
      if (!isNull(tableData) && tableData.length > 0) {
        this.prepareData(tableData);
      } else {
        this.setState({ pageLoading: false });
      }
    } catch (exception) {
      history.push('/error');
    }
  };

  /**
   *
   */
  setProfileApplicationStatus = () => {
    const { profileApplicationStatus } = this.state;
    return (
      <>
        <div style={styles.leftColStyle}>
          <SBTableHeader headerText="profile_application_status" />
          {isEmpty(profileApplicationStatus) ? (
            <>
              <div style={{ height: '25px' }}>{this.languageLabelCheck('no_group_name')}</div>
            </>
          ) : (
            <>
              <div className="clearfix mb10">
                <ul className="list-float">
                  <li>
                    <label>
                      <img
                        style={styles.icon}
                        alt=""
                        src="https://stara.bizconcier-dm.com/wide_img/renew_icon_resposn_comp.png"
                      />
                      {this.languageLabelCheck('successful_application')}
                    </label>
                  </li>
                  <li>
                    <label>
                      <img
                        style={styles.icon}
                        alt=""
                        src="https://stara.bizconcier-dm.com/wide_img/renew_icon_inquiring.png"
                      />
                      {this.languageLabelCheck('applying')}
                    </label>
                  </li>
                  <li>
                    <label>
                      <img
                        style={styles.icon}
                        alt=""
                        src="https://stara.bizconcier-dm.com/wide_img/renew_icon_policy_violation.png"
                      />
                      {this.languageLabelCheck(CONSTANTS.FAILED_TO_APPLY)}
                    </label>
                  </li>
                </ul>
              </div>
              <SBTable dataSource={profileApplicationStatus} columns={profileStatusCols} />
            </>
          )}
        </div>
      </>
    );
  };

  /**
   * setTableComponents is used to set the data for table components
   */
  setTableComponents = () => {
    return (
      <>
        {this.setNoticeList()}
        {this.setOtherNotifications()}
        {this.setDevicesWaitingForInformationUpdate()}
        {this.setRemoteStandByDevices()}
        {this.setProfileApplicationStatus()}
      </>
    );
  };

  /**
   * setChartComponents is used to set the data for the chart components
   */
  setChartComponents = () => {
    return (
      <>
        {this.setDeviceRegisteredComponent()}
        {this.setDeviceStatusComponent()}
        {this.setPolicyViolationChart()}
      </>
    );
  };

  /**
   * setPolicyViolationChart is used to set the data for violation chart
   */
  setPolicyViolationChart = () => {
    const { policyVoilation } = this.state;
    const colors = policyVoilation.map((record) => CHARTCOLORS[record.x]);
    const xaxisTitle = this.languageLabelCheck('detail_device');
    return (
      <>
        <div style={styles.rightColStyle}>
          <SBTableHeader headerText="security_policy_violation" />
          {policyVoilation.length > 0 ? (
            <SBBarChart
              height={80 * (policyVoilation.length + 1)}
              width={600}
              data={policyVoilation}
              color={colors}
              horizontalBar
              xaxisTitle={xaxisTitle}
              max={policyVoilation[0].y > 5 ? policyVoilation[0].y : 5}
            />
          ) : (
            <>
              <div style={{ height: '25px' }}>{this.languageLabelCheck('no_policy_violation')}</div>
            </>
          )}
        </div>
      </>
    );
  };

  /**
   * setDevicesWaitingForInformationUpdate is used to set the data for the device information waiting for update.
   */
  setDevicesWaitingForInformationUpdate = () => {
    const { deviceList } = this.state;
    return (
      <div style={styles.leftColStyle}>
        {deviceList.length > 0 ? (
          <>
            <DashboardTableHeader
              text="devices_waiting_header"
              hyperLinkText="devices_count"
              hyperLink={ROUTEENDPOINTS.DEVICELIST}
              dynamicLinkContent={[deviceList.length]}
              onClick={() => this.handleLinkHeaderClick()}
            />
            <p className="mb10">
              <span>{this.languageLabelCheck('devices_waiting_subtitle')}</span>
            </p>
            <SBTable dataSource={deviceList.slice(0, 5)} columns={deviceStatusColsInfo} />
          </>
        ) : (
          <>
            <SBTableHeader headerText="devices_waiting_header" />
            <div style={{ height: '25px' }}>{this.languageLabelCheck('no_waiting_devices')}</div>
          </>
        )}
      </div>
    );
  };

  /**
   * languageLabelCheck is used to get the label based on locale.
   * @param {String} label
   */

  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * setNoticeList is used to set the noticelist
   */
  setNoticeList = () => {
    const { noticeList } = this.state;
    const notices = this.generateNoticeList(noticeList);
    return (
      <div style={styles.leftColStyle}>
        <DashboardTableHeader
          hyperLinkText="notice_from_softbank"
          hyperLink={ROUTEENDPOINTS.NOTICELOOKLIST}
        />
        <div
          style={
            noticeList.length > 0
              ? { height: '150px', paddingTop: '5px' }
              : { height: '10px', paddingTop: '5px' }
          }
        >
          {notices}
        </div>
      </div>
    );
  };

  /**
   * setOtherNotifications is used to set the other notification
   */
  setOtherNotifications = () => {
    return (
      <div style={styles.leftColStyle}>
        <SBTableHeader headerText="other_notifications" />
        <div style={{ height: '25px' }} />
      </div>
    );
  };

  /**
   * setRemoteStandByDevices is used to set the remopte stand by devices
   */
  setRemoteStandByDevices = () => {
    return (
      <div style={styles.leftColStyle}>
        <SBTableHeader headerText="remote_stand_wipe_devices" />
        <div style={{ height: '25px' }}>
          {this.languageLabelCheck('no_remote_stand_wipe_devices')}
        </div>
      </div>
    );
  };

  /**
   * onNotificationLinkClick is used to navigate user to notification detail page
   */
  onNotificationLinkClick = (noticeId) => {
    const { history } = this.props;
    history.push({
      pathname: ROUTEENDPOINTS.NOTICEDETAIL,
      state: { noticeId },
    });
  };

  /**
   * onNotificationLinkKeyPress is used to navigate user to notification detail page
   */
  onNotificationLinkKeyPress = (noticeId, event) => {
    if (event.keyCode === 13) {
      this.onNotificationLinkClick(noticeId);
    }
  };

  /**
   * generateNoticeList is used to generate the notice data as link
   * @param {Array} customerNoticeList
   */
  generateNoticeList = (customerNoticeList) => {
    const { currentLanguage } = this.props;
    if (customerNoticeList.length === 0) return null;
    return customerNoticeList.slice(0, 5).map((notice) => {
      return (
        <dl className="clearfix" key={notice.noticeId}>
          <dt style={{ margin: '10px' }}>
            {`${dateBasedOnLocale(notice.dateOfPublication, currentLanguage)}`}
            <a
              tabIndex="0"
              href={CommonConstants.CommonConstants}
              onKeyDown={(event) => this.onNotificationLinkKeyPress(notice.noticeId, event)}
              onClick={() => this.onNotificationLinkClick(notice.noticeId)}
              style={{
                cursor: 'pointer',
                textDecoration: 'underline',
                paddingLeft: '20px',
                paddingRight: '8px',
              }}
            >
              {notice.notificationTitle}
            </a>
            {notice.notificationRead ? null : (
              <span className="red bold">{`[ ${this.languageLabelCheck('new')} ]`}</span>
            )}
          </dt>
          <dd />
        </dl>
      );
    });
  };

  /**
   *setDeviceStatusComponent is used to set the danut chart data.
   */
  setDeviceStatusComponent = () => {
    const { deviceStatusChartData } = this.state;
    return (
      <div style={styles.rightColStyle}>
        <DashboardTableHeader hyperLinkText="status" hyperLink={ROUTEENDPOINTS.DEVICELIST} />
        <div
          style={{
            width: '500px',
            margin: '30px',
            height: '230px',
            position: 'relative',
            left: '20px',
          }}
        >
          <DashboardDonutChart dataPoints={deviceStatusChartData} />
        </div>
      </div>
    );
  };

  /**
   * setDeviceRegisteredComponent is used to set the data for gaugeCircle Component.
   */
  setDeviceRegisteredComponent = () => {
    const { registeredDevices, totalDeviceCount } = this.state;
    return (
      <>
        <div style={styles.rightColStyle}>
          <SBTableHeader headerText="device_information" />
          <div style={styles.signedDevice}>
            <DashboardGuageCircle
              value={registeredDevices}
              totalDevice={totalDeviceCount}
              uncontracted={this.languageLabelCheck('no_agreement')}
            />
          </div>
        </div>
      </>
    );
  };

  /**
   * prepareData is used to transform the given data to chart and table data.
   * @param {Array} tableData
   */
  prepareData = (tableData) => {
    const deviceStatusMap = this.prepareDeviceStatusChartData(tableData);
    const violationBarChartData = this.preparePolicyViolationChartdata(tableData);
    const deviceWaitingList = this.calculateDurationList(tableData);
    this.setState({
      deviceList: deviceWaitingList,
      deviceStatusChartData: deviceStatusMap,
      policyVoilation: violationBarChartData,
      pageLoading: false,
    });
  };

  /**
   * calculateDurationList is used to calculate the time the device waiting for update
   * @param {array} tableData
   */
  calculateDurationList = (tableData = []) => {
    let updatingList = tableData.filter((device) => device.syncStatus === 2);
    if (updatingList.length > 0) {
      const hours = this.languageLabelCheck('hours');
      const days = this.languageLabelCheck('days');
      updatingList = updatingList.map((device) => {
        const date1 = new Date(device.dataCollectionTime);
        const date2 = new Date();
        let diff = (date2.getTime() - date1.getTime()) / 1000;
        diff /= 60 * 60;
        device.duration = Math.abs(Math.round(diff));
        return device;
      });
      updatingList.sort((a, b) => a.duration - b.duration).reverse();
      return updatingList.map((device) => {
        device.duration =
          device.duration >= 24
            ? `${Math.floor(device.duration / 24)} ${days}`
            : `${device.duration} ${hours}`;
        return device;
      });
    }
    return [];
  };

  /**
   * prepareDeviceStatusChartData is used to transform the devicelist data to the chart data
   * @param {array} tableData
   */
  prepareDeviceStatusChartData = (tableData) => {
    return [
      {
        name: this.languageLabelCheck(CONSTANTS.POLICY_VIOLATION),
        color: CHARTCOLORS.POLICY_VIOLATION,
        count: countBy(tableData, (device) => device.syncStatus === 0).true || 0,
      },
      {
        name: this.languageLabelCheck(CONSTANTS.SUCCESSFULL_UPDATE),
        color: CHARTCOLORS.SUCCESSFULL,
        count: countBy(tableData, (device) => device.syncStatus === 1).true || 0,
      },
      {
        name: this.languageLabelCheck(CONSTANTS.UPDATING),
        color: CHARTCOLORS.UPDATING,
        count: countBy(tableData, (device) => device.syncStatus === 2).true || 0,
      },
      {
        name: this.languageLabelCheck(CONSTANTS.SAFETYNET_DETECTION),
        color: CHARTCOLORS.SAFETYNET_DETECTION,
        count: countBy(tableData, (device) => device.syncStatus === 3).true || 0,
      },
    ];
  };

  /**
   * preparePolicyViolationChartdata is used to prepare the data based on the violated devices
   * @param {array} tableData
   */
  preparePolicyViolationChartdata = (tableData) => {
    const dataMap = {
      OS:
        countBy(tableData, (device) =>
          device.groupAuthorityFlag ? device.allVersionCheckFlag : device.versionCheckFlag,
        ).true || 0,
      IMEI:
        countBy(tableData, (device) =>
          device.groupAuthorityFlag ? device.allImeiCheckFlag : device.imeiCheckFlag,
        ).true || 0,
      APP:
        countBy(tableData, (device) =>
          device.groupAuthorityFlag ? device.allApplicationCheckFlag : device.applicationCheckFlag,
        ).true || 0,
      VIRUS:
        countBy(tableData, (device) =>
          device.groupAuthorityFlag ? device.allAntivirusCheckFlag : device.antivirusCheckFlag,
        ).true || 0,
    };
    const policyViolationdata = Object.entries(dataMap)
      .sort((a, b) => a[1] - b[1])
      .reverse();
    const violationBarChartData = [];
    policyViolationdata
      .filter((entries) => entries[1] > 0)
      .map((entries) => violationBarChartData.push({ x: entries.shift(), y: entries.shift() }));
    return violationBarChartData;
  };

  /**
   * handleLinkHeader is used to put the default data in local storage to open device screen with updating status.
   */
  handleLinkHeaderClick = () => {
    if ('deviceSearch' in localStorage) {
      const deviceSearchLocalData = JSON.parse(localStorage.getItem('deviceSearch'));
      deviceSearchLocalData.deviceSearch.checkBoxKeys.push(2);
      localStorage.setItem('deviceSearch', JSON.stringify(deviceSearchLocalData));
    } else {
      localStorage.setItem('deviceSearch', deviceListLocalStorage);
    }
  };

  handleGroupNameClick = () => {
    const { location } = this.props;
    const { state } = location;
    const { record } = state;
    if ('deviceSearch' in localStorage) {
      const deviceSearchLocalData = JSON.parse(localStorage.getItem('deviceSearch'));
      deviceSearchLocalData.groupSearch.push(record);
      localStorage.setItem('deviceSearch', JSON.stringify(deviceSearchLocalData));
    } else {
      const localData = JSON.parse(deviceListLocalStorage);
      localData.groupSearch.push(record);
      localStorage.setItem('deviceSearch', JSON.stringify(localData));
    }
  };

  render() {
    const { userVo } = this.props;
    const isAdmin = userVo.authorityId < 3;
    const { headerData, pageLoading } = this.state;
    let chartComponents = null;
    let tableComponents = null;
    if (!isAdmin) {
      chartComponents = this.setChartComponents();
      tableComponents = this.setTableComponents();
    }
    const renderingComponent = !isAdmin ? (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} />
          <div className="contents-main" style={{ width: '100%' }}>
            <div className="clearfix mb10" style={{ display: 'inline-flex', width: '100%' }}>
              <div className="right-columns" style={{ width: '50%' }}>
                {chartComponents}
              </div>
              <div className="left-columns" style={{ marginLeft: '40px', width: '50%' }}>
                {tableComponents}
              </div>
            </div>
          </div>
        </div>
      </CircularSpinner>
    ) : (
      <>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <DashboardAdmin authorityId={userVo.authorityId} />
      </>
    );
    return renderingComponent;
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { current, data } = state.language;
  return {
    localeData: data,
    userVo,
    currentLanguage: current,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
Dashboard.propTypes = {
  history: commonPropTypes().history.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  currentLanguage: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
Dashboard.defaultProps = {
  currentLanguage: 'ja',
};

export default connect(mapStateToProps)(Dashboard);
