/**
 * ContractInformationFooter is used to generate footer components for contract information screens
 * @author: V.Vijayaragavan
 */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import SBButton from '../../../common/component/sbbutton/SBButton';

class ContractInformationFooter extends PureComponent {
  /**
   * getFooterButtons is used to generate footer button
   * @param footerButton
   * @return SBButton
   */
  getFooterButtons = footerButton => {
    const { onHandleClick } = this.props;
    return <SBButton {...footerButton} onClick={() => onHandleClick(footerButton.key)} />;
  };

  /**
   * render is used to render the component
   */
  render() {
    const { buttonsList } = this.props;
    return (
      <div className="clearfix">
        <div className="floatright">
          {buttonsList &&
            buttonsList.map(footerButton => {
              return this.getFooterButtons(footerButton);
            })}
        </div>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ContractInformationFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  buttonsList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default ContractInformationFooter;
