/**
 * AppFieldConstants is used to load the field values
 * @author: P.Vinothpandian, A.Aakash
 */
import {
  tableDeviceListView,
  tableDeviceList,
  tablePolicyListView,
  tablePolicyList,
  tableAppNotifyListView,
  tableAppNotifyList,
} from './TableConstants';

export function emptyDataSource() {
  return {
    mailNotifyDeviceWaiting: false,
    mailNotifyDeviceWaitingDays: 0,
    mailNotifyPolicyViolation: false,
    mailNotifyPolicyViolationGroup: false,
    mailNotifyAppRelatedSetting: false,
  };
}

export function setTableView(mode, dynamicData, reference, dataSource) {
  if (mode !== 'update')
    return {
      deviceList: tableDeviceListView(dynamicData, reference),
      policyList: tablePolicyListView(),
      AppNotifyList: tableAppNotifyListView(),
    };

  return {
    deviceList: tableDeviceList(dynamicData, reference, dataSource),
    policyList: tablePolicyList(),
    AppNotifyList: tableAppNotifyList(),
  };
}
