/** Base Files */
import React, { createRef } from 'react';
import PropTypes from 'prop-types';

/** SBComponents */
import SearchFilter from '../../v1/common/component/SearchFilter';

/** Utility Files */
import { searchComponentList } from '../constants/groupSettingConstants';

/**
 * groupManagementSearch performs serch room component rendering
 * @author Aswin Palanisamy
 * @param props
 * @return searchFilter
 */
const groupManagementSearch = props => {
  const { onSearchAction, onKeyDown, reference, screenKey } = props;
  const makeSearchList = searchComponentList(reference)[screenKey];
  return (
    <div className="contents-main mb15">
      {makeSearchList && (
        <SearchFilter
          border
          searchOptions={makeSearchList}
          onKeyDown={(event, value) => onKeyDown(event, value)}
          onSearchAction={(key, event, values) => onSearchAction(key, event, values)}
        />
      )}
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
groupManagementSearch.propTypes = {
  onSearchAction: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  screenKey: PropTypes.string.isRequired,
  reference: PropTypes.instanceOf(createRef()),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
groupManagementSearch.defaultProps = {
  reference: createRef(),
};

export default groupManagementSearch;
