/** Base Files */
import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import isEmpty from 'lodash/isEmpty';
import fileDownload from 'js-file-download';
import PropTypes from 'prop-types';
import encoding from 'encoding';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementFooter from '../component/GroupManagementFooter';
import GroupManagementIndicator from '../component/GroupManagementIndicator';

/** DeviceManagement Components */
import DeviceIndicator from '../../deviceManagement/component/DeviceIndicator';

/** Utility Files */
import {
  setDataSource,
  messageConstants,
  errorMessageConstants,
} from '../constants/groupSettingConstants';
import {
  serviceMapper,
  initializeRefs,
  dialogKeyArray,
  getProbableDate,
  getGroupInformation,
  groupRegistrationApiList,
  getNewAppliedPolicyKeys,
  getCertFilesFromLocation,
  getDefaultPolicyData,
} from '../common/GroupSettingCommonFunctions';
import { Role } from '../../constants/Role';
import checkLanguage from '../../language/checkLanguage';
import { currentDate, getUserTodaysDate } from '../../common/utils/dateUtils';
import { post, postWithFormData, postWithBlobData } from '../../actions/commonActions';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import {
  tableList,
  tableView,
  validateModifyingSchedule,
} from '../constants/groupSettingTableConstants';
import { groupRegistrationHeader } from '../constants/groupSettingHeaderConstants';
import { makeGroupInformation, resetGroupInformation } from '../../actions/GroupAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

const moment = require('moment-timezone');

/**
 * GroupRegistration is to perform CRUD operation in groupManagement
 * @author Aswin Palanisamy
 */
class GroupRegistration extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const reference = initializeRefs();
    const { mode, groupId, file, location } = props;
    const defaultScreenKey = `group${mode === 4 ? 'Confirmation' : 'Registration'}`;
    this.state = {
      file,
      reference,
      isView: mode === 4,
      defaultScreenKey,
      pageLoading: false,
      initialState: true,
      dialogWidth: '400px',
      openErrorDialog: false,
      openDeviceDialog: false,
      openConfirmDialog: false,
      confirmDialogHeader: `${mode !== 4 ? 'save' : 'delete'}_confirmation`,
      confirmDialogContent: messageConstants[mode],
      dialogKey: 'deviceIndicator',
      currentLanguageLocale: '',
      footer: {
        reference,
        isVisible: true,
        disable: mode !== 4 && mode !== 3,
        screenKey: defaultScreenKey,
        authorityRoleType: Role.group1,
      },
      headerData: groupRegistrationHeader('groupRegistration', mode),
      dataSource: {
        ...setDataSource(props),
        certFiles: { ...getCertFilesFromLocation(location, props) },
        groupId: groupId || -1,
        appliedPolicyKeys: location && location.state ? location.state.appliedPolicyKeys : '',
      },
      dynamicData: {
        uploadCsvError: '',
        invalidFileError: '',
        groupNameError: '',
        kioskDataError: '',
        isSelectButtonOn: false,
        policyIsExist: false,
        deviceIndicator: false,
        isScheduleOn: false,
        isFileDisplay: file.name !== 'group_validation_temporary_file_method.txt',
        hideApplicationTimeText: true,
      },
    };
  }

  /**
   * componentDidMount is to fetch group information in edit,addCopy and delete mode
   */
  componentDidMount() {
    const { reference } = this.state;
    const { mode, isModified } = this.props;
    if (!isModified && mode !== 1) this.getGroupInformation();
    else {
      this.getProbableDate();
    }
    if (reference.groupNameRef.current) reference.groupNameRef.current.focus();
  }

  /**
   * componentDidUpdate is to set focus to the component based on visibility
   */
  componentDidUpdate() {
    const { reference, initialState, footer } = this.state;
    if (!footer.isVisible && reference.backButtonRef.current)
      reference.backButtonRef.current.focus();
    if (initialState && reference.groupNameRef.current) reference.groupNameRef.current.focus();
    if (footer.isVisible && reference.okButtonRef.current) reference.okButtonRef.current.focus();
  }

  /**
   * getGroupInformation is to fetch registered group information
   */
  getGroupInformation() {
    this.setState({ pageLoading: true }, async () => {
      const { mode } = this.props;
      const { footer } = this.state;
      const groupInformation = await getGroupInformation(
        this.state,
        this.props,
        mode === 3 && this.languageLabelCheck('enclosed_copy'),
      );
      const { dataSource } = groupInformation;
      if (mode === 4) footer.disable = dataSource.securityPolicyStatus;
      this.setState({ ...groupInformation, footer });
    });
  }

  /**
   * getGroupInformation is to fetch registered group information
   */
  getProbableDate() {
    const { isModified } = this.props;
    this.setState({ pageLoading: true }, async () => {
      const time = await getProbableDate();

      this.setState(({ footer, dynamicData, dataSource }) => {
        const { mainPolicyMapper, deviceList } = dataSource;
        return {
          dataSource: {
            ...dataSource,
            applicationScheduleSelect: time.applicationScheduleSelect,
          },
          dynamicData: {
            ...dynamicData,
            currentLanguageLocale: time.currentLanguageLocale,
            policyIsExist: !isEmpty(mainPolicyMapper),
            deviceIndicator: !isEmpty(deviceList) && <DeviceIndicator deviceList={deviceList} />,
          },
          footer: { ...footer, disable: !isModified },
          pageLoading: false,
        };
      });
    });
  }

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onTableFrameEvent is to perform group management input field events
   * @param key
   * @param event
   * @param value
   */
  onTableFrameEvent = (key, event, value, buttonKey, schdeuleValue) => {
    const { userVo, setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    const { type, target } = event;
    if (buttonKey || key === 'applicationScheduleDate') {
      setModified(true);
      this.changeToClear(buttonKey, key, value, event, schdeuleValue);
    } else if (key === 'scheduleCancel') {
      if (
        validateModifyingSchedule(
          dataSource.applicationSchedule,
          getUserTodaysDate(userVo.timeZoneOffSet),
          1,
        )
      ) {
        this.setState({
          dataSource: { ...dataSource, scheduleProgress: 'InProgress' },
          message: 'cannot_cancel_less_than_minute',
          pageLoading: false,
          openErrorDialog: true,
          dialogWidth: '400px',
        });
      } else {
        setModified(true);
        this.setState({
          dataSource: { ...dataSource, scheduleProgress: '', applicationSchedule: '' },
          dynamicData: { ...dynamicData, hideApplicationTimeText: true },
        });
      }
    } else if (key === 'editSchedule') this.editSchedule();
    else if (type === 'click') this.onHandleClick(key);
    else if (key === 'applicationScheduleType') {
      dynamicData.hideApplicationTimeText = value === 0 || isEmpty(dataSource.applicationSchedule);
      this.setState({ dynamicData });
      this.setTime(key, value, type, target);
    } else
      this.setState(({ dialogKey, footer, file }, { isModified }) => ({
        initialState: false,
        dataSource: {
          ...dataSource,
          [key]: value,
          mainPolicyMapper:
            !isEmpty(dataSource.mainPolicyMapper) && key === 'kioskEnable'
              ? {
                  ...dataSource.mainPolicyMapper,
                  kioskCustomLauncherEnabled: dataSource.kioskMultiApps.length > 0 && value,
                }
              : { ...dataSource.mainPolicyMapper },
        },
        footer: { ...footer, disable: !(isModified || type === 'change') },
        file: key === 'fileUpload' ? target.files[0] : file,
        dialogKey: key === 'selectMethod' ? dialogKeyArray[value ? 1 : 0] : dialogKey,
        dynamicData: {
          ...dynamicData,
          isFileDisplay: key === 'downloadDevice' ? false : dynamicData.isFileDisplay,
        },
      }));
  };

  /**
   * onKeyDown performs key press operation in group registration
   * @param event
   */
  onKeyDown = (event) => {
    if (event.keyCode === 13) this.onHandleClick(event.target.name);
  };

  /**
   * onHandleClick is to handle botton click events in group registration
   * @param screenKey
   * @return footerActionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      cancel: () => this.navigateListScreen(),
      back: () => this.navigatePreviousScreen(),
      downloadDevice: () => this.setState({ pageLoading: true }, () => this.downloadDeviceFile()),
      viewError: () => this.bootIndicatorDialog(screenKey),
      deviceIndicator: () => this.bootIndicatorDialog(screenKey),
      save: () => this.groupRegisterConfirmation(screenKey),
      groupConfirmation: () => this.navigateConfirmationScreen(screenKey),
      authority: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      selectApp: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      selectTarget: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      selectPolicy: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      customizeApp: () => this.navigateDetailScreen(serviceMapper(screenKey)),
    }[screenKey]());

  /**
   * setTime is to set next probable time for scheduling in group registration
   * @param key
   * @param value
   * @param type
   * @param target
   */
  setTime = (key, value, type, target) => {
    this.setState(({ dataSource, footer, file }, { isModified }) => ({
      initialState: false,
      pageLoading: false,
      dataSource: { ...dataSource, [key]: value },
      footer: { ...footer, disable: !(isModified || type === 'change') },
      file: key === 'selectTarget' ? target.files[0] : file,
    }));
  };

  /**
   * changeToClear is to handle botton click events in group registration
   * @param screenKey
   * @return footerActionMapper
   */
  changeToClear = (buttonKey, key, value, event, schdeuleValue) => {
    const { type, target } = event;
    let { dataSource } = this.state;
    const { dynamicData } = this.state;
    dataSource = {
      ...dataSource,
      applicationSchedule: '',
      [key]: value,
      applicationDateFormat: schdeuleValue,
    };
    dynamicData.isSelectButtonOn = buttonKey === 'select';
    dynamicData.hideApplicationTimeText =
      dataSource.applicationScheduleType === 0 ||
      buttonKey === 'clear' ||
      isEmpty(dataSource.applicationSchedule);
    this.setState(({ footer, file }, { isModified }) => ({
      initialState: false,
      dynamicData,
      dataSource,
      footer: { ...footer, disable: !(isModified || type === 'change') },
      file: key === 'selectTarget' ? target.files[0] : file,
    }));
  };

  /**
   * editSchedule used to edit the schedule information
   */
  editSchedule = () => {
    const { userVo } = this.props;
    const { dataSource, dynamicData } = this.state;
    if (
      validateModifyingSchedule(
        dataSource.applicationSchedule,
        getUserTodaysDate(userVo.timeZoneOffSet),
        60,
      )
    ) {
      this.setState({
        dataSource: { ...dataSource, scheduleProgress: 'InProgress' },
        message: 'cannot_edit_less_than_hour',
        pageLoading: false,
        openErrorDialog: true,
        dialogWidth: '400px',
      });
    } else {
      this.setState({
        dataSource: { ...dataSource, scheduleProgress: '' },
        dynamicData: { ...dynamicData, isScheduleOn: false },
      });
    }
  };

  /**
   * downloadDeviceFile used to download devicelist for that group
   */
  downloadDeviceFile = async () => {
    const { dataSource } = this.state;
    const { currentLanguage, mode } = this.props;
    const response = await postWithBlobData(groupRegistrationApiList[5], {
      groupId: dataSource.groupId,
    });
    const result = await response.json();
    const { status, message } = result;
    if (status === 'OK') {
      this.setState({ pageLoading: false }, () => {
        fileDownload(
          encoding.convert(message, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
          `DeviceList_${mode !== 1 ? dataSource.groupName : 'template'}.csv`,
        );
      });
    } else {
      this.setState({
        message,
        pageLoading: false,
        openErrorDialog: true,
        dialogWidth: '400px',
      });
    }
  };

  /**
   * bootIndicatorDialog is to display selected device in grid format
   */
  bootIndicatorDialog = (dialogKey) => {
    const { dataSource } = this.state;
    const { deviceList } = dataSource;
    const isDeviceIndicator = dialogKey === 'deviceIndicator';
    this.setState({
      dialogKey,
      dialogWidth: isDeviceIndicator ? '583px' : '400px',
      openDeviceDialog: isDeviceIndicator ? deviceList.length > 10 : true,
    });
  };

  /**
   * navigatePreviousScreen is to navigate from current screen to previous screen
   * @return pageNavigationOperation
   */
  navigatePreviousScreen = () => {
    const { mode } = this.props;
    const { footer: okButton } = this.state;
    return mode !== 4 && okButton.isVisible
      ? this.setState(({ footer, defaultScreenKey: screenKey }) => ({
          isView: false,
          initialState: true,
          footer: { ...footer, screenKey },
          headerData: groupRegistrationHeader(screenKey, mode),
        }))
      : this.navigateListScreen();
  };

  /**
   * navigateConfirmationScreen is to display group information after save completion
   * @param confirmScreen
   */
  navigateConfirmationScreen = (confirmScreen) => {
    const { mode, userVo } = this.props;
    const { dataSource, reference, file, dynamicData } = this.state;
    const {
      groupId,
      groupName,
      deviceList,
      selectMethod,
      applicationScheduleType,
      applicationScheduleDate,
      applicationScheduleSelect,
      applicationSchedule,
      kioskEnable,
      appSetting,
      kioskAppSelect,
      kioskMultiApps,
      mainPolicyMapper,
    } = dataSource;
    this.setState({ pageLoading: true }, async () => {
      let uploadCsvError = '';
      let kioskDataError = '';
      let updatedDevicelist = [];
      const currentDateValue = getUserTodaysDate(userVo.timeZoneOffSet);
      const scheduledValue = new Date(applicationSchedule);
      let groupNameError = !dataSource.groupName.trim() && 'emptyGroup';
      let fileUploadError =
        selectMethod &&
        file.name !== 'group_validation_temporary_file_method.txt' &&
        !file.name.endsWith('.csv') &&
        'invalidFileType';
      if (kioskEnable)
        if (mainPolicyMapper.kioskCustomLauncherEnabled)
          kioskDataError = !kioskMultiApps.length > 0 && 'emptyKioskData';
        else if (!appSetting) kioskDataError = !kioskAppSelect.length > 0 && 'emptyKioskData';
      const scheduleError =
        applicationScheduleType === 1 && !dynamicData.isSelectButtonOn && 'scheduleNotSet';
      const improperScheduleError =
        applicationScheduleType === 1 &&
        (!applicationScheduleDate || !applicationScheduleSelect) &&
        'setProperScheduleValues';
      const scheduleDateBeforeError =
        applicationScheduleType === 1 &&
        applicationSchedule &&
        scheduledValue < currentDateValue &&
        'setDateBeforeError';
      let hasError =
        groupNameError ||
        fileUploadError ||
        kioskDataError ||
        scheduleError ||
        scheduleDateBeforeError ||
        improperScheduleError;
      const formData = new FormData();
      formData.append('file', file);
      formData.append('mode', mode);
      formData.append('groupId', groupId);
      formData.append('selectMethod', selectMethod);
      formData.append('groupName', groupName);
      if (!hasError) {
        const response = await postWithFormData(groupRegistrationApiList[0], formData);
        const result = await response.json();
        const { csvErrorData, deviceEntityList, groupNameInvalid } = result;
        uploadCsvError = csvErrorData;
        groupNameError = groupNameInvalid && 'duplicateGroup';
        fileUploadError = !!csvErrorData && 'errorFile';
        hasError = groupNameError || fileUploadError;
        const updatedCsvDeviceList = isEmpty(deviceEntityList) ? [] : deviceEntityList;
        updatedDevicelist = selectMethod ? updatedCsvDeviceList : deviceList;
      }
      this.setState(
        ({ footer, defaultScreenKey: registerScreen }) => ({
          isView: !hasError,
          pageLoading: false,
          headerData: groupRegistrationHeader(
            !hasError ? confirmScreen : registerScreen,
            !hasError ? 0 : mode,
          ),
          footer: {
            ...footer,
            screenKey: !hasError ? confirmScreen : footer.screenKey,
          },
          dynamicData: {
            ...dynamicData,
            uploadCsvError: isEmpty(uploadCsvError) ? '' : uploadCsvError,
            groupNameError: errorMessageConstants[footer.screenKey][groupNameError],
            kioskDataError: errorMessageConstants[footer.screenKey][kioskDataError],
            invalidFileError: errorMessageConstants[footer.screenKey][fileUploadError],
            scheduleError: errorMessageConstants[footer.screenKey][scheduleError],
            improperScheduleError: errorMessageConstants[footer.screenKey][improperScheduleError],
            scheduleDateBeforeError:
              errorMessageConstants[footer.screenKey][scheduleDateBeforeError],
            deviceIndicator: !isEmpty(updatedDevicelist) && (
              <DeviceIndicator deviceList={updatedDevicelist} />
            ),
          },
          dataSource: { ...dataSource, deviceList: updatedDevicelist },
        }),
        () => {
          if (fileUploadError && reference.fileUploadRef.current)
            reference.fileUploadRef.current.focus();
          if (groupNameError && reference.groupNameRef.current)
            reference.groupNameRef.current.focus();
          if (kioskDataError && reference.kioskAppRef.current)
            reference.kioskAppRef.current.focus();
        },
      );
    });
  };

  /**
   * groupRegisterConfirmation is to prompt confirmation dialog before save operation
   * @param isConfirm
   */
  groupRegisterConfirmation = (isConfirm) => {
    const { mode } = this.props;
    this.setState({
      openConfirmDialog: !!isConfirm,
      dialogWidth: '400px',
      confirmDialogHeader: `${mode !== 4 ? 'save' : 'delete'}_confirmation`,
      confirmDialogContent: messageConstants[mode],
    });
  };

  /**
   * clearHomeDetails is used to clear home details
   * @param dataSource
   * @return dataSource
   */
  clearHomeDetails = (dataSource) => {
    dataSource.appSetting = false;
    dataSource.wallpaperHomeImage = null;
    dataSource.wallpaperHomeImageName = null;
    dataSource.wallpaperHomeImageUploadedDate = null;
    dataSource.wallpaperHomeImageUploadedBy = null;
    dataSource.iconSize = null;
    dataSource.homeApplicationsShortcut = [];
    return dataSource;
  };

  /**
   * groupRegistration is to save group information
   */
  groupRegistration = () => {
    const {
      mode,
      setModified,
      defaultPolicyData,
      applicationListMapper,
      kioskAppSelect,
      kioskBgApps,
      kioskMultiApps,
      location,
      homeBgSelect,
      mainPolicyMapper: appliedpolicy,
    } = this.props;
    let { dataSource } = this.state;
    const methodName = groupRegistrationApiList[mode];
    const mainPolicyMapper = !isEmpty(appliedpolicy)
      ? appliedpolicy
      : getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck);
    const { freezePeriods } = mainPolicyMapper.systemUpdate;
    const { wallpaperHomeImage } = dataSource;
    let kioskApps = [];
    if (dataSource.kioskEnable) {
      if (!mainPolicyMapper.kioskCustomLauncherEnabled && dataSource.appSetting) {
        kioskApps = [...homeBgSelect];
        dataSource.iconSize =
          dataSource.iconSize !== '' &&
          dataSource.iconSize !== undefined &&
          dataSource.iconSize !== null
            ? dataSource.iconSize
            : null;
      } else {
        kioskApps = mainPolicyMapper.kioskCustomLauncherEnabled
          ? kioskMultiApps
          : [...kioskAppSelect, ...kioskBgApps];
        dataSource = this.clearHomeDetails(dataSource);
      }
      mainPolicyMapper.applications = kioskApps;
    } else {
      dataSource = this.clearHomeDetails(dataSource);
      mainPolicyMapper.applications = applicationListMapper;
      mainPolicyMapper.kioskCustomization = null;
      mainPolicyMapper.kioskCustomizationObject = null;
      mainPolicyMapper.kioskCustomLauncherEnabled = false;
    }
    mainPolicyMapper.systemUpdate.freezePeriods =
      mainPolicyMapper.systemUpdate.type === 'SYSTEM_UPDATE_TYPE_UNSPECIFIED'
        ? []
        : freezePeriods.filter((record) => !isEmpty(record.startDate));
    const deviceList = dataSource.deviceList.map((record) => ({
      deviceId: record.deviceId,
      serialNumber: record.serialNumber,
    }));
    const formData = new FormData();
    if (dataSource.certFiles) {
      formData.set('certFile1', dataSource.certFiles.certFile1);
      formData.set('certFile2', dataSource.certFiles.certFile2);
    }
    if (mainPolicyMapper.wifiCSV && mainPolicyMapper.wifiCSV.csvfile) {
      formData.set('wificsvfile', mainPolicyMapper.wifiCSV.csvfile);
    }
    if (wallpaperHomeImage !== null) {
      formData.set('wallpaperHomeImage', wallpaperHomeImage);
    }
    if (dataSource.wallpaperHomeImageUploadedDate) {
      dataSource.wallpaperHomeImageUploadedDate = moment(
        dataSource.wallpaperHomeImageUploadedDate,
      ).format('YYYY/MM/DD HH:mm:ss');
    }
    const appliedPolicyKeys = getNewAppliedPolicyKeys(location, dataSource);
    const paramMapper = {
      deleteParam: dataSource.groupId,
      registerParam: {
        ...dataSource,
        mainPolicyMapper,
        deviceList,
        appliedPolicyKeys,
        copiedGroup: mode === 3,
      },
    };
    this.setState({ pageLoading: true }, async () => {
      let response = null;
      if (mode === 1 || mode === 2 || mode === 3) {
        formData.set('groupDataJson', JSON.stringify(paramMapper.registerParam));
        response = await postWithFormData(methodName, formData);
      } else {
        response = await post(
          methodName,
          mode !== 4 ? paramMapper.registerParam : paramMapper.deleteParam,
        );
      }
      const result = await response.json();
      const { status, message } = result;
      if (status === 'OK') {
        this.setState(({ footer }) => ({
          pageLoading: false,
          openConfirmDialog: false,
          footer: { ...footer, isVisible: false },
          headerData: groupRegistrationHeader(footer.screenKey, mode),
        }));
      } else {
        this.setState({
          message,
          pageLoading: false,
          openErrorDialog: true,
          dialogWidth: '400px',
          openConfirmDialog: false,
        });
      }
      setModified(false);
    });
  };

  /**
   * navigateDetailScreen is to navigate group registration screen
   * to select authority/device/app screens
   * @param screenName
   */
  navigateDetailScreen = (screenName) => {
    const { dataSource, file } = this.state;
    const { history, location, makeGroupData, mode, isModified } = this.props;
    makeGroupData({ ...dataSource, file });
    history.push(screenName, {
      mode,
      isModified,
      dataSource,
      groupId: dataSource.groupId,
      appliedPolicyKeys: getNewAppliedPolicyKeys(location, dataSource),
      ancestorScreen: location.pathname,
    });
  };

  /**
   * navigateListScreen is to navigate  from groupRegistration to groupManagement
   */
  navigateListScreen = () => {
    const { history, resetGroupData, setModified } = this.props;
    resetGroupData();
    setModified(false);
    this.setState({ openErrorDialog: false, dialogWidth: '400px' }, () =>
      history.push(serviceMapper('groupManagement')),
    );
  };

  /**
   * componentList is to frame error download link on CSV upload operation
   * @return errorLink
   */
  componentList = () => [
    <Link>
      <span>
        <a href onClick={() => this.downloadErrorText()}>
          {this.languageLabelCheck('download_csv_error')}
        </a>
      </span>
    </Link>,
    <SBButton
      type="button"
      title="view_error"
      name="view_error"
      id="view_error"
      key="viewError"
      onClick={() => this.onHandleClick('viewError')}
    />,
  ];

  /**
   * renderCsvResult used to render csv validation result and file name
   * @param dynamicData
   * @returns component
   */
  renderCsvResult = (dynamicData) => {
    const { file } = this.state;
    const fileName = dynamicData.isFileDisplay ? file.name : '';
    const errorComponent =
      dynamicData.uploadCsvError && dynamicData.uploadCsvError.split('\n').length > 5
        ? this.componentList()[0]
        : this.componentList()[1];
    return dynamicData.uploadCsvError ? errorComponent : fileName;
  };

  /**
   * downloadErrorText is to download csv error document
   */
  downloadErrorText = () => {
    const { dynamicData } = this.state;
    fileDownload(dynamicData.uploadCsvError, `Errors_${currentDate()}.txt`);
  };

  /**
   * render performs group registration screen rendering
   * @return groupRegistration
   */
  render() {
    const {
      footer,
      isView,
      message,
      dialogKey,
      reference,
      dataSource,
      headerData,
      pageLoading,
      dynamicData,
      dialogWidth,
      openErrorDialog,
      openDeviceDialog,
      openConfirmDialog,
      confirmDialogHeader,
      confirmDialogContent,
    } = this.state;
    const { mode, userVo } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div>
              <CustomTableFrame
                dataSource={dataSource}
                tableList={
                  isView
                    ? tableView(dynamicData)
                    : tableList(
                        {
                          ...dynamicData,
                          scheduleProgress: dataSource.scheduleProgress,
                          applicationSchedule: dataSource.applicationSchedule,
                          usersTodayDate: getUserTodaysDate(userVo.timeZoneOffSet),
                        },
                        reference,
                        this.renderCsvResult,
                      )
                }
                onKeyDown={(event) => this.onKeyDown(event)}
                onTableFrameEvent={(key, event, value, buttonKey, schdeuleValue) =>
                  this.onTableFrameEvent(key, event, value, buttonKey, schdeuleValue)
                }
              />
            </div>
            <div>
              <GroupManagementFooter
                footer={footer}
                onHandleClick={(key) => this.onHandleClick(key)}
              />
            </div>
            <br />
            <div>
              <span className="red bold">
                {mode === 4 && dataSource.securityPolicyStatus
                  ? this.languageLabelCheck('security_policy_exists')
                  : ''}
              </span>
            </div>
          </div>
          <GroupManagementIndicator
            notMultiLang
            hasHeightScroll
            gridHeight="350px"
            width={dialogWidth}
            dialogKey={dialogKey}
            dialogContent={
              dialogKey === 'deviceIndicator'
                ? dataSource.deviceList
                : dynamicData.uploadCsvError.split('\n')
            }
            openDeviceDialog={openDeviceDialog}
            closeDeviceDialog={() => this.setState({ openDeviceDialog: false })}
          />
          <SBDialog
            type="confirmation"
            width={dialogWidth}
            open={openConfirmDialog}
            header={confirmDialogHeader}
            content={confirmDialogContent}
            onOk={() => this.groupRegistration()}
            onCancel={() => this.groupRegisterConfirmation()}
          />
          <SBDialog
            type="error"
            width={dialogWidth}
            header="error"
            okButtonTitle="ok"
            content={message}
            open={openErrorDialog}
            onOk={() => this.navigateListScreen()}
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const {
    file,
    groupId,
    groupName,
    authority,
    deviceList,
    selectMethod,
    mainPolicyMapper,
    defaultPolicyData,
    applicationListMapper,
    applicationSchedule,
    applicationScheduleDate,
    applicationScheduleType,
    applicationScheduleSelect,
    kioskAppSelect,
    kioskBgApps,
    kioskMultiApps,
    kioskEnable,
    imeApps,
    asmApps,
    homePackageName,
    iconSize,
    url,
    urlTitle,
    shortcutType,
    homeSingleSelect,
    homeBgSelect,
    homeApplicationsShortcut,
    appSetting,
    wallpaperHomeImage,
    wallpaperHomeImageName,
    wallpaperHomeImageUploadedDate,
    wallpaperHomeImageUploadedBy,
    appliedPolicyKeys,
    wifiCertFileDetails,
    wifiCSVFileName,
    certFiles,
    showOldwifiCertFiles,
    sessionFolderName,
    isScheduledGroup,
  } = state.group;
  const { data: localeData, current: currentLanguage } = state.language;
  const { userVo } = state.user;
  const { isModified } = state.screenModified;
  return {
    userVo,
    file,
    groupId,
    groupName,
    authority,
    isModified,
    deviceList,
    localeData,
    selectMethod,
    currentLanguage,
    mainPolicyMapper,
    defaultPolicyData,
    applicationListMapper,
    applicationSchedule,
    applicationScheduleDate,
    applicationScheduleType,
    applicationScheduleSelect,
    kioskAppSelect,
    kioskBgApps,
    kioskMultiApps,
    kioskEnable,
    imeApps,
    asmApps,
    homePackageName,
    iconSize,
    url,
    urlTitle,
    shortcutType,
    homeSingleSelect,
    homeBgSelect,
    homeApplicationsShortcut,
    appSetting,
    wallpaperHomeImage,
    wallpaperHomeImageName,
    wallpaperHomeImageUploadedDate,
    wallpaperHomeImageUploadedBy,
    appliedPolicyKeys,
    wifiCertFileDetails,
    wifiCSVFileName,
    certFiles,
    showOldwifiCertFiles,
    sessionFolderName,
    isScheduledGroup,
  };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    resetGroupData: () => dispatch(resetGroupInformation()),
    makeGroupData: (data) => dispatch(makeGroupInformation(data)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
GroupRegistration.propTypes = {
  userVo: commonPropTypes().userVo.isRequired,
  isModified: PropTypes.bool,
  mode: PropTypes.number.isRequired,
  groupId: PropTypes.number.isRequired,
  setModified: PropTypes.func.isRequired,
  resetGroupData: PropTypes.func.isRequired,
  makeGroupData: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  currentLanguage: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      groupId: PropTypes.number,
      appliedPolicyKeysFromConfirm: PropTypes.string,
      certFiles: PropTypes.shape({
        certFile1: PropTypes.instanceOf(File),
        certFile2: PropTypes.instanceOf(File),
      }),
      appliedPolicyKeys: PropTypes.string,
    }),
    pathname: PropTypes.string,
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  mainPolicyMapper: PropTypes.objectOf(PropTypes.object).isRequired,
  defaultPolicyData: PropTypes.objectOf(PropTypes.object).isRequired,
  applicationListMapper: PropTypes.arrayOf(PropTypes.object).isRequired,
  kioskAppSelect: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
GroupRegistration.defaultProps = {
  isModified: false,
  isNotMultiLang: false,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(GroupRegistration));
