/**
 * forgetPasswordFloatRightList is used to forget password footer list
 * @param loginBackRef
 * @return arrayOf(Objects) compopnentJsonObject
 */
export function forgetPasswordFloatRightList(loginBackRef) {
  return [
    {
      component: 'button',
      type: 'button',
      color: 'green',
      id: 'ok-button',
      title: 'ok',
      key: 'ok',
    },
    {
      component: 'button',
      type: 'submit',
      id: 'cancel',
      title: 'cancel',
      noMarginRight: 'noMarginRight',
      key: 'cancel',
    },
    {
      component: 'button',
      type: 'button',
      id: 'back-to-login',
      title: 'back_to_login_screen',
      noMarginRight: 'noMarginRight',
      ref: loginBackRef,
    },
  ];
}

