/**
 * forgotPasswordTableList is used to forget password table list
 * @param dmCodeRef
 * @return arrayOf(Objects) compopnentJsonObject
 */
export function forgotPasswordTableList(dmCodeRef) {
  return [
    {
      label: 'dm_code',
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          id: 'dm-code-text-input',
          name: 'dmCode',
          maxLength: '20',
          size: '20',
          key: 'dmCode',
          ref: dmCodeRef,
        },
      ],
    },
    {
      label: 'login_id',
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          id: 'login-id-text-input',
          name: 'loginId',
          maxLength: '20',
          size: '20',
          key: 'loginId',
        },
      ],
    },
  ];
}

/**
 * changePasswordTableList is used to hold json data of change password input fields
 * @param dynamicData
 * @return arrayOf(Objects) compopnentJsonObject
 */
export function changePasswordTableList(dynamicData) {
  return [
    {
      label: 'current_password',
      component: 'textbox',
      type: 'password',
      id: 'current-password-text-input',
      name: 'currentPassword',
      maxLength: '20',
      size: '20',
      errorMessage: dynamicData.currentPasswordErrorMessage,
      key: 'currentPassword',
      ref: dynamicData.currentPasswordRef,
      style: { width: 300 },
    },
    {
      label: 'new_password',
      component: 'textbox',
      type: 'password',
      id: 'new-password-text-input',
      name: 'newPassword',
      maxLength: '20',
      size: '20',
      errorMessage: dynamicData.newPasswordErrorMessage,
      key: 'newPassword',
      ref: dynamicData.newPasswordRef,
      style: { width: 300 },
    },
    {
      label: 'new_password_for_confirmation',
      component: 'textbox',
      type: 'password',
      id: 'new-password-confirm-text-input',
      name: 'newPasswordConfirm',
      maxLength: '20',
      size: '20',
      errorMessage: dynamicData.confirmPasswordErrorMessage,
      key: 'newPasswordConfirm',
      ref: dynamicData.confirmPasswordRef,
      style: { width: 300 },
    },
    {
      label: 'email_address',
      component: 'textbox',
      type: 'text',
      size: 40,
      maxLength: 100,
      id: 'email-text-input',
      name: 'email',
      key: 'emailId',
      errorMessage: dynamicData.emailError,
      style: { width: 300 },
      ref: dynamicData.emailRef,
    },
  ];
}
