const moment = require('moment-timezone');

/**
 * validateModifyingSchedule is to used validate whether Modifying schedule is allowed or not
 * @param applicationSchedule
 * @param usersTodayDate
 * @param differenceInMinutesAllowed
 * @return boolean
 */
export function validateModifyingSchedule(
  applicationSchedule,
  usersTodayDate,
  differenceInMinutesAllowed,
) {
  if (applicationSchedule) {
    const scheduledDate = new Date(applicationSchedule);
    return scheduledDate - usersTodayDate < differenceInMinutesAllowed * 1000 * 60;
  }
  return true;
}
