/**
 * NotificationView is used to generate preview component to view Image and Notification content
 * @author: V.Vijayaragavan
 */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import { contentStyle } from '../../constants/InfoFromSoftBankConstants';

class NotificationView extends PureComponent {
  /**
   * render is used to render the component
   */
  render() {
    const {
      onDownloadButtonClick,
      htmlContent,
      showDownload,
      imageFileURL,
      removeMinHeight,
    } = this.props;
    return (
      <div className="border p15 mb10" style={contentStyle(removeMinHeight)}>
        <div dangerouslySetInnerHTML={{ __html: htmlContent }} />
        {imageFileURL && (
          <>
            <br />
            <img src={imageFileURL} alt="" />
          </>
        )}
        {showDownload && (
          <div className="clearfix">
            <div className="floatright">
              <SBButton
                className="ml10"
                type="button"
                title="download"
                onClick={() => onDownloadButtonClick()}
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
NotificationView.propTypes = {
  onDownloadButtonClick: PropTypes.func.isRequired,
  htmlContent: PropTypes.string.isRequired,
  showDownload: PropTypes.bool.isRequired,
  imageFileURL: PropTypes.string.isRequired,
  removeMinHeight: PropTypes.bool,
};

/**
 * defaultProps is used to supply default props value
 */
NotificationView.defaultProps = {
  removeMinHeight: false,
};

export default NotificationView;
