import React, { Component } from 'react';
import SBRadioButton from '../../../../common/component/sbradio/SBRadio';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBCheckbox from '../../../../common/component/checkbox/SBCheckbox';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBTransfer from '../../../../common/component/sbtransfer/SBTransfer';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

class Certificate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isCACertificateOn: false,
      isUserCertificateOn: false,
      isEachSetting: true,
      isTargetSelectByCSV: false,
      grantAppAccessSection: false,
    };
  }

  onCARadioButtonChange(event, selectedOption) {
    this.setState({
      isCACertificateOn: selectedOption === 'on',
    });
  }

  onUserRadioButtonChange(event, selectedOption) {
    this.setState({
      isUserCertificateOn: selectedOption === 'on',
    });
  }

  onCertificateCoopeartionChange(event, selectedOption) {
    this.setState({
      isEachSetting: selectedOption === 'eachSetting',
    });
  }

  onTargetSelectRadioChange(event, selectedOption) {
    this.setState({
      isTargetSelectByCSV: selectedOption === 'csv',
    });
  }

  onGrantAppsCheckBoxChange(event, selectedValues) {
    this.setState({
      grantAppAccessSection: selectedValues.length !== 0,
    });
  }

  onSearchAppsButtonClick = () => {};

  onClearAppsButtonClick = () => {};

  whiteList = () => {
    return [
      {
        title: 'com.mobiroo.xgen',
        value: 'com.mobiroo.xgen',
        name: 'App Pass',
      },
    ];
  };

  blackList = () => {
    return [
      {
        title: 'com.adobe.reader',
        value: 'com.adobe.reader',
        name: 'Adobe Acrobat',
      },
    ];
  };

  render() {
    const { isCACertificateOn } = this.state;
    const { isUserCertificateOn } = this.state;
    const { isEachSetting } = this.state;
    const { isTargetSelectByCSV } = this.state;
    const { grantAppAccessSection } = this.state;
    return (
      <table className="table-tate mb10">
        <tbody>
          <tr>
            <th>CA Certificate</th>
            <td>
              <SBRadioButton
                name="caCertificate"
                options={OPERATIONMANAGEMENTCONSTANTS.onOffRadioButtonOptions}
                filterValue="value"
                filterKey="key"
                defaultValue="off"
                onChange={(event, selectedOption) => {
                  this.onCARadioButtonChange(event, selectedOption);
                }}
              />
            </td>
          </tr>
          {isCACertificateOn && (
            <tr>
              <th>
                <br />
              </th>
              <td>
                <label
                  htmlFor="individual_csvfile"
                  className="btn-fileupload mr5"
                  style={{ width: '300' }}
                >
                  <SBButton
                    type="file"
                    name="caCertificateCSVFile"
                    id="ca-certificate-csv-file-input"
                  />
                </label>
                <br />
                <span className="red bold" />
              </td>
            </tr>
          )}
          <tr>
            <th>User Certificate</th>
            <td>
              <SBRadioButton
                name="userCertificate"
                options={OPERATIONMANAGEMENTCONSTANTS.onOffRadioButtonOptions}
                filterValue="value"
                filterKey="key"
                defaultValue="off"
                onChange={(event, selectedOption) => {
                  this.onUserRadioButtonChange(event, selectedOption);
                }}
              />
            </td>
          </tr>
          {isUserCertificateOn && (
            <tr>
              <th>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Certificate cooperation</th>
              <td>
                <SBRadioButton
                  name="certificateCooperation"
                  options={OPERATIONMANAGEMENTCONSTANTS.certificateCoopeartionOptions}
                  filterValue="value"
                  filterKey="key"
                  defaultValue="eachSetting"
                  onChange={(event, selectedOption) => {
                    this.onCertificateCoopeartionChange(event, selectedOption);
                  }}
                />
              </td>
            </tr>
          )}
          {isEachSetting && isUserCertificateOn && (
            <tr>
              <th>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Certificate + Individual CSV
                <br />
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(ZIP format)
              </th>
              <td>
                <label
                  htmlFor="individual_csvfile"
                  className="btn-fileupload mr5"
                  style={{ width: '300' }}
                >
                  <SBButton
                    type="file"
                    name="certPlusIndividualCSV"
                    id="cert-Plus-Individual-CSV"
                  />
                </label>
              </td>
            </tr>
          )}
          {((isCACertificateOn && !isUserCertificateOn) ||
            (isUserCertificateOn && !isEachSetting)) && [
              <tr>
                <th rowSpan="2">Selected</th>
                <td>
                  <SBRadioButton
                    name="targetSelectRadio"
                    options={OPERATIONMANAGEMENTCONSTANTS.selectDeviceTypeOptions}
                    filterValue="value"
                    filterKey="key"
                    defaultValue="deviceList"
                    onChange={(event, selectedOption) => {
                    this.onTargetSelectRadioChange(event, selectedOption);
                  }}
                  />
                </td>
              </tr>,
              <tr>
                <td>
                  {isTargetSelectByCSV ? (
                    <label
                      htmlFor="individual_csvfile"
                      className="btn-fileupload mr5"
                      style={{ width: '300' }}
                    >
                      <SBButton
                        type="file"
                        name="chooseDeviceByCSV"
                        id="choose-device-csv-file-input"
                      />
                    </label>
                ) : (
                  <SBButton
                    type="button"
                    title="Select"
                    name="selectDeviceButton"
                    id="selectDeviceButton"
                  />
                )}
                </td>
              </tr>,
          ]}
          {isUserCertificateOn && (
            <tr>
              <th>Grant Access</th>
              <td>
                <SBCheckbox
                  id="access_authority"
                  filterKey="name"
                  filterValue="name"
                  name="access_authority"
                  options={[{ name: '* Grant apps access to certification' }]}
                  onChange={(event, selectedOption) => {
                    this.onGrantAppsCheckBoxChange(event, selectedOption);
                  }}
                />
                {grantAppAccessSection && (
                  <ul
                    id="ulSelectApps"
                    className="mt10"
                    style={{ 'list-style-type': 'none', display: 'block' }}
                  >
                    <table className="table-yoko mb10">
                      <tbody>
                        <tr>
                          <th style={{ minWidth: '75px', width: '75px' }}>App name</th>
                          <td>
                            <div className="mb10">
                              <SBTextbox
                                type="text"
                                maxLength="100"
                                size="50"
                                name="appNameInput"
                                id="app-name-text-input"
                                style={{ width: '220px', marginRight: '10px' }}
                              />
                              <SBButton
                                type="button"
                                title="Search"
                                color="green"
                                name="searchButton"
                                id="search-apps-button"
                                onClick={() => {
                                  this.onSearchAppsButtonClick();
                                }}
                              />
                              <SBButton
                                type="button"
                                title="Clear"
                                name="clearAppsButton"
                                id="clear-apps-button"
                                onClick={() => {
                                  this.onClearAppsButtonClick();
                                }}
                              />
                            </div>
                            <SBTransfer
                              dataList={{
                                whiteList: this.whiteList(),
                                blackList: this.blackList(),
                              }}
                            />
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </ul>
                )}
              </td>
            </tr>
          )}
        </tbody>
      </table>
    );
  }
}

export default Certificate;
