/**
 * commonChangeAction is used to define change action in components.
 * @author:  Gobi R
 */
import { COMMON_CHANGE, COMMON_ON_SWITCH } from '../../actions/ActionTypes';

/**
 * commonChangeAction is used to check common components values change.
 * @param isModified
 */
const commonChangeAction = (isModified) => {
  return {
    type: COMMON_CHANGE,
    isModified,
  };
};

/**
 * commonOnChangeMenu is used to check menu change.
 * @param payload
 */
export const commonOnChangeMenu = (payload) => {
  return {
    type: COMMON_ON_SWITCH,
    payload,
  };
};

export default commonChangeAction;
