/**
 * NotificationValidations is used to perform validation functions used in Notification registration and update screens.
 * @author: V.Vijayaragavan
 */
import {
  isNotUndefinedOrNotEmpty,
  isLengthValid,
  validateFileSize,
} from '../../../common/CommonValidations/CommonValidations';
import { dateRegex } from '../constants/InfoFromSoftBankConstants';
import { getUserTodaysDate, validateMinMaxDate } from '../../../common/utils/dateUtils';

/**
 * validatePublicationPeriod is used to validate publication period
 * @param dateOfPublication
 * @param publicationPeriod
 * @param settingValue
 * @param offset
 * @return validationResult
 */
export function validatePublicationPeriod(
  dateOfPublication,
  publicationPeriod,
  settingValue,
  offset,
) {
  const validationResult = { status: true, key: '' };
  if (isNotUndefinedOrNotEmpty(publicationPeriod)) {
    if (dateRegex.test(publicationPeriod)) {
      const publicationPeriodValue = new Date(publicationPeriod);
      if (validateMinMaxDate(publicationPeriodValue)) {
        if (isNotUndefinedOrNotEmpty(dateOfPublication) || settingValue) {
          const dateOfPublicationValue = dateOfPublication
            ? new Date(dateOfPublication)
            : getUserTodaysDate(offset);
          if (publicationPeriodValue.getTime() === dateOfPublicationValue.setHours(0, 0, 0, 0)) {
            validationResult.status = false;
            validationResult.key = 'datesCannotBeSame';
          } else if (publicationPeriodValue < dateOfPublicationValue) {
            validationResult.status = false;
            validationResult.key = 'publicationPeriodBeforeDate';
          }
        } else if (!settingValue) {
          validationResult.status = false;
          validationResult.key = 'dateOfPublicationEmpty';
        }
      } else {
        validationResult.status = false;
        validationResult.key = 'publicationPeriodRange';
      }
    } else {
      validationResult.status = false;
      validationResult.key = 'publicationDateRegex';
    }
  }
  return validationResult;
}

/**
 * validateConfigurationDateTime is used to validate configuration date and time
 * @param date
 * @param time
 * @return validationResult
 */
export function validateConfigurationDateTime(date, time, offset) {
  const validationResult = { status: false, key: '' };
  if (isNotUndefinedOrNotEmpty(date) && !dateRegex.test(date)) {
    validationResult.status = false;
    validationResult.key = 'dateOfPublicationFormat';
    return validationResult;
  }
  if (isNotUndefinedOrNotEmpty(date)) {
    if (validateMinMaxDate(new Date(date))) {
      if (getUserTodaysDate(offset).setHours(0, 0, 0, 0) === new Date(date).getTime()) {
        validationResult.status = true;
      } else if (time === 'empty') {
        validationResult.status = false;
        validationResult.key = 'immediateerror';
      } else if (time && time !== 'empty') {
        validationResult.status = true;
      }
    } else {
      validationResult.status = false;
      validationResult.key = 'dateOfPublicationRange';
    }
  } else if (time === 'empty') {
    validationResult.status = true;
  }
  return validationResult;
}

/**
 * clearSettingLabel is used to find whether to clear setting Label or not
 * @param key
 * @return boolean
 */
export function clearSettingLabel(key) {
  return (
    key === 'dateOfPublication' ||
    key === 'dateOfPublicationTime' ||
    key === 'publishWithoutSendingEmail'
  );
}

/**
 * validateDateOfPublication is used to validate Date of Publication
 * @param date
 * @param time
 * @param settingValue
 * @return validationResult
 */
export function validateDateOfPublication(date, time, settingValue, offset) {
  const validationResult = { status: true, key: '' };
  if (isNotUndefinedOrNotEmpty(date)) {
    if (typeof date === 'string' ? dateRegex.test(date) : true) {
      if (validateMinMaxDate(new Date(date))) {
        if (isNotUndefinedOrNotEmpty(settingValue)) {
          if (
            new Date(`${date} ${time !== 'empty' ? time : ''}`).getTime() <
            (time !== 'empty'
              ? getUserTodaysDate(offset).getTime()
              : getUserTodaysDate(offset).setHours(0, 0, 0, 0))
          ) {
            validationResult.status = false;
            validationResult.key = 'pastDateOfPublication';
          } else {
            return validationResult;
          }
        } else {
          validationResult.status = false;
          validationResult.key = 'scheduleNotConfigured';
        }
      } else {
        validationResult.status = false;
        validationResult.key = 'dateOfPublicationRange';
      }
    } else {
      validationResult.status = false;
      validationResult.key = 'dateOfPublicationFormat';
    }
  }
  return validationResult;
}

/**
 * validateFileNameInput is used to validate file name
 * @param value
 * @param key
 * @return validationResult
 */
export function validateFileNameInput(value, key) {
  const validationResult = { status: true, key: '' };
  const fileNameRegex = /^[^:;/\\¥|,*?<>]+$/g;
  if (value) {
    if (fileNameRegex.test(value)) {
      if (!isLengthValid(value, 0, 30)) {
        validationResult.status = false;
        validationResult.key = `${key}LengthError`;
      }
    } else {
      validationResult.status = false;
      validationResult.key = `${key}NameError`;
    }
  }
  return validationResult;
}

/**
 * validateFileExtention is used to validate file extension
 * @param key
 * @param fileName
 * @return validationResult
 */
function validateFileExtention(key, fileName) {
  const lowerCasedFileName = fileName.toLowerCase();
  if (key === 'imageFileError') {
    return lowerCasedFileName.endsWith('.jpg') ||
      lowerCasedFileName.endsWith('.png') ||
      lowerCasedFileName.endsWith('.jpeg') ||
      lowerCasedFileName.endsWith('.gif')
      ? ''
      : 'imageFileExtensionError';
  }
  return '';
}

/**
 * validateFile is used to validate file and all other params
 * @param key
 * @param file
 * @return validationResult
 */
export function validateFile(key, file) {
  const validationResult = { status: true, fileError: '' };
  validationResult[key] = '';
  if (file) {
    const sizeValidationResult = validateFileSize(file, 10, 0);
    if (sizeValidationResult.status) {
      validationResult[key] = validateFileExtention(key, file.name);
      validationResult.status = !validationResult[key];
      return validationResult;
    }
    if (sizeValidationResult.key === 'file_size_greater') {
      validationResult[key] = validateFileExtention(key, file.name);
      validationResult.status = false;
      validationResult.fileError = 'fileSizeGreater';
    } else {
      validationResult.status = false;
      validationResult[key] = 'fileSizeZero';
    }
  }
  return validationResult;
}

/**
 * validateNotificationTitle is used to validate notification title
 * @param value
 * @return validationResult
 */
export function validateNotificationTitle(value) {
  const validationResult = { status: false, key: '' };
  if (value && value.split(' ').join('')) {
    if (isLengthValid(value, 0, 70)) {
      validationResult.status = true;
    } else {
      validationResult.key = 'titleLength';
    }
  } else {
    validationResult.key = 'titleError';
  }
  return validationResult;
}

/**
 * isImmediate is used to find whether notification is immediate or scheduled
 * @param date
 * @param time
 * @param offset
 * @param settingValue
 * @return boolean
 */
export function isImmediate(date, time, offset, settingValue) {
  if (date) {
    return time === 'empty' && Boolean(settingValue)
      ? new Date(date).setHours(0, 0, 0, 0) === getUserTodaysDate(offset).setHours(0, 0, 0, 0)
      : false;
  }
  return time === 'empty' && Boolean(settingValue);
}

/**
 * validateSearchDate is used to validateSearchDate format and range
 * @param date
 * @return String
 */
export function validateSearchDate(date) {
  if (date) {
    const dateRegexResult = typeof date === 'string' ? dateRegex.test(date) : true;
    if (dateRegexResult) {
      if (validateMinMaxDate(new Date(date))) {
        return '';
      }
      return 'range';
    }
    return 'format';
  }
  return '';
}
