import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import SBLayout from '../../../common/component/sblayout/SBLayout';
import RouteEndPointsV1 from '../constants/RouteEndPointsV1';
import DeviceList from '../../device/devicelist/container/DeviceList';
import SBError from '../../../common/component/sberror/SBError';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

const DeviceWrapperV1 = () => {
  return (
    <SBLayout>
      <Switch>
        <Route strict exact path={RouteEndPointsV1.DEVICELIST} component={DeviceList} />
        <Route>
          <SBError />
          <Redirect to={ROUTEENDPOINTS.ERROR} />
        </Route>
      </Switch>
    </SBLayout>
  );
};

export default DeviceWrapperV1;
