/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import fileDownload from 'js-file-download';
import isEmpty from 'lodash/isEmpty';
import encoding from 'encoding';
import PropTypes from 'prop-types';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import RoleResolver from '../../common/component/guard/RoleResolver';
/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { Role } from '../../constants/Role';
import { post, postWithBlobData } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import checkLanguage from '../../language/checkLanguage';
import deviceHeader from '../constants/deviceHeaderConstants';
import { tableList } from '../constants/deviceTableConstants';
import { serviceMapper } from '../common/deviceCommonFunctions';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import {
  getUserInformation,
  resetUserInformation,
  getUserGroupNameList,
  getUserSearchData,
} from '../../actions/DeviceAction';
import { getUserTodaysDate, dateBasedOnLocaleGeneral } from '../../common/utils/dateUtils';

/**
 * UserList is to display user information in grid
 * @author Srinivasan Ravi
 */
class UserList extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, userGroupList, location: getData } = props;
    const { userStatusList, userSearch } = (getData && getData.state) || {
      userStatusList: [],
      userSearch: '',
    };
    this.state = {
      disable: true,
      deleteDisable: false,
      editDisable: false,
      screenKey,
      userList: [],
      messageType: '',
      addDisable: false,
      pageLoading: true,
      gridLoading: false,
      openConfirmDialog: false,
      countInfoContents: 0,
      userSearch,
      statusList: userStatusList,
      userGroupList,
      selectedUserList: [],
      openErrorDialog: false,
      headerData: deviceHeader(screenKey),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount = () => {
    this.fetchUserList();
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param userSearch
   */
  onKeyDown = (event, userSearch) => {
    if (event.keyCode === 13)
      this.setState({ userSearch, gridLoading: true }, () => this.fetchUserList(true));
  };

  /**
   * onSearchText is to update the search condition text value
   * @param userSearch
   */
  onSearchText = (userSearch) => this.setState({ userSearch });

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   */
  onHandleClick = (screenKey) => {
    const { selectedUserList, statusList, userSearch } = this.state;
    const {
      history,
      location,
      getUserInformationData,
      resetUserInformationData,
      makeUserGroupList,
    } = this.props;
    if (screenKey === 'deleteUser' && selectedUserList.length > 1)
      this.setState({ openConfirmDialog: true });
    else if (screenKey === 'outputUserInformation' || screenKey === 'userCsvCreation') {
      this.setState({ gridLoading: true }, () => {
        this.downloadUserCsv(screenKey);
      });
    } else {
      if (screenKey === 'addUser') resetUserInformationData();
      if (screenKey === 'bulkUserDelete') makeUserGroupList();
      getUserInformationData({
        employeeIndex:
          screenKey === 'updateUser' || screenKey === 'deleteUser'
            ? this.getSingleSelectedUserData(selectedUserList)[0].employeeIndex
            : -1,
      });
      history.push(serviceMapper(screenKey), {
        parentScreen: location.pathname,
        userStatusList: statusList,
        userSearch,
      });
    }
  };

  /**
   * getUpdatedUserList used to get updated user list after multiple user delete operation
   * @return userList
   */
  getUpdatedUserList = () => {
    const { selectedUserList, userList } = this.state;
    return userList.map((userData) => {
      if (selectedUserList.indexOf(userData.employeeIndex) >= 0) {
        userData.statusId = 2;
        userData.employeeName = '';
      }
      return userData;
    });
  };

  /**
   * performMultiDelete used to perform multiple user delete operation
   */
  performMultiDelete = async () => {
    const { selectedUserList } = this.state;
    const response = await post(APIENDPOINTS.DEVICEMULTIUSERDELETE, selectedUserList);
    const result = await response.json();
    const { status, message } = result;
    if (status === 'OK') {
      const userListData = this.getUpdatedUserList();
      this.setState({
        userList: userListData,
        countInfoContents: userListData.length,
        disable: userListData.length === 0,
        deleteDisable: userListData.length && userListData[0].statusId === 2,
        editDisable: userListData.length === 0,
        selectedUserList: [],
        openConfirmDialog: false,
        openErrorDialog: true,
        messageType: 'success',
        message,
      });
    } else {
      this.setState({
        message,
        messageType: 'error',
        openErrorDialog: true,
      });
    }
    this.setState({ gridLoading: false, pageLoading: false, csvNodataError: false });
  };

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param value
   */
  handleSearchAction = (key, value) => {
    const searchActionMapper = {
      searchKeyword: () => this.onSearchText(value[key]),
      searchButton: () => this.fetchUserList(true, { ...value }),
    };
    return searchActionMapper[key] && searchActionMapper[key]();
  };

  /**
   * handleCheckboxChange is to handle grid checkbox events
   * @param selectedRowKeys
   */
  handleCheckboxChange = (selectedRowKeys) => {
    let { selectedUserList } = this.state;
    const { userList } = this.state;
    selectedUserList = selectedUserList.filter(
      (data) => !userList.some((record) => data === record.employeeIndex),
    );
    selectedRowKeys.forEach((data) => selectedUserList.push(userList[data].employeeIndex));
    const deleteDisableStatus =
      selectedRowKeys.length === 1
        ? this.getSingleSelectedUserData(selectedUserList)[0].statusId === 2
        : isEmpty(this.getMultiSelectedUserData(selectedUserList));
    this.setState({
      selectedUserList,
      disable: isEmpty(selectedRowKeys),
      editDisable: isEmpty(selectedRowKeys) ? true : selectedRowKeys.length > 1,
      deleteDisable: isEmpty(selectedRowKeys) ? true : deleteDisableStatus,
    });
  };

  /**
   * getSingleSelectedUserData used to return single selected user data
   * @param selectedUserList
   * @return userList
   */
  getSingleSelectedUserData = (selectedUserList) => {
    const { userList } = this.state;
    return userList.filter((data) => data.employeeIndex === selectedUserList[0]);
  };

  /**
   * getSingleSelectedUserData used to return multiple selected user data
   * @param selectedUserList
   * @return userList
   */
  getMultiSelectedUserData = (selectedUserList) => {
    const { userList } = this.state;
    return selectedUserList.filter(
      (data) => !userList.some((record) => data === record.employeeIndex && record.statusId === 2),
    );
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    this.setState({ disable: isEmpty(tableData) });
  };

  /**
   * handleSelectChange performs user grid rendering
   */
  handleSelectChange = () => {
    this.setState({ gridLoading: true }, () => this.getUserList());
  };

  /**
   * downloadUserCsv used to download user csv file based on serach filter.
   */
  downloadUserCsv = async (outputButtonName) => {
    const { userVo, currentLanguage } = this.props;
    const { userSearch, statusList, userGroupList } = this.state;
    const searchText = userSearch || '';
    const outputButtonKey = outputButtonName === 'outputUserInformation';
    const response = await postWithBlobData(APIENDPOINTS.CSVUSERDOWNLOAD, {
      searchText,
      userGroupList,
      statusList,
      outputButtonKey,
    });
    const result = await response.json();
    const { status, message } = result;
    if (status === 'OK') {
      const encodingType = currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8';
      this.setState({ gridLoading: false, csvNodataError: false }, () => {
        fileDownload(
          encoding.convert(message, outputButtonKey ? encodingType : 'UTF-8'),
          `${userVo.dmCode}_${userVo.businessName}_${
            outputButtonKey ? this.languageLabelCheck('user_information_list') : 'csv_creation_list'
          }_${dateBasedOnLocaleGeneral(getUserTodaysDate(userVo.timeZoneOffSet))}.csv`,
        );
      });
    } else if (message === 'userinfo_nodata_csv') {
      this.setState({
        message,
        userList: [],
        gridLoading: false,
        csvNodataError: true,
        countInfoContents: 0,
      });
    } else {
      this.setState({
        message,
        gridLoading: false,
        csvNodataError: false,
        messageType: 'error',
        openErrorDialog: true,
      });
    }
  };

  /**
   * languageLabelCheck is to set label content
   * @param label
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * fetchUserList is to load list of user records based on search data
   * @param value
   */
  fetchUserList = async (isReduxData, value) => {
    const { makeUserSearchData } = this.props;
    const { userSearch: userSearchData, statusList: statusListData } = this.state;
    const { searchKeyword: userSearch, checkBoxKeys: statusList } = value || {
      searchKeyword: userSearchData,
      checkBoxKeys: statusListData,
    };
    if (isReduxData) makeUserSearchData(statusList, userSearch);
    this.setState({ gridLoading: true, userSearch, statusList }, () => this.getUserList(!value));
  };

  /**
   * setGridDataIndex is to set index key of grid data list & set griDataList to redux
   * @param gridList
   * @param dataMapper
   * @return dataList
   */
  setGridDataIndex = (gridList = []) => {
    const userListData = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;
      return record;
    });
    return { userListData };
  };

  /**
   * setSelectedRecord is to handle initial grid selection
   * @param groupData
   * @return groupDataList
   */
  setSelectedRecord = () => {
    const { userList, selectedUserList } = this.state;
    return userList
      .filter((data) => selectedUserList.indexOf(data.employeeIndex) > -1)
      .map((record) => record.id);
  };

  /**
   * getUserList used to get user information list from db
   * @param onOpen
   */
  getUserList = async (onOpen) => {
    const { userSearch, statusList, userGroupList } = this.state;
    const searchText = userSearch || '';
    const response = await post(APIENDPOINTS.GETUSERLIST, {
      searchText,
      userGroupList,
      statusList,
    });
    const result = await response.json();
    const { status, data: userList, message } = result;
    if (status === 'OK') {
      const { userListData } = this.setGridDataIndex(userList);
      this.setState(
        {
          userList: userListData,
          countInfoContents: userListData.length,
          addDisable: message,
          disable: true,
          deleteDisable: true,
          selectedUserList: [],
          pageLoading: false,
        },
        () => {
          if (onOpen && this.searchRef.current) this.searchRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        gridLoading: false,
        pageLoading: false,
        messageType: 'error',
        openErrorDialog: true,
      });
    }
    this.setState({ gridLoading: false, pageLoading: false, csvNodataError: false });
  };

  /**
   * render performs user list screen rendering
   * @return UserList
   */
  render() {
    const {
      headerData,
      screenKey,
      userSearch,
      pageLoading,
      gridLoading,
      countInfoContents,
      userList,
      userGroupList,
      disable,
      deleteDisable,
      message,
      csvNodataError,
      openErrorDialog,
      statusList,
      messageType,
      addDisable,
      editDisable,
      openConfirmDialog,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <RoleResolver authorityRoleType={Role.userInformationAuthority}>
          <SBButton add disabled={addDisable} onClick={() => this.onHandleClick('addUser')} />
        </RoleResolver>
        <div className="contents-main">
          <CustomTableFrame
            tableList={tableList()}
            dataSource={{
              selectGroup: userGroupList
                .sort((present, next) => (present.toUpperCase() < next.toUpperCase() ? -1 : 1))
                .join(','),
            }}
            onTableFrameEvent={(key) => this.onHandleClick(key)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
        </div>
        <DeviceSearch
          searchHeader="search_phone_number_or_name"
          screenKey={screenKey}
          searchText={userSearch}
          defaultValues={statusList}
          reference={this.searchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '62%', left: 'calc(40% - 55px)' }}
        >
          <DeviceGrid
            pagination
            gridList={userList}
            screenKey={screenKey}
            countInfo="search_result_with_count"
            filterMessage="results_per_page"
            countInfoContents={countInfoContents}
            handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
            defaultSelected="userList"
            defaultSelectedKeys={this.setSelectedRecord()}
            type="checkbox"
            rowKey="id"
            handleCheckboxChange={(event, selectedRowKeys) =>
              this.handleCheckboxChange(selectedRowKeys)
            }
            handleSelectAll={(event, selectedRowKeys) => this.handleCheckboxChange(selectedRowKeys)}
            handleClearAll={(event, selectedRowKeys) => this.handleCheckboxChange(selectedRowKeys)}
            handleSelectChange={() => this.handleSelectChange()}
          />
          <DeviceFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            footer={{
              screenKey,
              disable,
              deleteDisable,
              editDisable,
            }}
          />
          <div className="red bold mt10" style={{ display: csvNodataError ? 'block' : 'none' }}>
            {this.languageLabelCheck(message)}
          </div>
        </CircularSpinner>
        <SBDialog
          type="confirmation"
          width="400px"
          open={openConfirmDialog}
          header="delete_confirmation"
          content="confirm_bulk_user_delete"
          onOk={() =>
            this.setState({ gridLoading: true, openConfirmDialog: false }, () =>
              this.performMultiDelete('confirmOk'),
            )
          }
          onCancel={() => this.setState({ openConfirmDialog: false })}
        />
        <SBDialog
          type={messageType}
          width="400px"
          header={messageType}
          okButtonTitle="ok"
          content={message}
          open={openErrorDialog}
          onOk={() => this.setState({ openErrorDialog: false })}
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from user/group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { userGroupList, userStatusList, userSearch } = state.device;
  const { userVo } = state.user;
  const { current, data } = state.language;
  return {
    userVo,
    userSearch,
    userGroupList,
    userStatusList,
    localeData: data,
    currentLanguage: current,
  };
};

/**
 * mapDispatchToProps performs data setup in user/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    getUserInformationData: (data) => {
      dispatch(getUserInformation(data));
    },
    resetUserInformationData: () => {
      dispatch(resetUserInformation());
    },
    makeUserGroupList: (data) => {
      dispatch(getUserGroupNameList(data));
    },
    makeUserSearchData: (userStatusList, userSearch) => {
      dispatch(getUserSearchData(userStatusList, userSearch));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
UserList.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      groupId: PropTypes.string,
    }),
  }).isRequired,
  currentLanguage: PropTypes.string.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  makeUserGroupList: PropTypes.func.isRequired,
  getUserInformationData: PropTypes.func.isRequired,
  resetUserInformationData: PropTypes.func.isRequired,
  makeUserSearchData: PropTypes.func.isRequired,
  userGroupList: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(UserList));
