/** Base Files */
import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Utils from '../../constants/Utils';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import checkLanguage from '../../language/checkLanguage';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import descriptionConverter from '../../common/utils/descriptionConverter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';

import PLAY_CONSTANTS from '../../constants/GooglePlayConstants';

/**
 * ApplicationEnrollment is to enroll application from android work store
 * @author Vengatesan Sivasamy
 */
class ApplicationEnrollment extends React.Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      message: '',
      dialogtitle: '',
      redirectPath: '',
      pageLoading: true,
      modalIsOpen: false,
      enterpriseError: false,
    };
  }

  /**
   * componentDidMount is to fetch data for android work store frames
   */
  async componentDidMount() {
    const { history } = this.props;
    const hostURL = window.location.origin;
    const googleplayURL = hostURL + ROUTEENDPOINTS.APPLICATIONADDGOOGLEPLAY;
    const thisOperator = this;
    try {
      const response = await post(APIENDPOINTS.ANDROIDWEBTOKEN, { parentURL: googleplayURL });
      const data = await response.json();
      const { value: token } = data;
      if (token === undefined) {
        this.setState({
          dialogtitle: 'error',
          modalIsOpen: true,
          message: 'enterprise_is_not_created',
          redirectPath: ROUTEENDPOINTS.ENTERPRISE,
          enterpriseError: true,
          pageLoading: false,
        });
      } else {
        const url = PLAY_CONSTANTS.URL;
        const finalUrl = url.replace('web_token', token);
        this.setState({ pageLoading: false });
        window.gapi.load('gapi.iframes', function googleAPI() {
          const options = {
            url: finalUrl,
            where: document.getElementById('container'),
            attributes: { style: 'height:inherit', scrolling: 'no' },
          };
          const iframe = window.gapi.iframes.getContext().openChild(options);
          iframe.register(
            'onproductselect',
            async function prepareApplication(event) {
              const approveResponse = event;
              const applicationPolicy = {};
              applicationPolicy.packageName = approveResponse.packageName;
              applicationPolicy.appStatus = approveResponse.action;
              applicationPolicy.provider = approveResponse.productId;
              applicationPolicy.installType = 'available';
              try {
                const receiver = await post(APIENDPOINTS.GETAPPLICATIONLIST, applicationPolicy);
                const result = await receiver.json();
                const { status, message } = result;
                if (status === 'OK') {
                  thisOperator.setState({
                    dialogtitle: 'success',
                    modalIsOpen: true,
                    message: 'save_successful',
                    redirectPath: ROUTEENDPOINTS.APPLICATIONADD,
                  });
                } else {
                  thisOperator.setState({
                    dialogtitle: 'error',
                    modalIsOpen: true,
                    message,
                    redirectPath: '',
                  });
                }
              } catch (Exception) {
                history.push('error');
              }
            },
            window.gapi.iframes.CROSS_ORIGIN_IFRAMES_FILTER,
          );
        });
      }
    } catch (Exception) {
      history.push('error');
    }
  }

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render performs android work store frame rendering
   * @return AndroidWorkFrame
   */
  render() {
    const {
      pageLoading,
      modalIsOpen,
      dialogtitle,
      redirectPath,
      message,
      enterpriseError,
    } = this.state;
    const { history } = this.props;
    const enterpriseURL = `${Utils.API_URL_ORIGIN}/setting/androidenterprise/enterprise`;
    return (
      <div>
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <div id="container" style={{ height: 'calc(100vh - 165px)' }}>
            {enterpriseError && (
              <div className="contents-headder clearfix" style={{ height: 'inherit' }}>
                <div className="mb15 clearfix" style={{ display: 'flex', height: 'inherit' }}>
                  <div className="contents-title">
                    <h1>{descriptionConverter(this.languageLabelCheck('google_play_header'))}</h1>
                  </div>
                  <div className="contents-description">
                    <div>
                      <p>
                        {descriptionConverter(this.languageLabelCheck('enterprise_not_created'))}
                      </p>
                      <a href={enterpriseURL}>
                        {descriptionConverter(this.languageLabelCheck('enterprise_link'))}
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </CircularSpinner>
        <SBDialog
          open={modalIsOpen}
          header={dialogtitle}
          type={dialogtitle}
          content={message}
          onOk={() => {
            this.setState({ modalIsOpen: false });
            if (redirectPath !== '' && redirectPath !== ROUTEENDPOINTS.ENTERPRISE)
              history.push(redirectPath);
          }}
          width="400px"
        />
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { data: localeData } = state.language;
  return { localeData };
};

/**
 * propTypes is used for validation props with its type.
 */
ApplicationEnrollment.propTypes = {
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  isNotMultiLang: PropTypes.bool,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
ApplicationEnrollment.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps, null)(withRouter(ApplicationEnrollment));
