/** Base Files */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import SBComboBox from '../../common/component/combobox/SBComboBox';
import RoleResolver from '../../common/component/guard/RoleResolver';

/** Utility Files */
import { buttonList, setStyle } from '../constants/deviceButtonConstants';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

/**
 * DeviceFooter is to render the group management CRUD operation buttons
 * @author Aswin Palanisamy
 */
class DeviceFooter extends Component {
  /**
   * onHandleClick performs button click operation in footer room
   * @param key
   */
  onHandleClick = key => {
    const { onHandleClick } = this.props;
    if (onHandleClick) onHandleClick(key);
  };

  /**
   * onHandleChange performs combobox change operation in footer room
   * @param key
   * @param event
   */
  onHandleChange = (key, event) => {
    const { onHandleChange } = this.props;
    if (onHandleChange) onHandleChange(key, event);
  };

  /**
   * renderFooterButton performs footer room button rendering
   * @param footer
   * @return footerButtons
   */
  renderFooterButton = footer => {
    const { footer: dynamicData } = this.props;
    footer.value = dynamicData.value;
    const footerComponent = {
      button: <SBButton {...footer} onClick={() => this.onHandleClick(footer.key)} />,
      comboBox: <SBComboBox {...footer} onChange={(event) => this.onHandleChange(footer.key, event)} />,
    };
    return (
      <RoleResolver authorityRoleType={footer.authorityRoleType}>
        {footerComponent[footer.type || 'button']}
      </RoleResolver>
    );
  };

  /**
   * render performs footer room component rendering
   * @return footerComponent
   */
  render() {
    const { footer, userVo } = this.props;
    const { screenKey, setView } = footer;
    const leftPanel = buttonList(footer, `${screenKey}LeftPanel`, userVo);
    const subPanel = buttonList(footer, `${screenKey}LeftSubPanel`, userVo);
    const rightPanel = buttonList(footer, `${screenKey}RightPanel`, userVo);
    return (
      <div className="clearfix" style={setStyle('userTab') || {}}>
        {leftPanel && (
          <div className="floatleft" style={setStyle('userTabLeftPanel') || { width: '65%' }}>
            {subPanel && (
              <span
                style={{
                  padding: '5px',
                  display: 'inline-block',
                  border: '1px solid #B2B2B2',
                }}
                className="mr10"
              >
                {subPanel.map(record => this.renderFooterButton(record))}
              </span>
            )}
            {!setView && leftPanel.map(record => this.renderFooterButton(record))}
          </div>
        )}
        {rightPanel && (
          <div className="floatright" style={setStyle('userTabRightPanel') || {}}>
            {rightPanel.map(record => this.renderFooterButton(record))}
          </div>
        )}
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { userVo } = state.user;
  return { userVo };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  onHandleChange: PropTypes.func.isRequired,
  footer: PropTypes.shape({
    screenKey: PropTypes.string.isRequired,
    setView: PropTypes.bool,
    value: PropTypes.string,
  }).isRequired,
  userVo: commonPropTypes().userVo.isRequired,
};

export default connect(mapStateToProps)(withRouter(DeviceFooter));
