/**
 * CommonFrame is used to create screen structure
 * @author:  P.Vinothpandian
 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import ConfirmationFrame from './ConfirmationFrame';
import TableFrame from './TableFrame';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBTable from '../../../common/component/sbtable/SBTable';
import SearchFilter from './SearchFilter';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import checkLanguage from '../../../language/checkLanguage';

class CommonFrame extends Component {
  onClick = (event, value) => {
    const { onaddClick } = this.props;
    if (onaddClick) {
      onaddClick(event, value);
    }
  };

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  onTableFrameEvent = (key, event, value) => {
    const { onTableFrameEvent } = this.props;
    if (onTableFrameEvent) {
      onTableFrameEvent(key, event, value);
    }
  };

  onSearchAction = (key, event, values) => {
    const { onSearchAction } = this.props;
    if (onSearchAction) {
      onSearchAction(key, event, values);
    }
  };

  onConfirmationFrameEvent = (event, value) => {
    const { onConfirmationFrameEvent } = this.props;
    if (onConfirmationFrameEvent) {
      onConfirmationFrameEvent(event, value);
    }
  };

  handleRadioChange = (event, value) => {
    const { onSBTableContentEvent } = this.props;
    if (onSBTableContentEvent) {
      onSBTableContentEvent(event, value);
    }
  };

  handleCheckboxChange = (event, value) => {
    const { onSBTableContentEvent } = this.props;
    if (onSBTableContentEvent) {
      onSBTableContentEvent(event, value);
    }
  };

  renderContentsMain = data => {
    return Object.keys(data).map(value => {
      const componentList = data[value];
      return <div style={{ marginBottom: 20 }}>{this.renderComponents(componentList, value)}</div>;
    });
  };

  renderComponents = (componentList, component) => {
    switch (component) {
      case 'addbutton':
        return (
          <RoleResolver authorityRoleType={componentList.authorityRoleType}>
            <SBButton
              add={componentList.add}
              ref={componentList.ref}
              disabled={componentList.disabled}
              onClick={(event, value) => {
                this.onClick(event, value);
              }}
            />
          </RoleResolver>
        );
      case 'searchpanel':
        return (
          <SearchFilter
            filterIconsWithCheckBox={componentList.filterIconsWithCheckBox}
            border={componentList.border}
            headerButton={componentList.headerButton}
            filterIcons={componentList.filterIcons}
            searchOptions={componentList.searchOptions}
            onSearchAction={(key, event, values) => this.onSearchAction(key, event, values)}
          />
        );
      case 'tableframe':
        return (
          <TableFrame
            onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            tableList={componentList.viewSource}
            dataSource={componentList.dataSource}
            className={componentList.className}
            tableHeaderList={componentList.tableHeaderList}
          />
        );
      case 'sbtable':
        return (
          <RoleResolver
            component="sbtable"
            columns={componentList.columns}
            authorityRoleType={componentList.authorityRoleType}
          >
            <SBTable
              {...componentList}
              handleRadioChange={(event, value) => this.handleRadioChange(event, value)}
              handleCheckboxChange={(event, value) => this.handleCheckboxChange(event, value)}
            />
          </RoleResolver>
        );
      case 'tableheader':
        return <SBTableHeader headerText={componentList.headerText} />;
      case 'div':
        return (
          <div className={componentList.class} style={componentList.style}>
            {this.renderDivComponents(componentList.componentList)}
          </div>
        );
      case 'span':
        return (
          <span key={componentList.key} className={componentList.class} style={componentList.style}>
            {this.languageLabelCheck(componentList.text)}
          </span>
        );
      default:
        return <div />;
    }
  };

  renderDivComponents = componentList => {
    return Object.entries(componentList).map(values => {
      const generatedList = values[1];
      return this.renderComponents(generatedList, values[0]);
    });
  };

  renderContentsFooter = () => {
    const { footerComponentList } = this.props;
    const { floatLeftList, floatRightList } = footerComponentList;
    return (
      <div>
        {(floatLeftList || floatRightList) && (
          <ConfirmationFrame
            floatLeftList={floatLeftList}
            floatRightList={floatRightList}
            onConfirmationFrameEvent={(key, value) => this.onConfirmationFrameEvent(key, value)}
          />
        )}
      </div>
    );
  };

  render() {
    const {
      headerInformation,
      mainComponentList,
      footerStyle,
      footerClass,
      footerComponentList,
    } = this.props;
    return (
      <div id="contents" className="clearfix">
        <div id="main">
          <div className="container">
            {headerInformation && (
              <div style={{ height: 90 }}>
                <SBContentHeader
                  title={headerInformation.title}
                  description={headerInformation.description}
                />
              </div>
            )}

            {mainComponentList &&
              mainComponentList.map(data => {
                return (
                  <div className={`contents-main ${data.class}`} style={data.style}>
                    {this.renderContentsMain(data.componentList)}
                  </div>
                );
              })}

            {footerComponentList && (
              <div className={`footerbtn clearfix ${footerClass || ''}`} style={footerStyle}>
                {this.renderContentsFooter()}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};
export default connect(mapStateToProps)(CommonFrame);
