/** Base File */
import React, { createRef } from 'react';
import PropTypes from 'prop-types';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import RoleResolver from '../../common/component/guard/RoleResolver';

/** Utility File */
import { Role } from '../../constants/Role';

/**
 * GroupManagementAdd perform add button rendering
 * @author Aswin Palanisamy
 */
const GroupManagementAdd = props => {
  const { reference, onHandleClick, buttonKey } = props;

  /**
   * onClick performs button click operation in register room
   * @param event
   */
  const onClick = event => {
    if (event.keyCode === 13 || event.keyCode === 32) event.preventDefault();
    if (onHandleClick) onHandleClick(buttonKey);
  };

  return (
    <RoleResolver authorityRoleType={Role.group1}>
      <SBButton add {...props} ref={reference} onClick={event => onClick(event)} />
    </RoleResolver>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
GroupManagementAdd.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
  buttonKey: PropTypes.string.isRequired,
  reference: PropTypes.instanceOf(createRef()),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
GroupManagementAdd.defaultProps = {
  disabled: false,
  reference: createRef(),
};

export default GroupManagementAdd;
