/**
 * IDUpdate is used to load the ID Update screen
 * @author: P.Vinothpandian
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import UNDERSCORE from '../../../v1/common/utils';
import {
  isLengthValid,
  validateMultipleIps,
  isNotUndefinedOrNotEmpty,
} from '../../../common/CommonValidations/CommonValidations';
import { post, put } from '../../../actions/commonActions';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import IDCRUDFooter from './IDCRUDFooter';
import checkLanguage from '../../../language/checkLanguage';
import {
  loginIdRegex,
  tableListView,
  userAuthority,
  tableUpdateList,
  errorMessageConstants,
  setReference,
} from '../Constants/IDCRUDconstants';
import headerInformation from '../Constants/IDHeaderConstants';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';

class IDUpdate extends Component {
  /**
   * constructor is used to initalize the state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { userVo, location, history } = props;
    let param = {};
    if (!userVo && !userVo.masterCode) {
      history.push(ROUTEENDPOINTS.ERROR);
    } else {
      param = location.state && location.state.param ? location.state.param : {};
    }
    this.state = {
      headerData: headerInformation('updateid'),
      dataSource: param,
      pageLoading: true,
      modalIsOpen: false,
      masterCode: userVo.masterCode,
      fieldDataList: [],
      dynamicData: {},
      errorMessage: '',
      lock: true,
      sessionlock: true,
      footer: { okDisable: true, primaryVisibile: true },
      reference: setReference(),
    };
  }

  /**
   * componentDidMount makes page loading state false
   */

  componentDidMount() {
    const { history } = this.props;
    const { reference, dataSource, dynamicData } = this.state;
    const getUserDetails = async () => {
      this.setState({ fieldDataList: tableUpdateList(dynamicData, reference), pageLoading: true });
      const data = await post(APIENDPOINTS.IDUSERDETAILS, dataSource);
      const userDetails = await data.json();
      if (userDetails.status) {
        const { idListVo } = userDetails;
        this.setState({
          pageLoading: false,
          dataSource: userDetails.idListVo,
          lock: idListVo.lockUserUpto,
          sessionlock: idListVo.loginStatus,
        });
      } else {
        history.push(ROUTEENDPOINTS.ERROR);
      }
      this.setInitialFocusToScreen();
    };
    getUserDetails();
  }

  /**
   * setInitialFocusToScreen sets focus on first component when screen is loaded
   */

  setInitialFocusToScreen = () => {
    const { reference, dataSource } = this.state;
    if (reference.loginIdRef.current && !userAuthority.includes(dataSource.authorityId))
      reference.loginIdRef.current.focus();
    else if (reference.ipAddressRef.current) {
      reference.ipAddressRef.current.focus();
      reference.ipAddressRef.current.selectionStart =
        reference.ipAddressRef.current.defaultValue.length;
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   *
   */

  onTableFrameEvent = (key, event) => {
    const { dataSource, footer } = this.state;
    dataSource[key] = event.target.value;
    footer.okDisable = false;
    this.setState({ dataSource, footer });
  };

  /**
   * onKeyDown is used to perform key down operation
   * @param event
   */

  onKeyDown = event => {
    const { footer } = this.state;
    const { okDisable } = footer;
    if (event.keyCode === 13 && !okDisable) {
      event.preventDefault();
      this.onHandleClick('save');
    }
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { history } = this.props;
    switch (key) {
      case 'save':
        this.showDialog();
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.IDLIST, JSON.parse(localStorage.getItem('idListParam')));
        break;
      case 'back':
        history.push(ROUTEENDPOINTS.IDLIST);
        break;
      case 'lock':
        this.unlockUser();
        break;
      case 'sessionlock':
        this.unlockSession();
        break;
      default:
        break;
    }
  };

  /**
   * setErrorAndFocus is used to display error and focus
   * @param key
   */

  setErrorAndFocus = (errorType, errorMessage) => {
    let { fieldDataList } = this.state;
    const { reference, dynamicData } = this.state;
    dynamicData[errorType] = errorMessage;
    fieldDataList = tableUpdateList(dynamicData, reference);
    this.setState({
      modalIsOpen: false,
      fieldDataList,
      pageLoading: false,
    });
    if (errorType === 'loginError') {
      reference.loginIdRef.current.focus();
    }
  };

  /**
   * onOkDialog is used to perfom save operation
   *
   */

  onOkDialog = () => {
    const { dataSource, footer, reference } = this.state;
    const { setModified, history } = this.props;
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      dataSource.ipAddress = isNotUndefinedOrNotEmpty(dataSource.ipAddress)
        ? dataSource.ipAddress.trim()
        : null;
      const response = await put(APIENDPOINTS.UPDATEADMIN, dataSource);
      const resultSet = await response.json();
      if (resultSet.status) {
        setModified(false);
        this.setState({
          modalIsOpen: false,
          dataSource: resultSet.idListVo,
          pageLoading: false,
        });
        history.push(ROUTEENDPOINTS.IDFINISH, {
          idListVo: resultSet.idListVo,
        });
      } else if (resultSet.validationError) {
        const { validationError } = resultSet;
        if (validationError && validationError.loginError) {
          this.setErrorAndFocus('loginError', resultSet.validationError.loginError);
        } else {
          footer.primaryVisibile = false;
          this.setState({
            modalIsOpen: false,
            footer,
            pageLoading: false,
            errorMessage: validationError.noUserError,
          });
          if (reference.backRef.current) reference.backRef.current.focus();
        }
      } else {
        footer.primaryVisibile = false;
        this.setState({
          footer,
          fieldDataList: tableListView(false),
          pageLoading: false,
          errorMessage: 'user_deleted_contact_admin',
        });
        setModified(false);
        if (reference.backRef.current) reference.backRef.current.focus();
      }
    };
    saveProcess();
  };

  /**
   * setValidationErrorMessage is used to set error validation message
   * @param key
   * @param validationResultObject
   */

  setValidationErrorMessage(key, validationResultObject) {
    const { dynamicData } = this.state;
    dynamicData[key] = errorMessageConstants[key][validationResultObject.error];
    this.setState(dynamicData);
  }

  /**
   * unlockUser is used to unlock the user
   * @param key
   */

  unlockUser = () => {
    const unlock = async () => {
      const { dataSource, footer, reference } = this.state;
      const response = await put(`${APIENDPOINTS.UNLOCKADMIN}/${dataSource.userId}`);
      const resultSet = await response.json();
      if (resultSet.status) {
        this.setState({
          errorMessage: '',
          lock: true,
        });
      } else if (resultSet.validationError) {
        const { validationError } = resultSet;
        footer.primaryVisibile = false;
        this.setState({
          footer,
          errorMessage: validationError.noUserError,
        });
        if (reference.backRef.current) reference.backRef.current.focus();
      }
    };
    unlock();
  };
  /**
   * unlockSession is used to unlock the user
   * @param key
   */

  unlockSession = () => {
    const unlockSession = async () => {
      const { dataSource, footer, reference } = this.state;
      const response = await put(`${APIENDPOINTS.SESSIONUNLOCK}/${dataSource.userId}`);
      const resultSet = await response.json();
      if (resultSet.status) {
        this.setState({
          sessionlock: false,
        });
      } else if (resultSet.validationError) {
        const { validationError } = resultSet;
        footer.primaryVisibile = false;
        this.setState({
          footer,
          errorMessage: validationError.noUserError,
        });
        if (reference.backRef.current) reference.backRef.current.focus();
      }
    };
    unlockSession();
  };
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * setFocusinerrorField is used to set focus in error field
   */

  setFocusinerrorField = () => {
    const { reference, dynamicData } = this.state;
    if (dynamicData.loginError && dynamicData.loginError !== '') {
      reference.loginIdRef.current.focus();
    } else if (dynamicData.authorityError && dynamicData.authorityError !== '') {
      reference.authorityRef.current.focus();
    } else if (dynamicData.ipError && dynamicData.ipError !== '') {
      reference.ipAddressRef.current.focus();
    }
  };

  /**
   * showDialog is used to show dialog
   * @param key
   */

  showDialog = () => {
    const { dynamicData, reference } = this.state;
    const inputStatus = this.validateInputs();
    this.setState({
      modalIsOpen: inputStatus,
      fieldDataList: tableUpdateList(dynamicData, reference),
    });
    this.setFocusinerrorField();
  };

  /**
   * validateAuthority is used to authority fields
   */

  validateAuthority = () => {
    const { dataSource, masterCode } = this.state;
    const result = { status: true, error: '' };
    if (parseInt(dataSource.authorityId, 10) !== 5 && dataSource.dmCode !== masterCode) {
      return { status: false, error: 'masterCode' };
    }
    if (parseInt(dataSource.authorityId, 10) === 5 && dataSource.dmCode === masterCode) {
      return { status: false, error: 'incorrectCode' };
    }
    return result;
  };

  getAuthorityValidationData = () => {
    const { dataSource } = this.state;
    if (parseInt(dataSource.authorityId, 10) === 5) {
      return {
        minLength: 13,
        maxLength: 13,
        lengthmsg: 'sbtmLength',
        msg: 'sbtm',
        regex: /^ISOC[0-9]{7,7}SV/,
      };
    }
    return {
      minLength: 4,
      maxLength: 20,
      lengthmsg: 'length',
      msg: 'alphanumeric',
      regex: /^[a-zA-Z0-9]+$/,
    };
  };

  /**
   * validateLoginId is used to check loginId
   * @param loginId
   * @return result
   */
  validateLoginId = () => {
    const { dataSource } = this.state;
    const { loginId } = dataSource;
    const authorityValidationData = this.getAuthorityValidationData();
    const result = { status: true, error: '' };
    if (isNotUndefinedOrNotEmpty(loginId)) {
      if (
        isLengthValid(loginId, authorityValidationData.minLength, authorityValidationData.maxLength)
      ) {
        if (loginIdRegex(loginId, authorityValidationData.regex)) {
          result.status = true;
        } else {
          result.status = false;
          result.error = authorityValidationData.msg;
        }
      } else {
        result.status = false;
        result.error = authorityValidationData.lengthmsg;
      }
    } else {
      result.status = false;
      result.error = 'empty';
    }
    return result;
  };
  /**
   * validateInputs is used to validate inputs
   * @return status
   */

  validateInputs = () => {
    const { dataSource } = this.state;
    this.setState({ dynamicData: {} });
    let authorityValidation = { status: true, error: '' };
    const loginValidation = this.validateLoginId();
    const ipValidation = validateMultipleIps(dataSource.ipAddress);
    this.setValidationErrorMessage('loginError', loginValidation);
    this.setValidationErrorMessage('ipError', ipValidation);
    if (!userAuthority.includes(dataSource.authorityId)) {
      authorityValidation = this.validateAuthority();
      this.setValidationErrorMessage('authorityError', authorityValidation);
    }
    return authorityValidation.status && loginValidation.status && ipValidation.status;
  };

  /**
   * validatePasswords is used to validate passwords fields
   * @return status
   */

  validatePasswords() {
    const { dataSource } = this.state;
    const result = { status: true, error: '' };
    if (!UNDERSCORE.isEqual(dataSource.password, dataSource.confirmpass)) {
      return { status: false, error: 'notsame' };
    }
    return result;
  }

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const {
      headerData,
      dataSource,
      modalIsOpen,
      dynamicData,
      reference,
      footer,
      pageLoading,
      fieldDataList,
      errorMessage,
      lock,
      sessionlock,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame
                  tableList={
                    fieldDataList.length ? fieldDataList : tableUpdateList(dynamicData, reference)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <IDCRUDFooter
                  update
                  lock={lock}
                  sessionlock={!sessionlock}
                  reference={reference}
                  footer={footer}
                  onHandleClick={key => this.onHandleClick(key)}
                />
              </div>
              <br />
              <span className="red bold">{this.languageLabelCheck(errorMessage)}</span>
              <SBDialog
                open={modalIsOpen}
                header="save_confirmation"
                type="confirmation"
                content="want_to_update_data"
                onCancel={() => {
                  this.setState({ modalIsOpen: false });
                }}
                onOk={() => {
                  this.onOkDialog();
                }}
                width="400px"
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  const { userVo } = state.user;
  return {
    userVo,
    localeData: data,
  };
};

/**
 * mapDispatchToProps is used to dispatch value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */

IDUpdate.propTypes = {
  history: commonPropTypes().history.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
IDUpdate.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(IDUpdate));
