/**
 * AppManageHeaderConstants is used to load the header operations
 * @author: P.Vinothpandian
 */

/**
 * headerInformation is used to load the header operations
 * @param key
 * @return headerInformationMap
 */

export default function headerInformation(key) {
  switch (key) {
    case 'registerapp':
      return {
        title: 'app_registration',
        description: ['app_registration_desc'],
      };
    case 'updateapp':
      return {
        title: 'app_update',
        description: ['app_update_desc'],
      };
    case 'operationinfo':
      return {
        title: 'operational_registrations',
        description: [],
      };
    case 'contractinfo':
      return {
        title: 'contract_registration',
        description: [],
      };
    case 'gisun':
      return {
        title: 'gisun',
        description: ['gisun_desc'],
      };
    case 'apppayment':
      return {
        title: 'app_approval',
        description: ['app_approval_desc'],
      };
    case 'appfinish':
      return {
        title: 'app_registration',
        description: ['app_complete_desc'],
      };
    case 'appupdatefinish':
      return {
        title: 'app_update',
        description: ['app_update_complete_desc'],
      };
    case 'contractfinish':
      return {
        title: 'contract_finish',
        description: [],
      };
    case 'operationfinish':
      return {
        title: 'operation_finish',
        description: [],
      };
    case 'applist':
      return {
        title: 'applist',
        description: ['applist_desc'],
      };
    case 'appinfo':
      return {
        title: 'appinfo',
        description: ['appinfo_desc'],
      };
    case 'dashboard':
      return {
        title: 'dashboard',
        description: ['dashboard_desc'],
      };

    case 'changepassword':
      return {
        title: 'password_change',
        description: ['user_change_password_desc'],
      };
    case 'changepasswordsuccess':
      return {
        title: 'password_change',
        description: ['user_change_password_success'],
      };
    case 'deleteapp':
      return {
        title: 'app_delete',
        description: ['app_delete_desc'],
      };
    default:
      return {};
  }
}
