/**
 * TableConstants is used to load the table frame with its components on screen
 * @author: Aakash Alagarsamy
 */

/**
 * filterData is used to load the field values of corresponding radio or combo box
 */
export const filterData = {
  yesorno: [
    { filterKey: true, filterValue: 'dashboard_setting_on' },
    { filterKey: false, filterValue: 'dashboard_setting_off' },
  ],
  periodOptions: [
    {
      value: '1d',
      option: 'every_day',
    },
    {
      value: '3d',
      option: 'every_three_days',
    },
    {
      value: '1w',
      option: 'every_week',
    },
    {
      value: '1m',
      option: 'every_month',
    },
  ],
  PeriodInfo: {
    period_1d: 'every_day',
    period_3d: 'every_three_days',
    period_1w: 'every_week',
    period_1m: 'every_month',
  },
};

/**
 * tableUpdateDeviceInfoListView is used to load the table values
 * @return tableUpdateDeviceInfoListView map
 */
export function tableUpdateDeviceInfoListView() {
  return [
    {
      label: 'automatic_collection_for_device_information',
      component: 'span',
      key: 'autoDashboardCollection',
      hasLang: true,
      render: (value) =>
        value.autoDashboardCollection ? 'dashboard_setting_on' : 'dashboard_setting_off',
    },
    {
      label: 'automatic_collection_period',
      component: 'span',
      key: 'autoCollectionPeriod',
      trStyle: (value) => (value.autoDashboardCollection ? {} : { display: 'none' }),
      hasLang: true,
      render: (value) =>
        value.autoDashboardCollection
          ? filterData.PeriodInfo[`period_${value.autoCollectionPeriod}`]
          : '',
    },
  ];
}

/**
 * tableUpdateDeviceInfoList is used to load the table values editable
 * @param dynamicData
 * @param reference
 * @param dataSource
 * @return tableUpdateDeviceInfoList map
 */
export function tableUpdateDeviceInfoList(dynamicData, reference, dataSource) {
  return [
    {
      label: 'automatic_collection_for_device_information',
      component: 'radio',
      name: 'automatic-collection-for-device-information',
      key: 'autoDashboardCollection',
      defaultValue: 1,
      options: filterData.yesorno,
      filterKey: 'filterKey',
      filterValue: 'filterValue',
      canModify: true,
      buttonStyle: 'true',
      render: (value) => value.autoDashboardCollection,
    },
    {
      label: 'automatic_collection_period',
      component: 'combobox',
      id: 'automatic-collection-period-input',
      name: 'automatic-collection-period-input',
      key: 'autoCollectionPeriod',
      defaultValue: '',
      filterKey: 'value',
      filterValue: 'option',
      options: filterData.periodOptions,
      className: 'select-box01',
      canModify: true,
      ref: reference.automaticCollectionPeriodRef,
      trStyle: () => (dataSource.autoDashboardCollection ? {} : { display: 'none' }),
    },
  ];
}
