/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceFooter from '../component/DeviceFooter';
import MultipleConditionTable from '../container/MultipleConditionTable';

/** Utility Files */
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import deviceHeader from '../constants/deviceHeaderConstants';
import { deviceComboData } from '../constants/deviceConstants';

/**
 * DeviceMultipleCondition is to display list of search condition for device filteration
 * @author Aswin Palanisamy
 */
class DeviceMultipleCondition extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey } = props;
    const { multipleConditionSearch } = JSON.parse(localStorage.getItem('deviceSearch'));
    this.state = {
      screenKey,
      disable: true,
      pageLoading: true,
      headerData: deviceHeader(screenKey),
      deviceFilterData: cloneDeep(multipleConditionSearch),
      deviceFilterCondition: cloneDeep(multipleConditionSearch),
    };
    this.markerRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    this.setState({ pageLoading: false }, () => this.markerRef.current.focus());
  }

  /**
   * onClear is to remove the applied filter condition
   */
  onClear = () => {
    const { defaultFilterList } = this.props;
    this.setState(
      {
        deviceFilterCondition: {
          deviceSearch: cloneDeep(defaultFilterList),
          policySearch: [],
          dummyDeviceSearch: [],
        },
        disable: false,
      },
      () => {
        if (this.child) this.child.setGridMarker();
      },
    );
  };

  /**
   * getBackToPreviousScreen is to set filter condition and navigate to device list screen
   * @param isConfirm
   */
  getBackToPreviousScreen = isConfirm => {
    const { deviceFilterCondition, deviceFilterData } = this.state;
    const { history } = this.props;
    const getStore = JSON.parse(localStorage.getItem('deviceSearch'));
    if (isConfirm) {
      const { aDetailMark, aPolicyMark } = this.child.getGridMarker();
      localStorage.setItem(
        'deviceSearch',
        JSON.stringify({
          ...getStore,
          aDetailMark,
          aPolicyMark,
          multipleConditionSearch: deviceFilterCondition,
        }),
      );
    } else {
      localStorage.setItem(
        'deviceSearch',
        JSON.stringify({ ...getStore, multipleConditionSearch: deviceFilterData }),
      );
    }
    history.push(ROUTEENDPOINTS.DEVICELIST);
  };

  /**
   * onHandleClick is to handle click events in the footer button
   * @param buttonKey
   */
  onHandleClick = buttonKey =>
    ({
      clear: () => this.onClear(),
      cancel: () => this.getBackToPreviousScreen(),
      ok: () => this.getBackToPreviousScreen(true),
    }[buttonKey]());

  /**
   * changeFilterCondition is to set device filter condition combo data
   * @param event
   * @param index
   * @param key
   * @param addRow
   */
  changeFilterCondition = (event, index, key, addRow) => {
    const { target } = event;
    const { value } = target;
    const { deviceFilterCondition } = this.state;
    const { deviceSearch } = deviceFilterCondition;
    let { dummyDeviceSearch } = deviceFilterCondition;
    const { defaultFilterList } = this.props;
    const { conditionOne } = deviceSearch[index];
    deviceSearch[index].isCategoryChange = false;
    deviceSearch[index].conditionOne =
      deviceSearch[index].category && key === 'characterString' ? '=' : conditionOne;
    deviceSearch[index][key] = value;
    if (addRow && !deviceSearch[index + 1]) {
      deviceSearch.push(
        isEmpty(dummyDeviceSearch[index + 1])
          ? cloneDeep(defaultFilterList[0])
          : { ...dummyDeviceSearch[index + 1], conditionTwo: '' },
      );
    }
    if (addRow && !value) {
      const tempDummyDeviceSearch = cloneDeep(dummyDeviceSearch);
      tempDummyDeviceSearch.splice(0, deviceSearch.length, ...deviceSearch);
      dummyDeviceSearch = isEmpty(dummyDeviceSearch)
        ? cloneDeep(deviceSearch)
        : tempDummyDeviceSearch;
      deviceSearch.splice(index + 1);
    }
    this.setState({
      disable: false,
      deviceFilterCondition: {
        ...deviceFilterCondition,
        dummyDeviceSearch,
      },
    });
  };

  /**
   * changeCharacterString is to update characterString value
   * @param event
   * @param index
   * @param key
   */
  changeCharacterString = (event, index, key) => {
    const { deviceFilterCondition } = this.state;
    const { target } = event;
    const { deviceSearch } = deviceFilterCondition;
    deviceSearch[index].isCategoryChange = false;
    deviceSearch[index].previousCharacterString = deviceSearch[index][key];
    deviceSearch[index][key] = target.value;
    this.setState({ disable: false, deviceFilterCondition });
  };

  /**
   * changeFilterCategory is to set device filter category condition combo data
   * @param event
   * @param index
   */
  changeFilterCategory = (event, index) => {
    const { target } = event;
    const { value } = target;
    const { defaultFilterList } = this.props;
    const { deviceFilterCondition } = this.state;
    const { deviceSearch } = deviceFilterCondition;
    deviceSearch[index].isCategoryChange = true;
    deviceSearch[index].category = value;
    if (value) {
      deviceSearch[index].previousCharacterString = deviceSearch[index].characterString;
      deviceSearch[index].characterString = '';
      deviceSearch[index].field = deviceComboData.field[deviceSearch[index].category][0].value;
      deviceSearch[index].conditionOne =
        deviceSearch[index].conditionOne || deviceComboData.conditionOne[0].value;
    } else {
      deviceSearch.splice(index + 1);
      deviceSearch[index] = {...cloneDeep(defaultFilterList).shift(), characterString: deviceSearch[index].characterString};
    }
    this.setState({ disable: false, deviceFilterCondition });
  };

  /**
   * changeFilterField is to set device filter field condition combo data
   * @param event
   * @param index
   */
  changeFilterField = (event, index) => {
    const { target } = event;
    const { value } = target;
    const { deviceFilterCondition } = this.state;
    const { deviceSearch } = deviceFilterCondition;
    deviceSearch[index].isCategoryChange = false;
    deviceSearch[index].field = value;
    deviceSearch[index].conditionOne = deviceComboData.conditionOne[0].value;
    this.setState({ disable: false, deviceFilterCondition });
  };

  /**
   * onPolicySearchFilter is to set device filter checkList data
   * @param event
   * @param key
   */
  onPolicySearchFilter = (event, key) => {
    const { target } = event;
    const { deviceFilterCondition } = this.state;
    let { policySearch } = deviceFilterCondition;
    policySearch = policySearch.filter(data => data !== key);
    if (target.checked) policySearch.push(key);
    this.setState({
      disable: false,
      deviceFilterCondition: { ...deviceFilterCondition, policySearch },
    });
  };

  /**
   * render is to frame DeviceMultipleCondition layout
   */
  render() {
    const { disable, screenKey, headerData, pageLoading, deviceFilterCondition } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <MultipleConditionTable
          markerRef={this.markerRef}
          onRef={ref => (this.child = ref)}
          deviceFilterCondition={deviceFilterCondition}
          changeFilterCondition={(event, index, key, addRow) =>
            this.changeFilterCondition(event, index, key, addRow)
          }
          changeCharacterString={(event, index, key) =>
            this.changeCharacterString(event, index, key)
          }
          onPolicySearchFilter={(event, key) => this.onPolicySearchFilter(event, key)}
          changeFilterField={(event, index) => this.changeFilterField(event, index)}
          changeFilterCategory={(event, index) => this.changeFilterCategory(event, index)}
        />
        <DeviceFooter
          onHandleClick={key => this.onHandleClick(key)}
          footer={{ disable, screenKey }}
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from device redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { defaultFilterList } = state.device;
  return { defaultFilterList };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceMultipleCondition.propTypes = {
  screenKey: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(withRouter(DeviceMultipleCondition));
