import React from 'react';
import { connect } from 'react-redux';
import commonChangeAction from '../../changeReducer/commonChangeAction';

/**
 * SBTextbox is to frame textbox component
 * @author Rinchu.T, Vinoth.P
 *
 */
const SBTextbox = React.forwardRef((props, ref) => {
  const changeModified = () => {
    const { setModified, canModify } = props;
    if (canModify) {
      setModified(true);
    }
  };

  const onTextBoxChange = event => {
    const { onChange } = props;
    changeModified();
    if (onChange) onChange(event, event.target.value);
  };

  const onTextBoxKeyDown = event => {
    const { onKeyDown } = props;
    if (onKeyDown) onKeyDown(event, event.target.value);
  };

  const onDropDragEvent = event => {
    event.preventDefault();
  };

  const onFocusEvent = event => {
    const { onFocus } = props;
    if (onFocus) onFocus(event);
  };

  const onBlurEvent = event => {
    const { onBlur } = props;
    if (onBlur) onBlur(event);
  };

  const {
    placeholder,
    type,
    maxLength,
    name,
    disabled,
    readOnly,
    size,
    style,
    defaultValue,
    id,
    classes,
    value,
  } = props;

  return (
    <input
      id={id}
      placeholder={placeholder}
      maxLength={maxLength}
      type={type}
      size={size}
      name={name}
      className={classes}
      readOnly={readOnly}
      tabIndex={readOnly ? '-1' : '0'}
      disabled={disabled}
      style={disabled ? { ...style, background: '#EEE' } : style}
      ref={ref}
      {...(defaultValue ? { defaultValue } : { value })}
      onChange={event => onTextBoxChange(event)}
      onKeyDown={event => onTextBoxKeyDown(event)}
      onDrop={event => onDropDragEvent(event)}
      onFocus={event => onFocusEvent(event)}
      onBlur={event => onBlurEvent(event)}
    />
  );
});

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(
  null,
  mapDispatchToProps,
  null,
  { forwardRef: true },
)(SBTextbox);
