import {
  isNotUndefinedOrNotEmpty,
  isLengthValid,
  isOSVersion,
} from '../../../../common/CommonValidations/CommonValidations';
import UNDERSCORE from '../../../../v1/common/utils';
import { errorMessageConstants } from './MessageConstant';

export function setFocusinerrorField(dynamicData, reference) {
  if (dynamicData.securityPolicyNameError && dynamicData.securityPolicyNameError !== '') {
    reference.securityPolicyNameRef.current.focus();
  } else if (dynamicData.uploadError && dynamicData.uploadError !== '') {
    reference.uploadRef.current.focus();
  }
  reference.uploadRef.current.value = '';
}

function validateSecurityPolicyName(securityPolicyName) {
  const minLength = 1;
  const maxLength = 100;
  const result = { status: true, error: '' };

  if (isNotUndefinedOrNotEmpty(securityPolicyName ? securityPolicyName.trim() : '')) {
    if (isLengthValid(securityPolicyName, minLength, maxLength)) {
      result.status = true;
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}
export function validateAndroidOSVersion(osVersion) {
  const minLength = 1;
  const maxLength = 6;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(osVersion)) {
    if (isOSVersion(osVersion)) {
      if (isLengthValid(osVersion, minLength, maxLength)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'text_length';
      }
    } else {
      result.status = false;
      result.error = 'format';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * setValidationErrorMessage is used to set error validation message
 *
 * @param key
 * @param validationResultObject
 * @param dynamicData
 * @return tempDynamicData
 *
 */

export function setValidationErrorMessage(key, validationResultObject, dynamicData) {
  const tempDynamicData = dynamicData;
  tempDynamicData[key] = errorMessageConstants[key][validationResultObject.error];
  return tempDynamicData;
}

export function validateInputs(dataSource, fileDataSource, dynamicData) {
  let tempDynamicData = dynamicData;
  const securityPolicyNameValidation = validateSecurityPolicyName(dataSource.securityPolicyName);
  tempDynamicData = setValidationErrorMessage(
    'securityPolicyNameError',
    securityPolicyNameValidation,
    tempDynamicData,
  );
  let fileValidation = { status: true, error: false };
  if (fileDataSource) {
    fileValidation = { status: false, error: 'filepresent' };
    tempDynamicData = setValidationErrorMessage('uploadError', fileValidation, tempDynamicData);
  }
  tempDynamicData.status = securityPolicyNameValidation.status && fileValidation.status;
  return tempDynamicData;
}
export function validateFileCheck(fileDataSource, dynamicData) {
  let tempDynamicData = dynamicData;
  let fileValidation = { status: true, error: false };
  if (fileDataSource) {
    fileValidation = { status: false, error: 'filepresent' };
    tempDynamicData = setValidationErrorMessage('uploadError', fileValidation, tempDynamicData);
  }
  tempDynamicData.status = fileValidation.status;
  return tempDynamicData;
}
export function setOsVersionList(param) {
  return {
    androidOSVersion: '',
    selectOSVersion:
      param.androidOSList && param.androidOSList.length ? param.androidOSList[0] : '',
    androidOSList: param.osVersion ? [...param.osVersion] : [],
  };
}

export function setApplicationList(param) {
  return {
    androidApplication: '',
    androidApplicationCheckMethod: param.androidApplicationCheckMethod
      ? param.androidApplicationCheckMethod
      : 'WhiteList',
    selectApplication:
      param.androidApplicationList && param.androidApplicationList.length ? param.osVersion[0] : '',
    androidApplicationList: param.osVersion ? [...param.osVersion] : [],
  };
}

export function validateAppList(appValue, appList) {
  const minLength = 0;
  const maxLength = 100;
  const maxAppListLength = 250;

  const result = { status: true, error: '' };

  if (appList.length <= maxAppListLength) {
    if (isNotUndefinedOrNotEmpty(appValue ? appValue.trim() : '')) {
      const appLength = appValue.length;
      if (appLength >= minLength && appLength <= maxLength) {
        if (!appValue.includes(',')) {
          if (UNDERSCORE.contains(appList, appValue)) {
            result.status = false;
            result.error = 'samecontent';
          } else {
            result.status = true;
          }
        } else {
          result.status = false;
          result.error = 'comma';
        }
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'empty';
    }
  } else {
    result.status = false;
    result.error = 'listlength';
  }

  return result;
}

export function validateList(osValue, osList) {
  const minLength = 1;
  const maxLength = 6;
  const listLength = 20;
  const result = { status: true, error: '' };
  if (osList.length < listLength) {
    if (isNotUndefinedOrNotEmpty(osValue)) {
      const osValueLength = osValue.length;
      if (osValueLength >= minLength && osValueLength <= maxLength) {
        if (isOSVersion(osValue)) {
          if (UNDERSCORE.contains(osList, osValue)) {
            result.status = false;
            result.error = 'samecontent';
          } else {
            result.status = true;
          }
        } else {
          result.status = false;
          result.error = 'format';
        }
      } else {
        result.status = false;
        result.error = 'text_length';
      }
    } else {
      result.status = false;
      result.error = 'empty';
    }
  } else {
    result.status = false;
    result.error = 'listlength';
  }
  return result;
}

/**
 * validateFileExtention is used to validate file extension
 * @param fileName
 * @return validationResult
 */

function validateFileExtention(fileName) {
  const validationResult = { status: true, error: '' };
  const lowerCasedFileName = fileName.toLowerCase();
  if (lowerCasedFileName.endsWith('.csv')) {
    validationResult.status = true;
  } else {
    validationResult.status = false;
    validationResult.error = 'fileExtension';
  }
  return validationResult;
}

/**
 * validateFile is used to validate file and all other params
 * @param key
 * @param file
 * @return validationResult
 */

export function validateFile(file) {
  const validationResult = { status: true, error: '' };
  if (file) {
    const fileTypeValidation = validateFileExtention(file.name);
    if (!fileTypeValidation.status) {
      return fileTypeValidation;
    }
  } else {
    validationResult.status = false;
    validationResult.error = 'nofile';
  }
  return validationResult;
}
