import React, { Component } from 'react';
import { Layout } from 'antd';
import { connect } from 'react-redux';
import { isNotUndefinedOrNotEmpty } from '../../CommonValidations/CommonValidations';
import { get } from '../../../actions/commonActions';
import APIEndPoints from '../../../constants/APIEndPoints';
import LoadingSpinner from '../loadingSpinner/LoadingSpinner';
import HeaderLayout from '../headerlayout/HeaderLayout';
import FooterLayout from '../footerlayout/FooterLayout';
import checkLanguage from '../../../language/checkLanguage';
import getLanguageData from '../../../language/languageUtils';
import languageChangerAction from '../../../language/languageActions';
import descriptionConverter from '../../utils/descriptionConverter';

const { Header, Footer } = Layout;

const header = {
  width: '100%',
  minWidth: 1000,
  background: '#F2F2F2',
  borderBottom: '2px solid #B2B2B2',
  display: 'block',
  padding: 0,
  height: 'auto',
  lineHeight: 0,
};

class SBError extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    const { localeData, changeLanguage } = this.props;
    const getlanguageMap = async () => {
      const userDetailResponse = await get(APIEndPoints.USERDETAIL);
      const userDetailData = await userDetailResponse.json();
      if (Object.keys(localeData) < 1) {
        const languageMap = await getLanguageData(userDetailData.userVo.languageCode);
        changeLanguage(
          userDetailData.userVo.languageCode,
          userDetailData.userVo.timeZoneCode,
          languageMap,
        );
        this.changeLoadingState();
      } else {
        this.changeLoadingState();
      }
      document.title = this.languageLabelCheck('application_title');
    };
    getlanguageMap();
  }

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  changeLoadingState = () => {
    this.setState({
      loading: false,
    });
  };

  render() {
    const { userVo, messageContent } = this.props;
    const noLogoutdisplay = isNotUndefinedOrNotEmpty(userVo);
    const { loading } = this.state;
    return (
      <LoadingSpinner loading={loading}>
        <Layout>
          <Header style={header}>
            <HeaderLayout userVo={userVo} noLogoutdisplay={!noLogoutdisplay} />
          </Header>
          <Layout>
            <center>
              <div id="contents" style={{ marginTop: 32 }}>
                {descriptionConverter(
                  this.languageLabelCheck(
                    messageContent || 'failed_to_display_internal_server_error',
                  ),
                )}
              </div>
            </center>
          </Layout>
          <Footer id="footer" style={{ paddingLeft: 15 }}>
            <FooterLayout />
          </Footer>
        </Layout>
      </LoadingSpinner>
    );
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { data: localeData } = state.language;
  return {
    userVo,
    localeData,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SBError);
