/**
 * ChangePasswordContainer is used to get a change password for an account
 * @author: Loganathan.P,V.Vijayaragavan
 */

import React, { Component, createRef } from 'react';
import cloneDeep from 'lodash/cloneDeep';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { Layout } from 'antd';
import { connect } from 'react-redux';
import { post } from '../../actions/commonActions';
import APIEndPoints from '../../constants/APIEndPoints';
import LoadingSpinner from '../../common/component/loadingSpinner/LoadingSpinner';
import HeaderLayout from '../../common/component/headerlayout/HeaderLayout';
import FooterLayout from '../../common/component/footerlayout/FooterLayout';
import RouteEndPoints from '../../constants/RouteEndPoints';
import {
  AuthenticationConstants,
  forgetPasswordFloatRightList,
  changePasswordErrorMessageConstants,
  changePasswordTableList,
} from '../AuthenticationConstants';
import languageChangerAction from '../../language/languageActions';
import defaultLocale from '../utils/defaultLocale';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import AuthenticationFooterComponent from '../component/AuthenticationFooterComponent';
import checkLanguage from '../../language/checkLanguage';

const { Header, Footer } = Layout;

class ChangePasswordContainer extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor() {
    super();
    this.state = {
      loading: true,
      dynamicData: {
        errorMessage: '',
        currentPasswordErrorMessage: '',
        newPasswordErrorMessage: '',
        confirmPasswordErrorMessage: '',
        currentPasswordRef: createRef(),
        newPasswordRef: createRef(),
        confirmPasswordRef: createRef(),
        emailRef: createRef(),
      },
      param: { emailId: '' },
    };
  }

  /**
   * componentDidMount invokes changePasswordStatus method
   */
  componentDidMount() {
    this.changePasswordStatus(false);
  }

  /**
   * onFooterButtonClick handles click event
   * @param key
   */
  onFooterButtonClick = key => {
    if (key === 'ok') {
      this.changePasswordStatus(true);
    } else {
      this.onCancelButtonClick();
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param value
   */
  onTableFrameEvent = (key, value) => {
    const { param } = this.state;
    param[key] = value;
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */

  onKeyDown = event => {
    if (event.keyCode === 13) {
      event.preventDefault();
      this.changePasswordStatus(true);
    }
  };

  /**
   * onCancelButtonClick is used to handle operation on clicking cancel button
   */
  onCancelButtonClick = () => {
    const { history } = this.props;
    const { param } = this.state;
    if (param.authorityId === 4) {
      history.push(RouteEndPoints.ISOC);
    } else {
      history.push(RouteEndPoints.LOGIN);
    }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * changePasswordStatus is used to show the change password status
   * @param showError
   */
  changePasswordStatus = async showError => {
    const { history, changeLanguage, defaultLang, defaultimeZoneCode } = this.props;
    const { param } = this.state;
    this.setState({
      loading: true,
    });
    const result = await post(APIEndPoints.CHANGEPASSWORD, param);
    const data = await result.json();
    if (data.status) {
      this.setCacheData();
      changeLanguage(defaultLang, defaultimeZoneCode, {});
      history.push(RouteEndPoints.DASHBOARD);
    } else if (data.resetPassword) {
      if (showError) {
        this.setValidationErrorMessage(data.validationError);
      }
      this.initialiseParam(data.userVo, showError);
      this.setDefaultLocale();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * setDefaultLocale performs api operation to load default locale
   */
  setDefaultLocale = async () => {
    const languageMap = await defaultLocale(this);
    if (Object.keys(languageMap).length >= 0) {
      this.setState({ loading: false });
      this.setFocus();
      this.clearPasswordsFields();
      document.title = this.languageLabelCheck('application_title');
    }
  };

  /**
   * setFocus is used to set focus in the first input field
   */
  setFocus = () => {
    const { dynamicData } = this.state;
    if (dynamicData.currentPasswordRef.current) {
      dynamicData.currentPasswordRef.current.focus();
    } else if (dynamicData.newPasswordRef.current) {
      dynamicData.newPasswordRef.current.focus();
    }
  };

  /**
   * setCacheData is to set localStorage data
   */
  setCacheData = () => {
    const { deviceFilterCondition } = this.props;
    const getStore =
      localStorage.getItem('deviceSearch') && JSON.parse(localStorage.getItem('deviceSearch'));
    if (getStore) {
      localStorage.setItem('deviceSearch', JSON.stringify({ ...getStore }));
    } else {
      localStorage.setItem(
        'deviceSearch',
        JSON.stringify({
          aDetailMark: '+',
          aPolicyMark: '+',
          groupSearch: [],
          departmentSearch: ['', '', '', ''],
          deviceSearch: { searchKeyword: '', checkBoxKeys: [] },
          multipleConditionSearch: cloneDeep(deviceFilterCondition),
        }),
      );
    }
  };

  /**
   * getValidationErrorMessage is used to set error validation messages
   * @param key
   * @param errorType
   */
  getValidationErrorMessage = (key, errorType) => {
    return changePasswordErrorMessageConstants[key][errorType];
  };

  /**
   * setValidationErrorMessage is set error message based on validation
   * @param error
   */
  setValidationErrorMessage = error => {
    const { dynamicData } = this.state;
    dynamicData.currentPasswordErrorMessage = this.getValidationErrorMessage(
      'currentPassword',
      error.currentPassword,
    );
    dynamicData.newPasswordErrorMessage = this.getValidationErrorMessage(
      'newPassword',
      error.newPassword,
    );
    dynamicData.confirmPasswordErrorMessage = this.getValidationErrorMessage(
      'confirmPassword',
      error.confirmPassword,
    );
    dynamicData.emailError = this.getValidationErrorMessage('emailError', error.emailError);
    dynamicData.errorMessage = error.commonError;
    this.setState({ dynamicData });
  };

  /**
   * clearPasswordsFields is used to clear password fields
   */
  clearPasswordsFields = () => {
    const { param, dynamicData } = this.state;
    if (dynamicData.currentPasswordRef.current) dynamicData.currentPasswordRef.current.value = '';
    if (dynamicData.newPasswordRef.current) dynamicData.newPasswordRef.current.value = '';
    if (dynamicData.confirmPasswordRef.current) dynamicData.confirmPasswordRef.current.value = '';
    if (dynamicData.emailRef.current) dynamicData.emailRef.current.value = param.emailId;
    this.setFocus();
  };

  /**
   * initialiseParam is used to load the inital values
   * @param userVo
   * @param updateParamEmail
   */
  initialiseParam = (userVo, updateParamEmail) => {
    const { param } = this.state;
    const tempParam = {};
    tempParam.dmCode = userVo.dmCode;
    tempParam.loginId = userVo.loginId;
    tempParam.authorityId = userVo.authorityId;
    tempParam.emailId = updateParamEmail ? param.emailId : userVo.emailID;
    this.setState({
      param: tempParam,
    });
  };

  /**
   * render is used to render the component
   */
  render() {
    const { loading, dynamicData, param } = this.state;
    return (
      <LoadingSpinner loading={loading}>
        <Layout>
          <Header style={AuthenticationConstants.forgotPasswordStyles.header}>
            <HeaderLayout noLogoutdisplay />
          </Header>
          <Layout>
            <div style={{ margin: '20px auto', width: 800 }}>
              <SBContentHeader {...AuthenticationConstants.changePasswordHeaderInformation} />
              <CustomTableFrame
                tableList={changePasswordTableList(dynamicData).slice(
                  param.authorityId === 1,
                  param.authorityId === 6 ? 4 : 3,
                )}
                dataSource={{}}
                defaultValue={{}}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, value)}
                onKeyDown={(event, value) => this.onKeyDown(event, value)}
              />
              <AuthenticationFooterComponent
                buttonsList={forgetPasswordFloatRightList().slice(0, 2)}
                onHandleClick={key => this.onFooterButtonClick(key)}
              />
              <br />
              <span className="red bold">{this.languageLabelCheck(dynamicData.errorMessage)}</span>
            </div>
          </Layout>
          <Footer id="footer" style={AuthenticationConstants.forgotPasswordStyles.footer}>
            <FooterLayout />
          </Footer>
        </Layout>
      </LoadingSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = dispatch => {
  return {
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
ChangePasswordContainer.propTypes = {
  changeLanguage: PropTypes.func.isRequired,
  defaultLang: PropTypes.string.isRequired,
  defaultimeZoneCode: PropTypes.string.isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  deviceFilterCondition: PropTypes.shape({
    deviceSearch: PropTypes.arrayOf(PropTypes.object),
    policySearch: PropTypes.arrayOf(PropTypes.string),
    dummyDeviceSearch: PropTypes.arrayOf(PropTypes.object),
  }).isRequired,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ChangePasswordContainer.defaultProps = {
  isNotMultiLang: false,
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    userVo,
    localeData: state.language.data,
    defaultLang: state.language.default,
    deviceFilterCondition: state.device.deviceFilterCondition,
    defaultimeZoneCode: state.language.timezoneCode,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(ChangePasswordContainer));
