import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

/**
 * defaultGroupSettingHeader is to set screen caption for group setting
 * @param hasAuthority
 * @return screenCaption
 */
export const defaultGroupSettingHeader = hasAuthority => ({
  title: 'default_group_information',
  description: [
    hasAuthority ? 'default_group_information_description' : 'default_group_restrict_description',
  ],
});

/**
 * serviceMapper is to map routeEndPoints to the specified service
 * @param key
 * @return serviceEndpoints
 */
export const serviceMapper = key =>
  ({
    add: ROUTEENDPOINTS.GROUPMANAGEMENT,
    changeDefaultGroup: ROUTEENDPOINTS.DEFAULTGROUPSETTINGCHANGE,
    cancel: ROUTEENDPOINTS.CONTRACTINFO,
  }[key]);

/**
 * buttonList is to frame group setting buttons
 * @param disabled
 * @param reference
 * @param screenKey
 * @return buttonList
 */
export const buttonList = ({ disabled, reference, authorityRoleType }, screenKey) =>
  ({
    defaultGroupInfo: [
      {
        disabled,
        color: 'green',
        title: 'change',
        canModify: false,
        authorityRoleType,
        id: 'change_button',
        noMarginRight: false,
        key: 'changeDefaultGroup',
      },
      {
        color: '',
        key: 'cancel',
        title: 'cancel',
        canModify: true,
        noMarginRight: true,
        id: 'cancel_button',
        ref: reference && reference,
      },
    ],
  }[screenKey]);

/**
 * getColumns is to set group setting grid column
 * @param renderComponent
 * @return columnList
 */
export const getColumns = renderComponent => [
  {
    sorter: false,
    title: 'group_name',
    rowKey: 'groupName',
    dataIndex: 'groupName',
    render: record => renderComponent(record),
  },
  {
    sorter: false,
    title: 'authority',
    rowKey: 'authority',
    dataIndex: 'authority',
  },
  {
    sorter: false,
    title: 'device_count',
    rowKey: 'groupDeviceCount',
    dataIndex: 'groupDeviceCount',
  },
  {
    sorter: false,
    title: 'date_created',
    rowKey: 'groupCreatedDate',
    dataIndex: 'groupCreatedDate',
  },
  {
    sorter: false,
    title: 'date_updated',
    rowKey: 'groupUpdatedDate',
    dataIndex: 'groupUpdatedDate',
  },
];
