/**
 * MultipleAdminFinish is used to load updated data
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import UNDERSCORE from '../../v1/common/utils';
import MultipleAdminCountHeader from './MultipleAdminCountHeader';
import { get } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import { tableView } from './MultipleAdminSettingConstants';
import headerInformation from './MultipleAdminHeaderConstants';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import MultipleAdminCRUDFooter from './MultipleAdminCRUDFooter';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import getUserEntity from '../../actions/UserActions';
import { getTenantEntity } from '../../actions/TenantActions';
import checkLanguage from '../../language/checkLanguage';

class MultipleAdminFinish extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history, tenantVo } = props;
    let param = {};
    if (location.state) {
      param = location.state;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('updateSuccessfully'),
      fieldDataList: tableView(tenantVo),
      dataSource: UNDERSCORE.isEmpty(param) ? {} : param.userVo,
      usersLength: param.usersLength,
      footer: { okDisable: true, primaryVisibile: false },
    };
  }

  /**
   * componentDidMount performs api operation to load user length
   */

  componentDidMount() {
    const { getUserEntity: getLoggedUser, getTenantEntity: getTenantDetail } = this.props;
    const generateComponent = async () => {
      const userDetailResponse = await get(APIENDPOINTS.USERDETAIL);
      const userDetailData = await userDetailResponse.json();
      getLoggedUser(userDetailData.userVo);
      getTenantDetail(userDetailData.tenantVo);
      document.title = this.languageLabelCheck('application_title');
      this.setState({ fieldDataList: tableView(userDetailData.tenantVo) });
    };
    generateComponent();
  }

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING);
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the component
   */

  render() {
    const { headerData, usersLength, fieldDataList, dataSource, footer } = this.state;
    const { tenantVo } = this.props;
    return (
      <>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <MultipleAdminCountHeader usersLength={usersLength} maxUsercount={tenantVo.userCount} />
          <div>
            <CustomTableFrame tableList={fieldDataList} dataSource={dataSource} defaultValue={{}} />
          </div>
          <div>
            <MultipleAdminCRUDFooter footer={footer} onHandleClick={() => this.onHandleClick()} />
          </div>
        </div>
      </>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = (state) => {
  const { tenantVo } = state.tenant;
  const { userVo } = state.user;
  const { data: localeData } = state.language;
  return {
    userVo,
    tenantVo,
    localeData,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = (dispatch) => {
  return {
    getUserEntity: (data) => {
      dispatch(getUserEntity(data));
    },
    getTenantEntity: (data) => {
      dispatch(getTenantEntity(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */

MultipleAdminFinish.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: commonPropTypes().userVo.isRequired,
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminFinish.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(MultipleAdminFinish));
