/**
 * This function returns the table list for RestrictDeviceRegistration Screen
 *
 * @param  dynamicData
 * @author Sivaprakash Shankar
 */

const restrictionDetails = (dynamicData, reference) => [
  {
    component: 'radio',
    label: 'restrict_device_registration',
    options: dynamicData.comboOptions,
    key: 'restrict_device_registration',
    value: dynamicData.restrictionSetting.restriction ? 'on' : 'off',
    name: 'restrictDeviceRegistrationRadioButton',
    id: 'restrictDeviceRegistrationRadioButton',
    filterValue: 'value',
    filterKey: 'key',
    headKey: 'restriction_setting',
    render: () => (dynamicData.restrictionSetting.restriction ? 'on' : 'off'),
  },
  {
    type: 'button',
    className: 'ml10',
    title: 'download_template',
    component: 'fileuploadwithspaninfoandbutton',
    label: 'managed_device_list',
    accept: '.csv',
    name: 'individual_csvfile',
    id: 'individual_csvfile',
    key: 'individual_csvfile',
    ref: reference,
    trStyle: () => ({
      display: dynamicData.restrictionSetting.restriction ? '' : 'none',
    }),
    headKey: 'individual_csvfile',
    buttonKey: 'download',
    renderSpan: () => dynamicData.buildErrorMessage(dynamicData.error),
  },
  {
    component: dynamicData.restrictionSetting.fileName === '' ? 'span' : 'spanWithButton',
    label: 'registered_list',
    title: 'imei_download',
    btntype: 'button',
    name: 'output_file',
    id: 'output_file',
    key: dynamicData.restrictionSetting.fileName === '' ? 'not_set' : 'output_file',
    btnkey: 'output_file',
    trStyle: () => ({
      display: dynamicData.restrictionSetting.restriction ? '' : 'none',
    }),
    hasLang: dynamicData.restrictionSetting.fileName === '',
    disabled: dynamicData.hasData || dynamicData.authority,
  },
];

export default restrictionDetails;
