/**
 * RestrictDeviceRegistration is to set the restriction for device registrationl
 *
 * @author: Sivaprakash Shankar
 */

import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import fileDownload from 'js-file-download';
import encoding from 'encoding';
import { connect } from 'react-redux';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../common/component/sbbutton/SBButton';
import APIEndPoints from '../../../constants/APIEndPoints';
import { get, post, postWithFormData } from '../../../actions/commonActions';
import checkLanguage from '../../../language/checkLanguage';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import restrictionDetails from '../constants/RestrictDeviceRegistrationConstants';
import restrictionDetailsConfirm from '../constants/RestrictDeviceRegistrationConfirmationConstants';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import descriptionConverter from '../../../common/utils/descriptionConverter';
import { Role } from '../../../constants/Role';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import RouteEndPoints from '../../../constants/RouteEndPoints';

class RestrictDeviceRegistration extends Component {
  constructor(props) {
    super(props);
    const { userVo } = props;
    const { authorityId, groupList } = userVo;
    let isAuthority = Role.restrictDeviceAuthority.includes(authorityId);
    if (authorityId === 7) {
      isAuthority = groupList.length !== 0;
    }
    this.state = {
      open: false,
      uploadedFile: null,
      uploadedFileName: null,
      uploadedFileData: null,
      pageLoading: true,
      count: 0,
      detailDisplay: '',
      confirmDisplay: 'none',
      description: ['restrict_device_registration_description'],
      dynamicData: {
        restrictionSetting: {
          restriction: false,
          fileData: [],
          fileName: '',
          file: null,
        },
        error: '',
        comboOptions: [
          { value: 'imei_on', key: 'on', ref: '' },
          { value: 'imei_off', key: 'off', ref: createRef },
        ],
        hasData: true,
        authority: isAuthority,
        buildErrorMessage: this.buildErrorMessage,
      },
    };
    this.initialFocus = createRef();
  }

  /**
   * This is to load data to the grid
   */
  componentDidMount() {
    this.fetchDetail();
  }

  onOffRadioButtonOptions = () => {
    return [
      { value: 'imei_on', key: 'on' },
      { value: 'imei_off', key: 'off' },
    ];
  };

  /**
   * Called when the restriction is turned ON or OFF
   *
   * @param  event
   * @param  selectedOption
   */
  onRadioButtonChange = (event, selectedOption) => {
    const { dynamicData } = this.state;
    if (selectedOption === 'on') {
      this.setState({
        dynamicData: {
          ...dynamicData,
          restrictionSetting: { ...dynamicData.restrictionSetting, restriction: true },
        },
      });
    } else {
      this.setState({
        dynamicData: {
          ...dynamicData,
          restrictionSetting: { ...dynamicData.restrictionSetting, restriction: false },
        },
      });
    }
  };

  /**
   * Called when Ok button is clicked
   */
  onOKButtonClick = () => {
    const { dynamicData } = this.state;
    if (dynamicData.restrictionSetting.restriction) {
      this.validateCheck();
    } else {
      this.setState({
        detailDisplay: 'none',
        confirmDisplay: '',
        description: ['restrict_device_confirm_description'],
      });
    }
  };

  /**
   * Called when Cancel button is clicked
   */
  onCancelButtonClick = () => {
    const { isModified, history } = this.props;
    if (isModified) {
      this.setState({ open: true });
    } else {
      history.push(RouteEndPoints.LOGSEARCH);
    }
  };

  /**
   * Called when Ok button in Confirmation page is clicked
   */
  onOKConfirmButtonClick = () => {
    this.saveData();
    this.setState({
      detailDisplay: 'none',
      confirmDisplay: 'none',
      description: ['imei_completed'],
    });
  };

  /**
   * Called when Cancel button in Confirmation page is clicked
   */
  onCancelConfirmButtonClick = () => {
    this.setState({
      detailDisplay: 'block',
      confirmDisplay: 'none',
      description: ['restrict_device_registration_description'],
      uploadedFile: null,
    });
  };

  /**
   * Called when Back button in Completion page is clicked
   */
  onBackButtonClick = () => {
    this.setState({
      detailDisplay: '',
      description: ['restrict_device_registration_description'],
      uploadedFile: null,
    });
    this.fetchDetail();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * To display and circular spinner
   */
  toggleDisplay = () => {
    const { pageLoading } = this.state;
    if (!pageLoading) {
      this.setState({ pageLoading: true });
    } else {
      this.setState({ pageLoading: false });
    }
  };

  /**
   * This function is to download the output file
   *
   */
  outputFileDownload = async () => {
    const { currentLanguage } = this.props;
    const { dynamicData } = this.state;
    const { fileName } = dynamicData.restrictionSetting;
    const response = await get(APIEndPoints.DEVICERESTRICTDOWNLOAD);
    const result = await response.json();
    fileDownload(
      encoding.convert(result.message, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
      fileName,
    );
  };

  /**
   * afterOnOkClick is to handle on ok click in error message
   */
  afterOnOkClick = () => {
    const { setModified } = this.props;
    this.setState({ open: false, uploadedFile: null });
    setModified(false);
    this.initialFocus.current.value = '';
    this.fetchDetail();
  };

  /**
   * onCancelHandler is to handle on cancel in error message
   */
  onCancelHandler = () => {
    this.setState({ open: false });
  };

  /**
   * this function is used to download a template csv file
   */
  downloadTemplate = () => {
    const { currentLanguage } = this.props;
    const header = 'IMEI,serial\n';
    fileDownload(
      encoding.convert(header, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
      'imei_serial.csv',
    );
  };

  /**
   * This is used to fetch data from api
   * and to set the values
   */
  fetchDetail = async () => {
    const { dynamicData } = this.state;
    const { setModified } = this.props;
    const response = await get(APIEndPoints.DEVICERESTRICTDETAIL);
    const result = await response.json();
    const initFocus = createRef();
    setModified(false);
    if (result.fileName !== 'doesnotexist') {
      this.setState({
        dynamicData: {
          ...dynamicData,
          restrictionSetting: result,
          hasData: result.fileName === null || '',
          error: '',
          comboOptions: [
            { value: 'imei_on', key: 'on', ref: result.restriction ? initFocus : '' },
            { value: 'imei_off', key: 'off', ref: result.restriction ? '' : initFocus },
          ],
        },
        pageLoading: false,
      });
    } else {
      this.setState({
        dynamicData: { ...dynamicData, hasData: true, error: '' },
        pageLoading: false,
      });
    }
  };

  /**
   * This validates the CSV file you have uploaded
   */
  validateCheck = async () => {
    const { uploadedFile, dynamicData } = this.state;
    this.setState({ pageLoading: true });
    if (uploadedFile === null) {
      this.setState({ dynamicData: { ...dynamicData, error: 'imei_no_file_upload' } });
      this.setState({ pageLoading: false });
    } else {
      const formData = new FormData();
      formData.append('file', uploadedFile);
      const response = await postWithFormData(APIEndPoints.DEVICERESTRICTVALIDATE, formData);
      const result = await response.json();
      if (result.status) {
        this.setState({
          uploadedFileData: result.fileData,
          count: result.count,
          dynamicData: { ...dynamicData, error: '' },
        });
        this.setState({
          pageLoading: false,
          detailDisplay: 'none',
          confirmDisplay: '',
          description: ['restrict_device_confirm_description'],
        });
      } else {
        this.setState({
          dynamicData: { ...dynamicData, error: result.message },
          uploadedFile: null,
        });
        this.setState({ pageLoading: false });
      }
    }
  };

  /**
   * This function is called when the data needs to be saved
   */
  saveData = async () => {
    let response;
    let result;
    const { setModified } = this.props;
    this.setState({ pageLoading: true });
    const { dynamicData, uploadedFileData, uploadedFileName, uploadedFile } = this.state;
    const { restrictionSetting } = dynamicData;
    const { restriction } = restrictionSetting;
    let { fileData, fileName, file } = restrictionSetting;
    if (uploadedFile !== null && restriction) {
      file = file === uploadedFile ? file : uploadedFile;
      fileData = fileData === uploadedFileData ? fileData : uploadedFileData;
      fileName = fileName === uploadedFileName ? fileName : uploadedFileName;
    }
    const param = { restriction, fileData, fileName };
    if (restriction) {
      const formData = new FormData();
      formData.append('file', file);
      response = await postWithFormData(APIEndPoints.DEVICERESTRICTUPLOAD, formData);
      response = await post(APIEndPoints.DEVICERESTRICTSAVE, param);
      result = await response.json();
    } else {
      response = await post(APIEndPoints.DEVICERESTRICTSAVE, param);
      result = await response.json();
    }
    this.setState({
      dynamicData: {
        ...dynamicData,
        restrictionSetting: result,
        hasData: result.fileName === null || '',
      },
    });
    setModified(false);
    this.setState({ pageLoading: false });
  };

  /**
   * This function is used to perform operation that are available in CustomTableFrame
   *
   * @param  key
   * @param  event
   * @param  value
   * @param  headkey
   */
  onTableFrameEvent = (key, event, value, headkey) => {
    const { setModified } = this.props;
    const { dynamicData } = this.state;
    if (key === 'restrict_device_registration') {
      this.onRadioButtonChange(event, value);
      if (dynamicData.authority === false) setModified(true);
    } else if (key === 'output_file') {
      this.outputFileDownload();
    } else if (headkey === 'individual_csvfile') {
      this.setState({
        uploadedFile: event.target.files[0],
        uploadedFileName: event.target.files[0].name,
      });
      if (dynamicData.authority === false) setModified(true);
    } else if (key === 'download') {
      this.downloadTemplate();
    }
  };

  /**
   * downloadErrorText is to download csv error document
   * @param errorData
   */
  downloadErrorText = (errorData) => {
    const { currentLanguage } = this.props;
    fileDownload(
      encoding.convert(errorData, currentLanguage === 'ja' ? 'UTF-8' : 'UTF-8'),
      `error.txt`,
    );
  };

  /**
   *
   * @param  errorMessage
   * To build and return error text for csv validation
   */
  buildErrorMessage = (errorMessage) => {
    if (errorMessage.includes('Line') || errorMessage.includes('行')) {
      return (
        <div>
          <span className="red bold">{this.languageLabelCheck('csv_error')}</span>
          <span>
            <a
              href
              tabIndex="0"
              style={{ cursor: 'pointer', textDecoration: 'underline', fontWeight: 'bold' }}
              onClick={() => this.downloadErrorText(errorMessage)}
            >
              {this.languageLabelCheck('download_wifi_csv_error')}
            </a>
          </span>
        </div>
      );
    }
    return (
      <span id="errormsg" className="red bold">
        {this.languageLabelCheck(errorMessage)}
      </span>
    );
  };

  render() {
    const { screenTitleData, isModified } = this.props;
    const {
      dynamicData,
      pageLoading,
      count,
      detailDisplay,
      confirmDisplay,
      description,
      uploadedFileName,
      open,
    } = this.state;
    const dataSource = {
      not_set: 'not_set',
      uploadedFile: uploadedFileName,
      output_file: dynamicData.restrictionSetting.fileName,
      selected: count,
      restriction: dynamicData.restrictionSetting.restriction ? 'imei_on' : 'imei_off',
    };
    return (
      <div id="contents" className="clearfix">
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <div style={{ width: '100%', 'padding-bottom': '150px' }}>
            <div id="main">
              <div className="container">
                <div style={{ height: '91px' }}>
                  <SBContentHeader title={screenTitleData.title} description={description} />
                  <p style={{ display: detailDisplay }} className="mb10">
                    <span>
                      {descriptionConverter(this.languageLabelCheck('device_no_imei_select'))}
                    </span>
                    {/* <SBInfoContent title="device_no_imei_select" /> */}
                  </p>
                  <div className="contents-main">
                    <div style={{ display: detailDisplay }}>
                      <CustomTableFrame
                        dataSource={dataSource}
                        tableList={restrictionDetails(dynamicData, this.initialFocus)}
                        onTableFrameEvent={(key, event, value, headkey) =>
                          this.onTableFrameEvent(key, event, value, headkey)
                        }
                      />
                    </div>
                    <div style={{ display: confirmDisplay }}>
                      <CustomTableFrame
                        dataSource={dataSource}
                        tableList={restrictionDetailsConfirm(dynamicData)}
                      />
                    </div>
                    <div className="clearfix">
                      <div style={{ display: detailDisplay }} className="floatright">
                        <SBButton
                          type="submit"
                          name="okbutton"
                          color="green"
                          title="ok"
                          disabled={!isModified || dynamicData.authority}
                          onClick={() => {
                            this.onOKButtonClick();
                          }}
                        />
                        <SBButton
                          type="button"
                          name="cancelbutton"
                          title="cancel"
                          noMarginRight
                          onClick={() => {
                            this.onCancelButtonClick();
                          }}
                        />
                      </div>
                      <div className="clearfix">
                        <div style={{ display: confirmDisplay }} className="floatright">
                          <SBButton
                            type="submit"
                            name="okbutton"
                            color="green"
                            title="ok"
                            onClick={() => {
                              this.onOKConfirmButtonClick();
                            }}
                          />
                          <SBButton
                            type="button"
                            name="cancelbutton"
                            title="cancel"
                            noMarginRight
                            onClick={() => {
                              this.onCancelConfirmButtonClick();
                            }}
                          />
                        </div>
                      </div>
                      <div className="clearfix">
                        <div
                          style={{ display: confirmDisplay === detailDisplay ? 'block' : 'none' }}
                          className="floatright"
                        >
                          <SBButton
                            type="submit"
                            name="backButton"
                            title="back"
                            onClick={() => {
                              this.onBackButtonClick();
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <SBDialog
                open={open}
                header="cancel_confirmation"
                type="confirmation"
                content="cancel_confirmation_message"
                onOk={() => this.afterOnOkClick()}
                onCancel={() => this.onCancelHandler()}
                width="400px"
              />
            </div>
          </div>
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStatetoProps = (state) => {
  const { userVo } = state.user;
  const { data: localeData, current: currentLanguage } = state.language;
  return {
    userVo,
    localeData,
    currentLanguage,
    isModified: state.screenModified.isModified,
  };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
  };
};
export default connect(mapStatetoProps, mapDispatchToProps)(withRouter(RestrictDeviceRegistration));
