/** Base Files */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import UNDERSCORE from '../../v1/common/utils';

/** Utility Files */
import checkLanguage from '../../language/checkLanguage';

import { Role } from '../../constants/Role';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import { serviceMapper, getDefaultPolicyData } from '../common/GroupSettingCommonFunctions';
import SBTable from '../../common/component/sbtable/SBTable';
import SBButton from '../../common/component/sbbutton/SBButton';

import { groupManagementHeader } from '../constants/groupSettingHeaderConstants';
import { kioskTableList } from '../constants/groupSettingTableConstants';
import KioskReference from '../constants/KioskReferenceConstants';
import {
  validateFile,
  validateOrderNumber,
  validateAppStatus,
  validateURL,
  validateTitle,
  validateIconValue,
} from '../constants/KioskValidations';
import { setValidationErrorMessage } from '../constants/KioskErrorConstants';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import {
  getPolicyMapper,
  makeGroupInformation,
  getKioskAppSelect,
  getKioskBgApps,
  getKioskMultiApps,
  getHomeSingleApp,
} from '../../actions/GroupAction';

import GroupManagementFooter from '../component/GroupManagementFooter';

const fileDownload = require('js-file-download');

/**
 * KioskCustomization is to set the device to work in KIOSK mode
 * @author Sivakotireddy P
 */
class KioskCustomization extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, location, mainPolicyMapper, defaultPolicyData } = props;
    const policyData = cloneDeep(
      !isEmpty(mainPolicyMapper)
        ? mainPolicyMapper
        : getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
    );
    const { dataSource, initialDataSource } = location.state;
    const { homeBgSelect, homeSingleSelect, homeApplicationsShortcut } = dataSource;
    dataSource.homeApplicationsShortcut = cloneDeep(homeApplicationsShortcut);
    dataSource.homeBgSelect = cloneDeep(homeBgSelect);
    dataSource.homeSingleSelect = cloneDeep(homeSingleSelect);
    const { kioskCustomLauncherEnabled } = policyData;
    this.state = {
      pageLoading: false,
      openErrorDialog: false,
      fileSource: null,
      headerData: groupManagementHeader(screenKey),
      mode: location.state.mode,
      footer: {
        disable: this.getDisableStatus(dataSource, kioskCustomLauncherEnabled),
        screenKey: 'kioskSettings',
        authorityRoleType: Role.group1,
      },
      mainPolicyMapper: policyData,
      initialDataSource: initialDataSource || { ...dataSource },
      dataSource,
      reference: KioskReference(),
      dynamicData: {},
      defaultSelectedKeys: [0],
      selectedRowValue:
        homeApplicationsShortcut && homeApplicationsShortcut.length
          ? homeApplicationsShortcut[0]
          : {},
    };
  }

  /**
   * getDisableStatus is to set disable status
   *
   * @param
   *
   * @return boolean
   */

  getDisableStatus = (dataSource, kioskCustomLauncherEnabled) => {
    const { kioskMultiApps, kioskAppSelect } = dataSource;
    if (kioskCustomLauncherEnabled && kioskMultiApps.length === 0) return true;
    if (dataSource.appSetting) return false;
    return !kioskCustomLauncherEnabled && kioskAppSelect.length === 0;
  };

  /**
   * languageLabelCheck is to set language label
   * @param label
   * @return languageLabel
   */

  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * handleRadioChange is used to handle radio change event
   * @param value
   */

  handleRadioChange = (value, keys) => {
    let { selectedRowValue } = this.state;
    selectedRowValue = value;
    const defaultSelectedKeys = [];
    defaultSelectedKeys.push(keys);
    this.setState({ selectedRowValue, defaultSelectedKeys });
  };

  /**
   * getColumns is load the column information
   * @return columndetails
   *
   */

  getColumns = () => {
    return [
      {
        title: '',
        dataIndex: 'render-radio',
        styles: { minWidth: 20, width: 20 },
        textAlign: 'center',
      },
      {
        title: 'order',
        dataIndex: 'orderNo',
        rowKey: 'orderNo',
        textAlign: 'center',
        sorter: true,
      },
      {
        title: 'type',
        dataIndex: 'type',
        rowKey: 'type',
        textAlign: 'center',
        sorter: false,
      },
      {
        title: 'packagename_url',
        dataIndex: 'packageNameOrUrl',
        rowKey: 'packageNameOrUrl',
        textAlign: 'center',
        sorter: false,
      },
      {
        title: 'title',
        dataIndex: 'title',
        rowKey: 'title',
        textAlign: 'center',
        sorter: false,
      },
    ];
  };

  /**
   * onKeyDown performs key press operation on kiosk customize screen.
   * @param event
   */
  onKeyDown = (event) => {
    if (event.keyCode === 13) this.onHandleClick(event.target.name);
  };

  setFocustoURLError = (orderStatus, urlStatus, urlTitleStatus) => {
    const { reference } = this.state;
    if (!orderStatus.status && reference.orderRef && reference.orderRef.current)
      reference.orderRef.current.focus();
    else if (!urlStatus.status && reference.urlRef && reference.urlRef.current)
      reference.urlRef.current.focus();
    else if (!urlTitleStatus.status && reference.titleRef && reference.titleRef.current)
      reference.titleRef.current.focus();
  };

  setFocustoPackageError = (orderStatus, appStatus) => {
    const { reference } = this.state;
    if (!orderStatus.status && reference.orderRef && reference.orderRef.current)
      reference.orderRef.current.focus();
    else if (!appStatus.status && reference.homePackageRef && reference.homePackageRef.current)
      reference.homePackageRef.current.focus();
  };

  validateURLPackage = () => {
    const { dataSource } = this.state;
    const { homeApplicationsShortcut } = dataSource;
    let { dynamicData } = this.state;
    const orderStatus = validateOrderNumber(dataSource.order, dataSource.homeApplicationsShortcut);
    const urlStatus = validateURL(dataSource.url);
    const urlTitleStatus = validateTitle(dataSource.urlTitle);
    dynamicData = setValidationErrorMessage('orderError', orderStatus, dynamicData);
    dynamicData = setValidationErrorMessage('urlError', urlStatus, dynamicData);
    dynamicData = setValidationErrorMessage('urlTitleError', urlTitleStatus, dynamicData);
    if (orderStatus.status && urlStatus.status && urlTitleStatus.status) {
      dynamicData.orderError = '';
      dynamicData.urlError = '';
      dynamicData.urlTitleError = '';
      const data = {
        orderNo: parseInt(dataSource.order, 10),
        type: 'web',
        packageNameOrUrl: dataSource.url,
        title: dataSource.urlTitle,
      };
      homeApplicationsShortcut.push(data);
      dataSource.homeApplicationsShortcut = cloneDeep(homeApplicationsShortcut);
      dataSource.order = '';
      dataSource.url = '';
      dataSource.urlTitle = '';
      this.setState({ dynamicData, dataSource });
    } else {
      this.setState({ dynamicData });
      this.setFocustoURLError(orderStatus, urlStatus, urlTitleStatus);
    }
  };

  validatePackageSelected = () => {
    const { dataSource } = this.state;
    const { order, homeSingleSelect, homeBgSelect, homeApplicationsShortcut } = dataSource;
    let { dynamicData } = this.state;
    const orderStatus = validateOrderNumber(order, homeApplicationsShortcut);
    const appStatus = validateAppStatus(homeSingleSelect);
    dynamicData = setValidationErrorMessage('orderError', orderStatus, dynamicData);
    dynamicData = setValidationErrorMessage('packageError', appStatus, dynamicData);
    if (orderStatus.status && appStatus.status) {
      homeBgSelect.push(homeSingleSelect[0]);
      dataSource.homeBgSelect = cloneDeep(homeBgSelect);
      dataSource.homeSingleSelect = [];
      const data = {
        orderNo: parseInt(order, 10),
        type: 'app',
        packageNameOrUrl: homeSingleSelect[0].packageName,
        title: '',
      };
      homeApplicationsShortcut.push(data);
      dataSource.homeApplicationsShortcut = cloneDeep(homeApplicationsShortcut);
      dataSource.order = '';
      dynamicData.orderError = '';
      dynamicData.packageError = '';
      this.setState({ dynamicData, dataSource });
    } else {
      this.setState({ dynamicData });
      this.setFocustoPackageError(orderStatus, appStatus);
    }
  };

  addPackageClick = () => {
    const { dataSource } = this.state;
    if (dataSource.shortcutType) {
      return this.validateURLPackage();
    }
    return this.validatePackageSelected();
  };

  deleteRow = () => {
    const { dataSource, selectedRowValue } = this.state;
    const { homeApplicationsShortcut, homeBgSelect } = dataSource;
    const index = UNDERSCORE.findIndex(homeApplicationsShortcut, {
      orderNo: selectedRowValue.orderNo,
    });
    const bgIndex = UNDERSCORE.findIndex(homeBgSelect, {
      packageName: selectedRowValue.packageNameOrUrl,
    });
    if (index >= 0) {
      homeApplicationsShortcut.splice(index, 1);
    }
    if (bgIndex >= 0) {
      homeBgSelect.splice(bgIndex, 1);
    }
    dataSource.homeApplicationsShortcut = cloneDeep(homeApplicationsShortcut);
    dataSource.homeBgSelect = cloneDeep(homeBgSelect);
    this.setState({
      selectedRowValue: homeApplicationsShortcut.length ? homeApplicationsShortcut[0] : {},
      defaultSelectedKeys: [0],
      dataSource,
    });
  };

  /**
   * onHandleClick is to handle botton click events kiosk customize screen.
   * @param screenKey
   * @return footerActionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      addPackage: () => this.addPackageClick(),
      wallpaperUpload: () => this.onUploadClick(),
      homeAppDelete: () => this.deleteUploadedFile(),
      homeAppUrl: () => this.downloadFile(),
      homeSingleSelect: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      multiAppSelect: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      bgAppSelect: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      singleAppSelect: () => this.navigateDetailScreen(serviceMapper(screenKey)),
      confirm: () => this.addKioskSettings(),
      cancel: () => this.goBackToPreviousScreen(),
      deleterow: () => this.deleteRow(),
    }[screenKey]());

  getUploadedFile = (event) => {
    let { fileSource } = this.state;
    [fileSource] = event.target.files;
    this.setState({ fileSource });
  };

  onUploadClick = () => {
    const { fileSource, dataSource, reference } = this.state;
    const { userVo } = this.props;
    const attachmentStatus = validateFile(fileSource, 5);
    if (attachmentStatus.status) {
      dataSource.wallpaperHomeImage = fileSource;
      dataSource.wallpaperHomeImageName = fileSource.name;
      dataSource.wallpaperHomeImageUploadedDate = new Date();
      dataSource.wallpaperHomeImageUploadedBy = userVo.loginId;
      this.setState({
        dataSource,
        fileSource: null,
        dynamicData: {},
      });
    } else {
      let { dynamicData } = this.state;
      dynamicData = setValidationErrorMessage(
        'wallPaperUploadError',
        attachmentStatus,
        dynamicData,
      );
      this.setState({ dynamicData });
    }
    if (reference.wallPaperUploadRef.current) reference.wallPaperUploadRef.current.value = '';
  };

  deleteUploadedFile = () => {
    const { dataSource } = this.state;
    dataSource.wallpaperHomeImage = null;
    dataSource.wallpaperHomeImageName = null;
    dataSource.wallpaperHomeImageUploadedDate = null;
    dataSource.wallpaperHomeImageUploadedBy = null;
    this.setState({
      dataSource,
      dynamicData: {},
    });
  };

  downloadFile = () => {
    const { dataSource } = this.state;
    const { wallpaperHomeImage } = dataSource;
    const fileName = wallpaperHomeImage.name;
    fileDownload(wallpaperHomeImage, fileName);
  };

  onkioskCustomLauncherClick = (key, value) => {
    const { mainPolicyMapper, dataSource, footer } = this.state;
    const { kioskMultiApps, kioskAppSelect } = dataSource;
    mainPolicyMapper[key] = value;
    return this.setState({
      mainPolicyMapper,
      dataSource,
      dynamicData: {},
      footer: { ...footer, disable: value ? kioskMultiApps.length === 0 : kioskAppSelect.length === 0 },
    });
  };

  shortcutClick = (value) => {
    const { dataSource, dynamicData } = this.state;
    if (value) {
      dataSource.shortcutType = value;
      dynamicData.packageError = '';
    } else {
      dataSource.url = '';
      dataSource.urlTitle = '';
      dataSource.shortcutType = value;
      dynamicData.urlError = '';
      dynamicData.urlTitleError = '';
    }
    return this.setState({
      dataSource,
      dynamicData,
    });
  };

  appSettingClick = (value) => {
    const { dataSource, footer, mainPolicyMapper } = this.state;
    if (value) {
      dataSource.appSetting = value;
      dataSource.kioskAppSelect = [];
      dataSource.kioskBgApps = [];
    } else {
      dataSource.appSetting = value;
      dataSource.iconSize = '';
      dataSource.order = '';
      dataSource.wallpaperHomeImage = null;
      dataSource.wallpaperHomeImageName = null;
      dataSource.wallpaperHomeImageUploadedDate = null;
      dataSource.wallpaperHomeImageUploadedBy = null;
      dataSource.url = '';
      dataSource.urlTitle = '';
      dataSource.homeApplicationsShortcut = [];
      dataSource.homeSingleSelect = [];
      dataSource.homeBgSelect = [];
      dataSource.shortcutType = false;
    }
    footer.disable = !(!mainPolicyMapper.kioskCustomLauncherEnabled && dataSource.appSetting);
    return this.setState({
      dataSource,
      footer,
      dynamicData: {},
    });
  };

  /**
   * onTableFrameEvent is to perform kiosk input field events
   * @param key
   * @param event
   * @param value
   */
  onTableFrameEvent = (key, event, value) => {
    const { type, target } = event;
    const { mainPolicyMapper, dataSource, footer } = this.state;
    if (key === 'wallpaperHomeImage') {
      return this.getUploadedFile(event);
    }
    if (key === 'shortcutType') {
      return this.shortcutClick(value);
    }
    if (key === 'order' || key === 'url' || key === 'urlTitle') {
      dataSource[key] = target.value;
      return this.setState({ dataSource });
    }
    if (key === 'iconSize') {
      dataSource[key] = target.value;
      return this.setState({ dataSource });
    }
    if (type === 'click') {
      return this.onHandleClick(key, value);
    }
    if (key === 'appSetting') {
      this.appSettingClick(value);
    } else if (key === 'kioskCustomLauncherEnabled') {
      return this.onkioskCustomLauncherClick(key, value);
    } else {
      mainPolicyMapper.kioskCustomization[key] = target.value;
    }
    return this.setState({
      mainPolicyMapper,
      dataSource,
      footer: { ...footer, disable: key === 'kioskCustomLauncherEnabled' ? true : footer.disable },
    });
  };

  /**
   * goBackToPreviousScreen is to navigate from current screen to previous screen
   */

  goBackToPreviousScreen = () => {
    const {
      policyMapperData,
      makeGroupData,
      makeKioskAppSelect,
      history,
      location: getData,
      makeKioskBgApps,
      mainPolicyMapper,
    } = this.props;
    const { dataSource, ancestorScreen } = getData.state;
    const { initialDataSource } = this.state;
    const { kioskBgApps, kioskAppSelect } = dataSource;
    initialDataSource.homeBgSelect = cloneDeep(initialDataSource.homeBgSelect);
    initialDataSource.homeApplicationsShortcut = cloneDeep(
      initialDataSource.homeApplicationsShortcut,
    );
    policyMapperData(mainPolicyMapper);
    makeGroupData({ ...initialDataSource, mainPolicyMapper });
    makeKioskAppSelect(kioskAppSelect);
    makeKioskBgApps(kioskBgApps);
    history.push(ancestorScreen, { ...getData.state, parentScreen: getData.pathname });
  };

  /**
   * navigateDetailScreen is to navigate kiosk customize screen
   * to select application selection screens
   * @param screenName
   */
  navigateDetailScreen = (screenName) => {
    const { mode, mainPolicyMapper, initialDataSource } = this.state;
    const { history, location: getData, makeGroupData, isModified } = this.props;
    const { dataSource } = getData.state;
    dataSource.mainPolicyMapper = mainPolicyMapper;
    dataSource.homeBgSelect = cloneDeep(dataSource.homeBgSelect);
    dataSource.homeApplicationsShortcut = cloneDeep(dataSource.homeApplicationsShortcut);
    makeGroupData(dataSource);
    history.push(screenName, {
      ...getData.state,
      mode,
      isModified,
      dataSource,
      initialDataSource,
      groupId: dataSource.groupId,
      parentScreen: getData.pathname,
    });
  };

  /**
   * addKioskSettings is to set kiosk application and customization.
   */
  addKioskSettings = () => {
    const {
      policyMapperData,
      makeGroupData,
      makeKioskAppSelect,
      setModified,
      history,
      location: getData,
      makeKioskBgApps,
      makeHomeSingleApp,
    } = this.props;
    const { ancestorScreen } = getData.state;
    const { mainPolicyMapper, dataSource } = this.state;
    const { reference } = this.state;
    let { dynamicData } = this.state;
    dataSource.url = '';
    dataSource.urlTitle = '';
    dataSource.shortcutType = false;
    dataSource.order = '';
    const { kioskBgApps, kioskAppSelect } = dataSource;
    const appViolation = kioskBgApps.some(
      (app) => app.packageName === kioskAppSelect[0].packageName,
    );
    if (appViolation && !mainPolicyMapper.kioskCustomLauncherEnabled) {
      return this.setState({ openErrorDialog: true });
    }
    if (!mainPolicyMapper.kioskCustomLauncherEnabled && dataSource.appSetting) {
      const iconStatus = validateIconValue(dataSource.iconSize);
      if (!iconStatus.status) {
        dynamicData = setValidationErrorMessage('iconSizeError', iconStatus, dynamicData);
        if (reference.iconSizeRef.current) reference.iconSizeRef.current.focus();
        return this.setState({ dynamicData });
      }
    }
    dataSource.homeBgSelect = cloneDeep(dataSource.homeBgSelect);
    dataSource.homeApplicationsShortcut = cloneDeep(dataSource.homeApplicationsShortcut);
    policyMapperData(mainPolicyMapper);
    makeGroupData({ ...dataSource, mainPolicyMapper });
    makeKioskAppSelect(kioskAppSelect);
    makeKioskBgApps(kioskBgApps);
    makeHomeSingleApp([]);
    setModified(true);
    return history.push(ancestorScreen, { ...getData.state, parentScreen: getData.pathname });
  };

  /**
   * render performs kiosk customize screen rendering
   * @return KioskCustomization
   */
  render() {
    const {
      headerData,
      pageLoading,
      footer,
      mainPolicyMapper,
      openErrorDialog,
      dataSource,
      reference,
      dynamicData,
      selectedRowValue,
      defaultSelectedKeys,
    } = this.state;
    const {
      appSetting,
      kioskAppSelect,
      kioskBgApps,
      homeApplicationsShortcut: gridSource,
    } = dataSource;
    return (
      <div id="contents" className="clearfix">
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <div style={{ width: '100%', paddingBottom: '150px' }}>
            <div id="main">
              <div className="container clearfix mb10">
                <SBContentHeader title={headerData.title} description={headerData.description} />
              </div>
              <div className="clearfix mb10">
                {kioskTableList(reference, dynamicData).map((record) => [
                  record.header && (
                    <h2 className="title-corner relative">
                      {this.languageLabelCheck(record.header)}
                    </h2>
                  ),
                  <CustomTableFrame
                    defaultValue={{}}
                    dataSource={{
                      ...dataSource,
                      mainPolicyMapper,
                      kioskAppSelect,
                      ...kioskBgApps,
                    }}
                    tableList={record.content}
                    onKeyDown={(event) => this.onKeyDown(event)}
                    onTableFrameEvent={(key, event, value, buttonKey) =>
                      this.onTableFrameEvent(key, event, value, buttonKey)
                    }
                  />,
                  record.header === 'application_selection' &&
                    !mainPolicyMapper.kioskCustomLauncherEnabled &&
                    appSetting &&
                    gridSource &&
                    gridSource.length > 0 && (
                      <div>
                        <SBTable
                          columns={this.getColumns()}
                          dataSource={[...gridSource]}
                          loading
                          screenKey="selectTarget"
                          defaultSelectedKeys={defaultSelectedKeys}
                          selectedRowValue={selectedRowValue}
                          handleRadioChange={(value, keys) => this.handleRadioChange(value, keys)}
                        />
                        <div className="clearfix mb10">
                          <div className="floatright">
                            <SBButton
                              id="btn_delete"
                              noMarginRight
                              type="button"
                              title="delete"
                              onClick={() => this.onHandleClick('deleterow')}
                            />
                          </div>
                        </div>
                      </div>
                    ),
                ])}
              </div>
              <GroupManagementFooter
                footer={footer}
                onHandleClick={(key) => this.onHandleClick(key)}
              />
            </div>
            <SBDialog
              type="error"
              width="400px"
              header="error"
              okButtonTitle="ok"
              content="kiosk_app_violation_error_msg"
              open={openErrorDialog}
              onOk={() => this.setState({ openErrorDialog: false })}
            />
          </div>
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { data: localeData } = state.language;
  const { userVo } = state.user;
  const {
    mainPolicyMapper,
    defaultPolicyData,
    kioskAppSelect,
    kioskBgApps,
    kioskMultiApps,
  } = state.group;
  return {
    localeData,
    mainPolicyMapper,
    defaultPolicyData,
    kioskAppSelect,
    kioskBgApps,
    kioskMultiApps,
    userVo,
  };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    policyMapperData: (data) => dispatch(getPolicyMapper(data)),
    makeGroupData: (data) => dispatch(makeGroupInformation(data)),
    makeKioskAppSelect: (data) => dispatch(getKioskAppSelect(data)),
    makeKioskBgApps: (data) => dispatch(getKioskBgApps(data)),
    makeKioskMultiApps: (data) => dispatch(getKioskMultiApps(data)),
    makeHomeSingleApp: (data) => dispatch(getHomeSingleApp(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(KioskCustomization));
