/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../common/component/tableLayout/SBTableLayout';
import SBComboBox from '../../common/component/combobox/SBComboBox';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Utility Files */
import { get } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import deviceHeader from '../constants/deviceHeaderConstants';
import { departmentSearchComboList } from '../constants/deviceConstants';
import { getDepartment } from '../../actions/DeviceAction';
import { serviceMapper } from '../common/deviceCommonFunctions';
import DeviceFooter from '../component/DeviceFooter';

/**
 * DeviceDepartmentSearch is to search device information based on department
 * @author Aswin Palanisamy
 */
class DeviceDepartmentSearch extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, location: getData, main, department } = props;
    const { mode, getDepartment: fetchDepartment } = getData.state || { mode: 0, getDepartment: department };
    const { departmentSearch } = main ? JSON.parse(localStorage.getItem('deviceSearch')) : { departmentSearch: fetchDepartment};
    this.state = {
      screenKey,
      departmentData: { departmentOne : [], departmentTwo: [], departmentThree: [], departmentFour: [] },
      department: cloneDeep(departmentSearch),
      pageLoading: false,
      openDialogError: false,
      disable: true,
      message: '',
      header: '',
      mode,
      headerData: deviceHeader(screenKey),
    };
    this.nameRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { device } = this.props;
    const { mode } = this.state;
    this.setState({ pageLoading: true }, () =>
      !device && !mode ? this.denyAccess() : this.getDepartmentList(true),
    );
  }

  /**
   * denyAccess used to show message when mode not exists.
   */
  denyAccess = () =>
    this.setState({
      pageLoading: false,
      openDialogError: true,
      message: 'data_does_not_exists',
      header: 'error',
    });

  /**
   * getDepartmentList is to fetch department information
   */
  getDepartmentList = async (onOpen) => {
    const response = await get(APIENDPOINTS.DEVICEDEPARTMENTDATA);
    const result = await response.json();
    const { status, message, data: dataMap } = result;
    if (status === 'OK') {
      this.setState({
        departmentData: dataMap,
        disable: this.checkDisabled(dataMap),
        pageLoading: false,
      }, () => {
        if (onOpen && this.nameRef.current) this.nameRef.current.focus();
      });
    } else {
      this.setState({
        openDialogError: true,
        message,
        pageLoading: false,
        header: 'error',
      });
    }
  };

  /**
   * checkDisabled is to check whether all the department information is empty
   * @param departmentMap
   */
  checkDisabled = (departmentMap) => {
    return isEmpty(departmentMap.departmentOne) && isEmpty(departmentMap.departmentTwo)
    && isEmpty(departmentMap.departmentThree) && isEmpty(departmentMap.departmentFour);
  }

  /**
   * getDepartmentSearch is to prepare department search List
   * @param departmentMap
   */
  getDepartmentSearch = (departmentMap) => 
    [departmentMap.departmentOne[0] || '', 
    departmentMap.departmentTwo[0] || '', 
    departmentMap.departmentThree[0] || '', 
    departmentMap.departmentFour[0] || ''];

  /**
   * changeHandler fetches selected depatrment value
   * @param value
   * @param key
   */
  changeHandler = (value, key) => {
    this.setState(({department}) => {
      department[key] = value;
      return {department};
    });
  };

  /**
   * departmentOkClick is to set selected department for search operation
   */
  departmentOkClick = () => {
    const getStore = JSON.parse(localStorage.getItem('deviceSearch'));
    const { main, makeDepartmentList } = this.props;
    const { department: departmentSearch } = this.state;
    if(main) {
      localStorage.setItem('deviceSearch', JSON.stringify({ ...getStore, departmentSearch }));
    } else {
      makeDepartmentList(departmentSearch);
    }
    this.getBacktoPreviousScreen();
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   */
  onHandleClick = screenKey => {
    const screenFunctionMapper = {
      departmentCancel: () => this.getBacktoPreviousScreen(),
      departmentOk: () => this.departmentOkClick(),
    };
    return screenFunctionMapper[screenKey]();
  };

  /**
   * renderComboBox is to render combobox component
   * @param props
   * @param style
   * @return comboBox
   */
  renderComboBox = (props) => {
    const { style, index } = props;
    return (
      <SBComboBox
        {...props}
        isNotMultiLang
        style={{ ...style }}
        onChange={event => this.changeHandler(event.target.value, index)}
      />
    );
  };

  /**
   * tableList is to set department component data
   * @return tableComponentList
   */
  tableList = () => {
    const { departmentData, department} = this.state;
    const tableComponentList = [];
    departmentSearchComboList(departmentData, department, this.nameRef, this.makeComboData).map(record =>
      tableComponentList.push({
        key: record.labelKey,
        value: this.renderComboBox(record),
      }),
    );
    return tableComponentList;
  };

  /**
   * makeComboData is to build department combo values
   * @param dataList 
   */
  makeComboData = (dataList) => {
    const comboDataList = [];
    if(!isEmpty(dataList)) comboDataList[0] = {key: '', value: ''};
    dataList.forEach(data => comboDataList.push({key: data, value: data}));
    return comboDataList;
  };

  /**
   * getBacktoPreviousScreen is to navigate to deviceList screen
   * @param isError
   */
  getBacktoPreviousScreen = isError => {
    const { history, location: getData, device } = this.props;
    const { parentScreen: previousScreen } = getData.state || { parentScreen: '' };
    const parentScreen = serviceMapper(device ? 'deviceList' : 'groupManagement');
    const screenName = isError || !previousScreen ? parentScreen : previousScreen;
    history.push(screenName, { ...getData.state, parentScreen });
  };

  /**
   * render performs department search screen rendering
   * @return departmentSearch
   */
  render() {
    const {
      openDialogError,
      message,
      pageLoading,
      headerData,
      screenKey,
      disable,
      header,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading}>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <SBTableLayout tableData={this.tableList()} filterKey="key" filterValue="value" />
          <DeviceFooter
            footer={{
              disable,
              screenKey,
            }}
            onHandleClick={key => this.onHandleClick(key)}
          />
        </div>
        <SBDialog
          open={openDialogError}
          header={header}
          type={header}
          okButtonTitle="ok"
          content={message}
          onOk={() => {
            this.setState(
              { openDialogError: false },
              this.getBacktoPreviousScreen(header === 'error'),
            );
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps perform data linking from the redux store
 * @param state 
 */
const mapStateToProps = (state) => {
  const { department } = state.device;
  return { department };
};

/**
 * mapDispatchToProps performs data setup in device redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeDepartmentList: data => {
      dispatch(getDepartment(data));
    }
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceDepartmentSearch.propTypes = {
  screenKey: PropTypes.string.isRequired,
  main: PropTypes.bool,
  device: PropTypes.bool,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      parentScreen: PropTypes.string,
      mode: PropTypes.number,
    }),
  }).isRequired,
  makeDepartmentList: PropTypes.func.isRequired,
  department: PropTypes.arrayOf(PropTypes.string),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DeviceDepartmentSearch.defaultProps = {
  main: false,
  device: false,
  department: ['','','',''],
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceDepartmentSearch));
