/** Base Files */
import React, { Component, createRef } from 'react';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import fileDownload from 'js-file-download';
import encoding from 'encoding';

/** SBComponents */
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import checkLanguage from '../../language/checkLanguage';

/** Device Components */
import DeviceFooter from '../component/DeviceFooter';
import GroupManagementIndicator from '../../groupSetting/component/GroupManagementIndicator';

/** Utility Files */
import { currentISODate } from '../../common/utils/dateUtils';
import { post, postWithFormData } from '../../actions/commonActions';
import deviceHeader from '../constants/deviceHeaderConstants';
import { getGroupNameList, getDepartment } from '../../actions/DeviceAction';

import {
  apiEndPointMapper,
  serviceMapper,
  enumMapper,
  getDeviceDetail,
} from '../common/deviceCommonFunctions';
import { tableView, csvBatchtableList } from '../constants/deviceTableConstants';

/**
 * DeviceUserDetail is to display list of device information
 * @author Aswin Palanisamy
 */
class DeviceUserDetail extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { location: getData, screenKey } = props;
    const { deviceId, wipeFlag } = getData.state || { deviceId: '', wipeFlag: 'no' };
    this.state = {
      deviceId,
      dataSource: { file: '', operationType: 'changeGroup' },
      pageLoading: false,
      openDialog: false,
      openCsvErrorDialog: false,
      message: '',
      limitReached: '',
      type: '',
      header: '',
      disable: false,
      headerData: deviceHeader(screenKey),
      openValidationDialog: false,
      wipeFlag,
      dynamicData: {
        uploadCsvError: '',
        invalidFileError: '',
      },
    };
    this.nameRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { location, screenKey } = this.props;
    if (screenKey === 'csvBatchOperation') this.setState({ pageLoading: false });
    else if (!location.state) this.denyAccess();
    else this.getDeviceUserInformation();
  }

  /**
   * denyAccess used to restrict the user from accessing the screen with a prompt.
   */
  denyAccess = () =>
    this.setState({
      pageLoading: false,
      openDialog: true,
      header: 'error',
      type: 'error',
      message: 'data_does_not_exists',
    });

  /**
   * getDeviceInformation is to fetch selected device information
   */
  getDeviceUserInformation = () => {
    const prevState = this.state;
    this.setState({ pageLoading: true }, async () => {
      const fetchProps = this.props;
      const deviceReport = await getDeviceDetail(
        {
          ...fetchProps,
          tabKey: fetchProps.screenKey === 'operationView' ? 'operationView' : 'user',
        },
        prevState,
      );
      this.setState({ pageLoading: false, dataSource: deviceReport.dataSource });
    });
    if (this.nameRef.current) this.nameRef.current.focus();
  };

  /**
   * navigateListScreen perform device list screen navigation
   */
  navigateListScreen = () => {
    const { history } = this.props;
    history.push(serviceMapper('deviceList'));
  };

  /**
   * downloadErrorText is to download csv error document
   */
  downloadErrorText = () => {
    const { dynamicData } = this.state;
    const { currentLanguage } = this.props;
    const fileName = `error${
      currentLanguage === 'ja' ? `-${currentISODate().replace('Z', '')}` : ''
    }.txt`;
    fileDownload(dynamicData.uploadCsvError, fileName);
  };

  /**
   * downloadTemplateCSV is to download csv template document
   */
  downloadTemplateFile = () => {
    const { dataSource } = this.state;
    const { currentLanguage } = this.props;
    const hasGroup = dataSource.operationType === 'changeGroup';
    fileDownload(
      encoding.convert(
        /* `tel, */ `serial${hasGroup ? ',group' : ''}\r\n`,
        currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8',
      ),
      /* `tel_ */ `serial${hasGroup ? '_group' : ''}.csv`,
    );
  };

  /**
   * componentList is to frame error download link on CSV upload operation
   * @return errorLink
   */
  componentList = () => [
    <Link>
      <span>
        <a href onClick={() => this.downloadErrorText()}>
          {this.languageLabelCheck('download_csv_error')}
        </a>
      </span>
    </Link>,
    <SBButton
      type="button"
      title="view_error"
      name="view_error"
      id="view_error"
      key="viewError"
      onClick={() => this.onHandleClick('viewError')}
    />,
  ];

  /**
   * renderCsvResult used to render csv validation result and file name
   * @param dynamicData
   * @returns component
   */
  renderCsvResult = (dynamicData) => {
    return (
      dynamicData.uploadCsvError &&
      (dynamicData.uploadCsvError.split('\n').length > 5
        ? this.componentList()[0]
        : this.componentList()[1])
    );
  };

  /**
   * getTableData perform component fetch based on tab switch
   */
  getTableData = () => {
    const { screenKey } = this.props;
    const { dynamicData } = this.state;
    switch (screenKey) {
      case 'remoteWipe':
      case 'resetPassword':
      case 'operationView':
      case 'remoteLock':
      case 'remoteUnlock':
        return tableView(screenKey);
      case 'deviceReboot':
        return tableView('user')[0].content;
      case 'csvBatchOperation':
        return csvBatchtableList(dynamicData, this.nameRef, this.renderCsvResult);
      default:
        return '';
    }
  };

  /**
   * getBackToPreviousScreen is navigate to deviceList screen
   */
  getBackToPreviousScreen = () => {
    const { history, location, screenKey } = this.props;
    const { deviceId } = this.state;
    const screenPath =
      screenKey === 'csvBatchOperation' ? serviceMapper('deviceList') : location.state.parentScreen;
    history.push(screenPath, { deviceId });
  };

  /**
   * onHandleClick is to handle button click events in table frame
   * @param screenKey
   * @return screenFunctionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      confirm: () => this.onOkClick(),
      cancel: () => this.getBackToPreviousScreen(),
      viewError: () => this.setState({ openCsvErrorDialog: true }),
      csvFileTemplate: () => this.downloadTemplateFile(),
    }[screenKey]());

  /**
   * languageLabelCheck is to set label content
   * @param label
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * csvBulkSetting is to operform batch operation on registered devices based on CSV option
   */
  csvBulkSetting = async () => {
    const { screenKey, history, makeGroupList, makeDepartmentList } = this.props;
    const { dataSource, dynamicData } = this.state;
    const { file, operationType } = dataSource;
    let invalidFileError = !file && `empty_file_${operationType}`;
    invalidFileError = file && file.size === 0 ? 'file_size_error' : invalidFileError;
    invalidFileError =
      file && !file.name.endsWith('.csv') ? `invalid_file_type_${operationType}` : invalidFileError;
    invalidFileError = file && file.name.length > 80 ? 'upload_file_name_length' : invalidFileError;
    if (invalidFileError) {
      this.setState({
        dynamicData: { ...dynamicData, invalidFileError, uploadCsvError: '' },
        dataSource: { ...dataSource, file: '' },
        limitReached: '',
        pageLoading: false,
      });
    } else {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('operationType', operationType);
      const response = await postWithFormData(apiEndPointMapper(screenKey), formData);
      const result = await response.json();
      const { status, message, data: deviceCsvMapper } = result;
      if (status === 'OK') {
        makeGroupList();
        makeDepartmentList();
        history.push(serviceMapper(`${operationType}BulkCsv`), {
          deviceCsvMapper: { ...deviceCsvMapper, message: operationType },
          parentScreen: serviceMapper('csvBatchOperation'),
        });
      } else if (message === 'error_log') {
        this.setState({
          dynamicData: {
            ...dynamicData,
            uploadCsvError: deviceCsvMapper.message,
            invalidFileError: '',
          },
          limitReached: '',
          pageLoading: false,
          dataSource: { ...dataSource, file: '' },
        });
      } else if (message === 'limit_reached') {
        this.setState({
          limitReached: deviceCsvMapper.message,
          dynamicData: { ...dynamicData, uploadCsvError: '', invalidFileError: '' },
          pageLoading: false,
          dataSource: { ...dataSource, file: '' },
        });
      } else {
        this.setState({
          message,
          type: 'error',
          header: 'error',
          limitReached: '',
          dataSource: { ...dataSource, file: '' },
          dynamicData: { ...dynamicData, uploadCsvError: '', invalidFileError: '' },
          openDialog: true,
          pageLoading: false,
        });
      }
    }
  };

  /**
   * onOkClick performs user specified device actions
   */
  onOkClick = () => {
    const { deviceId, wipeFlag } = this.state;
    const { screenKey } = this.props;
    if (screenKey === 'csvBatchOperation') {
      this.setState({ pageLoading: true }, () => {
        this.csvBulkSetting();
      });
    } else {
      this.setState({ pageLoading: true }, async () => {
        const enumList = [enumMapper(screenKey)];
        if (wipeFlag === 'no' && screenKey === 'remoteWipe')
          enumList.push('PRESERVE_RESET_PROTECTION_DATA');
        const paramMapper = {
          withoutEnum: { deviceId },
          withEnum: { deviceId, enumList },
        };
        const response = await post(
          apiEndPointMapper(screenKey),
          screenKey === 'deviceReboot' ? paramMapper.withoutEnum : paramMapper.withEnum,
        );
        const result = await response.json();
        const { message, status } = result;
        if (status === 'OK')
          this.setState({
            message,
            type: 'success',
            openDialog: true,
            pageLoading: false,
            header: 'information',
          });
        else
          this.setState({
            message,
            type: 'error',
            header: 'error',
            openDialog: true,
            pageLoading: false,
          });
      });
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   */
  onTableFrameEvent = (key, event, value) => {
    const { target } = event;
    if (key === 'csvFileTemplate') {
      this.onHandleClick(key);
    } else {
      this.setState(({ dataSource }) => ({
        dataSource: {
          ...dataSource,
          file: key === 'csvFile' ? target.files[0] : dataSource.file,
          operationType: key === 'operationType' ? value : dataSource.operationType,
        },
      }));
    }
  };

  /**
   * render perform device user tab rendering
   * @return DeviceUserTable
   */
  render() {
    const { screenKey } = this.props;
    const {
      pageLoading,
      openDialog,
      headerData,
      message,
      disable,
      type,
      header,
      limitReached,
      dataSource,
      openValidationDialog,
      openCsvErrorDialog,
      dynamicData,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div>
              <CustomTableFrame
                dataSource={dataSource}
                tableList={this.getTableData()}
                defaultValue={[]}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              />
            </div>
            <DeviceFooter
              onHandleClick={(key) => this.onHandleClick(key)}
              footer={{
                disable,
                screenKey:
                  screenKey === 'operationView' || screenKey === 'csvBatchOperation'
                    ? screenKey
                    : 'userDetail',
              }}
            />
            <div className="red bold" style={{ display: limitReached ? 'block' : 'none' }}>
              {this.languageLabelCheck(limitReached)}
            </div>
          </div>
        </div>
        <GroupManagementIndicator
          notMultiLang
          dialogContent={dynamicData.uploadCsvError.split('\n')}
          openDeviceDialog={openCsvErrorDialog}
          closeDeviceDialog={() => this.setState({ openCsvErrorDialog: false })}
        />
        <SBDialog
          type={type}
          header={header}
          open={openDialog}
          content={message}
          onCancel={() => {
            this.setState({ openDialog: false });
          }}
          onOk={() => this.navigateListScreen()}
          width="400px"
        />
        <SBDialog
          type={type}
          isNotMultiLang
          header={header}
          open={openValidationDialog}
          content={message}
          onCancel={() => {
            this.setState({ openValidationDialog: false });
          }}
          onOk={() => {
            this.setState({ openValidationDialog: false });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
DeviceUserDetail.propTypes = {
  screenKey: PropTypes.string.isRequired,
  currentLanguage: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      deviceId: PropTypes.string,
      operationId: PropTypes.string,
      parentScreen: PropTypes.string,
    }),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  makeGroupList: PropTypes.func.isRequired,
  makeDepartmentList: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  const { data, current } = state.language;
  return { localeData: data, currentLanguage: current };
};

/**
 * mapDispatchToProps performs data setup in device redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeGroupList: (data) => {
      dispatch(getGroupNameList(data));
    },
    makeDepartmentList: data => {
      dispatch(getDepartment(data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceUserDetail));
