/**
 * OperationUpload is used to create operation upload operation
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

const linkStyle = {
  color: '#036EB7',
  textDecoration: 'underline',
  cursor: 'pointer',
  background: 'none',
  font: 'inherit',
  padding: 0,
  border: 'none',
  letterSpacing: 0,
  whiteSpace: 'normal',
};

/**
 * OperationUpload is used to create operation upload operation
 * @param props
 * @return OperationUploadComponent
 */

const OperationUpload = props => {
  const { uploadedBy, fileName, dateTime, onURLClick } = props;
  return (
    <>
      <table className="table-tate mb5 mt5">
        <tbody>
          <tr>
            <td style={{ maxWidth: 375 }}>
              <span className="lineheight ">
                <button type="button" style={linkStyle} onClick={() => onURLClick()}>
                  {fileName}
                </button>
              </span>
            </td>
            <td>
              <span className="lineheight ">{dateTime}</span>
            </td>
            <td>
              <span className="lineheight ">{uploadedBy}</span>
            </td>
          </tr>
        </tbody>
      </table>
    </>
  );
};

/**
 * propTypes is used for validation props with its type.
 */

OperationUpload.propTypes = {
  dateTime: PropTypes.objectOf(PropTypes.any).isRequired,
  onURLClick: PropTypes.func.isRequired,
};

export default withRouter(OperationUpload);
