import React, { Component } from 'react';
import { connect } from 'react-redux';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';

import SBComboBox from '../../common/component/combobox/SBComboBox';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';

import {
  deviceComboData,
  deviceFilterCheckList,
  deviceFilterHeaderList,
} from '../constants/deviceConstants';
import checkLanguage from '../../language/checkLanguage';

class MultipleConditionTable extends Component {
  constructor(props) {
    super(props);
    this.state = { ...this.setGridMarker(true), prevString: '', changeCharacter: false, currentIndex: 0 };
    this.comboList = cloneDeep(deviceComboData.conditionOne.slice(0, 2));
  }

  componentDidMount() {
    const { onRef } = this.props;
    if (onRef) onRef(this);
  }

  componentWillUnmount() {
    const { onRef } = this.props;
    if (onRef) onRef();
  }

  setGridMarker = onOpen => {
    if (onOpen) {
      const { aDetailMark, aPolicyMark } = JSON.parse(localStorage.getItem('deviceSearch'));
      return {
        aDetailMark: aDetailMark || '+',
        aPolicyMark: aPolicyMark || '+',
        detailSearchVisibility: aDetailMark === '+' ? 'none' : 'table-row',
        policySearchVisibility: aPolicyMark === '+' ? 'none' : 'table-row',
      };
    }
    return this.setState({
      aDetailMark: '+',
      aPolicyMark: '+',
      detailSearchVisibility: 'none',
      policySearchVisibility: 'none',
    });
  };

  getGridMarker = () => {
    const { aDetailMark, aPolicyMark } = this.state;
    return { aDetailMark, aPolicyMark };
  };

  setClearFilter = () => {
    const { deviceFilterCondition } = this.props;
    const { deviceSearch, policySearch } = deviceFilterCondition;
    const [initialFilter] = deviceSearch;
    const policySearchApplied = policySearch.length;
    const deviceSearchApplied = !isEmpty(initialFilter.category);
    const { aDetailMark, aPolicyMark } = this.state;
    if (
      (!deviceSearchApplied && aDetailMark === '-') ||
      (!policySearchApplied && aPolicyMark === '-')
    )
      this.setState({
        aDetailMark: '+',
        aPolicyMark: '+',
        detailSearchVisibility: 'none',
        policySearchVisibility: 'none',
      });
  };

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  detailMarkClick = () => {
    let { aDetailMark, detailSearchVisibility } = this.state;
    if (aDetailMark === '+') {
      aDetailMark = '-';
      detailSearchVisibility = 'table-row';
    } else {
      aDetailMark = '+';
      detailSearchVisibility = 'none';
    }
    this.setState({ aDetailMark, detailSearchVisibility });
  };

  policyMarkClick = () => {
    let { aPolicyMark, policySearchVisibility } = this.state;
    if (aPolicyMark === '+') {
      aPolicyMark = '-';
      policySearchVisibility = 'table-row';
    } else {
      aPolicyMark = '+';
      policySearchVisibility = 'none';
    }
    this.setState({ aPolicyMark, policySearchVisibility });
  };

  changeFilterCondition = (event, index, key, addRow) => {
    const { changeFilterCondition, changeFilterCategory, changeFilterField } = this.props;
    this.setState({ changeCharacter: false });
    if (key === 'category') return changeFilterCategory(event, index);
    if (key === 'field') return changeFilterField(event, index);
    return changeFilterCondition(event, index, key, addRow);
  };

  changeCharacterString = (event, index, key) => {
    const { changeCharacterString } = this.props;
    return changeCharacterString(event, index, key);
  };

  onPolicySearchFilter = (event, key) => {
    const { onPolicySearchFilter } = this.props;
    onPolicySearchFilter(event, key);
  };

  setConditionOneCombo = (condition, index) => {
    const { changeCharacter, currentIndex } = this.state;
    if (!changeCharacter || currentIndex !== index) {
      this.comboList = cloneDeep(
        isEmpty(condition)
          ? deviceComboData.conditionOne.slice(0, 2)
          : deviceComboData.conditionOne,
      );
    }
    return this.comboList;
  };

  render() {
    const removeAnchorProperty = { minWidth: 30, borderRadius: 5 };
    const { deviceFilterCondition, markerRef } = this.props;
    const { deviceSearch, policySearch } = deviceFilterCondition;
    const { aDetailMark, aPolicyMark, detailSearchVisibility, policySearchVisibility } = this.state;

    return (
      <div className="mb10" style={{ 'overflow-x': 'scroll', 'overflow-y': 'hidden' }}>
        <table className="table-yoko table-yoko-fc-30px" style={{ 'min-width': '1000px;' }}>
          <tbody>
            <tr>
              <th width="5%">
                <div id="temp_div" style={{ display: 'none' }}>
                  {this.languageLabelCheck('header')}
                </div>
              </th>
              <th width="95%">{this.languageLabelCheck('narrow_down_items')}</th>
            </tr>
            <tr>
              <td className="tCenter">
                <button
                  type="button"
                  className="btn-accordion"
                  id="aDetailMark"
                  ref={markerRef}
                  style={removeAnchorProperty}
                  onClick={() => this.detailMarkClick()}
                >
                  {aDetailMark}
                </button>
              </td>
              <td>{this.languageLabelCheck('search_device_info')}</td>
            </tr>
            <tr id="Detailsearch_tr" style={{ display: detailSearchVisibility }}>
              <td />
              <td>
                <div className="mb10">
                  <table
                    className="table-yoko mb10 
                  table-yoko-nth2-30px table-yoko-nth3-30px 
                  table-yoko-nth4-30px table-yoko-nth5-30px table-yoko-lcd-30px"
                  >
                    <tbody>
                      <tr>
                        {deviceFilterHeaderList.map(data => (
                          <th>{this.languageLabelCheck(data)}</th>
                        ))}
                      </tr>
                      {deviceSearch.map((data, index) => (
                        <tr id={`items${data}_tr`}>
                          <td>
                            <SBComboBox
                              id={`items${index}_device_type`}
                              name={`items${index}_device_type`}
                              options={deviceComboData.deviceType}
                              filterKey="value"
                              filterValue="option"
                              style={{ width: 300 }}
                              value={{ ...deviceComboData.deviceType }.value}
                            />
                          </td>
                          <td>
                            <SBComboBox
                              id={`items${index}_category`}
                              name={`items${index}_category`}
                              options={deviceComboData.category}
                              filterKey="value"
                              filterValue="option"
                              style={{ width: 300 }}
                              value={deviceSearch[index].category}
                              onChange={event =>
                                this.changeFilterCondition(event, index, 'category')
                              }
                            />
                          </td>
                          <td>
                            <SBComboBox
                              id={`items${index}_field`}
                              name={`items${index}_field`}
                              options={deviceComboData.field[deviceSearch[index].category]}
                              filterKey="value"
                              filterValue="option"
                              style={{ width: 300 }}
                              value={deviceSearch[index].field}
                              onChange={event => this.changeFilterCondition(event, index, 'field')}
                            />
                          </td>
                          <td>
                            <SBTextbox
                              id={`items${index}_text`}
                              name={`items${index}_text`}
                              value={deviceSearch[index].characterString || ''}
                              maxLength={256}
                              type="text"
                              placeholder={
                                deviceSearch[index].field === 'dataCollectionTime' &&
                                'yyyy/mm/dd hh:mm'
                              }
                              onChange={event =>
                                this.changeCharacterString(event, index, 'characterString')
                              }
                              onFocus={event => {
                                event.target.placeholder = '';
                                this.setState({
                                  prevString: cloneDeep(deviceSearch[index].characterString),
                                  changeCharacter: true,
                                  currentIndex: index,
                                });
                              }}
                              onBlur={event => {
                                const { prevString } = this.state;
                                event.target.placeholder =
                                  deviceSearch[index].field === 'dataCollectionTime'
                                    ? 'yyyy/mm/dd hh:mm'
                                    : '';
                                if (prevString !== event.target.value) {
                                  this.changeFilterCondition(event, index, 'characterString');
                                }
                              }}
                            />
                          </td>
                          <td>
                            <SBComboBox
                              id={`items${index}_condition1`}
                              name={`items${index}_condition1`}
                              options={
                                (deviceSearch[index].field ||
                                  deviceSearch[index].category === 'groupName') &&
                                this.setConditionOneCombo(
                                  deviceSearch[index].isCategoryChange
                                    ? deviceSearch[index].previousCharacterString
                                    : deviceSearch[index].characterString, index
                                )
                              }
                              filterKey="value"
                              filterValue="option"
                              style={{ width: 300 }}
                              value={deviceSearch[index].conditionOne}
                              onChange={event =>
                                this.changeFilterCondition(event, index, 'conditionOne')
                              }
                            />
                          </td>
                          <td>
                            <SBComboBox
                              id={`items${index}_condition2`}
                              name={`items${index}_condition2`}
                              onChange={event =>
                                this.changeFilterCondition(event, index, 'conditionTwo', true)
                              }
                              options={
                                index + 1 < 5 &&
                                deviceSearch[index].conditionOne &&
                                deviceComboData.conditionTwo
                              }
                              value={deviceSearch[index].conditionTwo}
                              filterKey="value"
                              filterValue="option"
                              style={{ width: 300 }}
                            />
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </td>
            </tr>
            <tr>
              <td className="tCenter">
                <button
                  type="button"
                  className="btn-accordion"
                  id="aPolicyMark"
                  style={removeAnchorProperty}
                  onClick={() => this.policyMarkClick()}
                >
                  {aPolicyMark}
                </button>
              </td>
              <td>{this.languageLabelCheck('search_by_policy_violation')}</td>
            </tr>
            <tr id="Policy_tr" style={{ display: policySearchVisibility }}>
              <td />
              <td>
                <table
                  id="Policy_table"
                  name="Policy_table"
                  className="table-yoko table-yoko-fc-30px"
                >
                  <tbody>
                    {deviceFilterCheckList.map(data => {
                      const [firstData, secondData] = data;
                      return (
                        <tr>
                          <td className="center w30px">
                            <label className="checkbox">
                              <input
                                className="checkbox-input"
                                type="checkbox"
                                name={firstData.label}
                                id={firstData.label}
                                checked={policySearch.indexOf(firstData.value) > -1}
                                onClick={event => this.onPolicySearchFilter(event, firstData.value)}
                              />
                              <span className="checkbox-parts" />
                            </label>
                          </td>
                          <td>{this.languageLabelCheck(firstData.label)}</td>
                          {secondData && [
                            <td className="center w30px">
                              <label className="checkbox">
                                <input
                                  className="checkbox-input"
                                  type="checkbox"
                                  name={secondData.label}
                                  id={secondData.label}
                                  checked={policySearch.indexOf(secondData.value) > -1}
                                  onClick={event =>
                                    this.onPolicySearchFilter(event, secondData.value)
                                  }
                                />
                                <span className="checkbox-parts" />
                              </label>
                            </td>,
                            <td>{this.languageLabelCheck(secondData.label)}</td>,
                          ]}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { data: localeData } = state.language;
  return { localeData };
};

export default connect(mapStateToProps)(MultipleConditionTable);
