/**
 * SecurityPolicySearch is used to perform admin search operation
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import SearchFilter from '../../../../v1/common/component/SearchFilter';

/**
 * searchFilterProps is used to load the components
 * @param reference
 */

function searchFilterProps(reference, value) {
  return [
    {
      id: 'search-keyword',
      type: 'text',
      key: 'searchKeyword',
      defaultValue: value,
      style: { marginRight: '10px' },
      ref: reference,
      maxLength: 20,
    },
    {
      id: 'search-button',
      type: 'button',
      color: 'green',
      title: 'search',
      key: 'searchButton',
    },
  ];
}

const SecurityPolicySearch = (props) => {
  const { onSearchAction, onKeyDown, reference, defaultValue } = props;
  return (
    <div className="contents-main mb15">
      <SearchFilter
        border
        searchOptions={searchFilterProps(reference, defaultValue)}
        onSearchAction={(key, event, values) => onSearchAction(key, event, values)}
        onKeyDown={(event, value) => onKeyDown(event, value)}
      />
    </div>
  );
};
export default SecurityPolicySearch;
