/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementAdd from '../component/GroupManagementAdd';
import GroupManagementGrid from '../component/GroupManagementGrid';
import GroupManagementSearch from '../component/GroupManagementSearch';
import GroupManagementFooter from '../component/GroupManagementFooter';

/** Utility Files */
import { Role } from '../../constants/Role';
import { post } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import { resetGroupInformation, makeGroupInformation } from '../../actions/GroupAction';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import { groupManagementHeader } from '../constants/groupSettingHeaderConstants';
import { serviceMapper } from '../common/GroupSettingCommonFunctions';
import { messageConstants } from '../constants/groupSettingConstants';

/**
 * GroupManagement is to display list of group information
 * @author Aswin Palanisamy
 */

const resultPerPageKey = 'group_management';
class GroupManagement extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey } = props;
    this.state = {
      screenKey,
      message: '',
      groupList: [],
      disable: true,
      pageLoading: true,
      addDisable: false,
      changeDisable: false,
      gridLoading: false,
      selectedRowValue: {},
      countInfoContents: 0,
      openErrorDialog: false,
      dialogHeader: 'success',
      openConfirmDialog: false,
      searchParam: { groupName: '' },
      authorityRoleType: Role.group1,
      headerData: groupManagementHeader(screenKey),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { setModified } = this.props;
    this.setState({ gridLoading: true }, () => this.getGroup());
    setModified(false);
  }

  /**
   * componentWillUnmount performs group redux state reset operation
   */
  componentWillUnmount() {
    const { resetGroup } = this.props;
    resetGroup();
  }

  /**
   * getGroup is to load list of group records
   * @param groupName
   * @param isSearch
   */
  getGroup = async (groupName, isSearch) => {
    const searchParam = { groupName: (groupName || '').trim() };
    const response = await post(APIENDPOINTS.GETGROUP, searchParam);
    const result = await response.json();
    const { status, message, data: groupDataList } = result;
    if (status === 'OK') {
      const { groupList, selectedRowValue } = this.setGridDataIndex(groupDataList, isSearch);

      this.setState(
        {
          groupList,
          searchParam,
          selectedRowValue,
          pageLoading: false,
          gridLoading: false,
          addDisable: message,
          disable: isEmpty(groupList),
          countInfoContents: groupList.length,
        },
        () => {
          if (!isSearch && this.searchRef.current) this.searchRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        addDisable: true,
        pageLoading: false,
        gridLoading: false,
        openErrorDialog: true,
        dialogHeader: 'error',
      });
    }
  };

  /**
   * setGridDataIndex is to set grid row indexes
   * @param gridList
   * @param isSearch
   * @return dataList
   */
  setGridDataIndex = (gridList = [], isSearch) => {
    let matchIndex = -1;
    const groupFilterList = [];
    const { screenKey, selectedRowValue } = this.state;
    const defaultSetting = screenKey === 'defaultGroupSettingChange';
    const groupList = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;
      if (
        defaultSetting &&
        ((!isSearch && record.groupType === 'default') ||
          record.groupId === selectedRowValue.groupId)
      ) {
        groupFilterList.push(record);
        matchIndex = index;
      }
      return record;
    });
    if (matchIndex > -1) groupList.unshift(groupList.splice(matchIndex, 1)[0]);
    const [record] = defaultSetting ? groupFilterList : groupList;
    return { selectedRowValue: record || selectedRowValue, groupList };
  };

  /**
   * handleRadioChange performs radio button change operation in grid
   * @param selectedRowValue
   */
  handleRadioChange = (selectedRowValue) => {
    this.setState({ selectedRowValue, changeDisable: false });
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    const { screenKey, selectedRowValue } = this.state;
    const [record] = tableData;
    this.setState({
      selectedRowValue:
        screenKey === 'groupManagement' && !isEmpty(tableData) ? record : selectedRowValue,
    });
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, value) => {
    if (event.keyCode === 13)
      this.setState({ gridLoading: true }, () => this.getGroup(value, true));
  };

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param event
   * @param value
   */
  handleSearchAction = (key, event, value) => {
    const searchActionMapper = {
      searchButton: () =>
        this.setState({ gridLoading: true }, () => this.getGroup(value.searchKeyword, true)),
    };
    return searchActionMapper[key] && searchActionMapper[key]();
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param key
   */
  onHandleClick = (key) => {
    const { selectedRowValue } = this.state;
    const { history, location, resetGroup, makeGroupData } = this.props;
    if (key === 'addDefaultGroup') {
      this.setState({ openConfirmDialog: true });
    } else if (key === 'delete' && selectedRowValue.groupType === 'default') {
      this.setState({
        message: 'cannot_delete_default_group',
        openErrorDialog: true,
        dialogHeader: 'error',
        pageLoading: false,
      });
    } else if (key === 'addCopy' && !isEmpty(selectedRowValue.scheduledTime)) {
      this.setState({
        message: 'cannot_copy_schedule_group',
        openErrorDialog: true,
        dialogHeader: 'error',
        changeDisable: true,
        selectedRowValue,
      });
    } else {
      resetGroup();
      makeGroupData({ groupId: key !== 'add' ? selectedRowValue.groupId : -1 });
      history.push(serviceMapper(key), {
        parentScreen: location.pathname,
      });
    }
  };

  /**
   * setSelectedRecord is to select default grid index
   * @return selectedIndex
   */
  setSelectedRecord = () => {
    const { groupList, selectedRowValue, screenKey } = this.state;
    return screenKey === 'groupManagement'
      ? [0]
      : groupList
          .filter((data) => selectedRowValue.groupId === data.groupId)
          .map((record) => record.id);
  };

  /**
   * changeDefaultGroup performs default group change operation
   * @return GroupManagement
   */
  changeDefaultGroup = async () => {
    const { selectedRowValue } = this.state;
    const response = await post(APIENDPOINTS.CHANGEDEFAULTGROUP, selectedRowValue);
    const result = await response.json();
    const { status, message } = result;
    this.setState({
      message,
      dialogHeader: status === 'OK' ? 'success' : 'error',
      openConfirmDialog: false,
      openErrorDialog: true,
      pageLoading: false,
    });
  };

  /**
   * handleSelectChange performs device grid rendering
   */
  handleSelectChange = () => {
    const searchKey = document.getElementById('search-keyword').value;
    this.setState({ gridLoading: true }, () => this.getGroup(searchKey, false));
  };

  /**
   * render performs group management screen rendering
   * @return GroupManagement
   */
  render() {
    const {
      message,
      disable,
      groupList,
      screenKey,
      headerData,
      addDisable,
      pageLoading,
      gridLoading,
      searchParam,
      dialogHeader,
      changeDisable,
      openErrorDialog,
      openConfirmDialog,
      countInfoContents,
      authorityRoleType,
    } = this.state;
    const { history } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        {screenKey === 'groupManagement' && (
          <GroupManagementAdd
            buttonKey="add"
            id="registerButton"
            disabled={addDisable}
            onHandleClick={(key) => this.onHandleClick(key)}
          />
        )}
        <GroupManagementSearch
          screenKey="groupManagement"
          reference={this.searchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, event, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '70%', left: 'calc(60% - 55px)' }}
        >
          <GroupManagementGrid
            history={history}
            gridList={groupList}
            disable={addDisable}
            screenKey={screenKey}
            searchParam={searchParam}
            countInfoContents={countInfoContents}
            defaultSelectedKeys={this.setSelectedRecord()}
            handleRadioChange={(event) => this.handleRadioChange(event)}
            rowKey={screenKey === 'defaultGroupSettingChange' && 'id'}
            handleSelectChange={() => this.handleSelectChange()}
            handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
            countInfo={
              screenKey === 'groupManagement' ? 'registered_count_items' : 'select_result_count'
            }
            defaultSelected={resultPerPageKey}
          />
          <GroupManagementFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            footer={{
              disable,
              screenKey,
              addDisable,
              changeDisable,
              authorityRoleType,
            }}
          />
        </CircularSpinner>
        <SBDialog
          type="confirmation"
          header="save_confirmation"
          content={messageConstants[2]}
          open={openConfirmDialog}
          onOk={() => this.setState({ pageLoading: true }, () => this.changeDefaultGroup())}
          onCancel={() => {
            this.setState({ openConfirmDialog: false });
          }}
          width="400px"
        />
        <SBDialog
          type={dialogHeader}
          header={dialogHeader}
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              if (screenKey === 'defaultGroupSettingChange')
                history.push(serviceMapper('addDefaultGroup'));
              else if (this.searchRef.current) this.searchRef.current.focus();
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    resetGroup: () => dispatch(resetGroupInformation()),
    setModified: (data) => dispatch(commonChangeAction(data)),
    makeGroupData: (data) => dispatch(makeGroupInformation(data)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
GroupManagement.propTypes = {
  screenKey: PropTypes.string.isRequired,
  resetGroup: PropTypes.func.isRequired,
  makeGroupData: PropTypes.func.isRequired,
  setModified: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
  }).isRequired,
};

export default connect(null, mapDispatchToProps)(withRouter(GroupManagement));
