/** Base Files */
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/** SBComponent */
import SBDialog from '../../common/component/sbdialog/SBDialog';

/** GroupManagement Components */
import GroupManagementGrid from './GroupManagementGrid';

/** Utility File */
import checkLanguage from '../../language/checkLanguage';

/**
 * GroupManagementIndicator performs selected device dialog rendering
 * @param props
 * @author Aswin Palanisamy
 */
const GroupManagementIndicator = ({
  width,
  dialogKey,
  localeData,
  gridHeight,
  notMultiLang,
  dialogContent,
  isNotMultiLang,
  hasHeightScroll,
  openDeviceDialog,
  closeDeviceDialog,
}) => {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  const languageLabelCheck = (label) => {
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * errorContent used to render csv validationerror
   */
  const csvErrorContent = () => {
    return (
      <div>
        <ul style={{ listStyleType: 'disc' }}>
          {dialogContent.map((errroData) => (
            <li style={{ padding: 4, listStyle: 'inside' }}>{errroData}</li>
          ))}
        </ul>
      </div>
    );
  };

  return (
    <SBDialog
      width={width}
      type="infoDialog"
      open={openDeviceDialog}
      isNotMultiLang={notMultiLang}
      onOk={() => closeDeviceDialog()}
      okButtonTitle={languageLabelCheck('ok')}
      header={languageLabelCheck(
        dialogKey === 'deviceIndicator' ? 'selected_device' : 'csv_upload_errors',
      )}
      content={
        dialogKey === 'deviceIndicator' ? (
          <GroupManagementGrid
            restrictPagination
            gridHeight={gridHeight}
            gridList={dialogContent}
            screenKey="deviceIndicator"
            countInfo="select_result_count"
            hasHeightScroll={hasHeightScroll}
            filterMessage="display_items_colon"
            countInfoContents={dialogContent.length}
          />
        ) : (
          csvErrorContent()
        )
      }
    />
  );
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  return { localeData: state.language.data };
};

/**
 * propTypes is used for validating props with its type.
 */
GroupManagementIndicator.propTypes = {
  gridHeight: PropTypes.string,
  isNotMultiLang: PropTypes.bool,
  hasHeightScroll: PropTypes.bool,
  width: PropTypes.string.isRequired,
  notMultiLang: PropTypes.bool.isRequired,
  dialogKey: PropTypes.string.isRequired,
  openDeviceDialog: PropTypes.bool.isRequired,
  closeDeviceDialog: PropTypes.func.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string),
  dialogContent: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
GroupManagementIndicator.defaultProps = { localeData: {}, isNotMultiLang: false, hasHeightScroll: false, gridHeight: ''};

export default connect(mapStateToProps)(GroupManagementIndicator);
