/**
 * MultipleAdminFooter is used to load the grid footer buttons
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import checkLanguage from '../../language/checkLanguage';
import SBButton from '../../common/component/sbbutton/SBButton';
import RoleResolver from '../../common/component/guard/RoleResolver';
import { Role } from '../../constants/Role';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

class MultipleAdminFooter extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * renderFooterButton is used to render button components
   * @param footerButtonProps
   * @return sbbutton
   */

  renderFooterButton = (footerButtonProps) => {
    const { onHandleClick } = this.props;
    return (
      <RoleResolver authorityRoleType={footerButtonProps.authorityRoleType}>
        <SBButton
          {...footerButtonProps}
          type="button"
          onClick={() => onHandleClick(footerButtonProps.key)}
        />
      </RoleResolver>
    );
  };

  /**
   * render is used to render the footer component
   * @return component
   */

  render() {
    const { userList, tenantVo, register, countInfoContents, deleteDisable } = this.props;
    const showBulkFunction = false;
    return (
      <>
        <div className="clearfix">
          {showBulkFunction && (
            <div className="floatleft">
              {this.renderFooterButton({
                authorityRoleType: Role.customer,
                id: 'btn_csv_output',
                color: 'gray',
                title: 'csv_output',
                disabled: userList.length === 0,
                key: 'csvOutput',
                noMarginRight: false,
              })}
              {this.renderFooterButton({
                authorityRoleType: Role.multipleAdminRegisterAndChange,
                id: 'btn_csv_batch_operation',
                color: 'gray',
                title: 'csv_batch_operation',
                disabled: countInfoContents === tenantVo.userCount || register,
                key: 'csvBatchOperation',
                noMarginRight: false,
              })}
            </div>
          )}
          <div className="floatright">
            {this.renderFooterButton({
              authorityRoleType: Role.multipleAdminRegisterAndChange,
              id: 'btn_register',
              color: 'green',
              title: 'registration',
              disabled: countInfoContents === tenantVo.userCount || register,
              key: 'registration',
              noMarginRight: false,
            })}
            {this.renderFooterButton({
              authorityRoleType: Role.multipleAdminRegisterAndChange,
              id: 'btn_change',
              color: '',
              title: 'change',
              disabled: userList.length === 0,
              key: 'change',
              noMarginRight: false,
            })}
            {this.renderFooterButton({
              authorityRoleType: Role.multipleAdminDelete,
              id: 'btn_delete',
              color: '',
              title: 'delete',
              disabled: userList.length === 0 || deleteDisable,
              key: 'delete',
              noMarginRight: true,
            })}
          </div>
        </div>
      </>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  userList: PropTypes.arrayOf(PropTypes.object),
  tenantVo: commonPropTypes().history.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  countInfoContents: PropTypes.number.isRequired,
  register: PropTypes.bool,
  deleteDisable: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminFooter.defaultProps = {
  isNotMultiLang: false,
  userList: [],
  register: false,
  deleteDisable: true,
};

export default connect(mapStateToProps)(withRouter(MultipleAdminFooter));
