import React, { Component } from "react";
import PropTypes from "prop-types";
import _ from "lodash";

export default class DashboardStackedBar  extends Component {
  constructor(props) {
    super(props);
    const{data} = this.props;
    this.state = {
      listBars: [],
      data
    };
    this.onClick = this.onClick.bind(this);
  }


  componentDidMount() {
    const{data} = this.props;
    this.setState(state => ({
      ...state,
      listBars: this.getListBarWithOtherParameters(),
      data
    }));
  }


  componentDidUpdate(prevProps) {
    const{data} = this.props;
    if (!_.isEqual(this.props, prevProps)) {
      this.setState(state => ({
        ...state,
        listBars: this.getListBarWithOtherParameters(),
        data
      }));
    }
  }

   /**
   * OnClick Event
   */
   onClick(evt, bar) {
       const {onClick} = this.props;
    Object.assign(evt, { bar });
    if (onClick) {
      onClick(evt);
    }
  }

  

  /**
   * Returns the same list of bars with position and barWidth
   */
  getListBarWithOtherParameters() {
      const {outlineWidth,data} = this.props;
    const widthTotal = this.calcWidthTotal();
    let position = (outlineWidth * 2) / 100;
    let barWidth = 0;
    const listBars = data.map(bar => {
      position += barWidth;
      barWidth =
        (bar.value * 100) / widthTotal -
        (outlineWidth * 100) / bar.value / 100;
      bar = {
        position, barWidth, widthTotal,
        ...bar
      };
      return bar;
    });
    return listBars;
  }

 



  /**
   * Returns a list of texts of the bars into a div component
   */
  getListTextBar(showTextInsteadValue) {
    const { showTextWithValue } = this.props;
    const {listBars} = this.state;
    const listText = listBars.map((bar) => {
      return (
        <div
          key='bar'
          style={{
            position: "relative",
            float: "left",
            width: `${bar.barWidth}%`,
            fontSize: "90%"
          }}
        >
          {showTextInsteadValue && bar.name}
          {showTextInsteadValue && bar.name && showTextWithValue ? ": " : ""}
          {(!showTextInsteadValue || showTextWithValue) &&
            (bar.description || bar.value || "")}
        </div>
      );
    });
    return listText;
  }

  /**
   * Make the calculus of total width
   */
   calcWidthTotal() {
       const {data} = this.props;
    let widthTotal = 0;
    data.forEach(bar => {
      widthTotal += bar.value;
    });
    return widthTotal;
  }
  


  renderBars() {
    const bars = [];
    const { showTextWithValue, showTextIn, showValueIn } = this.props;
    const {listBars} = this.state;
    const{height,outlineWidth,data,outlineColor,fontColor} = this.props;

    bars.push(
      listBars.map((bar) => {
        return (
          <g key='key' onClick={e => this.onClick(e, bar)}>
            <rect
              width={`${bar.barWidth + 0.1}%`}
              height={height}
              style={{
                fill: bar.color ,
                strokeWidth: `${(outlineWidth * 100) /
                  (bar.widthTotal / data.length)}%`,
                stroke: outlineColor
              }}
              x={`${bar.position}%`}
            />
            {(showTextIn || showValueIn) && (
              <text
                style={{ fill:fontColor, fontSize: "100%", fontWeight: 'bold' }}
                x={bar.position === 0 ? `${((bar.barWidth + 0.1)/2)}%`: `${((bar.barWidth + 0.1)/2)+(bar.position)}%`}
                y="50%"
                dy="0.35em"
              >
                {showTextIn && bar.name}
                {bar.name && showTextIn ? ": " : ""}
                {(showValueIn || showTextWithValue) &&
                  (bar.description || bar.value || "1")}
              </text>
            )}
            <title>
              {`${bar.name || ""}${
              bar.name ? ": " : ""
            }${bar.description || bar.value || "1"}`}
            </title>
          </g>
        );
      })
    );
    return bars;
  }

  renderLabel(showTextInsteadValue) {
      const {id} = this.props;
    return (
      <div
        id={`${id}_text`}
        style={{
          textAlign: "left",
          display: "flex",
          width: "100%"
        }}
      >
        {this.getListTextBar(showTextInsteadValue)}
      </div>
    );
  }

  render() {
      const {showTextUp,showValueUp,id,height,showValueDown,showTextDown} = this.props;
    return (
      <>
        {(showTextUp || showValueUp) &&
          this.renderLabel(showTextUp)}
        <svg id={id} width="100%" height={height}>
          {this.renderBars()}
        </svg>
        {(showTextDown || showValueDown) &&
          this.renderLabel(showTextDown)}
      </>
    );
  }
}

DashboardStackedBar.propTypes = {
  id: PropTypes.string,
  height: PropTypes.number,
  showTextIn: PropTypes.bool,
  showTextUp: PropTypes.bool,
  showTextDown: PropTypes.bool,
  showTextWithValue: PropTypes.bool,
  showValueIn: PropTypes.bool,
  showValueUp: PropTypes.bool,
  showValueDown: PropTypes.bool,
  fontColor: PropTypes.string,
  outlineWidth: PropTypes.number,
  outlineColor: PropTypes.string
};

DashboardStackedBar.defaultProps = {
  height: 30,
  showTextIn: false,
  showTextUp: false,
  showTextDown: false,
  showTextWithValue: true,
  showValueIn: false,
  showValueUp: false,
  showValueDown: false,
  outlineWidth: 0,
  outlineColor: "black",
  fontColor: "white",
  id: "hsbar"
};