import React from 'react';
import { withRouter,Link } from 'react-router-dom';
import isUndefined from 'lodash/isUndefined';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';


const linkStyle = {
  color: '#FFFFFF',
  textDecoration: 'underline',
  cursor: 'pointer',
  background: 'none',
  font: 'inherit',
  padding: 0,
  border: 'none',
  letterSpacing: 0,
};
const languageLabelCheck = (label, props) => {
  const { isNotMultiLang, localeData ,dynamicLinkContent} = props;
  if(isNotMultiLang) return label;
   return isUndefined(dynamicLinkContent) ? checkLanguage(localeData,label)
   :addDynamicContent(checkLanguage(localeData,label),dynamicLinkContent);
};

const DashboardTableHeader = props => {
  const { hyperLinkText,hyperLink,dynamicLinkContent,text } = props;

  const handleClick = () => {
    const {onClick } = props;
    if(onClick) onClick();
  };

  return (
    <>
      { dynamicLinkContent ?( 
        <h2 className="title-corner mb15">
          <font style={{verticalAlign: "inherit;"}}>
            <font style={{verticalAlign: "inherit;"}}>
              {languageLabelCheck(text, props)}  
              {'  '}
              <Link to={hyperLink} style={linkStyle} onClick={() => handleClick()}>{languageLabelCheck(hyperLinkText, props)}</Link>
            </font>
          </font>
        </h2>
):(
  <h2 className="title-corner mb15">
    <Link to={hyperLink} style={linkStyle} onClick={() => handleClick()}>
      <font style={{verticalAlign: "inherit;"}}>
        <font style={{verticalAlign: "inherit;"}}>{languageLabelCheck(hyperLinkText, props)}</font>
      </font>
    </Link>
  </h2>
)
    }
    </>
  );
};
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(withRouter(DashboardTableHeader));
