/**
*DashboardGuageCircle is used to generate the customised gaugecircle based on tha data.
*@author Vijayavel Mohan
 */

import React from 'react';
import SemiCircleProgressBar from "react-progressbar-semicircle";


const DashboardGuageCircle = (props) => {
  const{value,totalDevice} = props;

   
        return(
          <>
            <SemiCircleProgressBar
              diameter={175}
              stroke='#88b44e'
              strokeWidth={15} 
              percentage={(value * 100)/totalDevice}
            />
            <span style={{marginLeft: '9px'}}>0</span>
            <span style={{marginLeft: '136px'}}>{totalDevice}</span>
            <div className="dashboard-icon-device">
              <img src="https://stara.bizconcier-dm.com/wide_img/icon-android.png" alt="" />
            </div>
            <div className="dashboard-device-data">
              <div style={{marginRight: '10px'}}>
                <dl>
                  <dt>
                    <font style={{verticalAlign: 'inherit'}}>
                      <font style={{verticalAlign: 'inherit'}}>Android</font>
                    </font>
                  </dt>
                  <dd>
                    <span style={{fontSize: '28px'}}>
                      <font style={{verticalAlign: 'inherit'}}>
                        <font style={{verticalAlign: 'inherit'}}>{value}</font>
                      </font>
                    </span>
                    <font style={{verticalAlign: 'inherit'}}>
                      <font style={{verticalAlign: 'inherit'}}>
                        /
                        {totalDevice}
                      </font>
                    </font>
                  </dd>
                </dl>
              </div>
            </div>
                                                    
          </>
        );
}
export default DashboardGuageCircle;