import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../../../../css/Style.css';
import descriptionConverter from '../../utils/descriptionConverter';
import checkLanguage from '../../../language/checkLanguage';

class SBScrollToTopBtn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isVisible: false,
    };
  }

  componentDidMount() {
    const scrollComponent = window.$('.ant-layout-content')[0];
    const scrollThis = this;
    scrollComponent.addEventListener('scroll', function() {
      scrollThis.toggleVisibility(scrollThis);
    });
  }

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  toggleVisibility = scrollThis => {
    const main = window.$('.ant-layout-content')[0];
    if (window.$(main).scrollTop() >= 80) {
      scrollThis.setState({
        isVisible: true,
      });
    } else {
      scrollThis.setState({
        isVisible: false,
      });
    }
  };

  scrollToTop = () => {
    const main = window.$('.ant-layout-content')[0];
    window.$(main).animate(
      {
        scrollTop: 0,
      },
      'smooth',
    );
  };

  render() {
    const { isVisible } = this.state;
    return (
      isVisible && (
        <div className="mt75">
          <a href onClick={() => this.scrollToTop()} onKeyPress={() => this.scrollToTop()}>
            <p id="page-top">
              <a href>{descriptionConverter(this.languageLabelCheck('back_to_page_top'))}</a>
            </p>
          </a>
        </div>
      )
    );
  }
}

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
  null,
)(SBScrollToTopBtn);
