import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import SBTableLayout from '../common/component/tableLayout/SBTableLayout';
import SBCombobox from '../common/component/combobox/SBComboBox';
import SBContentHeader from '../common/component/contentheader/SBContentHeader';
import SBButton from '../common/component/sbbutton/SBButton';
import languageChangerAction from '../language/languageActions';
import APIEndPoints from '../constants/APIEndPoints';
import { get, post } from '../actions/commonActions';
import getUserEntity from '../actions/UserActions';
import getLanguageData from '../language/languageUtils';
import CircularSpinner from '../common/component/loadingSpinner/CircularSpinner';
import commonChangeAction from '../common/changeReducer/commonChangeAction';
import checkLanguage from '../language/checkLanguage';
import { CommonConstants } from '../constants/CommonConstants';

/**
 * LanguageTimezone is to change langauage and timezone of current user.
 * @author Gobi R
 */
class LanguageTimezone extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { viewMode, currentLanguage, timezoneCode } = this.props;
    this.state = {
      language: currentLanguage,
      timeZone: timezoneCode,
      timeOptions: [],
      languageOptions: [],
      loading: true,
      stage: viewMode ? 1 : 2,
      isChanged: false,
    };
    this.changeRef = createRef();
    this.confirmRef = createRef();
    this.okRef = createRef();
  }

  /**
   * componentDidMount is to fetch combos data.
   */
  componentDidMount = async () => {
    await this.loadCombos();
  };

  /**
   * loadCombos is to fetch language and timezone combo data.
   */
  loadCombos = async () => {
    const getAllTimeZone = await get(APIEndPoints.ALLTIMEZONE);
    const langugeAndTimeData = await getAllTimeZone.json();
    this.mapComboOptions(langugeAndTimeData.languageList, 'languageOptions');
    this.mapComboOptions(langugeAndTimeData.timezoneList, 'timeOptions');
    if (Object.keys(langugeAndTimeData).length > 0) {
      this.setState({ loading: false }, () => this.setFocus());
    }
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, languageData } = this.props;
    return isNotMultiLang ? label : checkLanguage(languageData, label);
  };

  /**
   * mapComboOptions is to change automatic data content.
   * @param comboDataList
   * @param comboType
   */
  mapComboOptions = (comboDataList, comboType) => {
    const options = comboDataList.map((option) => {
      return {
        key: option.timeZoneCode,
        values:
          option.timeZoneCode === '0' ? this.languageLabelCheck('automatic') : option.timeZoneName,
      };
    });
    this.setState({ [comboType]: options });
  };

  /**
   * onComboChange is to handle combo change.
   * @param event
   */
  onComboChange = (event) => {
    this.setState({
      [event.target.id === 'sel_language' ? 'language' : 'timeZone']: event.target.value,
      isChanged: true,
    });
  };

  /**
   * setFocus is to set focus to components based on differant modes.
   */
  setFocus = () => {
    const { stage } = this.state;
    switch (stage) {
      case 1:
        this.changeRef.current.focus();
        break;
      case 2:
        this.confirmRef.current.focus();
        break;
      default:
        this.okRef.current.focus();
    }
  };

  /**
   * languageChangerHandler is to load initial data.
   */
  languageChangerHandler = () => {
    this.setState({ loading: true }, () => {
      this.languageChanger(true);
    });
  };

  /**
   * languageChanger is to load data.
   * @param initial
   */
  languageChanger = async (initial) => {
    const { language, timeZone } = this.state;
    const {
      currentLanguage,
      languageData,
      changeLanguage,
      getUserEntity: getLoggedUser,
      timezoneCode,
      setModified,
    } = this.props;
    const changer = await post('changeLanguage', {
      languageCode: language,
      timezoneCode: timeZone,
    });
    const data = await changer.json();
    if (data.status) {
      setModified(false);
      this.setState({ stage: 1, isChanged: false });
      let languageMap;
      if (timezoneCode !== timeZone) {
        const userDetailResponse = await get(APIEndPoints.USERDETAIL);
        const userDetailData = await userDetailResponse.json();
        getLoggedUser(userDetailData.userVo);
      }
      if (currentLanguage !== language) {
        languageMap = await getLanguageData(language);
      } else {
        languageMap = { ...languageData };
      }
      changeLanguage(language, timeZone, languageMap);
      document.title = this.languageLabelCheck('application_title');
      if (initial) {
        this.loadCombos();
      } else {
        this.setState({ loading: false });
      }
    } else {
      this.setState({ loading: false });
    }
  };

  /**
   * appendStyle is to add style to combo on view and edit mode.
   */
  appendStyle = () => {
    const { stage } = this.state;
    return stage === 3 ? { border: 'none', background: 'none' } : {};
  };

  /**
   * tableData is to fetch metadata for combos.
   * @returns array
   */
  tableData = () => {
    const { languageOptions, language, timeOptions, timeZone, stage } = this.state;
    return [
      {
        header: 'language',
        contentarea: (
          <SBCombobox
            id="sel_language"
            options={languageOptions}
            filterKey="key"
            isNotMultiLang
            value={language}
            canModify
            ref={this.confirmRef}
            onChange={this.onComboChange}
            style={this.appendStyle()}
            filterValue="values"
            disabled={stage !== 2}
          />
        ),
      },
      {
        header: 'time_zone',
        contentarea: (
          <SBCombobox
            id="sel_timezone"
            options={timeOptions}
            filterKey="key"
            filterValue="values"
            isNotMultiLang
            canModify
            value={timeZone}
            style={this.appendStyle()}
            onChange={this.onComboChange}
            disabled={stage !== 2}
          />
        ),
      },
    ];
  };

  /**
   * render is to load fields.
   * @returns jsx elements
   */
  render() {
    const { screenTitleData, currentLanguage, timezoneCode } = this.props;
    const { loading, stage, isChanged } = this.state;
    return (
      <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader title={screenTitleData.title} description={screenTitleData.description} />
        <SBTableLayout tableData={this.tableData()} filterKey="header" filterValue="contentarea" />
        <div className="floatright">
          {stage === 1 ? (
            <SBButton
              type="button"
              id="change"
              color="green"
              name="change"
              noMarginRight
              ref={this.changeRef}
              title="change"
              onClick={() => this.setState({ stage: 2 }, () => this.setFocus())}
            />
          ) : (
            <div>
              <SBButton
                type="button"
                id="btn_ok"
                color="green"
                name="btn_ok"
                className="ml10"
                ref={this.okRef}
                title={stage === 2 ? 'confirm' : 'ok'}
                onClick={
                  stage === 2
                    ? () => this.setState({ stage: 3 }, () => this.setFocus())
                    : this.languageChangerHandler
                }
                disabled={!isChanged}
              />
              <SBButton
                type="button"
                id="cancel"
                name="cancel"
                noMarginRight
                canModify={stage === 2}
                title="cancel"
                onClick={
                  stage !== 2
                    ? () => this.setState({ stage: 2 })
                    : () => {
                        this.setState({
                          language: currentLanguage,
                          timeZone: timezoneCode,
                          stage: 1,
                          isChanged: false,
                        });
                      }
                }
              />
            </div>
          )}
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapPropsFromState is to map store fields to props.
 * @param state
 */
const mapPropsFromState = (state) => {
  return {
    languageData: state.language.data,
    currentLanguage: state.language.current,
    timezoneCode: state.language.timezoneCode,
  };
};

/**
 * mapDispatchToProps is to map store actions to props.
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
    getUserEntity: (data) => {
      dispatch(getUserEntity(data));
    },
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(mapPropsFromState, mapDispatchToProps)(withRouter(LanguageTimezone));
