import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';

class NoResultsFound extends PureComponent {
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const { noDataLabel } = this.props;
    return (
      <div className="clearfix mb10">
        {this.languageLabelCheck(noDataLabel || 'no_results_found')}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(NoResultsFound);
