/**
 * MultipleAdminUpdateOperation is used to peform update oepration
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { filterData, setInitialValues, tableList } from './MultipleAdminSettingConstants';
import getUserEntity from '../../actions/UserActions';
import { getTenantEntity } from '../../actions/TenantActions';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { get, post, put } from '../../actions/commonActions';
import headerInformation from './MultipleAdminHeaderConstants';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import APIENDPOINTS from '../../constants/APIEndPoints';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import MultipleAdminCRUDFooter from './MultipleAdminCRUDFooter';
import { fetchLanguageList, fetchTimezoneList } from '../../actions/FieldsAction';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import MultipleAdminCountHeader from './MultipleAdminCountHeader';
import {
  initializeRefs,
  setFocusinerrorField,
  validateInputs,
} from './MultipleAdminCommonFunctions';
import languageChangerAction from '../../language/languageActions';
import getLanguageData from '../../language/languageUtils';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import { isNotUndefinedOrNotEmpty } from '../../common/CommonValidations/CommonValidations';

class MultipleAdminUpdateOperation extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('update'),
      fieldDataList: [],
      dataSource: setInitialValues(param),
      usersLength: 0,
      pageLoading: true,
      modalIsOpen: false,
      errorMessage: '',
      restrictSave: false,
      footer: { okDisable: true, primaryVisibile: true },
      dynamicData: {},
      reference: initializeRefs(),
      tempUserVo: {},
      updateRedux: false,
    };
  }

  /**
   * componentDidMount is used to load the user length data
   */

  componentDidMount() {
    const {
      fetchTimezoneList: getTimeZoneList,
      fetchLanguageList: getLanguageList,
      getUserEntity: getLoggedUser,
      getTenantEntity: getTenantDetail,
      timeZoneVo,
    } = this.props;
    const { dynamicData, reference } = this.state;
    const { dataSource } = this.state;
    let { usersLength } = this.state;
    const generateComponent = async () => {
      if (!timeZoneVo.length) {
        const timeZoneResponse = await get(APIENDPOINTS.TIMEZONELIST);
        const timeZoneList = await timeZoneResponse.json();
        getTimeZoneList(timeZoneList);
      }
        const languageResponse = await post(APIENDPOINTS.LANGUAGECODELIST, { searchedValue: '' });
        const languageList = await languageResponse.json();
        getLanguageList(languageList.filter(data => data.enabled));
      const response = await get(APIENDPOINTS.USERLENGTH);
      const responseData = await response.text();
      usersLength = parseInt(responseData, 10);
      const userDetailResponse = await get(APIENDPOINTS.USERDETAIL);
      const userDetailData = await userDetailResponse.json();
      getLoggedUser(userDetailData.userVo);
      getTenantDetail(userDetailData.tenantVo);
      if (dataSource.authorityId === 7 || dataSource.authorityId === 9) {
        dynamicData.disableReport = true;
      }
      this.setState({ usersLength, pageLoading: false, dynamicData, dataSource });
      if (reference.authorityRef.current) reference.authorityRef.current.focus();
    };
    generateComponent();
  }

  /**
   * componentWillUnmount is used to update the redux data
   */

  componentWillUnmount() {
    const { getUserEntity: getLoggedUser } = this.props;
    const { tempUserVo, updateRedux } = this.state;
    if (updateRedux) {
      getLoggedUser(tempUserVo);
    }
  }

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { history,tenantVo } = this.props;
    const { dynamicData, reference } = this.state;
    switch (key) {
      case 'save':
        this.setState({
          modalIsOpen: this.validateData(),
          fieldDataList: tableList(dynamicData, reference,tenantVo),
          errorMessage: '',
        });
        setFocusinerrorField(dynamicData, reference);
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING);
        break;
      default:
        break;
    }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    const { dataSource, footer, restrictSave } = this.state;
    if (key === 'authorityId') {
      this.generateReportValues(key, event.target.value);
    } else {
      dataSource[key] =
        event.target.type === 'text' || key === 'report' ? value : event.target.value;
      footer.okDisable = restrictSave;
      this.setState({ dataSource, footer });
    }
  };

  /**
   * generateReportValues is used to generate report values
   * @param key
   * @param event
   * @param value
   *
   */

  generateReportValues = (key, value) => {
    const { dataSource, footer, restrictSave, dynamicData, reference } = this.state;
    let { fieldDataList } = this.state;
    const { tenantVo } = this.props;
    dataSource[key] = value;
    if (parseInt(value, 10) === 7 || parseInt(value, 10) === 9) {
      if (parseInt(value, 10) === 7) {
        dataSource.report = 1;
      } else {
        dataSource.report = 0;
      }
      dynamicData.disableReport = true;
    } else {
      dynamicData.disableReport = false;
    }
    fieldDataList = tableList(dynamicData, reference,tenantVo);
    footer.okDisable = restrictSave;
    this.setState({ dataSource, fieldDataList, footer, dynamicData });
  };

  /**
   * updateUserReduxData is used to update redux data
   * @param resultSet
   *
   */
  updateUserReduxData = async resultSet => {
    const { userVo, getUserEntity: getLoggedUser, changeLanguage, localeData } = this.props;
    let languageMap = { ...localeData };
    let { tempUserVo } = this.state;
    if (resultSet.userVo.languageCode !== userVo.languageCode) {
      languageMap = await getLanguageData(resultSet.userVo.languageCode);
    }
    changeLanguage(resultSet.userVo.languageCode, resultSet.userVo.timeZoneCode, languageMap);
    if (resultSet.userVo.authorityId === userVo.authorityId) {
      getLoggedUser(resultSet.userVo);
    } else {
      const { authorityId } = resultSet.userVo;
      tempUserVo = resultSet.userVo;
      tempUserVo.authorityId = userVo.authorityId;
      getLoggedUser(tempUserVo);
      tempUserVo.authorityId = authorityId;
      this.setState({ updateRedux: true, tempUserVo });
    }
  };

  /**
   * onOkDialog is used to perfom save operation
   *
   */

  onOkDialog = () => {
    const { userVo, setModified, history,tenantVo } = this.props;
    const { dataSource, footer, dynamicData, reference } = this.state;
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      dataSource.ipAddress = isNotUndefinedOrNotEmpty(dataSource.ipAddress) ? dataSource.ipAddress.trim() : null;
      const response = await put(APIENDPOINTS.EDITSETTING, dataSource);
      const resultSet = await response.json();
      if (resultSet.status) {
        if (resultSet.userVo.userId === userVo.userId) {
          this.updateUserReduxData(resultSet);
        }
        setModified(false);
        this.setState({
          modalIsOpen: false,
          dataSource: resultSet.userVo,
          pageLoading: false,
        });
        history.push(ROUTEENDPOINTS.MULTIPLEADMINFINISH, {
          userVo: resultSet.userVo,
          usersLength: resultSet.usersLength,
        });
      } else {
        let { fieldDataList, restrictSave } = this.state;
        footer.okDisable = true;
        restrictSave = false;
        if (resultSet.validationError) {
          const { validationError } = resultSet;
          if (validationError.loginError) {
            dynamicData.loginError = validationError.loginError;
            fieldDataList = tableList(dynamicData, reference,tenantVo);
            this.setState({
              modalIsOpen: false,
              restrictSave,
              footer,
              fieldDataList,
              pageLoading: false,
            });
            if (reference.loginIdRef.current) reference.loginIdRef.current.focus();
          } else {
            this.setState({
              modalIsOpen: false,
              restrictSave,
              footer,
              pageLoading: false,
              errorMessage: validationError.noUserError,
            });
            if (reference.cancelRef.current) reference.cancelRef.current.focus();
          }
        } else {
          history.push(ROUTEENDPOINTS.ERROR);
        }
      }
    };
    saveProcess();
  };

  /**
   * setComboValues is used to load the combo values
   * @return status
   *
   */

  setComboValues = () => {
    const { languageVo, timeZoneVo } = this.props;
    filterData.timeZoneVo = timeZoneVo;
    filterData.languageVo = languageVo;
    timeZoneVo.find((element, index) => {
      if (element.timeZoneCode === '0') {
        filterData.timeZoneVo[index].timeZoneName = this.languageLabelCheck('automatic');
      }
      return false;
    });
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */

  onKeyDown = event => {
    const { footer } = this.state;
    const { okDisable } = footer;
    if (event.keyCode === 13 && !okDisable) {
      event.preventDefault();
      this.onHandleClick('save');
    }
  };

  /**
   * validateData is used to validate the data
   */

  validateData() {
    const { dynamicData, dataSource } = this.state;
    const validationResult = validateInputs(dataSource, dynamicData, true);
    this.setState({
      dynamicData: validationResult,
    });
    return validationResult.status;
  }

  /**
   * render is used to render the component
   */

  render() {
    const {
      headerData,
      errorMessage,
      usersLength,
      fieldDataList,
      dataSource,
      modalIsOpen,
      footer,
      pageLoading,
      dynamicData,
      reference,
    } = this.state;
    const { tenantVo } = this.props;
    this.setComboValues();

    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <MultipleAdminCountHeader usersLength={usersLength} maxUsercount={tenantVo.userCount} />
            <div>
              <CustomTableFrame
                tableList={fieldDataList.length ? fieldDataList : tableList(dynamicData, reference,tenantVo)}
                dataSource={dataSource}
                defaultValue={{}}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
                onKeyDown={(event, value) => this.onKeyDown(event, value)}
              />
            </div>
            <div>
              <MultipleAdminCRUDFooter
                footer={footer}
                reference={reference}
                onHandleClick={key => this.onHandleClick(key)}
              />
            </div>
            <span className="red bold">{this.languageLabelCheck(errorMessage)}</span>
          </div>
          <SBDialog
            open={modalIsOpen}
            header="save_confirmation"
            type="confirmation"
            content="want_to_update_data"
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={() => {
              this.onOkDialog();
            }}
            width="400px"
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = dispatch => {
  return {
    getUserEntity: data => {
      dispatch(getUserEntity(data));
    },
    getTenantEntity: (data) => {
      dispatch(getTenantEntity(data));
    },
    fetchLanguageList: data => {
      dispatch(fetchLanguageList(data));
    },
    fetchTimezoneList: data => {
      dispatch(fetchTimezoneList(data));
    },
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { tenantVo } = state.tenant;
  const { userVo } = state.user;
  const { timeZoneVo, languageVo } = state.commonFields;
  return {
    userVo,
    tenantVo,
    timeZoneVo,
    languageVo,
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminUpdateOperation.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: commonPropTypes().userVo.isRequired,
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  fetchTimezoneList: PropTypes.func.isRequired,
  fetchLanguageList: PropTypes.func.isRequired,
  changeLanguage: PropTypes.func.isRequired,
  getUserEntity: PropTypes.func.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  languageVo: PropTypes.arrayOf(PropTypes.object).isRequired,
  timeZoneVo: PropTypes.arrayOf(PropTypes.object).isRequired,
  setModified: PropTypes.func.isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminUpdateOperation.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(MultipleAdminUpdateOperation));
