import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../common/component/tableLayout/SBTableLayout';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import SBComboBox from '../../common/component/combobox/SBComboBox';
import SBTextArea from '../../common/component/sbtextarea/SBTextArea';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { get, post } from '../../actions/commonActions';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import APIENDPOINTS from '../../constants/APIEndPoints';
import RouteEndPoints from '../../constants/RouteEndPoints';
import checkLanguage from '../checkLanguage';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import { CommonConstants } from '../../constants/CommonConstants';

/**
 * LanguageContentAdd is to add or update user language's content.
 * @author Gobi R
 */
class LanguageContentAdd extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const getData = props.location.state;
    this.state = {
      mode: getData ? getData.mode : 'ADD',
      languageCodeList: [],
      selectedRow: getData ? getData.selectedRow : {},
      open: false,
      isSaved: false,
      loading: true,
      labelErrorMsg: '',
      textAreaErrMsg: '',
      saveMsg: '',
      modalIsOpen: false,
      isChanged: false,
      dataNotExist:false
    };
    this.initialFocus = createRef();
    this.modifyFocus = createRef();
    this.errorFocus = createRef();
  }

  /**
   * componentDidMount is to fetch already existing language data.
   */
  componentDidMount() {
    const { mode, selectedRow } = this.state;
    if(!isEmpty(selectedRow)){
      const paramMap = { languageCode:selectedRow.code, searchedValue:selectedRow.label };
      this.setState({ loading: true });
      post(APIENDPOINTS.CONTENTLIST, paramMap)
      .then((response) => response.json())
      .then((data) => {
         if(data.length === 0){
            this.setState({
              dataNotExist:true,
              loading:false
            })
         }else{
          this.setState({
            selectedRow:data[0] || {},
            loading: false,
          },() => {
              this.modifyFocus.current.focus();
              this.modifyFocus.current.selectionStart = this.modifyFocus.current.defaultValue.length;
          });
         }
      });
    }
    get(APIENDPOINTS.LANGUAGECODES)
      .then((response) => response.json())
      .then((data) => {
        const index = data.indexOf('ja');
        if (index > -1) {
          data.splice(index, 1);
        }
        if (mode === 'ADD') {
          [selectedRow.code] = data;
        }
        let stateMap =  {
          languageCodeList: data,
          loading:false
        }
        if(mode !== 'ADD'){
          stateMap =  {
            languageCodeList: data
          }
        }

        this.setState({...stateMap},
          () => {
            if (mode === 'ADD') {
              this.initialFocus.current.focus();
            } 
          },
        );
      });
  }

  /**
   * onSaveConfirmation is to open save confirmation dialog.
   */
  onSaveConfirmation = () => {
    this.setState({ modalIsOpen: true });
  };

  /**
   * onSaveButtonClick is to save the language.
   */
  onSaveButtonClick = () => {
    const { selectedRow, mode } = this.state;
    this.setState({ loading: true, modalIsOpen: false });
    post(`${APIENDPOINTS.CONTENTADD}/${mode}`, selectedRow)
      .then((response) => response.text())
      .then((data) => {
        if(data === 'data_not_exists'){
          this.setState({
            dataNotExist:true,
            loading:false
          })
        }else if (data === 'Success') {
          this.setState({
            open: true,
            isSaved: true,
            saveMsg: 'saved_successfully',
            loading: false,
          });
        } else {
          this.setState({ open: true, isSaved: false, saveMsg: data, loading: false });
        }
      });
  };

  /**
   * onCancelButtonClick is to cancel the current process and navigate to previous screen.
   */
  onCancelButtonClick = () => {
    const { history } = this.props;
    history.push(RouteEndPoints.LANGUAGECONTENT);
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * onContentChange is to valid content field on change.
   * @param event
   * @param key
   */
  onContentChange = (event, key) => {
    const { selectedRow } = this.state;
    selectedRow[key] = event.target.value;
    this.setState({ selectedRow, isChanged: true });
    if (key === 'label') {
      const regex = /^[a-z0-9_]*$/g;
      let labelErrorMsg = '';
      if (!regex.test(selectedRow[key])) {
        labelErrorMsg = 'label_charset';
      }
      this.setState({ labelErrorMsg });
    } else if (key === 'value') {
      if (selectedRow[key].length > 255) {
        this.setState({ textAreaErrMsg: 'value_ui_255_characters' });
      } else {
        this.setState({ textAreaErrMsg: '' });
      }
    }
  };

  /**
   * afterOnOkClick is to navigate to language content screen on ok.
   */
  afterOnOkClick = () => {
    const { isSaved } = this.state;
    const { setModified } = this.props;
    this.setState({ open: false });
    if (isSaved) {
      setModified(false);
      const { history } = this.props;
      history.push(RouteEndPoints.LANGUAGECONTENT);
    } else {
      this.errorFocus.current.focus();
    }
  };

  /**
   * setHeader is to change screen header based on mode.
   * @return label
   */
  setHeader = () => {
    const { isSaved, mode, saveMsg } = this.state;
    if (isSaved) {
      return mode === 'ADD' ? 'new_language_con_add_message' : 'language_con_updated_sucess';
    }
    return saveMsg;
  };

  /**
   * onkeyDown is to handle enter keydown to trigger save.
   * @param event
   */
  onkeyDown = (event) => {
    if (event.keyCode === 13 && !this.saveButtonDisableCheck()) {
      event.preventDefault();
      this.onSaveConfirmation();
    }
  };

  /**
   * saveButtonDisableCheck is to set condition for save button disable.
   * @returns boolean
   */
  saveButtonDisableCheck = () => {
    const { mode, selectedRow, textAreaErrMsg, labelErrorMsg, isChanged } = this.state;
    return !(
      selectedRow.code &&
      selectedRow.label &&
      selectedRow.value &&
      !textAreaErrMsg &&
      !labelErrorMsg &&
      (mode !== 'EDIT' || isChanged)
    );
  };

  /**
   * getTableData is define all fields in add language.
   * @returns array
   */
  getTableData = () => {
    const { mode, languageCodeList, selectedRow, labelErrorMsg, textAreaErrMsg } = this.state;
    return [
      {
        key: ['language_code_bold'],
        value: (
          <SBComboBox
            name="lang_code"
            id="lang_code"
            ref={this.initialFocus}
            options={languageCodeList}
            style={{ width: '30%' }}
            isNotMultiLang
            canModify
            defaultValue={selectedRow.code || languageCodeList[0]}
            value={selectedRow.code}
            disabled={mode === 'EDIT'}
            onChange={(event) => this.onContentChange(event, 'code')}
          />
        ),
      },
      {
        key: ['label_bold'],
        value: (
          <>
            <SBTextbox
              type="text"
              name="label_name"
              maxLength="100"
              classes="w50per"
              canModify
              defaultValue={selectedRow.label || ''}
              value={selectedRow.label}
              disabled={mode === 'EDIT'}
              onKeyDown={this.onkeyDown}
              onChange={(event) => this.onContentChange(event, 'label')}
              ref={this.errorFocus}
            />
            {labelErrorMsg && (
              <font id="label_msg" className="red bold">
                {this.languageLabelCheck(labelErrorMsg)}
              </font>
            )}
          </>
        ),
      },
      {
        key: ['value_bold'],
        value: (
          <>
            <SBTextArea
              name="label_value"
              classes="w50per"
              defaultValue={selectedRow.value || ''}
              cols={20}
              rows={4}
              canModify
              forwadedRef={this.modifyFocus}
              value={selectedRow.value}
              onChange={(event) => this.onContentChange(event, 'value')}
            />
            {textAreaErrMsg && (
              <font id="text_label_msg" className="red bold">
                <br />
                {this.languageLabelCheck(textAreaErrMsg)}
              </font>
            )}
          </>
        ),
      },
    ];
  };

  /**
   * render is to load fields.
   * @returns jsx elements
   */
  render() {
    const { mode, isSaved, open, loading, modalIsOpen,dataNotExist } = this.state;
    return (
      <div>
        <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
          <SBContentHeader
            title={mode === 'ADD' ? 'add_language_content' : 'edit_language_content'}
            description={
              mode === 'ADD' ? ['add_new_label_and_content'] : ['exit_selected_label_text']
            }
          />
          <SBTableLayout tableData={this.getTableData()} filterKey="key" filterValue="value" />
          <div className="floatright">
            <SBButton
              type="submit"
              name="okbutton"
              color="green"
              title="save"
              disabled={this.saveButtonDisableCheck()}
              onClick={() => this.onSaveConfirmation()}
            />
            <SBButton
              type="button"
              name="cancel"
              title="cancel"
              canModify
              noMarginRight
              onClick={() => this.onCancelButtonClick()}
            />
          </div>
          {dataNotExist ?  (
            <SBDialog
              open
              header='error'
              type='error'
              content='data_does_not_exists'
              onOk={() => this.onCancelButtonClick()}
              width="400px"
              okButtonTitle="ok"
            />
): (
  <SBDialog
    open={open}
    header={isSaved ? 'save_successful' : 'save_failed'}
    type={isSaved ? 'success' : 'error'}
    content={this.setHeader()}
    onOk={() => this.afterOnOkClick()}
    width="400px"
    okButtonTitle="ok"
  />
        )}
          <SBDialog
            open={modalIsOpen}
            header="save_confirmation"
            type="confirmation"
            content={mode === 'ADD' ? 'do_you_want_add_content' : 'do_you_want_save_changes'}
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={this.onSaveButtonClick}
            width="400px"
          />
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
LanguageContentAdd.propTypes = {
  localeData: PropTypes.objectOf(PropTypes.string),
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  setModified: PropTypes.func.isRequired,
  location: PropTypes.shape({
    mode: PropTypes.string,
    selectedRow: PropTypes.shape({
      code: PropTypes.string,
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  }).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
LanguageContentAdd.defaultProps = {
  localeData: {},
};

/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
    defaultLang: state.language.default,
  };
};

/**
 * mapDispatchToProps is to map store actions to props.
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LanguageContentAdd);
