import {
  isAlphaOrNumeric,
  isNotUndefinedOrNotEmpty,
  validateDmCode,
  validatePassword,
  validateLoginId,
  validateMultipleIps,
} from '../../../common/CommonValidations/CommonValidations';
import { setValidationErrorMessage } from './AppErrorConstants';
import {
  validateEmailId,
  validatePhoneNumberorPostalCode,
  remarksValidation,
  validateDate,
  validateLength,
} from './AppCommonValidations';

/**
 * setFocusinerrorField is used to set focus in error fields
 * @param dynamicData
 * @param reference
 */

export function setFocusinerrorField(dynamicData, reference) {
  if (dynamicData.businessNameError && dynamicData.businessNameError !== '') {
    reference.businessNameRef.current.focus();
  } else if (dynamicData.androidDeviceCountError && dynamicData.androidDeviceCountError !== '') {
    reference.androidDeviceCountRef.current.focus();
  } else if (dynamicData.dmCodeError && dynamicData.dmCodeError !== '') {
    reference.dmCodeRef.current.focus();
  } else if (dynamicData.loginError && dynamicData.loginError !== '') {
    reference.loginIdRef.current.focus();
  } else if (dynamicData.passwordError && dynamicData.passwordError !== '') {
    reference.passwordRef.current.focus();
  } else if (dynamicData.ipError && dynamicData.ipError !== '') {
    reference.ipAddressRef.current.focus();
  } else if (dynamicData.managersError && dynamicData.managersError !== '') {
    reference.managersRef.current.focus();
  } else if (dynamicData.contractInfoError && dynamicData.contractInfoError !== '') {
    reference.contractInfoRef.current.focus();
  } else if (dynamicData.salesPersonCodeError && dynamicData.salesPersonCodeError !== '') {
    reference.salesPersonCodeRef.current.focus();
  } else if (dynamicData.salesPersonNameError && dynamicData.salesPersonNameError !== '') {
    reference.salesPersonNameRef.current.focus();
  } else if (dynamicData.salesDeparmentNameError && dynamicData.salesDeparmentNameError !== '') {
    reference.salesDeparmentNameRef.current.focus();
  } else if (dynamicData.salesPhoneNumberError && dynamicData.salesPhoneNumberError !== '') {
    reference.salesPhoneNumberRef.current.focus();
  } else if (dynamicData.salesEmailAddressError && dynamicData.salesEmailAddressError !== '') {
    reference.salesEmailAddressRef.current.focus();
  } else if (dynamicData.companyNumberError && dynamicData.companyNumberError !== '') {
    reference.companyNumberRef.current.focus();
  } else if (dynamicData.trialDateError && dynamicData.trialDateError !== '') {
    reference.trialDateRef.current.focus();
  } else if (dynamicData.remarkOneError && dynamicData.remarkOneError !== '') {
    reference.remarkOneRef.current.focus();
  } else if (dynamicData.remarkTwoError && dynamicData.remarkTwoError !== '') {
    reference.remarkTwoRef.current.focus();
  }
}

/**
 * validateBusinessName is used to validate business name
 * @param businessName
 * @return status
 */

function validateBusinessName(businessName) {
  const minLength = 1;
  const maxLength = 50;
  const doubleByteRegex = /\u3000/g;
  const result = { status: true, error: '' };
  if (!doubleByteRegex.test(businessName)) {
    if (isNotUndefinedOrNotEmpty(businessName.trim())) {
      const businessNameLength = businessName.length;
      if (businessNameLength >= minLength && businessNameLength <= maxLength) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'empty';
    }
  } else {
    result.status = false;
    result.error = 'doublebyte';
  }
  return result;
}

/**
 * validateDeviceCount is used to validate device count
 * @param androidDeviceCount
 * @return status
 */

function validateDeviceCount(androidDeviceCount) {
  const maxValue = 100000;
  const numberRegex = /^[0-9]+$/;
  const result = { status: true, error: '' };
  if (androidDeviceCount!== '' && androidDeviceCount!== null && androidDeviceCount!== undefined) {
    if (numberRegex.test(androidDeviceCount)) {
      if (parseInt(androidDeviceCount, 10) <= maxValue) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'range';
      }
    } else {
      result.status = false;
      result.error = 'alpha';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateManagers is used to validate managers count
 * @param managerCount
 * @return status
 */

function validateManagers(managerCount) {
  const maxValue = 30;
  const numberRegex = /^[0-9]+$/;
  const result = { status: true, error: '' };
  if (managerCount !== '' && managerCount !== undefined &&  managerCount !==null) {
    if (numberRegex.test(managerCount)) {
      if (parseInt(managerCount, 10) <= maxValue) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'range';
      }
    } else {
      result.status = false;
      result.error = 'alpha';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validationSalesCode is used to validate sales code
 * @param salesCode
 * @return status
 */

function validationSalesCode(salesCode) {
  const maxLength = 7;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(salesCode)) {
    if (isAlphaOrNumeric(salesCode)) {
      const salesCodeLength = salesCode.length;
      if (salesCodeLength === maxLength) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'alphanumeric';
    }
  }
  return result;
}

/**
 * validateCompanyNumber is used to validate company number
 * @param value
 * @return status
 */

function validateCompanyNumber(value) {
  const maxLength = 6;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    if (isAlphaOrNumeric(value)) {
      const companyNumberLength = value.length;
      if (companyNumberLength === maxLength) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'alphanumeric';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * deviceCount is used to validate device count with contract status
 * @param value
 * @param maxlength
 * @return status
 */

function validateContractInfo(dataSource, contractStatus ,isUpdate) {
  const deviceCount = parseInt(dataSource.androidDeviceCount, 10);
  if (deviceCount === 0 && contractStatus !== 99 && contractStatus !== 4 && contractStatus !== 2) {
    return { status: false, error: 'zerocontract' };
  }

  if (isUpdate && deviceCount === 0 &&  contractStatus === 4 && dataSource.effectiveDateChange ==='' )  {
    return { status: false, error: 'valueContract' };
  }

  if (isUpdate && deviceCount === 0 && contractStatus === 2 && dataSource.cancellationDate ==='') {
    return { status: false, error: 'valueContract' };
  }
  if(isUpdate && deviceCount !== 0 
    && dataSource.effectiveDateChange ==='' 
    && contractStatus !==99 
    && contractStatus !==2) {
    return { status: false, error: 'valueContract' }; 
  } 
  if(isUpdate && deviceCount !== 0 && dataSource.cancellationDate ==='' && contractStatus ===2) {
    return { status: false, error: 'valueContract' }; 
  } 
  if (deviceCount !== 0 && contractStatus === 99) {
    return { status: false, error: 'valueContract' };
  }
  return { status: true, error: '' };
}

/**
 * validateInputs is used to validate fields
 * @param dataSource
 * @param dynamicData
 * @return tempDynamicData
 */

export function validateInputs(dataSource, dynamicData,isUpdate) {
  let tempDynamicData = dynamicData;
  const businessNameValidation = validateBusinessName(dataSource.businessName);
  const deviceCountValidation = validateDeviceCount(dataSource.androidDeviceCount);
  const dmCodeValidation = validateDmCode(dataSource.dmCode);
  const loginValidation = validateLoginId(dataSource.loginId);
  let passwordValidation ={ status: true, error: '' };
  if(!isUpdate) {
    passwordValidation = validatePassword(dataSource.password);
  }
  const ipValidation = validateMultipleIps(dataSource.ipAddress);
  const managersValidation = validateManagers(dataSource.userCount);
  const salesCodeValidation = validationSalesCode(dataSource.salesPersonCode);
  const salesPersonNameValidation = validateLength(dataSource.salesPersonName, 100);
  const salesDeptNameValidation = validateLength(dataSource.salesDepartmentName, 100);
  const salesPhoneNumberValidation = validatePhoneNumberorPostalCode(
    dataSource.salesPhoneNumber,
    20,
  );
  const salesEmailIdValidation = validateEmailId(dataSource.salesEmailAddress);
  const companyNumberValidation = validateCompanyNumber(dataSource.companyNumber);
  const trialDateValidation = validateDate(dataSource.trialEndDate, true);
  const remarksOneValidation = remarksValidation(dataSource.remarksOne, 500);
  const remarksTwoValidation = remarksValidation(dataSource.remarksTwo, 150);
  let contractInfoValidation;
  if (deviceCountValidation.status) {
    contractInfoValidation = validateContractInfo(
      dataSource,
      parseInt(dataSource.contractStatus, 10),
      isUpdate
    );
    tempDynamicData = setValidationErrorMessage(
      'contractInfoError',
      contractInfoValidation,
      tempDynamicData,
    );
  } else {
    tempDynamicData = setValidationErrorMessage(
      'contractInfoError',
      { status: true, error: '' },
      tempDynamicData,
    );
  }
  tempDynamicData = setValidationErrorMessage(
    'businessNameError',
    businessNameValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'androidDeviceCountError',
    deviceCountValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage('dmCodeError', dmCodeValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('loginError', loginValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('passwordError', passwordValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('ipError', ipValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('managersError', managersValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage(
    'salesPersonCodeError',
    salesCodeValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'salesPersonNameError',
    salesPersonNameValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'salesDeparmentNameError',
    salesDeptNameValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'salesPhoneNumberError',
    salesPhoneNumberValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'salesEmailAddressError',
    salesEmailIdValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'companyNumberError',
    companyNumberValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'trialDateError',
    trialDateValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'remarkOneError',
    remarksOneValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'remarkTwoError',
    remarksTwoValidation,
    tempDynamicData,
  );
  tempDynamicData.status =
    businessNameValidation.status &&
    deviceCountValidation.status &&
    dmCodeValidation.status &&
    loginValidation.status &&
    passwordValidation.status &&
    ipValidation.status &&
    managersValidation.status &&
    contractInfoValidation.status &&
    salesCodeValidation.status &&
    salesPersonNameValidation.status &&
    salesDeptNameValidation.status &&
    salesPhoneNumberValidation.status &&
    salesEmailIdValidation.status &&
    companyNumberValidation.status &&
    trialDateValidation.status &&
    remarksOneValidation.status &&
    remarksTwoValidation.status;
  return tempDynamicData;
}
