import { fetch } from './fetch';
import Utils from '../constants/Utils';

const { API_URL_ORIGIN } = Utils;

export const defaultHeaders = {
  Accept: 'application/json',
  'Content-Type': 'application/json',
  'Access-Control-Allow-Origin': '*',
};

export const blobHeaders = {
  'Content-Type': 'application/json',
  'Access-Control-Allow-Origin': '*',
};

function handlecaughtError(error) {
  if (error.statusCode !== 401) {
    localStorage.setItem('error', 'true');
    window.location.href = `${window.location.origin}/error`;
  }
}

export const get = (url, signal) => {
  const paramMap = {
    method: 'get',
    headers: defaultHeaders,
  };
  if (signal) {
    paramMap.signal = signal;
  }
  return fetch(`${API_URL_ORIGIN}/api/${url}`, paramMap).catch(error => {
    handlecaughtError(error);
  });
};

export const post = (url, postData) => {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'post',
    headers: defaultHeaders,
    body: JSON.stringify(postData),
  }).catch(error => {
    handlecaughtError(error);
  });
};

export const performDelete = url => {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'DELETE',
    headers: defaultHeaders,
  }).catch(error => {
    handlecaughtError(error);
  });
};

export const put = (url, postData) => {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'PUT',
    headers: defaultHeaders,
    body: JSON.stringify(postData),
  }).catch(error => {
    handlecaughtError(error);
  });
};

export const postWithFormData = function(url, formData) {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'POST',
    body: formData,
  }).catch(error => {
    handlecaughtError(error);
  });
};

export const putWithFormData = function(url, formData) {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'PUT',
    body: formData,
  }).catch(error => {
    handlecaughtError(error);
  });
};

export const postWithBlobData = (url, postData) => {
  return fetch(`${API_URL_ORIGIN}/api/${url}`, {
    method: 'post',
    headers: blobHeaders,
    body: JSON.stringify(postData),
  }).catch(error => {
    handlecaughtError(error);
  });
};
