import React, { Component } from 'react';
import cloneDeep from 'lodash/cloneDeep';
import { dateFormatSlash, dateFormatWithSlash } from '../../utils/dateUtils';

/**
 * SBDatePicker is to frame datepicker component
 * @author Rinchu.T, Aswin.P
 *
 */
class SBDatePicker extends Component {
  componentDidMount() {
    const { id, format, value: defaultDate, minDate, maxDate } = this.props;
    const option = (format || '').toLowerCase();
    switch (option) {
      case 'mm-dd':
      case 'dd-mm':
      case 'mm/dd':
      case 'dd/mm':
      case 'mmdd':
      case 'ddmm':
        window.$(`${id ? `#${id}` : ``}.datepicker`).datepicker({
          changeYear: true,
          changeMonth: true,
          dateFormat: 'yy/mm/dd',
          defaultDate,
          minDate,
          maxDate,
          beforeShow: (input) => this.beforeShow(input),
          beforeShowDay: (dateText) => this.setDateRange(dateText, minDate, maxDate),
          onSelect: (dateText, instance) => this.onSelect(dateText, instance, 'dd/mm'),
        });
        break;
      case 'dd/mm/yy':
        window.$(`${id ? `#${id}` : ``}.datepicker`).datepicker({
          changeYear: true,
          changeMonth: true,
          dateFormat: 'dd/mm/yy',
          defaultDate,
          minDate,
          maxDate,
          beforeShow: (input) => this.beforeShow(input),
          beforeShowDay: (dateText) => this.setDateRange(dateText, minDate, maxDate),
          onSelect: (dateText, instance) => this.onSelect(dateText, instance, 'dd/mm/yy'),
        });
        break;
      default:
        window.$(`${id ? `#${id}` : ``}.datepicker`).datepicker({
          changeMonth: true,
          changeYear: true,
          showButtonPanel: true,
          defaultDate,
          maxDate,
          minDate,
          dateFormat: 'yy/mm/dd',
          beforeShow: (input) => this.beforeShow(input),
          onSelect: (dateText, instance) => this.onSelect(dateText, instance),
        });
        break;
    }
  }

  /**
   * componentDidUpdate is to reset the daterange with default value
   */
  componentDidUpdate() {
    const { id, minDate, maxDate, clearRange } = this.props;
    if (clearRange) {
      window.$(`#${id}`).datepicker('option', 'minDate', minDate);
      window.$(`#${id}`).datepicker('option', 'maxDate', maxDate);
    }
  }

  /**
   * beforeShow used to handle datepicker before it open.
   * @param input
   */
  beforeShow = (input) => {
    const scrollComponent = window.$('.ant-layout-content')[0];
    const { scrollTop, scrollLeft } = scrollComponent;
    window.$('#ui-datepicker-div').addClass('datePickerZindex');
    window.$(input).after(window.$(input).datepicker('widget'));
    const inputOffset = window.$(input).offset();
    const { top: inputOffsetTop, left: inputOffsetLeft } = inputOffset;
    setTimeout(() => {
      window.$('#ui-datepicker-div').css({
        position: 'absolute',
        display: 'block',
        top: `${inputOffsetTop + scrollTop - 56}px`,
        left: `${inputOffsetLeft + scrollLeft - 218}px`,
      });
    }, 0);
  };

  setDateRange = (dateText, minDate, maxDate) => {
    const { restrictLeapYear } = this.props;
    const date = new Date(dateText);
    const leapYearConstraint = restrictLeapYear && +date.getMonth() === 1 && +date.getDate() === 29;
    return [!leapYearConstraint && (+date >= +minDate || +date <= +maxDate)];
  };

  calculateInterval = (date, interval, periodic) => {
    const leapYearDate = new Date(`${+date.getFullYear()}/02/29`);
    const isLeap = +leapYearDate.getMonth() === 1;
    if (periodic) return date < leapYearDate ? interval + 2 : interval + 1;
    return isLeap && date < leapYearDate ? interval : interval - 1;
  };

  onSelect = (dateText, instance, formater) => {
    const {
      id,
      onSelect,
      startRange,
      endRange,
      interval,
      periodicInterval,
      selectedRange,
    } = this.props;
    let identifier = cloneDeep(id);
    let checkEndDate;
    const lowerLimit = new Date(
      instance.selectedYear,
      instance.selectedMonth,
      instance.selectedDay,
    );
    let upperLimit = cloneDeep(lowerLimit);
    if (startRange) {
      identifier = cloneDeep(`#${identifier.replace(selectedRange, 0)}`);
      upperLimit.setDate(
        upperLimit.getDate() + (interval ? this.calculateInterval(upperLimit, interval) : 0),
      );
      const baseLimit = window.$(identifier).datepicker('getDate');
      baseLimit.setDate(
        baseLimit.getDate() -
          (periodicInterval ? this.calculateInterval(baseLimit, periodicInterval, true) : 0),
      );
      baseLimit.setFullYear(baseLimit.getFullYear() + 1);
      upperLimit = +baseLimit > +upperLimit ? upperLimit : baseLimit;
      identifier = cloneDeep(identifier);
      identifier = `${identifier
        .replace(0, selectedRange)
        .substring(0, identifier.lastIndexOf('_start-date'))}_end-date`;
      checkEndDate = window.$(identifier).datepicker('getDate') || upperLimit;
      upperLimit =
        +lowerLimit > +upperLimit
          ? new Date(upperLimit.setFullYear(upperLimit.getFullYear() + 1))
          : upperLimit;
      let validateDateText = +checkEndDate > +upperLimit ? upperLimit : checkEndDate;
      validateDateText = `${validateDateText.getFullYear()}/${`0${
        validateDateText.getMonth() + 1
      }`.slice(-2)}/${validateDateText.getDate()}`;
      window.$(identifier).datepicker('option', 'defaultDate', validateDateText);
      window.$(identifier).datepicker('option', 'minDate', lowerLimit);
      window.$(identifier).datepicker('option', 'maxDate', upperLimit);
    }
    if (startRange || endRange) {
      let startDateId = cloneDeep(identifier);
      startDateId = `${endRange ? '#' : ''}${startDateId.substring(
        0,
        startDateId.lastIndexOf('_end-date'),
      )}_start-date`;
      window
        .$(startDateId)
        .datepicker(
          'option',
          'maxDate',
          startRange && +checkEndDate <= +upperLimit ? checkEndDate : upperLimit,
        );
      window.$(`#${id}`).datepicker('option', 'defaultDate', dateText);
    }
    if (onSelect) onSelect(dateText, instance, formater);
  };

  dateObjectFormater = (date, option) => {
    switch (option) {
      case 'dd/mm':
        return { date: date.getDate(), month: date.getMonth() };
      default:
        return { date: date.getDate(), month: date.getMonth(), year: date.getFullYear() };
    }
  };

  handleChange = (event) => {
    const { onChange } = this.props;
    if (onChange) onChange(event);
  };

  changeDateFormat = (date) => {
    if (date) {
      if (date.length < dateFormatWithSlash.length) {
        return date;
      }
      const current = dateFormatSlash(date);
      const currentDate = new Date(current);
      return currentDate.getDay() ? current : date;
    }
    return '';
  };

  shortDateFormat = (date, format) => {
    if (date) {
      if (date.length < format.length) {
        return date;
      }
      return date.length < format.length
        ? date
        : date.toLocaleDateString('default', { month: 'numeric', day: 'numeric' });
    }
    return '';
  };

  dateTextFormater = (date) => {
    const { format } = this.props;
    const option = (format || '').toLowerCase();
    switch (option) {
      case 'mm-dd':
      case 'dd-mm':
      case 'mm/dd':
      case 'dd/mm':
      case 'mmdd':
      case 'ddmm':
        return this.shortDateFormat(date, option);
      default:
        return this.changeDateFormat(date);
    }
  };

  render() {
    const {
      name,
      id,
      width,
      className,
      disabled,
      value,
      style,
      maxLength,
      forwadedRef,
    } = this.props;
    return (
      <input
        id={id}
        name={name}
        type="text"
        maxLength={maxLength || 10}
        value={this.dateTextFormater(value)}
        disabled={disabled}
        ref={forwadedRef}
        className={`${className || ''} datepicker`}
        style={
          disabled ? { width: width || 200, background: '#EEE' } : { ...style, width: width || 200 }
        }
        onChange={(event) => this.handleChange(event)}
      />
    );
  }
}
export default SBDatePicker;
