/** Base Files */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBLink from '../../../common/component/sblink/SBLink';
import SBTable from '../../../common/component/sbtable/SBTable';

/** Utility Files */
import checkLanguage from '../../../language/checkLanguage';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import addDynamicContent from '../../../language/addDynamicContent';
import { getColumns } from '../constants/defaultGroupSettingConstants';

/**
 * DefaultGroupManagementGrid is to list the group management information
 * @author Aswin Palanisamy
 */
class DefaultGroupManagementGrid extends Component {
  /**
   * languageLabelCheck performs language based data rendering operation
   * @param label
   * @returns multiLangLabel
   */
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo performs data count rendering operation in grid
   * @param label
   * @param contents
   * @returns dynamicLangContent
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * renderComponent used to render link to navigate group information
   * @param record
   */
  renderComponent = record => {
    const { hasAuthority } = this.props;
    const { groupId, groupName } = record;
    return hasAuthority ? (
      <SBLink state={{ groupId }} content={groupName} pathname={ROUTEENDPOINTS.GROUPINFORMATION} />
    ) : (
      <span>{groupName}</span>
    );
  };

  /**
   * render performs grid component rendering
   * @returns gridComponent
   */
  render() {
    const columns = getColumns(this.renderComponent);
    const { gridList, countInfoContents, defaultSelected } = this.props;
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          <SBTable
            columns={columns}
            dataSource={gridList}
            loading
            countInfoContents={countInfoContents}
            defaultSelected={defaultSelected}
          />
        </div>
        {!isEmpty(gridList) ? null : (
          <div className="clearfix mb10" style={{ marginTop: 10 }}>
            {this.languageLabelCheck('no_results_found')}
          </div>
        )}
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from language redux state
 * @param state
 * @returns reduxState
 */
const mapStateToProps = state => {
  return { localeData: state.language.data };
};

/**
 * propTypes is used for validation props with its type.
 */
DefaultGroupManagementGrid.propTypes = {
  isNotMultiLang: PropTypes.bool,
  hasAuthority: PropTypes.bool.isRequired,
  countInfoContents: PropTypes.number.isRequired,
  gridList: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DefaultGroupManagementGrid.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(DefaultGroupManagementGrid));
