import { LANGUAGE_CHANGE } from '../actions/ActionTypes';

/**
 * languageChangerAction is to store changed user language, timezone and content
 * @param language
 * @param timezone
 * @param data
 * @return Object
 *
 * @author Gobi R
 */
const languageChangerAction = (language, timezone, data) => {
  return {
    type: LANGUAGE_CHANGE,
    current: language,
    timezone,
    data,
  };
};

export default languageChangerAction;
