/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

/** SBComponents */
import SBTabs from '../../common/component/Tabs/SBTabs';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceDetailTab from './DeviceDetailTab';
import DeviceGrid from '../component/DeviceGrid';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { tabList } from '../constants/deviceConstants';
import deviceHeader from '../constants/deviceHeaderConstants';
import { serviceMapper, getDeviceDetail } from '../common/deviceCommonFunctions';

/**
 * DeviceDetail is to display enrolled device information in an enterprise
 * @author Aswin Palanisamy
 */
class DeviceDetail extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey } = props;
    this.state = {
      screenKey,
      message: '',
      dataSource: [],
      pageLoading: true,
      activeKey: 'user',
      openErrorDialog: false,
      reference: { refreshRef: createRef() },
      screenHeader: deviceHeader(screenKey),
    };
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { location } = this.props;
    if (!location.state) this.denyAccess();
    else this.getDeviceInformation();
  }

  /**
   * getDeviceInformation is to fetch selected device information
   */
  getDeviceInformation = () => {
    const prevState = this.state;
    this.setState({ pageLoading: true }, async () => {
      const fetchProps = this.props;
      const deviceReport = await getDeviceDetail(
        { ...fetchProps, tabKey: fetchProps.screenKey },
        prevState,
      );
      this.setState({ ...deviceReport, dataSource: [deviceReport.dataSource] });
    });
  };

  /**
   * denyAccess used to restrict the user from accessing the screen with a prompt.
   */
  denyAccess = () =>
    this.setState({
      pageLoading: false,
      openErrorDialog: true,
      message: 'data_does_not_exists',
    });

  /**
   * onHandleClick perform button actions in device detail screen
   * @param buttonKey
   * @return footerActionMapper
   */
  onHandleClick = buttonKey =>
    ({
      back: () => this.navigateListScreen(),
      debugLog: () => null,
      downloadDebugLog: () => null,
    }[buttonKey]());

  /**
   * navigateListScreen perform device list screen navigation
   */
  navigateListScreen = () => {
    const { history } = this.props;
    history.push(serviceMapper('deviceList'));
  };

  /**
   * onTabClick is to set activeKey om tab switch
   * @param activeKey
   */
  onTabClick = activeKey => {
    this.setState({ activeKey });
  };

  /**
   * render performs group management screen rendering
   * @return dataSource
   */
  render() {
    const {
      message,
      screenKey,
      activeKey,
      pageLoading,
      dataSource,
      screenHeader,
      openErrorDialog,
      reference,
    } = this.state;
    const { history, location } = this.props;
    const headerData = screenHeader[activeKey];
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <DeviceGrid screenKey={screenKey} gridList={dataSource} />
        <SBTabs
          activeKey={activeKey}
          headerStyle={{ width: 202.75 }}
          onClick={key => this.onTabClick(key)}
        >
          {tabList &&
            tabList.map(tab => (
              <div {...tab} style={{ height: 320 }}>
                <DeviceDetailTab
                  reference={reference}
                  history={history}
                  location={location}
                  tabKey={tab.key}
                />
              </div>
            ))}
        </SBTabs>
        <DeviceFooter
          onHandleClick={key => this.onHandleClick(key)}
          footer={{ screenKey, setView: activeKey !== 'device' }}
        />
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => this.navigateListScreen());
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
DeviceDetail.propTypes = {
  screenKey: PropTypes.string.isRequired,
  department: PropTypes.string.isRequired,
  makeDepartment: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      deviceId: PropTypes.string,
    }),
  }).isRequired,
};

export default withRouter(DeviceDetail);
