/**
 * NotificationInputForm is used to generate input fields for notification registration screen
 * @author: V.Vijayaragavan
 */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import EditorContainer from '../../../../common/component/Editor/EditorContainer';
import SBTableHeader from '../../../../common/component/tableheader/SBTableHeader';
import NotificationCustomTableFrame from '../NotificationCustomTableFrame/NotificationCustomTableFrame';
import NotificationView from './NotificationView';
import '../../../../../css/editor.css';
import {
  notificationDetailsTableList,
  notificationAttachmentTableList,
  confirmPageTableList,
} from '../../constants/NotificationRegTableLists';

class NotificationInputForm extends PureComponent {
  /**
   * render is used to render the component
   */
  render() {
    const {
      dataSource,
      dynamicData,
      onNotificationInputAction,
      confirm,
      onEditorContentChanged,
      fileDataSource,
      uploadedImages,
    } = this.props;
    return (
      <>
        <NotificationCustomTableFrame
          tableList={
            confirm ? confirmPageTableList(dynamicData) : notificationDetailsTableList(dynamicData)
          }
          dataSource={dataSource}
          onTableFrameEvent={(key, event, value) => onNotificationInputAction(key, event, value)}
        />
        <SBTableHeader headerText="content" />
        {confirm ? (
          <NotificationView
            showDownload={Boolean(fileDataSource.attachmentFile)}
            imageFileURL={
              fileDataSource.imageFile ? URL.createObjectURL(fileDataSource.imageFile) : ''
            }
            onDownloadButtonClick={() => onNotificationInputAction('attachmentFileDownload')}
            htmlContent={dataSource.content || ''}
          />
        ) : (
          <>
            <div style={{ marginBottom: 10 }}>
              <EditorContainer
                maxNoOfImages={5}
                uploadedImages={uploadedImages}
                content={dataSource.content}
                imageAccept={['.png', '.jpeg', '.jpg', '.gif']}
                onEditorContentChanged={(htmlData, editorUploadedImages) =>
                  onEditorContentChanged(htmlData, editorUploadedImages)
                }
              />
            </div>
            <NotificationCustomTableFrame
              tableList={notificationAttachmentTableList(dynamicData)}
              dataSource={dataSource}
              onTableFrameEvent={(key, event, value) =>
                onNotificationInputAction(key, event, value)
              }
            />
          </>
        )}
      </>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
NotificationInputForm.propTypes = {
  dataSource: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.array, PropTypes.bool]),
  ).isRequired,
  dynamicData: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.array, PropTypes.bool]),
  ).isRequired,
  onNotificationInputAction: PropTypes.func.isRequired,
  confirm: PropTypes.bool,
  onEditorContentChanged: PropTypes.func.isRequired,
  fileDataSource: PropTypes.objectOf(File),
  uploadedImages: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NotificationInputForm.defaultProps = {
  fileDataSource: {},
  confirm: false,
  uploadedImages: {},
};

export default NotificationInputForm;
