import React  from 'react';
import Chart from 'react-apexcharts';


 const SBBarChart = (props) =>  {
        
    const getOptions  = (options) =>{
         const {data,horizontalBar,color,tooltip,showLegend,noDataText,chartTitle,xaxisTitle,yaxisTitle,max} = options;
            return {
              series: [{
                data
               }],
                options : {
                  title: {
                    text: chartTitle || ''
                  },
                    plotOptions: {
                      bar: {
                      barHeight:  '55%',
                      distributed: true,
                      horizontal: horizontalBar || false,
                    }
                  },
                  colors:color || [],
                  stroke: {
                    width: 1,
                    colors: ['#fff']
                  },
                  xaxis: {
                      type: 'category' ,
                      labels: {
                             show: true,
                      },
                      title: {
                        text: xaxisTitle || '',
                      }
                  },
                  yaxis: {
                    title: {
                      text: yaxisTitle || '',
                    },
                    min: 0,
                    max: max || 100,
                },
                  tooltip: {
                    enabled: tooltip || false,
                    },
                    legend: {
                        show: showLegend || false,
                    },
                    noData: {
                        text: noDataText,
                        align: 'center',
                        verticalAlign: 'middle',
                        offsetX: 0,
                        offsetY: 0,
                        style: {
                          fontSize: '14px',
                          fontFamily: undefined
                        }
                      }
                }
             }
}

    const {height,width} = props;
        const chartOptions = getOptions(props);
      return (
        <Chart options={chartOptions.options} series={chartOptions.series} type="bar" width={width} height={height} />
      );
  }


  
  
  /**
   * defaultProps is used to initialise non requiered props initial values
   */
  SBBarChart.defaultProps = {
    horizontalBar:false,
    noDataText:''
    
  };

  export default SBBarChart;