/**
 * addDynamicContent is to add dynamic content in label.
 * @param data
 * @param contents
 * @return label
 *
 * @author Gobi R
 */
const addDynamicContent = (data, ...contents) => {
  let label = data;
  contents.forEach((content, index) => {
    label = label.replace(`%${index + 1}$s`, content);
  });
  return label;
};

export default addDynamicContent;
