/**
 * DashboardSetting is used to display the Setting of dashboard screen
 * @author: Aakash Alagarsamy
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import LoadingSpinner from '../../../common/component/loadingSpinner/LoadingSpinner';
import DashboardSettingFooter from './DashboardSettingFooter';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get, put } from '../../../actions/commonActions';
import {
  emptyDataSource,
  setTableView,
} from '../dasboardsettingconstants/DashboardSettingFieldConstants';
import headerInformation from '../dasboardsettingconstants/DashboardSettingHeaderConstants';
import setRegisterReference from '../dasboardsettingconstants/DashboardSettingReferenceConstants';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';

class DashboardSetting extends Component {
  constructor() {
    super();
    this.state = {
      loading: true,
      headerInfo: headerInformation('dashboardsetting'),
      tableView: setTableView('view', {}, setRegisterReference(), emptyDataSource()),
      dataSource: emptyDataSource(),
      reference: setRegisterReference(),
      footer: { okDisable: true, primaryVisibile: false, browsingOnly: false },
    };
  }

  /**
   * getDerivedStateFromProps is used to set state from props
   */
  static getDerivedStateFromProps(props, state) {
    const { userVo } = props;
    const authorityList = [4, 8, 9];
    state.footer.browsingOnly = authorityList.some((item) => item === userVo.authorityId);
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const generateComponent = async () => {
      await this.getDashboardSettings();
    };
    generateComponent();
  }

  /**
   * getDashboardSettings is used to get already set Dashboard record
   */
  getDashboardSettings = async () => {
    const { history } = this.props;
    const response = await get(`${APIENDPOINTS.GET_DASHBOARD_SETTING}`);
    const responseData = await response.json();
    if (responseData.status) {
      this.setState({ loading: false, dataSource: this.makeDataSource(responseData) });
      this.onModeChange('view', false);
    } else {
      this.setState({ loading: false });
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * makeDataSource is used to set value to the fields
   * @param responseData
   */
  makeDataSource = (responseData) => {
    const period = responseData.value.autoDashboardCollection
      ? responseData.value.autoCollectionPeriod
      : '1d';
    return {
      autoDashboardCollection: responseData.value.autoDashboardCollection,
      autoCollectionPeriod: period,
    };
  };

  /**
   * onModeChange is used to handle operation performed on the component when it is changed
   * @param mode
   * @param primaryVisibile
   */
  onModeChange = (mode, primaryVisible) => {
    const { reference, footer, dataSource } = this.state;
    let { headerInfo } = this.state;
    footer.primaryVisibile = primaryVisible;
    footer.okDisable = mode === 'update';
    if (primaryVisible) {
      headerInfo = headerInformation('changedashboardsetting');
    } else {
      headerInfo = headerInformation('dashboardsetting');
    }
    this.setState({
      headerInfo,
      tableView: setTableView(mode, {}, reference, dataSource),
      footer,
    });
  };

  /**
   * onHandleClick is used to handle operation performed on the button click
   * @param key
   * @param value
   */
  onHandleClick = (key) => {
    const { setModified } = this.props;
    switch (key) {
      case 'change':
        this.onModeChange('update', true);
        break;
      case 'save':
        this.saveDashboardSettings();
        break;
      case 'cancel':
        setModified(false);
        this.onModeChange('view', false);
        break;
      default:
        break;
    }
  };

  /**
   * saveDashboardSettings is used to save the values in DB
   */
  saveDashboardSettings = async () => {
    const { setModified, history } = this.props;
    const { dataSource } = this.state;
    this.setState({ loading: true });
    const response = await put(APIENDPOINTS.SAVE_DASHBOARD_SETTING, dataSource);
    const responseData = await response.json();
    setModified(false);
    this.setState({ loading: false });
    if (responseData.status) {
      this.onModeChange('view', false);
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param value
   */
  onTableFrameEvent = (key, event, value) => {
    const { dataSource, footer } = this.state;
    const { target } = event;
    if (key.includes('autoCollectionPeriod')) value = target.value;
    dataSource[key] = value;
    footer.okDisable = false;
    this.setState({ dataSource, footer });
  };

  render() {
    const { headerInfo, dataSource, tableView, reference, footer, loading } = this.state;
    return (
      <LoadingSpinner loading={loading}>
        <>
          <SBContentHeader title={headerInfo.title} description={headerInfo.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <SBTableHeader headerText="update_device_information" />
                <CustomTableFrame
                  tableList={tableView.updateDeviceInfoList}
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <DashboardSettingFooter
                  reference={reference}
                  footer={footer}
                  onHandleClick={(key) => this.onHandleClick(key)}
                />
              </div>
            </div>
          </div>
        </>
      </LoadingSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  return {
    userVo,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DashboardSetting.propTypes = {
  history: commonPropTypes().history.isRequired,
  setModified: PropTypes.func.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
};
export default connect(mapStateToProps, mapDispatchToProps)(DashboardSetting);
