import React, { Component } from 'react';
import { PropTypes } from 'prop-types';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { performDelete } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
/**
 * CodeDeleteDialog is dialog to display information to delete language.
 * @author Gobi R
 */
export default class CodeDeleteDialog extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {};
  }

  /**
   * onOkClick is to delete selected language.
   */
  onOkClick = () => {
    const { closeDialog, code, refresh, openDeleteInfo } = this.props;
    performDelete(`${APIENDPOINTS.LANGUAGEDELETE}/${code}`)
      .then((response) => response.text())
      .then((data) => {
        if (data === 'Success') {
          refresh();
        } else {
          openDeleteInfo(data);
        }
      });
    closeDialog(true);
  };

  /**
   * onCancelClick is to cancel deletion process.
   */
  onCancelClick = () => {
    const { closeDialog } = this.props;
    closeDialog(false);
  };

  /**
   * content is to show dialog content.
   */
  content = () => {
    return ['deleteing_language_code'];
  };

  /**
   * render is to load sub components.
   * @returns jsx elements
   */
  render() {
    const { open ,content} = this.props;
    return (
      <div>
        <SBDialog
          open={open}
          header="delete_confirmation"
          type="confirmation"
          content={content}
          onOk={() => this.onOkClick()}
          onCancel={() => this.onCancelClick()}
          width="400px"
        />
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
CodeDeleteDialog.propTypes = {
  closeDialog: PropTypes.func.isRequired,
  code: PropTypes.string,
  refresh: PropTypes.func.isRequired,
  openDeleteInfo: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
CodeDeleteDialog.defaultProps = {
  code: null,
};
