import React, { useState } from 'react';
import { connect } from 'react-redux';
import commonChangeAction from '../../changeReducer/commonChangeAction';

/**
 * SBNumberbox is to frame number field component
 * @author Aswin.P
 *
 */
const SBNumberbox = React.forwardRef((props, ref) => {
  const [value, setValue] = useState(props.value);

  /* useEffect(() => {
    const { value: content } = props;
    if (value !== content) setValue(content);
  }, [value]); */

  const changeModified = () => {
    const { setModified, canModify } = props;
    if (canModify) setModified(true);
  };

  const onTextBoxChange = event => {
    const { onChange, maxValue, minValue } = props;
    let { value: content } = event.target;
    content = content || '0';
    content = +content.match(/\d+/g).shift();
    content = content > maxValue ? maxValue : content;
    content = content < minValue ? minValue : content;
    setValue(content);
    changeModified();
    if (onChange) onChange(event, content);
  };

  const onTextBoxKeyDown = event => {
    const { onKeyDown, maxValue, minValue, setCounter } = props;
    let content = value;
    content = setCounter && event.keyCode === 38 ? content + 1 : content;
    content = setCounter && event.keyCode === 40 ? content - 1 : content;
    content = setCounter && content < minValue ? minValue : content;
    content = setCounter && content > maxValue ? maxValue : content;
    setValue(content);
    if (content !== value) changeModified();
    if (onKeyDown) onKeyDown(event, content);
  };

  const onDropDragEvent = event => event.preventDefault();

  const {
    placeholder,
    type,
    maxLength,
    name,
    disabled,
    readOnly,
    size,
    style,
    defaultValue,
    id,
    classes,
  } = props;

  return (
    <input
      id={id}
      placeholder={placeholder}
      maxLength={maxLength}
      type={type}
      size={size}
      name={name}
      className={classes}
      readOnly={readOnly}
      disabled={disabled}
      style={style}
      ref={ref}
      {...(defaultValue ? { defaultValue: defaultValue || 0 } : { value: value || 0 })}
      onChange={event => onTextBoxChange(event)}
      onKeyDown={event => onTextBoxKeyDown(event)}
      onDrop={event => onDropDragEvent(event)}
    />
  );
});

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => dispatch(commonChangeAction(isModified)),
  };
};

export default connect(
  null,
  mapDispatchToProps,
  null,
  { forwardRef: true },
)(SBNumberbox);
