/**
 * RegisterNoticeConstants is used to load the Registration notice values
 * @author: R.Rajeshkumar
 */

/**
 * getDmCodeViewList is load the customcolumn information
 * @return customcolumndetails
 *
 */
export function getDmCodeViewList(dynamicData) {
  return [
    {
      label: 'dm_code',
      component: 'span',
      key: 'dmCode',
    },
    {
      label: 'business_name',
      component: 'span',
      key: 'businessName',
    },
    {
      label: 'registration_notification_type',
      component: 'radio',
      name: 'type',
      key: 'registerNoticeType',
      defaultValue: 0,
      options: [
        { filterKey: 0, filterValue: 'registration_notification_new' },
        { filterKey: 1, filterValue: 'registration_notification_change' },
      ],
      filterKey: 'filterKey',
      filterValue: 'filterValue',
      canModify: false,
    },
    {
      label: 'registration_notification_date',
      required: true,
      component: 'datepicker',
      key: 'fileNameDate',
      id: 'registration_notification_date',
      name: 'registration_notification_date',
      canModify: true,
      minDate: new Date('2011/01/01'),
      maxDate: new Date('2050/12/31'),
      maxLength: 8,
      forwadedRef: dynamicData.filenameRef,
      errorMessage: dynamicData.fileNameDateError,
      render: (value) => (value && value.fileNameDate ? value.fileNameDate : ''),
    },
  ];
}

/**
 * SetErrorAndFocus method is used to set focus on error field
 * @param dynamicData
 */
export function SetErrorAndFocus(dynamicData) {
  dynamicData.fileNameDateError = 'registration_notification_filename_empty';
  if (dynamicData.filenameRef.current) dynamicData.filenameRef.current.focus();
  return dynamicData;
}
