/** Base Files */
import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';

/** SBComponents */
import SBButton from '../../../common/component/sbbutton/SBButton';
import RoleResolver from '../../../common/component/guard/RoleResolver';

/** Utility Files */
import { buttonList } from '../constants/defaultGroupSettingConstants';

/**
 * DefaultGroupManagementFooter is to render the group management CRUD operation buttons
 * @author Aswin Palanisamy
 */
class DefaultGroupManagementFooter extends Component {
  /**
   * onHandleClick performs button click operation in footer room
   * @param key
   */
  onHandleClick = key => {
    const { onHandleClick } = this.props;
    if (onHandleClick) onHandleClick(key);
  };

  /**
   * renderFooterButton performs footer room button rendering
   * @param props
   * @returns footerButtons
   */
  renderFooterButton = props => {
    const { key, authorityRoleType } = props;
    return (
      <RoleResolver authorityRoleType={authorityRoleType}>
        <SBButton {...props} name={key} type="button" onClick={() => this.onHandleClick(key)} />
      </RoleResolver>
    );
  };

  /**
   * render performs footer room component rendering
   * @returns footerComponent
   */
  render() {
    const { screenKey, footer } = this.props;
    return (
      <div className="clearfix">
        <div className="floatright">
          {buttonList(footer, screenKey).map(record => this.renderFooterButton(record))}
        </div>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
DefaultGroupManagementFooter.propTypes = {
  screenKey: PropTypes.string.isRequired,
  onHandleClick: PropTypes.func.isRequired,
  footer: PropTypes.objectOf({
    disabled: PropTypes.bool,
    reference: PropTypes.instanceOf(createRef()),
    authorityRoleType: PropTypes.arrayOf(PropTypes.number),
  }).isRequired,
};

export default DefaultGroupManagementFooter;
