import React from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../changeReducer/commonChangeAction';

const SBComboBox = React.forwardRef((props, ref) => {
  const changeModified = () => {
    const { setModified, canModify } = props;
    if (canModify) {
      setModified(true);
    }
  };

  function onChange(event) {
    const { onChange: onChangeEvent } = props;
    changeModified();
    if (onChangeEvent) {
      onChangeEvent(event);
    }
  }

  const languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  const getOptions = (data, keyValue) => {
    const { filterKey, filterValue } = props;
    return (
      <option value={typeof data === 'object' ? data[filterKey] : data} key={keyValue.toString()}>
        {typeof data === 'object'
          ? languageLabelCheck(data[filterValue])
          : languageLabelCheck(data)}
      </option>
    );
  };

  const {
    options,
    name,
    style,
    id,
    defaultValue,
    value,
    className,
    size,
    disabled,
    customClassName,
  } = props;

  return (
    <span className={size ? '' : 'select-box01'}>
      <select
        name={name}
        id={id}
        ref={ref}
        size={size}
        defaultValue={defaultValue}
        value={value}
        onChange={event => onChange(event)}
        style={style}
        disabled={disabled}
        className={!customClassName && disabled ? 'disabled' : className}
      >
        {options &&
          options.map((data, keyValue) => {
            return getOptions(data, keyValue);
          })}
      </select>
    </span>
  );
});

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
  null,
  { forwardRef: true },
)(SBComboBox);
