import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { performDelete } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';

/**
 * ContentDeleteDialog is dialog to display information to delete selected language content.
 * @author Gobi R
 */
export default class ContentDeleteDialog extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {};
  }

  /**
   * onOkClick is to delete selected language content.
   */
  onOkClick = () => {
    const { closeDialog, selectedRow, refresh } = this.props;
    performDelete(`${APIENDPOINTS.CONTENTDELETE}/${selectedRow.code}/${selectedRow.label}`)
      .then((response) => response.text())
      .then((data) => {
        if (data === 'Success') {
          refresh();
        }
      });
    closeDialog(true);
  };

  /**
   * onCancelClick is to cancel deletion process.
   */
  onCancelClick = () => {
    const { closeDialog } = this.props;
    closeDialog(false);
  };

  /**
   * content is to show dialog content.
   */
  content = () => {
    return ['sure_delete_content'];
  };

  /**
   * render is to load sub components.
   * @returns jsx elements
   */
  render() {
    const { open } = this.props;
    return (
      <div>
        <SBDialog
          open={open}
          header="delete_confirmation"
          type="confirmation"
          content={this.content()}
          onOk={() => this.onOkClick()}
          onCancel={() => this.onCancelClick()}
          width="400px"
        />
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ContentDeleteDialog.propTypes = {
  closeDialog: PropTypes.func.isRequired,
  code: PropTypes.string,
  refresh: PropTypes.func.isRequired,
  selectedRow: PropTypes.shape({
    code: PropTypes.string,
    label: PropTypes.string,
    value: PropTypes.string,
  }).isRequired,
  open: PropTypes.bool.isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
ContentDeleteDialog.defaultProps = {
  code: null,
};
