import React, { Component } from 'react';
import { connect } from 'react-redux';
import UNDERSCORE from '../../../v1/common/utils';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../changeReducer/commonChangeAction';
import SBComboBox from '../combobox/SBComboBox';
import SBTextbox from '../sbtextbox/SBTextbox';

class SBRadioButton extends Component {
  changeModified = () => {
    const { setModified, canModify } = this.props;
    if (canModify) {
      setModified(true);
    }
  };

  onChange = (key, event, value, headkey, canModify) => {
    const { onChange } = this.props;
    this.changeModified();
    if (onChange) {
      onChange(key, event, value, headkey, canModify);
    }
  };

  /**
   * onKeyDown is used to handle on key down event
   * @param event
   * @param value
   * @param key
   */
  onKeyDown = (event, value, key, headKey) => {
    const { onKeyDown } = this.props;
    if (onKeyDown) onKeyDown(event, value, key, headKey);
  };

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  getComponent = (componentObj, dataSource, defaultValue) => {
    switch (componentObj.component) {
      case 'combobox':
        return (
          <div>
            <SBComboBox
              options={componentObj.options}
              name={componentObj.name}
              style={componentObj.style}
              id={componentObj.id}
              ref={componentObj.ref}
              defaultValue={defaultValue[componentObj.key]}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              filterKey={componentObj.filterKey}
              filterValue={componentObj.filterValue}
              className={componentObj.className}
              size={componentObj.size}
              disabled={componentObj.disabled}
              key={componentObj.key}
              headKey={componentObj.headKey}
              canModify={componentObj.canModify}
              isNotMultiLang={componentObj.isNotMultiLang}
              onChange={(event, value) =>
                this.onChange(
                  componentObj.key,
                  event,
                  value,
                  componentObj.headKey,
                  componentObj.canModify,
                )
              }
            />
          </div>
        );
      case 'textbox':
        return (
          <div>
            <SBTextbox
              placeholder={componentObj.placeholder}
              type={componentObj.type}
              maxLength={componentObj.maxLength}
              name={componentObj.name}
              classes={componentObj.classes}
              readOnly={componentObj.readOnly}
              size={componentObj.size}
              style={componentObj.style}
              defaultValue={componentObj.defaultValue}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              ref={componentObj.ref}
              id={componentObj.id}
              key={componentObj.key}
              headKey={componentObj.headKey}
              canModify={componentObj.canModify}
              onChange={(event, content) =>
                this.onChange(
                  componentObj.key,
                  event,
                  content,
                  componentObj.headKey,
                  componentObj.canModify,
                )
              }
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
              disabled={componentObj.disabled}
            />
          </div>
        );
      default:
        return componentObj.value;
    }
  };

  render() {
    const {
      name,
      options,
      value,
      defaultValue,
      filterKey,
      componentList,
      buttonStyle,
      vertical,
      disabled,
      style,
      dataSource,
      radioKey,
      headKey,
      canModify,
    } = this.props;
    return (
      <div style={vertical ? { display: 'grid' } : style}>
        {options &&
          options.map((option, index) => {
            let data = option;
            const dataType = typeof data;
            if (dataType === 'object') {
              data = data[filterKey];
            }
            return (
              <span
                className={buttonStyle ? 'radio-toggle' : ''}
                key={index.toString()}
                style={vertical ? { paddingBottom: 8 } : {}}
              >
                <input
                  key={`input${name}${index.toString()}`}
                  type="radio"
                  name={name}
                  id={`radio${name}${index}`}
                  onChange={(event) => this.onChange(radioKey, event, data, headKey, canModify)}
                  {...(!UNDERSCORE.isUndefined(value)
                    ? { checked: data === value }
                    : { defaultChecked: data === defaultValue })}
                  disabled={disabled}
                />
                <label
                  style={buttonStyle ? {} : { marginRight: 14 }}
                  key={`label${name}${index.toString()}`}
                  className={`${buttonStyle ? 'switch-off' : 'radiobtn'} ${
                    disabled ? 'disabled' : ''
                  }`}
                  htmlFor={`radio${name}${index}`}
                >
                  {this.getComponent(componentList[index], dataSource, defaultValue)}
                </label>
              </span>
            );
          })}
      </div>
    );
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SBRadioButton);
