import React from 'react';
import { connect } from 'react-redux';
import UNDERSCORE from '../../../v1/common/utils';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../changeReducer/commonChangeAction';

const SBRadioButton = React.forwardRef((props) => {
  const changeModified = () => {
    const { setModified, canModify } = props;
    if (canModify) {
      setModified(true);
    }
  };

  const onRadioChange = (event, value) => {
    const { onChange } = props;
    changeModified();
    if (onChange) {
      onChange(event, value);
    }
  };

  const languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  const {
    name,
    options,
    value,
    defaultValue,
    filterKey,
    filterValue,
    buttonStyle,
    vertical,
    disabled,
    style,
  } = props;
  return (
    <div style={vertical ? { display: 'grid' } : style}>
      {options &&
        options.map((option, index) => {
          let data = option;
          const dataType = typeof data;
          let dataValue = '';
          if (dataType === 'object') {
            dataValue = data[filterValue];
            data = data[filterKey];
          }
          return (
            <span
              className={buttonStyle ? 'radio-toggle' : ''}
              key={index.toString()}
              style={vertical ? { paddingBottom: 8 } : {}}
            >
              <input
                key={`input${name}${index.toString()}`}
                type="radio"
                name={name}
                id={`radio${name}${index}`}
                onChange={(event) => onRadioChange(event, data)}
                {...(!UNDERSCORE.isUndefined(value)
                  ? { checked: data === value }
                  : { defaultChecked: data === defaultValue })}
                disabled={disabled}
              />
              <label
                style={buttonStyle ? {} : { marginRight: 14 }}
                key={`label${name}${index.toString()}`}
                className={`${buttonStyle ? 'switch-off' : 'radiobtn'} ${
                  disabled ? 'disabled' : ''
                }`}
                htmlFor={`radio${name}${index}`}
              >
                {languageLabelCheck(dataType === 'object' ? dataValue : data)}
              </label>
            </span>
          );
        })}
    </div>
  );
});

const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(
  SBRadioButton,
);
