/**
 * SecurityPolicyDetail is used to load the data
 * @author: Loganathan R P
 */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SBDialog from '../../../../common/component/sbdialog/SBDialog';
import headerInformation from '../constants/HeaderConstant';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import CircularSpinner from '../../../../common/component/loadingSpinner/CircularSpinner';
import CustomTableFrame from '../../../../v1/common/component/CustomTableFrame';
import { detailView } from '../constants/TableConstant';
import {
  getSecurityPolicyGroupList,
  setSecurityPolicyDetail,
} from '../../../../actions/securityPolicyAction';
import RouteEndPoints from '../../../../constants/RouteEndPoints';
import { post } from '../../../../actions/commonActions';
import commonChangeAction from '../../../../common/changeReducer/commonChangeAction';
import APIENDPOINTS from '../../../../constants/APIEndPoints';
import { errorMessageConstants } from '../constants/MessageConstant';

class SecurityPolicyView extends Component {
  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    let isDelete = false;
    let update = false;

    let securityPolicyId = '';
    if (location.state) {
      param = location.state.showData;
      isDelete = location.state.param.delete === true ? location.state.param.delete : false;
      update = location.state.param.update ? location.state.param.update : false;

      securityPolicyId = location.state.param.securityPolicyId;
    } else {
      history.push('error');
    }
    this.state = {
      propData: location.state.param,
      pageLoading: true,
      dataSource: param,
      modalIsOpen: false,
      isDelete,
      update,
      securityPolicyId,
      deleteHasError: false,
      openErrorDialog: false,
      dialogWidth: '400px',
      message: '',
    };
    this.returnRef = createRef();
    this.okButtonRef = createRef();
  }

  componentDidMount() {
    this.setState({
      pageLoading: false,
    });
  }

  componentDidUpdate() {
    const { finish } = this.props;
    if (finish && this.returnRef.current) {
      this.returnRef.current.focus();
    } else if (this.okButtonRef.current) {
      this.okButtonRef.current.focus();
    }
  }

  onHandleClick = () => {
    const {
      history,
      finish,
      makeSecurityPolicyGroupList: setGroupListSelected,
      location,
    } = this.props;
    const { isDelete, propData } = this.state;
    if (finish || isDelete) {
      history.push(RouteEndPoints.SECURITYPOLICY);
    } else {
      console.log(propData);
      const { update } = location.state.param;
      let param = {};
      if (location.state) {
        param = location.state.param;
      }
      setGroupListSelected(param);
      if (update) {
        history.push(RouteEndPoints.CHANGESECURITYPOLICY, {
          param: { securityPolicyId: param.dataSource.securityPolicyId },
        });
      } else {
        history.push(RouteEndPoints.ADDSECURITYPOLICY);
      }
    }
  };

  handleOkClick = () => {
    const { isDelete, dataSource } = this.state;
    if (isDelete) {
      if (
        dataSource.groupList.length > 1 || dataSource.groupList.length === 1
          ? dataSource.groupList[0] !== ''
          : false
      ) {
        this.setState({ deleteHasError: true });
      } else {
        this.setState({
          modalIsOpen: true,
        });
      }
    } else {
      this.setState({
        modalIsOpen: true,
      });
    }
  };

  onOkDialog = () => {
    const { dynamicData, reference, dataSource, isDelete, securityPolicyId } = this.state;
    const {
      history,
      setModified,
      location,
      securityPolicyDetail,
      makeSecurityPolicyDetail,
    } = this.props;
    dataSource.groupList = dataSource.assignTarget ? dataSource.assignTarget.split(',') : [];

    dataSource.groupAssigned = securityPolicyDetail ? securityPolicyDetail.groupAssigned : false;
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      const response = await post(APIENDPOINTS.SECURITY_POLICY_ADD, dataSource);
      const resultSet = await response.json();
      if (resultSet.status === 'OK') {
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
        });
        setModified(false);
        history.push(RouteEndPoints.SECURITYPOLICYADDFINISH);
      } else if (resultSet.status === 'ALREADY_REPORTED') {
        const securityPolicyNameError = { error: 'samecontent' };
        dynamicData.securityPolicyNameError = securityPolicyNameError;
        this.setState({
          pageLoading: false,
          dynamicData,
          modalIsOpen: false,
        });
        reference.securityPolicyNameRef.current.focus();
      } else {
        setModified(false);
        history.push(RouteEndPoints.ERROR);
      }
    };
    const updateProcess = async () => {
      const response = await post(APIENDPOINTS.SECURITY_POLICY_CHANGE, dataSource);
      const resultSet = await response.json();
      if (resultSet.status === 'OK') {
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
        });
        setModified(false);
        makeSecurityPolicyDetail({});
        history.push(RouteEndPoints.SECURITYPOLICYUPDATEFINISH);
      } else if (resultSet.status === 'ALREADY_REPORTED') {
        const securityPolicyNameError = { error: 'samecontent' };
        dynamicData.securityPolicyNameError = securityPolicyNameError;
        this.setState({
          pageLoading: false,
          dynamicData,
          modalIsOpen: false,
        });
        reference.securityPolicyNameRef.current.focus();
      } else {
        setModified(false);
        makeSecurityPolicyDetail({});
        history.push(RouteEndPoints.ERROR);
      }
    };
    const deleteProcess = async () => {
      const response = await post(APIENDPOINTS.SECURITY_POLICY_DELETE, securityPolicyId);
      const resultSet = await response.json();
      setModified(false);
      if (resultSet.status === 'OK') {
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
        });
        history.push(RouteEndPoints.SECURITYPOLICYDELETEFINISH);
      } else if (resultSet.status === 'EXPECTATION_FAILED') {
        this.setState({
          message: resultSet.message,
          pageLoading: false,
          openErrorDialog: true,
          dialogWidth: '400px',
          modalIsOpen: false,
        });
      } else {
        history.push(RouteEndPoints.ERROR);
      }
    };
    if (location.state.param.update) {
      updateProcess();
    } else if (isDelete) {
      deleteProcess();
    } else {
      saveProcess();
    }
  };

  onOkErrorDialog = () => {
    const { history } = this.props;
    history.push(RouteEndPoints.SECURITYPOLICY);
  };

  render() {
    const {
      pageLoading,
      dataSource,
      modalIsOpen,
      isDelete,
      deleteHasError,
      update,
      message,
      openErrorDialog,
      dialogWidth,
    } = this.state;
    const { isCheck, finish, localData } = this.props;

    const addHeader = isCheck ? 'securityPolicyAddCheck' : 'securityPolicyAddFinish';
    const deleteHeader = isCheck ? 'securityPolicydelete' : 'deletesuccess';
    const updateHeader = isCheck ? 'securityPolicyUpdateCheck' : 'securityPolicyChangeFinish';
    let headerInfo = headerInformation(isDelete ? deleteHeader : addHeader);
    headerInfo = update ? headerInformation(updateHeader) : headerInfo;
    let saveConfirmation = update ? 'want_to_update_data' : 'want_to_insert_data';
    saveConfirmation = isDelete ? 'want_to_delete_data' : saveConfirmation;
    let confirmationHeader = update ? 'update_confirmation' : 'save_confirmation';
    confirmationHeader = isDelete ? 'delete_confirmation' : confirmationHeader;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          hasTitleDescription
          title={headerInfo.title}
          description={headerInfo.description}
        />
        <div className="contents-main">
          {!finish ? (
            <>
              <CustomTableFrame
                tableList={detailView().securityPolicy}
                dataSource={dataSource}
                defaultValue={{}}
              />
              {!isCheck && (
                <CustomTableFrame
                  tableList={detailView().createdPolicyAccount}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              )}
              <CustomTableFrame
                tableList={detailView().androidOS}
                dataSource={dataSource}
                defaultValue={{}}
              />

              <CustomTableFrame
                tableList={
                  isCheck ? detailView(localData).applicationNo : detailView(localData).application
                }
                dataSource={dataSource}
                defaultValue={{}}
              />

              <CustomTableFrame
                tableList={isCheck ? detailView(localData).imeiNo : detailView().imei}
                dataSource={dataSource}
                defaultValue={{}}
              />
              <CustomTableFrame
                tableList={detailView().antiVirus}
                dataSource={dataSource}
                defaultValue={{}}
              />

              <CustomTableFrame
                tableList={detailView().assignTarget}
                dataSource={dataSource}
                defaultValue={{}}
              />
            </>
          ) : (
            <div />
          )}
          <div className="clearfix">
            <div
              className="floatright"
              style={{ display: 'flex', justifyContent: 'space-between' }}
            >
              {isCheck && (
                <SBButton
                  id="btn_ok"
                  color="green"
                  type="button"
                  ref={this.okButtonRef}
                  title="ok"
                  style={{ display: 'block' }}
                  onClick={() => this.handleOkClick()}
                />
              )}
              <SBButton
                id="btn_back"
                noMarginRight
                type="button"
                ref={this.returnRef}
                title={finish ? 'security_policy_finish' : 'cancel'}
                style={{ display: 'block' }}
                onClick={event => this.onHandleClick(event)}
              />
            </div>
          </div>
          {deleteHasError && (
            <div>
              <CustomTableFrame
                tableList={[
                  {
                    errorMessage: errorMessageConstants.securityPolicyDeleteError.groupAssociation,
                    tdStyle: { border: 'unset', padding: '10px 0' },
                  },
                ]}
                dataSource={{}}
                defaultValue={{}}
              />
            </div>
          )}
        </div>
        <SBDialog
          open={modalIsOpen}
          header={confirmationHeader}
          type="confirmation"
          content={saveConfirmation}
          onCancel={() => {
            this.setState({ modalIsOpen: false });
          }}
          onOk={() => {
            this.onOkDialog();
          }}
          width="400px"
        />
        <SBDialog
          type="error"
          width={dialogWidth}
          header="error"
          okButtonTitle="ok"
          content={message}
          open={openErrorDialog}
          onOk={() => this.onOkErrorDialog()}
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = dispatch => {
  return {
    setModified: data => dispatch(commonChangeAction(data)),
    makeSecurityPolicyGroupList: data => dispatch(getSecurityPolicyGroupList(data)),
    makeSecurityPolicyDetail: data => dispatch(setSecurityPolicyDetail(data)),
  };
};
/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  const { securityPolicyDetail } = state.securityPolicy;
  return {
    localData: data,
    securityPolicyDetail,
  };
};
/**
 * propTypes is used for validation props with its type.
 */
SecurityPolicyView.propTypes = {
  setModified: PropTypes.func.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(SecurityPolicyView));
