import React, { Component } from 'react';
import SBComboBox from '../combobox/SBComboBox';

class SBTimePicker extends Component {
  onChange = event => {
    const { onChange } = this.props;
    if (onChange) {
      onChange(event);
    }
  };

  getTimeOptions = () => {
    const options = [{ value: '', key: 'empty' }];
    let index = 0;
    while (index < 24) {
      const value = `0${index}`.slice(-2).concat(':00');
      options.push({ value, key: value });
      index += 1;
    }
    return options;
  };

  render() {
    const { name, id, className, disabled, style, defaultValue, value, size } = this.props;
    return (
      <SBComboBox
        options={this.getTimeOptions()}
        name={name}
        style={style}
        id={id}
        defaultValue={defaultValue}
        value={value}
        filterKey="key"
        filterValue="value"
        className={className}
        size={size}
        disabled={disabled}
        onChange={event => this.onChange(event)}
        isNotMultiLang
      />
    );
  }
}
export default SBTimePicker;
