import { createRef } from 'react';
import { errorMessageConstants } from './MultipleAdminSettingConstants';
import {
  validatePassword,
  validateLoginId,
  validateEmail,
  validateMultipleIps,
} from '../../common/CommonValidations/CommonValidations';

/**
 * initializeRefs is used to create reference to component
 * @return referenceMap
 *
 */

export function initializeRefs() {
  return {
    authorityRef: createRef(),
    loginIdRef: createRef(),
    passwordRef: createRef(),
    emailIdRef: createRef(),
    ipAddressRef: createRef(),
    cancelRef: createRef(),
  };
}

/**
 * setFocusinerrorField is used to set focus to error field
 * @param dynamicData
 * @param reference
 *
 */

export function setFocusinerrorField(dynamicData, reference) {
  if (dynamicData.loginError && dynamicData.loginError !== '') {
    reference.loginIdRef.current.focus();
  } else if (dynamicData.passwordError && dynamicData.passwordError !== '') {
    reference.passwordRef.current.focus();
  } else if (dynamicData.emailError && dynamicData.emailError !== '') {
    reference.emailIdRef.current.focus();
  } else if (dynamicData.ipError && dynamicData.ipError !== '') {
    reference.ipAddressRef.current.focus();
  }
}

/**
 * setValidationErrorMessage is used to set error message if the validation is not satisified
 * @param key
 * @param validationResultObject
 * @param dynamicData
 *
 */

function setValidationErrorMessage(key, validationResultObject, dynamicData) {
  const tempDynamicData = dynamicData;
  tempDynamicData[key] = errorMessageConstants[key][validationResultObject.error];
  return tempDynamicData;
}

/**
 * validateInputs is used to validate inputs
 * @param dataSource
 * @param dynamicData
 * @param isUpdate
 *
 */

export function validateInputs(dataSource, dynamicData, isUpdate) {
  let tempDynamicData = dynamicData;
  const passwordRegex = /^[*]{8}$/;
  const loginValidation = validateLoginId(dataSource.loginId);
  const passwordValidation =
    isUpdate && passwordRegex.test(dataSource.password)
      ? { status: true }
      : validatePassword(dataSource.password);
  const emailValidation = validateEmail(dataSource.emailID);
  const ipValidation = validateMultipleIps(dataSource.ipAddress);
  tempDynamicData = setValidationErrorMessage('loginError', loginValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('passwordError', passwordValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('emailError', emailValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('ipError', ipValidation, tempDynamicData);
  tempDynamicData.status =
    loginValidation.status &&
    passwordValidation.status &&
    emailValidation.status &&
    ipValidation.status;
  return tempDynamicData;
}

