import UNDERSCORE from '../../v1/common/utils';

function AddEnterEventListener(inputId, buttonId) {
  const input = document.getElementById(inputId);
  if (!UNDERSCORE.isNull(input))
    input.addEventListener('keyup', function(event) {
      if (event.keyCode === 13) {
        event.preventDefault();
        document.getElementById(buttonId).click();
      }
    });
}

export default AddEnterEventListener;
