import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBTextbox from '../../../common/component/sbtextbox/SBTextbox';
import SBCheckbox from '../../../common/component/checkbox/SBCheckbox';
import TableFrame from './TableFrame';
import UNDERSCORE from '../utils';
import RoleResolver from '../../../common/component/guard/RoleResolver';

class SearchFilter extends Component {
  values = {};

  constructor(props) {
    super(props);
    const { filterIconsWithCheckBox, searchKeyword, searchText } = this.props;
    if (!UNDERSCORE.isUndefined(filterIconsWithCheckBox)) {
      this.values.checkBoxKeys = filterIconsWithCheckBox.defaultValues;
    }
    if (!UNDERSCORE.isUndefined(searchKeyword)) {
      this.values[searchKeyword] = searchText;
    }
  }

  onSearchAction = (key, event, canReset) => {
    const { onSearchAction } = this.props;
    if (canReset) {
      const getStore = JSON.parse(localStorage.getItem('deviceSearch'));
      const { searchKeyword: deviceSearch, checkBoxKeys: statusList } =
        (getStore && getStore.deviceSearch) || this.values;
      this.values.searchKeyword = deviceSearch;
      this.values.checkBoxKeys = statusList;
    }
    onSearchAction(key, event, this.values);
  };

  onTableFrameEvent = (key, event, value) => {
    if (
      event &&
      event.target &&
      event.target.type &&
      (event.target.type === 'checkbox' || event.target.type === 'radio')
    ) {
      this.values[key] = value;
    } else if (
      value &&
      ((value.input && value.input[0].className.includes('datepicker')) ||
        (value.target && value.target.classList.contains('datepicker')))
    ) {
      this.values[key] = event;
    } else {
      this.values[key] = event.target.value;
    }
  };

  onTextInputChange = (key, event) => {
    this.values[key] = event.target.value;
    const { onSearchAction } = this.props;
    onSearchAction(key, event, this.values);
  };

  onKeyDown = (event, value) => {
    const { onKeyDown } = this.props;
    if (onKeyDown) onKeyDown(event, value);
  };

  onChange = (key, event, selectedValues) => {
    this.values.checkBoxKeys = selectedValues;
    const { onSearchAction } = this.props;
    onSearchAction(key, event, this.values);
  };

  getComponent(searchOption) {
    switch (searchOption.type) {
      case 'button':
        return (
          <SBButton {...searchOption} onClick={(e) => this.onSearchAction(searchOption.key, e)} />
        );
      case 'tableframe':
        return (
          <TableFrame
            onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            tableList={searchOption.viewSource}
            dataSource={searchOption.dataSource}
          />
        );
      case 'text':
      case 'password':
        return (
          <SBTextbox
            placeholder={searchOption.placeholder}
            type={searchOption.type}
            maxLength={searchOption.maxLength}
            name={searchOption.name}
            disabled={searchOption.disabled}
            size={searchOption.size}
            style={searchOption.style}
            defaultValue={searchOption.defaultValue}
            value={searchOption.value}
            id={searchOption.id}
            ref={searchOption.ref}
            onChange={(e) => this.onTextInputChange(searchOption.key, e)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
        );
      case 'inputWithErrorAndButton':
        return (
          <>
            <SBTextbox
              placeholder={searchOption.placeholder}
              type="text"
              maxLength={searchOption.maxLength}
              name={`${searchOption.name}_input`}
              disabled={searchOption.disableInput}
              size={searchOption.size}
              style={searchOption.inputStyle}
              defaultValue={searchOption.defaultValue}
              value={searchOption.inputValue}
              id={`${searchOption.id}_input`}
              ref={searchOption.ref}
              onChange={(e) => this.onTextInputChange(searchOption.inputKey, e)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <SBButton
              type="button"
              title={searchOption.buttonTitle}
              name={`${searchOption.name}_button`}
              color={searchOption.buttonColor}
              style={searchOption.buttonStyle}
              disabled={searchOption.disableButton}
              noMarginRight={searchOption.noMarginRightButton}
              id={`${searchOption.id}_button`}
              onClick={(e) => this.onSearchAction(searchOption.buttonKey, e)}
            />
            {searchOption.showErrorMessage && (
              <span id={`${searchOption.key}_errormsg`} className="red bold">
                {this.languageLabelCheck(searchOption.errorMessage)}
              </span>
            )}
          </>
        );
      default:
        return (
          <div id={searchOption.id} style={searchOption.style}>
            {searchOption.htmlcomponents}
          </div>
        );
    }
  }

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const {
      border,
      screenKey,
      filterIcons,
      searchHeader,
      headerButton,
      searchOptions,
      checkActionKey,
      filterIconsWithCheckBox,
    } = this.props;
    return (
      <div
        className={border ? 'border p15' : ''}
        style={{
          paddingBottom: 5,
        }}
      >
        {headerButton && (
          <div
            className={headerButton.bottomBorder ? 'border-bottom-dotted pb10 mb10' : 'pb10 mb10'}
          >
            <RoleResolver authorityRoleType={headerButton.authorityRoleType}>
              <SBButton
                add={headerButton.add}
                type={headerButton.type}
                title={headerButton.title}
                name={headerButton.name}
                color={headerButton.color}
                style={headerButton.style}
                disabled={headerButton.disabled}
                id={headerButton.id}
                onClick={(e) => this.onSearchAction(headerButton.key, e, headerButton.canReset)}
              />
            </RoleResolver>
          </div>
        )}
        {screenKey === 'userList' && searchHeader && (
          <div className="mb10">
            <p>{this.languageLabelCheck(searchHeader)}</p>
          </div>
        )}
        {filterIcons && (
          <div className="clearfix mb10">
            {filterIconsWithCheckBox ? (
              <SBCheckbox
                name="check"
                horizontal="true"
                withColon
                image
                filterKey="checkBoxKey"
                filterValue="name"
                imageSrcKey="url"
                value={filterIconsWithCheckBox.defaultValues}
                onChange={(event, selectedValues) =>
                  this.onChange(checkActionKey, event, selectedValues)
                }
                options={filterIcons}
              />
            ) : (
              <ul className="list-float">
                {filterIcons.map((filterIcon) => (
                  <li key={filterIcon.checkBoxKey}>
                    <img className="icon" src={filterIcon.url} alt={filterIcon.url} />
                    <span>{` : ${this.languageLabelCheck(filterIcon.name)}`}</span>
                  </li>
                ))}
              </ul>
            )}
          </div>
        )}
        {screenKey === 'userCsvList' && searchHeader && (
          <div className="mb10">
            <p>{this.languageLabelCheck(searchHeader)}</p>
          </div>
        )}
        {searchOptions && (
          <div className="clearfix">
            <ul>
              {searchOptions.map((searchOption, index) => (
                <li
                  key={index.toString()}
                  style={{
                    float:
                      searchOption.style && !UNDERSCORE.isUndefined(searchOption.style.float)
                        ? searchOption.style.float
                        : 'left',
                    marginBottom: 10,
                  }}
                >
                  {this.getComponent(searchOption)}
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};
export default connect(mapStateToProps)(SearchFilter);
