/**
 * tableList is used to generate table component
 * @param dynamicData
 * @param reference
 * @return tablelist
 *
 */

export default function tableList(comboValues, reference) {
  return [
    {
      label: 'group',
      component: 'combobox',
      key: 'groupName',
      id: 'group_name',
      name: 'groupName',
      filterKey: 'groupName',
      filterValue: 'groupName',
      style: { 'min-width': 220 },
      options: comboValues,
      ref: reference,
      isNotMultiLang: true,
    },
  ];
}
