/**
 * NoticeListSearch is used to generate search filter components in notice list screen
 * @author: V.Vijayaragavan
 */
import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import NotificationCustomTableFrame from '../NotificationCustomTableFrame/NotificationCustomTableFrame';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import { searchTableList } from '../../constants/NotificationRegTableLists';
import { validateSearchDate } from '../../validations/NotificationValidations';

class NoticeListSearch extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      searchValues: {},
      errorMessage: '',
    };
  }

  /**
   * onTableFrameEvent is used to handle table frame events
   * @param key
   * @param event
   */
  onTableFrameEvent = (key, event) => {
    const { onSearchAction } = this.props;
    const { searchValues } = this.state;
    let { errorMessage } = this.state;
    if (key === 'startDate' || key === 'endDate') {
      searchValues[key] = event;
      errorMessage = '';
    } else {
      searchValues[key] = event.target.value;
    }
    onSearchAction(searchValues, false);
    this.setState({ searchValues, errorMessage });
  };

  /**
   * onSearchAction is used to validate date format and range and handle search action
   */
  onSearchAction = () => {
    const { onSearchAction } = this.props;
    const { searchValues } = this.state;
    const startDateResult = validateSearchDate(searchValues.startDate);
    const endDateResult = validateSearchDate(searchValues.endDate);
    if (startDateResult || endDateResult) {
      if (startDateResult === 'format' || endDateResult === 'format') {
        this.setState({ errorMessage: 'date_format_invalid' });
      } else if (startDateResult === 'range' || endDateResult === 'range') {
        this.setState({ errorMessage: 'date_range_invalid' });
      }
    } else {
      this.setState({ errorMessage: '' });
      onSearchAction(searchValues, true);
    }
  };

  /**
   * render is used to render the component
   */
  render() {
    const { reference, languageList, searchParam } = this.props;
    const { errorMessage } = this.state;
    return (
      <div className="contents-main mb15 border p15 clearfix">
        <NotificationCustomTableFrame
          onTableFrameEvent={(key, event) => this.onTableFrameEvent(key, event)}
          tableList={searchTableList(searchParam, reference, languageList, errorMessage)}
          dataSource={{}}
        />
        <div className="floatright">
          <SBButton
            id="search-button"
            type="button"
            color="green"
            title="search"
            key="searchButton"
            noMarginRight
            onClick={() => this.onSearchAction()}
          />
        </div>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
NoticeListSearch.propTypes = {
  onSearchAction: PropTypes.func.isRequired,
  reference: PropTypes.instanceOf(createRef()),
  languageList: PropTypes.arrayOf(PropTypes.object),
  searchParam: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NoticeListSearch.defaultProps = {
  reference: createRef(),
  languageList: [],
};

export default NoticeListSearch;
