import React, { PureComponent } from 'react';
import ClipLoader from 'react-spinners/ClipLoader';

class CircularSpinner extends PureComponent {
  render() {
    const { children, loading, css } = this.props;
    return (
      <>
        <ClipLoader
          css={css || { position: 'absolute', top: '50%', left: '50%' }}
          size={50}
          color="#a9cb02"
          loading={loading}
        />
        {!loading && children}
      </>
    );
  }
}

export default CircularSpinner;
