/**
 * groupRegistrationHeader is to set group registration screen caption
 * @param key
 * @param type
 * @return serviceHeaderMap
 */
export const groupRegistrationHeader = (key, type) => {
  const defaultHeader = {
    title: 'group_registration',
    description: ['group_registration_description'],
  };

  const defaultCompletionHeader = {
    title: defaultHeader.title,
    description: ['group_registration_complete'],
  };

  const serviceHeaderMap = {
    groupRegistration: [
      { title: 'group_information', description: ['group_information_description'] },
      { ...defaultHeader },
      { title: 'edit_group_information', description: ['edit_group_information_description'] },
      { ...defaultHeader },
      { title: 'delete_group', description: ['delete_group_description'] },
    ],
    groupConfirmation: [
      { title: defaultHeader.title, description: ['confirm_your_group_setting'] },
      { ...defaultCompletionHeader },
      { title: defaultHeader.title, description: ['group_change_done'] },
      { ...defaultCompletionHeader },
      { title: 'delete_group', description: ['group_delete_done'] },
    ],
  };
  return serviceHeaderMap[key][type];
};
