/**
 * DashboardSettingFooter is used to load the footer of the Dashboard Setting Screen
 * @author: Aakash Alagarsamy
 */

import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import SBButton from '../../../common/component/sbbutton/SBButton';

/**
 * DashboardSettingFooter is used to create footer component
 * @param props
 * @return DashboardSettingFooterComponent
 */
const DashboardSettingFooter = (props) => {
  const { footer, onHandleClick, reference } = props;
  return (
    <div className="clearfix">
      <div className="floatright">
        <SBButton
          id="btn_ok"
          color="green"
          type="button"
          title="ok"
          disabled={footer.okDisable}
          style={footer.primaryVisibile ? { display: '' } : { display: 'none' }}
          onClick={() => onHandleClick('save')}
        />
        <SBButton
          id="btn_cancel"
          noMarginRight
          type="button"
          title="cancel"
          style={footer.primaryVisibile ? { display: '' } : { display: 'none' }}
          onClick={() => onHandleClick('cancel')}
        />
        <SBButton
          id="btn_change"
          color="green"
          noMarginRight
          type="button"
          title="change"
          ref={reference.changeRef}
          disabled={footer.browsingOnly}
          style={footer.primaryVisibile ? { display: 'none' } : { display: 'block' }}
          onClick={() => onHandleClick('change')}
        />
      </div>
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
DashboardSettingFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  footer: PropTypes.objectOf(PropTypes.bool).isRequired,
};

export default withRouter(DashboardSettingFooter);
