/**
 * AppPayment is used to load payment screen
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import {
  isTextAreaLengthValid,
  isAlphaOrNumeric,
  isNotUndefinedOrNotEmpty,
} from '../../../common/CommonValidations/CommonValidations';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import checkLanguage from '../../../language/checkLanguage';
import { tableDMList } from '../Constants/AppFrameConstants';
import { setDmCode } from '../Constants/AppFieldConstants';
import { setPaymentReference } from '../Constants/AppReferenceConstants';
import headerInformation from '../Constants/AppManageHeaderConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import AppCRUDFooter from './AppCRUDFooter';
import PaymentRegistered from './PaymentRegistered';
import PaymentContent from './PaymentContent';
import UNDERSCORE from '../../../v1/common/utils';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import { setAppRegisterEntity, setAppUpdateEntity } from '../../../actions/TenantActions';

class AppPayment extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('apppayment'),
      dmSource: setDmCode(param),
      reference: setPaymentReference(),
      paymentSource: {
        approvalNumber: '',
        approvalRemarks: '',
        approvalSelected: 0,
        paymentNumber: param.paymentNumber ? [...param.paymentNumber] : [],
      },
      modified: false,
      errorMessage: { approvalNoError: '', contentError: '', selectError: '' },
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount is used to setb initial focus to the screen
   */

  componentDidMount() {
    const { reference, paymentSource } = this.state;
    const { paymentNumber } = paymentSource;
    if (paymentNumber.length && reference.registerRef.current) {
      reference.registerRef.current.focus();
    } else if (reference.paymentNumberRef.current) reference.paymentNumberRef.current.focus();
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    switch (key) {
      case 'load':
        this.loadPaymentSource();
        break;
      case 'add':
        this.addPaymentSource();
        break;
      case 'delete':
        this.deletePaymentSource();
        break;
      case 'save':
      case 'cancel':
        this.callService(key);
        break;
      default:
        break;
    }
  };

  /**
   * callService is used to call service based on key
   * @param key
   */

  callService = key => {
    const {
      location,
      history,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { paymentSource, modified } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    if (key === 'save') {
      param.paymentNumber = paymentSource.paymentNumber;
      if (location.state.isUpdate) {
        makeAppUpdateEntity({
          param,
          globalModified: modified || location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
      } else {
        makeAppRegisterEntity({
          param,
          autoGenertedDmCode: location.state.autoGenertedDmCode,
          globalModified: modified || location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
      }
    } else if (modified) {
      this.setState({ modalIsOpen: true });
    } else if (location.state.isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * onOkDialog is used to handle on ok click of dialog
   */

  onOkDialog = () => {
    const {
      location,
      history,
      setModified,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    setModified(location.state.globalModified);
    if (location.state.isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * loadPaymentSource is used to load payment source details
   */

  loadPaymentSource = () => {
    let { paymentSource } = this.state;
    const { paymentNumber, approvalSelected } = paymentSource;
    const errorMessage = { approvalNoError: '', contentError: '', selectError: '' };
    paymentSource = {
      approvalNumber: paymentNumber[parseInt(approvalSelected, 10)].approvalNumber,
      approvalRemarks: paymentNumber[parseInt(approvalSelected, 10)].approvalRemarks,
      paymentNumber,
      approvalSelected,
    };
    this.setState({ paymentSource, modified: true, errorMessage });
  };

  /**
   * performValidations is used to perform validations
   * @param approvalNumber
   * @param approvalRemarks
   * @param listLength
   * @return status
   */

  performValidations = (approvalNumber, approvalRemarks, listLength) => {
    const errorMessage = { approvalNoError: '', contentError: '', selectError: '' };
    const { reference } = this.state;
    let status = true;
    const maxLength = 20;
    const maxListLength = 20;
    const approvalNumberLength = approvalNumber.toString().length;
    if (listLength === maxListLength) {
      errorMessage.approvalNoError = 'max_payment_list_reach';
      status = false;
      if (reference.paymentNumberRef.current) reference.paymentNumberRef.current.focus();
    } else if (approvalNumber.toString().length === 0) {
      errorMessage.approvalNoError = 'enter_the_approval_number';
      status = false;
      if (reference.paymentNumberRef.current) reference.paymentNumberRef.current.focus();
    } else if (approvalNumberLength > maxLength) {
      errorMessage.approvalNoError = 'approval_number_alpha_numeric';
      status = false;
      if (reference.paymentNumberRef.current) reference.paymentNumberRef.current.focus();
    } else if (!isAlphaOrNumeric(approvalNumber)) {
      errorMessage.approvalNoError = 'approval_number_alpha_numeric';
      status = false;
      if (reference.paymentNumberRef.current) reference.paymentNumberRef.current.focus();
    }

    if (isNotUndefinedOrNotEmpty(approvalRemarks) && this.remarksValidation(approvalRemarks)) {
      errorMessage.contentError = 'settlement_remarks_invalid';
      if (status && reference.paymentRemarksRef.current)
        reference.paymentRemarksRef.current.focus();
      status = false;
    }
    this.setState({ errorMessage });
    return status;
  };

  /**
   * addPaymentSource is used to add approval data
   */

  addPaymentSource = () => {
    let approvalSelected = '';
    let { paymentSource } = this.state;
    const { approvalNumber, approvalRemarks, paymentNumber } = paymentSource;
    const validationStatus = this.performValidations(
      approvalNumber,
      approvalRemarks,
      paymentNumber.length,
    );
    if (validationStatus) {
      const index = UNDERSCORE.findIndex(paymentNumber, { approvalNumber });
      if (index >= 0) {
        paymentNumber.splice(index, 1, {
          approvalNumber: paymentSource.approvalNumber,
          approvalRemarks: paymentSource.approvalRemarks,
        });
        approvalSelected = index;
      } else {
        paymentNumber.push({
          approvalNumber: paymentSource.approvalNumber,
          approvalRemarks: paymentSource.approvalRemarks,
        });
        approvalSelected = paymentNumber.length - 1;
      }
      paymentSource = {
        approvalNumber: '',
        approvalRemarks: '',
        approvalSelected,
        paymentNumber,
      };
      this.setState({
        paymentSource,
        errorMessage: { approvalNoError: '', contentError: '', selectError: '' },
      });
    }
  };

  /**
   * deletePaymentSource is used to delete approval data
   */

  deletePaymentSource = () => {
    let { paymentSource } = this.state;
    const { errorMessage, reference } = this.state;
    const { paymentNumber, approvalNumber } = paymentSource;
    const index = UNDERSCORE.findIndex(paymentNumber, { approvalNumber });
    if (approvalNumber.toString().length === 0) {
      errorMessage.approvalNoError = '';
      errorMessage.contentError = '';
      errorMessage.selectError = 'gisun_select_data';
      paymentSource.approvalRemarks = '';
      if (reference.registerRef.current) reference.registerRef.current.focus();
      this.setState({ errorMessage, paymentSource });
    } else {
      if (index >= 0) {
        paymentNumber.splice(index, 1);
      }
      paymentSource = {
        approvalNumber: '',
        approvalRemarks: '',
        approvalSelected: 0,
        paymentNumber,
      };
      this.setState({
        paymentSource,
        errorMessage: { approvalNoError: '', contentError: '', selectError: '' },
      });
    }
  };

  /**
   * onHandleChange is used to handle change event
   */

  onHandleChange = (key, event, value) => {
    const { paymentSource } = this.state;
    const { setModified } = this.props;
    paymentSource[key] = key === 'approvalSelected' ? event.target.value : value;
    this.setState({ paymentSource, modified: true });
    setModified(true);
  };

  /**
   * onHandleChange is used to remarks validation
   * @return status
   */

  remarksValidation = value => {
    let status = true;
    const minLength = 1;
    const maxLength = 400;
    if (isTextAreaLengthValid(value, minLength, maxLength)) {
      status = false;
    }
    return status;
  };

  /**
   * render is used to render the component
   * @return AppPaymentComponent
   */

  render() {
    const {
      headerData,
      dmSource,
      paymentSource,
      reference,
      errorMessage,
      footer,
      modalIsOpen,
    } = this.state;
    return (
      <div>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <div className="clearfix mb10">
            <div className="mb10">
              <CustomTableFrame tableList={tableDMList} dataSource={dmSource} defaultValue={{}} />
            </div>
            <div className="mb10">
              <PaymentRegistered
                reference={reference}
                errorMessage={errorMessage}
                paymentSource={paymentSource}
                onHandleClick={key => this.onHandleClick(key)}
                onHandleChange={(key, event, value) => this.onHandleChange(key, event, value)}
              />
            </div>
            <div className="mb10">
              <PaymentContent
                errorMessage={errorMessage}
                reference={reference}
                paymentSource={paymentSource}
                onHandleClick={key => this.onHandleClick(key)}
                onHandleChange={(key, event, value) => this.onHandleChange(key, event, value)}
              />
            </div>
            <div>
              <AppCRUDFooter 
                reference={reference}
                footer={footer} 
                onHandleClick={key => this.onHandleClick(key)} 
              />
            </div>
            <SBDialog
              open={modalIsOpen}
              header="cancel_confirmation"
              type="confirmation"
              content="cancel_confirmation_message"
              onCancel={() => {
                this.setState({ modalIsOpen: false });
              }}
              onOk={() => {
                this.onOkDialog();
              }}
              width="400px"
            />

            <div />
          </div>
        </div>
      </div>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    setAppRegisterEntity: data => {
      dispatch(setAppRegisterEntity(data));
    },
    setAppUpdateEntity: data => {
      dispatch(setAppUpdateEntity(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

AppPayment.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
      globalModified: PropTypes.bool.isRequired,
      autoGenertedDmCode: PropTypes.string.isRequired,
    }),
    pathname: PropTypes.string.isRequired,
  }).isRequired,
  setAppRegisterEntity: PropTypes.func.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
AppPayment.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(AppPayment));
