/** Base Files */
import React, { createRef } from 'react';
import PropTypes from 'prop-types';
import { Role } from '../../constants/Role';

/** SBComponents */
import SearchFilter from '../../v1/common/component/SearchFilter';

/** Utility Files */
import { searchComponentList, searchFilterCheckBoxList } from '../constants/deviceConstants';

/**
 * DeviceSearch performs serch room component rendering
 * @author Aswin Palanisamy
 * @param props
 * @return searchFilter
 */
const DeviceSearch = (props) => {
  const {
    onSearchAction,
    onKeyDown,
    reference,
    screenKey,
    defaultValues,
    searchText,
    searchKeyword,
    disabled,
    searchHeader,
  } = props;
  const getComponent = searchComponentList(reference, searchText, disabled, Role.deviceAuthority);
  const searchOptions = getComponent[screenKey];
  return (
    <div className={`${screenKey !== 'app' && 'contents-main'} mb15`}>
      {searchOptions && (
        <SearchFilter
          border
          screenKey={screenKey}
          searchHeader={searchHeader}
          searchOptions={searchOptions}
          searchText={searchText}
          searchKeyword={searchKeyword}
          checkActionKey={`${screenKey}Search`}
          filterIconsWithCheckBox={{ defaultValues }}
          filterIcons={searchFilterCheckBoxList(screenKey)}
          onKeyDown={(event, value) => onKeyDown(event, value)}
          headerButton={getComponent[`${screenKey}headerButton`]}
          onSearchAction={(key, event, values) => onSearchAction(key, event, values)}
        />
      )}
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceSearch.propTypes = {
  disabled: PropTypes.bool,
  onSearchAction: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  screenKey: PropTypes.string.isRequired,
  searchText: PropTypes.string.isRequired,
  reference: PropTypes.instanceOf(createRef()),
  defaultValues: PropTypes.arrayOf(PropTypes.number),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DeviceSearch.defaultProps = {
  disabled: false,
  defaultValues: [],
  reference: createRef(),
};

export default DeviceSearch;
