import {
  FETCH_TENANT_ENTITY,
  FETCH_APPUPDATE_ENTITY,
  FETCH_APPREGISTER_ENTITY,
} from './ActionTypes';

/**
 * getTenantEntity is used fetch the tenant details of logged user
 * @param data
 */
export function getTenantEntity(data) {
  return {
    type: FETCH_TENANT_ENTITY,
    data,
  };
}

/**
 * settAppRegisterEntity is used set the application registration entity details
 * @param data
 */

export function setAppRegisterEntity(data) {
  return {
    type: FETCH_APPREGISTER_ENTITY,
    data,
  };
}

/**
 * setAppUpdateEntity is used set the application update entity details
 * @param data
 */

export function setAppUpdateEntity(data) {
  return {
    type: FETCH_APPUPDATE_ENTITY,
    data,
  };
}
