/**
 * checkLanguage is to label already exist, otherwise append missing to label.
 * @param localeData
 * @param label
 * @return label
 *
 * @author Gobi R
 */
const checkLanguage = (localeData, label) => {
  return localeData[label] || (label ? '[missing_]_'.concat(label) : '');
};

export default checkLanguage;
