/**
 * APPDelete is used to Delete Tenant
 * @author: R.Rajeshkumar
 *
 */

import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { get, performDelete } from '../../../actions/commonActions';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import APIEndPoints from '../../../constants/APIEndPoints';
import { CommonConstants } from '../../../constants/CommonConstants';
import headerInformation from '../Constants/AppManageHeaderConstants';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import {
  tableBasicView,
  tableBusinessView,
  tableDistinguishView,
  tableRemarkOneView,
  tableRemarkTwoView,
  tableSalesView,
} from '../Constants/AppFrameConstants';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBLinkHeader from '../../../common/component/sblinkheader/SBLinkHeader';
import AppCRUDFooter from './AppCRUDFooter';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import SBDialog from '../../../common/component/sbdialog/SBDialog';

class AppDelete extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      headerData: headerInformation('deleteapp'),
      dataSource: {},
      pageLoading: true,
      footer: { okDisable: true, primaryVisibile: true },
      dialogHeader: '',
      dialogContent: '',
      dialogKey: '',
      modalIsOpen: false,
    };
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const { location, history } = this.props;
    if (location.state && location.state.dmCode) {
      const generateComponent = async () => {
        await this.getApplicationDetails();
        await this.setDeleteEnable();
      };
      generateComponent();
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  }

  /**
   * getApplicationDetails is used to get application details
   */
  getApplicationDetails = async () => {
    const { location, history } = this.props;
    const response = await get(`${APIEndPoints.TENANTDETAILS}/${location.state.dmCode}`);
    const responseData = await response.json();
    if (responseData.status) {
      const { applicationVo } = responseData;
      this.setState({ dataSource: applicationVo });
      this.setState({ pageLoading: false });
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  };

  /**
   * onCancelClick is used to handle cancel click of dialog
   *
   */
  onCancelClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.APPLIST);
  };

  /**
   * onHandleClick is used to handle click operation based on key
   * @param key
   *
   */
  onHandleClick = (key) => {
    switch (key) {
      case 'save':
        this.setState({
          modalIsOpen: true,
          dialogContent: 'confirm_delete_data',
          dialogHeader: 'delete_confirmation',
          dialogKey: 'save',
        });
        break;
      case 'cancel':
        this.onCancelClick();
        break;
      default:
        break;
    }
  };

  /**
   * onOkDialog is used to handle ok click event of the dialog
   */
  onOkDialog = () => {
    const { history } = this.props;
    const { dialogKey } = this.state;
    if (dialogKey === 'save') {
      this.deleteProcess();
    } else {
      history.push(ROUTEENDPOINTS.APPLIST);
    }
  };

  /**
   * deleteProcess is used to delete the tenant data of selectef record.
   */
  deleteProcess = () => {
    const { history } = this.props;
    const { dataSource } = this.state;
    const deleteData = async () => {
      const response = await performDelete(
        `${APIEndPoints.APPLICATIONDELETE}/${dataSource.dmCode}`,
      );
      const resultSet = await response.json();
      if (resultSet.status) {
        history.push(ROUTEENDPOINTS.APPLIST);
      } else {
        history.push(ROUTEENDPOINTS.ERROR);
      }
    };
    deleteData();
  };

  setDeleteEnable = () => {
    const { dataSource } = this.state;
    if (dataSource.androidDeviceCount === 0) {
      this.setState({ footer: { okDisable: false, primaryVisibile: true } });
    }
  };

  render() {
    const {
      headerData,
      dataSource,
      footer,
      pageLoading,
      dialogHeader,
      dialogContent,
      modalIsOpen,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <SBTableHeader headerText="basic_information" />
                <CustomTableFrame
                  tableList={tableBasicView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBLinkHeader
                  hyperLinkText="contract_information"
                  hyperLink={ROUTEENDPOINTS.APPCONTRACTFINISH}
                  historyData={{ dataSource, isDelete: true, isUpdate: false }}
                />
              </div>
              <div>
                <SBTableHeader headerText="sales_information" />
                <CustomTableFrame
                  tableList={tableSalesView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBTableHeader headerText="business_information" />
                <CustomTableFrame
                  tableList={tableBusinessView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBLinkHeader
                  hyperLinkText="app_use_information"
                  hyperLink={ROUTEENDPOINTS.APPOPERATIONFINFOFINISH}
                  historyData={{ dataSource, isDelete: true, isUpdate: false }}
                />
              </div>
              <div>
                <SBTableHeader headerText="resales_agency_info" />
                <CustomTableFrame tableList={tableDistinguishView} dataSource={dataSource} />
              </div>
              <div>
                <SBTableHeader headerText="remark_one" />
                <CustomTableFrame
                  tableList={tableRemarkOneView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBTableHeader headerText="remark_two" />
                <CustomTableFrame
                  tableList={tableRemarkTwoView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <AppCRUDFooter
                  reference={{ backRef: createRef() }}
                  footer={footer}
                  onHandleClick={(key) => this.onHandleClick(key)}
                />
              </div>
              <SBDialog
                open={modalIsOpen}
                header={dialogHeader}
                type="confirmation"
                content={dialogContent}
                onCancel={() => {
                  this.setState({ modalIsOpen: false });
                }}
                onOk={() => {
                  this.onOkDialog();
                }}
                width="400px"
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
AppDelete.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dmCode: PropTypes.string,
    }),
  }).isRequired,
};

export default withRouter(AppDelete);
