/**
 * Security Policy List is used to load the Security Policy screens
 * @author: P.Vinothpandian
 */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../constants/HeaderConstant';
import CircularSpinner from '../../../../common/component/loadingSpinner/CircularSpinner';
import SearchPanel from '../component/SearchPanel';
import { post, get } from '../../../../actions/commonActions';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import RoleResolver from '../../../../common/component/guard/RoleResolver';
import { Role } from '../../../../constants/Role';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SecurityPolicyTable from '../component/SecurityPolicyTable';
import APIENDPOINTS from '../../../../constants/APIEndPoints';
import SBDialog from '../../../../common/component/sbdialog/SBDialog';
import { setSecurityPolicySearchParam } from '../../../../actions/securityPolicyAction';
import { errorMessageConstants } from '../constants/MessageConstant';

const resultsPerPageKey = 'security_policy';
class SecurityPolicy extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageLoading: true,
      dataLoading: false,
      openErrorDialog: false,
      message: '',
      securityPolicyList: [],
      searchKeyword: '',
      selectedRowValue: {},
      disableAdd: false,
    };
    this.searchBoxRef = createRef();
  }

  /**
   * componentDidMount is load the data in the grid
   */

  componentDidMount() {
    const { securityPolicySearch, location, setSearchParam } = this.props;
    const isSearch = location.state ? location.state.isSearch : false;
    let searchValue = '';
    if (isSearch) {
      searchValue = securityPolicySearch;
    } else {
      setSearchParam(searchValue);
    }
    const getSecurityPolicyList = async () => {
      const response = await post(`${APIENDPOINTS.SECURITY_POLICY_LIST}`, {
        search: searchValue,
      });
      const responseData = await response.json();
      const { status, message, data: gridList } = responseData;
      if (!status || status === 'OK') {
        this.setState({
          securityPolicyList: gridList,
          pageLoading: false,
          searchKeyword: searchValue,
          selectedRowValue: gridList[0],
        });
      } else {
        this.setState({
          pageLoading: false,
          dataLoading: false,
          searchKeyword: searchValue,
          message,
          disableAdd: false,
        });
      }
      if (this.searchBoxRef.current) this.searchBoxRef.current.focus();
    };

    getSecurityPolicyList();
  }
  /**
   * handleSearchAction is used to handle search operations
   * @param key
   * @param event
   * @param value
   *
   */

  handleSearchAction = (key, event, value) => {
    if (key === 'searchButton') {
      const { searchKeyword } = this.state;
      this.performSearch(searchKeyword);
    } else {
      this.setState({ searchKeyword: value.searchKeyword ? value.searchKeyword : '' });
    }
  };

  /**
   * handleRadioChange is used to handle radio change event
   * @param value
   */

  handleRadioChange = value => {
    let { selectedRowValue } = this.state;
    selectedRowValue = value;
    this.setState({ selectedRowValue });
  };

  /**
   * handlePaginationClick is used to handle pagination event
   * @param tableData
   */

  handlePaginationClick = tableData => {
    if (tableData.length) {
      this.setState({ selectedRowValue: tableData[0] });
    }
  };

  /**
   * handleSelectChange is used to handle select combo event
   * @param value
   */

  handleSelectChange = value => {
    const { searchKeyword } = this.state;
    this.performSearch(searchKeyword, value);
  };

  /**
   * onKeyDown is used to perform key down operations
   * @param event
   * @param value
   *
   */

  onKeyDown = (event, value) => {
    if (event.keyCode === 13) this.performSearch(value);
  };

  performSearch = searchKeyword => {
    this.setState({ dataLoading: true });
    const { setSearchParam } = this.props;
    setSearchParam(searchKeyword);
    const getSecurityPolicyList = async () => {
      const response = await post(`${APIENDPOINTS.SECURITY_POLICY_LIST}`, {
        search: searchKeyword,
      });
      const responseData = await response.json();
      const { status, message, data: gridList } = responseData;
      if (!status || status === 'OK') {
        this.setState({
          securityPolicyList: gridList,
          dataLoading: false,
          selectedRowValue: gridList ? gridList[0] : {},
        });
      } else {
        this.setState({ pageLoading: false, dataLoading: false, openErrorDialog: true, message });
      }
    };
    getSecurityPolicyList();
  };

  navigateDetailScreen = screenName => {
    const { history } = this.props;
    history.push(screenName);
  };

  /**
   * handleDeleteClick is used to perform delete selected security policy
   *
   */

  handleDeleteClick = () => {
    this.setState({ pageLoading: true });
    const { history } = this.props;
    const {
      selectedRowValue: { securityPolicyId },
    } = this.state;
    const getSecurityPolicyDetail = async () => {
      const response = await get(`${APIENDPOINTS.SECURITY_POLICY_DETAIL}/${securityPolicyId}`);
      const showData = await response.json();
      if (showData.policyId === securityPolicyId) {
        showData.assignTarget =
          showData.groupList && showData.groupList.length ? showData.groupList.join(',') : '';
        history.push(ROUTEENDPOINTS.SECURITYPOLICYVIEW, {
          showData,
          param: { delete: true, securityPolicyId },
        });
      } else {
        this.setState({
          pageLoading: false,
          dataLoading: true,
          openErrorDialog: true,
          message: errorMessageConstants.dataNotExistsError,
        });
        this.performSearch('');
      }
    };
    getSecurityPolicyDetail();
  };

  handleChangeClick = () => {
    this.setState({ pageLoading: true });
    const { history } = this.props;
    const {
      selectedRowValue: { securityPolicyId },
    } = this.state;
    const getSecurityPolicyDetail = async () => {
      const response = await get(`${APIENDPOINTS.SECURITY_POLICY_DETAIL}/${securityPolicyId}`);
      const showData = await response.json();
      if (showData.policyId === securityPolicyId) {
        showData.assignTarget =
          showData.groupList && showData.groupList.length ? showData.groupList.join(',') : '';
        showData.applicationList.map(value => ({
          checkMethod: value.applicationCheckMethod,
          applicationName: value.applicationName,
        }));
        history.push(ROUTEENDPOINTS.CHANGESECURITYPOLICY, {
          param: { update: true, securityPolicyId, policyDetail: showData },
        });
      } else {
        this.setState({
          pageLoading: false,
          openErrorDialog: true,
          message: errorMessageConstants.dataNotExistsError,
        });
      }
    };
    getSecurityPolicyDetail();
  };

  render() {
    const {
      pageLoading,
      dataLoading,
      securityPolicyList,
      message,
      openErrorDialog,
      disableAdd,
      selectedRowValue,
    } = this.state;
    const createdAccount = securityPolicyList.length === 0 ? '' : selectedRowValue.createdAccount;
    const { securityPolicySearch } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          hasTitleDescription
          title={headerInformation('securityPolicylist').title}
          description={headerInformation('securityPolicylist').description}
        />
        <div className="contents-main">
          <RoleResolver authorityRoleType={Role.group1}>
            <SBButton
              add
              title="security_policy_btn_add"
              onClick={() => {
                this.navigateDetailScreen(ROUTEENDPOINTS.ADDSECURITYPOLICY);
              }}
              disabled={disableAdd}
            />
          </RoleResolver>
          <SearchPanel
            defaultValue={securityPolicySearch}
            reference={this.searchBoxRef}
            onSearchAction={(key, event, values) => this.handleSearchAction(key, event, values)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
          <CircularSpinner
            loading={dataLoading}
            css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
          >
            <SecurityPolicyTable
              data={securityPolicyList}
              defaultSelected={resultsPerPageKey}
              createdAccount={createdAccount}
              handleRadioChange={event => this.handleRadioChange(event)}
              handlePaginationClick={tableData => this.handlePaginationClick(tableData)}
              handleSelectChange={value => this.handleSelectChange(value)}
              handleDeleteClick={() => this.handleDeleteClick()}
              handleChangeClick={() => this.handleChangeClick()}
            />
          </CircularSpinner>
          <SBDialog
            type="error"
            header="error"
            content={message}
            okButtonTitle="ok"
            open={openErrorDialog}
            onOk={() => {
              this.setState({ dataLoading: true, openErrorDialog: false });
              this.performSearch('');
            }}
            width="400px"
          />
        </div>
      </CircularSpinner>
    );
  }
}
const mapDispatchToProps = dispatch => {
  return {
    setSearchParam: data => dispatch(setSecurityPolicySearchParam(data)),
  };
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { securityPolicySearch } = state.securityPolicy;
  return { securityPolicySearch };
};
/**
 * propTypes is used for validation props with its type.
 */
SecurityPolicy.propTypes = {
  setSearchParam: PropTypes.func.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(SecurityPolicy));
