import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBCheckbox from '../../../../common/component/checkbox/SBCheckbox';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBTable from '../../../../common/component/sbtable/SBTable';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

const dataSource = [
  {
    deviceName: 'Device 1',
    userName: 'User1',
    /* phoneNumber: '9876543210', */
    imei: '12345679123456789',
    scanDate: '22/02/2020 13:54',
    detectDate: '22/02/2020 13:54',
    status: 'virusDetection',
  },
  {
    deviceName: 'Device 2',
    userName: 'User2',
    /* phoneNumber: '9876543210', */
    imei: '12345679123456789',
    scanDate: '22/02/2020 13:54',
    detectDate: '22/02/2020 13:54',
    status: 'notInstalledApplication',
  },
];

const resultsPerPagekey = 'antivirus_list';
class AntiVirusList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: OPERATIONMANAGEMENTCONSTANTS.antiVirusListGridColumns,
      datasource: dataSource,
    };
  }

  onSearchButtonClick = () => {};

  onCheckBoxChange = () => {};

  onOutputVirusListButtonClick = () => {};

  onSearchByDeptNameButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.DEPARTMENTDEVICESEARCH, {
      parentScreen: location.pathname,
    });
  };

  onSearchByGroupButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.REFINEGROUP, {
      parentScreen: location.pathname,
    });
  };

  render() {
    const { screenTitleData } = this.props;
    const { datasource } = this.state;
    const { columns } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <SBContentHeader
                title={screenTitleData.title}
                description={screenTitleData.description}
              />
              <div className="contents-main">
                <div className="border p15 mb15">
                  <div className="clearfix mb10">
                    <SBCheckbox
                      name="check"
                      horizontal="true"
                      withColon
                      image
                      filterKey="name"
                      filterValue="name"
                      imageSrcKey="url"
                      onChange={() => this.onCheckBoxChange()}
                      options={OPERATIONMANAGEMENTCONSTANTS.searchFilterCheckBoxList}
                    />
                  </div>
                  <SBTextbox
                    type="text"
                    name="search_data"
                    size="20"
                    maxlength="100"
                    style={{ marginRight: '10px' }}
                  />
                  <SBButton
                    type="button"
                    name="antiVirusListSearchButton"
                    color="green"
                    title="Search"
                    onClick={() => this.onSearchButtonClick()}
                  />
                  <SBButton
                    type="button"
                    name="searchByDeptName"
                    title="Search By Department Name"
                    onClick={() => this.onSearchByDeptNameButtonClick()}
                  />
                  <SBButton
                    type="button"
                    name="searchByGroupName"
                    title="Search by Group Name"
                    onClick={() => this.onSearchByGroupButtonClick()}
                  />
                </div>
              </div>
              <div className="contents-main w100per">
                <div className="mb10">
                  <div className="clearfix">
                    <SBTable
                      columns={columns}
                      dataSource={datasource}
                      loading={false}
                      pagination
                      countInfo={`Search result：${datasource.length} items`}
                      filterList={
                        OPERATIONMANAGEMENTCONSTANTS.antiVirusGridResultsPerPageComboItems
                      }
                      defaultSelected={resultsPerPagekey}
                    />
                  </div>
                  <div className="clearfix mb10">
                    <div className="floatleft">
                      <SBButton
                        type="button"
                        name="outputVirusList"
                        title="Output Virus List"
                        color="gray"
                        onClick={() => this.onOutputVirusListButtonClick()}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(AntiVirusList);
