/**
 * AppRegister is used to load register screen
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import { get, post, postWithFormData } from '../../../actions/commonActions';
import { setAppRegisterEntity } from '../../../actions/TenantActions';
import { CommonConstants } from '../../../constants/CommonConstants';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import AppCRUDFooter from './AppCRUDFooter';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import {
  tableBasicList,
  tableSalesList,
  tableBusinessList,
  tableDistinguish,
  tableRemarkOne,
  tableRemarkTwo,
} from '../Constants/AppFrameConstants';
import { emptyDataSource, filterData } from '../Constants/AppFieldConstants';
import { setRegisterReference } from '../Constants/AppReferenceConstants';
import { setFocusinerrorField, validateInputs } from '../Constants/AppRegisterValidations';
import headerInformation from '../Constants/AppManageHeaderConstants';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import {
  fetchLanguageList,
  fetchTimezoneList,
  fetchCampaignList,
  fetchCountryList,
} from '../../../actions/FieldsAction';
import SBLinkHeader from '../../../common/component/sblinkheader/SBLinkHeader';
import { isNotUndefinedOrNotEmpty } from '../../../common/CommonValidations/CommonValidations';

const moment = require('moment-timezone');

class AppRegister extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { appRegisterVo } = props;
    this.state = {
      headerData: headerInformation('registerapp'),
      fieldDataList: [],
      dataSource: isNotUndefinedOrNotEmpty(appRegisterVo) ? appRegisterVo.param : emptyDataSource(),
      pageLoading: true,
      modalIsOpen: false,
      dynamicData: {},
      autoGenertedDmCode: isNotUndefinedOrNotEmpty(appRegisterVo)
        ? appRegisterVo.autoGenertedDmCode
        : '',
      reference: setRegisterReference(),
      modified: isNotUndefinedOrNotEmpty(appRegisterVo) ? appRegisterVo.globalModified : false,
      dialogHeader: '',
      dialogContent: '',
      dialogKey: '',
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount is used to load initial data
   */

  componentDidMount() {
    const generateComponent = async () => {
      await this.getTimeZoneDetails();
      await this.getLanguageDetails();
      await this.getCampaignDetails();
      await this.getCountryDetails();
      await this.getAutoGeneratedDmCode();
      await this.setInitialFocus();
    };
    generateComponent();
  }

  /**
   * componentWillUnmount is used when component is being removed from the DOM
   */

  componentWillUnmount() {
    const { setAppRegisterEntity: makeAppRegisterEntity } = this.props;
    makeAppRegisterEntity({});
  }

  /**
   * getTimeZoneDetails is used to get timezone values
   */

  getTimeZoneDetails = async () => {
    const { fetchTimezoneList: getTimeZoneList, timeZoneVo } = this.props;
    if (!timeZoneVo.length) {
      const timeZoneResponse = await get(APIENDPOINTS.TIMEZONELIST);
      const timeZoneList = await timeZoneResponse.json();
      getTimeZoneList(timeZoneList);
    }
  };

  /**
   * getLanguageDetails is used to get language details
   */

  getLanguageDetails = async () => {
    const { fetchLanguageList: getLanguageList, languageVo } = this.props;
    if (!languageVo.length) {
      const languageResponse = await post(APIENDPOINTS.LANGUAGECODELIST, { searchedValue: '' });
      const languageList = await languageResponse.json();
      getLanguageList(languageList);
    }
  };

  /**
   * getCampaignDetails is used to get campaign details
   */

  getCampaignDetails = async () => {
    const { fetchCampaignList: getCampaignList, campaignVo } = this.props;
    if (!campaignVo.length) {
      const campaignResponse = await get(APIENDPOINTS.CAMPAIGNDETAILS);
      const campaignList = await campaignResponse.json();
      getCampaignList(campaignList);
    }
  };

  /**
   * getCountryDetails is used to get country details
   */

  getCountryDetails = async () => {
    const { fetchCountryList: getCountryList, countryVo } = this.props;
    if (!countryVo.length) {
      const countryResponse = await get(APIENDPOINTS.COUNTRYDETAILS);
      const countryList = await countryResponse.json();
      getCountryList(countryList);
    }
  };

  /**
   * getAutoGeneratedDmCode is used to get random generated dmcode
   */

  getAutoGeneratedDmCode = async () => {
    const { appRegisterVo } = this.props;
    const { dataSource } = this.state;
    if (!(isNotUndefinedOrNotEmpty(appRegisterVo) && appRegisterVo.autoGenertedDmCode)) {
      const response = await get(APIENDPOINTS.AUTODATA);
      const data = await response.json();
      dataSource.dmCode = data.dmCode;
      dataSource.password = data.password;
      this.setState({ autoGenertedDmCode: data.dmCode, dataSource });
    }
  };

  /**
   * setInitialFocus is used to set initial focus to the screen
   */

  setInitialFocus = () => {
    const { setModified } = this.props;
    const { reference, modified } = this.state;
    this.setState({ pageLoading: false });
    if (reference.businessNameRef.current) reference.businessNameRef.current.focus();
    setModified(modified);
  };

  /**
   * saveProcess is used to save the data that is filled in the screen
   */

  saveProcess = () => {
    const { dynamicData, reference } = this.state;
    const { history, setModified } = this.props;
    const { dataSource } = this.state;
    dataSource.firstUseStartDate = dataSource.firstUseStartDate
      ? new Date(dataSource.firstUseStartDate)
      : '';
    dataSource.trialEndDate = dataSource.trialEndDate ? new Date(dataSource.trialEndDate) : '';
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      const formData = new FormData();
      if (dataSource.attachDataFile) {
        formData.append('attachDataFile', dataSource.attachDataFile);
        dataSource.attachDataFile = null;
      }
      if (dataSource.individualDataFile) {
        formData.append('individualDataFile', dataSource.individualDataFile);
        dataSource.individualDataFile = null;
      }
      if (dataSource.attachFileUploadedDate) {
        dataSource.attachFileUploadedDate = moment(dataSource.attachFileUploadedDate).format(
          'YYYY/MM/DD HH:mm:ss',
        );
      }
      if (dataSource.individualFileUploadedDate) {
        dataSource.individualFileUploadedDate = moment(
          dataSource.individualFileUploadedDate,
        ).format('YYYY/MM/DD HH:mm:ss');
      }
      dataSource.ipAddress = isNotUndefinedOrNotEmpty(dataSource.ipAddress) ? dataSource.ipAddress.trim() : null;
      formData.append('dataSource', JSON.stringify(dataSource));
      const response = await postWithFormData(APIENDPOINTS.APPLICATIONREGISTER, formData);
      const resultSet = await response.json();
      if (resultSet.status) {
        setModified(false);
        history.push(ROUTEENDPOINTS.APPLICATIONREGISTERFINISH, { param: resultSet.applicationVo });
      } else if (resultSet.validationError) {
        const { validationError } = resultSet;
        dynamicData.dmCodeError = validationError.dmCodeError;
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
          dynamicData,
        });
        reference.dmCodeRef.current.focus();
      } else {
        setModified(false);
        history.push(ROUTEENDPOINTS.ERROR);
      }
    };
    saveProcess();
  };

  /**
   * onOkDialog is used to handle ok click event of the dialog
   */

  onOkDialog = () => {
    const { history, setModified } = this.props;
    const { dialogKey } = this.state;
    if (dialogKey === 'save') {
      this.saveProcess();
    } else {
      setModified(false);
      history.push(ROUTEENDPOINTS.APPLIST);
    }
  };

  /**
   * onRadioChange is used to handle on radio change event
   * @param radioKey
   * @param key
   * @param value
   * @param defaultValue
   */

  onRadioChange = (radioKey, key, value, defaultValue) => {
    const { dataSource } = this.state;
    if (value === 0) dataSource[key] = defaultValue;
    else dataSource[key] = '';
    dataSource[radioKey] = value;
    this.setState({ dataSource, modified: true });
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    const { dataSource, autoGenertedDmCode } = this.state;
    const { setModified } =this.props;
    if (key === 'gisunOrderNumber' || key === 'paymentNumber') {
      this.callService(key);
    } else if (key === 'radiodmcode') {
      this.onRadioChange('radiodmcode', 'dmCode', value, autoGenertedDmCode);
    } else if (key === 'managerOption') {
      this.onRadioChange('managerOption', 'userCount', value, '10');
    } else if (key === 'trial') {
      dataSource[key] = value;
      this.setState({ dataSource, modified: true });
    } else {
      dataSource[key] =
        event &&
        event.target &&
        (event.target.type === 'select-one' || event.target.type === 'textarea')
          ? event.target.value
          : value;
      setModified(true);
      this.setState({ dataSource, modified: true });
    }
  };

  /**
   * onCancelClick is used to handle cancel click of dialog
   *
   */

  onCancelClick = () => {
    const { history } = this.props;
    const { modified } = this.state;
    if (modified) {
      this.setState({
        modalIsOpen: true,
        dialogContent: 'cancel_confirmation_message',
        dialogHeader: 'cancel_confirmation',
        dialogKey: 'cancel',
      });
    } else {
      history.push(ROUTEENDPOINTS.APPLIST);
    }
  };

  /**
   * onHandleClick is used to handle click operation based on key
   * @param key
   *
   */

  onHandleClick = key => {
    const { dynamicData, reference } = this.state;
    switch (key) {
      case 'save':
        this.setState({
          modalIsOpen: this.validateData(),
          dialogContent: 'want_to_insert_data',
          dialogHeader: 'save_confirmation',
          dialogKey: 'save',
        });
        setFocusinerrorField(dynamicData, reference);
        break;
      case 'cancel':
        this.onCancelClick();
        break;
      default:
        break;
    }
  };

  /**
   * setComboValues is used to load combo values
   * @param status
   *
   */

  setComboValues = () => {
    const { languageVo, timeZoneVo, campaignVo, countryVo } = this.props;
    filterData.languageVo = languageVo;
    filterData.timeZoneVo = timeZoneVo;
    filterData.campaignVo = [];
    filterData.campaignVo.push({ campaignId: 'none', campignName: '' });
    filterData.campaignVo.push(...campaignVo);
    filterData.countryVo = countryVo;
    languageVo.find((element, index) => {
      if (element.code === 'non') {
        filterData.languageVo[index].languageName = this.languageLabelCheck('automatic');
      }
      return false;
    });
    timeZoneVo.find((element, index) => {
      if (element.timeZoneCode === '0') {
        filterData.timeZoneVo[index].timeZoneName = this.languageLabelCheck('automatic');
      }
      return false;
    });
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * callService is used to call the service based on the key
   * @param key
   */

  callService = key => {
    const { history } = this.props;
    const { dataSource, autoGenertedDmCode, modified } = this.state;
    const seviceKey = {
      gisunOrderNumber: ROUTEENDPOINTS.APPGISUN,
      paymentNumber: ROUTEENDPOINTS.APPPAYMENT,
    };
    history.push(seviceKey[key], {
      param: dataSource,
      autoGenertedDmCode,
      globalModified: modified,
    });
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */

  onKeyDown = event => {
    const { footer } = this.state;
    const { okDisable } = footer;
    if (event.keyCode === 13 && !okDisable) {
      event.preventDefault();
      this.onHandleClick('save');
    }
  };

  validateData() {
    const { dynamicData, dataSource } = this.state;
    const validationResult = validateInputs(dataSource, dynamicData);
    this.setState({
      dynamicData: validationResult,
    });
    return validationResult.status;
  }

  /**
   * render is used to render the component
   * @return AppRegisterComponent
   */

  render() {
    const {
      headerData,
      fieldDataList,
      dataSource,
      modalIsOpen,
      footer,
      pageLoading,
      autoGenertedDmCode,
      dialogHeader,
      dialogContent,
      reference,
      dynamicData,
      modified,
    } = this.state;
    this.setComboValues();
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <SBTableHeader headerText="basic_information" />
                <CustomTableFrame
                  tableList={
                    fieldDataList.length
                      ? fieldDataList
                      : tableBasicList(dynamicData, reference, dataSource)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div style={{ marginBottom: 10 }}>
                <SBLinkHeader
                  hyperLinkText="contract_information"
                  hyperLink={ROUTEENDPOINTS.APPCONTRACTINFO}
                  historyData={{ dataSource, autoGenertedDmCode, globalModified: modified }}
                  dynamicData={dynamicData}
                  reference={
                    reference && reference.contractInfoRef ? reference.contractInfoRef : null
                  }
                />
                {dynamicData && dynamicData.contractInfoError ? (
                  <>
                    <span className="red bold">
                      {this.languageLabelCheck(dynamicData.contractInfoError)}
                    </span>
                  </>
                ) : null}
              </div>
              <div>
                <SBTableHeader headerText="sales_information" />
                <CustomTableFrame
                  tableList={
                    fieldDataList.length ? fieldDataList : tableSalesList(dynamicData, reference)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <SBTableHeader headerText="business_information" />
                <CustomTableFrame
                  tableList={
                    fieldDataList.length
                      ? fieldDataList
                      : tableBusinessList(dataSource, dynamicData, reference)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <SBLinkHeader
                  hyperLinkText="app_use_information"
                  hyperLink={ROUTEENDPOINTS.APPOPERATIONINFO}
                  historyData={{ dataSource, autoGenertedDmCode, globalModified: modified }}
                />
              </div>
              <div>
                <SBTableHeader headerText="resales_agency_info" />
                <CustomTableFrame
                  tableList={fieldDataList.length ? fieldDataList : tableDistinguish()}
                  dataSource={dataSource}
                />
              </div>
              <div>
                <SBTableHeader headerText="remark_one" />
                <CustomTableFrame
                  tableList={
                    fieldDataList.length ? fieldDataList : tableRemarkOne(dynamicData, reference)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <SBTableHeader headerText="remark_two" />
                <CustomTableFrame
                  tableList={
                    fieldDataList.length ? fieldDataList : tableRemarkTwo(dynamicData, reference)
                  }
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                  onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <AppCRUDFooter 
                  reference={reference}
                  footer={footer} 
                  onHandleClick={key => this.onHandleClick(key)} 
                />
              </div>
              <SBDialog
                open={modalIsOpen}
                header={dialogHeader}
                type="confirmation"
                content={dialogContent}
                onCancel={() => {
                  this.setState({ modalIsOpen: false });
                }}
                onOk={() => {
                  this.onOkDialog();
                }}
                width="400px"
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = dispatch => {
  return {
    fetchLanguageList: data => {
      dispatch(fetchLanguageList(data));
    },
    fetchTimezoneList: data => {
      dispatch(fetchTimezoneList(data));
    },
    fetchCampaignList: data => {
      dispatch(fetchCampaignList(data));
    },
    fetchCountryList: data => {
      dispatch(fetchCountryList(data));
    },
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    setAppRegisterEntity: data => {
      dispatch(setAppRegisterEntity(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { timeZoneVo, languageVo, campaignVo, countryVo } = state.commonFields;
  const { appRegisterVo } = state.tenant;
  const { data } = state.language;
  return {
    timeZoneVo,
    languageVo,
    localeData: data,
    campaignVo,
    countryVo,
    appRegisterVo,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

AppRegister.propTypes = {
  history: commonPropTypes().history.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
  fetchLanguageList: commonPropTypes().fetchLanguageList.isRequired,
  fetchTimezoneList: PropTypes.func.isRequired,
  fetchCampaignList: PropTypes.func.isRequired,
  fetchCountryList: PropTypes.func.isRequired,
  setAppRegisterEntity: PropTypes.func.isRequired,
  languageVo: PropTypes.arrayOf(PropTypes.object).isRequired,
  appRegisterVo: PropTypes.objectOf(PropTypes.object),
  timeZoneVo: PropTypes.arrayOf(PropTypes.object).isRequired,
  campaignVo: PropTypes.arrayOf(PropTypes.object).isRequired,
  countryVo: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
AppRegister.defaultProps = {
  isNotMultiLang: false,
  appRegisterVo: {},
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(AppRegister));
