/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';

/** DefaultGroupManagement Components */
import DefaultGroupManagementGrid from '../component/DefaultGroupManagementGrid';
import DefaultGroupManagementFooter from '../component/DefaultGroupManagementFooter';

/** Utility Files */
import { Role } from '../../../constants/Role';
import { get } from '../../../actions/commonActions';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { makeGroupInformation } from '../../../actions/GroupAction';
import {
  defaultGroupSettingHeader,
  serviceMapper,
} from '../constants/defaultGroupSettingConstants';

/**
 * DefaultGroupManagement is to display list of default group information
 * @author Aswin Palanisamy
 */

const resultPerPageKey = 'default_group_management_list';
class DefaultGroupManagement extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      message: '',
      groupList: [],
      disabled: true,
      pageLoading: false,
      hasAuthority: true,
      openErrorDialog: false,
      headerData: defaultGroupSettingHeader(true),
    };
    this.buttonRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    this.setState({ pageLoading: true }, () => this.getDefaultGroup());
  }

  /**
   * checkAuthority is to restrict default group setting access based on user authority
   * @param getAccess
   * @param groupList
   * @return checkAuthority
   */
  checkAuthority = (getAccess, groupList = []) => {
    const { userVo } = this.props;
    return (
      !getAccess ||
      (getAccess &&
        groupList.filter(record => record.authority.split(',').indexOf(userVo.loginId) >= 0).length)
    );
  };

  /**
   * getDefaultGroup is to load list of default group records
   */
  getDefaultGroup = async () => {
    const response = await get(APIENDPOINTS.DEFAULTGROUP);
    const result = await response.json();
    const { status, message, data: groupList } = result;
    const disabled = isEmpty(groupList) || !!message;
    if (status === 'OK') {
      this.setState(
        {
          disabled,
          groupList,
          pageLoading: false,
          countInfoContents: groupList.length,
          headerData: defaultGroupSettingHeader(!disabled),
          hasAuthority: this.checkAuthority(message, groupList),
        },
        () => {
          if (this.buttonRef.current) this.buttonRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        pageLoading: false,
        openErrorDialog: true,
      });
    }
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param key
   */
  onHandleClick = key => {
    const { history, location } = this.props;
    history.push(serviceMapper(key), { parentScreen: location.pathname });
  };

  /**
   * render performs group management screen rendering
   * @returns DefaultGroupManagement
   */
  render() {
    const {
      message,
      disabled,
      groupList,
      headerData,
      pageLoading,
      hasAuthority,
      openErrorDialog,
      countInfoContents,
    } = this.state;
    const { screenKey, history } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <DefaultGroupManagementGrid
            history={history}
            gridList={groupList}
            hasAuthority={hasAuthority}
            countInfoContents={countInfoContents}
            defaultSelected={resultPerPageKey}
          />
          <DefaultGroupManagementFooter
            screenKey={screenKey}
            onHandleClick={key => this.onHandleClick(key)}
            footer={{ disabled, reference: this.buttonRef, authorityRoleType: Role.group1 }}
          />
        </div>
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              if (this.buttonRef.current) this.buttonRef.current.focus();
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { userVo } = state.user;
  const { data: localeData } = state.language;
  return { userVo, localeData };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = dispatch => {
  return {
    makeGroupInformation: data => dispatch(makeGroupInformation(data)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DefaultGroupManagement.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
  }).isRequired,
  userVo: PropTypes.shape({
    loginId: PropTypes.string,
  }).isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(DefaultGroupManagement));
