/**
 * NotificationFinish is used to show finish message and list of notifications registered.
 * @author: V.Vijayaragavan
 */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import NoticeList from './NoticeList';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../../constants/CommonConstants';

class NotificationFinish extends PureComponent {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history } = this.props;
    this.state = {
      pageLoading: true,
    };
    if (location.state && location.state.isFromConfirm) {
      this.state = {
        pageLoading: true,
        update: location.state.update,
        noticeId: location.state.noticeId,
      };
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  }

  /**
   * componentDidMount to stop page loading after component mounted.
   */
  componentDidMount() {
    this.setState({ pageLoading: false });
  }

  /**
   * render is used to render the component
   */
  render() {
    const { pageLoading, update, noticeId } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={notificationHeaderInformation(update ? 'updateFinish' : 'registerFinish').title}
          description={
            notificationHeaderInformation(update ? 'updateFinish' : 'registerFinish').description
          }
        />
        <NoticeList finishScreen noticeId={noticeId} />
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
NotificationFinish.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      isFromConfirm: PropTypes.bool,
      update: PropTypes.bool,
      noticeId: PropTypes.string.isRequired,
    }),
  }),
  history: commonPropTypes().history.isRequired,
};

NotificationFinish.defaultProps = {
  location: { state: { update: false, isFromConfirm: false } },
};

export default NotificationFinish;
