/** Base Files */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBLink from '../../common/component/sblink/SBLink';
import SBTable from '../../common/component/sbtable/SBTable';
import RoleResolver from '../../common/component/guard/RoleResolver';

/** Utility Files */
import { Role } from '../../constants/Role';
import checkLanguage from '../../language/checkLanguage';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import commonConstants from '../../constants/CommonConstants';
import { tableColumnList, searchFilterCheckBoxList } from '../constants/deviceConstants';

/**
 * DeviceGrid is to list the group management information
 * @author Aswin Palanisamy
 */
class DeviceGrid extends Component {
  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    const { handlePaginationClick } = this.props;
    if (handlePaginationClick) handlePaginationClick(tableData);
  };

  /**
   * handleRadioChange performs grid row selection operation
   * @param value
   */
  handleRadioChange = (value) => {
    const { handleRadioChange } = this.props;
    if (handleRadioChange) handleRadioChange(value);
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    const { handlePaginationClick } = this.props;
    if (handlePaginationClick) handlePaginationClick(tableData);
  };

  /**
   * handleCheckboxChange performs checkbox selection operation
   * @param event
   * @param selectedRowKeys
   */
  handleCheckboxChange = (event, selectedRowKeys) => {
    const { handleCheckboxChange } = this.props;
    if (handleCheckboxChange) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * handleSelectAll performs check all event in grid
   * @param event
   * @param selectedRowKeys
   */
  handleSelectAll = (event, selectedRowKeys) => {
    const { handleSelectAll, handleCheckboxChange } = this.props;
    if (handleSelectAll) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * handleSelectAll performs uncheck all event in grid
   * @param event
   * @param selectedRowKeys
   */
  handleClearAll = (event, selectedRowKeys) => {
    const { handleClearAll, handleCheckboxChange } = this.props;
    if (handleClearAll) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * languageLabelCheck performs language based data rendering operation
   * @param label
   * @return multiLangLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * renderComponent performs dynamic component rendering operation in grid
   * @param record
   * @param type
   * @return dynamicInnerGridComponent
   */
  renderComponent = (record, type) => {
    const { screenKey } = this.props;
    const {
      statusId,
      syncStatus,
      appStatus,
      deviceId,
      deviceName,
      policyViolationStatus,
      policyViolationDetail,
    } = record;
    const getIcon = {
      deviceList: { search: statusId || 0 },
      deviceCsvDetail: { search: syncStatus || 0 },
      app: { search: appStatus === 'unapproved' ? appStatus : 'approved' },
      userList: { search: statusId },
      userCsvList: { search: statusId },
    }[screenKey];
    if (type === 'icon') {
      const iconList = searchFilterCheckBoxList(screenKey);
      const result = iconList.filter((data) => data.checkBoxKey === getIcon.search)[0];
      if (policyViolationStatus > 0 && syncStatus !== 0 && screenKey === 'deviceList') {
        const policyViolation = iconList.filter((data) => data.checkBoxKey === 0)[0];
        return (
          <>
            <div className="sb-tooltip" style={{ marginTop: 3, marginLeft: 3, cursor: 'pointer' }}>
              <img
                src={policyViolation.url}
                alt={policyViolation.name}
                height="25px"
                width="25px"
              />
              {policyViolationStatus}
              {
                <div className="sb-tooltiptext">
                  {policyViolationDetail.map((detail) => (
                    <li key={detail} style={{ textAlign: 'center' }}>
                      {this.languageLabelCheck(detail)}
                    </li>
                  ))}
                </div>
              }
            </div>
            <img src={result.url} alt={result.name} height="25px" width="25px" />
          </>
        );
      }
      return <img src={result.url} alt={result.name} height="25px" width="25px" />;
    }
    if (type === 'policy' && screenKey === 'app') {
      const iconDetail = searchFilterCheckBoxList(screenKey);
      const appViolation = iconDetail.filter((data) => data.checkBoxKey === 'unapproved')[0];
      return (
        <span>
          <img src={appViolation.url} alt={appViolation.name} height="25px" width="25px" />
          <span style={{ verticalAlign: 'super' }}>{` ${record.appName}`}</span>
        </span>
      );
    }
    return (
      <SBLink
        state={{ deviceId }}
        content={deviceName}
        pathname={ROUTEENDPOINTS.DETAILDEVICELIST}
      />
    );
  };

  /**
   * render performs grid component rendering
   * @return gridComponent
   */
  render() {
    const {
      type,
      rowKey,
      gridList,
      countInfo,
      screenKey,
      pagination,
      isAscending,
      filterMessage,
      nonDataMessage,
      countInfoContents,
      countInfoTotalContents,
      defaultSelectedKeys,
      defaultSortColumn,
      defaultSelected,
      handleSelectChange,
    } = this.props;
    const getColumns = tableColumnList(this.renderComponent, screenKey, this.languageLabelCheck);
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          <RoleResolver
            component="sbtable"
            columns={getColumns}
            authorityRoleType={Role.deviceAuthority}
          >
            <SBTable
              loading
              type={type}
              rowKey={rowKey}
              columns={getColumns}
              defaultSelected={defaultSelected}
              countInfo={countInfo}
              dataSource={gridList}
              pagination={pagination}
              isAscending={isAscending}
              defaultSortColumn={defaultSortColumn}
              filterMessage={filterMessage}
              countInfoContents={countInfoContents}
              countInfoTotalContents={countInfoTotalContents}
              defaultSelectedKeys={defaultSelectedKeys}
              filterList={commonConstants.gridListComboOptions}
              handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
              handleRadioChange={(event) => this.handleRadioChange(event)}
              handleCheckboxChange={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleSelectAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleClearAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleSelectChange={() => handleSelectChange()}
            />
          </RoleResolver>
        </div>
        {!isEmpty(gridList) ? null : (
          <div className="clearfix mb10" style={{ marginTop: 10 }}>
            {this.languageLabelCheck(nonDataMessage || 'no_results_found')}
          </div>
        )}
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  return { localeData: state.language.data };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceGrid.propTypes = {
  type: PropTypes.string,
  rowKey: PropTypes.string,
  pagination: PropTypes.bool,
  countInfo: PropTypes.string,
  isNotMultiLang: PropTypes.bool,
  filterMessage: PropTypes.string,
  nonDataMessage: PropTypes.string,
  countInfoContents: PropTypes.number,
  countInfoTotalContents: PropTypes.number,
  screenKey: PropTypes.string.isRequired,
  handleClearAll: PropTypes.func.isRequired,
  handleSelectAll: PropTypes.func.isRequired,
  handleRadioChange: PropTypes.func.isRequired,
  gridList: PropTypes.arrayOf(PropTypes.object),
  handleCheckboxChange: PropTypes.func.isRequired,
  handlePaginationClick: PropTypes.func.isRequired,
  defaultSelectedKeys: PropTypes.arrayOf(PropTypes.number),
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DeviceGrid.defaultProps = {
  type: '',
  rowKey: '',
  gridList: [],
  pagination: false,
  nonDataMessage: '',
  countInfoContents: 0,
  countInfoTotalContents: 0,
  isNotMultiLang: false,
  defaultSelectedKeys: [],
  countInfo: 'registered_count_items',
  filterMessage: 'display_items_colon',
};

export default connect(mapStateToProps)(withRouter(DeviceGrid));
