/**
 * PaymentRegistered is used to load registered content
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import checkLanguage from '../../../language/checkLanguage';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBComboBox from '../../../common/component/combobox/SBComboBox';

class PaymentRegistered extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * paymentNumber is used to load  registered approval number
   * @param paymentNumber
   * @return filterValues
   */

  setComboValues = paymentNumber => {
    const filterValues = [];
    let index = 0;
    paymentNumber.forEach(function(item) {
      filterValues.push({ value: index, option: item.approvalNumber });
      index += 1;
    });
    return filterValues;
  };

  /**
   * render is used to render the component
   * @return paymentRegisteredComponent
   */

  render() {
    const { onHandleChange, onHandleClick, paymentSource, errorMessage, reference } = this.props;
    const { paymentNumber, approvalSelected } = paymentSource;
    const genertateOptions = this.setComboValues(paymentNumber);
    return (
      <table className="table-tate mb10">
        <tbody>
          <tr>
            <th rowSpan="2">{this.languageLabelCheck('registered_approval_number')}</th>
            <td>
              {paymentNumber && paymentNumber.length ? (
                <>
                  <div>
                    <SBComboBox
                      size="5"
                      id="approvalNumber"
                      name="approval_number"
                      filterKey="value"
                      filterValue="option"
                      canModify
                      isNotMultiLang
                      ref={reference.registerRef}
                      className="w100per border-gray p10 floatleft mb5"
                      value={approvalSelected}
                      style={{ 'min-width': 220 }}
                      options={genertateOptions}
                      onChange={(event, value) => onHandleChange('approvalSelected', event, value)}
                    />
                  </div>
                  <div style={{ marginBottom: 5 }}>
                    <SBButton
                      type="button"
                      id="btn_load"
                      title="payment_load"
                      style={{ float: 'right' }}
                      noMarginRight
                      onClick={() => onHandleClick('load')}
                    />
                  </div>
                  <span className="red bold">
                    {this.languageLabelCheck(errorMessage.selectError)}
                  </span>
                </>
              ) : null}
            </td>
          </tr>
        </tbody>
      </table>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

PaymentRegistered.propTypes = {
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
  onHandleClick: PropTypes.func.isRequired,
  onHandleChange: PropTypes.func.isRequired,
  paymentSource: PropTypes.objectOf(PropTypes.object),
  errorMessage: PropTypes.objectOf(PropTypes.object),
  reference: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
PaymentRegistered.defaultProps = {
  isNotMultiLang: false,
  reference: {},
  paymentSource: {},
  errorMessage: {},
};

export default connect(mapStateToProps)(withRouter(PaymentRegistered));
