/** Base Files */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementFooter from '../component/GroupManagementFooter';
import GroupManagementIndicator from '../component/GroupManagementIndicator';

/** Utility Files */
import {
  serviceMapper,
  initializeRefs,
  getGroupInformation,
} from '../common/GroupSettingCommonFunctions';
import { Role } from '../../constants/Role';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import { setDataSource } from '../constants/groupSettingConstants';
import { tableView } from '../constants/groupSettingTableConstants';
import { groupRegistrationHeader } from '../constants/groupSettingHeaderConstants';
import { makeGroupInformation, resetGroupInformation } from '../../actions/GroupAction';

/**
 * GroupInformation is to display selected group information
 * @author Aswin Palanisamy
 */
class GroupInformation extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { location: getData, mode } = props;
    const { groupId } = getData.state || { groupId: -1 };
    this.state = {
      pageLoading: false,
      dialogWidth: '400px',
      openErrorDialog: false,
      openDeviceDialog: false,
      footer: {
        reference: initializeRefs(),
        authorityRoleType: Role.group1,
        screenKey: mode ? 'groupConfirmation' : 'groupInformation',
      },
      headerData: groupRegistrationHeader('groupRegistration', mode),
      dynamicData: { policyIsExist: false, deviceIndicator: false },
      dataSource: { ...setDataSource(props), groupId: groupId || -1 },
    };
  }

  /**
   * componentDidMount is to fetch group information in edit,addCopy and delete mode
   */
  componentDidMount() {
    this.setState({ pageLoading: true }, async () => {
      const groupInformation = await getGroupInformation(this.state, this.props);
      this.setState({ ...groupInformation });
    });
  }

  /**
   * componentDidUpdate is to set focus to the component based on visibility
   */
  componentDidUpdate() {
    const { footer } = this.state;
    if (footer.reference.changeButtonRef.current) footer.reference.changeButtonRef.current.focus();
  }

  /**
   * onHandleClick is to handle botton click events in group information
   * @param screenKey
   * @return footerActionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      back: () => this.navigateListScreen(),
      change: () => this.navigateDetailScreen(serviceMapper(screenKey)),
    }[screenKey]());

  /**
   * navigateDetailScreen is to navigate group information screen
   * to group registration screen
   * @param screenName
   */
  navigateDetailScreen = (screenName) => {
    const { dataSource } = this.state;
    const { history, location } = this.props;
    history.push(screenName, {
      groupId: dataSource.groupId,
      parentScreen: location.pathname,
    });
  };

  /**
   * navigateListScreen is to navigate  from groupInformation to groupManagement
   */
  navigateListScreen = () => {
    const { history, resetGroupData } = this.props;
    resetGroupData();
    this.setState({ openErrorDialog: false }, () => history.push(ROUTEENDPOINTS.GROUPMANAGEMENT));
  };

  /**
   * onTableFrameEvent is to indicate selected device in grid format within dialog
   * @param key
   */
  onTableFrameEvent = (key) => {
    if (key === 'deviceIndicator') this.setState({ openDeviceDialog: true, dialogWidth: '583px' });
  };

  /**
   * render performs group information screen rendering
   * @return GroupInformation
   */
  render() {
    const {
      footer,
      message,
      dataSource,
      headerData,
      pageLoading,
      dynamicData,
      dialogWidth,
      openErrorDialog,
      openDeviceDialog,
    } = this.state;
    const { deviceList } = this.props;

    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div>
              <CustomTableFrame
                dataSource={dataSource}
                tableList={tableView(dynamicData)}
                onTableFrameEvent={(key) => this.onTableFrameEvent(key)}
              />
            </div>
            <div>
              <GroupManagementFooter
                footer={footer}
                onHandleClick={(key) => this.onHandleClick(key)}
              />
            </div>
          </div>
          <GroupManagementIndicator
            notMultiLang
            hasHeightScroll
            gridHeight="350px"
            dialogKey="deviceIndicator"
            dialogContent={deviceList}
            width={dialogWidth}
            openDeviceDialog={openDeviceDialog}
            closeDeviceDialog={() => this.setState({ openDeviceDialog: false })}
          />
          <SBDialog
            type="error"
            width={dialogWidth}
            header="error"
            okButtonTitle="ok"
            content={message}
            open={openErrorDialog}
            onOk={() => this.navigateListScreen()}
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const {
    groupId,
    groupName,
    authority,
    deviceList,
    selectMethod,
    kioskEnable,
    mainPolicyMapper,
    defaultPolicyData,
    applicationListMapper,
    kioskAppSelect,
    kioskBgApps,
    imeApps,
    asmApps,
  } = state.group;
  const { current: currentLanguage } = state.language;
  return {
    groupId,
    groupName,
    authority,
    deviceList,
    selectMethod,
    kioskEnable,
    mainPolicyMapper,
    defaultPolicyData,
    applicationListMapper,
    kioskAppSelect,
    kioskBgApps,
    imeApps,
    asmApps,
    currentLanguage,
  };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    resetGroupData: () => dispatch(resetGroupInformation()),
    makeGroupData: (data) => dispatch(makeGroupInformation(data)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
GroupInformation.propTypes = {
  resetGroupData: PropTypes.func.isRequired,
  mode: PropTypes.number.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      groupId: PropTypes.number,
    }),
    pathname: PropTypes.string,
  }).isRequired,
  deviceList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(GroupInformation));
