import { showErrorMessage } from './errorActions';

function responseInterceptor(response) {
  if (response.status === 401) {
    localStorage.setItem('error', 'true');
    window.location.href = window.location.origin + '/';
    const errorDetail = {
      statusCode: response.status,
    };
    throw errorDetail;
  }

  if (response.status === 301 && response.headers.get('redirect-url') !== null) {
    window.location.href = response.headers.get('redirect-url');
    return response;
  }

  if (response.status === 403) {
    showErrorMessage("You don't have permission to perform this action, please contact admin.");
  }

  if (response.status === 500) {
    const error = new Error(response.statusText);
    error.response = response;
    response.json().then(result => {
      if (result.message !== undefined) {
        showErrorMessage(result.message);
      }
    });
  }

  if (!response.ok) {
    const errorDetail = {
      statusText: response.statusText,
      url: response.url,
      statusCode: response.status,
    };
    const errorMessage = new Error(JSON.stringify(errorDetail));

    throw errorMessage;
  }

  return response;
}

const originalFetch = window.fetch;

export function fetch(request, init) {
  if (init) {
    init.credentials = 'same-origin';
  } else {
    init = {
      credentials: 'same-origin',
    };
  }

  return originalFetch(request, init).then(responseInterceptor);
}
