import React from 'react';
import { connect } from 'react-redux';
import SBDialog from './SBDialog';
import { commonOnChangeMenu } from '../../changeReducer/commonChangeAction';

const CommonSBDialog = (props) => {
  const afterOnOkClick = () => {
    const { onOk, setModified } = props;
    setModified({ open: false, isModified: false });
    if (onOk) {
      onOk();
    }
  };

  const onCancelHandler = () => {
    const { onCancel, setModified } = props;
    setModified({ open: false });
    if (onCancel) {
      onCancel();
    }
  };

  const { open } = props;
  return (
    <SBDialog
      open={open}
      header="cancel_confirmation"
      type="confirmation"
      content="cancel_confirmation_message"
      onOk={() => afterOnOkClick()}
      onCancel={() => onCancelHandler()}
      width="400px"
    />
  );
};
const mapDispatchToProps = (dispatch) => {
  return { setModified: (payload) => dispatch(commonOnChangeMenu(payload)) };
};

export default connect(null, mapDispatchToProps)(CommonSBDialog);
