/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';
import fileDownload from 'js-file-download';
import encoding from 'encoding';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { Role } from '../../constants/Role';
import { get, post, postWithBlobData } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import checkLanguage from '../../language/checkLanguage';
import deviceHeader from '../constants/deviceHeaderConstants';
import { tableList } from '../constants/deviceTableConstants';
import { serviceMapper } from '../common/deviceCommonFunctions';
import { getUserGroupNameList } from '../../actions/DeviceAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import {
  dateTimeUnderScoreWithoutOffset,
  getUserTodaysDate,
  dateBasedOnLocaleGeneral,
} from '../../common/utils/dateUtils';

/**
 * DeviceList is to display list of enrolled device in an enterprise
 * @author Aswin Palanisamy
 */

const resultPerPageKey = 'device_list';

const dateHourMinRegexp = new RegExp(
  /[0-9]{4}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1]) (2[0-3]|[01][0-9]):[0-5][0-9]$/,
);
const dateHourRegexp = new RegExp(
  /[0-9]{4}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1]) (2[0-3]|[01][0-9])$/,
);
const hourMinRegExp = new RegExp(/(2[0-3]|[01][0-9]):[0-5][0-9]$/);
class DeviceList extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, location, makeUserGroupList } = props;
    const { groupName } = location.state || { groupName: '' };
    this.groupName = groupName;
    makeUserGroupList();
    const { deviceSearch, groupSearch } = JSON.parse(localStorage.getItem('deviceSearch'));
    this.state = {
      screenKey,
      message: '',
      disable: true,
      deviceList: [],
      statusList: [],
      pageLoading: true,
      gridLoading: false,
      countInfoContents: 0,
      refreshDisable: false,
      openErrorDialog: false,
      isAscending: false,
      downloadType: 'deviceCsv',
      defaultSortColumn: 'enrolledDate',
      headerData: deviceHeader(screenKey),
      groupSearch: cloneDeep(groupSearch),
      deviceSearch: cloneDeep(deviceSearch),
      authorityRoleType: Role.deviceBatchAuthority,
    };
    this.searchRef = createRef();
    this.csvOutputRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    this.fetchDeviceList();
  }

  /**
   * languageLabelCheck is to set label content
   * @param label
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * getDeviceList is to load list of device records
   * @param onOpen
   */
  getDeviceList = async (onOpen) => {
    const {
      groupSearch: enumList,
      departmentSearch: searchList,
      multipleConditionSearch: deviceFilterCondition,
    } = JSON.parse(localStorage.getItem('deviceSearch'));
    const { deviceSearch, statusList } = this.state;
    const deviceId = deviceSearch || '';
    const { deviceSearch: deviceFilter, policySearch } = deviceFilterCondition;
    const multipleConditionSearch = {
      deviceSearch: cloneDeep(deviceFilter.filter((record) => record.category)),
      policySearch: cloneDeep(policySearch),
    };
    if (multipleConditionSearch.deviceSearch.some((obj) => obj.field === 'dataCollectionTime')) {
      this.getConvertedDate(multipleConditionSearch);
    }
    const response = await post(APIENDPOINTS.GETDEVICELIST, {
      deviceId,
      enumList,
      statusList,
      searchList,
      multipleConditionSearch,
    });
    const result = await response.json();
    const { status, message, data: deviceList } = result;
    if (status === 'OK') {
      this.setState(
        {
          deviceList,
          pageLoading: false,
          gridLoading: false,
          deviceSearch: deviceId,
          refreshDisable: !message,
          disable: isEmpty(deviceList),
          countInfoContents: deviceList.length,
        },
        () => {
          if (onOpen && this.searchRef.current) this.searchRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        pageLoading: false,
        gridLoading: false,
        refreshDisable: true,
        openErrorDialog: true,
      });
    }
  };

  /**
   * fetchDeviceList is to load list of device records based on search data
   * @param value
   */
  fetchDeviceList = (value) => {
    const getStore = JSON.parse(localStorage.getItem('deviceSearch'));
    if (this.groupName) {
      getStore.groupSearch = [this.groupName];
    }
    const { searchKeyword: deviceSearch, checkBoxKeys: statusList } =
      value || getStore.deviceSearch;
    localStorage.setItem(
      'deviceSearch',
      JSON.stringify({
        ...getStore,
        deviceSearch: { searchKeyword: deviceSearch, checkBoxKeys: statusList },
      }),
    );
    this.setState({ gridLoading: true, deviceSearch, statusList }, () =>
      this.getDeviceList(!value),
    );
  };

  /**
   * syncDeviceList is to sync updated device information from android API
   */
  syncDeviceList = () => {
    const { deviceSearch: getStore } = JSON.parse(localStorage.getItem('deviceSearch'));
    const { searchKeyword: deviceSearch, checkBoxKeys: statusList } = getStore;
    this.setState({ refreshDisable: true, deviceSearch, statusList }, () => {
      get(APIENDPOINTS.SYNCDEVICELIST);
    });
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    this.setState({ disable: isEmpty(tableData) });
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param deviceSearch
   */
  onKeyDown = (event, deviceSearch) => {
    if (event.keyCode === 13)
      this.setState({ deviceSearch, gridLoading: true }, () => this.getDeviceList());
  };

  /**
   * onSearchText is to update the search condition text value
   * @param deviceSearch
   */
  onSearchText = (deviceSearch) => this.setState({ deviceSearch });

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param value
   */
  handleSearchAction = (key, value) => {
    const searchActionMapper = {
      refresh: () => this.syncDeviceList(),
      departmentSearch: () => this.onHandleClick(key),
      searchKeyword: () => this.onSearchText(value[key]),
      multipleConditionSearch: () => this.onHandleClick(key),
      searchButton: () => this.fetchDeviceList({ searchKeyword: '', checkBoxKeys: [], ...value }),
    };
    return searchActionMapper[key] && searchActionMapper[key]();
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   */
  onHandleClick = (screenKey) => {
    const { history, location } = this.props;
    if (screenKey === 'csvDownload') {
      this.setState({ gridLoading: true }, () => this.downLoadCSVFile());
    } else {
      history.push(serviceMapper(screenKey), {
        parentScreen: location.pathname,
      });
    }
  };

  /**
   * onHandleChange is to change the device csv download type
   * @param event
   */
  onHandleChange = (event) => {
    const { value } = event.target;
    this.setState({ downloadType: value });
  };

  /**
   * downLoadCSVFile is used to fetch all device list and download as CSV File.
   *
   */
  downLoadCSVFile = async () => {
    const {
      groupSearch: enumList,
      departmentSearch: searchList,
      multipleConditionSearch: deviceFilterCondition,
    } = JSON.parse(localStorage.getItem('deviceSearch'));
    const { userVo, currentLanguage } = this.props;
    const { deviceSearch, statusList, downloadType: search } = this.state;
    const deviceId = deviceSearch || '';
    const { deviceSearch: deviceFilter, policySearch } = deviceFilterCondition;
    const multipleConditionSearch = {
      deviceSearch: cloneDeep(deviceFilter.filter((record) => record.category)),
      policySearch: cloneDeep(policySearch),
    };
    if (multipleConditionSearch.deviceSearch.some((obj) => obj.field === 'dataCollectionTime')) {
      this.getConvertedDate(multipleConditionSearch);
    }
    const response = await postWithBlobData(APIENDPOINTS.CSVDEVICEDOWNLOAD, {
      search,
      deviceId,
      enumList,
      statusList,
      searchList,
      multipleConditionSearch,
    });
    const result = await response.json();
    const { status, message, data: deviceList } = result;
    if (status === 'OK') {
      this.setState({ gridLoading: false, deviceList }, () => {
        fileDownload(
          encoding.convert(message, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
          `${userVo.dmCode}_${userVo.businessName}${this.languageLabelCheck(
            search,
          )}${dateBasedOnLocaleGeneral(getUserTodaysDate(userVo.timeZoneOffSet))}.csv`,
        );
      });
    } else {
      this.setState({
        message,
        pageLoading: false,
        gridLoading: false,
        refreshDisable: true,
        openErrorDialog: true,
      });
    }
    if (this.searchRef.current) this.searchRef.current.value = deviceId;
    if (this.csvOutputRef.current) this.csvOutputRef.current.focus();
  };

  /**
   * getConvertedDate is used to convert the given date to utc date
   * @param {obj} multipleConditionSearch
   */
  getConvertedDate = (multipleConditionSearch) => {
    multipleConditionSearch.deviceSearch
      .map((obj) => {
        obj.characterString = obj.characterString.replaceAll('/', '-');
        if(obj.characterString.endsWith(':')) obj.characterString = obj.characterString.replaceAll(':', '');
        return obj;
      })
      .filter((obj) => obj.field === 'dataCollectionTime')
      .map((obj) => {
        const validData = dateHourMinRegexp.test(obj.characterString);
        let trimSeconds = false;
        let trimDate = false;
        let appended = false;
        if(!validData){
               trimSeconds = dateHourRegexp.test(obj.characterString);
           if(!trimSeconds){
              trimDate = hourMinRegExp.test(obj.characterString);
            }
            if(new RegExp(
              /[0-9]{4}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1]) (2[0-3]|[01][0-9]):[0-9]$/,
            ).test(obj.characterString)){
              obj.characterString = `${obj.characterString}0`;
              appended = true;

            }
           if (trimDate) {
              obj.characterString = `01-01-1970 ${obj.characterString}`;
           }
      }
        const date = dateTimeUnderScoreWithoutOffset(obj.characterString);
        if (date !== 'Invalid date') {
          obj.convertedDate = date;
          obj.trimSeconds = trimSeconds;
          obj.trimDate = trimDate;
          obj.searchData = obj.characterString;
          obj.validData = validData;
          obj.appended = appended;
        }
        return obj;
      });
  };

  /**
   * handleSelectChange performs device grid rendering
   */
  handleSelectChange = () => {
    this.setState({ gridLoading: true }, () => this.getDeviceList());
  };

  /**
   * render performs group management screen rendering
   * @return DeviceList
   */
  render() {
    const {
      message,
      disable,
      screenKey,
      deviceList,
      statusList,
      headerData,
      pageLoading,
      gridLoading,
      groupSearch,
      isAscending,
      deviceSearch,
      downloadType,
      refreshDisable,
      openErrorDialog,
      countInfoContents,
      defaultSortColumn,
      authorityRoleType,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <CustomTableFrame
            tableList={tableList()}
            dataSource={{
              selectGroup: groupSearch
                .sort((present, next) => (present.toUpperCase() < next.toUpperCase() ? -1 : 1))
                .join(','),
            }}
            onTableFrameEvent={(key) => this.onHandleClick(key)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
        </div>
        <DeviceSearch
          disabled={refreshDisable}
          screenKey={screenKey}
          searchText={deviceSearch}
          searchKeyword="searchKeyword"
          defaultValues={statusList}
          reference={this.searchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '62%', left: 'calc(40% - 55px)' }}
        >
          <DeviceGrid
            pagination
            gridList={deviceList}
            isAscending={isAscending}
            defaultSortColumn={defaultSortColumn}
            screenKey={screenKey}
            countInfo="search_result_device_count"
            filterMessage="results_per_page"
            countInfoContents={countInfoContents}
            handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
            defaultSelected={resultPerPageKey}
            handleSelectChange={() => this.handleSelectChange()}
          />
          <DeviceFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            onHandleChange={(key, event) => this.onHandleChange(event)}
            footer={{
              disable,
              screenKey,
              authorityRoleType,
              value: downloadType,
              reference: this.csvOutputRef,
            }}
          />
        </CircularSpinner>
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @param state
 *
 */

const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { data, current } = state.language;
  return {
    userVo,
    localeData: data,
    currentLanguage: current,
  };
};

/**
 * mapDispatchToProps performs data setup in device redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeUserGroupList: (data) => {
      dispatch(getUserGroupNameList(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceList.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      groupId: PropTypes.string,
    }),
  }).isRequired,
  currentLanguage: PropTypes.string.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  makeUserGroupList: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceList));
