/**
 * ContractInformationChange is used to build components of Change Contract Information Screen.
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import { CommonConstants } from '../../../constants/CommonConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import {
  initialDataSource,
  administratorAccountViewTableList,
  basicInformationTableList,
  administratorAccountChangeTableList,
  initializeRefs,
  getFooterButton,
  setValidationErrorMessage,
} from '../constants/ContractInfoConstants';
import ContractInformationFooter from './ContractInformationFooter';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get, put } from '../../../actions/commonActions';
import getLanguageData from '../../../language/languageUtils';
import getUserEntity from '../../../actions/UserActions';
import languageChangerAction from '../../../language/languageActions';
import {
  isNotUndefinedOrNotEmpty,
  validateEmail,
  validateMultipleIps,
} from '../../../common/CommonValidations/CommonValidations';
import checkLanguage from '../../../language/checkLanguage';
import { Role } from '../../../constants/Role';

class ContractInformationChange extends Component {
  constructor(props) {
    super(props);
    const { tenantVo } = this.props;
    this.state = {
      pageLoading: true,
      dataSource: initialDataSource(),
      administratorTableList: administratorAccountViewTableList(this.languageLabelCheck, tenantVo),
      dynamicData: { disableOkButton: true },
      reference: initializeRefs(),
      isFinished: false,
    };
  }

  componentDidMount() {
    const { history, userVo, tenantVo } = this.props;
    const { dynamicData, reference } = this.state;
    let { dataSource } = this.state;
    if (Role.group1.includes(userVo.authorityId)) {
      const userStatus = async () => {
        const userDetailResponse = await get(
          `${APIENDPOINTS.APPLICATIONUSERDETAILS}/${userVo.dmCode}`,
        );
        const userDetailData = await userDetailResponse.json();
        if (userDetailData.status) {
          const { applicationVo } = userDetailData;
          dataSource = {
            ...{
              noOfAndroidDeviceReg: `${applicationVo.androidDeviceRegistered} / ${applicationVo.androidDeviceCount}`,
            },
            ...userDetailData.userVo,
            ...dataSource,
          };
          dynamicData.isApplicationUserAdmin = Role.systemAdminAppUser.includes(userVo.authorityId);
          dataSource.password = '********';
          this.setState({
            dataSource,
            administratorTableList: administratorAccountChangeTableList(
              dynamicData,
              reference,
              tenantVo,
            ),
            pageLoading: false,
          });
          if (reference.emailIdRef.current) {
            reference.emailIdRef.current.focus();
          }
        } else {
          history.push(RouteEndPoints.ERROR);
        }
      };
      userStatus();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */
  onTableFrameEvent = (key, event, value) => {
    const { setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    setModified(true);
    dynamicData.disableOkButton = false;
    dataSource[key] = event.target.type === 'text' || key === 'report' ? value : event.target.value;
    this.setState({ dataSource, dynamicData });
  };

  /**
   * updateUserReduxData is used to update redux data
   * @param resultSet
   *
   */
  updateUserReduxData = async (resultSet) => {
    const { userVo, getUserEntity: getLoggedUser, changeLanguage, localeData } = this.props;
    let languageMap = { ...localeData };
    if (resultSet.userVo.languageCode !== userVo.languageCode) {
      languageMap = await getLanguageData(resultSet.userVo.languageCode);
    }
    changeLanguage(resultSet.userVo.languageCode, resultSet.userVo.timeZoneCode, languageMap);
    getLoggedUser(resultSet.userVo);
  };

  /**
   * validateData is used to validate the data
   */
  validateData = () => {
    const { tenantVo } = this.props;
    const { dataSource, reference } = this.state;
    let { dynamicData } = this.state;
    const emailValidation = validateEmail(dataSource.emailID);
    const ipValidation = validateMultipleIps(dataSource.ipAddress);
    dynamicData = setValidationErrorMessage('emailError', emailValidation, dynamicData);
    dynamicData = setValidationErrorMessage('ipError', ipValidation, dynamicData);
    this.setState({
      dynamicData,
      administratorTableList: administratorAccountChangeTableList(dynamicData, reference, tenantVo),
    });
    return emailValidation.status && ipValidation.status;
  };

  /**
   * setFocusInErrorField is used to set focus on error input field
   *
   */
  setFocusInErrorField = () => {
    const { dynamicData, reference } = this.state;
    let errorFieldRef = null;
    if (dynamicData.emailError) {
      errorFieldRef = reference.emailIdRef;
    } else if (dynamicData.ipError) {
      errorFieldRef = reference.ipAddressRef;
    }
    if (errorFieldRef && errorFieldRef.current) {
      errorFieldRef.current.focus();
    }
  };

  /**
   * onOkButtonClick is used to perfom save operation
   *
   */
  onOkButtonClick = () => {
    const { userVo, setModified, history, tenantVo } = this.props;
    const { dataSource, reference } = this.state;
    if (this.validateData()) {
      this.setState({ pageLoading: true });
      const saveProcess = async () => {
        dataSource.ipAddress = isNotUndefinedOrNotEmpty(dataSource.ipAddress)
          ? dataSource.ipAddress.trim()
          : null;
        const response = await put(APIENDPOINTS.UPDATEAPPLICATIONUSER, dataSource);
        const resultSet = await response.json();
        if (resultSet.status) {
          if (resultSet.userVo.userId === userVo.userId) {
            this.updateUserReduxData(resultSet);
          }
          setModified(false);
          this.setState({
            dataSource: { ...dataSource, ...resultSet.userVo },
            pageLoading: false,
            isFinished: true,
            administratorTableList: administratorAccountViewTableList(
              this.languageLabelCheck,
              tenantVo,
            ).slice(1, 7),
          });
          if (reference.backButtonRef.current) {
            reference.backButtonRef.current.focus();
          }
        } else {
          history.push(RouteEndPoints.ERROR);
        }
      };
      saveProcess();
    } else {
      this.setFocusInErrorField();
    }
  };

  /**
   * onFooterComponentAction is used to handle footercomponent action
   * @param key
   */
  onFooterComponentAction = (key) => {
    const { history } = this.props;
    if (key === 'ok') {
      this.onOkButtonClick();
    } else {
      history.push(RouteEndPoints.CONTRACTINFO);
    }
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */
  onKeyDown = (event) => {
    const { dynamicData } = this.state;
    if (event.keyCode === 13 && !dynamicData.disableOkButton) {
      event.preventDefault();
      this.onOkButtonClick();
    }
  };

  /**
   * render is used to render components of the screen
   */
  render() {
    const { screenTitleData } = this.props;
    const {
      pageLoading,
      dataSource,
      administratorTableList,
      dynamicData,
      isFinished,
      reference,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={screenTitleData.title}
          description={
            isFinished ? ['change_contract_info_finish_desc'] : screenTitleData.description
          }
        />
        <div className="contents-main">
          <SBTableHeader headerText="basic_information" />
          <CustomTableFrame tableList={basicInformationTableList()} dataSource={dataSource} />
          <SBTableHeader headerText="administrator_account" />
          <CustomTableFrame
            tableList={administratorTableList}
            dataSource={dataSource}
            onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
          <ContractInformationFooter
            buttonsList={
              isFinished
                ? getFooterButton({
                    ...dynamicData,
                    ref: { backButtonRef: reference.backButtonRef },
                  }).slice(2, 3)
                : getFooterButton(dynamicData).slice(0, 2)
            }
            onHandleClick={(key) => this.onFooterComponentAction(key)}
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ContractInformationChange.propTypes = {
  setModified: commonPropTypes().setModified.isRequired,
  screenTitleData: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.shape([PropTypes.string]),
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  history: commonPropTypes().history.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  changeLanguage: PropTypes.func.isRequired,
  getUserEntity: PropTypes.func.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ContractInformationChange.defaultProps = {
  isNotMultiLang: false,
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { tenantVo } = state.tenant;
  return {
    tenantVo,
    userVo,
    localeData: state.language.data,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 */
const mapDispatchToProps = (dispatch) => {
  return {
    getUserEntity: (data) => {
      dispatch(getUserEntity(data));
    },

    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },

    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ContractInformationChange));
