/**
 * KioskErrorConstants is used to throw error values
 * @author: P.Vinothpandian
 */

/**
 * errorMessageConstants contains error message type details
 */

export const errorMessageConstants = {
    wallPaperUploadError: {
        fileNameLength: 'upload_file_name_length',
        fileSizeGreater: 'upload_file_size_greater',
        fileExtension: 'wallpaper_file_extension',
        nofile: 'upload_no_file',
    },
    orderError: {
        empty: 'order_value_empty',
        invalid: 'order_value_invalid',
        range: 'order_value_invalid',
        same: 'order_value_same',
    },
    urlError: {
        empty: 'url_value_empty',
        invalid: 'url_value_invalid',
    },
    urlTitleError: {
        empty: 'url_title_value_empty',
        length: 'url_title_length_invalid',
    },
    packageError: {
        empty: 'package_name_not_selected',
    },
    iconSizeError: {
        length: 'icon_size_length',
        range: 'icon_size_invalid',
        invalid: 'icon_size_invalid',
    }
}

/**
 * setValidationErrorMessage is used to set error validation message
 *
 * @param key
 * @param validationResultObject
 * @param dynamicData
 * @return tempDynamicData
 *
 */

export function setValidationErrorMessage(key, validationResultObject, dynamicData) {
    const tempDynamicData = dynamicData;
    tempDynamicData[key] = errorMessageConstants[key][validationResultObject.error];
    return tempDynamicData;
  }
  