/**
 * SecurityPolicyTable is used to load the data
 * @author: P.Vinothpandian ,V.Vijayaragavan
 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import checkLanguage from '../../../../language/checkLanguage';
import addDynamicContent from '../../../../language/addDynamicContent';
import SBTable from '../../../../common/component/sbtable/SBTable';
import RoleResolver from '../../../../common/component/guard/RoleResolver';
import commonPropTypes from '../../../../common/commonPropTypes/commonPropTypes';
import { Role } from '../../../../constants/Role';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';

class SecurityPolicyTable extends Component {
  /**
   * handleRadioChange is used to handle radio change in the grid
   * @param value
   *
   */

  handleRadioChange = value => {
    const { handleRadioChange } = this.props;
    if (handleRadioChange) {
      handleRadioChange(value);
    }
  };

  /**
   * handlePaginationClick is used to handle pagination event
   * @param tableData
   *
   */

  handlePaginationClick = tableData => {
    const { handlePaginationClick } = this.props;
    if (handlePaginationClick) {
      handlePaginationClick(tableData);
    }
  };

  /**
   * handleSelectChange is used to handle data count combo change
   * @param value
   *
   */

  handleSelectChange = value => {
    const { handleSelectChange } = this.props;
    if (handleSelectChange) {
      handleSelectChange(value);
    }
  };

  handleClick = () => {
    const { handleChangeClick } = this.props;
    if (handleChangeClick) handleChangeClick();
  };
  /**
   * handleDeleteClick is used to handle delete button click action
   *
   */

  handleDeleteClick = () => {
    const { handleDeleteClick } = this.props;
    if (handleDeleteClick) handleDeleteClick();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * hasEnableAutority is used to enable/disable Delete button based on user autority.
   * @return boolean
   *
   */

  hasEnableAutority = () => {
    const { createdAccount, userVo } = this.props;
    if (
      Role.group4.includes(userVo.authorityId) ||
      userVo.loginId === createdAccount ||
      (userVo.authorityId === 7 && userVo.groupList.length === 0)
    ) {
      return false;
    }
    return true;
  };

  /**
   * getColumns is load the column information
   * @return columndetails
   *
   */

  getColumns = () => {
    return [
      {
        title: '',
        dataIndex: 'render-radio',
        styles: { minWidth: 20, width: 20 },
        textAlign: 'center',
      },
      {
        title: 'security_policy_name',
        dataIndex: 'securityPolicyName',
        rowKey: 'securityPolicyName',
        styles: { minWidth: 20, width: 20 },
        sorter: false,
        textAlign: 'left',
        render: record => {
          return (
            <Link to={{ pathname: ROUTEENDPOINTS.SECURITYPOLICYDETAIL, state: record }}>
              <u>{record.securityPolicyName}</u>
            </Link>
          );
        },
      },
      {
        title: 'security_policy_assign_target',
        dataIndex: 'assignTarget',
        rowKey: 'assignTarget',
        styles: { minWidth: 20, width: 80 },
        sorter: false,
        textAlign: 'left',
      },
      {
        title: 'created_account',
        dataIndex: 'createdAccount',
        rowKey: 'createdAccount',
        styles: { minWidth: 20, width: 80 },
        sorter: false,
        textAlign: 'left',
      },
    ];
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const columns = this.getColumns();
    const { data, defaultSelected } = this.props;
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          <RoleResolver
            component="sbtable"
            columns={columns}
            authorityRoleType={Role.multipleAdminRegisterAndChange}
          >
            <SBTable
              columns={columns}
              dataSource={data}
              loading
              pagination
              defaultSelected={defaultSelected}
              filterList={[
                { key: 25, value: '25_items' },
                { key: 50, value: '50_items' },
                { key: 100, value: '100_items' },
                { key: 'all', value: 'all' },
              ]}
              defaultSelectedKeys={[0]}
              countInfo="security_policy_list_count"
              countInfoContents={data.length}
              handleRadioChange={value => this.handleRadioChange(value)}
              handlePaginationClick={tableData => this.handlePaginationClick(tableData)}
              handleSelectChange={value => this.handleSelectChange(value)}
            />
          </RoleResolver>
          {data.length ? null : (
            <div className="clearfix mb10" style={{ marginTop: 10 }}>
              {this.languageLabelCheck('no_results_found')}
              <br />
            </div>
          )}
          <div className="clearfix">
            <div className="floatright">
              <RoleResolver authorityRoleType={Role.securityPolicyCUD}>
                <SBButton
                  id="btn_change"
                  type="button"
                  title="security_policy_btn_edit"
                  color="green"
                  onClick={() => this.handleClick()}
                  disabled={!data.length || this.hasEnableAutority()}
                />
              </RoleResolver>
              <RoleResolver authorityRoleType={Role.securityPolicyCUD}>
                <SBButton
                  id="btn_delete"
                  noMarginRight
                  type="button"
                  title="security_policy_btn_delete"
                  onClick={() => this.handleDeleteClick()}
                  disabled={!data.length || this.hasEnableAutority()}
                />
              </RoleResolver>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    localeData: state.language.data,
    userVo,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
SecurityPolicyTable.propTypes = {
  userVo: commonPropTypes().userVo.isRequired,
};

export default connect(mapStateToProps)(withRouter(SecurityPolicyTable));
