import ReactHtmlParser from 'react-html-parser';

const regex = /\[([A-Z][A-Za-z]*)\]/;
const endTag = /\[\/([A-Z][A-Za-z]*)\]/;
const htmlregex = /\[([a-z/]+)\]/;

const splitContent = data => {
  return (
    data.charAt(1) + data.substring(2, data.length - 1).replace(/([A-Z]+)/g, ' $1')
  ).toLowerCase();
};

const convertUserDefinedClass = description => {
  let value = description;
  while (value.match(regex)) {
    value = value.replace(regex, content => {
      return '<span class="'.concat(splitContent(content)).concat('">');
    });
  }
  while (value.match(endTag)) {
    value = value.replace(endTag, () => '</span>');
  }
  return value;
};

const convertHtmlTag = (description, tag) => {
  let value = description;
  while (value.match(tag)) {
    value = value.replace(tag, text => '<'.concat(text.substring(1, text.length - 1)).concat(' >'));
  }
  return value;
};

const descriptionConverter = description => {
  let value = convertUserDefinedClass(description);
  value = convertHtmlTag(value, htmlregex);
  return ReactHtmlParser(value);
};

export default descriptionConverter;
