import React, { Component } from 'react';

import SunEditor from 'suneditor-react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';
import commonPropTypes from '../../commonPropTypes/commonPropTypes';
import { validateFileSize } from '../../CommonValidations/CommonValidations';
import COMMONCONSTANTS from '../../../constants/CommonConstants';
import '../../../../css/editor.css';

class EditorContainer extends Component {
  /**
   * constructor is used to initialise state
   */
  constructor(props) {
    super(props);
    const { content, uploadedImages } = this.props;
    this.state = {
      content,
      uploadedImages,
      urlFileMap: {},
      errorMessage: '',
      fileSize: 1,
      fileCount: 0,
    };
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * convertB64ToFile is used to convert given base64 value as file
   * @param image
   */
  convertB64ToFile = image => {
    const contentType = image.split(';')[0].split(':')[1];
    const byteString = atob(image.split(',')[1]);
    const arrayBuffer = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(arrayBuffer);
    for (let i = 0; i < byteString.length; i += 1) {
      ia[i] = byteString.charCodeAt(i);
    }
    const newBlob = new Blob([arrayBuffer], {
      type: contentType,
    });
    const fileName =
      new Date().getTime() +
      (contentType ? `.${contentType.slice(contentType.indexOf('/') + 1)}` : '');
    const file = new File([newBlob], fileName, { type: contentType });
    return file;
  };

  /**
   * createFile is used to create file from given image url
   * @param imgUrl
   */
  createFile = imgUrl => {
    if (imgUrl.startsWith('data:image/')) {
      return this.convertB64ToFile(imgUrl);
    }
    let file = null;
    const getFile = async () => {
      file = await this.fetchFileFromURL(imgUrl);
    };
    getFile();
    return file;
  };

  /**
   * updateContent is used to uupdate editor content to state
   * @param value
   */
  updateContent = value => {
    const { onEditorContentChanged } = this.props;
    const { urlFileMap } = this.state;
    let { uploadedImages } = this.state;
    uploadedImages = [];
    value.split('<img src="').forEach(splittedValue => {
      if (splittedValue.startsWith('data:image/') || splittedValue.startsWith('blob')) {
        const imgUrl = splittedValue.slice(0, splittedValue.indexOf('"'));
        if (urlFileMap[imgUrl]) {
          uploadedImages.push(urlFileMap[imgUrl]);
        } else {
          urlFileMap[imgUrl] = this.createFile(imgUrl);
          uploadedImages.push(urlFileMap[imgUrl]);
        }
      }
    });
    if (onEditorContentChanged) {
      onEditorContentChanged(value, uploadedImages);
    }
    this.setState({
      fileCount: uploadedImages.length,
      content: value,
      errorMessage: '',
      uploadedImages,
      urlFileMap,
    });
  };

  /**
   * fetchFileFromURL is used to fetch file from fileUrl
   * @param fileUrl
   * @return File
   */
  fetchFileFromURL = async fileUrl => {
    const response = await fetch(fileUrl);
    const fileBlob = new Blob([response], { type: 'image/*' });
    return new File([fileBlob], new Date().getTime());
  };

  /**
   * onImageUploadBefore is used to validate and save file in state before image upload
   * @param files
   * @param info
   * @param uploadHandler
   */
  onImageUploadBefore = (files, info, uploadHandler) => {
    const { maxNoOfImages, maxFileSizeInMB, imageAccept } = this.props;
    const { fileCount } = this.state;
    if (fileCount >= maxNoOfImages) {
      this.setState({ fileSize: maxNoOfImages, errorMessage: 'max_limit_of_files' });
      uploadHandler('');
      return;
    }
    const file = files[0];
    const fileExtension = file ? file.name.slice(file.name.lastIndexOf('.')).toLowerCase() : '';
    if (files.length === 0 || (file.name.includes('.') && !imageAccept.includes(fileExtension))) {
      this.setState({ errorMessage: 'image_file_extension_error' });
      uploadHandler('');
      return;
    }
    const validationResult = validateFileSize(file, maxFileSizeInMB, 0);
    if (validationResult.status) {
      this.setState({
        errorMessage: '',
        fileCount: fileCount + 1,
      });
      uploadHandler(files);
    } else {
      this.setState({
        errorMessage: validationResult.key,
        fileSize: validationResult.key === 'file_size_greater' ? maxFileSizeInMB : 0,
      });
      uploadHandler('');
    }
  };

  /**
   * addCountInfo is used to add dynamic contents to label
   * @param label
   * @param contents
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * onPaste is used to check pasted content has image and whether it exceeds image limit
   * @param event
   * @param cleanData
   * @returns boolean
   */
  onPaste = (event, cleanData) => {
    const { maxNoOfImages } = this.props;
    const { fileCount } = this.state;
    const isFileLimitExceeded =
      (cleanData.includes('<img src="')
        ? cleanData.split('<img src="').length - 1 + fileCount
        : fileCount) > maxNoOfImages;
    if (isFileLimitExceeded) {
      this.setState({ fileSize: maxNoOfImages, errorMessage: 'max_limit_of_files' });
      return false;
    }
    return true;
  };

  /**
   * render is used to render editor components
   * @returns component
   */
  render() {
    const { imageAccept } = this.props;
    const { content, errorMessage, fileSize } = this.state;
    return (
      <>
        <SunEditor
          onChange={this.updateContent}
          setContents={content}
          height={300}
          setOptions={{
            defaultStyle: COMMONCONSTANTS.defaultFontFamilySize,
            maxCharCount: 65535,
            charCounter: true,
            buttonList: [
              [
                'bold',
                'italic',
                'underline',
                'fontSize',
                'fontColor',
                'hiliteColor',
                'align',
                'link',
                'image',
              ],
            ],
            colorList: COMMONCONSTANTS.defaultColorList,
            imageUrlInput: false,
            imageFileInput: true,
            imageAccept: imageAccept.toString(),
            resizingBar: false,
          }}
          onPaste={this.onPaste}
          onImageUploadBefore={this.onImageUploadBefore}
        />
        <br />
        <span className="red bold">{this.addCountInfo(errorMessage, fileSize)}</span>
      </>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
EditorContainer.propTypes = {
  content: PropTypes.string,
  onEditorContentChanged: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  maxNoOfImages: PropTypes.number,
  maxFileSizeInMB: PropTypes.number,
  uploadedImages: PropTypes.arrayOf(PropTypes.object),
  imageAccept: PropTypes.shape([PropTypes.string]).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
EditorContainer.defaultProps = {
  isNotMultiLang: false,
  content: '',
  maxNoOfImages: 1,
  maxFileSizeInMB: 1,
  uploadedImages: [],
};
export default connect(mapStateToProps)(EditorContainer);
