export const defaultAuthorityValue = '7';

export const password = '********';

export const disableReportRadioAuthorityValue = '9';

export const authorityValues = {
  7: 'user_administrator',
  8: 'read_only',
  9: 'only_lock_wipe_and_clear_passcode',
};

/**
 * mapToFilterData is used to map filter data
 * @param map
 *
 */

function mapToFilterData(map) {
  const filterData = [];
  Object.keys(map).forEach((mapKey) => {
    const tempMap = { value: mapKey, option: map[mapKey] };
    filterData.push(tempMap);
  });
  return filterData;
}

export const filterData = {
  authority: mapToFilterData(authorityValues),
  report: [
    { filterKey: 1, filterValue: 'display' },
    { filterKey: 0, filterValue: 'hide' },
  ],
};

