export function setInitialValues(param) {
  const tempMap = {};
  tempMap.userId = param.userId;
  tempMap.loginId = param.loginId;
  tempMap.emailID = param.emailID;
  tempMap.ipAddress = param.ipAddress;
  tempMap.timeZoneCode = param.timeZoneCode;
  tempMap.languageCode = param.languageCode;
  tempMap.report = param.report;
  tempMap.authorityId = param.authorityId;
  tempMap.password = '********';
  return tempMap;
}

/**
 * bulkRegTableList is used to generate component map
 * @param dynamicData
 * @param renderCsvResult
 * @return map
 */
export function bulkRegTableList(dynamicData, renderCsvResult) {
  return [
    {
      type: 'button',
      className: 'ml10',
      title: 'download_template',
      accept: '.csv',
      hasLang: false,
      required: false,
      key: 'csvFile',
      name: 'csvFile',
      id: 'select-csv-button',
      label: 'csv_file',
      ref: dynamicData.fileUploadRef,
      component: 'fileuploadwithspaninfoandbutton',
      errorMessage: dynamicData.fileError,
      spanStyle: {
        display: `${dynamicData.uploadCsvError ? 'initial' : 'block'}`,
        marginTop: '4px',
      },
      renderSpan: () => renderCsvResult(dynamicData),
    },
  ];
}

