/**
 * NotificationCustomTableFrame is used to generate custom components for notification screens
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import checkLanguage from '../../../../language/checkLanguage';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBComboBox from '../../../../common/component/combobox/SBComboBox';
import SBCheckbox from '../../../../common/component/checkbox/SBCheckbox';
import SBDatePicker from '../../../../common/component/sbdatepicker/SBDatePicker';
import SBTimePicker from '../../../../common/component/sbtimepicker/SBTimePicker';
import LABELCONSTANTS from '../../../../constants/LabelConstants';
import commonPropTypes from '../../../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../../../constants/CommonConstants';

class NotificationCustomTableFrame extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * buildDatePickerComponent is used to build datepicker component
   * @param componentObj
   * @return SBDatePicker
   */
  buildDatePickerComponent = componentObj => {
    const { onTableFrameEvent } = this.props;
    return (
      <SBDatePicker
        {...componentObj}
        onSelect={(dateText, instance) => onTableFrameEvent(componentObj.key, dateText, instance)}
        onChange={event => onTableFrameEvent(componentObj.key, event.target.value, event)}
      />
    );
  };

  /**
   * setTableDesc is used to build given table components
   * @param componentObj
   * @param dataSource
   * @return givenComponent (TextBox, ComboBox, DatePicker, FileUpload, span, dateofpublication, daterange)
   */
  setTableDesc = (componentObj, dataSource) => {
    const { onTableFrameEvent } = this.props;
    switch (componentObj.component) {
      case 'textbox':
        return this.buildTextFieldComponent(SBTextbox, componentObj, dataSource);
      case 'combobox':
        return this.buildTextFieldComponent(SBComboBox, componentObj, dataSource);
      case 'datepicker':
        return <div className="mb10">{this.buildDatePickerComponent(componentObj)}</div>;
      case 'fileupload':
        return (
          <>
            <div className="mb10">
              <label
                htmlFor={componentObj.id}
                className="btn-fileupload mr5"
                onChange={event => {
                  onTableFrameEvent(componentObj.key, event);
                }}
              >
                <input
                  {...componentObj.fileInput}
                  type="file"
                  size="40"
                  style={{ width: '100%' }}
                />
              </label>
            </div>
            {componentObj.displayInfoLabel && (
              <>
                {this.languageLabelCheck(componentObj.infoLabel)}
                <a
                  className="mr10 ml10"
                  href={componentObj.downloadHref}
                  style={CommonConstants.href_underline_style}
                  onClick={event => onTableFrameEvent(`${componentObj.key}Download`, event)}
                >
                  {this.languageLabelCheck('download')}
                </a>
                <a
                  href={componentObj.deleteHref}
                  style={CommonConstants.href_underline_style}
                  onClick={event => onTableFrameEvent(`${componentObj.key}Delete`, event)}
                >
                  {this.languageLabelCheck('delete_file')}
                </a>
              </>
            )}
          </>
        );
      case 'span':
        return (
          <span {...componentObj}>
            {componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]}
          </span>
        );
      case 'dateOfPublication':
        return (
          <div>
            <SBDatePicker
              {...componentObj}
              forwadedRef={componentObj.dateRef}
              id={`${componentObj.id}date`}
              className="w140px mr10"
              value={dataSource.dateOfPublication ? dataSource.dateOfPublication : ''}
              onSelect={(dateText, instance) =>
                onTableFrameEvent('dateOfPublication', dateText, instance)}
              onChange={event => onTableFrameEvent('dateOfPublication', event.target.value, event)}
            />
            <SBTimePicker
              {...componentObj}
              id={`${componentObj.id}time`}
              value={dataSource.dateOfPublicationTime ? dataSource.dateOfPublicationTime : '12:00'}
              onChange={(event, value) => onTableFrameEvent('dateOfPublicationTime', event, value)}
            />
            <SBCheckbox
              {...componentObj}
              id={`${componentObj.id}checkBox`}
              style={{ display: 'inline-table' }}
              defaultValue={dataSource.sendMail ? [''] : ['1']}
              onChange={(event, value) =>
                onTableFrameEvent('publishWithoutSendingEmail', event, value)}
            />
            <SBButton
              {...componentObj}
              id={`${componentObj.id}button`}
              key={componentObj.settingButtonKey}
              type="button"
              onClick={event => onTableFrameEvent(componentObj.settingButtonKey, event)}
            />
          </div>
        );
      case 'daterange':
        return (
          <div className="mb10">
            {this.getDateRangeComponent('startDate', componentObj)}
            {LABELCONSTANTS.TILDE}
            {this.getDateRangeComponent('endDate', componentObj)}
          </div>
        );
      default:
        return componentObj.text;
    }
  };

  /**
   * buildTextFieldComponent is used to build given text field componentt
   * @param TextFields
   * @param componentObj
   * @param dataSource
   * @return TextFields
   */
  buildTextFieldComponent = (TextFields, componentObj, dataSource) => {
    const { onTableFrameEvent } = this.props;
    return (
      <div className="mb10">
        <TextFields
          {...componentObj}
          value={
            componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
          }
          onChange={(event, content) => onTableFrameEvent(componentObj.key, event, content)}
        />
      </div>
    );
  };

  /**
   * generateTableStructue is used to generate outer table structure
   * @param tableObj
   * @param dataSource
   * @return completeTable
   */
  generateTableStructue = (tableObj, dataSource) => {
    return (
      <tr>
        {(tableObj.label || tableObj.required) && (
          <th rowSpan={tableObj.rowSpan}>
            {this.languageLabelCheck(tableObj.label)}
            {tableObj.required ? <span className="red bold">*</span> : ''}
          </th>
        )}
        <td style={tableObj.tdStyle}>
          {tableObj.componentList.map(componentObj => {
            return (
              <>
                {this.setTableDesc(componentObj, dataSource)}
                {componentObj.errorMessage && (
                  <span id={`${componentObj.key}_errormsg`} className="red bold">
                    {this.languageLabelCheck(componentObj.errorMessage)}
                  </span>
                )}
              </>
            );
          })}
        </td>
      </tr>
    );
  };

  /**
   * getDateRangeComponent is used to generate date range component
   * @param key
   * @param componentObj
   * @returns DateRangeComponent
   */
  getDateRangeComponent = (key, componentObj) => {
    const tempComponentObj = { ...componentObj };
    tempComponentObj.component = 'datepicker';
    tempComponentObj.id = componentObj.id + key;
    tempComponentObj.className = `w140px mr10${key === 'endDate' ? ' ml10' : ''}`;
    tempComponentObj.key = componentObj[`${key}Key`];
    tempComponentObj.value = componentObj[`default${key}`];
    tempComponentObj.format = componentObj[`${key}Format`];
    tempComponentObj.minDate = componentObj[`${key}MinValue`];
    tempComponentObj.maxDate = componentObj[`${key}MaxValue`];
    return this.buildDatePickerComponent(tempComponentObj);
  };

  /**
   * render is used to render the component
   */
  render() {
    const { tableList, dataSource } = this.props;
    return (
      <table className="table-tate mb10">
        <tbody>
          {tableList &&
            tableList.map(tableObj => {
              return this.generateTableStructue(tableObj, dataSource);
            })}
        </tbody>
      </table>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NotificationCustomTableFrame.propTypes = {
  onTableFrameEvent: PropTypes.func.isRequired,
  dataSource: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.array, PropTypes.bool]),
  ).isRequired,
  tableList: PropTypes.arrayOf(PropTypes.object).isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NotificationCustomTableFrame.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(NotificationCustomTableFrame);
