/**
 * headerInformation is used to load the header operations
 * @param key
 * @return headerInformationMap
 */

export default function headerInformation(key) {
  switch (key) {
    case 'notifysetting':
      return {
        title: 'notify_setting_admin',
        description: ['notify_setting_admin_desc'],
      };
    case 'changenotifysetting':
      return {
        title: 'change_notify_setting_admin',
        description: ['change_notify_setting_admin_desc'],
      };
    default:
      return {};
  }
}
