/**
 * IDChangePassword is used to update the password of the user
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { put } from '../../../actions/commonActions';
import {
  isNotUndefinedOrNotEmpty,
  validatePassword,
} from '../../../common/CommonValidations/CommonValidations';
import {
  tableChangePassword,
  setReference,
  errorMessageConstants,
} from '../Constants/IDCRUDconstants';
import checkLanguage from '../../../language/checkLanguage';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import headerInformation from '../Constants/IDHeaderConstants';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import IDCRUDFooter from './IDCRUDFooter';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';

class IDChangePassword extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    if (!isNotUndefinedOrNotEmpty(location.state)) {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('changepassword'),
      fieldDataList: [],
      reference: setReference(),
      initialState: true,
      dynamicData: {},
      errorMessage: '',
      modalIsOpen: false,
      pageLoading: false,
      dataSource: { newPassword: '', confirmPassword: '' },
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount makes page loading state false
   */

  componentDidMount() {
      const { setModified } = this.props;
      const { dynamicData, reference } = this.state;
      setModified(false);
      this.setState({fieldDataList: tableChangePassword(dynamicData, reference)});
  }

  /**
   * componentDidUpdate makes focus on first component when screen is loaded
   */

  componentDidUpdate() {
    const { initialState, reference } = this.state;
    if (initialState && reference.passwordRef.current) {
      reference.passwordRef.current.focus();
      this.setState({ initialState: false });
    }
  }

  /**
   * setFocusinerrorField is used to set focus in error field
   */

  setFocusinerrorField = () => {
    const { reference, dynamicData } = this.state;
    if (isNotUndefinedOrNotEmpty(dynamicData) && reference.passwordRef.current) {
      reference.passwordRef.current.focus();
    }
  };

  /**
   * setValidationErrorMessage is used to set error validation message
   * @param key
   * @param validationResultObject
   */

  setValidationErrorMessage(key, validationResultObject) {
    const { dynamicData } = this.state;
    dynamicData[key] = errorMessageConstants[key][validationResultObject.error];
    this.setState(dynamicData);
  }

  /**
   * validateInputs is used to validate inputs
   * @return status
   */

  validateInputs = () => {
    const { dataSource } = this.state;
    this.setState({ dynamicData: {}, errorMessage: '' });
    let passwordValiation = { status: true, error: '' };
    const newPasswordValidation = validatePassword(dataSource.newPassword);
    const confirmPasswordValidation = validatePassword(dataSource.confirmPassword);
    this.setValidationErrorMessage('newPasswordError', newPasswordValidation);
    this.setValidationErrorMessage('confirmError', confirmPasswordValidation);
    if (newPasswordValidation.status && confirmPasswordValidation.status) {
      if (dataSource.newPassword !== dataSource.confirmPassword) {
        passwordValiation = { status: false, error: 'mismatch' };
        this.setState({ errorMessage: 'user_password_mismatch' });
      }
    }
    return (
      newPasswordValidation.status && confirmPasswordValidation.status && passwordValiation.status
    );
  };

  /**
   * showDialog is used to show dialog
   * @param key
   */

  showDialog = () => {
    const { dynamicData, reference } = this.state;
    const inputStatus = this.validateInputs();
    this.setState({
      modalIsOpen: inputStatus,
      fieldDataList: tableChangePassword(dynamicData, reference),
    });
    if (!inputStatus) {
      this.setState({ dataSource: { newPassword: '', confirmPassword: '' } });
    }
    this.setFocusinerrorField();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   *
   */

  onTableFrameEvent = (key, event) => {
    const { dataSource, footer } = this.state;
    dataSource[key] = event.target.value;
    footer.okDisable = false;
    this.setState({ dataSource, footer });
  };

  /**
   * onKeyDown is used to perform key down operation
   * @param event
   */

  onKeyDown = event => {
    const { footer } = this.state;
    const { okDisable } = footer;
    if (event.keyCode === 13 && !okDisable) {
      event.preventDefault();
      this.onHandleClick('save');
    }
  };

  /**
   * onOkDialog is used to perfom save operation
   *
   */

  onOkDialog = () => {
    const { dataSource, footer, reference } = this.state;
    const { setModified, history, location } = this.props;
    const param = location.state;
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      const response = await put(
        `${APIENDPOINTS.IDCHANGEPASSWORD}/${param.userId}/${dataSource.newPassword}`,
      );
      const resultSet = await response.json();
      if (resultSet.status) {
        setModified(false);
        footer.primaryVisibile = false;
        this.setState({
          modalIsOpen: false,
          pageLoading: false,
          fieldDataList: [],
          headerData: headerInformation('changepasswordsuccess'),
        });
      } else if (resultSet.validationError) {
        const { validationError } = resultSet;
        if (validationError && validationError.noUserError) {
          footer.primaryVisibile = false;
          this.setState({
            modalIsOpen: false,
            footer,
            fieldDataList: [],
            pageLoading: false,
            errorMessage: validationError.noUserError,
          });
        }
      } else {
        setModified(false);
        history.push(ROUTEENDPOINTS.ERROR);
      }
      if (reference.backRef.current) reference.backRef.current.focus();
    };
    saveProcess();
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { location, history } = this.props;
    switch (key) {
      case 'save':
        this.showDialog();
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.IDUPDATE, { param: location.state });
        break;
      case 'back':
        history.push(ROUTEENDPOINTS.IDUPDATE, { param: location.state });
        break;
      default:
        break;
    }
  };

  /**
   * render is used to render the component
   */

  render() {
    const {
      headerData,
      errorMessage,
      pageLoading,
      fieldDataList,
      modalIsOpen,
      dataSource,
      reference,
      footer,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <div>
            <CustomTableFrame
              tableList={fieldDataList}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
          </div>
          <div>
            <IDCRUDFooter
              reference={reference}
              footer={footer}
              onHandleClick={key => this.onHandleClick(key)}
            />
          </div>
          <br />
          <span className="red bold">{this.languageLabelCheck(errorMessage)}</span>
          <SBDialog
            open={modalIsOpen}
            header="save_confirmation"
            type="confirmation"
            content="want_to_update_data"
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={() => {
              this.onOkDialog();
            }}
            width="400px"
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  const { userVo } = state.user;
  return {
    userVo,
    localeData: data,
  };
};

/**
 * mapDispatchToProps is used to dispatch value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */

IDChangePassword.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.number]),
      ),
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(IDChangePassword));
