/**
 * IDCRUDFooter is used to create footer component
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import SBButton from '../../../common/component/sbbutton/SBButton';

const IDCRUDFooter = props => {
  const { footer, onHandleClick, lock, sessionlock, update, reference } = props;
  return (
    <div className="clearfix">
      {update && (
        <div className="floatleft">
          <SBButton
            id="btn_lock"
            color="gray"
            type="button"
            title="remote_unlock"
            disabled={lock}
            style={{ display: footer.primaryVisibile ? null : 'none' }}
            onClick={() => onHandleClick('lock')}
          />
          <SBButton
            id="btn_session_lock"
            color="gray"
            noMarginRight
            type="button"
            title="session_unlock"
            canModify
            disabled={sessionlock}
            style={{ display: footer.primaryVisibile ? null : 'none' }}
            onClick={() => onHandleClick('sessionlock')}
          />
        </div>
      )}
      <div className="floatright">
        <SBButton
          id="btn_ok"
          color="green"
          type="button"
          title="ok"
          ref={reference.okRef}
          disabled={footer.okDisable}
          style={{ display: footer.primaryVisibile ? null : 'none' }}
          onClick={() => onHandleClick('save')}
        />
        <SBButton
          id="btn_cancel"
          noMarginRight
          type="button"
          title="cancel"
          ref={reference.cancelRef}
          canModify
          style={{ display: footer.primaryVisibile ? null : 'none' }}
          onClick={() => onHandleClick('cancel')}
        />
        <SBButton
          id="btn_back"
          noMarginRight
          type="button"
          title="back"
          ref={reference.backRef}
          style={{ display: footer.primaryVisibile ? 'none' : 'block' }}
          onClick={() => onHandleClick('back')}
        />
      </div>
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
IDCRUDFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  footer: PropTypes.objectOf(PropTypes.bool).isRequired,
};

export default withRouter(IDCRUDFooter);
