import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBTable from '../../../../common/component/sbtable/SBTable';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

const resultsPerPagekey = 'certificate_list';
const dataSource = [
  {
    settingsName: 'Device 1',
    certificateType: 'User1',
    certificateCoOperation: '9876543210',
    explanation: 'Test',
    applicationSchedule: '123',
    deploymentRequestDate: '22/02/2020 13:54',
    removalRequestDate: '22/02/2020 13:54',
  },
];

class Certificate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datasource: dataSource,
      shouldDisableButton: true,
    };
  }

  onAddButtonClick() {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.REGISTERCERTIFICATION);
  }

  onChangeButtonClick() {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.CHANGECERTIFICATESETTING);
  }

  onDeleteCertificateButtonClick() {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.DELETECERTIFICATION);
  }

  onDeleteButtonClick() {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.DELETECERTIFICATESETTING);
  }

  handleRadioChange = () => {
    this.setState({
      shouldDisableButton: false,
    });
  };

  getColumns = () => {
    return OPERATIONMANAGEMENTCONSTANTS.certificateListGridColumns;
  };

  render() {
    const { screenTitleData } = this.props;
    const { shouldDisableButton } = this.state;
    const columns = this.getColumns();
    const { datasource } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <div style={{ height: '91px' }}>
                <SBContentHeader
                  title={screenTitleData.title}
                  description={screenTitleData.description}
                />
                <div className="contents-main mb15 w100per">
                  <div className="mb15">
                    <SBButton
                      add
                      onClick={() => {
                        this.onAddButtonClick();
                      }}
                    />
                  </div>
                  <SBTable
                    columns={columns}
                    dataSource={datasource}
                    loading={false}
                    pagination
                    countInfo={`Deployment List Total：${datasource.length}`}
                    filterList={OPERATIONMANAGEMENTCONSTANTS.securityPolicyGridComboItemsValue}
                    handleRadioChange={data => this.handleRadioChange(data)}
                    defaultSelected={resultsPerPagekey}
                  />
                  <div className="clearfix mb10">
                    <div className="floatleft">
                      <SBButton
                        type="button"
                        name="deleteCertification"
                        color="gray"
                        title="Delete Certification"
                        disabled={shouldDisableButton}
                        onClick={() => {
                          this.onDeleteCertificateButtonClick();
                        }}
                      />
                    </div>
                    <div className="floatright">
                      <SBButton
                        type="submit"
                        name="change"
                        color="green"
                        title="Change"
                        disabled={shouldDisableButton}
                        onClick={() => {
                          this.onChangeButtonClick();
                        }}
                      />
                      <SBButton
                        type="button"
                        name="delete"
                        title="Delete"
                        disabled={shouldDisableButton}
                        noMarginRight
                        onClick={() => {
                          this.onDeleteButtonClick();
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Certificate);
