/** Base Files */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import fileDownload from 'js-file-download';
import PropTypes from 'prop-types';
import encoding from 'encoding';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceFooter from '../component/DeviceFooter';
import DeviceIndicator from '../component/DeviceIndicator';
import GroupManagementIndicator from '../../groupSetting/component/GroupManagementIndicator';

/** utility file */
import checkLanguage from '../../language/checkLanguage';
import { currentDate } from '../../common/utils/dateUtils';
import { post, postWithFormData } from '../../actions/commonActions';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import {
  setUserDataSource,
  errorMessageConstants,
  userRegistrationApiList,
  userRegistrationMessageContentList,
} from '../constants/deviceConstants';
import { getUserInformation, resetUserInformation } from '../../actions/DeviceAction';
import deviceHeader from '../constants/deviceHeaderConstants';
import { serviceMapper, initializeUserInfoRefs } from '../common/deviceCommonFunctions';
import { userTableList, userTableView } from '../constants/userTableConstants';

/**
 * userDetail is to perform crud operation for user information
 * @author Srinivasan Ravi
 */
class userDetail extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const reference = initializeUserInfoRefs();
    const { mode, userInfoInitialState, location: getData, userVo, history } = this.props;
    const { parentScreen, deviceId } = (getData && getData.state) || {
      parentScreen: '',
      deviceId: '',
    };
    const { groupList } = userVo;
    if ((mode === 0 && groupList.length) || ((mode === 1 || mode === 2) && !getData.state)) {
      history.push(serviceMapper('error'));
    }
    this.state = {
      reference,
      deviceId,
      parentScreen,
      message: '',
      dialogWidth: '400px',
      pageLoading: false,
      initialError: false,
      isView: mode === 2,
      openDeviceDialog: false,
      messageHeader: '',
      messageContent: '',
      openErrorDialog: false,
      openConfirmDialog: false,
      dialogKey: 'deviceIndicator',
      headerData: deviceHeader('userRegistration')[mode],
      dataSource: {
        ...setUserDataSource(userInfoInitialState),
      },
      dynamicData: {
        isUpdate: mode === 1,
        deviceIndicator: false,
        mandatoryFieldError: '',
        // phoneNumberError: '',
        employeeNameError: '',
        employeeNumberError: '',
        deptOneTextError: '',
        deptTwoTextError: '',
        deptThreeTextError: '',
        deptFourTextError: '',
        optionOneError: '',
        optionTwoError: '',
        optionThreeError: '',
        optionFourError: '',
        multiRegFileError: '',
        multiRegUploadCsvError: '',
        multiRegLimitReached: '',
        bulkFileError: '',
        bulkUploadCsvError: '',
        bulkLimitReached: '',
        csvFileName: '',
        deptOneDisabled: userInfoInitialState.deptOneMethod === 'textboxDept',
        deptTwoDisabled: userInfoInitialState.deptTwoMethod === 'textboxDept',
        deptThreeDisabled: userInfoInitialState.deptThreeMethod === 'textboxDept',
        deptFourDisabled: userInfoInitialState.deptFourMethod === 'textboxDept',
        departmentOneList: userInfoInitialState.departmentOneList,
        departmentTwoList: userInfoInitialState.departmentTwoList,
        departmentThreeList: userInfoInitialState.departmentThreeList,
        departmentFourList: userInfoInitialState.departmentFourList,
      },
      footer: {
        reference,
        disable: mode !== 2,
        screenKey: mode === 3 ? 'userBulkDelete' : 'userRegistration',
      },
    };
  }

  /**
   * componentDidMount is to fetch user information in edit,update and delete mode
   */
  componentDidMount() {
    const { isModified, mode } = this.props;
    const { reference } = this.state;
    if (!isModified && mode !== 3) {
      this.setState({ pageLoading: true }, () => this.getUserInformation());
    } else {
      this.setState(
        ({ dataSource, dynamicData, footer }) => ({
          dataSource: {
            ...setUserDataSource({ ...dataSource }),
          },
          dynamicData: {
            ...dynamicData,
            deviceIndicator: !isEmpty(dataSource.userDeviceMap) && (
              <DeviceIndicator deviceList={[dataSource.userDeviceMap]} />
            ),
          },
          footer: {
            ...footer,
            disable: !isModified,
          },
          pageLoading: false,
        }),
        () => {
          if (mode !== 3 && reference.employeeNameRef.current)
            reference.employeeNameRef.current.focus();
          if (mode === 3 && reference.bulkDeletefileUploadRef.current)
            reference.bulkDeletefileUploadRef.current.focus();
        },
      );
    }
  }

  /**
   * getUserInformation used to get user information from db.
   */
  getUserInformation = async () => {
    const { mode } = this.props;
    const { reference, dataSource } = this.state;
    const response = await post(userRegistrationApiList[6], {
      employeeIndex: dataSource.employeeIndex,
      mode,
    });
    const result = await response.json();
    const { status, data: userInfo, message } = result;
    if (status === 'OK') {
      const departmentList = this.getDepartmentList(
        userInfo,
        'departmentOneSelect',
        mode ? userInfo.departmentOne : '',
        true,
      );
      const { departmentTwoList, departmentThreeList, departmentFourList } = departmentList;
      this.setState(
        ({ dynamicData }) => ({
          dataSource: {
            ...setUserDataSource({
              ...dataSource,
              ...userInfo,
              userDeviceMap: userInfo.deviceEntity,
              departmentOneSelect: mode ? userInfo.departmentOne : '',
              departmentTwoSelect: mode ? userInfo.departmentTwo : '',
              departmentThreeSelect: mode ? userInfo.departmentThree : '',
              departmentFourSelect: mode ? userInfo.departmentFour : '',
              departmentEntity: userInfo.departmentEntity,
              departmentOneList: userInfo.departmentOneList,
              departmentTwoList,
              departmentThreeList,
              departmentFourList,
            }),
          },
          pageLoading: false,
          initialError: false,
          dynamicData: {
            ...dynamicData,
            deviceIndicator: !isEmpty(!userInfo.userDeviceMap && dataSource.userDeviceMap) && (
              <DeviceIndicator deviceList={[!userInfo.userDeviceMap && dataSource.userDeviceMap]} />
            ),
            departmentOneList: userInfo.departmentOneList,
            departmentTwoList,
            departmentThreeList,
            departmentFourList,
            csvFileName: userInfo.csvFileName,
          },
        }),
        () => {
          if ((mode === 1 || mode === 0) && reference.employeeNameRef.current)
            reference.employeeNameRef.current.focus();
          else if (mode === 2 && reference.confirmButtonRef.current)
            reference.confirmButtonRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        isView: false,
        pageLoading: false,
        openErrorDialog: true,
        initialError: true,
      });
    }
  };

  /**
   * getDepartmentList used to form department combo data's.
   * @param userDataList
   * @param changedCombo
   * @param changedValue
   */
  getDepartmentList = (dataSource, changedCombo, changedValue, isOpen) => {
    let filteredDeptTwoUserData = [];
    let filteredDeptThreeUserData = [];
    let filteredDeptFourUserData = [];
    const filteredDeptTwoData = [];
    const filteredDeptThreeData = [];
    const filteredDeptFourData = [];
    const {
      departmentTwo,
      departmentThree,
      departmentOneSelect,
      departmentTwoSelect,
      departmentEntity,
    } = dataSource;
    const { mode } = this.props;
    const updDepartmentTwo = isOpen && mode ? departmentTwo : '';
    const updDepartmentThree = isOpen && mode ? departmentThree : '';
    if (changedCombo === 'departmentOneSelect') {
      filteredDeptTwoUserData = departmentEntity.filter(
        (userData) => userData.departmentOne === changedValue,
      );
      filteredDeptThreeUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === changedValue && userData.departmentTwo === updDepartmentTwo,
      );
      filteredDeptFourUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === changedValue &&
          userData.departmentTwo === updDepartmentTwo &&
          userData.departmentThree === updDepartmentThree,
      );
    } else if (changedCombo === 'departmentTwoSelect') {
      filteredDeptTwoUserData = departmentEntity.filter(
        (userData) => userData.departmentOne === departmentOneSelect,
      );
      filteredDeptThreeUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === departmentOneSelect && userData.departmentTwo === changedValue,
      );
      filteredDeptFourUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === departmentOneSelect &&
          userData.departmentTwo === changedValue &&
          userData.departmentThree === '',
      );
    } else if (changedCombo === 'departmentThreeSelect') {
      filteredDeptTwoUserData = departmentEntity.filter(
        (userData) => userData.departmentOne === departmentOneSelect,
      );
      filteredDeptThreeUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === departmentOneSelect &&
          userData.departmentTwo === departmentTwoSelect,
      );
      filteredDeptFourUserData = departmentEntity.filter(
        (userData) =>
          userData.departmentOne === departmentOneSelect &&
          userData.departmentTwo === departmentTwoSelect &&
          userData.departmentThree === changedValue,
      );
    }
    filteredDeptTwoUserData.map((userData) => {
      if (filteredDeptTwoData.indexOf(userData.departmentTwo) < 0) {
        filteredDeptTwoData.push(userData.departmentTwo);
      }
      return filteredDeptTwoData;
    });
    filteredDeptThreeUserData.map((userData) => {
      if (filteredDeptThreeData.indexOf(userData.departmentThree) < 0) {
        filteredDeptThreeData.push(userData.departmentThree);
      }
      return filteredDeptThreeData;
    });
    filteredDeptFourUserData.map((userData) => {
      if (filteredDeptFourData.indexOf(userData.departmentFour) < 0) {
        filteredDeptFourData.push(userData.departmentFour);
      }
      return filteredDeptFourData;
    });
    return {
      departmentTwoList: filteredDeptTwoData,
      departmentThreeList: filteredDeptThreeData,
      departmentFourList: filteredDeptFourData,
    };
  };

  /**
   * resetSelectedDepartment used to reset the dept selected values based on department combo list.
   * @param departmentList
   * @param changedKey
   */
  resetSelectedDepartment = (departmentList, changedKey) => {
    let updatedDepartmentTwoSelect = '';
    let updatedDepartmentThreeSelect = '';
    let updatedDepartmentFourSelect = '';
    const { dataSource } = this.state;
    const { departmentTwoSelect, departmentThreeSelect, departmentFourSelect } = dataSource;
    const { departmentTwoList, departmentThreeList, departmentFourList } = departmentList;
    if (changedKey === 'departmentOneSelect') {
      updatedDepartmentTwoSelect =
        departmentTwoList.indexOf(departmentTwoSelect) < 0 ? '' : departmentTwoSelect;
      updatedDepartmentThreeSelect =
        departmentThreeList.indexOf(departmentThreeSelect) < 0 ? '' : departmentThreeSelect;
      updatedDepartmentFourSelect =
        departmentFourList.indexOf(departmentFourSelect) < 0 ? '' : departmentFourSelect;
    } else if (changedKey === 'departmentTwoSelect') {
      updatedDepartmentTwoSelect = departmentTwoSelect;
      updatedDepartmentThreeSelect =
        departmentThreeList.indexOf(departmentThreeSelect) < 0 ? '' : departmentThreeSelect;
      updatedDepartmentFourSelect =
        departmentFourList.indexOf(departmentFourSelect) < 0 ? '' : departmentFourSelect;
    } else if (changedKey === 'departmentThreeSelect') {
      updatedDepartmentTwoSelect = departmentTwoSelect;
      updatedDepartmentThreeSelect = departmentThreeSelect;
      updatedDepartmentFourSelect =
        departmentFourList.indexOf(departmentFourSelect) < 0 ? '' : departmentFourSelect;
    } else if (changedKey === 'departmentFourSelect') {
      updatedDepartmentTwoSelect = departmentTwoSelect;
      updatedDepartmentThreeSelect = departmentThreeSelect;
    }
    return {
      departmentTwoSelect: updatedDepartmentTwoSelect,
      departmentThreeSelect: updatedDepartmentThreeSelect,
      departmentFourSelect: updatedDepartmentFourSelect,
    };
  };

  /**
   * onTableFrameEvent is to perform user registration input field events
   * @param key
   * @param event
   * @param value
   */
  onTableFrameEvent = (key, event, value) => {
    const { type, target } = event;
    const { setModified } = this.props;
    const { dynamicData, dataSource } = this.state;
    let inputValue = '';
    let departmentList = {};
    let selectedDepartmentMap = {};
    const isDeptComboChange = key.includes('department');
    const isDeptComboChangeExceptFour = isDeptComboChange && !key.includes('departmentFourSelect');
    if (type === 'click') this.onHandleClick(key);
    else {
      if (isDeptComboChange) {
        inputValue = target.value;
        if (!key.includes('departmentFourSelect'))
          departmentList = this.getDepartmentList(dataSource, key, inputValue);
        selectedDepartmentMap = this.resetSelectedDepartment(
          key.includes('departmentFourSelect') ? dataSource : departmentList,
          key,
          inputValue,
        );
      } else {
        inputValue = value;
      }
      if (!(key === 'fileBulk' || key === 'userRegistrationMethod')) setModified(true);
      this.setState(
        ({ footer }) => ({
          dataSource: {
            ...dataSource,
            departmentTwoSelect: isDeptComboChange
              ? selectedDepartmentMap.departmentTwoSelect
              : dataSource.departmentTwoSelect,
            departmentThreeSelect: isDeptComboChange
              ? selectedDepartmentMap.departmentThreeSelect
              : dataSource.departmentThreeSelect,
            departmentFourSelect: isDeptComboChange
              ? selectedDepartmentMap.departmentFourSelect
              : dataSource.departmentFourSelect,
            departmentTwoList: isDeptComboChangeExceptFour
              ? departmentList.departmentTwoList
              : dataSource.departmentTwoList,
            departmentThreeList: isDeptComboChangeExceptFour
              ? departmentList.departmentThreeList
              : dataSource.departmentThreeList,
            departmentFourList: isDeptComboChangeExceptFour
              ? departmentList.departmentFourList
              : dataSource.departmentFourList,
            [key]: key.includes('file') ? target.files[0] : inputValue,
          },
          dynamicData: {
            ...dynamicData,
            deptOneDisabled:
              key !== 'deptOneMethod' ? dynamicData.deptOneDisabled : inputValue === 'textboxDept',
            deptTwoDisabled:
              key !== 'deptTwoMethod' ? dynamicData.deptTwoDisabled : inputValue === 'textboxDept',
            deptThreeDisabled:
              key !== 'deptThreeMethod'
                ? dynamicData.deptThreeDisabled
                : inputValue === 'textboxDept',
            deptFourDisabled:
              key !== 'deptFourMethod'
                ? dynamicData.deptFourDisabled
                : inputValue === 'textboxDept',
            departmentTwoList: isDeptComboChangeExceptFour
              ? departmentList.departmentTwoList
              : dynamicData.departmentTwoList,
            departmentThreeList: isDeptComboChangeExceptFour
              ? departmentList.departmentThreeList
              : dynamicData.departmentThreeList,
            departmentFourList: isDeptComboChangeExceptFour
              ? departmentList.departmentFourList
              : dynamicData.departmentFourList,
          },
          footer: {
            ...footer,
            disable: false,
          },
        }),
        () => {
          if (key.includes('Method')) this.setFocusToFields(key, value);
        },
      );
    }
  };

  /**
   * setFocusToFields used to set focus to input fields.
   * @param key
   * @param value
   */
  setFocusToFields = (key, value) => {
    const { reference } = this.state;
    switch (key) {
      case 'deptOneMethod':
        this.setFocus(value === 'comboDept' ? reference.deptOneComboRef : reference.deptOneTextRef);
        break;
      case 'deptTwoMethod':
        this.setFocus(value === 'comboDept' ? reference.deptTwoComboRef : reference.deptTwoTextRef);
        break;
      case 'deptThreeMethod':
        this.setFocus(
          value === 'comboDept' ? reference.deptThreeComboRef : reference.deptThreeTextRef,
        );
        break;
      case 'deptFourMethod':
        this.setFocus(
          value === 'comboDept' ? reference.deptFourComboRef : reference.deptFourTextRef,
        );
        break;
      case 'userRegistrationMethod':
        this.setFocus(value === 'singleUser' ? reference.employeeNameRef : reference.fileUploadRef);
        break;
      default:
        break;
    }
  };

  /**
   * setFocus used to set focus to respective input
   * @param fieldReference
   */
  setFocus = (fieldReference) => {
    if (fieldReference.current) fieldReference.current.focus();
  };

  /**
   * onHandleClick is to handle botton click events in user registration
   * @param screenKey
   */
  onHandleClick = (screenKey) =>
    ({
      registerUser: () => this.validateUserInformation(),
      cancel: () => this.navigatePreviousScreen(true),
      back: () => this.navigatePreviousScreen(),
      confirmOk: () => this.setState({ pageLoading: true }, () => this.registerUserInformation()),
      confirmCancel: () => this.backToDetail(),
      userSelectTarget: () => this.navigateDeviceSelectScreen(serviceMapper(screenKey)),
      viewError: () => this.bootIndicatorDialog(screenKey),
      deviceIndicator: () => this.bootIndicatorDialog(screenKey),
      bulkUserDelete: () => this.setState({ pageLoading: true }, () => this.validateBulkDelete()),
      csvBulkDelete: () => this.downloadBulkDeleteTemplateFile(),
      csvBulkRegister: () => this.downloadBulkRegisterTemplateFile(),
    }[screenKey]());

  /**
   * backToDetail used to navigate user detail screen
   */
  backToDetail = () => {
    const { mode } = this.props;
    this.setState({ openConfirmDialog: false, isView: mode === 2 });
  };

  /**
   * downloadBulkDeleteTemplateFile is to download csv template document
   */
  downloadBulkDeleteTemplateFile = () => {
    const { currentLanguage } = this.props;
    fileDownload(
      encoding.convert(`serial\r\n`, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
      `serial.csv`,
    );
  };

  /**
   * downloadBulkDeleteTemplateFile is to download multi user register csv template document
   */
  downloadBulkRegisterTemplateFile = () => {
    const { currentLanguage, localeData } = this.props;
    fileDownload(
      encoding.convert(
        localeData.user_multi_reg_template_header.concat('\r\n'),
        currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8',
      ),
      `userInfoReg.csv`,
    );
  };

  /**
   * validateUserInformation used to validate user information input
   */
  validateUserInformation = () => {
    const { mode } = this.props;
    const { dataSource, reference } = this.state;
    const {
      userRegistrationMethod,
      file,
      // phoneNumber,
      userDeviceMap,
      employeeName,
      employeeNumber,
      deptOneMethod,
      deptTwoMethod,
      deptThreeMethod,
      deptFourMethod,
      deptOneText,
      deptTwoText,
      deptThreeText,
      deptFourText,
      optionOne,
      optionTwo,
      optionThree,
      optionFour,
    } = dataSource;
    const isSingleUserRegistration = userRegistrationMethod === 'singleUser';
    let hasError = '';
    let multiRegFileError = '';
    let mandatoryFieldError = '';
    // let phoneNumberError = '';
    let employeeNameError = '';
    let employeeNumberError = '';
    let deptOneTextError = '';
    let deptTwoTextError = '';
    let deptThreeTextError = '';
    let deptFourTextError = '';
    let optionOneError = '';
    let optionTwoError = '';
    let optionThreeError = '';
    let optionFourError = '';
    // if (isSingleUserRegistration && mode === 0 && !phoneNumber.trim() && isEmpty(userDeviceMap)) {
    if (isSingleUserRegistration && mode === 0 && isEmpty(userDeviceMap)) {
      mandatoryFieldError = 'userInfoMandatoryError';
    } else if (userRegistrationMethod === 'addMultipleByCSV') {
      multiRegFileError = !file && 'emptyFile';
      multiRegFileError = file && file.size === 0 ? 'fileSizeError' : multiRegFileError;
      multiRegFileError =
        file && !file.name.endsWith('.csv') ? 'invalidFileType' : multiRegFileError;
      multiRegFileError = file && file.name.length > 80 ? 'invalidFileLength' : multiRegFileError;
    }
    if (isSingleUserRegistration) {
      /* if (phoneNumber.trim() && (phoneNumber.length !== 10 || phoneNumber.length !== 14)) {
        phoneNumberError = 'phoneNumberLengthError';
      } */
      if (employeeName.length > 35) employeeNameError = 'employeeNameLengthError';
      if (employeeName.includes(',')) employeeNameError = 'commaError';
      if (employeeNumber.length > 100) employeeNumberError = 'employeeNumberLengthError';
      if (employeeNumber.includes(',')) employeeNumberError = 'commaError';
      if (deptOneMethod === 'textboxDept' && deptOneText.length > 30)
        deptOneTextError = 'deptLengthError';
      if (deptTwoMethod === 'textboxDept' && deptTwoText.length > 30)
        deptTwoTextError = 'deptLengthError';
      if (deptThreeMethod === 'textboxDept' && deptThreeText.length > 30)
        deptThreeTextError = 'deptLengthError';
      if (deptFourMethod === 'textboxDept' && deptFourText.length > 30)
        deptFourTextError = 'deptLengthError';
      if (optionOne.length > 100) optionOneError = 'optionLengthError';
      if (optionOne.includes(',')) optionOneError = 'commaError';
      if (optionTwo.length > 100) optionTwoError = 'optionLengthError';
      if (optionTwo.includes(',')) optionTwoError = 'commaError';
      if (optionThree.length > 100) optionThreeError = 'optionLengthError';
      if (optionThree.includes(',')) optionThreeError = 'commaError';
      if (optionFour.length > 100) optionFourError = 'optionLengthError';
      if (optionFour.includes(',')) optionFourError = 'commaError';
    }
    // hasError = mandatoryFieldError || phoneNumberError || fileError;
    hasError =
      mandatoryFieldError ||
      multiRegFileError ||
      employeeNameError ||
      employeeNumberError ||
      deptOneTextError ||
      deptTwoTextError ||
      deptThreeTextError ||
      deptFourTextError ||
      optionOneError ||
      optionTwoError ||
      optionThreeError ||
      optionFourError;
    if (hasError) {
      this.setState(
        ({ dynamicData, footer }) => ({
          openConfirmDialog: false,
          dynamicData: {
            ...dynamicData,
            mandatoryFieldError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][mandatoryFieldError]
              : dynamicData.mandatoryFieldError,
            /* phoneNumberError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][phoneNumberError]
              : dynamicData.phoneNumberError, */
            employeeNameError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][employeeNameError]
              : dynamicData.employeeNameError,
            employeeNumberError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][employeeNumberError]
              : dynamicData.employeeNumberError,
            deptOneTextError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][deptOneTextError]
              : dynamicData.deptOneTextError,
            deptTwoTextError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][deptTwoTextError]
              : dynamicData.deptTwoTextError,
            deptThreeTextError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][deptThreeTextError]
              : dynamicData.deptThreeTextError,
            deptFourTextError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][deptFourTextError]
              : dynamicData.deptFourTextError,
            optionOneError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][optionOneError]
              : dynamicData.optionOneError,
            optionTwoError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][optionTwoError]
              : dynamicData.optionTwoError,
            optionThreeError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][optionThreeError]
              : dynamicData.optionThreeError,
            optionFourError: isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][optionFourError]
              : dynamicData.optionFourError,
            multiRegFileError: !isSingleUserRegistration
              ? errorMessageConstants[footer.screenKey][multiRegFileError]
              : dynamicData.multiRegFileError,
            multiRegUploadCsvError: '',
            multiRegLimitReached: '',
          },
          dataSource: { ...dataSource, file: '' },
        }),
        () => {
          if (!isSingleUserRegistration && multiRegFileError && reference.fileUploadRef.current)
            reference.fileUploadRef.current.focus();
          /* if (phoneNumberError && reference.phoneNumberRef.current)
            reference.phoneNumberRef.current.focus(); */
          if (isSingleUserRegistration && employeeNameError && reference.employeeNameRef.current)
            reference.employeeNameRef.current.focus();
          else if (
            isSingleUserRegistration &&
            employeeNumberError &&
            reference.employeeNumberRef.current
          )
            reference.employeeNumberRef.current.focus();
          else if (
            isSingleUserRegistration &&
            mandatoryFieldError &&
            reference.deviceBtnRef.current
          )
            reference.deviceBtnRef.current.focus();
          else if (isSingleUserRegistration && deptOneTextError && reference.deptOneTextRef.current)
            reference.deptOneTextRef.current.focus();
          else if (isSingleUserRegistration && deptTwoTextError && reference.deptTwoTextRef.current)
            reference.deptTwoTextRef.current.focus();
          else if (
            isSingleUserRegistration &&
            deptThreeTextError &&
            reference.deptThreeTextRef.current
          )
            reference.deptThreeTextRef.current.focus();
          else if (
            isSingleUserRegistration &&
            deptFourTextError &&
            reference.deptFourTextRef.current
          )
            reference.deptFourTextRef.current.focus();
          else if (isSingleUserRegistration && optionOneError && reference.optionOneRef.current)
            reference.optionOneRef.current.focus();
          else if (isSingleUserRegistration && optionTwoError && reference.optionTwoRef.current)
            reference.optionTwoRef.current.focus();
          else if (isSingleUserRegistration && optionThreeError && reference.optionThreeRef.current)
            reference.optionThreeRef.current.focus();
          else if (isSingleUserRegistration && optionFourError && reference.optionFourRef.current)
            reference.optionFourRef.current.focus();
        },
      );
    } else {
      this.setState(({ dynamicData }) => ({
        dataSource: {
          ...dataSource,
          departmentOne:
            dataSource.deptOneMethod === 'comboDept'
              ? dataSource.departmentOneSelect
              : dataSource.deptOneText,
          departmentTwo:
            dataSource.deptTwoMethod === 'comboDept'
              ? dataSource.departmentTwoSelect
              : dataSource.deptTwoText,
          departmentThree:
            dataSource.deptThreeMethod === 'comboDept'
              ? dataSource.departmentThreeSelect
              : dataSource.deptThreeText,
          departmentFour:
            dataSource.deptFourMethod === 'comboDept'
              ? dataSource.departmentFourSelect
              : dataSource.deptFourText,
        },
        dynamicData: {
          ...dynamicData,
          mandatoryFieldError: isSingleUserRegistration ? '' : dynamicData.mandatoryFieldError,
          // phoneNumberError: isSingleUserRegistration ? '' : dynamicData.phoneNumberError,
          multiRegFileError: !isSingleUserRegistration ? '' : dynamicData.multiRegFileError,
        },
        openConfirmDialog: true,
        messageHeader: mode === 2 ? 'delete_confirmation' : 'save_confirmation',
        messageContent: userRegistrationMessageContentList[mode],
      }));
    }
  };

  /**
   * validateBulkDelete used to validate bulk delete csv file
   */
  validateBulkDelete = async () => {
    const { history } = this.props;
    const { dataSource, reference, dynamicData } = this.state;
    const { fileBulk } = dataSource;
    let bulkFileError = !fileBulk && 'user_bulk_csv_empty_file';
    bulkFileError = fileBulk && fileBulk.size === 0 ? 'user_csv_size_error' : bulkFileError;
    bulkFileError =
      fileBulk && !fileBulk.name.endsWith('.csv') ? 'user_invalid_file_type' : bulkFileError;
    bulkFileError =
      fileBulk && fileBulk.name.length > 80 ? 'user_upload_file_name_length' : bulkFileError;
    if (bulkFileError) {
      this.setState(
        {
          dynamicData: {
            ...dynamicData,
            bulkFileError,
            bulkUploadCsvError: '',
            bulkLimitReached: '',
          },
          dataSource: { ...dataSource, fileBulk: '' },

          pageLoading: false,
        },
        () => {
          if (reference.bulkDeletefileUploadRef && reference.bulkDeletefileUploadRef.current)
            reference.bulkDeletefileUploadRef.current.focus();
        },
      );
    } else {
      const formData = new FormData();
      formData.append('file', fileBulk);
      const response = await postWithFormData(userRegistrationApiList[3], formData);
      const result = await response.json();
      const { status, message, data: deviceUserCsvMapper } = result;
      if (status === 'OK') {
        this.setState(
          {
            isView: false,
            pageLoading: false,
            dataSource: { ...dataSource, fileBulk: '' },
            dynamicData: {
              ...dynamicData,
              bulkUploadCsvError: '',
              bulkFileError: '',
              bulkLimitReached: '',
            },
          },
          () => {
            history.push(serviceMapper('bulkUserDeleteList'), {
              deviceUserCsvMapper: { ...deviceUserCsvMapper },
              parentScreen: serviceMapper('userList'),
            });
          },
        );
      } else if (message === 'error_log') {
        this.setState(
          {
            dynamicData: {
              ...dynamicData,
              bulkUploadCsvError: deviceUserCsvMapper.message,
              bulkFileError: '',
              bulkLimitReached: '',
            },
            pageLoading: false,
            dataSource: { ...dataSource, fileBulk: '' },
          },
          () => {
            if (reference.bulkDeletefileUploadRef && reference.bulkDeletefileUploadRef.current)
              reference.bulkDeletefileUploadRef.current.focus();
          },
        );
      } else if (message === 'limit_reached') {
        this.setState(
          {
            dynamicData: {
              ...dynamicData,
              bulkLimitReached: deviceUserCsvMapper.message,
              bulkUploadCsvError: '',
              bulkFileError: '',
            },
            pageLoading: false,
            dataSource: { ...dataSource, fileBulk: '' },
          },
          () => {
            if (reference.bulkDeletefileUploadRef && reference.bulkDeletefileUploadRef.current)
              reference.bulkDeletefileUploadRef.current.focus();
          },
        );
      } else {
        this.setState({
          message,
          isView: false,
          pageLoading: false,
          openErrorDialog: true,
          dataSource: { ...dataSource, fileBulk: '' },
          dynamicData: {
            ...dynamicData,
            bulkUploadCsvError: '',
            bulkFileError: '',
            bulkLimitReached: '',
          },
        });
      }
    }
  };

  /**
   * registerUserInformation used to perform save operation
   */
  registerUserInformation = async () => {
    const { mode, setModified } = this.props;
    const { dataSource, reference } = this.state;
    const { userRegistrationMethod, file } = dataSource;
    // let phoneNumberError = '';
    let multiRegUploadCsvError = '';
    let hasError = '';
    const formData = new FormData();
    formData.append('file', file);
    const response =
      userRegistrationMethod === 'singleUser'
        ? await post(userRegistrationApiList[mode], {
            ...dataSource,
            mode,
          })
        : await postWithFormData(userRegistrationApiList[5], formData);
    const result = await response.json();
    const { status, message, data: validateResult } = result;
    if (status === 'OK') {
      /* if (userRegistrationMethod === 'singleUser') {
        phoneNumberError = validateResult.includes('phoneNumberDuplicateError')
          ? 'phoneNumberDuplicateError'
          : phoneNumberError;
      } else { */
      if (userRegistrationMethod !== 'singleUser')
        multiRegUploadCsvError = validateResult || multiRegUploadCsvError;
      // hasError = phoneNumberError || uploadCsvError;
      hasError = multiRegUploadCsvError;
      this.setState(
        ({ dynamicData, footer }) => ({
          openConfirmDialog: false,
          pageLoading: false,
          isView: !hasError,
          headerData: deviceHeader(!hasError ? 'userConfirmation' : 'userRegistration')[mode],
          dynamicData: {
            ...dynamicData,
            multiRegUploadCsvError,
            multiRegFileError: '',
            multiRegLimitReached: '',
            // phoneNumberError: errorMessageConstants[footer.screenKey][phoneNumberError],
          },
          dataSource: {
            ...dataSource,
            file: '',
          },
          footer: {
            ...footer,
            screenKey:
              //! hasError && userRegistrationMethod !== 'singleUser'
              !hasError ? 'userConfirmation' : footer.screenKey,
          },
        }),
        () => {
          /* if (phoneNumberError && reference.phoneNumberRef.current)
            reference.phoneNumberRef.current.focus(); */
          if (reference.backButtonRef && reference.backButtonRef.current)
            reference.backButtonRef.current.focus();
        },
      );
      setModified(!!hasError);
    } else if (message === 'error_log') {
      this.setState(
        ({ dynamicData }) => ({
          isView: false,
          dynamicData: {
            ...dynamicData,
            multiRegUploadCsvError: validateResult,
            multiRegFileError: '',
            multiRegLimitReached: '',
          },
          openConfirmDialog: false,
          pageLoading: false,
          dataSource: { ...dataSource, file: '' },
        }),
        () => {
          if (reference.fileUploadRef && reference.fileUploadRef.current)
            reference.fileUploadRef.current.focus();
        },
      );
    } else if (message === 'limit_reached') {
      this.setState(
        ({ dynamicData }) => ({
          isView: false,
          dynamicData: {
            ...dynamicData,
            multiRegLimitReached: validateResult,
            multiRegUploadCsvError: '',
            multiRegFileError: '',
          },
          pageLoading: false,
          openConfirmDialog: false,
          dataSource: { ...dataSource, file: '' },
        }),
        () => {
          if (reference.fileUploadRef && reference.fileUploadRef.current)
            reference.fileUploadRef.current.focus();
        },
      );
    } else {
      this.setState(({ dynamicData }) => ({
        message,
        isView: false,
        pageLoading: false,
        openConfirmDialog: false,
        initialError: true,
        openErrorDialog: true,
        dataSource: { ...dataSource, file: '' },
        dynamicData: {
          ...dynamicData,
          multiRegUploadCsvError: '',
          multiRegFileError: '',
          multiRegLimitReached: '',
        },
      }));
    }
  };

  /**
   * bootIndicatorDialog is to display selected device in grid format &
   * show error message for csv validation
   * @param dialogKey
   */
  bootIndicatorDialog = (dialogKey) =>
    this.setState({
      openDeviceDialog: dialogKey !== 'deviceIndicator',
      dialogKey,
    });

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * downloadErrorText is to download csv error text document
   */
  downloadErrorText = (csvError) => {
    const { dynamicData } = this.state;
    fileDownload(dynamicData[csvError], `Errors_${currentDate()}.txt`);
  };

  /**
   * componentList is to frame error download link on CSV upload operation
   * @return errorLink
   */
  componentList = (csvError) => [
    <Link>
      <span>
        <a href onClick={() => this.downloadErrorText(csvError)}>
          {this.languageLabelCheck('download_csv_error')}
        </a>
      </span>
    </Link>,
    <SBButton
      type="button"
      title="view_error"
      name="view_error"
      id="view_error"
      key="viewError"
      onClick={() => this.onHandleClick('viewError')}
    />,
  ];

  /**
   * renderCsvResult used to render component for csv validation result and file name
   * @param dynamicData
   * @returns component
   */
  renderCsvResult = (dynamicData) => {
    const { mode } = this.props;
    const csvError = mode === 3 ? 'bulkUploadCsvError' : 'multiRegUploadCsvError';
    const errorComponent =
      dynamicData[csvError] && dynamicData[csvError].split('\n').length > 5
        ? this.componentList(csvError)[0]
        : this.componentList(csvError)[1];
    return dynamicData[csvError] && errorComponent;
  };

  /**
   * navigateDeviceSelectScreen used to navigate device select for select the device.
   * @param screenPath
   */
  navigateDeviceSelectScreen = (screenPath) => {
    const { history, mode, getUserInformationData: makeUserInformation, isModified } = this.props;
    const { dataSource, parentScreen } = this.state;
    makeUserInformation(dataSource);
    history.push(screenPath, { mode, parentScreen, dataSource, isModified });
  };

  /**
   * navigatePreviousScreen used to navigate user list screen
   */
  navigatePreviousScreen = (resetReduxData) => {
    const {
      history,
      resetUserInformationData,
      setModified,
      userStatusList,
      userSearch,
    } = this.props;
    const { parentScreen, deviceId, initialError } = this.state;
    resetUserInformationData();
    setModified(false);
    if (parentScreen.includes('detailDeviceList'))
      history.push(
        initialError ? serviceMapper('deviceList') : parentScreen,
        initialError ? {} : { deviceId },
      );
    else
      history.push(
        parentScreen,
        resetReduxData ? { userStatusList: [], userSearch: '' } : { userStatusList, userSearch },
      );
  };

  /**
   * getTableList used to get component JSON for detail screen in edit mode
   */
  getTableList = () => {
    const { mode } = this.props;
    const { dataSource, dynamicData, reference } = this.state;
    const { userRegistrationMethod } = dataSource;
    const fullUserTableList = userTableList(dynamicData, reference, this.renderCsvResult);
    switch (mode) {
      case 0:
        return userRegistrationMethod === 'singleUser'
          ? fullUserTableList.splice(0, 12)
          : [fullUserTableList[0], fullUserTableList[12], fullUserTableList[13]];
      case 3:
        return [fullUserTableList[14]];
      default:
        return fullUserTableList.splice(1, 2).concat(fullUserTableList.splice(2, 8));
    }
  };

  /**
   * getTableView used to get component JSON for detail screen in view mode
   *
   */
  getTableView = () => {
    const { mode } = this.props;
    const { dataSource, dynamicData } = this.state;
    const { userRegistrationMethod } = dataSource;
    const fullUserTableView = userTableView(dynamicData);
    switch (userRegistrationMethod) {
      case 'singleUser':
        return mode === 0
          ? fullUserTableView
          : fullUserTableView.splice(1, 2).concat(fullUserTableView.splice(2, 8));
      default:
        return [];
    }
  };

  /**
   * render performs user detail screen rendering
   * @return UserDetail
   */
  render() {
    const { mode } = this.props;
    const {
      isView,
      footer,
      message,
      dialogKey,
      dataSource,
      headerData,
      dynamicData,
      pageLoading,
      dialogWidth,
      messageHeader,
      messageContent,
      openErrorDialog,
      openConfirmDialog,
      openDeviceDialog,
    } = this.state;
    const { userRegistrationMethod, userDeviceMap } = dataSource;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div>
              <CustomTableFrame
                dataSource={dataSource}
                tableList={isView ? this.getTableView() : this.getTableList()}
                defaultValue={[]}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              />
            </div>
            <DeviceFooter onHandleClick={(key) => this.onHandleClick(key)} footer={footer} />
            <div
              className="red bold mt5"
              style={{
                display:
                  // dynamicData.mandatoryFieldError ||
                  dynamicData.bulkLimitReached || dynamicData.multiRegLimitReached
                    ? 'block'
                    : 'none',
              }}
            >
              {userRegistrationMethod === 'singleUser'
                ? this.languageLabelCheck(
                    // dynamicData.mandatoryFieldError ||
                    dynamicData.bulkLimitReached,
                  )
                : this.languageLabelCheck(dynamicData.multiRegLimitReached)}
            </div>
          </div>
        </div>
        <GroupManagementIndicator
          notMultiLang
          width={dialogWidth}
          dialogKey={dialogKey}
          dialogContent={
            dialogKey === 'deviceIndicator'
              ? [userDeviceMap]
              : dynamicData[mode === 3 ? 'bulkUploadCsvError' : 'multiRegUploadCsvError'].split(
                  '\n',
                )
          }
          openDeviceDialog={openDeviceDialog}
          closeDeviceDialog={() => this.setState({ openDeviceDialog: false })}
        />
        <SBDialog
          type="confirmation"
          width="400px"
          open={openConfirmDialog}
          header={messageHeader}
          content={messageContent}
          onOk={() => this.onHandleClick('confirmOk')}
          onCancel={() => this.onHandleClick('confirmCancel')}
        />
        <SBDialog
          type="error"
          width="400px"
          header="error"
          okButtonTitle="ok"
          content={message}
          open={openErrorDialog}
          onOk={() => this.navigatePreviousScreen()}
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from device/group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { userInfoInitialState, userStatusList, userSearch } = state.device;
  const { isModified } = state.screenModified;
  const { data: localeData, current: currentLanguage } = state.language;
  const { userVo } = state.user;
  return {
    userInfoInitialState,
    isModified,
    localeData,
    currentLanguage,
    userVo,
    userStatusList,
    userSearch,
  };
};

/**
 * mapDispatchToProps performs data setup in device/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    getUserInformationData: (data) => {
      dispatch(getUserInformation(data));
    },
    resetUserInformationData: () => {
      dispatch(resetUserInformation());
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
userDetail.propTypes = {
  isModified: PropTypes.bool,
  isNotMultiLang: PropTypes.bool,
  mode: PropTypes.number.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      groupId: PropTypes.string,
    }),
  }).isRequired,
  currentLanguage: PropTypes.string.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  getUserInformationData: PropTypes.func.isRequired,
  resetUserInformationData: PropTypes.func.isRequired,
  userInfoInitialState: PropTypes.shape({
    departmentOne: PropTypes.string.isRequired,
    departmentTwo: PropTypes.string.isRequired,
    departmentThree: PropTypes.string.isRequired,
    departmentFour: PropTypes.string.isRequired,
    deptOneMethod: PropTypes.string.isRequired,
    deptTwoMethod: PropTypes.string.isRequired,
    deptThreeMethod: PropTypes.string.isRequired,
    deptFourMethod: PropTypes.string.isRequired,
    departmentOneList: PropTypes.arrayOf(PropTypes.string).isRequired,
    departmentTwoList: PropTypes.arrayOf(PropTypes.string).isRequired,
    departmentThreeList: PropTypes.arrayOf(PropTypes.string).isRequired,
    departmentFourList: PropTypes.arrayOf(PropTypes.string).isRequired,
  }).isRequired,
  userVo: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.number]),
  ).isRequired,
  userSearch: PropTypes.string.isRequired,
  userStatusList: PropTypes.arrayOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
userDetail.defaultProps = {
  isModified: false,
  isNotMultiLang: false,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(userDetail));
