/**
 * NoticeLookList is used to list notification for users
 * @author: V.Vijayaragavan
 */
import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get } from '../../../actions/commonActions';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';
import SBComboBox from '../../../common/component/combobox/SBComboBox';
import { CommonConstants } from '../../../constants/CommonConstants';
import { INITIAL_INDEX, FOURTH_INDEX } from '../constants/InfoFromSoftBankConstants';
import { dateBasedOnLocale } from '../../../common/utils/dateUtils';
import checkLanguage from '../../../language/checkLanguage';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import SBPagination from '../../../common/component/pagination/SBPagination';

const resultsPerPageKey = 'notice_look_list';
class NoticeLookList extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      customerNoticeList: [],
      pageLoading: true,
      listSize: localStorage.getItem(resultsPerPageKey) ||25,
      totalRecordCount : 0,
      pageNo : 1
    };

    this.comboBoxRef = createRef();
    this.pagination = createRef();
    this.activePagination = null;

  }

  /**
   * componentDidMount performs api operation to load notice list
   */
  componentDidMount() {
    this.setState({
      pageLoading:true
    },() => {
    this.getCustomerNoticeList();
    });
  }

  

   getCustomerNoticeList = async (focus = true) => {
    const {pageNo,listSize} = this.state;
    const response = await get(`${APIENDPOINTS.NOTICELOOKLIST}?limit=${localStorage.getItem(resultsPerPageKey) ||25}
    &page=${pageNo}`);
    const responseData = await response.json();
    
    this.setState(
      {
        customerNoticeList : responseData.customerNoticeList,
        pageLoading: false,
        totalRecordCount: responseData.totalRecordCount,
        listSize,
        pageNo: responseData.page
      },
      () => {
        if (focus && this.comboBoxRef.current) {
          this.comboBoxRef.current.focus()
        }else{
          this.pagination.current.focus(pageNo - 1,this.activePagination);
        };
      },
    );
  };

  

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onNotificationLinkClick is used to navigate user to notification detail page
   */
  onNotificationLinkClick = (noticeId) => {
    const { history } = this.props;
    history.push({
      pathname: ROUTEENDPOINTS.NOTICEDETAIL,
      state: { noticeId },
    });
  };

  /**
   * onNotificationLinkKeyPress is used to navigate user to notification detail page
   */
  onNotificationLinkKeyPress = (noticeId, event) => {
    if (event.keyCode === 13) {
      this.onNotificationLinkClick(noticeId);
    }
  };

  /**
   * onComboChange is used to handle combo change
   * @param event
   */
  onComboChange =  (event) => {
    localStorage.setItem(resultsPerPageKey, event.target.value);
    this.setState({
      pageLoading: true,
      listSize: parseInt(event.target.value,10)
    },() => {
      this.getCustomerNoticeList(true);
    });
  };

  
  
  

  /**
   * renderCustomerNoticeList is used to build component for every notification
   * @return component
   */
  renderCustomerNoticeList = () => {
    const { currentLanguage } = this.props;
    const {customerNoticeList} = this.state;
    return customerNoticeList.map((notice) => {
      return (
        <dl className="clearfix" key={notice.noticeId}>
          <dt>
            {`${dateBasedOnLocale(
              notice.dateOfPublication,
              currentLanguage,
            )} ${this.languageLabelCheck('notice_customer_update')}`}
            {notice.notificationRead ? null : (
              <span className="new">{`[ ${this.languageLabelCheck('new')} ]`}</span>
            )}
          </dt>
          <dd>
            <a
              tabIndex="0"
              href={CommonConstants.CommonConstants}
              onKeyDown={(event) => this.onNotificationLinkKeyPress(notice.noticeId, event)}
              onClick={() => this.onNotificationLinkClick(notice.noticeId)}
              style={CommonConstants.href_underline_style}
            >
              {notice.notificationTitle}
            </a>
          </dd>
        </dl>
      );
    });
  };

  handlePaginationClick = (pageNo=1,pos) => {
    this.setState({
      pageNo,
      pageLoading:true
    },() => {
      this.activePagination = pos || 'top';
    this.getCustomerNoticeList(false);
    });
  }

  /**
   * render is used to render the component
   */
  render() {
    const { pageLoading,totalRecordCount,listSize,pageNo} = this.state;
    const noticeList = this.renderCustomerNoticeList();

    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={notificationHeaderInformation('noticeLookList').title}
          description={notificationHeaderInformation('noticeLookList').description}
        />
        <div className="contents-main w100per" ref={this.divElement}>
          <div className="mb10">
            <div className="clearfix">
              <div className="floatleft">
                <span>{`${this.languageLabelCheck('results_per_page')} `}</span>
                <SBComboBox
                  ref={this.comboBoxRef}
                  id="results_per_page"
                  name="results_per_page"
                  defaultValue={localStorage.getItem(resultsPerPageKey)}
                  filterKey="key"
                  filterValue="value"
                  options={CommonConstants.gridListComboOptions.slice(INITIAL_INDEX, FOURTH_INDEX)}
                  onChange={(event) => this.onComboChange(event)}
                />
              </div>
              <div>
                <SBPagination
                  bottomPagination
                  ref={this.pagination}
                  totalCount={totalRecordCount}
                  displayCount={listSize}
                  defaultPage={pageNo}
                  handlePaginationClick={(start,last,page,pos) => this.handlePaginationClick(page,pos)}
                >
                  <div className="border p15 mb10">
                    <div className="list-topics mb20">{noticeList}</div>
                  </div>
                </SBPagination>
              </div>
            </div>
          </div>
          
          {noticeList.length > 0 ? null : (
            <span>{`${this.languageLabelCheck('empty_notice_list')} `}</span>
          )}
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { current, data } = state.language;
  return {
    localeData: data,
    currentLanguage: current,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NoticeLookList.propTypes = {
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  currentLanguage: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NoticeLookList.defaultProps = {
  isNotMultiLang: false,
  currentLanguage: 'ja',
};

export default connect(mapStateToProps)(withRouter(NoticeLookList));
