/**
 * NotificationRegistration is used to register or update a notification from softbank
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import {
  getDayName,
  errorMessageConstants,
  setFocusInErrorField,
  initialState,
  getPublicationPeriodValue,
  createRefsForAllInputs,
} from '../constants/InfoFromSoftBankConstants';
import {
  validateConfigurationDateTime,
  validatePublicationPeriod,
  clearSettingLabel,
  validateFileNameInput,
  validateDateOfPublication,
  validateFile,
  validateNotificationTitle,
} from '../validations/NotificationValidations';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { post } from '../../../actions/commonActions';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import NotificationInputForm from '../component/NotificationRegistration/NotificationInputForm';
import { fetchLanguageList } from '../../../actions/FieldsAction';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import NotificationFooterComponent from '../component/NotificationFooterComponent/NotificationFooterComponent';
import { getRegisterFooterButton } from '../constants/NotificationRegTableLists';
import LabelConstants from '../../../constants/LabelConstants';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../../constants/CommonConstants';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';

const fileDownload = require('js-file-download');

class NotificationRegistration extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history, update } = this.props;
    this.state = { ...initialState('register') };
    if (location && location.state && location.state.isFromConfirm) {
      this.state = {
        ...location.state,
        headerInformation: notificationHeaderInformation(
          location.state.update ? 'update' : 'register',
        ),
      };
    } else if (update && location.state) {
      this.state = { ...initialState('update', location.state.dataSource) };
    } else if (update) {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  }

  /**
   * componentDidMount performs api operation to load language list
   */
  componentDidMount() {
    const { fetchLanguageList: getLanguageList, languageVo } = this.props;
    const { dynamicData } = this.state;
    let languageList = languageVo;
    dynamicData.refs = createRefsForAllInputs();
    const generateComponent = async () => {
      if (!languageVo.length) {
        const languageResponse = await post(APIENDPOINTS.LANGUAGECODELIST, { searchedValue: '' });
        languageList = await languageResponse.json();
        getLanguageList(languageList);
      }
      dynamicData.languageList = languageList;
      await this.setState({
        pageLoading: false,
        dynamicData,
      });
      this.setInitialFocus();
    };
    generateComponent();
  }

  /**
   * onSettingButtonClick is used to handle setting button click event
   * @param key
   */
  onSettingButtonClick = key => {
    const { dynamicData } = this.state;
    dynamicData.dateOfPublicationError = '';
    this.setState({ dynamicData });
    if (key === 'clearButton') {
      this.changeButtonTitleAndSetSettingLabel('configureButton', '');
    } else {
      this.validateDateAndConfigureSettingLabel();
    }
  };

  /**
   * onNotificationInputAction is used to handle notification input fields change actions
   * @param key
   * @param event
   * @param value
   */
  onNotificationInputAction = (key, event, value) => {
    const { setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    if (key.includes('Download')) {
      this.downloadFile();
    } else if (key.includes('Delete')) {
      this.removeAddedFile('attachmentFile', 'attachmentName');
    } else if (key === 'configureButton' || key === 'clearButton') {
      this.onSettingButtonClick(key);
    } else if (key === 'dateOfPublication' || key === 'publicationPeriod') {
      dataSource[key] = event;
      dynamicData[key] = dataSource[key];
    } else if (event.target.type === 'checkbox') {
      dataSource[key] = value;
    } else if (key.includes('File')) {
      this.getUploadedFile(key, event);
    } else {
      dataSource[key] = event.target.value;
    }
    if (clearSettingLabel(key)) {
      this.changeButtonTitleAndSetSettingLabel('configureButton', '');
    }
    dynamicData.disableOkButton = false;
    setModified(true);
    this.setState({
      dataSource,
      dynamicData,
    });
  };

  /**
   * onEditorContentChanged is used to handle editor content change event
   * @param htmlData
   * @param uploadedImages
   */
  onEditorContentChanged = (htmlData, uploadedImages) => {
    const { setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    dynamicData.disableOkButton = false;
    dataSource.content = htmlData;
    this.setState({ dynamicData, dataSource, uploadedImages });
    setModified(true);
  };

  /**
   * onOkButtonClick is used to handle ok button click event
   */
  onOkButtonClick = () => {
    const { dynamicData } = this.state;
    if (this.validateInputs()) {
      this.updateAndNavigateToConfirm();
    } else {
      setFocusInErrorField(dynamicData);
    }
  };

  /**
   * setInitialFocus is used to set focus in the first input field
   */
  setInitialFocus = () => {
    const { dynamicData } = this.state;
    if (dynamicData.refs.titleRef.current) dynamicData.refs.titleRef.current.focus();
  };

  /**
   * onCancelButtonClick is used to handle cancel button click event
   */
  onCancelButtonClick = () => {
    const { history } = this.props;
    const { update } = this.state;
    const { dynamicData } = this.state;
    if (update) {
      history.push(ROUTEENDPOINTS.NOTIFICATIONLIST);
    } else {
      const { languageList } = dynamicData;
      this.setState(
        {
          ...initialState('register', {}, languageList),
          pageLoading: false,
          showComponent: true,
        },
        () => {
          this.setInitialFocus();
        },
      );
    }
  };

  /**
   * setValidationErrorMessage is used to update validation error in state
   * @param key
   * @param validationResult
   * @param additionalKey
   */
  setValidationErrorMessage = (key, validationResult, additionalKey) => {
    const { dynamicData } = this.state;
    dynamicData[key] = validationResult ? '' : errorMessageConstants[additionalKey || key];
    this.setState({ dynamicData });
  };

  /**
   * getUploadedFile is used to get the uploaded file from input field
   * @param key
   * @param event
   */
  getUploadedFile = (key, event) => {
    const { fileDataSource } = this.state;
    [fileDataSource[key]] = event.target.files;
    this.setState({ fileDataSource });
  };

  /**
   * removeAddedFile is used to get the remove added file and filename from state
   * @param fileKey
   * @param fileNameKey
   */
  removeAddedFile = (fileKey, fileNameKey) => {
    const { dataSource, fileDataSource, dynamicData } = this.state;
    fileDataSource[fileKey] = null;
    dataSource[fileNameKey] = '';
    dynamicData[`show${fileKey}`] = false;
    this.setState({ fileDataSource, dataSource, dynamicData });
  };

  /**
   * updateAddedFile is used to save the added file and filename in state
   * @param fileKey
   * @param fileNameKey
   */
  updateAddedFile = (fileKey, fileNameKey) => {
    const { dynamicData, dataSource, fileDataSource } = this.state;
    if (fileDataSource[fileKey]) {
      dynamicData[`show${fileKey}`] = true;
      if (!dataSource[fileNameKey]) {
        const fileName = fileDataSource[fileKey].name;
        dataSource[fileNameKey] = fileName.includes('.')
          ? fileName.slice(0, fileName.lastIndexOf('.'))
          : fileName;
      }
    }
    this.setState({ fileDataSource, dataSource, dynamicData });
  };

  /**
   * downloadFile is used to handle download button click
   */
  downloadFile = () => {
    const { fileDataSource, dataSource } = this.state;
    const fileToDownload = fileDataSource.attachmentFile;
    const fileExtension = fileToDownload.name.slice(fileToDownload.name.lastIndexOf('.'));
    fileDownload(fileToDownload, dataSource.attachmentName + fileExtension);
  };

  /**
   * validateDateAndConfigureSettingLabel is used to validate dates and update setting label
   */
  validateDateAndConfigureSettingLabel = () => {
    const { userVo } = this.props;
    const { dataSource } = this.state;
    const validationResult = validateConfigurationDateTime(
      dataSource.dateOfPublication,
      dataSource.dateOfPublicationTime,
      userVo.timeZoneOffSet,
    );
    if (validationResult.status) {
      const settingLabel = this.getSettingValue(
        dataSource.dateOfPublication,
        dataSource.dateOfPublicationTime,
        !dataSource.publishWithoutSendingEmail.includes('1'),
      );
      this.changeButtonTitleAndSetSettingLabel('clearButton', settingLabel);
    } else {
      this.setValidationErrorMessage('dateOfPublicationError', false, validationResult.key);
    }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * changeButtonTitleAndSetSettingLabel is used to change the button title and setting value
   */
  changeButtonTitleAndSetSettingLabel = (settingButtonKey, settingLabel) => {
    const { dataSource, dynamicData } = this.state;
    dataSource.settingValue = settingLabel;
    dynamicData.settingButtonKey = settingButtonKey;
    dynamicData.buttonTitle = settingButtonKey === 'configureButton' ? 'configure' : 'clear';
    this.setState({
      dynamicData,
      dataSource,
    });
  };

  /**
   * validateInputs is used to validate all the inputs in Notification register screen
   */
  validateInputs = () => {
    const { userVo } = this.props;
    const { dataSource, fileDataSource, dynamicData } = this.state;
    const titleValidationResult = validateNotificationTitle(dataSource.notificationTitle);

    const dateOfPublicationResult = validateDateOfPublication(
      dataSource.dateOfPublication,
      dataSource.dateOfPublicationTime,
      dataSource.settingValue,
      userVo.timeZoneOffSet,
    );
    const publicationPeriodResult = validatePublicationPeriod(
      dataSource.dateOfPublication,
      dataSource.publicationPeriod,
      dataSource.settingValue,
      userVo.timeZoneOffSet,
    );
    const attachmentFileResult = validateFile('attachmentFileError', fileDataSource.attachmentFile);
    if (attachmentFileResult.status) {
      this.updateAddedFile('attachmentFile', 'attachmentName');
    } else {
      this.removeAddedFile('attachmentFile', 'attachmentName');
    }
    if (dynamicData.refs.attachmentFileRef.current) {
      dynamicData.refs.attachmentFileRef.current.value = '';
    }
    const attachmentNameValidationResult = validateFileNameInput(
      dataSource.attachmentName,
      'attachment',
    );
    this.setValidationErrorMessage(
      'attachmentFileError',
      !attachmentFileResult.attachmentFileError,
      attachmentFileResult.attachmentFileError,
    );
    this.setValidationErrorMessage('fileError', !attachmentFileResult.fileError, 'fileSizeGreater');
    this.setValidationErrorMessage(
      'titleError',
      titleValidationResult.status,
      titleValidationResult.key,
    );
    this.setValidationErrorMessage(
      'dateOfPublicationError',
      dateOfPublicationResult.status,
      dateOfPublicationResult.key,
    );
    this.setValidationErrorMessage(
      'publicationPeriodError',
      publicationPeriodResult.status,
      publicationPeriodResult.key,
    );
    this.setValidationErrorMessage(
      'attachmentNameError',
      attachmentNameValidationResult.status,
      attachmentNameValidationResult.key,
    );
    return (
      titleValidationResult.status &&
      dateOfPublicationResult.status &&
      publicationPeriodResult.status &&
      attachmentNameValidationResult.status &&
      attachmentFileResult.status
    );
  };

  /**
   * updateAndNavigateToConfirm is used to update state and navigate to confirm page
   */
  updateAndNavigateToConfirm = () => {
    const { userVo, history } = this.props;
    const { dataSource, dynamicData, update, fileDataSource, uploadedImages } = this.state;
    dynamicData.unpublished = this.languageLabelCheck('unpublished');
    dataSource.sendMail = dataSource.publishWithoutSendingEmail
      ? !dataSource.publishWithoutSendingEmail.includes('1')
      : true;
    dataSource.publicationPeriodValue = getPublicationPeriodValue(
      dataSource.dateOfPublication,
      dataSource.publicationPeriod,
      dataSource.settingValue,
      userVo.timeZoneOffSet,
      this.languageLabelCheck('no_expiration_date'),
      this.languageLabelCheck('day_s'),
    );
    dataSource.languageName = dynamicData.languageList.filter(ele => {
      if (ele.code === dataSource.languageCode) return true;
      return false;
    })[0].languageName;
    dynamicData.refs = {};
    history.push({
      pathname: ROUTEENDPOINTS.NOTIFICATIONCONFIRM,
      state: {
        isFromRegister: true,
        update,
        dataSource,
        fileDataSource,
        uploadedImages,
        dynamicData,
      },
    });
  };

  /**
   * onFooterComponentAction is used to handled footercomponent action
   * @param key
   */
  onFooterComponentAction = key => {
    const { history } = this.props;
    switch (key) {
      case 'ok':
        this.onOkButtonClick();
        break;
      case 'cancel':
        this.setState({ showComponent: false }, () => {
          this.onCancelButtonClick();
        });
        break;
      default:
        history.push(ROUTEENDPOINTS.NOTIFICATIONLIST);
    }
  };

  /**
   * getSettingValue is used to build setting value
   * @param date
   * @param time
   * @param sendMail
   * @return settingValue
   */
  getSettingValue = (date, time, sendMail) => {
    if (time === 'empty') {
      return `${this.languageLabelCheck('apply_immediately')} (${this.languageLabelCheck(
        sendMail ? 'send_by_email' : 'dont_send_by_email',
      )})`;
    }
    return `${date} (${this.languageLabelCheck(getDayName(new Date(date).getDay()))}) ${time} ${
      LabelConstants.TILDE
    } (${this.languageLabelCheck(sendMail ? 'send_by_email' : 'dont_send_by_email')})`;
  };

  /**
   * render is used to render the component
   */
  render() {
    const {
      headerInformation,
      pageLoading,
      uploadedImages,
      dataSource,
      dynamicData,
      fileDataSource,
      showComponent,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={headerInformation.title}
          description={headerInformation.description}
        />
        {showComponent && (
          <div className="contents-main">
            <NotificationInputForm
              dataSource={dataSource}
              fileDataSource={fileDataSource}
              uploadedImages={uploadedImages}
              dynamicData={dynamicData}
              onNotificationInputAction={(key, event, value) =>
                this.onNotificationInputAction(key, event, value)
              }
              onEditorContentChanged={(htmlData, uploadedEditorImages) =>
                this.onEditorContentChanged(htmlData, uploadedEditorImages)
              }
            />
            <br />
            <span className="red bold">{this.languageLabelCheck(dynamicData.fileError)}</span>
            <br />
            <span className="red bold">{this.languageLabelCheck(dynamicData.contentError)}</span>
            <NotificationFooterComponent
              buttonsList={getRegisterFooterButton(dynamicData.disableOkButton, true)}
              onHandleClick={key => this.onFooterComponentAction(key)}
            />
          </div>
        )}
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = dispatch => {
  return {
    fetchLanguageList: data => {
      dispatch(fetchLanguageList(data));
    },
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  const { userVo } = state.user;
  const { languageVo } = state.commonFields;
  return {
    userVo,
    languageVo,
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NotificationRegistration.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      dataSource: PropTypes.objectOf(PropTypes.string),
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  update: PropTypes.bool,
  fetchLanguageList: commonPropTypes().fetchLanguageList.isRequired,
  languageVo: PropTypes.arrayOf(PropTypes.object),
  setModified: commonPropTypes().setModified.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NotificationRegistration.defaultProps = {
  update: false,
  isNotMultiLang: false,
  languageVo: [],
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(NotificationRegistration));
