/**
 * csvBatchtableList is to frame table components in CSV screen
 * @param reference
 * @param dynamicData
 * @param renderCsvResult
 * @return tableList
 */
export const csvBatchtableList = (dynamicData, reference, renderCsvResult) => [
  {
    defaultValue: 'changeGroup',
    filterKey: 'key',
    component: 'radio',
    key: 'operationType',
    filterValue: 'value',
    name: 'operationType',
    label: 'operation_type',
    options: deviceComboData.csvBatchOperation,
  },
  {
    type: 'button',
    className: 'ml10',
    title: 'download',
    accept: '.csv',
    hasLang: false,
    required: false,
    key: 'csvFile',
    buttonKey: 'csvFileTemplate',
    name: 'csvBulkSetting',
    id: 'select-bulk-csv-button',
    label: 'csv_file',
    ref: reference && reference.fileUploadRef,
    component: 'fileuploadwithspaninfoandbutton',
    errorMessage: dynamicData.invalidFileError,
    spanStyle: { display: `${dynamicData.uploadCsvError ? 'initial' : 'block'}`, marginTop: '4px' },
    renderSpan: () => renderCsvResult(dynamicData),
  },
];

