import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../../../common/component/tableLayout/SBTableLayout';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';

class RegisterChangeDeleteCheck extends Component {
  basicSettingsTableData = [
    {
      key: ['Settings Name', <span className="red bold">*</span>],
      value: 'Test',
    },
  ];

  certificateDeploymentTableData = [
    {
      key: 'CA Certificate',
      value: 'GeoTrust_Primary_CA.pem',
    },
    {
      key: 'User Certificate',
      value: '',
    },
    {
      key: 'Selected',
      value: 'Device List',
    },
  ];

  scheduleSettingTableData = [
    {
      key: 'Application Schedule',
      value: 'Immediate',
    },
  ];

  otherSettingTableData = [
    {
      key: 'Memo',
      value: 'Test',
    },
  ];

  onOkButtonClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.ANDROIDCERTIFICATELIST);
  };

  onCancelButtonClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.ANDROIDCERTIFICATELIST);
  };

  render() {
    const { screenTitleData } = this.props;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <div style={{ height: '91px' }}>
                <SBContentHeader
                  title={screenTitleData.title}
                  description={screenTitleData.description}
                />
                <div className="contents-main">
                  <h2 className="title-corner">Basic Setting</h2>
                  <SBTableLayout
                    tableData={this.basicSettingsTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <h2 className="title-corner">Certification Deployment Settings</h2>
                  <SBTableLayout
                    tableData={this.certificateDeploymentTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <h2 className="title-corner">Schedule Setting</h2>
                  <SBTableLayout
                    tableData={this.scheduleSettingTableData}
                    filterKey="key"
                    filterValue="value"
                    rowspan="rowspan"
                  />
                  <h2 className="title-corner">Other Setting</h2>
                  <SBTableLayout
                    tableData={this.otherSettingTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <div className="floatright">
                    <SBButton
                      type="submit"
                      name="okbutton"
                      color="green"
                      title="OK"
                      onClick={() => {
                        this.onOkButtonClick();
                      }}
                    />
                    <SBButton
                      type="button"
                      name="cancel"
                      title="Cancel"
                      noMarginRight
                      onClick={() => {
                        this.onCancelButtonClick();
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(RegisterChangeDeleteCheck);
