/**
 * NotificationRegTableLists is used to hold component object values used to generate components in Notification screens
 * @author: V.Vijayaragavan
 */

/**
 * notificationDetailsTableList is used to build object values for notification registration basic details components
 * @param dynamicData
 * @return componentObjectValue
 */
export function notificationDetailsTableList(dynamicData) {
  return [
    {
      label: 'title',
      required: true,
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          id: 'title-text-input',
          name: 'titleTextInput',
          maxLength: '70',
          errorMessage: dynamicData.titleError,
          key: 'notificationTitle',
          style: { width: '100%' },
          ref: dynamicData.refs.titleRef,
        },
      ],
    },
    {
      label: 'date_publication_email',
      rowSpan: 2,
      componentList: [
        {
          component: 'dateOfPublication',
          id: 'dateOfPublication',
          name: 'dateOfPublication',
          minDate: new Date(),
          maxDate: new Date('2050/12/31'),
          filterKey: 'value',
          filterValue: 'option',
          options: [
            {
              value: '1',
              option: 'publish_without_sending_email',
              styleProperies: { marginRight: 0, lineHeight: '12px' },
            },
          ],
          settingButtonKey: dynamicData.settingButtonKey,
          title: dynamicData.buttonTitle,
          errorMessage: dynamicData.dateOfPublicationError,
          dateRef: dynamicData.refs.dateOfPublicationRef,
        },
      ],
    },
    {
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          key: 'settingValue',
          readOnly: true,
          style: { width: 350, border: 'none' },
        },
      ],
    },
    {
      label: 'publication_period',
      componentList: [
        {
          component: 'datepicker',
          id: 'publicationPeriod',
          name: 'publicationPeriod',
          key: 'publicationPeriod',
          minDate: new Date(),
          maxDate: new Date('2050/12/31'),
          errorMessage: dynamicData.publicationPeriodError,
          value: dynamicData.publicationPeriod ? dynamicData.publicationPeriod : '',
          forwadedRef: dynamicData.refs.publicationPeriodRef,
        },
      ],
    },
    {
      label: 'language',
      componentList: [
        {
          component: 'combobox',
          key: 'languageCode',
          id: 'language-code',
          name: 'languageCode',
          filterKey: 'code',
          filterValue: 'languageName',
          options: dynamicData.languageList,
          errorMessage: '',
          isNotMultiLang: true,
          defaultValue: 'ja',
        },
      ],
    },
  ];
}

/**
 * notificationAttachmentTableList is used to build object values for notification registration basic attachment components
 * @param dynamicData
 * @return componentObjectValue
 */
export function notificationAttachmentTableList(dynamicData) {
  return [
    {
      label: 'attachment_name',
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          id: 'attachement-name-text-input',
          name: 'attachmentNameTextInput',
          maxLength: '30',
          errorMessage: dynamicData.attachmentNameError,
          key: 'attachmentName',
          style: { width: '100%' },
          ref: dynamicData.refs.attachmentNameRef,
        },
      ],
    },
    {
      label: 'attachment',
      componentList: [
        {
          component: 'fileupload',
          id: 'attachement-file-upload',
          name: 'attachementFileUpload',
          key: 'attachmentFile',
          errorMessage: dynamicData.attachmentFileError,
          fileInput: { id: 'attachement-file-upload', ref: dynamicData.refs.attachmentFileRef },
          infoLabel: 'attachment_file_info_label',
          displayInfoLabel: dynamicData.showattachmentFile,
          hrefStyle: { cursor: 'pointer', textDecoration: 'underline' },
        },
      ],
    },
  ];
}

/**
 * confirmPageTableList is used to build object values for notification registration confirm components
 * @param dynamicData
 * @return componentObjectValue
 */
export function confirmPageTableList(dynamicData) {
  return [
    {
      label: 'title',
      componentList: [
        {
          component: 'span',
          key: 'notificationTitle',
          ref: dynamicData.refs.titleSpanRef,
          style: { outline: 'none' },
          tabIndex: '-1',
        },
      ],
    },
    {
      label: 'date_publication_email',
      componentList: [
        {
          component: 'span',
          key: 'settingValue',
          render: value => (value.settingValue ? value.settingValue : dynamicData.unpublished),
        },
      ],
    },
    {
      label: 'publication_period',
      componentList: [
        {
          component: 'span',
          key: 'publicationPeriodValue',
        },
      ],
    },
    {
      label: 'language',
      componentList: [
        {
          component: 'span',
          key: 'languageName',
        },
      ],
    },
  ];
}

/**
 * searchTableList is used to build object values for notification search filter
 * @param searchParam
 * @param reference
 * @param languageList
 * @param errorMessage
 * @return componentObjectValue
 */
export function searchTableList(searchParam, reference, languageList, errorMessage) {
  return [
    {
      label: 'title',
      componentList: [
        {
          component: 'textbox',
          type: 'text',
          id: 'title-text-input',
          name: 'notificationTitleKeyword',
          maxLength: '30',
          errorMessage: '',
          key: 'notificationTitleKeyword',
          ref: reference,
          defaultValue: searchParam.notificationTitleKeyword,
        },
      ],
    },
    {
      label: 'notice_release_date',
      componentList: [
        {
          component: 'daterange',
          id: 'daterange',
          startDateKey: 'startDate',
          endDateKey: 'endDate',
          defaultstartDate: searchParam.startDate ? searchParam.startDate : '',
          defaultendDate: searchParam.endDate ? searchParam.endDate : '',
          startDateMinValue: new Date('2011/01/01'),
          startDateMaxValue: new Date('2050/12/31'),
          endDateMinValue: new Date('2011/01/01'),
          endDateMaxValue: new Date('2050/12/31'),
          errorMessage,
        },
      ],
    },
    {
      label: 'language',
      componentList: [
        {
          component: 'combobox',
          key: 'languageCode',
          id: 'language',
          name: 'language',
          defaultValue: 'empty',
          filterKey: 'code',
          filterValue: 'languageName',
          isNotMultiLang: true,
          options: languageList,
          errorMessage: '',
        },
      ],
    },
  ];
}

/**
 * getListFootersButton is used to build object values for notification list footerbuttons
 * @param disableButton
 * @return componentObjectValue
 */
export function getListFootersButton(disableButton) {
  return [
    {
      id: 'btn_change',
      type: 'button',
      title: 'change',
      color: 'green',
      disabled: disableButton,
      key: 'change',
    },
    {
      id: 'btn_delete',
      noMarginRight: true,
      type: 'button',
      title: 'delete',
      disabled: disableButton,
      key: 'delete',
    },
  ];
}

/**
 * getRegisterFooterButton is used to build object values for notification register footerbuttons
 * @param disableButton
 * @return componentObjectValue
 */
export function getRegisterFooterButton(disableOkButton, canModify) {
  return [
    {
      id: 'btn_ok',
      color: 'green',
      type: 'button',
      title: 'ok',
      disabled: disableOkButton,
      key: 'ok',
    },
    {
      id: 'btn_cancel',
      type: 'button',
      title: 'cancel',
      canModify,
      noMarginRight: true,
      key: 'cancel',
    },
  ];
}
