import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import commonPropTypes from '../../commonPropTypes/commonPropTypes';

class SBPolicyTab extends Component {
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const { id, tabTitle, onChange, checked, className } = this.props;
    return (
      <a href onClick={() => onChange()} className={className} id={id}>
        <label className="checkbox mr5 ">
          <input
            id={id}
            className="checkbox-input"
            type="checkbox"
            style={{ opacity: 0 }}
            disabled
            checked={checked}
          />
          <span className="checkbox-parts" />
        </label>
        {this.languageLabelCheck(tabTitle)}
      </a>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
SBPolicyTab.propTypes = {
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  id: PropTypes.string.isRequired,
  tabTitle: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  checked: PropTypes.bool,
  className: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
SBPolicyTab.defaultProps = {
  isNotMultiLang: false,
  checked: false,
  className: '',
};

export default connect(mapStateToProps, null)(SBPolicyTab);
