import React, { createRef } from 'react';
import { Link } from 'react-router-dom';
import checkLanguage from '../../../../language/checkLanguage';
import addDynamicContent from '../../../../language/addDynamicContent';

const fileDownload = require('js-file-download');

function handleClick(data, fileName) {
  fileDownload(data.join('\r\n'), fileName);
}
export const filterData = {
  onOrOff: [{ filterKey: 1, filterValue: 'on_value' }, { filterKey: 0, filterValue: 'off_value' }],
  application: [
    { filterKey: 'WhiteList', filterValue: 'WhiteList' },
    { filterKey: 'BlackList', filterValue: 'BlackList' },
  ],
};
export function setRegisterReference() {
  return {
    securityPolicyNameRef: createRef(),
    androidOSVersionCheckRef: createRef(),
    androidOSVersionRef: createRef(),
    textOSVersionRef: createRef(),
    selectAndroidOsRef: createRef(),
    textApplicationRef: createRef(),
    selectApplicationRef: createRef(),
    uploadRef: createRef(),
  };
}
export const refAndKey = {
  androidOSVersion: 'textOSVersionRef',
  application: 'textApplicationRef',
};
export const osVersionFilter = [];
export const applicationFilter = [];

export function detailView(localeData) {
  return {
    securityPolicy: [
      {
        label: 'security_policy_name',
        component: 'span',
        key: 'policyName',
      },
    ],
    createdPolicyAccount: [
      {
        label: 'created_policy_account',
        component: 'span',
        key: 'createdBy',
      },
    ],
    androidOS: [
      {
        label: 'android_os_version_check',
        component: 'span',
        key: 'osVersionCheck',
        render: data => (data.osVersionCheck === true ? 'ON' : 'OFF'),
      },
      {
        label: 'android_os_version',
        component: 'span',
        key: 'androidOSVersionList',
        render: data => {
          return data.androidOSVersionList && data.androidOSVersionList.length ? (
            <div>
              {data.androidOSVersionList.map(value => {
                return (
                  <span>
                    {value}
                    <br />
                  </span>
                );
              })}
            </div>
          ) : (
            '-'
          );
        },
      },
    ],
    application: [
      {
        label: 'application_check',
        component: 'span',
        key: 'applicationCheck',
        render: data => (data.applicationCheck === true ? 'ON' : 'OFF'),
      },
      {
        label: 'android_check_method',
        component: 'span',
        key: 'checkMethod',
        render: data => checkLanguage(localeData, data.checkMethod),
      },
      {
        label: 'application_security_policy',
        component: 'span',
        render: data =>
          data.applicationList &&
          data.applicationList.length !== 0 &&
          data.applicationList.filter(
            value => data.applicationCheck === value.applicationCheckMethod,
          ).length !== 0 ? (
            <Link
              onClick={() => {
                const status = data.checkMethod === 'WhiteList';
                const result = data.applicationList
                  .filter(value => value.applicationCheckMethod === status)
                  .map(value => value.applicationName);
                handleClick(
                  result,
                  `${data.policyName}_${checkLanguage(
                    localeData,
                    'application_security_policy',
                  )}.txt`,
                );
              }}
            >
              <u>
                {`${data.policyName}_ ${checkLanguage(
                  localeData,
                  'application_security_policy',
                )}.txt`}
              </u>
            </Link>
          ) : (
            '-'
          ),
      },
    ],
    applicationNo: [
      {
        label: 'application_check',
        component: 'span',
        key: 'applicationCheck',
        render: data => (data.applicationCheck === true ? 'ON' : 'OFF'),
      },
      {
        label: 'android_check_method',
        component: 'span',
        key: 'checkMethod',
        hasLang: true,
      },
      {
        label: 'number_of_reg',
        component: 'span',
        render: data => {
          const checkMethodStatus = data.checkMethod === 'WhiteList';
          const appList = data.applicationList.filter(
            value => value.applicationCheckMethod === checkMethodStatus,
          );
          if (appList && appList.length !== 0) {
            return localeData && localeData.no_of_cases
              ? addDynamicContent(localeData.no_of_cases, appList.length)
              : appList.length;
          }
          return '-';
        },
      },
    ],
    imei: [
      {
        label: 'imei_check',
        component: 'span',
        key: 'imeiCheck',
        render: data => (data.imeiCheck === true ? 'ON' : 'OFF'),
      },
    ],
    imeiNo: [
      {
        label: 'imei_check',
        component: 'span',
        key: 'imeiCheck',
        render: data => (data.imeiCheck === true ? 'ON' : 'OFF'),
      },
      {
        label: 'number_of_reg',
        component: 'span',
        key: 'imeiCheck',
        render: data => {
          if (data.imeiList && data.imeiList.length !== 0) {
            return localeData && localeData.no_of_cases
              ? addDynamicContent(localeData.no_of_cases, data.imeiList.length)
              : data.imeiList.length;
          }
          return '-';
        },
      },
    ],
    antiVirus: [
      {
        label: 'anti_virus',
        component: 'span',
        key: 'antivirusCheck',
        render: data => (data.antivirusCheck ? 'ON' : 'OFF'),
      },
    ],
    assignTarget: [
      {
        label: 'group_allocation',
        component: 'span',
        render: value => (value && value.assignTarget ? value.assignTarget : ''),
      },
    ],
  };
}
export function secuirtyPolicyName(dynamicData, reference) {
  return [
    {
      label: 'security_policy_name',
      required: true,
      component: 'textbox',
      type: 'text',
      size: 20,
      maxLength: 100,
      id: 'security_policy_name-text-input',
      name: 'securityPolicyName',
      key: 'securityPolicyName',
      canModify: true,
      disabled: dynamicData.disabled,
      errorMessage: dynamicData.securityPolicyNameError,
      style: { width: 548 },
      ref: reference.securityPolicyNameRef,
    },
  ];
}
