      maxLength: 6,
      textStyle: { marginRight: 10 },
      textid: 'android_os_version',
      textname: 'android_os_version',
      textkey: 'androidOSVersion',
      canModify: true,
      ref: reference.textOSVersionRef,
      btnid: 'add_android_os_version',
      btnname: 'add_android_os_version',
      btnkey: 'addOsVersion',
      btntype: 'button',
      title: 'add',
      errorMessage: dynamicData.androidOSVersionValueError,
