      size: 20,
      maxLength: 100,
      textStyle: { marginRight: 10 },
      textid: 'android_application',
      textname: 'android_application',
      textkey: 'androidApplication',
      canModify: true,
      ref: reference.textApplicationRef,
      btnid: 'add_android_application',
      btnname: 'add_android_application',
      btnkey: 'addApplication',
