import { LANGUAGE_CHANGE } from '../actions/ActionTypes';
/**
 * languageReducer is to store changed user language, timezone and content
 * @param state
 * @param action
 * @return state
 *
 * @author Gobi R
 */

const initialState = {
  default: 'ja',
  current: 'ja',
  data: {},
  timezoneCode: '0',
};

const languageReducer = (state = initialState, action) => {
  if (action.type === LANGUAGE_CHANGE && action.data) {
    return {
      ...initialState,
      current: action.current,
      data: { ...action.data },
      timezoneCode: action.timezone,
    };
  }
  return state;
};

export default languageReducer;
