/**
 * ContractInfoChangePassword is used to build components of Change Application User Password.
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import { CommonConstants } from '../../../constants/CommonConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import {
  administratorChangePasswordTableList,
  initializeRefs,
  getFooterButton,
  changePasswordErrorMessageConstants,
} from '../constants/ContractInfoConstants';
import ContractInformationFooter from './ContractInformationFooter';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import { post } from '../../../actions/commonActions';
import checkLanguage from '../../../language/checkLanguage';
import { Role } from '../../../constants/Role';
import APIEndPoints from '../../../constants/APIEndPoints';

class ContractInfoChangePassword extends Component {
  constructor() {
    super();
    this.state = {
      pageLoading: false,
      dataSource: {},
      dynamicData: { disableOkButton: true, ref: initializeRefs() },
      isFinished: false,
    };
  }

  componentDidMount() {
    const { history, userVo, setModified } = this.props;
    // const { dynamicData } = this.state;
    this.setState({ pageLoading: false });
    if (Role.systemAdminAppUser.includes(userVo.authorityId)) {
      setModified(false);
      this.setFocus();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onOkButtonClick is used to perfom save operation
   *
   */
  onOkButtonClick = () => {
    const { setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    this.setState({ pageLoading: true });
    setModified(false);
    const saveProcess = async () => {
      const response = await post(APIEndPoints.CHANGEUSERPASSWORD, dataSource);
      const resultSet = await response.json();
      if (resultSet.status) {
        this.setState({ isFinished: true, pageLoading: false });
        if (dynamicData.ref.backButtonRef.current) {
          dynamicData.ref.backButtonRef.current.focus();
        }
      } else {
        this.setValidationErrorMessage(resultSet.validationError);
        this.setState({
          pageLoading: false,
          dataSource: {},
        });
        this.setFocus();
        this.clearPasswordsFields();
      }
    };
    saveProcess();
  };

  /**
   * clearPasswordsFields is used to clear password fields
   */
  clearPasswordsFields = () => {
    const { dynamicData } = this.state;
    if (dynamicData.ref.currentPasswordRef.current)
      dynamicData.ref.currentPasswordRef.current.value = '';
    if (dynamicData.ref.newPasswordRef.current) dynamicData.ref.newPasswordRef.current.value = '';
    if (dynamicData.ref.confirmPasswordRef.current)
      dynamicData.ref.confirmPasswordRef.current.value = '';
    this.setFocus();
  };

  /**
   * setFocus is used to set focus in the first input field
   */
  setFocus = () => {
    const { dynamicData } = this.state;
    if (dynamicData.ref.currentPasswordRef.current) {
      dynamicData.ref.currentPasswordRef.current.focus();
    }
  };

  /**
   * getValidationErrorMessage is used to set error validation messages
   * @param key
   * @param errorType
   */
  getValidationErrorMessage = (key, errorType) => {
    return changePasswordErrorMessageConstants[key][errorType];
  };

  /**
   * setValidationErrorMessage is set error message based on validation
   * @param error
   */
  setValidationErrorMessage = (error) => {
    const { dynamicData } = this.state;
    dynamicData.disableOkButton = true;
    dynamicData.currentPasswordErrorMessage = this.getValidationErrorMessage(
      'currentPassword',
      error.currentPassword,
    );
    dynamicData.newPasswordErrorMessage = this.getValidationErrorMessage(
      'newPassword',
      error.newPassword,
    );
    dynamicData.confirmPasswordErrorMessage = this.getValidationErrorMessage(
      'confirmPassword',
      error.confirmPassword,
    );
    dynamicData.errorMessage = error.commonError;
    this.setState({
      dynamicData,
    });
  };

  /**
   * onFooterComponentAction is used to handle footercomponent action
   * @param key
   */
  onFooterComponentAction = (key) => {
    const { history } = this.props;
    if (key === 'ok') {
      this.onOkButtonClick();
    } else {
      history.push(RouteEndPoints.CHANGECONTRACTINFO);
    }
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */
  onKeyDown = (event) => {
    const { dynamicData } = this.state;
    if (event.keyCode === 13 && !dynamicData.disableOkButton) {
      event.preventDefault();
      this.onOkButtonClick();
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */
  onTableFrameEvent = (key, event, value) => {
    const { setModified } = this.props;
    const { dataSource, dynamicData } = this.state;
    setModified(true);
    dynamicData.disableOkButton = false;
    dataSource[key] = value;
    this.setState({ dataSource, dynamicData });
  };

  /**
   * render is used to render components of the screen
   */
  render() {
    const { screenTitleData } = this.props;
    const { pageLoading, dataSource, dynamicData, isFinished } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={screenTitleData.title}
          description={
            isFinished ? ['change_contract_pass_finish_desc'] : screenTitleData.description
          }
        />
        <div className="contents-main">
          {!isFinished && (
            <CustomTableFrame
              tableList={administratorChangePasswordTableList(dynamicData)}
              dataSource={dataSource}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
          )}
          <ContractInformationFooter
            buttonsList={
              isFinished
                ? getFooterButton(dynamicData).slice(2, 3)
                : getFooterButton(dynamicData).slice(0, 2)
            }
            onHandleClick={(key) => this.onFooterComponentAction(key)}
          />
          <br />
          <span className="red bold">{this.languageLabelCheck(dynamicData.errorMessage)}</span>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ContractInfoChangePassword.propTypes = {
  setModified: commonPropTypes().setModified.isRequired,
  screenTitleData: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.shape([PropTypes.string]),
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  history: commonPropTypes().history.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  userVo: commonPropTypes().userVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ContractInfoChangePassword.defaultProps = {
  isNotMultiLang: false,
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  return {
    userVo,
    localeData: state.language.data,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ContractInfoChangePassword));
