/** Base File */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

/** SBComponents */
import SBButton from '../../common/component/sbbutton/SBButton';
import RoleResolver from '../../common/component/guard/RoleResolver';

/** Utility File */
import buttonList from '../constants/groupSettingButtonConstants';

/**
 * GroupManagementFooter is to render the group management CRUD operation buttons
 * @author Aswin Palanisamy
 */
class GroupManagementFooter extends Component {
  /**
   * onHandleClick performs button click operation in footer room
   * @param key
   */
  onHandleClick = key => {
    const { onHandleClick } = this.props;
    if (onHandleClick) onHandleClick(key);
  };

  /**
   * renderFooterButton performs footer room button rendering
   * @param props
   * @return footerButtons
   */
  renderFooterButton = props => {
    const { key, authorityRoleType } = props;
    return (
      <RoleResolver authorityRoleType={authorityRoleType}>
        <SBButton {...props} name={key} type="button" onClick={() => this.onHandleClick(key)} />
      </RoleResolver>
    );
  };

  /**
   * render performs footer room component rendering
   * @return footerComponent
   */
  render() {
    const { footer } = this.props;
    const { screenKey } = footer;
    const leftPanel = buttonList(footer)[`${screenKey}LeftPanel`];
    const rightPanel = buttonList(footer)[`${screenKey}RightPanel`];

    return (
      <div className="clearfix">
        {leftPanel && (
          <div className="floatleft">
            {leftPanel.map(record => this.renderFooterButton(record))}
          </div>
        )}
        {rightPanel && (
          <div className="floatright">
            {rightPanel.map(record => this.renderFooterButton(record))}
          </div>
        )}
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
GroupManagementFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  footer: PropTypes.shape({
    screenKey: PropTypes.string.isRequired,
  }).isRequired,
};

export default GroupManagementFooter;
