import {
  isEmailValid,
  isNotUndefinedOrNotEmpty,
  isTextAreaLengthValid,
} from '../../../common/CommonValidations/CommonValidations';
import { validateMinMaxDate } from '../../../common/utils/dateUtils';

/**
 * validatePhoneNumber is used to validate phone number
 * @param value
 * @return status
 */

export function validatePhoneNumberorPostalCode(value, maxLength) {
  const regex = /^[0-9-]+$/;
  const minlength = 1;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    const valueLength = value.length;
    if (valueLength >= minlength && valueLength <= maxLength) {
      if (regex.test(value)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'invalid';
      }
    } else {
      result.status = false;
      result.error = 'length';
    }
  }
  return result;
}

/**
 * validateEmailId is used to validate email id
 * @param value
 * @return status
 */

export function validateEmailId(value) {
  const result = { status: true, error: '' };
  const minLength = 1;
  const maxLength = 100;
  if (isNotUndefinedOrNotEmpty(value)) {
    const valueLength = value.length;
    if (valueLength >= minLength && valueLength <= maxLength) {
      if (isEmailValid(value)) {
        result.error = '';
        result.status = true;
      } else {
        result.error = 'invalidEmail';
        result.status = false;
      }
    } else {
      result.error = 'length';
      result.status = false;
    }
  }
  return result;
}

/**
 * validateDate is used to validate date
 * @param value
 * @return status
 */

export function validateDate(value, skipEmpty) {
  const dateRegex = /([12]\d{3}[/](0[1-9]|1[0-2])[/](0[1-9]|[12]\d|3[01]))/;
  const yearMonthRegex = /^(([12]\d{3}[/]([1-9]{1}))|([12]\d{3}[/](0[1-9]|1[0-2])))$/;
  const yearMonthRegexWithSlash = /^(([12]\d{3}[/]([1-9]{1})[/])|([12]\d{3}[/](0[1-9]|1[0-2])[/]))$/;
  const monthSingleRegex = /^([12]\d{3}[/]([1-9]{1})[/](0[1-9]|[12]\d|3[01]))$/;
  const dateSingleRegex = /^([12]\d{3}[/](0[1-9]|1[0-2])[/]([1-9]{1}))$/;
  const monthDateSingleRegex = /^([12]\d{3}[/]([1-9]{1})[/]([1-9]{1}))$/;
  const dateNumberRegex = /([12]\d{3}(0[1-9]|1[0-2])(0[1-9]|[12]\d|3[01]))/;
  const numberRegex = /^[0-9]+$/;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    if (
      (numberRegex.test(value) && dateNumberRegex.test(value.slice(0, 8))) ||
      monthSingleRegex.test(value) ||
      dateSingleRegex.test(value) ||
      yearMonthRegex.test(value) ||
      monthDateSingleRegex.test(value) ||
      yearMonthRegexWithSlash.test(value)
    ) {
      result.status = false;
      result.error = 'invalidformat';
    } else if (dateRegex.test(value)) {
      if (validateMinMaxDate(new Date(value))) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'range';
      }
    } else {
      result.status = false;
      result.error = 'invalid';
    }
  } else if (skipEmpty) {
    return result;
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * remarksValidation is used to validate remarks
 * @param value
 * @param maxlength
 * @return status
 */

export function remarksValidation(value, maxlength) {
  const minlength = 1;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    if (isTextAreaLengthValid(value, minlength, maxlength)) {
      result.status = true;
    } else {
      result.status = false;
      result.error = 'length';
    }
  }
  return result;
}

/**
 * validateLength is used to validate maximum and minimum length validations
 * @param value
 * @param maxlength
 * @return status
 */

export function validateLength(value, maxLength) {
  const minLength = 0;
  const valueLength = value ? value.length : 0;
  const result = { status: true, error: '' };
  if (valueLength >= minLength && valueLength <= maxLength) {
    result.status = true;
  } else {
    result.status = false;
    result.error = 'length';
  }
  return result;
}
