/**
 * setInitialValues is used to set initial data source
 * @param dataSource
 * @return initalSourceMap
 */

export function setInitialValues(dataSource) {
  return {
    businessName: dataSource.businessName,
    androidDeviceCount: dataSource.androidDeviceCount,
    androidAntiVirus: dataSource.androidAntiVirus,
    dmCode: dataSource.dmCode,
    loginId: dataSource.loginId,
    ipAddress: dataSource.ipAddress,
    managerOption: dataSource.managerOption,
    userCount: dataSource.userCount,
    languageCode: dataSource.languageCode,
    timeZoneCode: dataSource.timeZoneCode,
    contractStatus: dataSource.contractStatus,
    firstUseStartDate: dataSource.firstUseStartDate
      ? dataSource.firstUseStartDate.replace(/-/g, '/')
      : '',
    effectiveDateChange: dataSource.effectiveDateChange
      ? dataSource.effectiveDateChange.replace(/-/g, '/')
      : '',
    cancellationDate: dataSource.cancellationDate
      ? dataSource.cancellationDate.replace(/-/g, '/')
      : '',
    unitPriceBasic: dataSource.unitPriceBasic,
    rentalPlanName: dataSource.rentalPlanName,
    unitPriceRental: dataSource.unitPriceRental,
    salesPersonCode: dataSource.salesPersonCode,
    salesPersonName: dataSource.salesPersonName,
    salesDepartmentName: dataSource.salesDepartmentName,
    salesPhoneNumber: dataSource.salesPhoneNumber,
    salesEmailAddress: dataSource.salesEmailAddress,
    companyNumber: dataSource.companyNumber,
    trial: dataSource.trial,
    trialEndDate: dataSource.trialEndDate ? dataSource.trialEndDate.replace(/-/g, '/') : '',
    gisun: dataSource.gisun === null ? [] : dataSource.gisun,
    paymentNumber: dataSource.paymentNumber === null ? [] : dataSource.paymentNumber,
    campaignId: dataSource.campaignId,
    requestSplit: dataSource.requestSplit,
    countryCode: dataSource.countryCode,
    distinguish: dataSource.distinguish,
    remarksOne: dataSource.remarksOne,
    remarksTwo: dataSource.remarksTwo,
    fullNameOne: dataSource.fullNameOne,
    contactInfoOne: dataSource.contactInfoOne,
    emailOne: dataSource.emailOne,
    departmentNameOne: dataSource.departmentNameOne,
    fullNameTwo: dataSource.fullNameTwo,
    contactInfoTwo: dataSource.contactInfoTwo,
    emailTwo: dataSource.emailTwo,
    departmentNameTwo: dataSource.departmentNameTwo,
    fullNameThree: dataSource.fullNameThree,
    contactInfoThree: dataSource.contactInfoThree,
    emailThree: dataSource.emailThree,
    departmentNameThree: dataSource.departmentNameThree,
    declarationNonContractor: dataSource.declarationNonContractor,
    companiesDeclared: dataSource.companiesDeclared,
    identificationRemarks: dataSource.identificationRemarks,
    ipadIdentification: dataSource.ipadIdentification,
    machineShippingRequest: dataSource.machineShippingRequest,
    deviceDestinationFixed: dataSource.deviceDestinationFixed,
    companyName: dataSource.companyName,
    postalCode: dataSource.postalCode,
    streetAddress: dataSource.streetAddress,
    phoneNumber: dataSource.phoneNumber,
    personInChargeDeptName: dataSource.personInChargeDeptName,
    contactName: dataSource.contactName,
    alternativeDeviceRemarks: dataSource.alternativeDeviceRemarks,
    alternativeReceptionFixed: dataSource.alternativeReceptionFixed,
    receptionistInformation: dataSource.receptionistInformation,
    receptionRemarks: dataSource.receptionRemarks,
    report: dataSource.report,
    existenceSpecialNotes: dataSource.existenceSpecialNotes,
    operationMemoOne: dataSource.operationMemoOne,
    operationMemoTwo: dataSource.operationMemoTwo,
    operationMemoThree: dataSource.operationMemoThree,
    operationMemoFour: dataSource.operationMemoFour,
    operationMemoFive: dataSource.operationMemoFive,
    attachDataFile: null,
    attachFileName: dataSource.attachFileName,
    attachFileUploadedDate: dataSource.originalAttachFileUploadedDate,
    individualDataFile: null,
    individualFileName: dataSource.individualFileName,
    individualFileUploadedDate: dataSource.originalIndividualFileUploadedDate,
  };
}
