/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';
import encoding from 'encoding';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementPolicy from '../component/GroupManagementPolicy';

/** Utility Files */
import { Role } from '../../constants/Role';
import { post, postWithBlobData, putWithFormData } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import {
  getPolicyMapper,
  getChangePolicyMapper,
  makeGroupInformation,
} from '../../actions/GroupAction';
import {
  allowedPolicyPanelKeys,
  defaultDynamicDataValues,
  serviceMapper,
  generateFileUploadBottomComponent,
  generateWifiFileUploadBottomComponent,
  getCertificateFiles,
  getDefaultPolicyData,
  wifiCertCSVHeaders,
  wifiCSVHeaders,
  maintainFilesOnTabSwitch,
  otherPolicyPanelKeys,
} from '../common/GroupSettingCommonFunctions';
import policyPanelMapper from '../constants/groupSettingPolicyConstants';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import { groupComboData, buttonList } from '../constants/groupSettingConstants';
import { groupManagementHeader } from '../constants/groupSettingHeaderConstants';
import checkLanguage from '../../language/checkLanguage';
import UNDERSCORE from '../../v1/common/utils';

const fileDownload = require('js-file-download');

/**
 * PolicyRegistration is to add and embed policy within a specific group
 * @author Aswin Palanisamy
 */
class PolicyRegistration extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const {
      policyModified,
      bcpLanguageList,
      mainPolicyMapper,
      defaultPolicyData,
      changePolicyMapper,
      changePolicyWifiList,
      changePolicyShortMessageList,
      changePolicyLongMessageList,
      changePolicyShortDeleteMessageList,
      changePolicyLongDeleteMessageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
      location: getData,
      screenKey,
    } = props;
    const { mode, groupId, dataSource, selectedTab, isButtonDisabled } = getData.state || {
      mode: 0,
      groupId: -1,
      dataSource: {},
    };
    let appliedPolicyKeys = [];
    if (getData.state) {
      if (getData.state.appliedPolicyKeys) {
        appliedPolicyKeys = [...getData.state.appliedPolicyKeys.split(',')];
      }
      if (getData.state.appliedPolicyKeysFromConfirm) {
        appliedPolicyKeys = [...getData.state.appliedPolicyKeysFromConfirm.split(',')];
      }
    }
    const isEdit = mode === 2 || mode === 3;
    const { NetworkConfigurations: defaultwifiList } = defaultPolicyData.openNetworkConfiguration;
    const mainPolicyMapperObject = cloneDeep(
      !isEmpty(mainPolicyMapper)
        ? mainPolicyMapper
        : getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
    );
    this.state = {
      mode,
      isEdit,
      message: '',
      currentIndex: 0,
      defaultwifiList,
      defaultBcpLanguageCd: '',
      bcpLanguageList,
      pageLoading: false,
      limitContent: false,
      openDialogError: false,
      mainPolicyMapper: mainPolicyMapperObject,
      changePolicyMapper: cloneDeep(changePolicyMapper),
      changePolicyWifiList,
      policyPanelKey:
        selectedTab || (appliedPolicyKeys.length === 0 ? 'passcode_setting' : appliedPolicyKeys[0]),
      headerData: groupManagementHeader(screenKey),
      groupId: Number.isNaN(+groupId) ? -1 : groupId,
      changePolicyShortMessageList,
      changePolicyLongMessageList,
      changePolicyShortDeleteMessageList,
      changePolicyLongDeleteMessageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
      dataSource,
      dynamicData: cloneDeep(
        defaultDynamicDataValues(
          mainPolicyMapperObject,
          this.renderCsvResult,
          this.renderWifiCsvResult,
          bcpLanguageList,
        ),
      ),
      footer: {
        screenKey,
        authorityRoleType: Role.group1,
        reference: { confirmPolicyRef: createRef() },
        disable: isButtonDisabled === undefined ? !policyModified : isButtonDisabled,
      },
      setTimer: this.fetchTimerData(mainPolicyMapperObject.systemUpdate),
      createdPolicyPanelKeys: appliedPolicyKeys,
      openIndividualCSVErrorDialog: false,
      individualCSVErrorContent: '',
      WifiCSVErrorText: '',
    };
  }

  /**
   * componentDidMount perform initial policy load operation
   */
  componentDidMount() {
    const { mode } = this.state;
    const { policyModified } = this.props;
    if (!mode) {
      this.setState({
        pageLoading: false,
        openDialogError: true,
        message: 'data_does_not_exists',
      });
    } else if (!policyModified) {
      this.setState({ pageLoading: true }, () => {
        this.getBcpLanguageList();
      });
    }
  }

  /**
   * setPanelKey is update policyPanelKey
   * @param policyPanelKey
   */
  setPanelKey = (policyPanelKey) => {
    const { mainPolicyMapper } = this.state;
    const { wifiCertificateConfiguration, wifiCSV } = mainPolicyMapper;
    wifiCSV.uploadedcsvfileName = wifiCSV.csvfileName;
    maintainFilesOnTabSwitch(wifiCertificateConfiguration);
    this.setState({ policyPanelKey, mainPolicyMapper });
  };

  /**
   * createButtonClick is update createdPolicyPanelKeys
   * @param policyPanelKey
   */
  createButtonClick = (policyPanelKey) => {
    const { defaultPolicyData } = this.props;
    const { mainPolicyMapper, bcpLanguageList, createdPolicyPanelKeys } = this.state;
    const mainPolicyMapperObject = cloneDeep(
      !isEmpty(mainPolicyMapper)
        ? mainPolicyMapper
        : getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
    );
    this.setState({
      dynamicData: cloneDeep(
        defaultDynamicDataValues(
          mainPolicyMapperObject,
          this.renderCsvResult,
          this.renderWifiCsvResult,
          bcpLanguageList,
        ),
      ),
      createdPolicyPanelKeys: allowedPolicyPanelKeys(policyPanelKey, createdPolicyPanelKeys),
    });
  };

  /**
   * onKeyDown performs key press operation in policy panel
   * @param event
   * @param value
   * @param key
   */
  onKeyDown = (event, value, key, headKey) => {
    if (event.keyCode === 13 && event.preventDefault())
      this.onHandleClick(event.target.name, value, key);
    if (
      key === 'startHour' ||
      key === 'endHour' ||
      key === 'startMinutes' ||
      key === 'endMinutes'
    ) {
      this.formChangePolicyMap(key, headKey);
      this.setTimer(key, value);
    }
  };

  /**
   * setTimer is to make start and end minutes in system update policy
   * @param key
   * @param value
   * @param systemUpdateData
   */
  setTimer = (key, value) => {
    const { setTimer, mainPolicyMapper } = this.state;
    const { setModified } = this.props;
    const setPrefix = key.indexOf('start') > -1 ? 'start' : 'end';
    const isHour = key.indexOf('Hour') > -1;
    const makeMinutes =
      (isHour ? +value : +setTimer[`${setPrefix}Hour`]) * 60 +
      (isHour ? +setTimer[`${setPrefix}Minutes`] : +value);
    mainPolicyMapper.systemUpdate[`${setPrefix}Minutes`] = makeMinutes;
    setModified(true);
    this.setState({ setTimer: { ...setTimer, [key]: value }, mainPolicyMapper });
    return mainPolicyMapper.systemUpdate;
  };

  /**
   * validateTab is to check whether SSID and Passphrase is filled in wifi setting
   * @param wifiList
   * @param currentIndex
   * @return addNewConfig
   */
  validateTab = (wifiList, currentIndex) => {
    let addNewConfig = true;
    let { dynamicData } = this.state;
    if (wifiList[currentIndex].WiFi.SSID === '') {
      dynamicData = { ...dynamicData, ssidError: 'ssid_should_not_be_empty' };
      addNewConfig = false;
    }
    if (wifiList[currentIndex].WiFi.Security !== 'None') {
      if (wifiList[currentIndex].WiFi.Passphrase === '') {
        dynamicData = { ...dynamicData, passwordError: 'password_should_not_be_empty' };
        addNewConfig = false;
      } else if (wifiList[currentIndex].WiFi.Security === 'WEP-PSK') {
        let passwordErrorValue = '';
        if (wifiList[currentIndex].WiFi.Passphrase === '') {
          passwordErrorValue = 'password_should_not_be_empty';
          addNewConfig = false;
        } else if (
          !(
            wifiList[currentIndex].WiFi.Passphrase.length === 10 ||
            wifiList[currentIndex].WiFi.Passphrase.length === 26
          )
        ) {
          passwordErrorValue = 'password_length_should_contain_10_or_16';
          addNewConfig = false;
        }
        dynamicData = { ...dynamicData, passwordError: passwordErrorValue };
      } else if (wifiList[currentIndex].WiFi.Passphrase.length < 10) {
        dynamicData = {
          ...dynamicData,
          passwordError: 'password_length_should_contain_minimum_10',
        };
        addNewConfig = false;
      }
    }
    this.setState({ dynamicData });
    return { addNewConfig, dynamicData };
  };

  /**
   * validateWiFiCertificateTab is to check WiFi Setting Certificate tab data
   * @param validationResult
   * @param errorPanelKey
   * @param mainPolicyMapper
   * @return boolean
   */
  validateWiFiCertificateTab = (validationResult, errorPanelKey, mainPolicyMapper) => {
    const { createdPolicyPanelKeys, dataSource, groupId } = this.state;
    let { dynamicData } = validationResult;
    const { wifiCertificateConfiguration } = mainPolicyMapper;
    dynamicData.wificert = {};
    this.setState({ pageLoading: true });
    if (createdPolicyPanelKeys.includes('wifi_settings_certificate')) {
      const eapMethod =
        wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP.Outer;
      if (wifiCertificateConfiguration.cacertificate) {
        wifiCertificateConfiguration.certFiles.certFile1 =
          wifiCertificateConfiguration.caCertificateFile;
      }
      if (eapMethod === 'PEAP' || eapMethod === 'EAP-TTLS') {
        wifiCertificateConfiguration.certFiles.certFile2 =
          wifiCertificateConfiguration.individualCSVFile;
      } else if (eapMethod === 'EAP-TLS') {
        wifiCertificateConfiguration.certFiles.certFile2 = wifiCertificateConfiguration.certpluscsv;
      }
      wifiCertificateConfiguration.wifiCertFileDetails = dataSource.wifiCertFileDetails;
      wifiCertificateConfiguration.useOldCertFiles = dataSource.showOldwifiCertFiles;
      wifiCertificateConfiguration.groupId = groupId;
      wifiCertificateConfiguration.sessionFolderName = dataSource.sessionFolderName;
      wifiCertificateConfiguration.scheduledGroup = dataSource.isScheduledGroup;
      const formData = new FormData();
      formData.set('certFile1', wifiCertificateConfiguration.certFiles.certFile1);
      formData.set('certFile2', wifiCertificateConfiguration.certFiles.certFile2);
      formData.set('wifiCertJsonData', JSON.stringify(wifiCertificateConfiguration));
      const validateCertificate = async () => {
        const response = await putWithFormData(APIENDPOINTS.VALIDATEWiFiCERT, formData);
        const data = await response.json();
        dynamicData = { ...dynamicData, wificert: { ...data.error } };
        const certificateResult = {
          status: validationResult.addNewConfig && data.status,
          dynamicData,
        };
        getCertificateFiles(wifiCertificateConfiguration, data.error, eapMethod === 'EAP-TLS');
        mainPolicyMapper.wifiCertificateConfiguration = wifiCertificateConfiguration;
        this.navigateToConfirm(
          certificateResult,
          errorPanelKey === '' && !data.status ? 'wifi_settings_certificate' : errorPanelKey,
          mainPolicyMapper,
        );
      };
      validateCertificate();
    } else {
      this.navigateToConfirm(
        { status: validationResult.addNewConfig, dynamicData },
        errorPanelKey,
        mainPolicyMapper,
      );
    }
  };

  /**
   * validateIntialTab is to check whether SSID and Passphrase is filled in wifi setting
   * @param wifiList
   * @param currentIndex
   * @return addNewConfig
   */
  validateIntialTab = (wifiList, currentIndex) => {
    let addNewConfig = true;
    let { dynamicData } = this.state;
    if (
      wifiList[currentIndex].WiFi.SSID !== '' ||
      (wifiList[currentIndex].WiFi.Passphrase !== '' &&
        wifiList[currentIndex].WiFi.Security !== 'None')
    ) {
      if (wifiList[currentIndex].WiFi.SSID === '') {
        dynamicData = { ...dynamicData, ssidError: 'ssid_should_not_be_empty' };
        addNewConfig = false;
      }
      if (wifiList[currentIndex].WiFi.Security !== 'None') {
        if (wifiList[currentIndex].WiFi.Passphrase === '') {
          dynamicData = { ...dynamicData, passwordError: 'password_should_not_be_empty' };
          addNewConfig = false;
        } else if (wifiList[currentIndex].WiFi.Security === 'WEP-PSK') {
          let passwordErrorValue = '';
          if (wifiList[currentIndex].WiFi.Passphrase === '') {
            passwordErrorValue = 'password_should_not_be_empty';
            addNewConfig = false;
          } else if (
            !(
              wifiList[currentIndex].WiFi.Passphrase.length === 10 ||
              wifiList[currentIndex].WiFi.Passphrase.length === 26
            )
          ) {
            passwordErrorValue = 'password_length_should_contain_10_or_16';
            addNewConfig = false;
          }
          dynamicData = { ...dynamicData, passwordError: passwordErrorValue };
        } else if (wifiList[currentIndex].WiFi.Passphrase.length < 10) {
          dynamicData = {
            ...dynamicData,
            passwordError: 'password_length_should_contain_minimum_10',
          };
          addNewConfig = false;
        }
      }
      this.setState({ dynamicData });
    }
    return { addNewConfig, dynamicData };
  };

  /**
   * setPageChange is set data for wifi pages when switched
   * @param index
   * @param wifiList
   * @param currentIndex
   */
  setPageChange = (wifiList, index, currentIndex) => {
    const wifiTabSwitch = this.validateTab(wifiList, currentIndex).addNewConfig;
    if (wifiTabSwitch) {
      this.setState({ currentIndex: index, limitContent: false });
    } else {
      this.setState({ currentIndex, limitContent: false });
    }
  };

  /**
   * addRemoveClick is to add or remove wifi pages
   * @param modifiedPolicyPanelKey
   * @param buttonName
   */
  addRemoveClick = (modifiedPolicyPanelKey, buttonName) => {
    const { defaultPolicyData } = this.props;
    const {
      mainPolicyMapper,
      currentIndex,
      defaultwifiList,
      footer,
      dataSource,
      bcpLanguageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
      changePolicyMapper,
    } = this.state;
    let { createdPolicyPanelKeys } = this.state;
    const [defaultList] = cloneDeep(defaultwifiList);
    const { NetworkConfigurations: wifiList } = mainPolicyMapper.openNetworkConfiguration;
    const canAdd = this.validateTab(wifiList, currentIndex).addNewConfig;
    switch (buttonName) {
      case 'buttonAdd':
        if (canAdd && wifiList.length < 10) {
          wifiList.push(cloneDeep(defaultList));
          this.setState({
            currentIndex: wifiList.length - 1,
            mainPolicyMapper,
            limitContent: false,
          });
        } else if (canAdd) {
          this.setState({
            limitContent: true,
          });
        }
        break;
      case 'buttonRemove':
        if (modifiedPolicyPanelKey === 'wifi_settings' && wifiList.length > 1) {
          if (wifiList[currentIndex].WiFi.SSID) {
            changePolicyWifiDeleteList.push(wifiList[currentIndex].WiFi.SSID);
            changePolicyMapper.wifi_settings.wifiSettingsDelete = 'wifiSettingsDelete';
          }
          wifiList.splice(currentIndex, 1);
          this.setState({
            changePolicyWifiDeleteList,
            changePolicyMapper,
            mainPolicyMapper,
            limitContent: false,
            currentIndex: wifiList.length - 1,
            dynamicData: cloneDeep(
              defaultDynamicDataValues(
                getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
                this.renderCsvResult,
                this.renderWifiCsvResult,
                bcpLanguageList,
              ),
            ),
            footer: { ...footer, disable: createdPolicyPanelKeys.length === 0 },
          });
        } else if (modifiedPolicyPanelKey === 'wifi_settings') {
          if (wifiList[0].WiFi.SSID) {
            changePolicyWifiDeleteList.push(wifiList[currentIndex].WiFi.SSID);
            changePolicyMapper.wifi_settings.wifiSettingsDelete = 'wifiSettingsDelete';
          }
          changePolicyMapper.wifi_settings.wifiSetting = '';
          createdPolicyPanelKeys.splice(createdPolicyPanelKeys.indexOf(modifiedPolicyPanelKey), 1);
          this.setState({
            createdPolicyPanelKeys,
            changePolicyWifiDeleteList,
            changePolicyMapper,
            currentIndex: 0,
            mainPolicyMapper: {
              ...mainPolicyMapper,
              openNetworkConfiguration: {
                ...cloneDeep(
                  getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck)
                    .openNetworkConfiguration,
                ),
              },
              wifiCSV: {
                ...cloneDeep(
                  getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck).wifiCSV,
                ),
              },
            },
            limitContent: false,
            footer: { ...footer, disable: createdPolicyPanelKeys.length === 0 },
          });
        } else if (modifiedPolicyPanelKey === 'wifi_settings_certificate') {
          if (
            mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration
              .NetworkConfigurations.WiFi.SSID
          ) {
            changePolicyWifiCertDeleteList.push(
              mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration
                .NetworkConfigurations.WiFi.SSID,
            );
            changePolicyMapper.wifi_settings_certificate.wifiSettingsDelete = 'wifiSettingsDelete';
          }
          changePolicyMapper.wifi_settings_certificate.wifiSettingsCertificate = '';
          createdPolicyPanelKeys.splice(createdPolicyPanelKeys.indexOf(modifiedPolicyPanelKey), 1);
          this.setState({
            createdPolicyPanelKeys,
            changePolicyWifiCertDeleteList,
            changePolicyMapper,
            dataSource: {
              ...dataSource,
              showOldwifiCertFiles: false,
            },
            mainPolicyMapper: {
              ...mainPolicyMapper,
              wifiCertificateConfiguration: {
                ...cloneDeep(
                  getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck)
                    .wifiCertificateConfiguration,
                ),
              },
            },
            limitContent: false,
            footer: { ...footer, disable: createdPolicyPanelKeys.length === 0 },
          });
        } else {
          createdPolicyPanelKeys = UNDERSCORE.difference(
            createdPolicyPanelKeys,
            otherPolicyPanelKeys,
          );
          this.setState({
            createdPolicyPanelKeys,
            mainPolicyMapper: {
              ...cloneDeep(getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck)),
              wifiCertificateConfiguration: mainPolicyMapper.wifiCertificateConfiguration,
              wifiCSV: mainPolicyMapper.wifiCSV,
              openNetworkConfiguration: mainPolicyMapper.openNetworkConfiguration,
              kioskCustomLauncherEnabled: mainPolicyMapper.kioskCustomLauncherEnabled,
              kioskCustomization: mainPolicyMapper.kioskCustomization,
              adjustVolumeDisabled: mainPolicyMapper.adjustVolumeDisabled,
              wifiConfigDisabled: mainPolicyMapper.wifiConfigDisabled,
              bluetoothDisabled: mainPolicyMapper.bluetoothDisabled,
            },
            limitContent: false,
            footer: { ...footer, disable: createdPolicyPanelKeys.length === 0 },
          });
        }
        break;
      default:
        break;
    }
  };

  /**
   * resetComboData is to reset passcode combo values when password type is changed
   */
  resetComboData = () => {
    this.setState(({ mainPolicyMapper }) => {
      return {
        mainPolicyMapper: {
          ...mainPolicyMapper,
          passwordPolicies: [
            {
              ...mainPolicyMapper.passwordPolicies[0],
              passwordMinimumLetters: 0,
              passwordMinimumLowerCase: 0,
              passwordMinimumUpperCase: 0,
              passwordMinimumNonLetter: 0,
              passwordMinimumNumeric: 0,
              passwordMinimumSymbols: 0,
            },
          ],
        },
      };
    });
  };

  /**
   * formChangePolicyMap used to maintain the changes in the policy component
   * @param key
   * @param headKey
   */
  formChangePolicyMap = (key, headKey) => {
    const { setModified } = this.props;
    if (
      key === 'shortLanguageMessage' ||
      key === 'shortLanguageCd' ||
      key === 'shortLanguageRegistered' ||
      key === 'longLanguageMessage' ||
      key === 'longLanguageCd' ||
      key === 'longLanguageRegistered'
    )
      return null;
    if (
      key === 'type' ||
      key === 'startHour' ||
      key === 'endHour' ||
      key === 'startMinutes' ||
      key === 'endMinutes'
    )
      key = 'maintenanceTime';
    if (headKey === 'passcode_setting') key = 'passwordSetting';
    if (headKey === 'wifi_settings') key = 'wifiSetting';
    if (headKey === 'wifi_settings_certificate') key = 'wifiSettingsCertificate';
    if (key === 'buttonRemove') key = 'freezePeriods';
    if (key === 'locationMode' || key === 'shareLocationDisabled') key = 'connectivityLocation';
    const { changePolicyWifiList, changePolicyMapper, currentIndex } = this.state;
    if (headKey === 'wifi_settings' && changePolicyWifiList.indexOf(currentIndex) < 0)
      changePolicyWifiList.push(currentIndex);
    changePolicyMapper[headKey][key] = key;
    setModified(true);
    return this.setState(({ footer }) => {
      return {
        changePolicyWifiList,
        changePolicyMapper,
        footer: { ...footer, disable: false },
      };
    });
  };

  /**
   * onTableFrameEvent is to perform policy input field events
   * @param key
   * @param event
   * @param value
   * @param dateList
   */
  onTableFrameEvent = (key, event, value, headKey, dateList) => {
    const { currentLanguage } = this.props;
    const { type, target } = event;
    const { mainPolicyMapper, dynamicData, currentIndex } = this.state;
    const { defaultPolicyData } = this.props;
    const { keyguardDisabledFeatures, systemUpdate } = mainPolicyMapper;
    if (key === 'wifiCertCsvTemplateDownload') {
      fileDownload(
        encoding.convert(wifiCertCSVHeaders, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
        'individual_csv_template.csv',
      );
    }
    if (key === 'wifiCsvTemplateDownload') {
      fileDownload(
        encoding.convert(wifiCSVHeaders, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
        'wifi_csv_template.csv',
      );
    }
    if (
      (type === 'click' &&
        key === 'buttonRemove' &&
        !isEmpty(systemUpdate.freezePeriods[systemUpdate.freezePeriods.length - 1].startDate)) ||
      type !== 'click'
    )
      this.formChangePolicyMap(key, headKey);
    if (type === 'click') return this.onHandleClick(key, value, headKey);
    switch (key) {
      case 'shortLanguageCd':
        dynamicData[key] = target.value;
        dynamicData.shortLanguageMessage =
          dynamicData.shortMessagesMap[dynamicData.shortLanguageCd] || '';
        dynamicData.shortDeleteDisabled = !dynamicData.shortMessagesMap[
          dynamicData.shortLanguageCd
        ];
        dynamicData.shortCustomizeMessageError = '';
        break;
      case 'longLanguageCd':
        dynamicData[key] = target.value;
        dynamicData.longLanguageMessage =
          dynamicData.longMessagesMap[dynamicData.longLanguageCd] || '';
        dynamicData.longDeleteDisabled = !dynamicData.longMessagesMap[dynamicData.longLanguageCd];
        dynamicData.longCustomizeMessageError = '';
        break;
      case 'shortLanguageMessage':
      case 'longLanguageMessage':
        dynamicData[key] = target.value;
        break;
      case 'shortLanguageRegistered':
      case 'longLanguageRegistered':
        dynamicData[key] = target.value;
        break;
      case 'shortDefaultMessage':
        mainPolicyMapper.shortSupportMessage.defaultMessage = target.value;
        break;
      case 'longDefaultMessage':
        mainPolicyMapper.longSupportMessage.defaultMessage = target.value;
        break;
      case 'passwordScope':
      case 'passwordMinimumLength':
      case 'passwordHistoryLength':
      case 'requirePasswordUnlock':
      case 'passwordMinimumLetters':
      case 'passwordMinimumNumeric':
      case 'passwordMinimumSymbols':
      case 'passwordMinimumNonLetter':
      case 'passwordMinimumLowerCase':
      case 'passwordMinimumUpperCase':
      case 'passwordExpirationTimeout':
      case 'maximumFailedPasswordsForWipe':
      case 'passwordQuality':
        mainPolicyMapper.passwordPolicies = [
          {
            ...cloneDeep(
              key === 'passwordQuality'
                ? getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck)
                : mainPolicyMapper,
            ).passwordPolicies[0],
            [key]: target.value,
          },
        ];
        break;
      case 'factoryResetDisabled':
        if (!value) {
          mainPolicyMapper.frpAdminEmails = [];
        }
        mainPolicyMapper[key] = value;
        break;
      case 'frpAdminEmails':
      case 'stayOnPluggedModes':
        mainPolicyMapper[key] = [target.value];
        break;
      case 'defaultMessage':
        mainPolicyMapper.deviceOwnerLockScreenInfo[key] = target.value;
        break;
      case 'lockdownEnabled':
        mainPolicyMapper.alwaysOnVpnPackage[key] = value;
        break;
      case 'locationMode':
      case 'maximumTimeToLock':
      case 'defaultPermissionPolicy':
        mainPolicyMapper[key] = target.value;
        break;
      case 'type':
        mainPolicyMapper.systemUpdate[key] = target.value;
        break;
      case 'startMinutes':
      case 'endMinutes':
      case 'startHour':
      case 'endHour':
        mainPolicyMapper.systemUpdate = this.setTimer(key, value);
        break;
      case 'freezePeriods':
        mainPolicyMapper.systemUpdate[key] = dateList;
        break;
      case 'keyguardDisabled':
        if (value) {
          mainPolicyMapper.keyguardDisabledFeatures = [];
        }
        mainPolicyMapper[key] = value;
        break;
      case 'FACE':
      case 'IRIS':
      case 'CAMERA':
      case 'BIOMETRICS':
      case 'TRUST_AGENTS':
      case 'NOTIFICATIONS':
      case 'DISABLE_FINGERPRINT':
      case 'DISABLE_REMOTE_INPUT':
      case 'UNREDACTED_NOTIFICATIONS':
        mainPolicyMapper.keyguardDisabledFeatures = keyguardDisabledFeatures.filter(
          (record) => record !== key && record !== 'KEYGUARD_DISABLED_FEATURE_UNSPECIFIED',
        );
        if (value) mainPolicyMapper.keyguardDisabledFeatures.push(key);
        if (isEmpty(mainPolicyMapper.keyguardDisabledFeatures))
          mainPolicyMapper.keyguardDisabledFeatures.push('KEYGUARD_DISABLED_FEATURE_UNSPECIFIED');
        break;
      case 'SSID':
      case 'AutoConnect':
      case 'Passphrase':
        mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[currentIndex].WiFi[
          key
        ] = value;
        if (key === 'SSID') {
          mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[
            currentIndex
          ].GUID = value;
          mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[
            currentIndex
          ].Name = value;
        }
        break;
      case 'InputOrCsvRadioSwitch':
        mainPolicyMapper.wifiCSV.WifiRadio = value;
        break;
      case 'WiFiCSV':
        [mainPolicyMapper.wifiCSV.csvfile] = event.target.files;
        mainPolicyMapper.wifiCSV.csvfileName = mainPolicyMapper.wifiCSV.csvfile.name;
        break;

      case 'Security':
        mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[currentIndex].WiFi[key] =
          target.value;
        break;
      case 'WiFiCertSSID':
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.SSID = value;
        break;
      case 'WiFiCertAutoConnect':
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.AutoConnect =
          target.value === 'true';
        break;
      case 'WiFiCertHiddenSSID':
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.HiddenSSID =
          target.value === 'true';
        break;
      case 'WiFiCertSecurityType':
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.Security =
          target.value;
        break;
      case 'WiFiCertEAPType': {
        const phaseTwo =
          mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration
            .NetworkConfigurations.WiFi.EAP.Inner;
        if (target.value === 'PEAP' && (phaseTwo === 'PAP' || phaseTwo === 'MSCHAP')) {
          mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP.Inner =
            'Automatic';
        }
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP.Outer =
          target.value;
        break;
      }
      case 'WiFiCertPhaseTwoAuthentication':
        mainPolicyMapper.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP.Inner =
          target.value;
        break;
      case 'WiFiCertCACertificate':
        mainPolicyMapper.wifiCertificateConfiguration.cacertificate = value;
        break;
      case 'WiFiCertUserCertificate':
        mainPolicyMapper.wifiCertificateConfiguration.usercert = value;
        break;
      case 'WiFiCertPlusCSV':
        [mainPolicyMapper.wifiCertificateConfiguration.certpluscsv] = event.target.files;
        break;
      case 'WiFiCertIndividualCSV': {
        [mainPolicyMapper.wifiCertificateConfiguration.individualCSVFile] = event.target.files;
        mainPolicyMapper.wifiCertificateConfiguration.individualCsv = !!event.target.files.length;
        break;
      }
      case 'WiFiCertCACertfiicateFile':
        [mainPolicyMapper.wifiCertificateConfiguration.caCertificateFile] = event.target.files;
        break;
      default:
        mainPolicyMapper[key] = value;
        break;
    }
    return this.setState({ mainPolicyMapper, dynamicData });
  };

  /**
   * navigateDetailScreen is to navigate application list screen
   * to select application
   * @param screenName
   */
  navigateDetailScreen = (screenName) => {
    const {
      policyMapperData,
      changePolicyMapperData,
      makeGroupData,
      history,
      location: getData,
    } = this.props;
    const { dataSource } = this.state;
    const {
      mainPolicyMapper,
      changePolicyMapper,
      changePolicyWifiList,
      createdPolicyPanelKeys,
      changePolicyLongMessageList,
      changePolicyShortMessageList,
      changePolicyLongDeleteMessageList,
      changePolicyShortDeleteMessageList,
      bcpLanguageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
    } = this.state;
    policyMapperData(mainPolicyMapper);
    changePolicyMapperData(
      changePolicyMapper,
      changePolicyWifiList,
      changePolicyShortMessageList,
      changePolicyLongMessageList,
      changePolicyShortDeleteMessageList,
      changePolicyLongDeleteMessageList,
      bcpLanguageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
    );
    makeGroupData({ ...dataSource, mainPolicyMapper });

    history.push(serviceMapper(screenName), {
      ...getData.state,
      dataSource,
      createdPolicyPanelKeys,
      groupId: dataSource.groupId,
      ancestorScreen: getData.pathname,
      isButtonDisabled: document.getElementById('policyConfirmationButton').disabled,
    });
  };

  /**
   * onHandleClick is to handle botton click events in policy panel
   * @param buttonKey
   * @param value
   * @return buttonFunction
   */
  onHandleClick = (buttonKey, value, headKey) => {
    const footerActionMapper = {
      clear: () => this.getPolicyData(),
      confirm: () => this.confirmPolicy(),
      buttonAdd: () => this.makeFreezePeriod(value),
      buttonRemove: () => this.makeFreezePeriod(value),
      shortAddMessage: () => this.validateCustomizeMessage(buttonKey, headKey),
      longAddMessage: () => this.validateCustomizeMessage(buttonKey, headKey),
      chooseImeApplication: () => this.navigateDetailScreen(buttonKey),
      chooseAsmApplication: () => this.navigateDetailScreen(buttonKey),
      shortDeleteMessage: () =>
        this.deleteLanguageMessage(
          'shortSupportMessage',
          'shortLanguageCd',
          'shortLanguageMessage',
          'shortMessagesMap',
          'shortLanguageRegistered',
          'shortDeleteDisabled',
          'shortCustomizeMessageError',
          buttonKey,
          headKey,
        ),
      longDeleteMessage: () =>
        this.deleteLanguageMessage(
          'longSupportMessage',
          'longLanguageCd',
          'longLanguageMessage',
          'longMessagesMap',
          'longLanguageRegistered',
          'longDeleteDisabled',
          'shortCustomizeMessageError',
          buttonKey,
          headKey,
        ),
      shortLanguageLoad: () =>
        this.loadMessage(
          'shortLanguageCd',
          'shortLanguageMessage',
          'shortMessagesMap',
          'shortLanguageRegistered',
          'shortDeleteDisabled',
          'shortCustomizeMessageError',
        ),
      longLanguageLoad: () =>
        this.loadMessage(
          'longLanguageCd',
          'longLanguageMessage',
          'longMessagesMap',
          'longLanguageRegistered',
          'longDeleteDisabled',
          'longCustomizeMessageError',
        ),
    }[buttonKey];
    return footerActionMapper && footerActionMapper();
  };

  /**
   * validateCustomizeMessage used to validate customize message.
   * @param buttonKey
   * @param headKey
   */
  validateCustomizeMessage = (buttonKey, headKey) => {
    const { dynamicData } = this.state;
    if (buttonKey === 'shortAddMessage')
      if (dynamicData.shortLanguageMessage.length > 200) {
        dynamicData.shortCustomizeMessageError = 'customize_message_length_error';
        this.setState({ dynamicData });
      } else
        this.addLanguageMessage(
          'shortSupportMessage',
          'shortLanguageCd',
          'shortLanguageMessage',
          'shortMessagesMap',
          'shortLanguageRegistered',
          'shortDeleteDisabled',
          'shortCustomizeMessageError',
          buttonKey,
          headKey,
        );
    if (buttonKey === 'longAddMessage')
      if (dynamicData.longLanguageMessage.length > 500) {
        dynamicData.longCustomizeMessageError = 'customize_message_length_error';
        this.setState({ dynamicData });
      } else
        this.addLanguageMessage(
          'longSupportMessage',
          'longLanguageCd',
          'longLanguageMessage',
          'longMessagesMap',
          'longLanguageRegistered',
          'longDeleteDisabled',
          'longCustomizeMessageError',
          buttonKey,
          headKey,
        );
  };

  /**
   * addLanguageMessage is to used to add langugae map to localized combo
   * @param supportMessage
   * @param languageCode
   * @param languageMessage
   * @param messageMap
   * @param langugaeAdded
   * @param deleteBtnDisabled
   * @param errorKey
   * @param buttonKey
   * @param headKey
   */
  addLanguageMessage = (
    supportMessage,
    languageCode,
    languageMessage,
    messageMap,
    langugaeAdded,
    deleteBtnDisabled,
    errorKey,
    buttonKey,
    headKey,
  ) => {
    const { setModified } = this.props;
    setModified(true);
    this.setState((prevState) => {
      const {
        mainPolicyMapper,
        dynamicData,
        footer,
        changePolicyMapper,
        changePolicyShortMessageList,
        changePolicyLongMessageList,
        changePolicyLongDeleteMessageList,
        changePolicyShortDeleteMessageList,
      } = prevState;
      if (
        buttonKey === 'shortAddMessage' &&
        changePolicyShortMessageList.indexOf(dynamicData[languageCode]) < 0
      ) {
        changePolicyShortMessageList.push(dynamicData[languageCode]);
        if (changePolicyShortDeleteMessageList.indexOf(dynamicData[languageCode]) >= 0) {
          if (changePolicyShortDeleteMessageList.length === 1)
            changePolicyMapper[headKey].shortDeleteMessage = '';
          changePolicyShortDeleteMessageList.splice(dynamicData[languageCode], 1);
        }
      }
      if (
        buttonKey === 'longAddMessage' &&
        changePolicyLongMessageList.indexOf(dynamicData[languageCode]) < 0
      ) {
        changePolicyLongMessageList.push(dynamicData[languageCode]);
        if (changePolicyLongDeleteMessageList.indexOf(dynamicData[languageCode]) >= 0) {
          if (changePolicyLongDeleteMessageList.length > 0)
            changePolicyMapper[headKey].longDeleteMessage = '';
          changePolicyLongDeleteMessageList.splice(dynamicData[languageCode], 1);
        }
      }
      changePolicyMapper[headKey][buttonKey] = buttonKey;
      mainPolicyMapper[supportMessage].localizedMessages[dynamicData[languageCode]] =
        dynamicData[languageMessage];
      return {
        mainPolicyMapper,
        changePolicyMapper,
        changePolicyLongMessageList,
        changePolicyShortMessageList,
        changePolicyShortDeleteMessageList,
        changePolicyLongDeleteMessageList,
        dynamicData: {
          ...dynamicData,
          [messageMap]: mainPolicyMapper[supportMessage].localizedMessages,
          [langugaeAdded]: dynamicData[languageCode],
          [deleteBtnDisabled]: false,
          [errorKey]: '',
        },
        footer: { ...footer, disable: false },
      };
    });
  };

  /**
   * deleteLanguageMessage is to used to delete langugae map from localized combo
   * @param supportMessage
   * @param languageCode
   * @param languageMessage
   * @param messageMap
   * @param languageSelect
   * @param deleteBtnDisabled
   * @param errorKey
   * @param buttonKey
   * @param headKey
   */
  deleteLanguageMessage = (
    supportMessage,
    languageCode,
    languageMessage,
    messageMap,
    languageSelect,
    deleteBtnDisabled,
    errorKey,
    buttonKey,
    headKey,
  ) => {
    const { setModified } = this.props;
    setModified(true);
    this.setState((prevState) => {
      const {
        mainPolicyMapper,
        changePolicyMapper,
        dynamicData,
        footer,
        defaultBcpLanguageCd,
        changePolicyLongMessageList,
        changePolicyShortMessageList,
        changePolicyShortDeleteMessageList,
        changePolicyLongDeleteMessageList,
      } = prevState;
      delete mainPolicyMapper[supportMessage].localizedMessages[dynamicData[languageCode]];
      if (buttonKey === 'shortDeleteMessage') {
        if (changePolicyShortMessageList.length === 1)
          changePolicyMapper[headKey].shortAddMessage = '';
        changePolicyShortMessageList.splice(
          changePolicyShortMessageList.indexOf(dynamicData[languageCode]),
          1,
        );
        if (changePolicyShortDeleteMessageList.indexOf(dynamicData[languageCode]) < 0)
          changePolicyShortDeleteMessageList.push(dynamicData[languageCode]);
      }
      if (buttonKey === 'longDeleteMessage') {
        if (changePolicyLongMessageList.length === 1)
          changePolicyMapper[headKey].longAddMessage = '';
        changePolicyLongMessageList.splice(
          changePolicyLongMessageList.indexOf(dynamicData[languageCode]),
          1,
        );
        if (changePolicyLongDeleteMessageList.indexOf(dynamicData[languageCode]) < 0)
          changePolicyLongDeleteMessageList.push(dynamicData[languageCode]);
      }

      changePolicyMapper[headKey][buttonKey] = buttonKey;
      return {
        mainPolicyMapper,
        changePolicyMapper,
        changePolicyLongMessageList,
        changePolicyShortMessageList,
        changePolicyShortDeleteMessageList,
        changePolicyLongDeleteMessageList,
        dynamicData: {
          ...dynamicData,
          [messageMap]: mainPolicyMapper[supportMessage].localizedMessages,
          [languageMessage]: '',
          [languageCode]: defaultBcpLanguageCd,
          [deleteBtnDisabled]: true,
          [errorKey]: '',
          [languageSelect]: isEmpty(mainPolicyMapper[supportMessage].localizedMessages)
            ? ''
            : Object.keys(mainPolicyMapper[supportMessage].localizedMessages)[0],
        },
        footer: { ...footer, disable: false },
      };
    });
  };

  /**
   * loadMessage is to used to load langugae map from localized combo to laocale & customize message
   * @param languageCode
   * @param languageMessage
   * @param messageMap
   * @param languageSelect
   * @param deleteButtonDisabled
   * @param errorKey
   */
  loadMessage = (
    languageCode,
    languageMessage,
    messageMap,
    languageSelected,
    deleteButtonDisabled,
    errorKey,
  ) => {
    this.setState((prevState) => {
      const { dynamicData } = prevState;
      return {
        dynamicData: {
          ...dynamicData,
          [languageCode]: dynamicData[languageSelected],
          [languageMessage]: dynamicData[messageMap][dynamicData[languageSelected]],
          [deleteButtonDisabled]: false,
          [errorKey]: '',
        },
      };
    });
  };

  /**
   * makeFreezePeriod is to add/remove freezePeriod
   * @param freezePeriods
   */
  makeFreezePeriod = (freezePeriods) => {
    this.setState((prevState) => {
      const { mainPolicyMapper } = prevState;
      mainPolicyMapper.systemUpdate.freezePeriods = cloneDeep(freezePeriods);
      return { mainPolicyMapper };
    });
  };

  /**
   * validateWifiConfiguration is used to validate all wifi fields
   *
   * return boolean
   */
  validateWifiConfiguration = () => {
    const { mainPolicyMapper, currentIndex } = this.state;
    const { NetworkConfigurations: wifiList } = mainPolicyMapper.openNetworkConfiguration;
    const { wifiConfigDisabled } = mainPolicyMapper;
    let result;
    if (wifiConfigDisabled) {
      result = this.validateTab(wifiList, currentIndex);
    } else if (!wifiConfigDisabled && currentIndex === 0)
      result = this.validateIntialTab(wifiList, currentIndex);
    else {
      result = this.validateTab(wifiList, currentIndex);
    }
    return result;
  };

  /**
   * confirmPolicy sets the current policy data for group registration
   */
  confirmPolicy = () => {
    const { dynamicData, createdPolicyPanelKeys, mainPolicyMapper } = this.state;
    let errorPanelKey = '';
    let validationResult = { addNewConfig: true, dynamicData };
    if (createdPolicyPanelKeys.includes('wifi_settings')) {
      if (mainPolicyMapper.wifiCSV.WifiRadio === 'csv') {
        this.setState({ pageLoading: true });
        const { wifiCSV } = mainPolicyMapper;
        const formData = new FormData();
        formData.set('wificsv', wifiCSV.csvfile);
        const validateCSV = async () => {
          const response = await putWithFormData(APIENDPOINTS.VALIDATEWiFiCSV, formData);
          const data = await response.json();
          if (data.status) {
            wifiCSV.uploadedcsvfileName = wifiCSV.csvfileName;
          } else {
            wifiCSV.csvfile = null;
            wifiCSV.csvfileName = '';
            wifiCSV.uploadedcsvfileName = '';
          }
          this.validateWiFiCertificateTab(
            {
              addNewConfig: data.status,
              dynamicData: { ...dynamicData, wificsv: { ...data.error } },
            },
            data.status ? '' : 'wifi_settings',
            mainPolicyMapper,
          );
        };
        validateCSV();
      } else {
        validationResult = this.validateWifiConfiguration();
        if (!validationResult.addNewConfig) {
          errorPanelKey = 'wifi_settings';
        }
        this.validateWiFiCertificateTab(validationResult, errorPanelKey, mainPolicyMapper);
      }
    } else {
      this.validateWiFiCertificateTab(validationResult, errorPanelKey, mainPolicyMapper);
    }
  };

  /**
   * validateSystemUpdateSetting is used to validate system update setting time
   * @param validationResult
   * @return updatedValidationResult
   */
  validateSystemUpdateSetting = (validationResult) => {
    const { dynamicData } = validationResult;
    const updatedValidationResult = validationResult;
    const { status } = updatedValidationResult;
    const { mainPolicyMapper } = this.state;
    if (mainPolicyMapper.systemUpdate && mainPolicyMapper.systemUpdate.type === 'WINDOWED') {
      const { setTimer } = this.state;
      const startMinutes = setTimer.startHour * 60 + setTimer.startMinutes;
      const endMinutes = setTimer.endHour * 60 + setTimer.endMinutes;
      const startDateError = startMinutes > endMinutes;
      const endDateError = endMinutes - startMinutes < 30;
      dynamicData.systemUpdateStartDateError = startDateError && 'start_time_max_end_time_min';
      dynamicData.systemUpdateEndDateError =
        !startDateError && endDateError && 'start_time_end_time_difference_min';
      updatedValidationResult.status = startDateError || endDateError ? false : status;
      this.setState({ dynamicData });
      updatedValidationResult.dynamicData = dynamicData;
    }
    return updatedValidationResult;
  };

  /**
   * validatePolicyTransparency is used to validate policy transparency message
   * @param validationResult
   * @return updatedValidationResult
   */
  validatePolicyTransparency = (validationResult) => {
    const { createdPolicyPanelKeys } = this.state;
    const updatedValidationResult = validationResult;
    if (createdPolicyPanelKeys.includes('policy_transparency')) {
      const { dynamicData } = validationResult;
      const { status } = updatedValidationResult;
      const { mainPolicyMapper } = this.state;

      const shortMessageEmpty =
        mainPolicyMapper.shortSupportMessage &&
        isEmpty(mainPolicyMapper.shortSupportMessage.defaultMessage);
      dynamicData.shortDefaultMessageEmptyError = shortMessageEmpty && 'empty_default_message';

      const shortMessageLength =
        mainPolicyMapper.shortSupportMessage &&
        mainPolicyMapper.shortSupportMessage.defaultMessage.length > 200;
      dynamicData.shortDefaultMessageLengthError =
        shortMessageLength && 'default_message_length_error';

      const longMessageEmpty =
        mainPolicyMapper.longSupportMessage &&
        isEmpty(mainPolicyMapper.longSupportMessage.defaultMessage) &&
        !isEmpty(mainPolicyMapper.longSupportMessage.localizedMessages);
      dynamicData.longDefaultMessageEmptyError = longMessageEmpty && 'empty_default_message';

      const longMessageLength =
        mainPolicyMapper.longSupportMessage &&
        mainPolicyMapper.longSupportMessage.defaultMessage.length > 500;
      dynamicData.longDefaultMessageLengthError =
        longMessageLength && 'default_message_length_error';

      updatedValidationResult.status =
        shortMessageEmpty || longMessageEmpty || longMessageLength || shortMessageLength
          ? false
          : status;
      this.setState({ dynamicData });
      updatedValidationResult.dynamicData = dynamicData;
    }
    return updatedValidationResult;
  };

  /**
   * navigateToConfirm is to update Dynamic data and navigate to confirm page
   * @param validationResult
   * @param errorPanelKey
   * @param mainPolicyMapper
   */
  navigateToConfirm = (validationResult, errorPanelKey, mainPolicyMapper) => {
    const {
      policyMapperData,
      changePolicyMapperData,
      makeGroupData,
      setModified,
      history,
      location: getData,
    } = this.props;
    const { dataSource } = this.state;
    const {
      bcpLanguageList,
      changePolicyMapper,
      changePolicyWifiList,
      createdPolicyPanelKeys,
      changePolicyLongMessageList,
      changePolicyShortMessageList,
      changePolicyLongDeleteMessageList,
      changePolicyShortDeleteMessageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
    } = this.state;
    this.setState({
      pageLoading: false,
      dynamicData: validationResult.dynamicData,
      mainPolicyMapper,
    });
    policyMapperData(mainPolicyMapper);
    changePolicyMapperData(
      changePolicyMapper,
      changePolicyWifiList,
      changePolicyShortMessageList,
      changePolicyLongMessageList,
      changePolicyShortDeleteMessageList,
      changePolicyLongDeleteMessageList,
      bcpLanguageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
    );
    makeGroupData({ ...dataSource, mainPolicyMapper });
    const updatedValidationResult = this.validateSystemUpdateSetting(validationResult);
    if (!errorPanelKey && !updatedValidationResult.status) {
      errorPanelKey = 'system_update_settings';
    }
    const updatedValidation = this.validatePolicyTransparency(updatedValidationResult);
    if (!errorPanelKey && !updatedValidation.status) {
      errorPanelKey = 'policy_transparency';
    }
    if (updatedValidation.status) {
      setModified(false);
      history.push(serviceMapper('viewPolicy'), {
        ...getData.state,
        dataSource,
        certFiles: mainPolicyMapper.wifiCertificateConfiguration.certFiles,
        createdPolicyPanelKeys,
        parentScreen: getData.pathname,
      });
    } else if (errorPanelKey) {
      this.setPanelKey(errorPanelKey);
      this.setState({ dynamicData: updatedValidationResult.dynamicData });
    }
  };

  /**
   * makePolicyPanelData is to validate the passcode combo fields based on password type
   * @return policyPanelMapper
   */
  makePolicyPanelData = () => {
    const { mainPolicyMapper, currentIndex, dynamicData } = this.state;
    const { passwordPolicies } = mainPolicyMapper;
    const comboData = cloneDeep(groupComboData).countList;
    const {
      passwordQuality,
      passwordMinimumLength,
      passwordMinimumLetters,
      passwordMinimumNumeric,
      passwordMinimumSymbols,
      passwordMinimumLowerCase,
      passwordMinimumUpperCase,
      passwordMinimumNonLetter,
    } = passwordPolicies[0];
    if (+passwordMinimumLetters + +passwordMinimumNonLetter > +passwordMinimumLength) {
      this.resetComboData();
    }
    const minLetterOpt = cloneDeep(comboData).splice(
      0,
      (passwordQuality === 'ALPHANUMERIC'
        ? +passwordMinimumLength - passwordMinimumNumeric
        : +passwordMinimumLength - passwordMinimumNonLetter) + 1,
    );
    const minLLetterOpt = cloneDeep(comboData).splice(
      0,
      passwordMinimumLetters - passwordMinimumUpperCase + 1,
    );

    const minULetterOpt = cloneDeep(comboData).splice(
      0,
      passwordMinimumLetters - passwordMinimumLowerCase + 1,
    );

    const minNonLetterOpt = cloneDeep(comboData).splice(
      0,
      passwordMinimumLength - passwordMinimumLetters + 1,
    );
    const minNumOpt = cloneDeep(comboData).splice(
      0,
      (passwordQuality === 'ALPHANUMERIC'
        ? passwordMinimumLength - passwordMinimumLetters
        : passwordMinimumNonLetter - passwordMinimumSymbols) + 1,
    );
    const minSybOpt = cloneDeep(comboData).splice(
      0,
      passwordMinimumNonLetter - passwordMinimumNumeric + 1,
    );
    return policyPanelMapper(
      mainPolicyMapper,
      {
        minNumOpt,
        minSybOpt,
        minLetterOpt,
        minLLetterOpt,
        minULetterOpt,
        minNonLetterOpt,
      },
      currentIndex,
      dynamicData,
      this.languageLabelCheck,
    );
  };

  /**
   * getBcpLanguageList is to fetch bcp47 language list code
   */
  getBcpLanguageList = async () => {
    const response = await post(APIENDPOINTS.GETBCPLANGUAGELIST);
    const result = await response.json();
    const { status, message, data: bcpLanguageListData } = result;
    if (status === 'OK') {
      this.setState(
        ({ isEdit }) => {
          return {
            pageLoading: isEdit,
            bcpLanguageList: this.formbcpLanguageListData(bcpLanguageListData),
            defaultBcpLanguageCd: Object.values(bcpLanguageListData)[0],
          };
        },
        () => {
          this.getPolicyData();
        },
      );
    } else {
      this.setState({ pageLoading: false, openDialogError: true, message });
    }
  };

  /**
   * formbcpLanguageListData is to form bcplanguage map for loading locale combo
   * @param languageMap
   */
  formbcpLanguageListData = (languageMap) => {
    const filterData = [];
    Object.keys(languageMap).map((languageCode) => {
      const languageValue = languageMap[languageCode];
      if (!isEmpty(languageValue) && languageCode !== 'und') {
        const tempMap = { value: languageCode, option: languageMap[languageCode] };
        return filterData.push(tempMap);
      }
      return null;
    });
    return filterData;
  };

  /**
   * getPolicyData is to fetch policy information from DB in edit and addCopy mode
   */
  getPolicyData = () => {
    const {
      groupId,
      isEdit,
      dynamicData,
      footer,
      mainPolicyMapper,
      dataSource,
      bcpLanguageList,
      defaultBcpLanguageCd,
    } = this.state;
    const { defaultPolicyData, location: getData } = this.props;
    if (isEdit) {
      this.setState({ pageLoading: true }, async () => {
        const response = await post(APIENDPOINTS.GETPOLICY, groupId);
        const result = await response.json();
        const { status, message, data: resultMainPolicyMapper } = result;
        if (status === 'OK') {
          resultMainPolicyMapper.alwaysOnVpnPackage =
            resultMainPolicyMapper.alwaysOnVpnPackageObject ||
            defaultPolicyData.alwaysOnVpnPackageObject;
          resultMainPolicyMapper.deviceOwnerLockScreenInfo =
            resultMainPolicyMapper.deviceOwnerLockScreenInfoObject ||
            defaultPolicyData.deviceOwnerLockScreenInfoObject;
          resultMainPolicyMapper.permittedInputMethods =
            resultMainPolicyMapper.permittedInputMethodsObject ||
            defaultPolicyData.permittedInputMethodsObject;
          resultMainPolicyMapper.permittedAccessibilityServices =
            resultMainPolicyMapper.permittedAccessibilityServicesObject ||
            defaultPolicyData.permittedAccessibilityServicesObject;
          resultMainPolicyMapper.systemUpdate =
            resultMainPolicyMapper.systemUpdateObject || defaultPolicyData.systemUpdateObject;
          resultMainPolicyMapper.shortSupportMessage =
            resultMainPolicyMapper.shortSupportMessageObject ||
            defaultPolicyData.shortSupportMessageObject;
          resultMainPolicyMapper.longSupportMessage =
            resultMainPolicyMapper.longSupportMessageObject ||
            defaultPolicyData.longSupportMessageObject;
          if (isEmpty(resultMainPolicyMapper.systemUpdate.freezePeriods)) {
            resultMainPolicyMapper.systemUpdate.freezePeriods = cloneDeep(
              defaultPolicyData.systemUpdateObject.freezePeriods,
            );
          }
          if (
            isEmpty(resultMainPolicyMapper.openNetworkConfiguration.NetworkConfigurations) ||
            dataSource.wifiCSVFileName
          ) {
            resultMainPolicyMapper.openNetworkConfiguration.NetworkConfigurations = cloneDeep(
              defaultPolicyData.openNetworkConfiguration.NetworkConfigurations,
            );
          }
          if (isEmpty(resultMainPolicyMapper.wifiCSV)) {
            resultMainPolicyMapper.wifiCSV = cloneDeep(defaultPolicyData.wifiCSV);
            if (dataSource && !isEmpty(dataSource.wifiCSVFileName)) {
              resultMainPolicyMapper.wifiCSV.WifiRadio = 'csv';
            }
          }
          if (isEmpty(resultMainPolicyMapper.wifiCertificateConfiguration)) {
            resultMainPolicyMapper.wifiCertificateConfiguration = cloneDeep(
              defaultPolicyData.wifiCertificateConfiguration,
            );
          } else {
            resultMainPolicyMapper.wifiCertificateConfiguration = {
              ...resultMainPolicyMapper.wifiCertificateConfiguration,
              cacertificate: !!dataSource.wifiCertFileDetails.CACertificate,
              usercert: !!dataSource.wifiCertFileDetails.UserCertificate,
              individualCsv: !!dataSource.wifiCertFileDetails.IndividualCSV,
              certFiles: {},
            };
          }
          const appliedPolicyKeys =
            getData.state && getData.state.appliedPolicyKeys
              ? [...getData.state.appliedPolicyKeys.split(',')]
              : [];
          this.setState({
            dataSource: { ...dataSource, showOldwifiCertFiles: true },
            footer: { ...footer, disable: true },
            currentIndex: 0,
            mainPolicyMapper: {
              ...resultMainPolicyMapper,
              kioskCustomLauncherEnabled: mainPolicyMapper.kioskCustomLauncherEnabled,
              kioskCustomization: mainPolicyMapper.kioskCustomization,
              adjustVolumeDisabled: mainPolicyMapper.adjustVolumeDisabled,
              wifiConfigDisabled: mainPolicyMapper.wifiConfigDisabled,
              bluetoothDisabled: mainPolicyMapper.bluetoothDisabled,
            },
            createdPolicyPanelKeys: appliedPolicyKeys,
            policyPanelKey:
              appliedPolicyKeys.length === 0 ? 'passcode_setting' : appliedPolicyKeys[0],
            pageLoading: false,
            setTimer: this.fetchTimerData(resultMainPolicyMapper.systemUpdate),
            changePolicyWifiList: [],
            changePolicyShortMessageList: [],
            changePolicyLongMessageList: [],
            changePolicyShortDeleteMessageList: [],
            changePolicyLongDeleteMessageList: [],
            changePolicyWifiDeleteList: [],
            changePolicyWifiCertDeleteList: [],
            dynamicData: {
              ...dynamicData,
              bcpLanguageList,
              wificert: {},
              shortMessagesMap: resultMainPolicyMapper.shortSupportMessage.localizedMessages,
              longMessagesMap: resultMainPolicyMapper.longSupportMessage.localizedMessages,
              shortLanguageCd: defaultBcpLanguageCd,
              shortLanguageMessage: '',
              shortLanguageRegistered: isEmpty(
                resultMainPolicyMapper.shortSupportMessage.localizedMessages,
              )
                ? ''
                : Object.keys(
                    resultMainPolicyMapper.shortSupportMessage.localizedMessages,
                  ).sort()[0],
              longLanguageCd: defaultBcpLanguageCd,
              longLanguageMessage: '',
              longLanguageRegistered: isEmpty(
                resultMainPolicyMapper.longSupportMessage.localizedMessages,
              )
                ? ''
                : Object.keys(
                    resultMainPolicyMapper.longSupportMessage.localizedMessages,
                  ).sort()[0],
            },
          });
        } else {
          this.setState({ pageLoading: false, openDialogError: true, message });
        }
      });
    } else {
      this.setState({
        createdPolicyPanelKeys: [],
        policyPanelKey: 'passcode_setting',
        mainPolicyMapper: cloneDeep(
          getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
        ),
        changePolicyWifiList: [],
        changePolicyShortMessageList: [],
        changePolicyLongMessageList: [],
        changePolicyShortDeleteMessageList: [],
        changePolicyLongDeleteMessageList: [],
        changePolicyWifiDeleteList: [],
        changePolicyWifiCertDeleteList: [],
        dynamicData: cloneDeep(
          defaultDynamicDataValues(
            getDefaultPolicyData(defaultPolicyData, this.languageLabelCheck),
            this.renderCsvResult,
            this.renderWifiCsvResult,
            bcpLanguageList,
          ),
        ),
      });
    }
  };

  /**
   * fetchTimerData is to generate setTimer data
   * @param systemUpdateObject
   * @return setTimerObject
   */
  fetchTimerData = ({ startMinutes, endMinutes }) => ({
    endMinutes: endMinutes % 60,
    startMinutes: startMinutes % 60,
    endHour: Math.floor(endMinutes / 60),
    startHour: Math.floor(startMinutes / 60),
  });

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * renderCsvResult used to render csv validation result and file name
   * @param key
   * @returns component
   */
  renderCsvResult = (key) => {
    const { dynamicData, dataSource, mainPolicyMapper } = this.state;
    const { wifiCertificateConfiguration } = mainPolicyMapper;
    return generateFileUploadBottomComponent(
      key,
      dynamicData,
      this.languageLabelCheck,
      this.downloadErrorText,
      dataSource,
      wifiCertificateConfiguration,
      this.downloadPreviousFile,
      this.showCsvErrorDialog,
    );
  };

  /**
   * renderWifiCsvResult used to render csv validation result and file name
   * @param key
   * @returns component
   */
  renderWifiCsvResult = () => {
    const { dynamicData, mainPolicyMapper, dataSource } = this.state;
    const { wifiCSV } = mainPolicyMapper;
    return generateWifiFileUploadBottomComponent(
      dynamicData,
      this.languageLabelCheck,
      this.downloadErrorText,
      wifiCSV,
      this.wifishowCsvErrorDialog,
      dataSource,
      this.downloadPreviousWiFiCSVFile,
    );
  };

  /**
   * downloadPreviousFile is to download previously uploaded certificate file from HDFS
   * @param fileKey
   */
  downloadPreviousFile = (fileKey) => {
    const { groupId, dataSource } = this.state;
    postWithBlobData(APIENDPOINTS.CERTIFICATEFILEDOWNLOAD, {
      groupId,
      fileKey,
      fileName: dataSource.wifiCertFileDetails[fileKey],
      fileUploadedDate:
        dataSource.wifiCertFileDetails[
          fileKey === 'CACertificate' ? 'ca_cert_updated_date' : 'cert_file2_updated_date'
        ],
      sessionFolderName: dataSource.sessionFolderName,
      isScheduledGroup: dataSource.isScheduledGroup,
    })
      .then((response) => response.blob())
      .then((blobData) => {
        const printData = new Blob([blobData]);
        if (printData.size) {
          fileDownload(printData, dataSource.wifiCertFileDetails[fileKey]);
        } else {
          this.setState({
            pageLoading: false,
            openDialogError: true,
            message: 'data_does_not_exists',
          });
        }
      });
  };

  /**
   * downloadPreviousWiFiCSVFile is to download previously uploaded wifi csv file from HDFS
   * @param fileKey
   */
  downloadPreviousWiFiCSVFile = () => {
    const { groupId, dataSource } = this.state;
    postWithBlobData(APIENDPOINTS.WIFICSVFILEDOWNLOAD, {
      groupId,
      fileName: dataSource.wifiCSVFileName,
      sessionFolderName: dataSource.sessionFolderName,
    })
      .then((response) => response.blob())
      .then((blobData) => {
        const printData = new Blob([blobData]);
        if (printData.size) {
          fileDownload(printData, dataSource.wifiCSVFileName);
        } else {
          this.setState({
            pageLoading: false,
            openDialogError: true,
            message: 'data_does_not_exists',
          });
        }
      });
  };

  /**
   * showCsvErrorDialog is to display errors in dialog when error count <=5
   * @param individualCSVErrorContent
   */
  showCsvErrorDialog = (individualCSVErrorContent) => {
    this.setState({
      openIndividualCSVErrorDialog: true,
      individualCSVErrorContent,
    });
  };

  /**
   * closeDeviceDialog is to close the displayed errors dialog when error count <=5
   */
  closeDeviceDialog = () => {
    this.setState({
      openIndividualCSVErrorDialog: false,
      individualCSVErrorContent: '',
    });
  };

  /**
   * csvErrorContent is to display formatting of dialog
   */
  csvErrorContent = () => {
    const { individualCSVErrorContent } = this.state;
    return (
      <div>
        <ul style={{ listStyleType: 'disc' }}>
          {individualCSVErrorContent.split('\n').map((errroData) => (
            <li style={{ padding: 4, listStyle: 'inside' }}>{errroData}</li>
          ))}
        </ul>
      </div>
    );
  };

  /**
   * downloadErrorText is to download csv error document
   * @param errorData
   */
  downloadErrorText = (errorData) => {
    const { currentLanguage } = this.props;
    fileDownload(
      encoding.convert(errorData, currentLanguage === 'ja' ? 'Shift-JIS' : 'UTF-8'),
      `error.txt`,
    );
  };

  /**
   * wifishowCsvErrorDialog is to display errors in dialog when error count <=5
   * @param individualCSVErrorContent
   */
  wifishowCsvErrorDialog = (WifiCSVErrorText) => {
    this.setState({
      openWifiCSVErrorDialog: true,
      WifiCSVErrorText,
    });
  };

  /**
   * wificloseDeviceDialog is to close the displayed errors dialog when error count <=5
   */
  wificloseDeviceDialog = () => {
    this.setState({
      openWifiCSVErrorDialog: false,
      WifiCSVErrorText: '',
    });
  };

  /**
   * wifiCsvErrorContent is to display formatting of dialog
   */
  wifiCsvErrorContent = () => {
    const { WifiCSVErrorText } = this.state;
    return (
      <div>
        <ul style={{ listStyleType: 'disc' }}>
          {WifiCSVErrorText.split('\n').map((errroData) => (
            <li style={{ padding: 4, listStyle: 'inside' }}>{errroData}</li>
          ))}
        </ul>
      </div>
    );
  };

  /**
   * render performs policy registration screen rendering
   * @return policyRegistration
   */
  render() {
    const {
      footer,
      message,
      headerData,
      pageLoading,
      currentIndex,
      limitContent,
      policyPanelKey,
      openDialogError,
      mainPolicyMapper,
      createdPolicyPanelKeys,
      openIndividualCSVErrorDialog,
      openWifiCSVErrorDialog,
    } = this.state;
    const { history } = this.props;
    const { NetworkConfigurations: wifiList } = mainPolicyMapper.openNetworkConfiguration;
    return (
      <div id="contents" className="clearfix">
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <div style={{ width: '100%', paddingBottom: '150px' }}>
            <div id="main">
              <div className="container" style={{ height: '80px' }}>
                <SBContentHeader title={headerData.title} description={headerData.description} />
              </div>
              <GroupManagementPolicy
                footer={footer}
                getList={wifiList}
                currentIndex={currentIndex}
                limitContent={limitContent}
                policyPanelKey={policyPanelKey}
                mainPolicyMapper={cloneDeep(mainPolicyMapper)}
                buttonList={buttonList(policyPanelKey, mainPolicyMapper)}
                policyPanel={this.makePolicyPanelData()}
                createdPolicyPanelKeys={createdPolicyPanelKeys}
                setPanelKey={(key) => this.setPanelKey(key)}
                onKeyDown={(event, value, key, headKey) =>
                  this.onKeyDown(event, value, key, headKey)
                }
                onHandleClick={(key, value) => this.onHandleClick(key, value)}
                addRemoveClick={(modifiedPolicyPanelKey, buttonName) =>
                  this.addRemoveClick(modifiedPolicyPanelKey, buttonName)
                }
                setPageChange={(getList, index, initialIndex) =>
                  this.setPageChange(getList, index, initialIndex)
                }
                onTableFrameEvent={(key, event, value, headKey, dateList) =>
                  this.onTableFrameEvent(key, event, value, headKey, dateList)
                }
                createButtonClick={(key) => this.createButtonClick(key)}
              />
            </div>
          </div>

          <SBDialog
            width="400px"
            type="infoDialog"
            isNotMultiLang
            open={openWifiCSVErrorDialog}
            onOk={() => this.wificloseDeviceDialog()}
            okButtonTitle={this.languageLabelCheck('ok')}
            header={this.languageLabelCheck('csv_upload_errors')}
            content={this.wifiCsvErrorContent()}
          />
          <SBDialog
            width="400px"
            type="infoDialog"
            isNotMultiLang
            open={openIndividualCSVErrorDialog}
            onOk={() => this.closeDeviceDialog()}
            okButtonTitle={this.languageLabelCheck('ok')}
            header={this.languageLabelCheck('csv_upload_errors')}
            content={this.csvErrorContent()}
          />
          <SBDialog
            type="error"
            header="error"
            content={message}
            key="policy_error"
            open={openDialogError}
            onOk={() => {
              this.setState({ openDialogError: false }, () =>
                history.push(serviceMapper('groupManagement')),
              );
            }}
            width="400px"
          />
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const {
    mainPolicyMapper,
    defaultPolicyData,
    changePolicyMapper,
    changePolicyWifiList,
    policyModified,
    imeApps,
    asmApps,
    bcpLanguageList,
    changePolicyLongMessageList,
    changePolicyShortMessageList,
    changePolicyLongDeleteMessageList,
    changePolicyShortDeleteMessageList,
    changePolicyWifiDeleteList,
    changePolicyWifiCertDeleteList,
  } = state.group;
  const { data, current: currentLanguage } = state.language;
  return {
    localeData: data,
    currentLanguage,
    mainPolicyMapper,
    defaultPolicyData,
    changePolicyMapper,
    changePolicyWifiList,
    policyModified,
    imeApps,
    asmApps,
    bcpLanguageList,
    changePolicyLongMessageList,
    changePolicyShortMessageList,
    changePolicyLongDeleteMessageList,
    changePolicyShortDeleteMessageList,
    changePolicyWifiDeleteList,
    changePolicyWifiCertDeleteList,
  };
};

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    policyMapperData: (data) => dispatch(getPolicyMapper(data)),
    changePolicyMapperData: (
      data,
      headList,
      shortMessageList,
      longMessageList,
      shortDeleteMessageList,
      longDeleteMessageList,
      bcpLanguageList,
      changePolicyWifiDeleteList,
      changePolicyWifiCertDeleteList,
    ) =>
      dispatch(
        getChangePolicyMapper(
          data,
          headList,
          shortMessageList,
          longMessageList,
          shortDeleteMessageList,
          longDeleteMessageList,
          bcpLanguageList,
          changePolicyWifiDeleteList,
          changePolicyWifiCertDeleteList,
        ),
      ),
    makeGroupData: (data) => dispatch(makeGroupInformation(data)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
PolicyRegistration.propTypes = {
  screenKey: PropTypes.string.isRequired,
  policyModified: PropTypes.bool.isRequired,
  policyMapperData: PropTypes.func.isRequired,
  currentLanguage: PropTypes.string.isRequired,
  isNotMultiLang: PropTypes.bool.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  makeGroupData: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      mode: PropTypes.number,
      groupId: PropTypes.number,
      parentScreen: PropTypes.string,
      previousParentScreen: PropTypes.string,
    }),
  }).isRequired,
  setModified: PropTypes.func.isRequired,
  changePolicyMapperData: PropTypes.func.isRequired,
  bcpLanguageList: PropTypes.objectOf(PropTypes.object).isRequired,
  mainPolicyMapper: PropTypes.objectOf(PropTypes.object).isRequired,
  defaultPolicyData: PropTypes.objectOf(PropTypes.object).isRequired,
  changePolicyMapper: PropTypes.objectOf(PropTypes.object).isRequired,
  changePolicyWifiList: PropTypes.arrayOf(PropTypes.number).isRequired,
  changePolicyLongMessageList: PropTypes.arrayOf(PropTypes.string).isRequired,
  changePolicyShortMessageList: PropTypes.arrayOf(PropTypes.string).isRequired,
  changePolicyLongDeleteMessageList: PropTypes.arrayOf(PropTypes.string).isRequired,
  changePolicyShortDeleteMessageList: PropTypes.arrayOf(PropTypes.string).isRequired,
  changePolicyWifiDeleteList: PropTypes.arrayOf(PropTypes.string).isRequired,
  changePolicyWifiCertDeleteList: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(PolicyRegistration);
