import { combineReducers } from 'redux';
import user from '../common/component/sblayout/reducer/UserReducer';
import tenant from '../common/component/sblayout/reducer/TenantReducer';
import device from '../device/reducer/deviceReducer';
import group from '../group/reducer/groupReducer';
import language from '../language/languageReducer';
import commonFields from '../common/component/sblayout/reducer/FieldsReducer';
import screenModified from '../common/changeReducer/CommonChangeReducer';
import securityPolicy from '../operationmanagement/android/securitypolicy/SecurityReducer';

const reducer = combineReducers({
  user,
  tenant,
  commonFields,
  device,
  group,
  language,
  screenModified,
  securityPolicy,
});

export default reducer;
