/** deviceReducer is used to map the reponse data from the device to the redux.
 * @author Aswin Palanisamy
 */
import cloneDeep from 'lodash/cloneDeep';
import {
  FETCH_DEPARTMENT,
  FETCH_DEVICE_FILTER,
  FETCH_GROUP_NAME_LIST,
  FETCH_USER_INFORMATION,
  RESET_USER_INFORMATION,
  FETCH_USER_GROUP_NAME_LIST,
  FETCH_USER_SEARCH_DATA,
} from '../../actions/ActionTypes';

const defaultFilterList = [
  {
    deviceType: '',
    category: '',
    field: '',
    characterString: '',
    isCategoryChange: false,
    previousCharacterString: '',
    conditionOne: '',
    conditionTwo: '',
  },
];

const initialState = {
  groupList: [],
  userGroupList: [],
  userStatusList: [],
  userSearch: '',
  userInfoInitialState: {
    employeeIndex: -1,
    userRegistrationMethod: 'singleUser',
    file: '',
    fileBulk: '',
    employeeNumber: '',
    employeeName: '',
    phoneNumber: '',
    userDeviceMap: {},
    deptOneMethod: 'comboDept',
    deptTwoMethod: 'comboDept',
    deptThreeMethod: 'comboDept',
    deptFourMethod: 'comboDept',
    departmentOneSelect: '',
    departmentTwoSelect: '',
    departmentThreeSelect: '',
    departmentFourSelect: '',
    deptOneText: '',
    deptTwoText: '',
    deptThreeText: '',
    deptFourText: '',
    departmentOne: '',
    departmentTwo: '',
    departmentThree: '',
    departmentFour: '',
    optionOne: '',
    optionTwo: '',
    optionThree: '',
    optionFour: '',
    statusId: '',
    departmentEntity: [],
    departmentOneList: [],
    departmentTwoList: [],
    departmentThreeList: [],
    departmentFourList: [],
  },
  department: ['', '', '', ''],
  defaultFilterList,
  deviceFilterCondition: {
    deviceSearch: cloneDeep(defaultFilterList),
    policySearch: [],
    dummyDeviceSearch: [],
  },
};

export default function device(state = initialState, action) {
  const { type, data, userStatus, userSearchText } = action;
  const {
    groupList,
    userGroupList,
    userStatusList,
    userSearch,
    department,
    deviceFilterCondition,
    userInfoInitialState,
  } = initialState;
  switch (type) {
    case FETCH_GROUP_NAME_LIST:
      return {
        ...state,
        groupList: data || groupList,
      };
    case FETCH_USER_INFORMATION:
      return {
        ...state,
        userInfoInitialState: { ...userInfoInitialState, ...data },
      };
    case RESET_USER_INFORMATION:
      return {
        ...state,
        userInfoInitialState,
      };
    case FETCH_USER_GROUP_NAME_LIST:
      return {
        ...state,
        userGroupList: data || userGroupList,
      };
    case FETCH_USER_SEARCH_DATA:
      return {
        ...state,
        userStatusList: userStatus || userStatusList,
        userSearch: userSearchText || userSearch,
      };
    case FETCH_DEPARTMENT:
      return {
        ...state,
        department: data || department,
      };
    case FETCH_DEVICE_FILTER:
      return {
        ...state,
        deviceFilterCondition: data || deviceFilterCondition,
      };
    default:
      return state;
  }
}
