import React, { Component } from 'react';
import { Layout } from 'antd';
import 'antd/es/layout/style';
import { withRouter } from 'react-router-dom';
import { PropTypes } from 'prop-types';
import { connect } from 'react-redux';
import LoadingSpinner from '../loadingSpinner/LoadingSpinner';
import SideLayout from '../sidelayout/SideLayout';
import HeaderLayout from '../headerlayout/HeaderLayout';
import FooterLayout from '../footerlayout/FooterLayout';
import SBScrollToTopBtn from '../sbbutton/SBScrollToTopBtn';
import { get } from '../../../actions/commonActions';
import APIEndPoints from '../../../constants/APIEndPoints';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import getUserEntity from '../../../actions/UserActions';
import { getTenantEntity } from '../../../actions/TenantActions';
import { UrlsRole, Role } from '../../../constants/Role';
import languageChangerAction from '../../../language/languageActions';
import getLanguageData from '../../../language/languageUtils';
import checkLanguage from '../../../language/checkLanguage';
// import { fetchLanguageList, fetchTimezoneList } from '../../../actions/FieldsAction';

const { Header, Footer, Sider, Content } = Layout;

const styles = {
  sider: {
    overflow: 'auto',
    height: 'calc(100vh - 121px)',
    float: 'left',
    background: '#FFF',
  },
  footer: {
    position: 'fixed',
    left: 0,
    bottom: 0,
    width: '100%',
    minWidth: 960,
    height: 42,
    background: '#F2F2F2',
    zIndex: 9999,
    borderTop: '2px solid #B2B2B2',
    display: 'block',
    padding: 0,
    paddingLeft: 15,
  },
  header: {
    width: '100%',
    minWidth: 1040,
    background: '#F2F2F2',
    borderBottom: '2px solid #B2B2B2',
    display: 'block',
    padding: 0,
    height: 79,
    lineHeight: 0,
  },
};

class SBLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  componentDidMount() {
    const {
      history,
      getUserEntity: getLoggedUser,
      getTenantEntity: getTenantDetail,
      localeData,
      changeLanguage,
    } = this.props;
    const userStatus = async () => {
      const response = await get(APIEndPoints.LOGINSTATUS);
      const data = await response.json();
      if (data.resetPasswordStatus || !data.authStatus) {
        history.push(RouteEndPoints.LOGIN);
      } else {
        const userDetailResponse = await get(APIEndPoints.USERDETAIL);
        const userDetailData = await userDetailResponse.json();
        getLoggedUser(userDetailData.userVo);
        getTenantDetail(userDetailData.tenantVo);
        if (Object.keys(localeData) < 1) {
          const languageMap = await getLanguageData(userDetailData.userVo.languageCode);
          changeLanguage(
            userDetailData.userVo.languageCode,
            userDetailData.userVo.timeZoneCode,
            languageMap,
          );
          if (Object.keys(languageMap).length > 0) {
            this.changeLoadingState();
          } else {
            this.changeLoadingState();
          }
        } else {
          this.changeLoadingState();
        }
      }
      document.title = this.languageLabelCheck('application_title');
    };
    userStatus();
  }

  isValidRole = () => {
    const { userVo, location, userType } = this.props;

    const urls = UrlsRole();
    if (location.pathname === RouteEndPoints.REPORT) {
      return (
        userVo.report &&
        urls[location.pathname] &&
        urls[location.pathname].includes(userVo.authorityId)
      );
    }

    if (
      location.pathname.includes(RouteEndPoints.HOME) ||
      (userType && Role[userType].includes(userVo.authorityId))
    ) {
      return !(urls[location.pathname] && !urls[location.pathname].includes(userVo.authorityId));
    }
    if (!userType) {
      return urls[location.pathname] ? urls[location.pathname].includes(userVo.authorityId) : true;
    }
    return false;
  };

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  changeLoadingState = () => {
    this.setState({
      loading: false,
    });
  };

  render() {
    const { children, userVo, tenantVo } = this.props;
    const { loading } = this.state;
    return (
      <LoadingSpinner loading={loading}>
        <Layout>
          <Header style={styles.header}>
            <HeaderLayout userVo={userVo} />
          </Header>
          <Layout>
            <Sider width={220} style={styles.sider}>
              <SideLayout userVo={userVo} tenantVo={tenantVo} />
            </Sider>
            <Content
              style={{
                padding: 20,
                background: '#FFF',
                height: 'calc(100vh - 165px)',
                overflow: 'auto',
              }}
            >
              <div>
                {this.isValidRole() ? children : <p>{this.languageLabelCheck('no_authority')}</p>}
              </div>
              <SBScrollToTopBtn />
            </Content>
          </Layout>
          <Footer id="footer" style={styles.footer}>
            <FooterLayout />
          </Footer>
        </Layout>
      </LoadingSpinner>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getUserEntity: (data) => {
      dispatch(getUserEntity(data));
    },
    getTenantEntity: (data) => {
      dispatch(getTenantEntity(data));
    },
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

const mapStateToProps = (state) => {
  const { userVo, langContent } = state.user;
  const { tenantVo } = state.tenant;
  return {
    userVo,
    tenantVo,
    langContent,
    localeData: state.language.data,
  };
};

SBLayout.contextTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }),
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SBLayout));
