/**
 * AppCRUDFooter is used to create footer component
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import SBButton from '../../../common/component/sbbutton/SBButton';

/**
 * AppCRUDFooter is used to create footer component
 * @param props
 * @return AppCRUDFooterComponent
 */

const AppCRUDFooter = props => {
  const { footer, onHandleClick, reference } = props;
  return (
    <div className="clearfix">
      <div className="floatright">
        <SBButton
          id="btn_ok"
          color="green"
          type="button"
          title="ok"
          disabled={footer.okDisable}
          style={footer.primaryVisibile ? { display: '' } : { display: 'none' }}
          onClick={() => onHandleClick('save')}
        />
        <SBButton
          id="btn_cancel"
          noMarginRight
          type="button"
          title="cancel"
          style={footer.primaryVisibile ? { display: '' } : { display: 'none' }}
          onClick={() => onHandleClick('cancel')}
        />
        <SBButton
          id="btn_back"
          noMarginRight
          type="button"
          title="back"
          ref={reference.backRef}
          style={footer.primaryVisibile ? { display: 'none' } : { display: 'block' }}
          onClick={() => onHandleClick('back')}
        />
      </div>
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */

AppCRUDFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  footer: PropTypes.objectOf(PropTypes.bool).isRequired,
};

export default withRouter(AppCRUDFooter);
