/**
 * SecurityPolicyActions is used fetch the security policy data.
 * @author P.Vinothpandian
 */

import {
  FETCH_SECURITY_GROUP_LIST,
  SECURITY_POLICY_SEARCH_PARAM,
  SECURITY_POLICY_DETAIL,
} from './ActionTypes';

/**
 * getSecurityPolicyGroupList is used fetch the group list
 * @param data
 */
export function getSecurityPolicyGroupList(data) {
  return {
    type: FETCH_SECURITY_GROUP_LIST,
    data,
  };
}
export function setSecurityPolicySearchParam(data) {
  return {
    type: SECURITY_POLICY_SEARCH_PARAM,
    data,
  };
}
export function setSecurityPolicyDetail(data) {
  return {
    type: SECURITY_POLICY_DETAIL,
    data,
  };
}

export default getSecurityPolicyGroupList;
