/**
 * MultipleAdminBulkRegistration is used to register or update a notification from softbank
 * @author: V.Vijayaragavan
 */

import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import checkLanguage from '../../language/checkLanguage';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import RouteEndPoints from '../../constants/RouteEndPoints';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../constants/CommonConstants';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import { bulkRegTableList, multipleAdminCSVHeaderConstant } from './MultipleAdminSettingConstants';
import MultipleAdminCRUDFooter from './MultipleAdminCRUDFooter';
import { validateFileSize } from '../../common/CommonValidations/CommonValidations';
import { get, putWithFormData } from '../../actions/commonActions';
import APIEndPoints from '../../constants/APIEndPoints';
import { currentDate } from '../../common/utils/dateUtils';
import MultipleAdminCountHeader from './MultipleAdminCountHeader';

const fileDownload = require('js-file-download');

class MultipleAdminBulkRegistration extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      pageLoading: true,
      dataSource: {},
      okDisable: true,
      dynamicData: { fileUploadRef: createRef() },
      backButtonRef: createRef(),
      isFinished: false,
    };
  }

  /**
   * componentDidMount performs api operation to load notice list
   */
  componentDidMount() {
    const { tenantVo } = this.props;
    const { dynamicData } = this.state;
    const generateComponent = async () => {
      const response = await get(APIEndPoints.USERLENGTH);
      const responseData = await response.text();
      const registeredUsersCount = parseInt(responseData, 10);
      this.setState({
        usersLength: registeredUsersCount,
        pageLoading: false,
        okDisable: !(registeredUsersCount === tenantVo.userCount),
        dynamicData: {
          ...dynamicData,
          pageErrorMessage:
            registeredUsersCount === tenantVo.userCount ? 'number_of_admin_exceeded' : '',
        },
      });
    };
    generateComponent();
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   */
  onTableFrameEvent = (key, event) => {
    const { tenantVo } = this.props;
    const { usersLength } = this.state;
    if (event.target.type === 'file') {
      const { dataSource } = this.state;
      [dataSource[key]] = event.target.files;
      this.setState({ dataSource, okDisable: usersLength === tenantVo.userCount });
    } else {
      fileDownload(multipleAdminCSVHeaderConstant(), `Multiple_Admin_Bulk_Register_Template.csv`);
    }
  };

  /**
   * renderCsvResult used to render csv validation result and file name
   * @param dynamicData
   * @returns component
   */
  renderCsvResult = (dynamicData) => {
    const errorComponent = dynamicData.uploadedCsvError ? (
      <>
        <span className="red bold">{this.languageLabelCheck('csv_error')}</span>
        <Link>
          <span>
            <a href onClick={() => this.downloadErrorText()}>
              {this.languageLabelCheck('download_csv_error')}
            </a>
          </span>
        </Link>
      </>
    ) : null;
    return errorComponent;
  };

  /**
   * onFooterButtonClick is used to handle foooter button click
   */
  onFooterButtonClick = (key) => {
    const { history } = this.props;
    if (key === 'save') {
      this.onOKButtonClick();
    } else {
      history.push(RouteEndPoints.MULTIPLEADMINSETTING);
    }
  };

  /**
   * onOKButtonClick is used to handle OK button click
   */
  onOKButtonClick = () => {
    const { dataSource } = this.state;
    const dynamicData = {};
    if (dataSource.csvFile) {
      const validationResult = validateFileSize(dataSource.csvFile, 1, 0);
      if (validationResult.status) {
        if (dataSource.csvFile.name.toLowerCase().endsWith('.csv')) {
          this.validateCSVFile();
        } else {
          dynamicData.fileError = 'select_csv_file_format';
        }
      } else {
        dynamicData.fileError = validationResult.key.includes('min')
          ? 'zero_kb_file'
          : 'file_size_greater_than_one';
      }
    } else {
      dynamicData.fileError = 'csv_file_required_error';
    }
    this.setState({ dynamicData });
  };

  /**
   * validateCSVFile is used to send API call, validate CSV and register
   */
  validateCSVFile = () => {
    const { dataSource, dynamicData, backButtonRef } = this.state;
    const formData = new FormData();
    formData.set('csvFile', dataSource.csvFile);
    dynamicData.fileError = '';
    dynamicData.uploadedCsvError = '';
    dynamicData.pageErrorMessage = '';
    dataSource.csvFile = '';
    if (dynamicData.fileUploadRef.current) {
      dynamicData.fileUploadRef.current.value = '';
    }
    this.setState({ pageLoading: true });
    const saveProcess = async () => {
      const response = await putWithFormData(APIEndPoints.BULKREGISTERADMINCSV, formData);
      const data = await response.json();
      if (data.status) {
        dynamicData.uploadedCsvError = '';
        this.setState({ isFinished: true });
      } else if (data.validationError && data.validationError.uploadedCsvError) {
        dynamicData.uploadedCsvError = data.validationError.uploadedCsvError;
      } else {
        dynamicData.pageErrorMessage = data.message;
      }
      this.setState({ dynamicData, okDisable: true, pageLoading: false });
      if (backButtonRef.current) {
        backButtonRef.current.focus();
      }
    };
    saveProcess();
  };

  /**
   * downloadErrorText is to download csv error document
   */
  downloadErrorText = () => {
    const { dynamicData } = this.state;
    fileDownload(dynamicData.uploadedCsvError, `Errors_${currentDate()}.txt`);
  };

  /**
   * render is used to render the component
   */
  render() {
    const { tenantVo } = this.props;
    const {
      pageLoading,
      dataSource,
      okDisable,
      dynamicData,
      usersLength,
      isFinished,
      backButtonRef,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title="multiple_admin_bulk_reg_header"
          description={[
            isFinished ? 'multiple_admin_bulk_finish_desc' : 'multiple_admin_bulk_reg_desc',
          ]}
        />
        <div className="contents-main">
          {!isFinished && (
            <MultipleAdminCountHeader usersLength={usersLength} maxUsercount={tenantVo.userCount} />
          )}
          <CustomTableFrame
            tableList={isFinished ? [] : bulkRegTableList(dynamicData, this.renderCsvResult)}
            dataSource={dataSource}
            defaultValue={{}}
            onTableFrameEvent={(key, event) => this.onTableFrameEvent(key, event)}
          />
          <MultipleAdminCRUDFooter
            footer={{ primaryVisibile: !isFinished, okDisable }}
            usersLength={2}
            maxUsercount={10}
            reference={{ backButtonRef }}
            register={false}
            onHandleClick={(key) => this.onFooterButtonClick(key)}
          />
          <br />
          <span className="red bold">{this.languageLabelCheck(dynamicData.pageErrorMessage)}</span>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { tenantVo } = state.tenant;
  return {
    tenantVo,
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminBulkRegistration.propTypes = {
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminBulkRegistration.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(MultipleAdminBulkRegistration));
