import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import { UserMenuBarData, systemAdminMenuBarData } from '../../../constants/menuList';
import { Role } from '../../../constants/Role';
import checkLanguage from '../../../language/checkLanguage';
import { commonOnChangeMenu } from '../../changeReducer/commonChangeAction';

const stlyes = {
  list: {
    display: 'block',
    paddingLeft: 0,
    paddingBottom: 5,
  },
};

class SideLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: this.getActivePath(props),
      subActive: this.setSubActivePath(props),
      nestedMenuList: '',
      openList: false,
      divStyle: {},
      toggleKeys: [],
      menuBarData: [],
      menuHeight: 0,
    };
  }

  componentDidMount() {
    const { userVo } = this.props;
    let { menuBarData } = this.state;
    if (Role.admin.includes(userVo.authorityId)) {
      menuBarData = systemAdminMenuBarData;
    } else {
      menuBarData = UserMenuBarData;
    }
    this.setState({ menuBarData });
    window.addEventListener('beforeunload', (event) => this.beforeUnload(event));
  }

  componentDidUpdate() {
    const { menuHeight: previousMenuHeight } = this.state;
    const currentMenuHeight = document.getElementById('menu').clientHeight;
    if (previousMenuHeight !== currentMenuHeight) {
      const divStyle = this.calculateHeight();
      this.setState({ divStyle, menuHeight: currentMenuHeight });
    }
  }

  beforeUnload = (event) => {
    const { isModified } = this.props;
    if (!localStorage.getItem('error') && isModified) {
      event.preventDefault();
      event.returnValue = '';
    } else {
      localStorage.removeItem('error');
    }
  };

  getActivePath = (props) => {
    const path = props.location.pathname;
    if (path.includes('setting/contractinformation')) {
      return 'setting';
    }
    if (path.includes('opmanagement/logsearch')) {
      return 'opmanagement';
    }
    if (path.includes('groupsetting/groupmanagement')) {
      return 'group';
    }
    if (path.includes('device/devicelist')) {
      return 'device';
    }
    if (path.includes('idmanagement/idregister')) {
      return 'idmanagement';
    }
    if (path.includes('appmanagement/appregister')) {
      return 'appmanagement';
    }
    if (path.includes('sysmanagement/logsearch')) {
      return 'sysmanagement';
    }
    if (path.includes('setting/languagetime')) {
      return 'settinglang';
    }
    return 'home';
  };

  setSubActivePath = (props) => {
    const activePath = this.getActivePath(props);
    if (activePath === 'setting') {
      return 'contractinfo';
    }
    if (activePath === 'settinglang') {
      return 'language';
    }
    if (activePath === 'opmanagement') {
      return 'logsearch';
    }
    if (activePath === 'group') {
      return 'groupmanagement';
    }
    if (activePath === 'device') {
      return 'deviceList';
    }
    if (activePath === 'idmanagement') {
      return 'idregister';
    }
    if (activePath === 'appmanagement') {
      return 'appregister';
    }
    if (activePath === 'sysmanagement') {
      return 'logsearch';
    }
    return 'dashboard';
  };

  /**
   * onKeyDownMenu used to navigate respective screen during keydown
   * @param event
   * @param data
   * @param clickHandler
   */
  onKeyDownMenu = (event, data, clickHandler) => {
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.preventDefault();
      this.loadMainMenuClick(data, clickHandler);
    }
  };

  loadMainMenuClick = (data, clickHandler, values) => {
    const { onChangeHandler, isModified, location } = this.props;
    if (isModified && (values ? values.path : data.path) !== location.pathname) {
      onChangeHandler({ open: true, onOk: () => clickHandler(data, values) });
    } else {
      clickHandler(data, values);
    }
  };

  /**
   * checkAuthority used to check authority for menu & submenu while menuClick
   * @param nestedMenuData
   * @param menuCheck
   * @returns currentNestedMenu
   */
  checkAuthority = (nestedMenuData, menuCheck) => {
    let currentNestedMenu = {};
    nestedMenuData.every((menuData) => {
      if (menuData.authorityRoleType) {
        if (this.roleResolver(menuData.authorityRoleType)) {
          currentNestedMenu = menuData;
          return false;
        }
      } else {
        currentNestedMenu = menuData;
        return false;
      }
      return true;
    });
    return menuCheck ? !isEmpty(currentNestedMenu) : currentNestedMenu;
  };

  handleMainMenuClick = (data) => {
    const { history } = this.props;
    const currentNestedMenu = this.checkAuthority(data.nested);
    localStorage.removeItem('idListParam');
    this.setState(
      {
        active: data.key,
        subActive: currentNestedMenu.key,
        openList: false,
        nestedMenuList: '',
        toggleKeys: [],
      },
      () => {
        history.push(currentNestedMenu.path);
        this.forceUpdate();
      },
    );
  };

  handleSubMenuClick = (data) => {
    const { history } = this.props;
    this.setState(
      { subActive: data.key, openList: false, nestedMenuList: '', toggleKeys: [] },
      () => {
        history.push(data.path);
      },
    );
    localStorage.removeItem('idListParam');
  };

  openToggleList = (toggleKey) => {
    const { toggleKeys, isOpened } = this.state;
    if (toggleKeys.includes(toggleKey)) {
      toggleKeys.splice(toggleKeys.indexOf(toggleKey), 1);
    } else {
      toggleKeys.push(toggleKey);
    }
    this.setState({ openList: !isOpened, toggleKeys }, () => this.forceUpdate());
  };

  onToggleClick = (data, nestedData) => {
    const { history } = this.props;
    this.setState({ subActive: data.key, nestedMenuList: nestedData.key, toggleKeys: [data.key] });
    history.push(nestedData.path);
  };

  /**
   * onKeydownOpenToggleList used to open toggle list during keydown
   * @param event
   * @param nestedDataKey
   */
  onKeydownOpenToggleList = (event, nestedDataKey) => {
    if (
      event.keyCode === 40 ||
      event.keyCode === 38 ||
      event.keyCode === 13 ||
      event.keyCode === 32
    ) {
      this.openToggleList(nestedDataKey);
    }
  };

  /**
   * onKeydownLoadToggleList used to load toggel list during keydown
   * @param event
   * @param nestedData
   * @param subData
   */
  onKeydownLoadToggleList = (event, nestedData, subData) => {
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.preventDefault();
      this.loadtoggleClick(nestedData, subData);
    }
  };

  calculateHeight = () => {
    const sideLayoutHeight = document.getElementsByClassName('ant-layout-has-sider')[0]
      .clientHeight;
    const sideMenuHeight = document.getElementById('menu').clientHeight;
    return {
      height: `${sideLayoutHeight - sideMenuHeight}px`,
      borderRight: '2px solid #b2b2b2',
      borderTop: '2px solid #b2b2b2',
    };
  };

  resolveRoleAndRender = (nestedData) => {
    const { userVo, tenantVo } = this.props;
    if (nestedData.id === 'enterprise' && !(tenantVo.androidDeviceCount > 0)) return '';
    const showMenu = this.roleResolver(nestedData.authorityRoleType);
    if (showMenu) {
      if (nestedData.key !== 'report') {
        return this.renderTags(nestedData);
      }
      if (tenantVo.reportMenu && userVo.report && nestedData.key === 'report') {
        return this.renderTags(nestedData);
      }
    }
    return '';
  };

  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  loadtoggleClick = (subData, nestedData) => {
    this.loadMainMenuClick(subData, this.onToggleClick, nestedData);
  };

  addNestedClass(key) {
    const { openList, toggleKeys } = this.state;
    if (openList && toggleKeys.includes(key)) {
      return 'on-other';
    }
    return '';
  }

  roleResolver(authorityRoleType) {
    const { userVo } = this.props;
    return !(authorityRoleType && !authorityRoleType.includes(userVo.authorityId));
  }

  renderTags = (nestedData) => {
    const { subActive, openList, nestedMenuList, toggleKeys } = this.state;
    return (
      <ul key={nestedData.key} style={stlyes.list}>
        <li className={nestedData.key === subActive ? 'on' : this.addNestedClass(nestedData.key)}>
          {nestedData.list ? (
            <div>
              <span
                role="button"
                tabIndex={0}
                key={nestedData.key}
                onClick={() => this.openToggleList(nestedData.key)}
                onKeyDown={(event) => this.onKeydownOpenToggleList(event, nestedData.key)}
                className="subcategory-top icon-arrow-right"
              >
                {this.languageLabelCheck(nestedData.label)}
              </span>
              {openList &&
                toggleKeys.includes(nestedData.key) &&
                nestedData.list.map((subData) => {
                  return this.roleResolver(subData.authorityRoleType) ? (
                    <ul key={subData.key} style={stlyes.list}>
                      <li className={subData.key === nestedMenuList ? 'on' : ''}>
                        <span
                          tabIndex={0}
                          role="button"
                          onClick={() => this.loadtoggleClick(nestedData, subData)}
                          onKeyDown={(event) =>
                            this.onKeydownLoadToggleList(event, nestedData, subData)
                          }
                        >
                          {this.languageLabelCheck(subData.label)}
                        </span>
                      </li>
                    </ul>
                  ) : (
                    ''
                  );
                })}
            </div>
          ) : (
            <span className="subcategory-top">
              <span
                style={{ paddingLeft: '0px' }}
                tabIndex={0}
                role="button"
                onClick={() => this.loadMainMenuClick(nestedData, this.handleSubMenuClick)}
                onKeyDown={(event) =>
                  this.onKeyDownMenu(event, nestedData, this.handleSubMenuClick)
                }
              >
                {this.languageLabelCheck(nestedData.label)}
              </span>
            </span>
          )}
        </li>
      </ul>
    );
  };

  render() {
    const { active, divStyle, menuBarData } = this.state;
    const { userVo } = this.props;
    return (
      <span>
        <div id="sidebar">
          <div id="menu">
            {menuBarData.map((data) => {
              const auth = data.authorityRoleType !== userVo.authorityId;
              return this.checkAuthority(data.nested, true) && auth ? (
                <div key={data.key}>
                  <span className={`category-top ${data.key === active ? 'on' : ''} ${data.icon}`}>
                    <span
                      tabIndex={0}
                      role="button"
                      onClick={() => this.loadMainMenuClick(data, this.handleMainMenuClick)}
                      onKeyDown={(event) =>
                        this.onKeyDownMenu(event, data, this.handleMainMenuClick)
                      }
                    >
                      {this.languageLabelCheck(data.label)}
                    </span>
                  </span>
                  {data.nested &&
                    data.key === active &&
                    data.nested.map((nestedData) => {
                      return this.resolveRoleAndRender(nestedData);
                    })}
                </div>
              ) : (
                ''
              );
            })}
          </div>
          <div style={divStyle} />
        </div>
      </span>
    );
  }
}

SideLayout.contextTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }),
};
const mapPropsFromState = (state) => {
  return {
    localeData: state.language.data,
    isModified: state.screenModified.isModified,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onChangeHandler: (payload) => dispatch(commonOnChangeMenu(payload)),
  };
};
export default connect(mapPropsFromState, mapDispatchToProps)(withRouter(SideLayout));
