/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementGrid from '../component/GroupManagementGrid';
import GroupManagementFooter from '../component/GroupManagementFooter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import { getManagedConfiguration } from '../../actions/GroupAction';
import { applicationListHeader } from '../constants/groupSettingHeaderConstants';
import { serviceMapper, configModeMapper } from '../common/GroupSettingCommonFunctions';
import { CommonConstants } from '../../constants/CommonConstants';

/**
 * ApplicationList is to display list of application information
 * @author Aswin Palanisamy
 */
class ApplicationConfigList extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, location: getData } = props;
    const { packageName: packageValue } = getData.state || { packageName: '' };
    this.state = {
      screenKey,
      message: '',
      packageValue,
      disable: true,
      selectedRows: [],
      pageLoading: true,
      applicationList: [],
      countInfoContents: 0,
      openErrorDialog: false,
      headerData: applicationListHeader(screenKey),
    };
    this.addButtonRef = createRef();
  }

  /**
   * componentDidMount is to load list of approved application records
   */
  componentDidMount() {
    const { location: getData } = this.props;
    this.setState({ pageLoading: true }, () =>
      getData.state ? this.getGridData() : this.denyAccess(),
    );
  }

  /**
   * denyAccess used to show message when mode not exists.
   */
  denyAccess = () =>
    this.setState({
      pageLoading: false,
      openErrorDialog: true,
      message: 'data_does_not_exists',
    });

  /**
   * getGridData is to get dataList to be loaded in grid.
   */
  getGridData = async () => {
    const { packageValue, screenKey, disable } = this.state;
    const response = await post(APIENDPOINTS.APPCONFIG, {
      packageValue,
    });
    const result = await response.json();
    const { status, message, data: applicationList } = result;
    if (status === 'OK') {
      const groupDataList = this.setGridDataIndex(applicationList);
      this.setState(
        {
          applicationList: groupDataList,
          selectedRows:
            !isEmpty(applicationList) && screenKey === 'appConfigList' ? applicationList[0] : [{}],
          disable: screenKey === 'appConfigList' ? isEmpty(applicationList) : disable,
          pageLoading: false,
          countInfoContents: applicationList.length,
        },
        () => {
          if (this.addButtonRef.current) this.addButtonRef.current.focus();
        },
      );
    } else {
      this.setState({ message, openErrorDialog: true, pageLoading: false });
    }
  };

  /**
   * setGridDataIndex is to set grid row indexes
   * @param gridList
   * @return dataList
   */
  setGridDataIndex = (gridList = []) => {
    const dataList = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;
      return record;
    });
    return dataList;
  };

  /**
   * handleRadioChange performs to list the propert of each package
   * @param event
   */
  handleRadioChange = (event) => {
    this.setState({ selectedRows: event, disable: false });
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   * @param screenFunctionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      selectApp: () => this.navigateDetailScreen('', '', true),
      cancelApp: () => this.navigateDetailScreen('', '', false),
      cancelAppList: () => this.navigateDetailScreen(serviceMapper(screenKey), '', false),
      addConfig: () =>
        this.navigateDetailScreen(serviceMapper(screenKey), configModeMapper(screenKey), true),
      changeConfig: () =>
        this.navigateDetailScreen(serviceMapper(screenKey), configModeMapper(screenKey), true),
      deleteConfig: () =>
        this.navigateDetailScreen(serviceMapper(screenKey), configModeMapper(screenKey), true),
    }[screenKey]());

  /**
   * navigateDetailScreen used to navigate detail sub screens.
   * @param screenName
   * @param isConfirm
   * @param appMode
   * @return historyObject
   */
  navigateDetailScreen = (screenName, appMode, isChanged) => {
    return this.getBackToPreviousScreen(screenName, appMode, isChanged);
  };

  /**
   * setSelectedRecord used to return the data index which need to get selected in checkbox.
   * @param selectedGridData
   * return index
   */
  setSelectedRecord = () => {
    const { location: getData } = this.props;
    const { mcmId } = getData.state;
    const { applicationList, screenKey } = this.state;
    if (screenKey === 'appConfigList') return [0];
    return applicationList
      .filter((record) => record.mcmId === mcmId)
      .map((finalValue) => finalValue.id);
  };

  /**
   * getBackToPreviousScreen used to navigate previous screen.
   * @param screenName
   * @param isConfirm
   * @param appMode
   */
  getBackToPreviousScreen = (screenName, appMode, isChanged) => {
    const {
      history,
      location: getData,
      makeManagedConfiguration,
      managedConfiguration,
    } = this.props;
    const { packageValue, selectedRows } = this.state;
    const { mcmId, parentScreen } = getData.state;
    if (selectedRows)
      managedConfiguration[packageValue] = isChanged ? selectedRows.mcmId || mcmId : mcmId;
    makeManagedConfiguration(managedConfiguration);
    history.push(screenName || parentScreen, {
      ...getData.state,
      packageName: packageValue,
      parentScreen: getData.pathname,
      appMode,
      isChanged,
      mcmId: isChanged ? selectedRows.mcmId || mcmId : mcmId,
    });
  };

  /**
   * render performs group management screen rendering
   * @return GroupManagement
   */
  render() {
    const {
      disable,
      message,
      screenKey,
      headerData,
      pageLoading,
      openErrorDialog,
      applicationList,
      countInfoContents,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <GroupManagementGrid
          rowKey="id"
          screenKey={screenKey}
          gridList={applicationList}
          countInfo="registered_count_items"
          countInfoContents={countInfoContents}
          defaultSelectedKeys={this.setSelectedRecord()}
          handleRadioChange={(event, selectedRowKeys) =>
            this.handleRadioChange(event, selectedRowKeys)
          }
        />
        <GroupManagementFooter
          footer={{
            disable,
            screenKey,
          }}
          onHandleClick={(key) => this.onHandleClick(key)}
        />
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              if (this.addButtonRef.current) this.addButtonRef.current.focus();
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeManagedConfiguration: (data) => dispatch(getManagedConfiguration(data)),
  };
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { managedConfiguration } = state.group;
  return { managedConfiguration };
};

/**
 * propTypes is used for validation props with its type.
 */
ApplicationConfigList.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      mode: PropTypes.number,
      groupId: PropTypes.number,
    }),
  }).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ApplicationConfigList));
