import React from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import { commonOnChangeMenu } from '../../changeReducer/commonChangeAction';

const SBButton = React.forwardRef((props, ref) => {
  const addDisablStyle = { pointerEvents: 'none', opacity: 0.4 };
  const onClick = (event, data) => {
    const { onChangeHandler, isModified, canModify, onClick: onHandleClick } = props;
    if (canModify && isModified) {
      onChangeHandler({
        open: true,
        onOk: onHandleClick ? () => onHandleClick(event) : () => {},
      });
    } else if (onHandleClick) {
      onHandleClick(event, data);
    }
  };

  function handleKeyDown(event) {
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.type = 'click';
      onClick(event);
    }
  }

  const languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  const {
    add,
    type,
    title,
    noClassName,
    name,
    color,
    style,
    disabled,
    noMarginRight,
    operationTitleName,
    className,
    operationIcon,
    id,
  } = props;
  return (
    <span>
      {operationIcon && (
        <button
          id={id}
          type="button"
          className={className}
          style={style}
          ref={ref}
          title={languageLabelCheck(operationTitleName)}
          disabled={disabled}
          onClick={e => onClick(e)}
        >
          {title}
        </button>
      )}
      {add && (
        <div
          className={noClassName ? '' : 'mb15'}
          style={{ ...(style || {}), ...(disabled ? addDisablStyle : {}) }}
        >
          <span
            role="button"
            tabIndex={disabled ? -1 : 0}
            id={id}
            onClick={e => onClick(e)}
            ref={ref}
            onKeyDown={event => handleKeyDown(event)}
            className="btn-newregist"
          >
            {languageLabelCheck(title || 'add')}
          </span>
        </div>
      )}
      {!add && !operationIcon && (
        <input
          id={id}
          className={
            noMarginRight ? `btn-${color} ${className}` : `btn-${color} mr10  ${className}`
          }
          type={type}
          name={name}
          value={languageLabelCheck(title)}
          style={style}
          ref={ref}
          disabled={disabled}
          onClick={e => onClick(e)}
        />
      )}
    </span>
  );
});
const mapDispatchProps = dispatch => {
  return {
    onChangeHandler: payload => dispatch(commonOnChangeMenu(payload)),
  };
};
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
    isModified: state.screenModified.isModified,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchProps,
  null,
  { forwardRef: true },
)(SBButton);
