/** Base Files */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import SBLink from '../../common/component/sblink/SBLink';
import SBTable from '../../common/component/sbtable/SBTable';
import RoleResolver from '../../common/component/guard/RoleResolver';

/** Utility Files */
import { Role } from '../../constants/Role';
import checkLanguage from '../../language/checkLanguage';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import commonConstants from '../../constants/CommonConstants';
import { tableColumnList } from '../constants/groupSettingConstants';

/**
 * GroupManagementGrid is to list the group management information
 * @author Aswin Palanisamy
 */
class GroupManagementGrid extends Component {
  /**
   * handleRadioChange performs radio button change operation in grid
   * @param value
   */
  handleRadioChange = (value) => {
    const { handleRadioChange } = this.props;
    if (handleRadioChange) handleRadioChange(value);
  };

  /**
   * handleRadioChange performs radio button change operation in grid
   * @param value
   */
  handleButtonHandleClick = (event) => {
    const { handleButtonHandleClick } = this.props;
    if (handleButtonHandleClick) handleButtonHandleClick(event);
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    const { handlePaginationClick } = this.props;
    if (handlePaginationClick) handlePaginationClick(tableData);
  };

  /**
   * handleSelectChange performs pagination combobox change operation in grid
   * @param tableData
   */
  handleSelectChange = (tableData) => {
    const { handleSelectChange } = this.props;
    if (handleSelectChange) handleSelectChange(tableData);
  };

  /**
   * handleCheckboxChange performs checkbox change events in grid
   * @param event
   * @param selectedRowKeys
   */
  handleCheckboxChange = (event, selectedRowKeys) => {
    const { handleCheckboxChange } = this.props;
    if (handleCheckboxChange) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * handleComboboxChange performs combobox change events in grid
   * @param event
   * @param selectedRowKeys
   */
  handleComboboxChange = (event, selectedRowKeys) => {
    const { handleComboboxChange } = this.props;
    if (handleComboboxChange) handleComboboxChange(event, selectedRowKeys);
  };

  /**
   * handleMultiComboboxChange performs multi select combobox change events in grid
   * @param event
   * @param selectedRowKeys
   */
  handleMultiComboboxChange = (event, selectedRowKeys) => {
    const { handleMultiComboboxChange } = this.props;
    if (handleMultiComboboxChange) handleMultiComboboxChange(event, selectedRowKeys);
  };

  /**
   * handleSelectAll performs check all change event in grid
   * @param event
   * @param selectedRowKeys
   */
  handleSelectAll = (event, selectedRowKeys) => {
    const { handleSelectAll, handleCheckboxChange } = this.props;
    if (handleSelectAll) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * handleClearAll performs uncheck all change event in grid
   * @param event
   * @param selectedRowKeys
   */
  handleClearAll = (event, selectedRowKeys) => {
    const { handleClearAll, handleCheckboxChange } = this.props;
    if (handleClearAll) handleCheckboxChange(event, selectedRowKeys);
  };

  /**
   * languageLabelCheck performs language based data rendering operation
   * @param label
   * @return multiLangLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * renderComponent performs dynamic component rendering operation in grid
   * @param record
   * @return dynamicInnerGridComponent
   */
  renderComponent = (record) => {
    const { screenKey, disable } = this.props;
    const { groupId, groupName, icon, appName } = record;
    const componentList = {
      link: (
        <SBLink
          state={{ groupId }}
          content={groupName}
          pathname={ROUTEENDPOINTS.GROUPINFORMATION}
        />
      ),
      span: <span>{groupName}</span>,
      icon: <img style={{ width: '50px', height: '50px' }} src={icon} alt={appName} />,
    };
    const makeComponent = {
      default: componentList.link,
      applicationList: componentList.icon,
      selectApplication: componentList.icon,
      defaultGroupInfo: disable ? componentList.span : componentList.link,
    };
    return makeComponent[screenKey] || makeComponent.default;
  };

  /**
   * render performs grid component rendering
   * @return gridComponent
   */
  render() {
    const {
      type,
      rowKey,
      gridList,
      countInfo,
      screenKey,
      disableKey,
      disableAll,
      gridHeight,
      restrictPagination,
      dynamicData,
      columnProps,
      filterMessage,
      hasHeightScroll,
      countInfoContents,
      defaultSelectedKeys,
      defaultSelected,
    } = this.props;
    const getColumns = tableColumnList(this.renderComponent, screenKey, columnProps, dynamicData);
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          <RoleResolver component="sbtable" columns={getColumns} authorityRoleType={Role.group1}>
            <SBTable
              loading
              type={type}
              rowKey={rowKey}
              columns={getColumns}
              gridHeight={gridHeight}
              hasHeightScroll={hasHeightScroll}
              defaultSelected={defaultSelected}
              pagination={!restrictPagination}
              countInfo={countInfo}
              dataSource={gridList}
              disableAll={disableAll}
              screenKey={screenKey}
              disableKey={disableKey || null}
              filterMessage={filterMessage}
              countInfoContents={countInfoContents}
              defaultSelectedKeys={defaultSelectedKeys}
              filterList={commonConstants.gridListComboOptions}
              handleComboboxChange={(event, previousValue, rowIndex) =>
                this.handleComboboxChange(event, previousValue, rowIndex)
              }
              handleCheckboxChange={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleSelectAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleClearAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleRadioChange={(event) => this.handleRadioChange(event)}
              handleSelectChange={(tableData) => this.handleSelectChange(tableData)}
              handleButtonHandleClick={(event) => this.handleButtonHandleClick(event)}
              handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
              handleMultiComboboxChange={(event, rowIndex) =>
                this.handleMultiComboboxChange(event, rowIndex)
              }
            />
          </RoleResolver>
        </div>
        {isEmpty(gridList) && (
          <div className="clearfix mb10" style={{ marginTop: 10 }}>
            {this.languageLabelCheck('no_results_found')}
          </div>
        )}
      </div>
    );
  }
}

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  return { userVo, localeData: state.language.data };
};

/**
 * propTypes is used for validation props with its type.
 */
GroupManagementGrid.propTypes = {
  type: PropTypes.string,
  disable: PropTypes.bool,
  rowKey: PropTypes.string,
  countInfo: PropTypes.string,
  gridHeight: PropTypes.string,
  disableKey: PropTypes.string,
  isNotMultiLang: PropTypes.bool,
  handleClearAll: PropTypes.func,
  hasHeightScroll: PropTypes.bool,
  handleSelectAll: PropTypes.func,
  filterMessage: PropTypes.string,
  countInfoContents: PropTypes.number,
  screenKey: PropTypes.string.isRequired,
  handleRadioChange: PropTypes.func.isRequired,
  gridList: PropTypes.arrayOf(PropTypes.object),
  handleSelectChange: PropTypes.func.isRequired,
  handleCheckboxChange: PropTypes.func.isRequired,
  handleComboboxChange: PropTypes.func.isRequired,
  handleMultiComboboxChange: PropTypes.func.isRequired,
  handlePaginationClick: PropTypes.func.isRequired,
  dynamicData: PropTypes.arrayOf(PropTypes.object),
  handleButtonHandleClick: PropTypes.func.isRequired,
  defaultSelectedKeys: PropTypes.arrayOf(PropTypes.number),
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
GroupManagementGrid.defaultProps = {
  type: '',
  rowKey: '',
  gridList: [],
  dynamicData: [],
  disable: false,
  disableKey: '',
  gridHeight: '',
  countInfoContents: 0,
  isNotMultiLang: false,
  hasHeightScroll: false,
  defaultSelectedKeys: [],
  countInfo: 'registered_count_items',
  filterMessage: 'display_items_colon',
  handleClearAll: PropTypes.instanceOf(),
  handleSelectAll: PropTypes.instanceOf(),
};

export default connect(mapStateToProps)(GroupManagementGrid);
