/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import deviceHeader from '../constants/deviceHeaderConstants';
import { tableList } from '../constants/deviceTableConstants';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import { getUserGroupNameList } from '../../actions/DeviceAction';
import { serviceMapper } from '../common/deviceCommonFunctions';
import { userRegistrationApiList } from '../constants/deviceConstants';
import RouteEndPoints from '../../constants/RouteEndPoints';

/**
 * UserCsvList is to list device for csv user list
 * @author Srinivasan Ravi
 */
class UserCsvList extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, location: getData, userGroupList } = props;
    const { deviceUserCsvMapper } = getData.state || {
      deviceUserCsvMapper: { userList: [] },
    };
    const { userList = [] } = deviceUserCsvMapper;
    this.state = {
      screenKey,
      disable: true,
      statusList: [],
      csvDataList: [],
      csvDataSearch: '',
      message: '',
      pageLoading: false,
      gridLoading: false,
      counter: userList.length,
      groupSearch: cloneDeep(userGroupList),
      openDialogConfirmation: false,
      deviceUserCsvMapper: cloneDeep(deviceUserCsvMapper),
      headerData: deviceHeader(screenKey),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { location: getData, history } = this.props;
    if (!getData.state) {
      history.push(RouteEndPoints.ERROR);
    } else {
      this.setState({ pageLoading: true }, () => this.getCsvDataList(true));
    }
  }

  /**
   * getCsvDataList is to fetch user information
   * @param onOpen
   */
  getCsvDataList = async (onOpen) => {
    const csvDataList = this.getSearchList();
    this.setState(
      {
        csvDataList,
        disable: false,
        gridLoading: false,
        pageLoading: false,
      },
      () => {
        if (onOpen && this.searchRef.current) this.searchRef.current.focus();
      },
    );
  };

  /**
   * getSearchList is to filter the data based on the search input
   */
  getSearchList = () => {
    const { csvDataSearch, statusList, groupSearch, deviceUserCsvMapper } = this.state;
    const search = (csvDataSearch || '').toLowerCase();
    const MINUS_ONE = -1;
    const { userList } = deviceUserCsvMapper;
    return cloneDeep(userList)
      .filter((user) => isEmpty(statusList) || statusList.indexOf(user.statusId) > MINUS_ONE)
      .filter(
        (userRecord) => isEmpty(groupSearch) || groupSearch.indexOf(userRecord.group) > MINUS_ONE,
      )
      .filter(
        (userData) =>
          isEmpty(search) ||
          userData.employeeName.toLowerCase().indexOf(search) > MINUS_ONE ||
          userData.serial.toLowerCase().indexOf(search) > MINUS_ONE,
      );
  };

  /**
   * performBulkUserDelete used to perform bulk delete for selected users in csv
   */
  performBulkUserDelete = () => {
    const { deviceUserCsvMapper } = this.state;
    this.setState({ pageLoading: true }, async () => {
      const response = await post(userRegistrationApiList[4], { ...deviceUserCsvMapper });
      const result = await response.json();
      const { status, message, data } = result;
      if (status === 'OK') {
        const { history, makeUserGroupList } = this.props;
        makeUserGroupList();
        this.setState(
          {
            openDialogError: false,
            pageLoading: false,
            openDialogConfirmation: false,
          },
          () => {
            history.push(serviceMapper('bulkUserDeleteFinish'), {
              deletedCount: data,
              alreadyDeletedCount: message,
              parentScreen: serviceMapper('userList'),
            });
          },
        );
      } else {
        this.setState({
          message,
          openDialogError: true,
          pageLoading: false,
          openDialogConfirmation: false,
        });
      }
    });
  };

  /**
   * getBacktoPreviousScreen navigates to user bulk delete screen
   */
  getBacktoPreviousScreen = () => {
    const { history, makeUserGroupList } = this.props;
    makeUserGroupList();
    history.push(serviceMapper('bulkUserDelete'), {
      parentScreen: serviceMapper('userList'),
    });
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, csvDataSearch) => {
    if (event.keyCode === 13) {
      this.setState({ csvDataSearch, gridLoading: true }, () => this.getCsvDataList());
    }
  };

  /**
   * handleSearchAction performs group search operation
   * @param key
   * @param value
   */
  handleSearchAction = (key, value) => {
    if (key === 'searchButton') {
      this.setState(
        { csvDataSearch: value.searchKeyword, statusList: value.checkBoxKeys, gridLoading: true },
        () => this.getCsvDataList(),
      );
    } else if (key === 'searchKeyword') {
      this.setState({ csvDataSearch: value[key] });
    }
  };

  /**
   * onHandleClick performs button click events in the screen
   * @param screenKey
   * @return buttonAction
   */
  onHandleClick = (screenKey) =>
    ({
      bulkUserDelete: () => this.setState({ openDialogConfirmation: true }),
      cancel: () => this.getBacktoPreviousScreen(),
      selectGroup: () => this.navigateGroupSelectGroup(screenKey),
    }[screenKey]());

  /**
   * navigateGroupSelectGroup is to navigate to group search page
   * @param screenKey
   */
  navigateGroupSelectGroup = (screenKey) => {
    const { history, location } = this.props;
    const { groupSearch, deviceUserCsvMapper } = this.state;
    history.push(serviceMapper(screenKey), {
      deviceUserCsvMapper,
      getGroup: groupSearch,
      parentScreen: location.pathname,
    });
  };

  /**
   * addCountInfo is to set grid data count in screen header label
   * @param label
   * @param  contents
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render perform csv userList screen rendering
   * @return UserCsvList
   */
  render() {
    const {
      disable,
      counter,
      screenKey,
      statusList,
      headerData,
      message,
      groupSearch,
      pageLoading,
      gridLoading,
      csvDataList,
      csvDataSearch,
      openDialogError,
      openDialogConfirmation,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          isNotMultiLang
          title={this.languageLabelCheck(headerData.title)}
          description={[this.addCountInfo(headerData.description, counter)]}
        />
        <div className="contents-main">
          <CustomTableFrame
            tableList={tableList()}
            dataSource={{
              selectGroup: groupSearch
                .sort((present, next) => (present.toUpperCase() < next.toUpperCase() ? -1 : 1))
                .join(','),
            }}
            onTableFrameEvent={(key) => this.onHandleClick(key)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
        </div>
        <DeviceSearch
          searchHeader="search_phone_number_name_serial"
          screenKey={screenKey}
          defaultValues={statusList}
          searchText={csvDataSearch}
          reference={this.searchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '70%', left: 'calc(60% - 55px)' }}
        >
          <DeviceGrid
            pagination
            isAscending={false}
            screenKey={screenKey}
            gridList={csvDataList}
            filterMessage="results_per_page"
            countInfoContents={csvDataList.length}
            countInfo="select_result_count"
            defaultSelected="userCsvList"
            handleSelectChange={() =>
              this.setState({ gridLoading: true }, () => this.getCsvDataList())
            }
          />
          <DeviceFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            footer={{
              screenKey: 'userBulkDelete',
              disable,
            }}
          />
        </CircularSpinner>
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openDialogError}
          onOk={() => {
            this.setState({ openDialogError: false }, this.getBacktoPreviousScreen());
          }}
          width="400px"
        />
        <SBDialog
          type="confirmation"
          content="confirm_bulk_user_delete"
          header="save_changes"
          open={openDialogConfirmation}
          onCancel={() => {
            this.setState({ openDialogConfirmation: false });
          }}
          onOk={() => this.performBulkUserDelete()}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps perform data linking from the redux store
 * @param state
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  const { userGroupList } = state.device;
  return { localeData: data, userGroupList };
};

/**
 * mapDispatchToProps performs data setup in device redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeUserGroupList: (data) => {
      dispatch(getUserGroupNameList(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
UserCsvList.propTypes = {
  isNotMultiLang: PropTypes.bool.isRequired,
  screenKey: PropTypes.string.isRequired,
  userGroupList: PropTypes.arrayOf(PropTypes.string).isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    deviceCsvMapper: PropTypes.objectOf(PropTypes.any),
  }).isRequired,
  makeUserGroupList: PropTypes.func.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(UserCsvList));
