import React from 'react';
import { withRouter } from 'react-router-dom';
import isUndefined from 'lodash/isUndefined';
import { connect } from 'react-redux';
import arrowIcon from './images/icon-arrow.png';
import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';


const linkStyle = {
  color: '#036EB7',
  textDecoration: 'underline',
  cursor: 'pointer',
  background: 'none',
  font: 'inherit',
  padding: 0,
  border: 'none',
  letterSpacing: 0,
};
const languageLabelCheck = (label, props) => {
  const { isNotMultiLang, localeData ,dynamicLinkContent} = props;
  if(isNotMultiLang) return label;
   return isUndefined(dynamicLinkContent) ? checkLanguage(localeData,label)
   :addDynamicContent(checkLanguage(localeData,label),dynamicLinkContent);
 
  
};

const SBLinkHeader = props => {
  const { hyperLinkText, reference } = props;

  const handleClick = () => {
    const { history, hyperLink, historyData,onClick } = props;
    if(onClick) onClick();
    history.push(hyperLink, { ...historyData });
  };

  return (
    <>
      <h2 className="title-corner2 mb15">
        <button ref={reference} type="button" style={linkStyle} onClick={() => handleClick()}>
          {languageLabelCheck(hyperLinkText, props)}
          <img src={arrowIcon} className="title-corner-arrow" alt="title-corner-arrow" />
        </button>
      </h2>
    </>
  );
};
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(withRouter(SBLinkHeader));
