import {
  FETCH_TIMEZONE_ENTITY,
  FETCH_LANGUAGE_ENTITY,
  FETCH_AUTHORITY_ENTITY,
  FETCH_CAMPAIGN_ENTITY,
  FETCH_COUNTRY_ENTITY,
} from './ActionTypes';

/**
 * fetchTimezoneList is used fetch the timeZone data
 */

export function fetchTimezoneList(data) {
  return { type: FETCH_TIMEZONE_ENTITY, data };
}

/**
 * fetchLanguageList is used fetch the language data
 */

export function fetchLanguageList(data) {
  return { type: FETCH_LANGUAGE_ENTITY, data };
}

/**
 * getAuthorityEntity is used fetch the timeZone data
 * @param data
 */

export function getAuthorityEntity(data) {
  return {
    type: FETCH_AUTHORITY_ENTITY,
    data,
  };
}

/**
 * fetchLanguageList is used fetch the master campaign  data
 */

export function fetchCampaignList(data) {
  return { type: FETCH_CAMPAIGN_ENTITY, data };
}

/**
 * fetchLanguageList is used fetch the master campaign  data
 */

export function fetchCountryList(data) {
  return { type: FETCH_COUNTRY_ENTITY, data };
}
