import { get } from '../actions/commonActions';

/**
 * getLanguageData is to get language content of given language.
 * @param language
 * @return languageContent
 *
 * @author Gobi R
 */
const getLanguageData = async (language) => {
  const languages = await get(`languages/change-language/${language}`);
  const languageContent = await languages.json();
  return languageContent;
};

export default getLanguageData;
