/**
 * MultipleAdminSearch is used to perform admin search operation
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import PropTypes from 'prop-types';
import SearchFilter from '../../v1/common/component/SearchFilter';

/**
 * searchFilterProps is used to load the components
 * @param reference
 */

function searchFilterProps(reference) {
  return [
    {
      id: 'search-keyword',
      type: 'text',
      defaultValue: '',
      key: 'searchKeyword',
      style: { marginRight: '10px' },
      ref: reference,
      maxLength: 100,
    },
    {
      id: 'search-button',
      type: 'button',
      color: 'green',
      title: 'search',
      key: 'searchButton',
    },
    {
      id: 'search-by-group-button',
      type: 'button',
      title: 'search_by_group_name',
      key: 'searchByGroupButton',
    },
  ];
}

const MultipleAdminSearch = props => {
  const { onSearchAction, onKeyDown, reference } = props;
  return (
    <div className="contents-main mb15">
      <SearchFilter
        border
        searchOptions={searchFilterProps(reference)}
        onSearchAction={(key, event, values) => onSearchAction(key, event, values)}
        onKeyDown={(event, value) => onKeyDown(event, value)}
      />
    </div>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminSearch.propTypes = {
  onSearchAction: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  reference: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminSearch.defaultProps = {
  reference: {},
};

export default MultipleAdminSearch;
