/**
 * checkComponentVisibility is to check whether component can be made visible in screen
 * @param mainPolicyMapper
 * @param fieldKey
 * @param currentIndex
 * @return boolean
 */
function checkComponentVisibility(mainPolicyMapper, fieldKey, currentIndex) {
  switch (fieldKey) {
    case 'csvbtn':
      return mainPolicyMapper.wifiCSV.WifiRadio === 'csv';
    case 'selection_method':
      return mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations.length > 1;
    case 'password': {
      if (mainPolicyMapper.wifiCSV.WifiRadio === 'input') {
        if (
          mainPolicyMapper &&
          mainPolicyMapper.wifiCSV &&
          mainPolicyMapper.wifiCSV.WifiRadio === 'input'
        ) {
          return (
            !isEmpty(
              mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[currentIndex],
            ) &&
            mainPolicyMapper.openNetworkConfiguration.NetworkConfigurations[currentIndex].WiFi
              .Security === 'None'
          );
        }
      }
      return true;
    }
    default:
      return false;
  }
}

