/** buildWifiSettingViewPanelMapper is used to build WiFi Setting View Panel
 * @param setComboData
 * @param wifiCertificateConfiguration
 * @param wifiCertFileDetails
 * @param dynamicData
 * @return wifiSettingViewPanel
 */
export function buildWifiSettingViewPanelMapper(
  setComboData,
  wifiCertificateConfiguration,
  wifiCertFileDetails,
  dynamicData,
) {
  const wifiSettingsCertificate = [
    {
      component: 'span',
      label: 'ssid',
      key: 'WiFiCertSSID',
      name: 'wificertssid',
      id: 'ssid_name',
      render: (appliedPolicy) =>
        appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations
          .WiFi.SSID,
    },
    {
      component: 'span',
      key: 'WiFiCertAutoConnect',
      name: 'wificertAutoConnect',
      id: 'auto_connection',
      label: 'auto_connection',
      hasLang: true,
      render: (appliedPolicy) =>
        appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations
          .WiFi.AutoConnect
          ? 'on'
          : 'off',
    },
    {
      component: 'span',
      key: 'WiFiCertHiddenSSID',
      name: 'WiFiCertHiddenSSID',
      id: 'WiFiCertHiddenSSID',
      label: 'hidden_ssid',
      hasLang: true,
      render: (appliedPolicy) =>
        appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations
          .WiFi.HiddenSSID
          ? 'on'
          : 'off',
    },
    {
      label: 'security_type',
      component: 'span',
      key: 'WiFiCertSecurityType',
      id: 'wifi_security_type',
      name: 'wififcertSecurityType',
      hasLang: true,
      render: (appliedPolicy) =>
        setComboData(
          groupComboData.wifiCertSecurityType,
          appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations
            .WiFi.Security,
        ),
    },
    {
      label: 'eap_method',
      component: 'span',
      key: 'WiFiCertEAPType',
      id: 'WiFiCertEAPType',
      name: 'wififcertEAPType',
      hasLang: true,
      render: (appliedPolicy) =>
        setComboData(
          groupComboData.EapMethod,
          appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations
            .WiFi.EAP.Outer,
        ),
    },
  ];
  if (wifiCertificateConfiguration) {
    const eapMethod =
      wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP.Outer;
    const isTls = eapMethod === 'EAP-TLS';
    if (!isTls) {
      wifiSettingsCertificate.push({
        label: 'phase_two_authentication',
        component: 'span',
        key: 'WiFiCertPhaseTwoAuthentication',
        id: 'WiFiCertPhaseTwoAuthentication',
        name: 'WiFiCertPhaseTwoAuthentication',
        hasLang: true,
        render: (appliedPolicy) =>
          setComboData(
            groupComboData.phaseTwoAuthentication,
            appliedPolicy.wifiCertificateConfiguration.openNetworkConfiguration
              .NetworkConfigurations.WiFi.EAP.Inner,
          ),
      });
    }
    wifiSettingsCertificate.push({
      component: 'span',
      key: 'WiFiCertCACertificate',
      name: 'WiFiCertCACertificate',
      id: 'WiFiCertCACertificate',
      label: 'ca_certificate',
      hasLang: true,
      render: (appliedPolicy) =>
        appliedPolicy.wifiCertificateConfiguration.cacertificate ? 'on' : 'off',
    });
    if (wifiCertificateConfiguration.cacertificate) {
      wifiSettingsCertificate.push({
        label: 'empty',
        component: 'span',
        key: 'WiFiCertCACertificateFileName',
        name: 'WiFiCertCACertificateFileName',
        id: 'WiFiCertCACertificateFileName',
        hasLang: false,
        render: () =>
          wifiCertificateConfiguration.updatedCertFileNames &&
          wifiCertificateConfiguration.updatedCertFileNames.CACertificate
            ? wifiCertificateConfiguration.updatedCertFileNames.CACertificate
            : wifiCertFileDetails && wifiCertFileDetails.CACertificate,
      });
    }
    if (isTls) {
      wifiSettingsCertificate.push({
        component: 'span',
        key: 'WiFiCertUserCertificate',
        name: 'wififcertUserCertificate',
        id: 'WiFiCertUserCertificate',
        label: 'user_certificate',
        hasLang: true,
        render: (appliedPolicy) =>
          appliedPolicy.wifiCertificateConfiguration.usercert ? 'on' : 'off',
      });
      if (wifiCertificateConfiguration.usercert) {
        wifiSettingsCertificate.push({
          component: 'span',
          key: 'WiFiCertUserCertificateFileName',
          name: 'wififcertUserCertificateFileName',
          id: 'WiFiCertUserCertificateFileName',
          label: 'certificate_individual_csvformat',
          hasLang: false,
          render: () =>
            wifiCertificateConfiguration.updatedCertFileNames &&
            wifiCertificateConfiguration.updatedCertFileNames.UserCertificate
              ? wifiCertificateConfiguration.updatedCertFileNames.UserCertificate
              : wifiCertFileDetails && wifiCertFileDetails.UserCertificate,
        });
      }
    } else {
      wifiSettingsCertificate.push({
        component: 'span',
        key: 'WiFiCertIndividualCSV',
        name: 'WiFiCertIndividualCSV',
        id: 'WiFiCertIndividualCSV',
        label: 'individual_csv',
        hasLang: false,
        render: () =>
          wifiCertificateConfiguration.updatedCertFileNames &&
          wifiCertificateConfiguration.updatedCertFileNames.IndividualCSV
            ? wifiCertificateConfiguration.updatedCertFileNames.IndividualCSV
            : wifiCertFileDetails && wifiCertFileDetails.IndividualCSV,
      });
    }
  }
  return {
    wifiSettingsCertificate,
    wifiSettingsDelete: {
      required: false,
      component: 'span',
      key: 'wifiSettingsDelete',
      name: 'wifiSettingsDelete',
      id: 'deleted_wifi_settings',
      label: 'deleted_wifi_settings',
      style: { height: '70px', width: '100%', overflow: 'auto', resize: 'vertical' },
      render: () => dynamicData.changePolicyWifiCertDeleteList.join(', ') || '--',
    },
  };
}
