/**
 * IDSearchPanel is used to load the ID searchpanel of IDList screen
 * @author: P.Vinothpandian
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBButton from '../../../common/component/sbbutton/SBButton';

/**
 * tableSearchList is used to load the  table search list
 * @param dynamicData
 * @param reference
 * @return tableSearchList
 */

function tableSearchList(dynamicData, reference) {
  return [
    {
      label: 'dm_code',
      component: 'textbox',
      type: 'text',
      size: 20,
      maxLength: 20,
      id: 'dmcode',
      name: 'dmcode',
      key: 'dmCode',
      errorMessage: dynamicData.dmCodeError,
      style: { width: 220 },
      ref: reference.dmCodeRef,
    },
    {
      label: 'login_id',
      component: 'textbox',
      type: 'text',
      size: 20,
      maxLength: 20,
      id: 'login-id-text-input',
      name: 'name',
      key: 'loginId',
      errorMessage: dynamicData.loginError,
      style: { width: 220 },
      ref: reference.loginIdRef,
    },
  ];
}

class IDSearchPanel extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const {
      errorMessage,
      searchSource,
      onHandleClick,
      onTableFrameEvent,
      onKeyDown,
      reference,
    } = this.props;
    return (
      <div className="contents-main">
        <div className="border p15">
          <div>
            <CustomTableFrame
              tableList={tableSearchList(errorMessage, reference)}
              dataSource={searchSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => onKeyDown(event, value)}
            />
          </div>
          <div className="clearfix">
            <div className="floatleft">
              <span className="red bold">
                {this.languageLabelCheck(errorMessage.noConditionsError)}
              </span>
            </div>
            <div className="floatright">
              <SBButton
                id="search"
                name="search"
                type="button"
                title="search"
                color="green"
                onClick={() => onHandleClick('search')}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

IDSearchPanel.propTypes = {
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  onHandleClick: PropTypes.func.isRequired,
  onTableFrameEvent: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  searchSource: PropTypes.objectOf(PropTypes.string).isRequired,
  errorMessage: PropTypes.objectOf(PropTypes.string).isRequired,
  reference: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */

IDSearchPanel.defaultProps = {
  isNotMultiLang: false,
  reference: {},
};

export default connect(mapStateToProps)(withRouter(IDSearchPanel));
