import { COMMON_CHANGE, COMMON_ON_SWITCH } from '../../actions/ActionTypes';

const initialState = {
  isModified: false,
  open: false,
  onOk: () => {},
  onOkEvent: null,
  onCancel: () => {},
  onCancelEvent: null,
  data: {},
};

const CommonChangeReducer = (state = initialState, action) => {
  switch (action.type) {
    case COMMON_CHANGE:
      return { ...state, isModified: action.isModified };
    case COMMON_ON_SWITCH:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};

export default CommonChangeReducer;
