import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';

class SBTabs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: this.setActiveTab(props),
    };
  }

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  setActiveTab = props => {
    if (props.activeKey) {
      return props.activeKey;
    }
    if (props.children) {
      return props.children[0].key;
    }
    return '';
  };

  onClickTabItem = tab => {
    const { onClick } = this.props;
    this.setState({ activeTab: tab });
    if (onClick) {
      onClick(tab);
    }
  };

  render() {
    const { children, headerStyle } = this.props;
    const { activeTab } = this.state;
    return (
      <div className="mb10" style={{ display: 'flow-root' }}>
        <ul className="bcdm-tab clearfix">
          {children &&
            children.map((child, index) => {
              return (
                <li
                  className="center"
                  style={headerStyle}
                  key={index.toString()}
                  onClick={() => this.onClickTabItem(child.key)}
                  role="presentation"
                >
                  <span className={activeTab === child.key ? 'selected' : ''}>
                    {this.languageLabelCheck(child.props.label)}
                  </span>
                </li>
              );
            })}
        </ul>
        <div className="bcdm-tab-panel">
          {children &&
            children.map(child => {
              return activeTab === child.key ? child.props.children : '';
            })}
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(SBTabs);
