import React, { PureComponent } from 'react';
import { Route } from 'react-router-dom';
import PropTypes from 'prop-types';

/**
 * SBRoute used to navigate the screen to respective path
 */
class SBRoute extends PureComponent {
  render() {
    const { ScreenName, path, attribute } = this.props;
    return (
      <Route strict exact path={path} render={props => <ScreenName {...props} {...attribute} />} />
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
SBRoute.propTypes = {
  ScreenName: PropTypes.func.isRequired,
  path: PropTypes.string.isRequired,
  attribute: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number])),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
SBRoute.defaultProps = {
  attribute: {},
};

export default SBRoute;
