import React, { Component,createRef } from 'react';
import { connect } from 'react-redux';
import { CompactPicker } from 'react-color'
import { isNull } from 'underscore';
import { get, post ,performDelete} from '../../actions/commonActions';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../common/component/tableLayout/SBTableLayout';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import SBInfoIcon from '../../common/component/sbicons/SBInfoIcon';
import APIENDPOINTS from '../../constants/APIEndPoints';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import SBRadio from '../../common/component/sbradio/SBRadio';

const fileExtension = ['bmp', 'gif', 'ico', 'jpeg', 'png', 'webp','wbmp','dng'];
const mode = { CREATE:'create',UPDATE : 'update',DELETE : 'delete'};
class EnterpriseMain extends Component {
  constructor(props) {
    super(props);
    const { location } = this.props;
    const { enterpriseToken, pmcolor, url } = this.getQueryParams(location.search);
    this.state = {
      enterpriseName: null,
      enterpriseToken,
      initialColor: '#ffffff',
      primaryColor: pmcolor || null,
      logoUrl: url || null,
      pageLoading: true,
      updateDisable:true,
      openErrorDialog:false,
      invalidLogoUrl : false,
      noOfDevices : 0,
      openDelete:false,
      dmCode:null,
      openInfo:false,
      showColorPicker : false,
      radioValue:'none'
    };
    this.currentMode = null; 
    this.logoInputRefs = createRef();
    }

  async componentDidMount() {
    const { location, history,setModified } = this.props;
    const { enterpriseToken } = this.state;
    let { primaryColor, logoUrl,dmCode } = this.state;
    try {
      const response = await (location.search
        ? post(APIENDPOINTS.CREATEENTERPRISE, { enterpriseToken, primaryColor, logoUrl })
        : get(APIENDPOINTS.GETENTERPRISE));
        if(location.search) {
          history.push(window.location.pathname)
        }
      const data = await response.json();
      if(data.existing_enterprise){
        this.currentMode = mode.CREATE;
        this.setState({ pageLoading: false,openErrorDialog:true});
        return;
      }
      const enterpriseName = data.enterpriseName || null;
      primaryColor = data.primaryColor || primaryColor;
      logoUrl = data.logoUrl || logoUrl;
      dmCode = data.dmCode;
      this.setState({ pageLoading: false, enterpriseName, data, primaryColor, logoUrl,dmCode });
      this.setColor(primaryColor);
      if(data.primaryColor){
       this.setState({radioValue:'color',showColorPicker:true});
      }else{
        this.setState({radioValue:'none',showColorPicker:false});
      }
      setModified(false);
      this.logoInputRefs.current.focus();
      this.currentMode = data && data.length > 0 ? mode.UPDATE:mode.CREATE;
    } catch (Exception) {
      history.push('error');
    }
  }

  /**
   * handle Create Enterprise is used to create the enterprise.
   */
  handleCreateEnterprise = async () => {
    const { history,setModified } = this.props;
    let { primaryColor } = this.state;
     const { logoUrl,radioValue } = this.state;
    const windowOrigin = window.location.origin;
    primaryColor = (radioValue === 'none')?null:primaryColor;
    try {
      if(this.isValidLogoUrl()){
        setModified(false);
      const response = await post(APIENDPOINTS.ENTERPRISESIGNUP, { windowOrigin, primaryColor, logoUrl });
      const data = await response.text();
      window.location.href = data;
      }else{
        this.setState({invalidLogoUrl:true});
        this.logoInputRefs.current.focus();
      }
    } catch (Exception) {
      history.push('error');
    }
  };

  /**
   * handleUpdateEnterprise is used to update the enterprise 
   */ 
  handleUpdateEnterprise = async () => {
    const { history,setModified } = this.props;
    const { enterpriseName, logoUrl,radioValue } = this.state;
    let {primaryColor}=this.state;
    primaryColor = (radioValue === 'none')?null:primaryColor;
    this.currentMode = mode.UPDATE;
    try {
      if(this.isValidLogoUrl()){
      this.setState({ pageLoading: true });
     const response = await post(APIENDPOINTS.UPDATEENTERPRISE, { enterpriseName, primaryColor, logoUrl });
      const status = await response.text();
      if(status === 'success'){
      this.setState({ pageLoading: false,updateDisable:true,invalidLogoUrl:false });
      setModified(false);
      }else{
        this.setState({openErrorDialog:true, pageLoading: false,invalidLogoUrl:false});
      }
      this.setState({noOfDevices:0});
      this.setColor(primaryColor);
      }else{
        this.setState({invalidLogoUrl:true});
      }
      this.logoInputRefs.current.focus();
    } catch (Exception) {
      history.push('error');
    }
  };

  /**
   * Handle delete enterprise is used to delete the enterprise.
   */

  handleDeleteEnterprise = async () => {
    const { history,setModified } = this.props;
    const { dmCode } = this.state;
    this.currentMode = mode.DELETE;
    try {
      this.setState({ pageLoading: true });
     const response = await performDelete(`${APIENDPOINTS.DELETEENTERPRISE}/${dmCode}`);
      const status = await response.text();
      if(status === 'success'){
      this.setState({ pageLoading: false, enterpriseName:'',openDelete:false,
      primaryColor:null,logoUrl:null,radioValue:'none',showColorPicker:false});
      setModified(false);
      }else{
        this.setState({openErrorDialog:true, pageLoading: false,openDelete:false,invalidLogoUrl:false});
      }
      const {primaryColor} = this.state; 
      this.setColor(primaryColor);
      this.logoInputRefs.current.focus();
       
    } catch (Exception) {
      history.push('error');
    }
  };


  

  /**
   * getQueryParams is used to get the parameters from the url
   * @param {object} location 
   * @returns {object}
   */
  getQueryParams = (location) => {
    const params = new URLSearchParams(location);
    return {
      'enterpriseToken': params.get('enterpriseToken'),
      'pmcolor': params.get('primaryColor'),
      'url': params.get('logoUrl')
    }
  };

/**
 * ValidateToDelete is validate the enterprise can be deleted or not.
 */
  validateToDelete = async () => {
    const { history } = this.props;
    const { enterpriseName} = this.state;
    const enterpriseId = enterpriseName.split('/').pop();
    try {
     const response = await post(APIENDPOINTS.NO_OF_DEVICES_IN_ENTERPRISE,{enterpriseId});
      const count = await response.text();
      if(count  > 0){
        this.setState({ noOfDevices: count });
      }else{
        this.setState({noOfDevices:0, openDelete: true});
      }
    } catch (Exception) {
      history.push('error');
    }
  }

   /**
    * isValidLogoUrl is used to check whether the logourl is valid
    * @returns {boolean}
    */
   isValidLogoUrl = () =>{
     let { logoUrl } = this.state;
     if(isNull(logoUrl) || logoUrl === '') return true;
     try{
      logoUrl = new URL(logoUrl);
      if(!(logoUrl.protocol === 'https:' || logoUrl.protocol === 'http:')){
           return false;
      }
      const fileType = logoUrl.toString().split('.').pop();
     return fileExtension.some(types => types === fileType);
     }catch(error){
         return false; 
     }
    }

    showColorPicker = (value) => {
      const { setModified } = this.props;
      setModified(true);
      const {initialColor} = this.state;
       this.setState({updateDisable:false,showColorPicker : value === 'color',
      radioValue:value,primaryColor:(value === 'none'?null:initialColor)});
      this.setColor((value === 'none'?null:initialColor));
    }

  /**
   * makeColorPickerAndUrl is used to render color picker 
   *  @returns {Array}
   */
  makeColorPickerAndUrl = () => {
    const { primaryColor, initialColor, logoUrl,invalidLogoUrl,showColorPicker,radioValue } = this.state;
    return [
      {
        key: 'url_logo',
        value: (
          <>
            <div style={{display:'inline-grid'}}>
              <div>
                <SBTextbox
                  type="text"
                  maxlength="100"
                  size="45"
                  id="url_logo"
                  name="url_logo"
                  onChange={event => this.logoData(event)}
                  required="required"
                  value={logoUrl}
                  ref={this.logoInputRefs}
                />
                <SBInfoIcon id="logo_info" onClick={() => this.setState({openInfo:true})} />
              </div>
              {invalidLogoUrl && (
              <font id="lang_name_msg" className="red bold">
                {this.languageLabelCheck('invalid_url')}
              </font>
              )
              }
            </div>
          </>
        )
      },
      {
        key: 'color_input',
        value: (
          <>
            <SBRadio
              options={[{ key: 'none', value: 'none' }, { key: 'color', value: 'other_color' }]}
              defaultValue="none"
              filterKey="key" 
              filterValue="value"
              onChange={(event,value) => this.showColorPicker(value)}
              value={radioValue}
            />
            <div style={showColorPicker ? {display:'block'}:{display:'none'}}>
              <SBTextbox
                id="color_input"
                disabled
                size='10'
                name='color_input'
              />
              <div id='compact-picker' style={{ display: 'inline-table', top: '-5' }}>
                <CompactPicker
                  color={primaryColor || initialColor}
                  onChange={this.handleColorChange}
                />
              </div>
            </div>
          </>
        )
      }
    ]
  }

  
  /**
   * HandleColorChange is used to the change the color on the input 
   * 
   * @param {String} color 
   */
  handleColorChange = (color) => {
    const { setModified } = this.props;
    setModified(true);
    const colorInput = document.getElementById('color_input');
    colorInput.style.backgroundColor = color.hex;
    this.setState({ primaryColor: color.hex,updateDisable:false });
  };

  /**
   * logodata is used to set the logo url in the state of the component
   * @param {object} event 
   */
  logoData = (event) => {
    const { value } = event.target;
    const { setModified } = this.props;
    setModified(true);
    this.setState({ logoUrl: value,invalidLogoUrl:false,updateDisable:false });
  }

  /**
   * afterOnOkClick is to handle ok button event
   */
  afterOnOkClick = () => {
    this.setState({ openErrorDialog: false });
  };

  /**
   * setColor is used to set the background color on the text input
   * @param {String} primaryColor 
   */
  setColor = (primaryColor) => {
    const { initialColor } = this.state;
    const colorInput = document.getElementById('color_input');
    if(primaryColor){
      colorInput.style.backgroundColor = primaryColor;
    }else{
      colorInput.style.backgroundColor = initialColor;
    }
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = label => {
    const {  localeData } = this.props;
    return   localeData[label] || label;
  };



  


  getErrorMsg = () => {
    if(this.currentMode === mode.CREATE){
      return {
        header:'create_failed',
        content:'create_enterprise_error'
      }
    }
    return {
      header:this.currentMode === mode.UPDATE ? 'update_failed':'delete_failed',
      content:this.currentMode === mode.UPDATE ? 'failed_to_update':'failed_to_delete'
    }
  };

   /**
   * makeTable is used to make the table components and render with them data
   * @returns {array}
   */
  makeTable() {
    const { data } = this.state;
    const renderComponents = this.makeColorPickerAndUrl();
    return [
      { key: 'dm_code', value: data.dmCode },
      { key: 'enterprise_name', value: data.enterpriseName },
      { key: 'enterprise_token', value: data.enterpriseToken},
      { key: 'url_logo', value: renderComponents[0].value },
      { key: 'color_input', value: renderComponents[1].value },
      { key: 'created_by', value: data.createdBy },
      { key: 'created_date', value: (data.createdDate) }
    ];
  }


  render() {
    const { enterpriseName, pageLoading,updateDisable,openErrorDialog,openDelete,noOfDevices,openInfo} = this.state;
    const errorMsgObj = openErrorDialog?this.getErrorMsg():{};
    return (
      <div id="enterprise">
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <SBContentHeader title="enterprise" description={['android_enterprise_organisation']} />
          <div style={{ display: 'flex', alignItems: 'center' }}>
            {!enterpriseName ? (
              <div>
                <SBTableLayout tableData={this.makeColorPickerAndUrl()} filterKey="key" filterValue="value" />
                <SBButton
                  type="button"
                  name="create_enterprise"
                  color="gray"
                  title="create_enterprise"
                  onClick={() => this.handleCreateEnterprise()}
                />
              </div>
                ) : (
                  <div>
                    <SBTableLayout tableData={this.makeTable()} filterKey="key" filterValue="value" />
                    <SBButton
                      type="button"
                      name="update_enterprise"
                      color="gray"
                      title="update_enterprise"
                      onClick={() => this.handleUpdateEnterprise()}
                      disabled={updateDisable}
                    />
                    <SBButton
                      type="button"
                      name="delete_enterprise"
                      color="gray"
                      title="delete_enterprise"
                      onClick={() => this.validateToDelete()}
                    />
                    {noOfDevices > 0 && (
                      <font id="lang_name_msg" className="red bold">
                        {this.languageLabelCheck('device_link_enterprise')}
                      </font>
                    )}
                  </div>
                  )}
          </div>
          <SBDialog
            open={openErrorDialog}
            header={errorMsgObj.header}
            type='error'
            content={errorMsgObj.content}
            onOk={() => this.afterOnOkClick()}
            width="400px"
            okButtonTitle="ok"
          />
          <SBDialog
            open={openDelete}
            header="delete_confirmation"
            type="confirmation"
            content='do_you_want_delete_the_enterprise'
            onCancel={() => {this.setState({ openDelete: false });}}
            onOk={this.handleDeleteEnterprise}
            width="400px"
          />
          <SBDialog
            open={openInfo}
            header='information'
            content='logo_url_info'
            onOk={() => {this.setState({ openInfo: false });}}
            width="400px"
            type='info'
            okButtonTitle="ok"
          />

            
        </CircularSpinner>
      </div>
        );
  }
}


/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = state => {
  return {
    localeData: state.language.data
    }
  };

  /**
 * mapDispatchToProps is to map store fields to props.
 * @param dispatch
 */
const mapDispatchToProps = dispatch => {
  return {
  setModified: (isModified) => {
    dispatch(commonChangeAction(isModified));
  }
}
};

export default connect(mapStateToProps,mapDispatchToProps)(EnterpriseMain);
