/**
 * DashboardSettingFieldConstants is used to load the field values
 * @author: Aakash Alagarsamy
 */
import {
  tableUpdateDeviceInfoListView,
  tableUpdateDeviceInfoList,
} from './DashboardSettingTableConstants';

/**
 * emptyDataSource is used to load the initial DataSource
 * @return emptyDataSourceMap
 */
export function emptyDataSource() {
  return {
    autoDashboardCollection: false,
    autoCollectionPeriod: undefined,
  };
}

/**
 * setTableView is used to load the table in dashboard setting screen
 * @param mode
 * @param dynamicData
 * @param reference
 * @param dataSource
 * @return emptyDataSourceMap
 */
export function setTableView(mode, dynamicData, reference, dataSource) {
  if (mode !== 'update')
    return {
      updateDeviceInfoList: tableUpdateDeviceInfoListView(dynamicData, reference),
    };

  return {
    updateDeviceInfoList: tableUpdateDeviceInfoList(dynamicData, reference, dataSource),
  };
}
