import React from 'react';
import { connect } from 'react-redux';
import isUndefined from 'lodash/isUndefined';
import {systemAdminMenuBarData} from '../../constants/menuList';
import checkLanguage from '../../language/checkLanguage';
/** SBComponents */
import SBLink from '../../common/component/sblink/SBLink';

const titleContent ={
    idmanagement:'id_management_subtitle',
    appmanagement:'app_management_subtitle',
    sysmanagement:'system_management_subtitle',
    languagesetting:'settings_subtitle'
}



const DashboardAdmin = (props) => {
   
/**
   * languageLabelCheck is used to get the label based on locale.
   * @param {String} label 
   */

   const languageLabelCheck = (label) => {
    const { localeData } = props;
    return checkLanguage(localeData, label);
  };


    const { authorityId } = props;
    return(
      <>
        <div className="row profile-management">
          {systemAdminMenuBarData
          .filter(menu => menu.key !== 'home')
          .map( data => (
            <div key='cols' className="three columns">
              <h2 className="title-corner">
                <font style={{verticalAlign:'inherit'}}>
                  <font style={{verticalAlign:'inherit'}}>
                    {languageLabelCheck(data.label)}
                  </font>
                </font>
              </h2>
              <div className="explain mb10" style={{height: '13px'}}>
                <p>
                  <font style={{verticalAlign:'inherit'}}>
                    <font style={{verticalAlign:'inherit'}}>
                      {languageLabelCheck(titleContent[data.key])}
                    </font>
                  </font>
                </p>
              </div>
            
              {data.nested.filter( entry => isUndefined(entry.authorityRoleType) || entry.authorityRoleType.includes(authorityId))
                .map(menu => (
                  <div className="list">
                    <ul>
                      <li key='lists'>
                        <SBLink content={languageLabelCheck(menu.label)} pathname={menu.path} />
                      </li>
                    </ul>
                  </div>
                )
                )}
           
            </div>
      )
      )}
        </div>
      </>
    )
}


/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  return {
    localeData: state.language.data
  };
};

export default connect(mapStateToProps)(DashboardAdmin);