/**
 * operationInfo is used to load Operational information page
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { setAppRegisterEntity, setAppUpdateEntity } from '../../../actions/TenantActions';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import OperationUpload from './OperationUpload';
import AppCRUDFooter from './AppCRUDFooter';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import { isNotUndefinedOrNotEmpty } from '../../../common/CommonValidations/CommonValidations';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import {
  indexMap,
  tableSpecialNote,
  tableInfoList,
  tableAttachList,
  tableIndividualUpload,
  tableIndentifyList,
  tableSubstituteList,
  tableIndividualList,
  tableReportList,
} from '../Constants/AppFrameConstants';
import {
  setInfoIndex,
  setSpecialIndex,
  operationKeys,
  setFileDataSource,
} from '../Constants/operationalConstants';
import { setOperationalInitialValues } from '../Constants/AppFieldConstants';
import {
  validateInputs,
  setFocusinerrorField,
  validateFile,
} from '../Constants/OperationValidations';
import { setOperationalReference } from '../Constants/AppReferenceConstants';
import { setValidationErrorMessage } from '../Constants/AppErrorConstants';
import { CommonConstants } from '../../../constants/CommonConstants';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../Constants/AppManageHeaderConstants';
import checkLanguage from '../../../language/checkLanguage';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBButton from '../../../common/component/sbbutton/SBButton';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

const fileDownload = require('js-file-download');

class operationInfo extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('operationinfo'),
      operationSource: setOperationalInitialValues(param),
      pageLoading: true,
      dynamicData: {},
      focusKey: 'fullName1Ref',
      infoIndex: setInfoIndex(param),
      specialIndex: setSpecialIndex(param),
      reference: setOperationalReference(),
      modified: false,
      fileDataSource: setFileDataSource(),
      fileKey: '',
      dialogHeader: '',
      dialogContent: '',
      dialogType: '',
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount is used to change the state of page loading
   */

  componentDidMount() {
    this.setState({ pageLoading: false });
  }

  /**
   * componentDidUpdate is used to set focus based on the key provided
   */

  componentDidUpdate() {
    const { focusKey, reference } = this.state;
    if (isNotUndefinedOrNotEmpty(focusKey) && reference[focusKey].current) {
      reference[focusKey].current.focus();
      this.setState({ focusKey: '' });
    }
  }

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    if (key === 'attachDataFile' || key === 'individualDataFile') {
      this.getUploadedFile(key, event);
    } else if (key === 'operationalUpload' || key === 'individualUpload') {
      this.onUploadClick(key, key === 'operationalUpload' ? 'attachRef' : 'individualRef');
    } else {
      const { operationSource } = this.state;
      operationSource[key] = event.target.type === 'textarea' ? event.target.value : value;
      this.setState({ operationSource, modified: true });
    }
  };

  /**
   * getUploadedFile is used to get the uploaded file from input field
   * @param key
   * @param event
   */
  getUploadedFile = (key, event) => {
    const { setModified } = this.props;
    const { fileDataSource } = this.state;
    [fileDataSource[key]] = event.target.files;
    this.setState({ fileDataSource, modified: true });
    setModified(true);
  };

  /**
   * onUploadClick is used to make the data ready to save in database
   */

  onUploadClick = (buttonKey, referenceKey) => {
    const { fileDataSource, operationSource, reference } = this.state;
    let { dynamicData } = this.state;
    dynamicData = {};
    const key = buttonKey === 'individualUpload' ? 'individualDataFile' : 'attachDataFile';
    const attachmentFileResult = validateFile(
      fileDataSource[key],
      buttonKey === 'individualUpload' ? 16 : 5,
    );
    if (attachmentFileResult.status) {
      const dateTimeKey =
        buttonKey === 'individualUpload' ? 'individualFileUploadedDate' : 'attachFileUploadedDate';
      operationSource[key] = fileDataSource[key];
      operationSource[dateTimeKey] = new Date();
      operationSource[buttonKey === 'individualUpload' ? 'individualFileName' : 'attachFileName'] =
        fileDataSource[key].name;
      fileDataSource[key] = null;
      this.setState({ dynamicData, operationSource, fileDataSource });
    } else {
      fileDataSource[key] = null;
      if (attachmentFileResult.error === 'nofile') {
        this.setState({
          dynamicData,
          fileDataSource,
          dialogHeader: 'error',
          dialogContent: 'upload_no_file',
          dialogType: 'error',
          modalIsOpen: true,
        });
      } else {
        dynamicData = setValidationErrorMessage(
          buttonKey === 'individualUpload' ? 'individualUploadError' : 'attachmentUploadError',
          attachmentFileResult,
          dynamicData,
        );
        this.setState({ dynamicData, fileDataSource });
      }
    }
    if (reference[referenceKey].current) {
      reference[referenceKey].current.value = '';
    }
  };

  /**
   * downloadFile is used to handle download button click
   * @param key
   */

  downloadFile = key => {
    const { operationSource } = this.state;
    const fileToDownload = operationSource[key];
    const fileName =
      key === 'individualDataFile'
        ? operationSource.individualFileName
        : operationSource.attachFileName;
    fileDownload(fileToDownload, fileName);
  };

  /**
   * removeFile is used to handle remove the file uploaded
   * @param key
   */

  removeFile = key => {
    this.setState({
      modalIsOpen: true,
      dialogHeader: 'delete_confirmation',
      dialogContent: 'is_it_ok_to_delete',
      dialogType: 'confirmation',
      fileKey: key,
    });
  };

  /**
   * onHandleClick is used to handle click event based on key
   * @param key
   */

  onHandleClick = key => {
    switch (key) {
      case 'add':
        this.incrementInfo();
        break;
      case 'delete':
        this.decrementInfo();
        break;
      case 'specialadd':
        this.incrementSpecial();
        break;
      case 'specialdelete':
        this.decrementSpecial();
        break;
      case 'attachDataFile':
      case 'individualDataFile':
        this.removeFile(key);
        break;
      case 'save':
      case 'cancel':
        this.callService(key);
        break;
      default:
        break;
    }
  };

  /**
   * generateParam is used to manipulate data that is reworked
   * @param globalData
   */

  generateParam = globalData => {
    const param = globalData;
    const { operationSource } = this.state;
    const mainkeys = operationKeys();
    mainkeys.forEach(value => {
      param[value] = operationSource[value];
    });
    return param;
  };

  /**
   * setdataToParam is used to set data to param
   */

  setdataToParam = () => {
    const {
      location,
      history,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { modified } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    }
    param = this.generateParam(param);
    if (location.state.isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: modified || location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: modified || location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * callService is used to call service based on key
   * @param key
   */

  callService = key => {
    const {
      location,
      history,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { modified } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    }
    if (key === 'save') {
      const errorStatus = this.validateData();
      if (errorStatus) {
        this.setdataToParam();
      }
    } else if (modified) {
      this.setState({
        modalIsOpen: true,
        dialogHeader: 'cancel_confirmation',
        dialogContent: 'cancel_confirmation_message',
        dialogType: 'confirmation',
      });
    } else if (location.state.isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * onOkDialog is used to handle on ok click of dialog
   */

  onOkDialog = () => {
    const { dialogType, fileKey, reference } = this.state;
    if (
      dialogType === 'confirmation' &&
      (fileKey === 'individualDataFile' || fileKey === 'attachDataFile')
      ) {
      const { setModified } = this.props;
      const { operationSource } = this.state;
      operationSource[fileKey] = null;
      operationSource[
        fileKey === 'individualDataFile' ? 'individualFileUploadedDate' : 'attachFileUploadedDate'
      ] = null;
      operationSource[
        fileKey === 'individualDataFile' ? 'individualFileName' : 'attachFileName'
      ] = null;
      this.setState({ operationSource, modified: true, fileKey: '', modalIsOpen: false });
      if (reference[fileKey === 'attachDataFile' ? 'attachRef' : 'individualRef'].current) {
        reference[fileKey === 'attachDataFile' ? 'attachRef' : 'individualRef'].current.focus();
      }
      setModified(true);
    } else if (dialogType === 'confirmation') {
      const {
        location,
        history,
        setModified,
        setAppRegisterEntity: makeAppRegisterEntity,
        setAppUpdateEntity: makeAppUpdateEntity,
      } = this.props;
      let param = {};
      if (location.state) {
        param = location.state.dataSource;
      }
      setModified(location.state.globalModified);
      if (location.state.isUpdate) {
        makeAppUpdateEntity({
          param,
          globalModified: location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
      } else {
        makeAppRegisterEntity({
          param,
          autoGenertedDmCode: location.state.autoGenertedDmCode,
          globalModified: location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
      }
    } else {
      this.setState({ modalIsOpen: false });
    }
  };

  /**
   * incrementInfo is used to handle add info click
   */

  incrementInfo = () => {
    const { infoIndex } = this.state;
    this.setState({ infoIndex: infoIndex + 1, focusKey: `fullName${infoIndex + 1}Ref` });
  };

  /**
   * decrementInfo is used to handle delete info click
   */

  decrementInfo = () => {
    const { infoIndex, operationSource, dynamicData } = this.state;
    operationSource[`fullName${indexMap[infoIndex]}`] = '';
    operationSource[`contactInfo${indexMap[infoIndex]}`] = '';
    operationSource[`email${indexMap[infoIndex]}`] = '';
    operationSource[`departmentName${indexMap[infoIndex]}`] = '';
    dynamicData[`fullName${infoIndex}Error`] = '';
    dynamicData[`contactInfo${infoIndex}Error`] = '';
    dynamicData[`departmentName${infoIndex}Error`] = '';
    dynamicData[`email${infoIndex}Error`] = '';
    this.setState({
      infoIndex: infoIndex - 1,
      operationSource,
      dynamicData,
      focusKey: infoIndex - 1 === 1 ? 'addInfoRef' : '',
    });
  };

  /**
   * incrementSpecial is used to handle add special info click
   */

  incrementSpecial = () => {
    const { specialIndex } = this.state;
    this.setState({
      specialIndex: specialIndex + 1,
      focusKey: `operationMemo${specialIndex + 1}Ref`,
    });
  };

  /**
   * decrementSpecial is used to handle delete special info click
   */

  decrementSpecial = () => {
    const { specialIndex, operationSource, dynamicData } = this.state;
    dynamicData[`operationMemo${specialIndex}Error`] = '';
    operationSource[`operationMemo${indexMap[specialIndex]}`] = '';
    this.setState({
      specialIndex: specialIndex - 1,
      operationSource,
      dynamicData,
      focusKey: specialIndex - 1 === 1 ? 'addSpecialRef' : '',
    });
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * validateData is used to validate data
   * @return validateData
   *
   */

  validateData() {
    const { operationSource, reference } = this.state;
    const validationResult = validateInputs(operationSource);
    this.setState({
      dynamicData: validationResult,
    });
    setFocusinerrorField(validationResult, reference);
    return validationResult.status;
  }

  /**
   * render is used to render the component
   * @return operationInfoComponent
   */

  render() {
    const {
      headerData,
      infoIndex,
      dynamicData,
      operationSource,
      footer,
      pageLoading,
      reference,
      specialIndex,
      modalIsOpen,
      dialogHeader,
      dialogContent,
      dialogType,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="border p15 mb10">
              <div>
                <SBTableHeader headerText="operator_information" />
                <CustomTableFrame
                  tableList={tableInfoList(dynamicData, reference, infoIndex)}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
                <div className="clearfix mb10">
                  <div className="floatright">
                    <SBButton
                      type="button"
                      name="btn_spl_Add"
                      id="btn_spl_Add"
                      title="add"
                      color="green"
                      ref={reference.addInfoRef}
                      style={infoIndex === 3 ? { display: 'none' } : {}}
                      onClick={() => this.onHandleClick('add')}
                    />
                    <SBButton
                      type="button"
                      name="btn_spl_Delete"
                      id="btn_spl_Delete"
                      title="delete"
                      style={infoIndex === 1 ? { display: 'none' } : {}}
                      onClick={() => this.onHandleClick('delete')}
                    />
                  </div>
                </div>
              </div>
              <div>
                <SBTableHeader headerText="operational_identification" />
                <CustomTableFrame
                  tableList={tableIndentifyList(dynamicData, reference)}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
              </div>
              <div>
                <SBTableHeader headerText="substitute_operation" />
                <CustomTableFrame
                  tableList={tableSubstituteList(dynamicData, reference)}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
              </div>
              <div>
                <SBTableHeader headerText="report_operation" />
                <CustomTableFrame
                  tableList={tableReportList}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
              </div>
              <div>
                <SBTableHeader headerText="operation_attached_data" />
                <CustomTableFrame
                  tableList={tableAttachList(dynamicData, reference)}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
                {operationSource && operationSource.attachFileName ? (
                  <OperationUpload
                    fileName={operationSource.attachFileName}
                    dateTime={operationSource.attachFileUploadedDate}
                    onHandleClick={() => this.onHandleClick('attachDataFile')}
                    onURLClick={() => this.downloadFile('attachDataFile')}
                  />
                ) : null}
              </div>
              <div>
                <SBTableHeader headerText="operation_other_individual" />
                <CustomTableFrame
                  tableList={tableIndividualList()}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
                <CustomTableFrame
                  tableList={tableSpecialNote(specialIndex, dynamicData, reference)}
                  dataSource={operationSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
                {operationSource && operationSource.existenceSpecialNotes ? (
                  <>
                    <div className="clearfix mb10">
                      <div className="floatright">
                        <SBButton
                          type="button"
                          name="btnAdd"
                          id="btnAdd"
                          title="add"
                          color="green"
                          ref={reference.addSpecialRef}
                          style={specialIndex === 5 ? { display: 'none' } : {}}
                          onClick={() => this.onHandleClick('specialadd')}
                        />
                        <SBButton
                          type="button"
                          name="btnDelete"
                          id="btnDelete"
                          title="delete"
                          style={specialIndex === 1 ? { display: 'none' } : {}}
                          onClick={() => this.onHandleClick('specialdelete')}
                        />
                      </div>
                    </div>
                    <CustomTableFrame
                      tableList={tableIndividualUpload(dynamicData, reference)}
                      dataSource={operationSource}
                      defaultValue={{}}
                      onTableFrameEvent={(key, event, value) =>
                        this.onTableFrameEvent(key, event, value)
                      }
                    />
                    {operationSource && operationSource.individualFileName ? (
                      <OperationUpload
                        fileName={operationSource.individualFileName}
                        dateTime={operationSource.individualFileUploadedDate}
                        onHandleClick={() => this.onHandleClick('individualDataFile')}
                        onURLClick={() => this.downloadFile('individualDataFile')}
                      />
                    ) : null}
                  </>
                ) : null}
              </div>
            </div>
            <div>
              <AppCRUDFooter 
                reference={reference} 
                footer={footer} 
                onHandleClick={key => this.onHandleClick(key)} 
              />
            </div>

            <SBDialog
              open={modalIsOpen}
              header={dialogHeader}
              type={dialogType}
              content={dialogContent}
              okButtonTitle={dialogType === 'error' ? 'ok' : 'yes'}
              onCancel={() => {
                this.setState({ modalIsOpen: false, fileKey: '' });
              }}
              onOk={() => {
                this.onOkDialog();
              }}
              width="400px"
            />
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    setAppRegisterEntity: data => {
      dispatch(setAppRegisterEntity(data));
    },
    setAppUpdateEntity: data => {
      dispatch(setAppUpdateEntity(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

operationInfo.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dataSource: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
      globalModified: PropTypes.bool.isRequired,
      autoGenertedDmCode: PropTypes.string.isRequired,
    }),
  }).isRequired,
  setAppRegisterEntity: PropTypes.func.isRequired,
  setAppUpdateEntity: PropTypes.func.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
operationInfo.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(operationInfo));
