/**
 * TableConstants is used to load the table frame with its components on screen
 * @author: P.Vinothpandian, A.Aakash
 */

/**
 * filterData is used to load the field values of corresponding radio or combo box
 */

export const filterData = {
  yesorno: [
    { filterKey: true, filterValue: 'on_setting_value' },
    { filterKey: false, filterValue: 'off_setting_value' },
  ],
};

export function tableDeviceListView() {
  return [
    {
      label: 'mail_notify_device',
      component: 'span',
      key: 'mailNotifyDeviceWaiting',
      hasLang: true,
      render: (value) => (value.mailNotifyDeviceWaiting ? 'on_setting_value' : 'off_setting_value'),
    },
    {
      label: 'mail_notify_device',
      component: 'span',
      key: 'mailNotifyDeviceWaitingDays',
      dynamicLabel: 'notify_if_unanswered_over_days_value',
      hasLang: true,
      render: (value) => value.mailNotifyDeviceWaitingDays,
      trStyle: (value) => (value.mailNotifyDeviceWaiting ? {} : { display: 'none' }),
    },
  ];
}

export function tableDeviceList(dynamicData, reference, dataSource) {
  return [
    {
      label: 'mail_notify_device',
      component: 'radio',
      name: 'mail_notify_device',
      key: 'mailNotifyDeviceWaiting',
      defaultValue: 1,
      options: filterData.yesorno,
      filterKey: 'filterKey',
      filterValue: 'filterValue',
      canModify: true,
      render: (value) => value.mailNotifyDeviceWaiting,
      buttonStyle: 'true',
    },
    {
      label: 'mail_notify_device',
      component: 'textboxwithspan',
      type: 'text',
      size: 40,
      maxLength: 3,
      id: 'mail_notify_device',
      name: 'mail_notify_device',
      key: 'mailNotifyDeviceWaitingDays',
      canModify: true,
      errorMessage: dynamicData.mailNotifyDeviceWaitingDaysError,
      style: { width: 74 },
      ref: reference.mailNotifyDeviceWaitingDaysRef,
      hasLang: true,
      trStyle: () => (dataSource.mailNotifyDeviceWaiting ? {} : { display: 'none' }),
      renderSpan: () => 'notify_if_unanswered_over_days',
    },
  ];
}

