import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import { isUndefined } from 'underscore';
import SBTable from '../../common/component/sbtable/SBTable';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import { dateTimeFormatWithoutOffset } from '../../common/utils/dateUtils';
import { get, post } from '../../actions/commonActions';
import CodeDeleteDialog from '../component/CodeDeleteDialog';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import APIENDPOINTS from '../../constants/APIEndPoints';
import RouteEndPoints from '../../constants/RouteEndPoints';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import checkLanguage from '../checkLanguage';
import { CommonConstants } from '../../constants/CommonConstants';
import { fetchLanguageList } from '../../actions/FieldsAction';

const styles = {
  space: {
    marginRight: '10px',
  },
};

const column = [
  {
    title: '',
    dataIndex: 'render-radio',
    styles: { width: 50, minWidth: 50 },
    textAlign: 'center',
  },
  {
    title: 'language_code',
    dataIndex: 'code',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'name',
    dataIndex: 'languageName',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'enabled',
    dataIndex: 'enable',
    sorter: true,
    textAlign: 'center',
    render: (record) => {
      if (record.enabled) {
        return (
          <img
            src="https://stara.bizconcier-dm.com/wide_img/renew_icon_resposn_comp.png"
            alt="renew_icon_resposn_comp"
            height="25px"
            width="25px"
          />
        );
      }
      return (
        <img
          src="https://stara.bizconcier-dm.com/wide_img/mdm_del.png"
          alt="renew_icon_resposn_comp"
          height="25px"
          width="25px"
        />
      );
    },
  },
  {
    title: 'creator',
    dataIndex: 'createdBy',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'created_date',
    dataIndex: 'createdDate',
    sorter: true,
    textAlign: 'center',
    render: (record) => dateTimeFormatWithoutOffset(record.createdDate),
  },
  {
    title: 'updated_by',
    dataIndex: 'updatedBy',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'updated_date',
    dataIndex: 'updatedDate',
    sorter: true,
    textAlign: 'center',
    render: (record) => dateTimeFormatWithoutOffset(record.updatedDate),
  },
];
const resultsPerPageKey = 'lang_list';
let currentRowIndex = 0;
/**
 * LanguageContent is to view user languages.
 * @author Gobi R
 */
class LanguageSetting extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      languageList: [],
      selectedRow: {},
      isButtonDisabled: true,
      openDelete: false,
      loading: true,
      isDefaultCode: false,
      openDialog: false,
      deleteMsg: '',
      searchInput: '',
      defaultPage: 1,
      gridLoading: false,
      errorMsg: '',
    };
    this.initialFocus = createRef();
  }

  /**
   * componentDidMount is to fetch already existing language data.
   */
  componentDidMount() {
    this.load(true);
  }

  /**
   * load is to initiate load grid data.
   * @param initial
   */
  load = (initial) => {
    const { searchInput } = this.state;
    const { putAllLanguageData } = this.props;
    const paramMap = { searchedValue: searchInput };
    this.setState({ gridLoading: true });
    post(APIENDPOINTS.LANGUAGECODELIST, paramMap)
      .then((response) => response.json())
      .then((data) => {
        const selectedRow = data.length === 0 ? {} : data[0];
        this.setState(
          {
            languageList: data,
            loading: false,
            selectedRow,
            defaultPage: 1,
            gridLoading: false,
          },
          this.setSelectedRow(selectedRow),
        );
        putAllLanguageData(data.filter((lang) => lang.enabled));
        if (initial && this.initialFocus.current) {
          this.initialFocus.current.focus();
        }
      });
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * handlePaginationClick is to handle pagenation.
   * @param data
   */
  handlePaginationClick = (data) => {
    this.setSelectedRow(data[0]);
  };

  /**
   * handleSort is to hadle sorting.
   * @param data
   */
  handleSort = (data) => {
    this.setSelectedRow(data.length === 0 ? {} : data[currentRowIndex]);
    if(!isUndefined(currentRowIndex)) this.setState({isDefaultCode:this.checkIsDefaultCode(data[currentRowIndex].code)});
  };

  /**
   * handleSelectChange is to handle no of rows display.
   * @param data
   */
  handleSelectChange = (data) => {
    this.setState({ gridLoading: true });
    setTimeout(() => {
      this.setSelectedRow(data.length === 0 ? {} : data[0]);
      this.setState({ gridLoading: false });
    }, 2000);
  };

  /**
   * setSelectedRow is to set Selected Row data.
   * @param selectedRow
   */
  setSelectedRow = (selectedRow) => {
    this.setState({
      selectedRow,
      isButtonDisabled: false,
      isDefaultCode: this.checkIsDefaultCode(selectedRow.code),
    });
  };

  /**
   * OnContentViewClick is to navigate to language content list to display
   * selected language content list.
   */
  OnContentViewClick = () => {
    const { history } = this.props;
    const { selectedRow } = this.state;
    history.push({ pathname: RouteEndPoints.LANGUAGECONTENT, languageCode: selectedRow.code });
  };

  /**
   * handleRadioChange is to handle row select.
   * @param selectedRow
   */
  handleRadioChange = (selectedRow,selectedKey) => {
    this.setSelectedRow(selectedRow);
    currentRowIndex = selectedKey;
  };

  /**
   * checkIsDefaultCode is to check default language
   * and diable change and delete buttons accordingly.
   * @param code
   */
  checkIsDefaultCode = (code) => {
    let isDefaultCode = false;
    if (code === 'ja') {
      isDefaultCode = true;
    }
    return isDefaultCode;
  };

  /**
   * onDeleteClick is to handle delete button click.
   */
  OnDeleteClick = async () => {
    const { selectedRow } = this.state;
    const { history } = this.props;
    const { code } = selectedRow;
    try {
      const response = await get(`${APIENDPOINTS.NOTICELISTBASEDLANGCODE}/${code}`);
      const notification = await response.json();
      if (notification.length > 0) {
        this.setState({ openDelete: true, deleteMsg: 'deleteing_notification_language_code' });
      } else {
        this.setState({ openDelete: true, deleteMsg: 'deleteing_language_code' });
      }
    } catch (Exception) {
      history.push('error');
    }
  };

  /**
   * closeDeleteDialog is to delete close dialog without saving.
   * @param loading
   */
  closeDeleteDialog = (loading) => {
    this.setState({ openDelete: false, loading });
  };

  /**
   * onChangeClick is to open language edit page.
   */
  OnChangeClick = () => {
    const { history } = this.props;
    const { selectedRow } = this.state;
    history.push(RouteEndPoints.LANGUAGESETTINGUPDATE, { mode: 'EDIT', selectedRow });
  };

  /**
   * onAddLanguage is to handle new langauage addition.
   */
  onAddLanguage = () => {
    const { history } = this.props;
    history.push('/setting/adminLangAdd');
  };

  /**
   * onDeleteInfoClick on click delete info okay.
   */
  onDeleteInfoClick = () => {
    this.setState({ openDialog: false });
  };

  /**
   * languageCodeChange is to fetch data based on search input value
   * @param event
   */
  languageCodeChange = (event) => {
    this.setState({ searchInput: event.target.value });
  };

  /**
   * pageChangeHandler is to handle page change.
   * @param event
   */
  pageChangeHandler = (newPage) => {
    this.setState({ defaultPage: newPage });
  };

  /**
   * onKeyDown is to handle enter event to load grid.
   * @param event
   */
  onKeyDown = (event) => {
    if (event.keyCode === 13) this.load();
  };

  /**
   * render is to load fields.
   * @returns jsx elements
   */
  render() {
    const {
      languageList,
      isButtonDisabled,
      openDelete,
      selectedRow,
      loading,
      isDefaultCode,
      openDialog,
      deleteMsg,
      searchInput,
      defaultPage,
      gridLoading,
      errorMsg,
    } = this.state;
    return (
      <div style={{ width: '100%', marginBottom: '110px' }}>
        <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
          <SBContentHeader title="language_code_list" description={['displays_language_code']} />
          <SBButton add onClick={(event) => this.onAddLanguage(event)} />
          <div className="border mb15 p15">
            <SBTextbox
              type="text"
              name="search_data"
              width={200}
              className="mr10"
              value={searchInput}
              ref={this.initialFocus}
              onKeyDown={this.onKeyDown}
              onChange={(event) => this.languageCodeChange(event)}
              style={styles.space}
            />
            <SBButton
              type="submit"
              name="Search"
              color="green"
              title="search"
              onClick={() => this.load()}
            />
          </div>
          <CircularSpinner
            loading={gridLoading}
            css={{ position: 'absolute', top: '62%', left: 'calc(40% - 55px)' }}
          >
            {languageList.length ? (
              <SBTable
                dataSource={languageList}
                columns={column}
                pagination
                defaultPage={defaultPage}
                countInfo="language_list_count"
                countInfoContents={languageList.length}
                filterMessage="results_per_page"
                defaultSelected={resultsPerPageKey}
                filterList={[
                  { key: 25, value: '25_items' },
                  { key: 50, value: '50_items' },
                  { key: 100, value: '100_items' },
                  { key: 'all', value: 'all' },
                ]}
                handleRadioChange={(data,selectedKey) => this.handleRadioChange(data,selectedKey)}
                defaultSelectedKeys={[0]}
                handlePaginationClick={(data) => this.handlePaginationClick(data)}
                handleSort={(data) => this.handleSort(data)}
                handleSelectChange={(data) => this.handleSelectChange(data)}
                onPageChange={this.pageChangeHandler}
              />
            ) : (
              <span>{this.languageLabelCheck('no_result_found')}</span>
            )}
            <div className="floatright">
              <SBButton
                type="button"
                name="change"
                id="change"
                title="change"
                color="green"
                disabled={isDefaultCode || isButtonDisabled || !languageList.length}
                onClick={(event) => this.OnChangeClick(event)}
              />
              <SBButton
                type="button"
                name="view"
                id="view"
                title="view_content"
                disabled={isButtonDisabled || !languageList.length}
                onClick={(event) => this.OnContentViewClick(event)}
              />
              <SBButton
                type="button"
                name="delete"
                id="delete"
                title="delete"
                disabled={isDefaultCode || isButtonDisabled || !languageList.length}
                onClick={() => this.OnDeleteClick()}
              />
            </div>
          </CircularSpinner>
          <CodeDeleteDialog
            open={openDelete}
            closeDialog={(isloading) => this.closeDeleteDialog(isloading)}
            code={selectedRow.code}
            refresh={() => this.load(true)}
            content={deleteMsg}
            openDeleteInfo={(msg) =>
              this.setState({ loading: false, openDialog: true, errorMsg: msg })
            }
          />
          <SBDialog
            open={openDialog}
            header="delete_failed"
            type="error"
            content={errorMsg}
            okButtonTitle="ok"
            onOk={() => this.onDeleteInfoClick()}
            width="400px"
          />
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
LanguageSetting.propTypes = {
  localeData: PropTypes.objectOf(PropTypes.string),
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
LanguageSetting.defaultProps = {
  localeData: {},
  isNotMultiLang: false,
};

/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = (state) => {
  const { langContent } = state.user;
  return {
    langContent,
    defaultLang: state.language.default,
    localeData: state.language.data,
  };
};

/**
 * mapDispatchToProps is to map store actions to props.
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    putAllLanguageData: (data) => {
      dispatch(fetchLanguageList(data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(LanguageSetting));
