/**
 * ContractInformation is used to build components of Contract Information Screen.
 * @author: V.Vijayaragavan
 */
import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import { CommonConstants } from '../../../constants/CommonConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBButton from '../../../common/component/sbbutton/SBButton';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import { Role } from '../../../constants/Role';
import checkLanguage from '../../../language/checkLanguage';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import {
  initialDataSource,
  administratorAccountViewTableList,
  basicInformationTableList,
  supportInfoTableList,
  latestInformationTableList,
  androidTableList,
  getStartDateAndFirstUseDate,
} from '../constants/ContractInfoConstants';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get } from '../../../actions/commonActions';

class ContractInformation extends Component {
  constructor(props) {
    super(props);
    const { tenantVo } = this.props;
    this.state = {
      pageLoading: true,
      dataSource: initialDataSource(this.languageLabelCheck),
      administratorTableList: administratorAccountViewTableList(this.languageLabelCheck, tenantVo),
      changeButtonRef: createRef(),
    };
  }

  componentDidMount() {
    const { history, userVo, currentLanguage } = this.props;
    let { dataSource } = this.state;
    const { changeButtonRef } = this.state;
    const userStatus = async () => {
      const userDetailResponse = await get(
        `${APIENDPOINTS.APPLICATIONUSERDETAILS}/${userVo.dmCode}`,
      );
      const userDetailData = await userDetailResponse.json();
      if (userDetailData.status) {
        const { applicationVo } = userDetailData;
        dataSource = { ...userDetailData.userVo, ...dataSource };
        dataSource = {
          ...{
            noOfAndroidDeviceReg: `${applicationVo.androidDeviceRegistered} / ${applicationVo.androidDeviceCount}`,
          },
          ...dataSource,
          ...getStartDateAndFirstUseDate(applicationVo, currentLanguage),
        };
        this.setState({ dataSource, pageLoading: false });
        if (changeButtonRef.current) {
          changeButtonRef.current.focus();
        }
      } else {
        history.push(RouteEndPoints.ERROR);
      }
    };
    userStatus();
  }

  /**
   * onChangeButtonClick is used to handle change button click action
   * @param key
   */
  onChangeButtonClick = () => {
    const { history } = this.props;
    history.push(RouteEndPoints.CHANGECONTRACTINFO);
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render components of the screen
   */
  render() {
    const { screenTitleData } = this.props;
    const { pageLoading, dataSource, administratorTableList, changeButtonRef } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader title={screenTitleData.title} description={screenTitleData.description} />
        <div className="contents-main">
          <SBTableHeader headerText="basic_information" />
          <CustomTableFrame tableList={basicInformationTableList()} dataSource={dataSource} />
          <h2 className="title-corner relative">
            {this.languageLabelCheck('administrator_account')}
            <RoleResolver authorityRoleType={Role.group1}>
              <SBButton
                type="button"
                onClick={() => this.onChangeButtonClick()}
                id="change"
                title="change"
                className="btn-change"
                noMarginRight
                ref={changeButtonRef}
              />
            </RoleResolver>
          </h2>
          <CustomTableFrame tableList={administratorTableList} dataSource={dataSource} />
          <SBTableHeader headerText="support_info" />
          <CustomTableFrame
            tableList={supportInfoTableList()}
            dataSource={dataSource}
            onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
          <SBTableHeader headerText="latest_information" />
          <CustomTableFrame tableList={latestInformationTableList()} dataSource={dataSource} />
          <SBTableHeader headerText="android" />
          <CustomTableFrame tableList={androidTableList()} dataSource={dataSource} />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ContractInformation.propTypes = {
  currentLanguage: PropTypes.string,
  userVo: commonPropTypes().userVo.isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  screenTitleData: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.shape([PropTypes.string]),
  }).isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ContractInformation.defaultProps = {
  isNotMultiLang: false,
  currentLanguage: 'ja',
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { tenantVo } = state.tenant;
  const { appRegisterVo } = state.tenant;
  const { data, current } = state.language;
  return {
    appRegisterVo,
    tenantVo,
    userVo,
    localeData: data,
    currentLanguage: current,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ContractInformation));
