import UNDERSCORE from '../../v1/common/utils';
import {
  isNotUndefinedOrNotEmpty,
} from '../../common/CommonValidations/CommonValidations';

/**
 * validateFileExtention is used to validate file extension
 * @param fileName
 * @return validationResult
 */

function validateFileExtention(fileName) {
    const validationResult = { status: true, error: '' };
    const lowerCasedFileName = fileName.toLowerCase();
    if (
      lowerCasedFileName.endsWith('.png') ||
      lowerCasedFileName.endsWith('.jpeg') ||
      lowerCasedFileName.endsWith('.jpg')
    ) {
      validationResult.status = true;
    } else {
      validationResult.status = false;
      validationResult.error = 'fileExtension';
    }
    return validationResult;
}

/**
 * validateFileSize is used to validate file size
 * @param file
 * @param maxFileSize
 * @return validationResult
 */

function validateFileSize(file, maxFileSize) {
    const validationResult = { status: true, error: '' };
    if (Math.ceil(file.size / 1048576) > maxFileSize) {
      validationResult.status = false;
      validationResult.error = 'fileSizeGreater';
    }
    return validationResult;
}

export function validateFile(file, maxFileSize) {
    const validationResult = { status: true, error: '' };
    if (file) {
      const fileTypeValidation = validateFileExtention(file.name);
      if (!fileTypeValidation.status) {
        return fileTypeValidation;
      }
      const sizeValidationResult = validateFileSize(file, maxFileSize);
      if (!sizeValidationResult.status) {
        return sizeValidationResult;
      }
      if (file.name.length > 80) {
        validationResult.status = false;
        validationResult.error = 'fileNameLength';
        return validationResult;
      }
    } else {
      validationResult.status = false;
      validationResult.error = 'nofile';
    }
    return validationResult;
  }


export function validateOrderNumber(value, homeApplicationsShortcut) {
  const minOrder = 1;
  const maxOrder = 1000;
  const result = { status: true, error: '' };
  const numberRegex = /^\d+$/;
  if(!(value === '' || value === undefined || value === null)) {
    if(numberRegex.test(value)) {
      if (parseInt(value,10) >= minOrder && parseInt(value,10) <= maxOrder) {
        const index = UNDERSCORE.findIndex(homeApplicationsShortcut, { orderNo : parseInt(value,10) });
        if(index > -1) {
          result.status = false;
          result.error = 'same';
        } else {
          result.status = true;
        }
      } else {
        result.status = false;
        result.error = 'range';
      }
    } else {
        result.status = false;
        result.error = 'invalid';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
    return result;
}

export function validateURL(value) {
  const result = { status: true, error: '' };
  const urlRegex = /^((http(s?)?):\/\/)?([wW]{3}\.)?[a-zA-Z0-9\-.]+\.[a-zA-Z]{2,}(\.[a-zA-Z]{2,})?$/g;
  if(!(value === '' || value === undefined || value === null)) {
    if(urlRegex.test(value)) {
      result.status = true;
    } else {
      result.status = false;
      result.error = 'invalid';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
    return result;
}

export function validateTitle(value) {
  const minLength = 1;
  const maxLength = 50;
  const result = { status: true, error: '' };
  if(!(value === '' || value === undefined || value === null)) {
    if(value.length >= minLength && value.length <= maxLength) {
      result.status = true;
      result.error = '';
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
    return result;
}

export function validateIconValue(value) {
  const result = { status: true, error: '' };
  const numberRegex = /^[0-9]+$/;
  const decimalregex  =  /[0-9]+\.[0-9]+$/;
  const minLength = 1;
  const maxLength = 10;
  if(isNotUndefinedOrNotEmpty(value)) {
    if(numberRegex.test(value) || decimalregex.test(value)) {
      if(value >= 0.5 && value <= 5) {
        if(
          value.toString().length >= minLength &&
          value.toString().length <=maxLength
        ) {
          result.status = true;
        } else {
          result.status = false;
          result.error = 'length';
        }
      } else {
        result.status = false;
        result.error = 'range';  
      }
    } else {
      result.status = false;
      result.error = 'invalid';
   }
  }
  return result;
}

export function validateAppStatus(appList) {
  const result = { status: true, error: '' };
  if(!appList.length) {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}