/**
 * MultipleAdminCRUDFooter is used to create footer component
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import SBButton from '../../common/component/sbbutton/SBButton';

class MultipleAdminCRUDFooter extends Component {
  /**
   * renderButton is used to render button components
   * @param componentJsonObject
   * @return sbbutton
   */

  renderButton = (componentJsonObject) => {
    const { onHandleClick } = this.props;
    return (
      <SBButton
        {...componentJsonObject}
        type="button"
        onClick={() => onHandleClick(componentJsonObject.key)}
      />
    );
  };
  /**
   * render is used to render the footer area
   * @return component
   */

  render() {
    const { footer, register, usersLength, maxUsercount, reference } = this.props;
    return (
      <div className="clearfix">
        {register && (
          <div className="floatleft">
            {this.renderButton({
              id: 'btn_cont_reg',
              color: 'gray',
              title: 'continuance_registration',
              disabled: footer.okDisable || maxUsercount - 1 === usersLength,
              style: footer.primaryVisibile ? { display: '' } : { display: 'none' },
              key: 'continue_register',
              noMarginRight: false,
              canModify: false,
            })}
          </div>
        )}
        <div className="floatright">
          {this.renderButton({
            id: 'btn_ok',
            color: 'green',
            title: 'ok',
            disabled: footer.okDisable,
            style: { display: footer.primaryVisibile ? '' : 'none' },
            key: 'save',
            noMarginRight: false,
            canModify: false,
          })}
          {this.renderButton({
            id: 'btn_cancel',
            title: 'cancel',
            disabled: false,
            style: { display: footer.primaryVisibile ? '' : 'none' },
            key: 'cancel',
            noMarginRight: true,
            canModify: true,
            ref: reference && reference.cancelRef ? reference.cancelRef : '',
          })}
          {this.renderButton({
            id: 'btn_back',
            title: 'back',
            disabled: false,
            style: { display: footer.primaryVisibile ? 'none' : 'block' },
            key: 'back',
            noMarginRight: true,
            canModify: false,
            ref: reference && reference.backButtonRef ? reference.backButtonRef : '',
          })}
        </div>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminCRUDFooter.propTypes = {
  onHandleClick: PropTypes.func.isRequired,
  maxUsercount: PropTypes.number,
  usersLength: PropTypes.number,
  register: PropTypes.bool,
  footer: PropTypes.objectOf(PropTypes.bool).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminCRUDFooter.defaultProps = {
  maxUsercount: 0,
  usersLength: 0,
  register: false,
};

export default withRouter(MultipleAdminCRUDFooter);
