import React, { Component } from 'react';
import { connect } from 'react-redux';
import QRCode from 'qrcode.react';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTabs from '../../common/component/Tabs/SBTabs';
import { get } from '../../actions/commonActions';
import LoadingSpinner from '../../common/component/loadingSpinner/LoadingSpinner';
import Utils from '../../constants/Utils';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import descriptionConverter from '../../common/utils/descriptionConverter';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import RouteEndPoints from '../../constants/RouteEndPoints';

class Enrollment extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeKey: 'qr',
      qrData: '',
      dpcData: '',
      dpcName: 'setup',
      loading: false,
      message: '',
      openErrorDialog: false,
    };
  }

  componentDidMount() {
    this.setState({ loading: true }, () => this.getEnterpriseData());
  }

  async getEnterpriseData() {
    const { history, androidDeviceCount } = this.props;
    try {
      const response = await get('android/device/getEnrollmenttoken');
      const enterprise = await response.json();
      const { qrCode: qrData, value: dpcData, additionalData: DeviceCount } = enterprise;
      if (parseInt(DeviceCount, 10) >= androidDeviceCount) {
        this.setState({
          message: 'number_of_devices_more_than_allowed_limit',
          openErrorDialog: true,
          loading: false,
        });
      } else if (qrData) {
        this.setState({ qrData, dpcData, loading: false });
      } else {
        this.setState({ loading: false });
      }
    } catch (Exception) {
      this.setState({ loading: false });
      history.push('error');
    }
  }

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  setDynamicContent = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  tableContent = () => {
    const { qrData, dpcName, dpcData } = this.state;
    const enterpriseURL = `${Utils.API_URL_ORIGIN}/setting/androidenterprise/enterprise`;
    return {
      qr: {
        header: 'qr_code',
        description: qrData
          ? [
              descriptionConverter(this.languageLabelCheck('qr_code_one_description')),
              descriptionConverter(this.languageLabelCheck('qr_code_two_description')),
              descriptionConverter(this.languageLabelCheck('qr_code_three_description')),
            ]
          : [
              descriptionConverter(this.languageLabelCheck('enterprise_not_created')),
            <a href={enterpriseURL}>{this.languageLabelCheck('enterprise_link')}</a>,
            ],
      },
      dpc: {
        header: 'dpc_identifier',
        description: dpcData
          ? [
              descriptionConverter(this.languageLabelCheck('dpc_identifier_one_description')),
              descriptionConverter(this.languageLabelCheck('dpc_identifier_two_description')),
              descriptionConverter(
                this.setDynamicContent('dpc_identifier_three_description', dpcName),
              ),
              descriptionConverter(
                this.setDynamicContent('dpc_identifier_four_description', dpcData),
              ),
            ]
          : [
              descriptionConverter(this.languageLabelCheck('enterprise_not_created')),
            <a href={enterpriseURL}>{this.languageLabelCheck('enterprise_link')}</a>,
            ],
      },
    };
  };

  onTabClick = activeKey => {
    this.setState = { activeKey };
  };

  /**
   * navigateListScreen is to navigate  from groupRegistration to groupManagement
   */
  navigateListScreen = () => {
    const { history } = this.props;
    this.setState({ openErrorDialog: false }, () => history.push(RouteEndPoints.ENTERPRISE));
  };

  render() {
    const { activeKey, qrData, loading, message, openErrorDialog } = this.state;
    const { dpc, qr } = this.tableContent();
    return (
      <LoadingSpinner loading={loading}>
        <div id="contents" className="clearfix">
          <div style={{ width: '100%', 'padding-bottom': '150px' }}>
            <div id="main">
              <div className="container" style={{ height: '80px' }}>
                <SBContentHeader title="enrollment" />
                <SBTabs
                  activeKey={activeKey}
                  onClick={Key => this.onTabClick(Key)}
                  headerStyle={{ width: 200 }}
                >
                  <div label={qr.header} key="qr">
                    <QRComponent qrData={qrData} tableContent={qr.description} />
                  </div>
                  <div label={dpc.header} key="dpc">
                    <DpcComponent tableContent={dpc.description} />
                  </div>
                </SBTabs>
              </div>
              <SBDialog
                type="error"
                width="400px"
                header="error"
                okButtonTitle="ok"
                content={message}
                open={openErrorDialog}
                onOk={() => this.navigateListScreen()}
              />
            </div>
          </div>
        </div>
      </LoadingSpinner>
    );
  }
}

const mapStateToProps = state => {
  const { data: localeData } = state.language;
  const { androidDeviceCount } = state.tenant.tenantVo;
  return { localeData, androidDeviceCount };
};

export default connect(mapStateToProps)(Enrollment);

function DpcComponent({ tableContent }) {
  return (
    <div id="enrollmentmain" className="contents-main mb15 p15">
      <p>{tableContent}</p>
    </div>
  );
}

function QRComponent({ qrData, tableContent }) {
  const styleValue = {
    position: 'absolute',
    right: 150,
    top: 165,
  };
  return (
    <div id="enrollmentmain" className="contents-main mb15 p15" style={{ height: 320 }}>
      <p>{tableContent}</p>
      {qrData && <QRCode style={styleValue} value={qrData} size={300} />}
    </div>
  );
}
