/** TenantReducer is used to map the repose data for the group to the redux.
 * @author P.Vinothpandian
 */
import {
  FETCH_TENANT_ENTITY,
  FETCH_APPREGISTER_ENTITY,
  FETCH_APPUPDATE_ENTITY,
} from '../../../../actions/ActionTypes';

const initialState = {
  tenantVo: {},
  appRegisterVo: {},
};

function tenant(state = initialState, action) {
  const { type, data } = action;
  switch (type) {
    case FETCH_TENANT_ENTITY:
      return {
        ...state,
        tenantVo: data || {},
      };
    case FETCH_APPREGISTER_ENTITY:
      return {
        ...state,
        appRegisterVo: data || {},
      };
    case FETCH_APPUPDATE_ENTITY:
      return {
        ...state,
        appUpdateVo: data || {},
      };
    default:
      return state;
  }
}

export default tenant;
