/**
 * OperationUpload is used to create operation upload operation
 * @author: P.Vinothpandian
 *
 */

import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import SBButton from '../../../common/component/sbbutton/SBButton';

const moment = require('moment-timezone');

const linkStyle = {
  color: '#036EB7',
  textDecoration: 'underline',
  cursor: 'pointer',
  background: 'none',
  font: 'inherit',
  padding: 0,
  border: 'none',
  letterSpacing: 0,
  whiteSpace: 'normal',
};

/**
 * FileDownloadDesc is used to create operation upload operation
 * @param props
 * @return FileDownloadDescComponent
 */

const FileDownloadDesc = props => {
  /**
   * getDateTime is used to get date and time
   * @param dateTime
   * @return momentdateTime
   */

  const getDateTime = dateTime => {
    const { userVo } = props;
    return moment(dateTime)
      .utcOffset(userVo.timeZoneOffSet)
      .format('YYYY/MM/DD HH:mm');
  };
  const { file, onHandleClick ,dateTime , uploadedBy , onURLClick } = props;
  return (
    <>
      <table className="table-tate mb5 mt5">
        <tbody>
          <tr>
            <td style={{ maxWidth: 375 }}>
              <span className="lineheight ">
                <button type="button" style={linkStyle} onClick={(event) => onURLClick(event)}>
                  {file.name}
                </button>
              </span>
            </td>
            <td>
              <span className="lineheight ">{getDateTime(dateTime)}</span>
            </td>
            <td>
              <span className="lineheight ">{uploadedBy}</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div className="clearfix mb5">
        <div className="floatright">
          <SBButton type="button" title="delete" onClick={(event) => onHandleClick(event)} />
        </div>
      </div>
    </>
  );
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    userVo,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

FileDownloadDesc.propTypes = {
  userVo: commonPropTypes().userVo.isRequired,
  onHandleClick: PropTypes.func.isRequired,
  dateTime: PropTypes.objectOf(PropTypes.any).isRequired,
  onURLClick: PropTypes.func.isRequired,
};

export default connect(mapStateToProps)(withRouter(FileDownloadDesc));
