/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import deviceHeader from '../constants/deviceHeaderConstants';
import { tableList } from '../constants/deviceTableConstants';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import { getGroupNameList, getDepartment } from '../../actions/DeviceAction';
import { apiEndPointMapper, serviceMapper } from '../common/deviceCommonFunctions';
import RouteEndPoints from '../../constants/RouteEndPoints';

/**
 * DeviceCsvDetail is to list device for csv batch operation
 * @author Aswin Palanisamy
 */
class DeviceCsvDetail extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, operationType, groupList, location: getData, department } = props;
    const { deviceCsvMapper } = getData.state || { deviceCsvMapper : { deviceList: [], inputDeviceInfoList:[] }};
    const { deviceList=[] } = deviceCsvMapper;
    this.state = {
      screenKey,
      header: '',
      disable: true,
      statusList: [],
      csvDataList: [],
      csvDataSearch: '',
      pageLoading: false,
      gridLoading: false,
      openDialogError: false,
      counter: deviceList.length,
      openDialogConfirmation: false,
      groupSearch: cloneDeep(groupList),
      departmentSearch: cloneDeep(department),
      deviceCsvMapper: cloneDeep(deviceCsvMapper),
      headerData: deviceHeader(`${operationType}BulkCsv`),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { location: getData, history } = this.props;
    if(!getData.state) {
      history.push(RouteEndPoints.ERROR);
    } else {
      this.setState({ pageLoading: true }, () => this.getCsvDataList(true));
    }
  }

  /**
   * getSearchList is to filter the data based on the search group/department condition
   * @param onOpen 
   */
  getSearchList = () => {
    const { csvDataSearch, statusList, groupSearch, departmentSearch, deviceCsvMapper } = this.state;
    const search = (csvDataSearch || '').toLowerCase();
    const MINUS_ONE = -1;
    const { deviceList } = deviceCsvMapper;
    const [ departmentOne = '', departmentTwo = '', departmentThree = '', departmentFour = '' ] = departmentSearch;
    return cloneDeep(deviceList).filter(device => isEmpty(statusList) || statusList.indexOf(device.syncStatus) > MINUS_ONE)
    .filter(deviceRecord => isEmpty(groupSearch) || groupSearch.indexOf(deviceRecord.groupName) > MINUS_ONE)
    .filter(deviceRecord => isEmpty(departmentOne) || departmentOne === deviceRecord.departmentOne)
    .filter(deviceRecord => isEmpty(departmentTwo) || departmentTwo === deviceRecord.departmentTwo)
    .filter(deviceRecord => isEmpty(departmentThree) || departmentThree === deviceRecord.departmentThree)
    .filter(deviceRecord => isEmpty(departmentFour) || departmentFour === deviceRecord.departmentFour)
    .filter(deviceData => isEmpty(search) || (deviceData.deviceName.toLowerCase().indexOf(search) > MINUS_ONE
    || deviceData.deviceModel.toLowerCase().indexOf(search) > MINUS_ONE
    || deviceData.serialNumber.toLowerCase().indexOf(search) > MINUS_ONE
    || deviceData.androidVersion.toLowerCase().indexOf(search) > MINUS_ONE));
  }

  /**
   * getCsvDataList is to fetch group information
   * @param onOpen
   */
  getCsvDataList = async onOpen => {
    const csvDataList = this.getSearchList();
    this.setState({
      csvDataList,
      disable: false,
      gridLoading: false,
      pageLoading: false,
    });
    if (onOpen && this.searchRef.current) this.searchRef.current.focus();
  };

  /**
   * getBacktoPreviousScreen navigates to csvBatchOperation screen
   */
  getBacktoPreviousScreen = () => {
    const { history } = this.props;
    history.push(serviceMapper('csvBatchOperation'), {
      parentScreen: serviceMapper('deviceList'),
    });
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, csvDataSearch) => {
    if (event.keyCode === 13) {
      this.setState({ csvDataSearch, gridLoading: true }, () => this.getCsvDataList());
    }
  };

  /**
   * handleSearchAction performs group search operation
   * @param key
   * @param value
   */
  handleSearchAction = (key, value) => {
    if (key === 'searchButton') {
      this.setState({ csvDataSearch: value.searchKeyword, statusList: value.checkBoxKeys, 
        gridLoading: true }, () => this.getCsvDataList());
    } else if(key === 'searchKeyword') {
      this.setState({ csvDataSearch: value[key]});
    } else if(key === 'departmentSearch') {
      this.searchPage(`${key}Csv`);
    }
  };

  /**
   * onHandleClick performs button click events in the screen
   * @param screenKey
   * @return buttonAction
   */
  onHandleClick = screenKey =>
    ({
      ok: () => this.performCsvOperation(),
      cancel: () => this.getBacktoPreviousScreen(),
      selectGroup: () => this.searchPage(screenKey),
    }[screenKey]());

  /**
   * searchPage is to navigate to group, department search page
   * @param screenKey
   */
  searchPage = (screenKey) => {
    const { history, location } = this.props;
    const { groupSearch, departmentSearch, deviceCsvMapper } = this.state;
      history.push(serviceMapper(screenKey), {
        deviceCsvMapper,
        setBatch: true,
        getGroup: groupSearch,
        getDepartment: departmentSearch,
        parentScreen: location.pathname,
      });
  };

  /**
   * performCsvOperation prompts confirmation dialog for device bulk operation
   */
  performCsvOperation = () => {
    this.setState({ openDialogConfirmation: true });
  };
  
    /**
     * performBulkSetting is to migrate selected device to the selected group
     */
     performBulkSetting = () => {
       const { deviceCsvMapper } = this.state;
       this.setState({ pageLoading: true }, async () => {
         const response = await post(apiEndPointMapper('csvBulkSetting'), {...deviceCsvMapper});
         const result = await response.json();
         const { status, message, data } = result;
         const openDialogError = status !== 'OK';
         this.setState({
           message,
           counter: data,
           openDialogError,
           pageLoading: false,
           openDialogConfirmation: false,
           header: openDialogError && 'error',
         }, () => {
           if(!openDialogError) {
             const { history, operationType, makeGroupList, makeDepartmentList } = this.props;
             const { counter } = this.state;
             makeGroupList();
             makeDepartmentList();
             history.push(serviceMapper(`${operationType}BatchComplete`), {
               counter,
               parentScreen: serviceMapper('deviceList'),
             });
           }
         });
       });
     };

  /**
   * addCountInfo is to set grid data count in screen header label
   * @param label 
   * @param  contents 
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * getMessage is to fetch the confirmation message based on operationType
   */
  getMessage = () => {
    const { operationType } = this.props;
    return {
      changeGroup: "group_changes_save",
      deviceDelete: "device_delete_save",
      passcodeReset: "passcode_reset_save",
    }[operationType];
  }

  /**
   * render perform groupSelection screen rendering
   * @return DeviceGroupSelect
   */
  render() {
    const {
      header,
      disable,
      message,
      counter,
      screenKey,
      statusList,
      headerData,
      groupSearch,
      pageLoading,
      gridLoading,
      csvDataList,
      csvDataSearch,
      openDialogError,
      openDialogConfirmation,
    } = this.state;
    const { operationType } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader 
          isNotMultiLang 
          title={this.languageLabelCheck(headerData.title)}
          description={[this.addCountInfo(headerData.description, counter)]} 
        />
        <div className="contents-main">
          <CustomTableFrame
            tableList={tableList()}
            dataSource={{
              selectGroup: groupSearch
                .sort((present, next) => (present.toUpperCase() < next.toUpperCase() ? -1 : 1))
                .join(','),
            }}
            onTableFrameEvent={(key) => this.onHandleClick(key)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
        </div>
        <DeviceSearch
          screenKey={screenKey}
          defaultValues={statusList}
          searchText={csvDataSearch}
          reference={this.searchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '70%', left: 'calc(60% - 55px)' }}
        >
          <DeviceGrid
            pagination
            isAscending={false}
            screenKey={screenKey}
            gridList={csvDataList}
            countInfoTotalContents={counter}
            filterMessage="results_per_page"
            defaultSortColumn="dataCollectionTime"
            countInfoContents={csvDataList.length}
            countInfo="search_result_device_total_count"
            defaultSelected={`${screenKey}${operationType}`}
            handleSelectChange={() => this.setState({ gridLoading: true }, () => this.getCsvDataList())}
          />
          <DeviceFooter
            onHandleClick={key => this.onHandleClick(key)}
            footer={{
              disable,
              screenKey,
            }}
          />
        </CircularSpinner>
        <SBDialog
          type={header}
          header={header}
          content={message}
          okButtonTitle="ok"
          open={openDialogError}
          onOk={() => {
            this.setState(
              { openDialogError: false },
              this.getBacktoPreviousScreen(),
            );
          }}
          width="400px"
        />
        <SBDialog
          type="confirmation"
          content={this.getMessage()}
          header="save_changes"
          open={openDialogConfirmation}
          onCancel={() => {
            this.setState({ openDialogConfirmation: false });
          }}
          onOk={() => this.performBulkSetting()}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps perform data linking from the redux store
 * @param state 
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  const { groupList, department } = state.device;
  return { localeData: data, groupList, department };
};

/**
 * mapDispatchToProps performs data setup in device redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeGroupList: data => {
      dispatch(getGroupNameList(data));
    },
    makeDepartmentList: data => {
      dispatch(getDepartment(data));
    }
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceCsvDetail.propTypes = {
  isNotMultiLang: PropTypes.bool.isRequired,
  operationType: PropTypes.string.isRequired,
  screenKey: PropTypes.string.isRequired,
  groupList: PropTypes.arrayOf(PropTypes.string).isRequired,
  department: PropTypes.arrayOf(PropTypes.string).isRequired,
  makeGroupList: PropTypes.func.isRequired,
  makeDepartmentList: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    deviceCsvMapper: PropTypes.objectOf(PropTypes.any),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceCsvDetail));
