/**
 * administratorAccountChangeTableList is used to build object values for Adminstrator Accoutn change scren
 * @param dynamicData
 * @param reference
 * @param tenantVo
 * @return componentObjectValue
 */
export function administratorAccountChangeTableList(dynamicData, reference, tenantVo) {
  return [
    {
      label: 'login_id',
      component: 'span',
      key: 'loginId',
    },
    {
      label: 'password',
      component: dynamicData.isApplicationUserAdmin ? 'link' : 'span',
      key: 'password',
      pathname: RouteEndPoints.CHANGEADMINPASS,
      linkLabel: 'contract_password_change',
      languageCheck: true,
    },
    {
      label: 'email_address',
      required: true,
      component: 'textbox',
      type: 'text',
      size: 40,
      maxLength: 100,
      id: 'email-text-input',
      name: 'email',
      key: 'emailID',
      canModify: true,
      errorMessage: dynamicData.emailError,
      style: { width: 300 },
      ref: reference.emailIdRef,
    },
    {
      label: 'ip_address_restriction',
      component: 'textarea',
      name: 'iplimit',
      cols: 20,
      rows: 4,
      class: 'w100per',
      key: 'ipAddress',
      canModify: true,
      errorMessage: dynamicData.ipError,
      ref: reference.ipAddressRef,
    },
    {
      label: 'report',
      component: 'radio',
      name: 'incident',
      key: 'report',
      defaultValue: 1,
      errorMessage: dynamicData.reportError,
      options: [
        { filterKey: 1, filterValue: 'display' },
        { filterKey: 0, filterValue: 'hide' },
      ],
      filterKey: 'filterKey',
      filterValue: 'filterValue',
      canModify: true,
      disabled: dynamicData.disableReport,
      render: (value) => (value.report ? 1 : 0),
      trStyle: () => {
        return tenantVo.reportMenu ? {} : { display: 'none' };
      },
    },
  ];
}

/**
 * initializeRefs is used to create reference to component
 * @return referenceMap
 *
 */
export function initializeRefs() {
  return {
    emailIdRef: createRef(),
    ipAddressRef: createRef(),
    currentPasswordRef: createRef(),
    newPasswordRef: createRef(),
    confirmPasswordRef: createRef(),
    backButtonRef: createRef(),
  };
}

/**
 * initializeRefs is used to create reference to component
 * @param applicationVo
 * @param currentLanguage
 * @return startDate
 *
 */
export function getStartDateAndFirstUseDate(applicationVo, currentLanguage) {
  if (applicationVo.firstUseStartDate) {
    if (applicationVo.effectiveDateChange && !applicationVo.cancellationDate) {
      return {
        startDate: dateBasedOnLocale(applicationVo.effectiveDateChange, currentLanguage),
        firstStartDate: dateBasedOnLocale(applicationVo.firstUseStartDate, currentLanguage),
      };
    }
    return {
      startDate: dateBasedOnLocale(applicationVo.firstUseStartDate, currentLanguage),
      firstStartDate: '-',
    };
  }
  return {
    startDate: '-',
    firstStartDate: '-',
  };
}

/**
 * getFooterButton is used to build object values for  footerbuttons
 * @param disableButton
 * @return componentObjectValue
 */
export function getFooterButton(dynamicData) {
  return [
    {
      id: 'btn_ok',
      color: 'green',
      type: 'button',
      title: 'ok',
      disabled: dynamicData.disableOkButton,
      key: 'ok',
    },
    {
      id: 'btn_cancel',
      type: 'button',
      title: 'cancel',
      canModify: true,
      noMarginRight: true,
      key: 'cancel',
    },
    {
      id: 'btn_back',
      type: 'button',
      title: 'back',
      noMarginRight: true,
      key: 'back',
      ref: dynamicData.ref ? dynamicData.ref.backButtonRef : createRef(),
    },
  ];
}

/**
 * errorMessageConstants is used to hold all error message constant values
 */
export const errorMessageConstants = {
  emailError: {
    empty: 'manager_email_cannot_empty',
    length: 'email_max_length',
    invalidEmail: 'enter_form_of_email_address',
  },
  ipError: {
    length: 'ip_address_max_length',
    invalidIp: 'enter_form_ip_address',
  },
};

/**
 * setValidationErrorMessage is used to set error message if the validation is not satisified
 * @param key
 * @param validationResultObject
 * @param dynamicData
 *
 */
export function setValidationErrorMessage(key, validationResultObject, dynamicData) {
  const tempDynamicData = dynamicData;
  tempDynamicData[key] = errorMessageConstants[key][validationResultObject.error];
  return tempDynamicData;
}

