import React, { Component } from 'react';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import SBDatePicker from './SBDatePicker';
import LABELCONSTANTS from '../../../constants/LabelConstants';

import BackgroundAddImage from '../image/button_add.png';
import BackgroundRemoveImage from '../image/button_remove.png';

import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';

/**
 * SBDatePickerRange is to frame datepicker component witin specific range
 * @author Aswin.P
 */
class SBDatePickerRange extends Component {
  constructor(props) {
    super(props);
    this.state = {
      canReset: false,
      clearRange: false,
      restrictionRange: this.validateDateInterval(),
      disabledStyle: { opacity: 0.4, pointerEvents: 'none' },
    };
  }

  /**
   * componentDidUpdate is to reset the daterange with default value
   */
  componentDidUpdate() {
    const { value } = this.props;
    const [initialRange] = value;
    const { canReset } = this.state;
    if (canReset && isEmpty(initialRange.startDate))
      this.setState({
        canReset: false,
        clearRange: true,
        restrictionRange: this.validateDateInterval(),
      });
  }

  /**
   * calculateInterval is to calculate interval between date range
   * @param date
   * @param interval
   * @param periodic
   */
  calculateInterval = (date, interval, periodic) => {
    const leapYearDate = new Date(`${+date.getFullYear()}/02/29`);
    const isLeap = +leapYearDate.getMonth() === 1;
    if (periodic) return date < leapYearDate ? interval + 2 : interval + 1;
    return isLeap && date < leapYearDate ? interval : interval - 1;
  };

  /**
   * validateDateInterval is to set date range limit
   */
  validateDateInterval = () => {
    const { value, interval, periodicInterval } = this.props;
    const currentYear = new Date().getFullYear();
    const minDate = new Date(currentYear, 0, 1);
    const maxDate = new Date(currentYear, 11, 31);
    let upperBound = value[0].startDate;
    let lowerBound = value[value.length - 1].endDate;
    lowerBound =
      value.length > 1 && isEmpty(lowerBound) ? value[value.length - 2].endDate : lowerBound;
    lowerBound = !isEmpty(lowerBound)
      ? new Date(currentYear, +lowerBound.month, +lowerBound.day)
      : cloneDeep(maxDate);
    upperBound = !isEmpty(upperBound)
      ? new Date(currentYear, +upperBound.month, +upperBound.day)
      : cloneDeep(minDate);
    const initialDate = cloneDeep(upperBound);
    lowerBound = new Date(
      +upperBound > +lowerBound ? currentYear + 1 : currentYear,
      +lowerBound.getMonth(),
      +lowerBound.getDate(),
    );
    lowerBound.setDate(
      lowerBound.getDate() + this.calculateInterval(lowerBound, periodicInterval, true),
    );
    upperBound.setDate(
      upperBound.getDate() - this.calculateInterval(upperBound, periodicInterval, true),
    );
    upperBound.setFullYear(upperBound.getFullYear() + 1);
    lowerBound = +lowerBound > +upperBound ? cloneDeep(minDate) : lowerBound;
    const restrictionRange = value.map((record, index, list) => {
      const startDate = !isEmpty(record.startDate)
        ? new Date(currentYear, +record.startDate.month, +record.startDate.day)
        : cloneDeep(index ? lowerBound : minDate);
      const endDate = !isEmpty(record.endDate)
        ? new Date(currentYear, +record.endDate.month, +record.endDate.day)
        : cloneDeep(index ? upperBound : maxDate);
      const prevEndDate =
        index &&
        new Date(currentYear, +list[index - 1].endDate.month, +list[index - 1].endDate.day);
      if (+initialDate > +startDate) startDate.setFullYear(startDate.getFullYear() + 1);
      if (+startDate > +endDate) endDate.setFullYear(endDate.getFullYear() + 1);
      if (index && +initialDate > +prevEndDate)
        prevEndDate.setFullYear(prevEndDate.getFullYear() + 1);
      let startLimit = index
        ? new Date(
            prevEndDate.setDate(
              +prevEndDate.getDate() + this.calculateInterval(prevEndDate, periodicInterval, true),
            ),
          )
        : cloneDeep(minDate);
      startLimit = +startLimit > +upperBound ? upperBound : startLimit;
      let endLimit = cloneDeep(startDate);
      endLimit = new Date(
        endLimit.setDate(+endLimit.getDate() + this.calculateInterval(endLimit, interval)),
      );
      endLimit = +endLimit > +upperBound ? upperBound : endLimit;
      return {
        startDate: { maxDate: endDate, minDate: startLimit },
        endDate: { minDate: cloneDeep(startDate), maxDate: endLimit },
      };
    });
    return restrictionRange;
  };

  validateRowDateInterval = (dateInstance, row, key, restrictionRange) => {
    const dateRange = restrictionRange[row];
    const { value, interval, periodicInterval } = this.props;
    const isStart = key === 'startDate';
    const currentYear = new Date().getFullYear();
    const getUserDate = new Date(
      dateInstance.currentYear,
      dateInstance.currentMonth,
      dateInstance.currentDay,
    );
    value[row][key] = { day: +dateInstance.currentDay, month: +dateInstance.currentMonth };
    dateRange.endDate[isStart ? 'minDate' : 'maxDate'] = cloneDeep(getUserDate);
    let baseLimit = value[0].startDate;
    baseLimit = new Date(currentYear, +baseLimit.month, +baseLimit.day);
    const initialDate = cloneDeep(baseLimit);
    baseLimit.setDate(
      +baseLimit.getDate() - this.calculateInterval(baseLimit, periodicInterval, true),
    );
    baseLimit.setFullYear(baseLimit.getFullYear() + 1);
    const addIntervalDate = new Date(
      getUserDate.setDate(+getUserDate.getDate() + this.calculateInterval(getUserDate, interval)),
    );
    baseLimit = +addIntervalDate > +baseLimit ? baseLimit : addIntervalDate;
    dateRange.endDate.maxDate = isStart ? baseLimit : dateRange.endDate.maxDate;
    if (isStart) {
      const prevMaxEndDate = dateRange.endDate.maxDate;
      const checkEndDate = isEmpty(value[row].endDate)
        ? { day: +prevMaxEndDate.getDate(), month: +prevMaxEndDate.getMonth() }
        : value[row].endDate;
      const getEndDate = new Date(currentYear, +checkEndDate.month, +checkEndDate.day);
      if (+initialDate > +getEndDate) getEndDate.setFullYear(getEndDate.getFullYear() + 1);
      value[row].endDate =
        +getEndDate > +prevMaxEndDate
          ? { day: +prevMaxEndDate.getDate(), month: +prevMaxEndDate.getMonth() }
          : checkEndDate;
    }
    let dateRangeMax = dateRange.startDate.maxDate;
    dateRangeMax = new Date(currentYear, value[row].endDate.month, value[row].endDate.day);
    if (+initialDate > +dateRangeMax)
      dateRange.startDate.maxDate = dateRangeMax.setFullYear(dateRangeMax.getFullYear() + 1);
    return { restrictionRange, value };
  };

  setAddRemoveButton = () => [
    { id: 'buttonAdd', src: BackgroundAddImage, alt: '' },
    { id: 'buttonRemove', src: BackgroundRemoveImage, alt: '' },
  ];

  addRemoveClick = (buttonKey, event) => {
    const { addRemoveClick, value, interval, periodicInterval } = this.props;
    const { restrictionRange } = this.state;
    const restrictionRangeLength = restrictionRange.length - 1;
    const currentRange = value[restrictionRangeLength];
    let currentYear = new Date().getFullYear();
    let clearRange = false;
    if (addRemoveClick && buttonKey === 'buttonAdd' && !isEmpty(currentRange.startDate)) {
      const startRange = value[0].startDate;
      const endRange = currentRange.endDate;
      const upperBound = new Date(currentYear, +startRange.month, +startRange.day);
      let lowerBound = new Date(currentYear, +endRange.month, +endRange.day);
      currentYear = +upperBound > +lowerBound ? currentYear + 1 : currentYear;
      lowerBound = new Date(currentYear, +lowerBound.getMonth(), +lowerBound.getDate());
      lowerBound.setDate(
        lowerBound.getDate() + this.calculateInterval(lowerBound, periodicInterval, true),
      );
      upperBound.setDate(
        upperBound.getDate() - this.calculateInterval(upperBound, periodicInterval, true),
      );
      upperBound.setFullYear(upperBound.getFullYear() + 1);
      if (+lowerBound < +upperBound) {
        let validateLowerLimit = cloneDeep(lowerBound);
        validateLowerLimit.setDate(
          validateLowerLimit.getDate() + this.calculateInterval(validateLowerLimit, interval),
        );
        validateLowerLimit = +validateLowerLimit > +upperBound ? upperBound : validateLowerLimit;
        restrictionRange.push({
          startDate: { minDate: lowerBound, maxDate: upperBound },
          endDate: { minDate: lowerBound, maxDate: validateLowerLimit },
        });
        value.push({ startDate: {}, endDate: {} });
      }
    } else if (addRemoveClick && buttonKey === 'buttonRemove') {
      restrictionRange.pop();
      value.pop();
      clearRange = !value.length;
      if (clearRange) {
        const defaultRange = {
          minDate: new Date(currentYear, 0, 1),
          maxDate: new Date(currentYear, 11, 31),
        };
        value.push({ startDate: {}, endDate: {} });
        restrictionRange.push({
          startDate: cloneDeep(defaultRange),
          endDate: cloneDeep(defaultRange),
        });
      }
    }
    if (addRemoveClick) addRemoveClick(buttonKey, event, value);
    this.setState({ restrictionRange, clearRange });
  };

  makeList = () => ['_start-date', '', '_end-date'];

  onSelect = (dateText, dateInstance, row, key) => {
    const { onSelect, addRemove } = this.props;
    let { value } = this.props;
    let { restrictionRange } = this.state;
    if (addRemove) {
      const validateDate = this.validateRowDateInterval(dateInstance, row, key, restrictionRange);
      value = validateDate.value;
      restrictionRange = validateDate.restrictionRange;
    }
    if (onSelect) onSelect(dateText, dateInstance, value);
    this.setState({ restrictionRange, clearRange: false, canReset: true });
  };

 /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is to set grid data count in screen header label
   * @param label 
   * @param  contents 
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  render() {
    const {
      id,
      name,
      width,
      value,
      title,
      format,
      disabled,
      interval,
      separator,
      addRemove,
      periodicInterval,
      restrictLeapYear,
    } = this.props;
    const { restrictionRange, clearRange, disabledStyle } = this.state;
    const currentYear = new Date().getFullYear();
    const dateRangeLength = value.length - 1;
    return value.map((dateRecord, row) => {
      return (
        <div style={{ marginBottom: 5, padding: 2 }}>
          {this.makeList().map((element, index) => {
            const identifier = `${id || ''}${row}${element}`;
            const minDate =
              restrictionRange[row][`${index ? 'end' : 'start'}Date`].minDate ||
              new Date(currentYear, 0, 1);
            const maxDate =
              restrictionRange[row][`${index ? 'end' : 'start'}Date`].maxDate ||
              new Date(currentYear, 11, 31);
            let startDate = isEmpty(dateRecord.startDate)
              ? ''
              : new Date(currentYear, +dateRecord.startDate.month, +dateRecord.startDate.day);
            startDate =
              startDate && +startDate < +minDate
                ? new Date(startDate.setFullYear(startDate.getFullYear() + 1))
                : startDate;
            let endDate = isEmpty(dateRecord.endDate)
              ? ''
              : new Date(currentYear, +dateRecord.endDate.month, +dateRecord.endDate.day);
            endDate =
              endDate && +endDate < +minDate
                ? new Date(endDate.setFullYear(endDate.getFullYear() + 1))
                : endDate;
            endDate =
              endDate && +endDate > +maxDate
                ? new Date(endDate.setFullYear(endDate.getFullYear() - 1))
                : endDate;

            return [
              !index && (
                <span style={disabled ? disabledStyle : {}} className="mr10">
                  {this.addCountInfo(title, row + 1)}
                </span>
              ),
              element ? (
                <SBDatePicker
                  name={name}
                  width={width}
                  format={format}
                  id={identifier}
                  key={identifier}
                  minDate={minDate}
                  maxDate={maxDate}
                  className="w140px dateMonth"
                  endRange={!!index}
                  startRange={!index}
                  interval={interval}
                  selectedRange={row}
                  clearRange={clearRange}
                  periodicInterval={periodicInterval}
                  restrictLeapYear={restrictLeapYear}
                  value={!index ? startDate : endDate}
                  disabled={disabled || (!!index && !startDate) || row !== dateRangeLength}
                  onSelect={(dateText, dateInstance) =>
                    this.onSelect(dateText, dateInstance, row, !index ? 'startDate' : 'endDate')
                  }
                />
              ) : (
                <span style={disabled ? disabledStyle : {}}>
                  {` ${separator || LABELCONSTANTS.TILDE} `}
                </span>
              ),
              addRemove &&
                !row &&
                index > 1 &&
                this.setAddRemoveButton().map(button => (
                  <div className="floatright" style={disabled ? disabledStyle : {}}>
                    <a href onClick={event => this.addRemoveClick(button.id, event)}>
                      <img className="icon ml10" id={button.id} src={button.src} alt={button.alt} />
                    </a>
                  </div>
                )),
            ];
          })}
        </div>
      );
    });
  }
}

/**
 * mapStateToProps performs data fetch from language redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { data: localeData } = state.language;
  return { localeData }
}

export default connect(mapStateToProps)(SBDatePickerRange);
