/**
 * NoticeList is used to list notifications registered and perform search
 * @author: V.Vijayaragavan
 */
import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { post, performDelete } from '../../../actions/commonActions';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import NoticeListSearch from '../component/NotificationList/NoticeListSearch';
import { fetchLanguageList } from '../../../actions/FieldsAction';
import NotificationFooterComponent from '../component/NotificationFooterComponent/NotificationFooterComponent';
import NoticeListGrid from '../component/NotificationList/NoticeListGrid';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';
import checkLanguage from '../../../language/checkLanguage';
import { getListFootersButton } from '../constants/NotificationRegTableLists';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../../constants/CommonConstants';
import { changeNoticeListOrder } from '../constants/InfoFromSoftBankConstants';

const resultsPerPage = 'notice_list';
class NoticeList extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      selectedRowValue: {},
      gridMessage: '',
      searchParam: { languageCode: 'empty' },
      pageLoading: true,
      gridLoading: false,
      openDialog: false,
      searchBoxRef: createRef(),
      noticeList: [],
      languageDropDownValues: [{ code: 'empty', languageName: this.languageLabelCheck('all') }],
    };
  }

  /**
   * componentDidMount performs api operation to load notice list and language list
   */
  componentDidMount() {
    const { fetchLanguageList: getLanguageList, languageVo } = this.props;
    const { searchBoxRef, languageDropDownValues } = this.state;
    let languageList = languageVo;
    const generateComponent = async () => {
      await this.performSearch();
      if (!languageVo.length) {
        const languageResponse = await post(APIENDPOINTS.LANGUAGECODELIST, { searchedValue: '' });
        languageList = await languageResponse.json();
        getLanguageList(languageList);
      }
      await this.setState({
        pageLoading: false,
        languageDropDownValues: languageDropDownValues.concat(languageList),
      });
      if (searchBoxRef.current) searchBoxRef.current.focus();
    };
    generateComponent();
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * handleRadioChange is used to handle radio button change event in Notice List Grid
   * @param value
   */
  handleRadioChange = (value) => {
    let { selectedRowValue } = this.state;
    selectedRowValue = value;
    this.setState({ selectedRowValue });
  };

  /**
   * handlePaginationClick is used to handle pagination event in Notice List Grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    if (tableData.length) {
      this.setState({ selectedRowValue: tableData[0] });
    }
  };

  /**
   * handleSelectChange is used to handle select combo event
   */
  handleSelectChange = () => {
    this.performSearch();
  };

  /**
   * onDeleteButtonClick is used to handle delete button event in Notice List Grid
   */
  onDeleteButtonClick = () => {
    const { selectedRowValue } = this.state;
    performDelete(`${APIENDPOINTS.NOTICEDELETE}/${selectedRowValue.noticeId}`)
      .then((response) => response.json())
      .then((data) => {
        if (data.status) {
          this.performSearch();
          this.setState({
            gridMessage: 'deletion_is_completed',
          });
        } else {
          this.setState({
            gridMessage: data.message,
          });
        }
      });
  };

  /**
   * onHandleClick is used to handle footer button event in Notice List Grid
   */
  onHandleClick = (key) => {
    const { history } = this.props;
    const { selectedRowValue } = this.state;
    if (key === 'change') {
      history.push({
        pathname: ROUTEENDPOINTS.NOTIFICATIONUPDATE,
        state: { dataSource: selectedRowValue },
      });
    } else if (key === 'delete') {
      this.onDeleteButtonClick();
    }
  };

  /**
   * handleSearchAction is used toperform search operation in Notice List Grid
   * @param values
   */
  handleSearchAction = async (searchValues, performSearch) => {
    const searchParam = searchValues;
    if (!searchValues.languageCode) {
      searchParam.languageCode = 'empty';
    }
    await this.setState({ searchParam });
    if (performSearch) this.performSearch();
  };

  /**
   * performSearch is used to perform api search operation in Notice List Grid
   */
  performSearch = async () => {
    const { noticeId, finishScreen } = this.props;
    this.setState({
      gridLoading: true,
    });
    const { searchParam } = this.state;
    if (searchParam.startDate) searchParam.startDate = new Date(searchParam.startDate);
    if (searchParam.endDate) searchParam.endDate = new Date(searchParam.endDate);
    const response = await post(APIENDPOINTS.NOTICELIST, searchParam);
    let noticeVoList = await response.json();
    if (noticeVoList.length && finishScreen && noticeId) {
      noticeVoList = changeNoticeListOrder(noticeVoList, noticeId);
    }
    if (noticeVoList.length) {
      this.setState({ selectedRowValue: noticeVoList[0] });
    }
    this.setState({
      noticeList: noticeVoList,
      gridLoading: false,
    });
  };

  /**
   * render is used to render the component
   */
  render() {
    const { finishScreen } = this.props;
    const {
      gridMessage,
      pageLoading,
      searchBoxRef,
      gridLoading,
      openDialog,
      languageDropDownValues,
      noticeList,
      searchParam,
    } = this.state;

    return (
      <>
        <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
          {!finishScreen && (
            <>
              <SBContentHeader
                title={notificationHeaderInformation('list').title}
                description={notificationHeaderInformation('list').description}
              />
              <NoticeListSearch
                searchParam={searchParam}
                languageList={languageDropDownValues}
                reference={searchBoxRef}
                onSearchAction={(searchValues, performSearch) =>
                  this.handleSearchAction(searchValues, performSearch)
                }
              />
            </>
          )}
          <CircularSpinner loading={gridLoading} css={CommonConstants.gridCircularSpinnerStyle}>
            <NoticeListGrid
              searchParam={searchParam}
              noticeList={noticeList}
              countInfoContents={noticeList.length}
              handleRadioChange={(event) => this.handleRadioChange(event)}
              handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
              handleSelectChange={() => this.handleSelectChange()}
              defaultSelected={resultsPerPage}
            />
            <NotificationFooterComponent
              buttonsList={getListFootersButton(!noticeList.length)}
              onHandleClick={(key) => this.onHandleClick(key)}
            />
          </CircularSpinner>
          <SBDialog
            open={openDialog}
            header="error"
            type="error"
            okButtonTitle="ok"
            content="user_already_deleted"
            onOk={() => {
              this.setState({ openDialog: false });
            }}
            width="400px"
          />
        </CircularSpinner>
        <span className="red bold">{this.languageLabelCheck(gridMessage)}</span>
      </>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    fetchLanguageList: (data) => {
      dispatch(fetchLanguageList(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { languageVo } = state.commonFields;
  return {
    languageVo,
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NoticeList.propTypes = {
  fetchLanguageList: commonPropTypes().fetchLanguageList.isRequired,
  languageVo: PropTypes.arrayOf(PropTypes.object),
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  history: commonPropTypes().history.isRequired,
  finishScreen: PropTypes.bool,
  noticeId: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NoticeList.defaultProps = {
  isNotMultiLang: false,
  languageVo: [],
  finishScreen: false,
  noticeId: '',
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NoticeList));
