/** FieldsReducer is used to map the repose data to the fields
 * @author P.Vinothpandian
 */

import {
  FETCH_TIMEZONE_ENTITY,
  FETCH_LANGUAGE_ENTITY,
  FETCH_AUTHORITY_ENTITY,
  FETCH_CAMPAIGN_ENTITY,
  FETCH_COUNTRY_ENTITY,
} from '../../../../actions/ActionTypes';

const initialState = {
  timeZoneVo: [],
  languageVo: [],
  AuthorityVo: [],
  campaignVo: [],
  countryVo: [],
};

function commonFields(state = initialState, action) {
  const { type, data } = action;
  switch (type) {
    case FETCH_TIMEZONE_ENTITY:
      return {
        ...state,
        timeZoneVo: data || {},
      };
    case FETCH_LANGUAGE_ENTITY:
      return {
        ...state,
        languageVo: data || {},
      };
    case FETCH_AUTHORITY_ENTITY:
      return {
        ...state,
        AuthorityVo: data || {},
      };
    case FETCH_CAMPAIGN_ENTITY:
      return {
        ...state,
        campaignVo: data || {},
      };
    case FETCH_COUNTRY_ENTITY:
      return {
        ...state,
        countryVo: data || {},
      };
    default:
      return state;
  }
}

export default commonFields;
