/**
 * setInfoIndex is used to load compoents based on information content
 * @param param
 * @return infoindex
 */

export function setInfoIndex(param) {
  if (
    isNotUndefinedOrNotEmpty(param.fullNameThree) ||
    isNotUndefinedOrNotEmpty(param.contactInfoThree) ||
    isNotUndefinedOrNotEmpty(param.emailThree) ||
    isNotUndefinedOrNotEmpty(param.departmentNameThree)
  ) {
    return 3;
  }

  if (
    isNotUndefinedOrNotEmpty(param.fullNameTwo) ||
    isNotUndefinedOrNotEmpty(param.contactInfoTwo) ||
    isNotUndefinedOrNotEmpty(param.emailTwo) ||
    isNotUndefinedOrNotEmpty(param.departmentNameTwo)
  ) {
    return 2;
  }
  return 1;
}

/**
 * setSpecialIndex is used to load component based on information content
 * @param param
 * @return special Index
 */

export function setSpecialIndex(param) {
  if (isNotUndefinedOrNotEmpty(param.operationMemoFive)) {
    return 5;
  }
  if (isNotUndefinedOrNotEmpty(param.operationMemoFour)) {
    return 4;
  }
  if (isNotUndefinedOrNotEmpty(param.operationMemoThree)) {
    return 3;
  }
  if (isNotUndefinedOrNotEmpty(param.operationMemoTwo)) {
    return 2;
  }
  return 1;
}

/**
 * setFileDataSource is used to load  file source
 * @return fileSourceMap
 */

export function setFileDataSource() {
  return {
    individualDataFile: null,
    attachDataFile: null,
  };
}
