import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { isUndefined } from 'underscore';
import SBTable from '../../common/component/sbtable/SBTable';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBComboBox from '../../common/component/combobox/SBComboBox';
import { get, post } from '../../actions/commonActions';
import { dateTimeFormatWithoutOffset, currentDate } from '../../common/utils/dateUtils';
import ContentDeleteDialog from '../component/ContentDeleteDialog';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import APIENDPOINTS from '../../constants/APIEndPoints';
import checkLanguage from '../checkLanguage';
import { CommonConstants } from '../../constants/CommonConstants';
import RouteEndPoints from '../../constants/RouteEndPoints';


const fileDownload = require('js-file-download');

const styles = {
  space: {
    marginRight: '10px',
  },
};

const column = [
  {
    title: '',
    dataIndex: 'render-radio',
    styles: { width: 50, minWidth: 50 },
    textAlign: 'center',
  },
  {
    title: 'language_code',
    styles: { width: 120, minWidth: 120 },
    dataIndex: 'code',
    textAlign: 'center',
    sorter: true,
  },
  {
    title: 'label',
    dataIndex: 'label',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'value',
    dataIndex: 'value',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'updated_by',
    dataIndex: 'updatedBy',
    sorter: true,
    textAlign: 'center',
  },
  {
    title: 'updated_date',
    dataIndex: 'updatedDate',
    sorter: true,
    textAlign: 'center',
    render: (record) => dateTimeFormatWithoutOffset(record.updatedDate),
  },
];
const resultPerPageKey = 'lang_content_list';
let currentRowIndex = 0;
/**
 * LanguageContent is to view user language contents.
 * @author Gobi R
 */
class LanguageContent extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      searchInput: '',
      searchedValue: '',
      languageContentList: [],
      languageCode: props.location.languageCode || 'All',
      languageCodeList: [],
      isButtonDisabled: true,
      selectedRow: {},
      defaultPage: 1,
      openDelete: false,
      loading: true,
      isDefaultCode: false,
      gridLoading: false,
      langCodeObj: {},
    };
    this.initialFocus = createRef();

  }

  /**
   * componentDidMount is to fetch already existing language data.
   */
  componentDidMount() {
    this.load(true);
    get(APIENDPOINTS.LANGUAGECODES)
      .then((response) => response.json())
      .then((data) => {
        data.unshift(this.languageLabelCheck('all'));
        const map = {
          ...data,
        };
        map[this.languageLabelCheck('all')] = 'All';
        this.setState({
          languageCodeList: data,
          langCodeObj: map,
        });
      });
  }

  /**
   * load is to initiate load grid data.
   * @param initial
   */
  load = (initial) => {
    const { searchInput } = this.state;
    this.setState({ searchedValue: searchInput }, () => this.searchInputResults(initial));
  };

  /**
   * searchInputResults is to load grid data.
   * @param initial
   */
  searchInputResults = (initial) => {
    const { languageCode, searchedValue } = this.state;
    const paramMap = { languageCode, searchedValue };
    this.setState({ gridLoading: true });
    post(APIENDPOINTS.CONTENTLIST, paramMap)
      .then((response) => response.json())
      .then((data) => {
        const selectedRow = data.length === 0 ? {} : data[0];
        this.setState(
          {
            languageContentList: data,
            loading: false,
            selectedRow,
            defaultPage: 1,
            gridLoading: false,
          },
          this.setSelectedRow(selectedRow),
        );
        if (initial && this.initialFocus.current) {
          this.initialFocus.current.focus();
        }
      });
  };

  /**
   * setSelectedRow is to set Selected Row data.
   * @param selectedRow
   */
  setSelectedRow = (selectedRow) => {
    this.setState({
      selectedRow,
      isButtonDisabled: false,
      isDefaultCode: this.checkIsDefaultCode(selectedRow.code),
    });
  };

  /**
   * handlePaginationClick is to handle pagenation.
   * @param data
   */
  handlePaginationClick = (data) => {
    this.setSelectedRow(data[0]);
  };

  /**
   * handleSort is to hadle sorting.
   * @param data
   */
  handleSort = (data) => {
    this.setSelectedRow(data.length === 0 ? {} : data[currentRowIndex]);
    if(!isUndefined(currentRowIndex)) this.setState({isDefaultCode:this.checkIsDefaultCode(data[currentRowIndex].code)});
  };

  /**
   * handleRadioChange is to handle row select.
   * @param selectedRow
   */
  handleRadioChange = (selectedRow,selectedKey) => {
    this.setSelectedRow(selectedRow);
    currentRowIndex = selectedKey;
  };

  /**
   * handleSelectChange is to handle no of rows display.
   * @param data
   */
  handleSelectChange = (data) => {
    this.setState({ gridLoading: true });
    setTimeout(() => {
      this.setSelectedRow(data.length === 0 ? {} : data[0]);
      this.setState({ gridLoading: false });
    }, 2000);
  };

  /**
   * checkIsDefaultCode is to check default language
   * and diable change and delete buttons accordingly.
   * @param code
   */
  checkIsDefaultCode = (code) => {
    let isDefaultCode = false;
    if (code === 'ja') {
      isDefaultCode = true;
    }
    return isDefaultCode;
  };

  /**
   * onDeleteClick is to handle delete button click.
   */
  onDeleteClick = () => {
    this.setState({ openDelete: true });
  };

  /**
   * closeDeleteDialog is to delete close dialog without saving.
   * @param loading
   */
  closeDeleteDialog = (loading) => {
    this.setState({ openDelete: false, loading });
  };

  /**
   * onChangeClick is to open content edit page.
   */
  onChangeClick = () => {
    const { history } = this.props;
    const { selectedRow } = this.state;
    history.push(RouteEndPoints.LANGUAGECONTENTUPDATE, { mode: 'EDIT', selectedRow });
  };

  /**
   * onAddLanguage is to handle new langauage content addition.
   */
  onAddLanguage = () => {
    const { history } = this.props;
    history.push('/setting/adminLangContentAdd');
  };

  /**
   * onCSVDownload is to download CSV file with current records in grid.
   */
  onCSVDownload = () => {
    const { languageCode, searchedValue } = this.state;
    const paramMap = { languageCode, searchedValue };
    post(APIENDPOINTS.CONTENTDOWNLOAD, paramMap)
      .then((response) => response.text())
      .then((data) => {
        if (data !== 'Failed') {
          fileDownload(data, `LanguageContent_${currentDate()}.csv`);
        }
      });
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * languageCodeChange is to handle language change.
   * @param event
   */
  languageCodeChange = (event) => {
    const { langCodeObj } = this.state;
    if (event.target) {
      if (event.target.id === 'lang_code') {
        this.setState({ languageCode: langCodeObj[event.target.value] || event.target.value }, () =>
          this.load(),
        );
      } else {
        this.setState({ searchInput: event.target.value });
      }
    }
  };

  /**
   * pageChangeHandler is to handle page change.
   * @param event
   */
  pageChangeHandler = (newPage) => {
    this.setState({ defaultPage: newPage });
  };

  /**
   * onKeyDown is to handle enter event to load grid.
   * @param event
   */
  onKeyDown = (event) => {
    if (event.keyCode === 13) this.load();
  };

  /**
   * render is to load fields.
   * @returns jsx elements
   */
  render() {
    const {
      languageContentList,
      languageCode,
      languageCodeList,
      isButtonDisabled,
      openDelete,
      selectedRow,
      loading,
      isDefaultCode,
      searchInput,
      defaultPage,
      gridLoading,
    } = this.state;
    return (
      <div style={{ width: '100%' }}>
        <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
          <SBContentHeader
            title="language_content_list"
            description={['display_list_language_content']}
          />
          <SBButton add onClick={(event) => this.onAddLanguage(event)} />
          <div className="border mb15 p15">
            <SBTextbox
              type="text"
              name="search_data"
              width={200}
              ref={this.initialFocus}
              className="mr10"
              value={searchInput}
              onKeyDown={this.onKeyDown}
              onChange={(event) => this.languageCodeChange(event)}
              style={styles.space}
            />
            <SBButton
              type="submit"
              name="Search"
              color="green"
              title="search"
              onClick={() => this.load()}
            />
            <span className="lineheight mr5">{this.languageCodeChange('language_code_colon')}</span>
            <SBComboBox
              name="lang_code"
              id="lang_code"
              style={{ width: 80 }}
              value={languageCode}
              isNotMultiLang
              defaultValue={languageCode}
              onChange={(event) => this.languageCodeChange(event)}
              options={languageCodeList}
            />
          </div>

          <CircularSpinner
            loading={gridLoading}
            css={{ position: 'absolute', top: '62%', left: 'calc(40% - 55px)' }}
          >
            {languageContentList.length ? (
              <SBTable
                dataSource={languageContentList}
                columns={column}
                pagination
                defaultPage={defaultPage}
                countInfo="language_list_count"
                countInfoContents={languageContentList.length}
                filterMessage="results_per_page"
                defaultSelected={resultPerPageKey}
                filterList={[
                  { key: 25, value: '25_items' },
                  { key: 50, value: '50_items' },
                  { key: 100, value: '100_items' },
                  { key: 'all', value: 'all' },
                ]}
                handleRadioChange={(data,selectedKey) => this.handleRadioChange(data,selectedKey)}
                defaultSelectedKeys={[0]}
                handlePaginationClick={(data) => this.handlePaginationClick(data)}
                handleSort={(data) => this.handleSort(data)}
                handleSelectChange={(data) => this.handleSelectChange(data)}
                onPageChange={this.pageChangeHandler}
              />
            ) : (
              <>
                <span>{this.languageLabelCheck('no_result_found')}</span>
                <br />
              </>
            )}
            <SBButton
              type="button"
              name="csv download"
              id="csv download"
              title="csv_download"
              color="gray"
              onClick={(event) => this.onCSVDownload(event)}
            />

            <div className="floatright">
              <SBButton
                type="button"
                name="change"
                id="change"
                title="change"
                color="green"
                disabled={isDefaultCode || isButtonDisabled || !languageContentList.length}
                onClick={(event) => this.onChangeClick(event)}
              />
              <SBButton
                type="button"
                name="delete"
                id="delete"
                title="delete"
                disabled={isDefaultCode || isButtonDisabled || !languageContentList.length}
                onClick={(event) => this.onDeleteClick(event)}
              />
            </div>
          </CircularSpinner>
        </CircularSpinner>
        <ContentDeleteDialog
          open={openDelete}
          closeDialog={(isloading) => this.closeDeleteDialog(isloading)}
          selectedRow={selectedRow}
          refresh={() => this.load(true)}
        />
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
LanguageContent.propTypes = {
  localeData: PropTypes.objectOf(PropTypes.string),
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  location: PropTypes.shape({
    mode: PropTypes.string,
    languageCode: PropTypes.string,
    selectedRow: PropTypes.shape({
      code: PropTypes.string,
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  }).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
LanguageContent.defaultProps = {
  localeData: {},
  isNotMultiLang: false,
};

/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    defaultLang: state.language.default,
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(LanguageContent);
