/**
 * AppSearchPanel is used to load search panel
 * @author: P.Vinothpandian
 *
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import checkLanguage from '../../../language/checkLanguage';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBComboBox from '../../../common/component/combobox/SBComboBox';

/**
 * getComboOptions is used to get the  combo options
 * @return comboOptions
 */

const comboOptions = [
  { value: 1, option: 'csv_manage_info' },
  { value: 2, option: 'csv_id_difference' },
  { value: 4, option: 'csv_order_number' },
  { value: 5, option: 'approval_number' },
];

const deletedStatus = [
  { filterKey: 0, filterValue: 'all' },
  { filterKey: 1, filterValue: 'yes' },
  { filterKey: 2, filterValue: 'no' },
];

/**
 * tableSearchList is used to load the  table search list
 * @param dynamicData
 * @param reference
 * @return tableSearchList
 */

function tableSearchList(dynamicData, reference) {
  return [
    {
      label: 'dm_code',
      component: 'textbox',
      type: 'text',
      size: 20,
      maxLength: 20,
      id: 'dmcode',
      name: 'dmcode',
      key: 'dmCode',
      errorMessage: dynamicData.dmCodeError,
      style: { width: 220 },
      ref: reference.dmCodeRef,
    },
    {
      label: 'business_name',
      component: 'textbox',
      classes: 'w100per',
      type: 'text',
      maxLength: 100,
      id: 'business-name-text-input',
      name: 'name',
      key: 'businessName',
      errorMessage: dynamicData.businessNameError,
      ref: reference.businessNameRef,
    },
    {
      label: 'company_number',
      classes: 'w100per',
      component: 'textbox',
      type: 'text',
      maxLength: 100,
      id: 'company_number_text_input',
      name: 'company_number',
      key: 'companyNumber',
      errorMessage: dynamicData.companyNumberError,
      ref: reference.companyNumberRef,
    },
    {
      label: 'use_start_date',
      idStart: 'start_date',
      idStop: 'end_date',
      component: 'daterange',
      startkey: 'startDate',
      stopkey: 'stopDate',
      width: 142,
      minDate: new Date('2011/01/01'),
      maxDate: new Date('2050/12/31'),
      forwadedRefStart: reference.startDateRef,
      forwadedRefStop: reference.stopDateRef,
      renderStart: value => (value.startDate ? value.startDate : ''),
      renderStop: value => (value.stopDate ? value.stopDate : ''),
      errorMessage: dynamicData.dateError,
    },
    {
      label: 'deleted_status',
      component: 'radio',
      name: 'dmCodeStatus',
      key: 'dmCodeStatus',
      options: deletedStatus,
      filterKey: 'filterKey',
      filterValue: 'filterValue',
      canModify: true,
      render: value => value.dmCodeStatus,
    },
  ];
}

class AppSearchPanel extends Component {
  constructor() {
    super();
    this.state = {
      csvKey: 1,
    };
  }
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const {
      errorMessage,
      searchSource,
      onHandleClick,
      onTableFrameEvent,
      onKeyDown,
      reference,
    } = this.props;
    const { csvKey } = this.state;
    return (
      <div className="contents-main">
        <div className="border p15">
          <div>
            <CustomTableFrame
              tableList={tableSearchList(errorMessage, reference)}
              dataSource={searchSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => onKeyDown(event, value)}
            />
          </div>
          <div className="clearfix">
            <div className="floatleft">
              <SBComboBox
                name="csv_type"
                id="csv_type"
                style={{ marginRight: 10 }}
                options={comboOptions}
                filterKey="value"
                filterValue="option"
                onChange={event => this.setState({ csvKey: event.target.value })}
              />
              <SBButton
                id="btn_csv_contribution"
                type="button"
                title="csv_output"
                color="gray"
                onClick={() => onHandleClick('csvoutput', csvKey)}
              />
            </div>

            <div className="floatright">
              <SBButton
                id="search"
                name="search"
                type="button"
                title="search"
                color="green"
                onClick={() => onHandleClick('search')}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

AppSearchPanel.propTypes = {
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  onHandleClick: PropTypes.func.isRequired,
  onTableFrameEvent: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  searchSource: PropTypes.objectOf(PropTypes.string).isRequired,
  errorMessage: PropTypes.objectOf(PropTypes.string).isRequired,
  reference: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */

AppSearchPanel.defaultProps = {
  isNotMultiLang: false,
  reference: {},
};

export default connect(mapStateToProps)(withRouter(AppSearchPanel));
