import React, { Component } from 'react';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../../common/component/tableLayout/SBTableLayout';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBComboBox from '../../../common/component/combobox/SBComboBox';
import SBTextbox from '../../../common/component/sbtextbox/SBTextbox';
import SBTable from '../../../common/component/sbtable/SBTable';
import LABELCONSTANTS from '../../../constants/LabelConstants';
import OPERATIONMANAGEMENTCONSTANTS from '../../OperationManagementConstants';

function onDateTextInputChange() {}
const tableData = [
  {
    key: 'Type',
    value: (
      <SBComboBox
        name="logtype"
        id="logtype"
        options={OPERATIONMANAGEMENTCONSTANTS.logTypeOptions}
        filterKey="value"
        filterValue="option"
      />
    ),
  },
  {
    key: 'Term',
    value: [
      <SBTextbox
        placeholder="yyyy/mm/dd hh:mm"
        type="text"
        name="search_from_date"
        size="20"
        maxlength="16"
        style={{ marginRight: 10 }}
        onChange={() => onDateTextInputChange()}
      />,
      LABELCONSTANTS.TILDE,
      <SBTextbox
        placeholder="yyyy/mm/dd hh:mm"
        type="text"
        name="search_to_date"
        size="20"
        maxlength="16"
        style={{ marginLeft: 10 }}
        onChange={() => onDateTextInputChange()}
      />,
    ],
  },
  {
    key: 'Key Word',
    value: (
      <SBTextbox
        type="text"
        name="search_text"
        size="20"
        maxlength="20"
        style={{ width: 264 }}
        onChange={() => onDateTextInputChange()}
      />
    ),
  },
];

const dataSource = [
  {
    date: '2020/03/02 15:29:22',
    loginId: 'User1',
    type: 'login',
    result: 'success',
    content: 'Proper Login',
    sourceIpAddress: '118.185.219.234',
  },
  {
    date: '2020/03/02 15:29:22',
    loginId: 'User2',
    type: 'login',
    result: 'failed',
    content: 'Login failed( Wrong password)',
    sourceIpAddress: '118.185.219.234',
  },
  {
    date: '2020/03/02 15:29:22',
    loginId: 'User1',
    type: 'login',
    result: 'success',
    content: 'Proper Login',
    sourceIpAddress: '118.185.219.234',
  },
];

const comboItemsValue = [
  {
    key: 25,
    value: '25 items',
  },
  {
    key: 50,
    value: '50 items',
  },
  {
    key: 100,
    value: '100 items',
  },
];

const resultsPerPageKey = 'log_search';
class LogSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [],
      datasource: [],
      shouldDisplay: false,
    };
  }

  onSearchButtonClick() {
    this.setState({
      columns: this.getColumns(),
      datasource: dataSource,
      shouldDisplay: true,
    });
  }

  getColumns = () => {
    return [
      {
        title: 'Date',
        dataIndex: 'date',
        rowKey: 'date',
        sorter: false,
        styles: { minWidth: 80, width: 80 },
      },
      {
        title: 'Login ID',
        dataIndex: 'loginId',
        rowKey: 'loginId',
        sorter: false,
      },
      {
        title: 'Type',
        dataIndex: 'type',
        rowKey: 'type',
        sorter: false,
      },
      {
        title: 'Result',
        dataIndex: 'result',
        rowKey: 'result',
        sorter: false,
      },
      {
        title: 'Content',
        dataIndex: 'content',
        rowKey: 'content',
        sorter: false,
      },
      {
        title: 'Source IP Address',
        dataIndex: 'sourceIpAddress',
        rowKey: 'sourceIpAddress',
        sorter: false,
      },
    ];
  };

  onCSVOutputButtonClick = () => {};

  render() {
    const { screenTitleData } = this.props;
    const { shouldDisplay } = this.state;
    const { datasource } = this.state;
    const { columns } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <div style={{ height: '91px' }}>
                <SBContentHeader
                  title={screenTitleData.title}
                  description={screenTitleData.description}
                />
                <div className="contents-main mb15">
                  <div className="border p15">
                    <div className="mb10">
                      <SBTableLayout tableData={tableData} filterKey="key" filterValue="value" />
                    </div>
                    <div style={{ height: 24 }}>
                      <div className="floatleft">
                        {shouldDisplay && (
                          <SBButton
                            type="button"
                            name="csvOutput"
                            color="gray"
                            title="CSV Output"
                            disabled={!datasource.length}
                            onClick={() => {
                              this.onCSVOutputButtonClick();
                            }}
                          />
                        )}
                      </div>
                      <div className="floatright">
                        <SBButton
                          type="submit"
                          name="search"
                          color="green"
                          title="Search"
                          noMarginRight
                          onClick={() => {
                            this.onSearchButtonClick();
                          }}
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="contents-main w100per">
                  {shouldDisplay && (
                    <SBTable
                      columns={columns}
                      dataSource={datasource}
                      loading={false}
                      pagination
                      countInfo={`Search result：${datasource.length} items`}
                      filterList={comboItemsValue}
                      handleRadioChange={data => this.handleRadioChange(data)}
                      defaultSelected={resultsPerPageKey}
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default LogSearch;
