/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/** SBComponents */
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';

/** GroupManagement Components */
import GroupManagementFooter from './GroupManagementFooter';

/** Utility Files */
import checkLanguage from '../../language/checkLanguage';
import { validateTableData } from '../common/GroupSettingCommonFunctions';
import SBPolicyTab from '../../common/component/sbpolicytab/SBPolicyTab';
import SBButton from '../../common/component/sbbutton/SBButton';

/**
 * GroupManagementPolicy is to render policyRegistration panel
 * @author Aswin Palanisamy
 */
class GroupManagementPolicy extends Component {
  /**
   * languageLabelCheck is to set language label
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * onListClick performs tab switch operation in policy registration
   * @param policyPanelKey
   */
  onListClick = (policyPanelKey) => {
    const { setPanelKey } = this.props;
    if (setPanelKey) setPanelKey(policyPanelKey);
  };

  /**
   * onHandleClick performs button click events
   * @param buttonKey
   */
  onHandleClick = (buttonKey) => {
    const { onHandleClick } = this.props;
    if (onHandleClick) onHandleClick(buttonKey);
  };

  /**
   * onTableFrameEvent handle table frame events
   * @param key
   * @param event
   * @param value
   * @param dateList
   */
  onTableFrameEvent = (key, event, value, headKey, dateList) => {
    const { onTableFrameEvent } = this.props;
    if (onTableFrameEvent) onTableFrameEvent(key, event, value, headKey, dateList);
  };

  /**
   * onKeyDown performs key press event in policy registration
   * @param event
   * @param value
   * @param key
   */
  onKeyDown = (event, value, key, headKey) => {
    const { onKeyDown } = this.props;
    if (onKeyDown) onKeyDown(event, value, key, headKey);
  };

  /**
   * setPageChange is set data for selected wifi panel
   * @param policyPanelKey
   * @param index
   * @param wifiList
   * @param currentIndex
   */
  setPageChange = (wifiList, index, currentIndex) => {
    const { setPageChange } = this.props;
    if (setPageChange) {
      setPageChange(wifiList, index, currentIndex);
    }
  };

  /**
   * addRemoveClick perform add/remove wifi panel click event
   * @param policyPanelKey
   * @param option
   */
  addRemoveClick = (policyPanelKey, option) => {
    const { addRemoveClick } = this.props;
    if (addRemoveClick) addRemoveClick(policyPanelKey, option);
  };

  /**
   * buildPanelBody is used to build body of the policy panel
   * @return component
   */
  buildPanelBody = () => {
    const {
      getList,
      buttonList,
      policyPanel,
      currentIndex,
      limitContent,
      policyPanelKey,
      mainPolicyMapper,
      createdPolicyPanelKeys,
      createButtonClick,
    } = this.props;
    const panelMapper = policyPanel[policyPanelKey];
    const createButtonComponent = () => (
      <div id="no-set" className="no-set" style={{ minHeight: 'inherit' }}>
        <div className="no-set-container">
          <div className="title">{this.languageLabelCheck(policyPanelKey)}</div>
          <SBButton
            id="create-btn"
            type="button"
            name="create"
            title="create"
            onClick={() => createButtonClick(policyPanelKey)}
          />
        </div>
      </div>
    );
    const panelComponents = () => (
      <>
        <div>
          <SetHeader
            caption={this.languageLabelCheck(policyPanelKey)}
            setEvent={(name) => this.addRemoveClick(policyPanelKey, name)}
            buttonList={buttonList}
          >
            {buttonList && policyPanelKey === 'wifi_settings' && (
              <SwitchTabButton
                getList={getList}
                currentIndex={currentIndex}
                setPageEvent={(index) => this.setPageChange(getList, index, currentIndex)}
              />
            )}
          </SetHeader>
          {panelMapper &&
            panelMapper.map((record) => [
              record.header && (
                <h2 className="title-corner relative">{this.languageLabelCheck(record.header)}</h2>
              ),
              <CustomTableFrame
                defaultValue={{}}
                key={policyPanelKey}
                dataSource={mainPolicyMapper}
                tableList={validateTableData(
                  record.content,
                  mainPolicyMapper,
                  record.header,
                  currentIndex,
                )}
                onKeyDown={(event, value, key, headKey) =>
                  this.onKeyDown(event, value, key, headKey)
                }
                onTableFrameEvent={(key, event, value, headKey, dateList) =>
                  this.onTableFrameEvent(key, event, value, headKey, dateList)
                }
              />,
            ])}
        </div>
        <div
          className="red bold"
          style={{ display: limitContent && policyPanelKey === 'wifi_settings' ? 'block' : 'none' }}
        >
          {this.languageLabelCheck('wifi_limit_message')}
        </div>
      </>
    );
    return createdPolicyPanelKeys.includes(policyPanelKey)
      ? panelComponents()
      : createButtonComponent();
  };

  /**
   * render performs policy registration screen rendering
   * @return PolicyRegistration
   */
  render() {
    const { footer, policyPanel, policyPanelKey, createdPolicyPanelKeys } = this.props;
    return (
      <div className="easy-setting contents-main-border clearfix">
        <div className="clearfix mb10">
          <div className="floatleft left-column">
            <ul className="tab">
              {Object.keys(policyPanel).map((panelKey) => {
                return (
                  <li key={panelKey} style={{ cursor: 'pointer' }}>
                    <SBPolicyTab
                      id={policyPanelKey}
                      name={policyPanelKey}
                      onChange={() => this.onListClick(panelKey)}
                      tabTitle={panelKey}
                      checked={createdPolicyPanelKeys.includes(panelKey)}
                      className={panelKey === policyPanelKey ? 'selected' : ''}
                    />
                  </li>
                );
              })}
            </ul>
          </div>
          <div className="floatright right-column">
            <div className="panel">{this.buildPanelBody()}</div>
          </div>
        </div>
        <div>
          <GroupManagementFooter footer={footer} onHandleClick={(key) => this.onHandleClick(key)} />
        </div>
      </div>
    );
  }
}

/**
 * SetHeader is to set policy header panel
 * @param props
 * @return policyHeader
 */
const SetHeader = (props) => {
  const { caption, buttonList, setEvent, children } = props;
  return (
    <div className="clearfix mb10">
      <h2 className="bold floatleft">{caption}</h2>
      <div className="floatright">
        {buttonList &&
          buttonList.map((data) => {
            return (
              <a
                href
                onKeyDown={(event) => {
                  if (event.keyCode === 13) {
                    event.preventDefault();
                    setEvent(data.id);
                  }
                }}
                onClick={() => setEvent(data.id)}
              >
                <img
                  tabIndex="0"
                  className="icon ml10"
                  id={data.id}
                  src={data.src}
                  alt={data.alt}
                />
              </a>
            );
          })}
      </div>
      {children}
    </div>
  );
};

/**
 * SwitchTabButton is to set add/remove wifi panel indicators
 * @param getList
 * @param currentIndex
 * @param setPageEvent
 * @return panelIndicator
 */
const SwitchTabButton = ({ getList, currentIndex, setPageEvent }) => (
  <ul className="pagination floatright mr10">
    {getList.length !== 1 &&
      getList.length <= 10 &&
      getList.map((setter, index) => {
        return (
          <li>
            {currentIndex !== index ? (
              <a
                href
                style={{
                  color: '#7F7F7F',
                  border: '1px solid #B2B2B2',
                  background: '#fefefe',
                  textDecoration: 'none',
                  display: 'block',
                  padding: '0 8px',
                  borderRadius: 3,
                  height: 23,
                  fontSize: 13,
                  cursor: 'pointer',
                }}
                onClick={() => setPageEvent(index)}
              >
                {index + 1}
              </a>
            ) : (
              <span>{index + 1}</span>
            )}
          </li>
        );
      })}
  </ul>
);

/**
 * mapStateToProps performs data fetch from language redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { data: localeData } = state.language;
  return { localeData };
};

/**
 * propTypes is used for validation props with its type.
 */
SetHeader.propTypes = {
  setEvent: PropTypes.func.isRequired,
  caption: PropTypes.string.isRequired,
  children: PropTypes.element.isRequired,
  buttonList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * propTypes is used for validation props with its type.
 */
SwitchTabButton.propTypes = {
  setPageEvent: PropTypes.func.isRequired,
  currentIndex: PropTypes.number.isRequired,
  getList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * propTypes is used for validation props with its type.
 */
GroupManagementPolicy.propTypes = {
  onKeyDown: PropTypes.func.isRequired,
  setPanelKey: PropTypes.func.isRequired,
  limitContent: PropTypes.bool.isRequired,
  setPageChange: PropTypes.func.isRequired,
  onHandleClick: PropTypes.func.isRequired,
  addRemoveClick: PropTypes.func.isRequired,
  currentIndex: PropTypes.number.isRequired,
  policyPanelKey: PropTypes.string.isRequired,
  onTableFrameEvent: PropTypes.func.isRequired,
  getList: PropTypes.arrayOf(PropTypes.object).isRequired,
  buttonList: PropTypes.arrayOf(PropTypes.object).isRequired,
  policyPanel: PropTypes.objectOf(PropTypes.array).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  mainPolicyMapper: PropTypes.objectOf(PropTypes.object).isRequired,
  footer: PropTypes.shape({
    screenKey: PropTypes.string,
    authorityRoleType: PropTypes.string,
    reference: PropTypes.instanceOf(createRef()),
  }).isRequired,
  createdPolicyPanelKeys: PropTypes.shape([PropTypes.string]),
  createButtonClick: PropTypes.func.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
GroupManagementPolicy.defaultProps = {
  createdPolicyPanelKeys: [],
};

export default connect(mapStateToProps)(GroupManagementPolicy);
