/**
 * NotificationConfirm is review page of notification registration
 * @author: V.Vijayaragavan
 */
import React, { PureComponent, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { isImmediate } from '../validations/NotificationValidations';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { postWithFormData, putWithFormData } from '../../../actions/commonActions';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import NotificationInputForm from '../component/NotificationRegistration/NotificationInputForm';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import NotificationFooterComponent from '../component/NotificationFooterComponent/NotificationFooterComponent';
import { getRegisterFooterButton } from '../constants/NotificationRegTableLists';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { CommonConstants } from '../../../constants/CommonConstants';
import { renameFile } from '../constants/InfoFromSoftBankConstants';

const fileDownload = require('js-file-download');

class NotificationConfirm extends PureComponent {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history } = this.props;
    this.state = {
      pageLoading: true,
    };
    if (location.state && location.state.isFromRegister) {
      this.state = {
        pageLoading: true,
        update: location.state.update,
        dataSource: location.state.dataSource,
        fileDataSource: location.state.fileDataSource,
        uploadedImages: location.state.uploadedImages,
        dynamicData: location.state.dynamicData,
      };
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  }

  /**
   * componentDidMount to stop page loading after component mounted.
   */
  componentDidMount() {
    const { dynamicData } = this.state;
    dynamicData.refs.titleSpanRef = createRef();
    this.setState({ dynamicData, pageLoading: false }, () => {
      if (dynamicData.refs.titleSpanRef.current) {
        dynamicData.refs.titleSpanRef.current.focus();
      }
    });
  }

  /**
   * onFooterComponentAction is used to handled footercomponent action
   * @param key
   */
  onFooterComponentAction = key => {
    const { history } = this.props;
    switch (key) {
      case 'ok':
        this.onOkButtonClick();
        break;
      case 'cancel':
        this.onCancelButtonClick();
        break;
      default:
        history.push(ROUTEENDPOINTS.NOTIFICATIONLIST);
    }
  };

  /**
   * onOkButtonClick is used to handle ok button click event
   */
  onOkButtonClick = () => {
    this.registerNotification();
  };

  /**
   * onCancelButtonClick is used to handle cancel button click event
   */
  onCancelButtonClick = () => {
    const { history } = this.props;
    const { update, dataSource, fileDataSource, uploadedImages, dynamicData } = this.state;
    dynamicData.refs = {};
    history.push({
      pathname: update
        ? ROUTEENDPOINTS.NOTIFICATIONUPDATE
        : ROUTEENDPOINTS.NOTIFICATIONREGISTRATION,
      state: {
        showComponent: true,
        isFromConfirm: true,
        update,
        dataSource,
        fileDataSource,
        uploadedImages,
        dynamicData,
      },
    });
  };

  /**
   * registerNotification is used to register notification with api
   */
  registerNotification = () => {
    const { history, setModified, userVo } = this.props;
    const { update, dataSource, dynamicData, fileDataSource, uploadedImages } = this.state;
    const { attachmentFile } = fileDataSource;
    this.setState({ pageLoading: true });
    const tempParam = dataSource;
    tempParam.immediate = isImmediate(
      dataSource.dateOfPublication,
      dataSource.dateOfPublicationTime,
      userVo.timeZoneOffSet,
      dataSource.settingValue,
    );
    tempParam.dateOfPublication = new Date(
      `${dataSource.dateOfPublication} ${
        dataSource.dateOfPublicationTime === 'empty' ? '' : dataSource.dateOfPublicationTime
      }`,
    );
    tempParam.publicationPeriod = new Date(dataSource.publicationPeriod);
    const formData = new FormData();
    let fileIndex = 0;
    let content = '';
    tempParam.content = dataSource.content ? dataSource.content : '';
    tempParam.content.split('<img src="').forEach(splittedValue => {
      if (splittedValue.startsWith('data:image/') || splittedValue.startsWith('blob')) {
        content += `<img src="cid:image${fileIndex}${splittedValue.slice(
          splittedValue.indexOf('"'),
        )}`;
        formData.append('imageFiles', renameFile(uploadedImages[fileIndex], `image${fileIndex}`));
        fileIndex += 1;
      } else {
        content += splittedValue;
      }
    });
    tempParam.content = content;
    if (attachmentFile) {
      const fileExtension = attachmentFile.name.includes('.')
        ? attachmentFile.name.slice(attachmentFile.name.lastIndexOf('.'))
        : '';
      formData.set('attachmentFile', renameFile(attachmentFile, dataSource.attachmentName));
      tempParam.attachmentName = dataSource.attachmentName + fileExtension;
    }
    formData.set('notificationJSONData', JSON.stringify(tempParam));
    const saveProcess = async () => {
      let response = {};
      if (update) {
        response = await putWithFormData(APIENDPOINTS.NOTIFICATIONUPDATE, formData);
      } else {
        response = await postWithFormData(APIENDPOINTS.NOTIFICATIONREGISTER, formData);
      }
      const resultSet = await response.json();
      setModified(!resultSet.status);
      if (resultSet.status) {
        history.push({
          pathname: ROUTEENDPOINTS.NOTIFICATIONFINISH,
          state: { update, isFromConfirm: true, noticeId: resultSet.notificationVo.noticeId },
        });
      } else {
        const errorFieldKey = Object.keys(resultSet.validationError)[0];
        dynamicData[errorFieldKey] = resultSet.validationError[errorFieldKey];
      }
      this.setState({ dynamicData }, () => this.onCancelButtonClick());
    };
    saveProcess();
  };

  /**
   * onNotificationInputAction is used to handle notification input fields change actions
   */
  onNotificationInputAction = () => {
    const { fileDataSource, dataSource } = this.state;
    const fileToDownload = fileDataSource.attachmentFile;
    const fileExtension = fileToDownload.name.slice(fileToDownload.name.lastIndexOf('.'));
    fileDownload(fileToDownload, dataSource.attachmentName + fileExtension);
  };

  /**
   * render is used to render the component
   */
  render() {
    const { pageLoading, dataSource, fileDataSource, uploadedImages, dynamicData } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader {...notificationHeaderInformation('confirm')} />
        <div className="contents-main">
          <NotificationInputForm
            dataSource={dataSource}
            fileDataSource={fileDataSource}
            uploadedImages={uploadedImages}
            dynamicData={dynamicData}
            confirm
            onNotificationInputAction={this.onNotificationInputAction}
          />
          <NotificationFooterComponent
            buttonsList={getRegisterFooterButton(dynamicData.disableOkButton, false)}
            onHandleClick={key => this.onFooterComponentAction(key)}
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    userVo,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NotificationConfirm.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      isFromConfirm: PropTypes.bool,
      update: PropTypes.bool,
      noticeId: PropTypes.string.isRequired,
    }),
  }),
  history: commonPropTypes().history.isRequired,
};

NotificationConfirm.defaultProps = {
  location: { state: { update: false, isFromConfirm: false } },
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(NotificationConfirm));
