/**
 * ForgotPasswordContainer is used to get a new password for an account
 * @author: Loganathan.P,V.Vijayaragavan
 */

import React, { Component, createRef } from 'react';
import { Layout } from 'antd';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { post } from '../../actions/commonActions';
import APIEndPoints from '../../constants/APIEndPoints';
import HeaderLayout from '../../common/component/headerlayout/HeaderLayout';
import FooterLayout from '../../common/component/footerlayout/FooterLayout';
import CommonFrame from '../../v1/common/component/CommonFrame';
import RouteEndPoints from '../../constants/RouteEndPoints';
import {
  AuthenticationConstants,
  forgetPasswordFloatRightList,
  forgotPasswordTableList,
} from '../AuthenticationConstants';
import AddEnterEventListener from '../../common/CommonListenEvents/CommonListenEvents';
import { validateLoginId } from '../../common/CommonValidations/CommonValidations';
import languageChangerAction from '../../language/languageActions';
import LoadingSpinner from '../../common/component/loadingSpinner/LoadingSpinner';
import defaultLocale from '../utils/defaultLocale';
import checkLanguage from '../../language/checkLanguage';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

const { Header, Footer } = Layout;

class ForgotPasswordContainer extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor() {
    super();
    this.state = {
      errorMessage: '',
      param: {},
      headerDescription: AuthenticationConstants.forgotPassWordHeaderDescription,
      shouldDisplay: true,
      loading: false,
    };
    this.dmCodeRef = createRef();
    this.backLoginRef = createRef();
  }

  /**
   * componentDidMount performs api operation to load default locale
   */

  componentDidMount = async () => {
    this.setState({ loading: true });
    const languageMap = await defaultLocale(this);
    if (Object.keys(languageMap).length >= 0) {
      document.title = this.languageLabelCheck('application_title');
      this.setState({ loading: false });
      this.bindListenEvents();
      this.dmCodeRef.current.focus();
    }
  };

  /**
   * onConfirmationFrameEvent handles click event
   * @param event
   */

  onConfirmationFrameEvent(event) {
    if (event.target.id === 'ok-button') {
      this.onOkButtonClick();
    } else {
      this.onCancelButtonClick();
    }
  }

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   */

  onTableFrameEvent(key, event, value) {
    const { param } = this.state;
    param[key] = value;
    this.setState({ param });
  }

  /**
   * onOkButtonClick is used to handle operation on clicking ok button
   */

  onOkButtonClick() {
    if (this.validateInputs()) {
      this.setState({
        loading: true,
      });
      this.forgotPasswordCall();
    }
  }

  /**
   * onCancelButtonClick is used to handle operation on clicking cancel button
   */

  onCancelButtonClick() {
    const { history } = this.props;
    history.push(RouteEndPoints.LOGIN);
  }

  /**
   * onCancelButtonClick is used to generate password by making a call the
   * server
   */

  forgotPasswordCall = async () => {
    const { param } = this.state;
    const result = await post(APIEndPoints.FORGOTPASSWORD, param);
    const data = await result.json();
    if (data.status) {
      this.setState({
        headerDescription: AuthenticationConstants.forgotPasswordSuccessDescription,
        shouldDisplay: false,
        loading: false,
        errorMessage: '',
      });
      this.backLoginRef.current.focus();
    } else {
      this.setState({
        loading: false,
        errorMessage: data.message,
      });
      this.bindListenEvents();
      this.dmCodeRef.current.focus();
    }
  };

  /**
   * bindListenEvents is used to bind listen events
   */

  bindListenEvents = () => {
    AddEnterEventListener('dm-code-text-input', 'ok-button');
    AddEnterEventListener('login-id-text-input', 'ok-button');
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * validateInputs is used to validate inputs
   */

  validateInputs() {
    const { param } = this.state;
    const dmCodeValidation = validateLoginId(param.dmCode);
    const loginIdValidation = validateLoginId(param.loginId);
    if (dmCodeValidation.error === 'empty') {
      this.setState({
        errorMessage: 'dm_code_cannot_be_empty',
      });
      return false;
    }
    if (loginIdValidation.error === 'empty') {
      this.setState({
        errorMessage: 'login_id_cannot_be_empty',
      });
      return false;
    }
    if (!dmCodeValidation.status || !loginIdValidation.status) {
      this.setState({
        errorMessage: 'the_item_you_entered_is_incorrect',
      });
      return false;
    }
    this.setState({
      errorMessage: '',
    });
    return dmCodeValidation.status && loginIdValidation.status;
  }

  /**
   * render is used to render the component
   */

  render() {
    const { errorMessage, headerDescription, shouldDisplay, loading, param } = this.state;
    const headerInformation = {
      title: 'reissue_password',
      description: headerDescription,
    };

    const tableList = forgotPasswordTableList(this.dmCodeRef);

    const forgetPasswordMainComponentList = [
      {
        class: '',
        style: {},
        componentList: {
          tableframe: { viewSource: tableList, dataSource: param },
        },
      },
    ];

    const forgetPasswordFloatLeftList = [
      {
        component: 'span',
        class: 'red bold',
        innerComponents: [{ text: errorMessage }, { component: 'break' }],
      },
    ];

    const footerComponentList = {
      floatLeftList: forgetPasswordFloatLeftList,
      floatRightList: shouldDisplay
        ? forgetPasswordFloatRightList(this.backLoginRef).slice(0, 2)
        : forgetPasswordFloatRightList(this.backLoginRef).slice(2),
    };

    return (
      <LoadingSpinner loading={loading}>
        <Layout>
          <Header style={AuthenticationConstants.forgotPasswordStyles.header}>
            <HeaderLayout noLogoutdisplay />
          </Header>
          <Layout>
            <div style={{ margin: '20px auto', width: 800 }}>
              <CommonFrame
                headerInformation={headerInformation}
                mainComponentList={shouldDisplay ? forgetPasswordMainComponentList : []}
                footerComponentList={footerComponentList}
                footerStyle={{ width: 800 }}
                onConfirmationFrameEvent={(event) => this.onConfirmationFrameEvent(event)}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              />
            </div>
          </Layout>
          <Footer id="footer" style={AuthenticationConstants.forgotPasswordStyles.footer}>
            <FooterLayout />
          </Footer>
        </Layout>
      </LoadingSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = (dispatch) => {
  return {
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
  };
};

/**
 * propTypes is used for validation props with its type.
 */
ForgotPasswordContainer.propTypes = {
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ForgotPasswordContainer.defaultProps = {
  isNotMultiLang: false,
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
    defaultLang: state.language.default,
    defaultimeZoneCode: state.language.timezoneCode,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ForgotPasswordContainer);
