/** Base Files */
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

/** Utility File */
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

/**
 * SBLink is to generate hyperlink for the specified path navigation
 * @author Aswin Palanisamy
 * @param props
 * @return hyperLink
 */
const SBLink = ({ pathname, state, content, history }) => {
  /**
   * handleKeyEvent is to navigate to specified screen path on same browser screen
   * in (click+ctrl/shift/alt)/space key events
   * @param event
   */
  const handleKeyEvent = event => {
    const { type, keyCode } = event;
    if (type === 'click' || keyCode === 32) {
      history.push(pathname, { ...state });
      event.preventDefault();
    }
  };

  return (
    <Link
      to={{ pathname: ROUTEENDPOINTS.BLOCKED }}
      onClick={event => handleKeyEvent(event)}
      onKeyDown={event => handleKeyEvent(event)}
    >
      <u>{content}</u>
    </Link>
  );
};

/**
 * propTypes is used for validation props with its type.
 */
SBLink.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  content: PropTypes.string.isRequired,
  pathname: PropTypes.string.isRequired,
  state: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number])),
};

/**
 * defaultProps is used to initialise non required props initial values
 */
SBLink.defaultProps = { state: {} };

export default withRouter(SBLink);
