/**
 * contentStyle is used to hold content editor css
 */
export function contentStyle(removeMinHeight) {
  return {
    minHeight: removeMinHeight ? 40 : 250,
    overflow: 'auto',
    borderTop: '1px solid #ccc',
    borderBottom: '1px solid #ccc',
  };
}

/**
 * getDayName is used to get day name code of given day
 * @param day
 * @return DayName
 */
export function getDayName(day) {
  const days = [
    'sunday_short',
    'monday_short',
    'tuesday_short',
    'wednesday_short',
    'thursday_short',
    'friday_short',
    'saturday_short',
  ];
  return days[day];
}

/**
 * dateRegex is used to verify date Format "YYYY/MM/DD"
 */
export const dateRegex = /^([0-9]\d{3}[/](0[1-9]|1[0-2])[/](0[1-9]|[12]\d|3[01]))$/;

/**
 * createRefsForAllInputs is used to create refs for all inputs
 * @return ref Object
 */
export function createRefsForAllInputs() {
  return {
    titleRef: createRef(),
    dateOfPublicationRef: createRef(),
    publicationPeriodRef: createRef(),
    attachmentNameRef: createRef(),
    attachmentFileRef: createRef(),
    titleSpanRef: createRef(),
    editorRef: createRef(),
  };
}

/**
 * initialDynamicDataValue is used to build initial dynamicData values
 * @param dataSource
 * @param languageList
 * @return dynamicData
 */
function initialDynamicDataValue(dataSource, languageList) {
  return {
    languageList,
    buttonTitle: 'configure',
    settingButtonKey: 'configureButton',
    disableOkButton: true,
    displayBackButton: false,
    dateOfPublication:
      dataSource && dataSource.dateOfPublication ? dataSource.dateOfPublication : '',
    publicationPeriod:
      dataSource && dataSource.publicationPeriod ? dataSource.publicationPeriod : '',
    refs: createRefsForAllInputs(),
  };
}

/**
 * initialDataSource is used to build initial dataSource values
 * @return dataSource
 */
function initialDataSource() {
  return {
    dateOfPublicationTime: '12:00',
    languageCode: 'ja',
    publishWithoutSendingEmail: [],
    contentPlainText: '',
    sendMail: true,
  };
}

/**
 * setFocusInErrorField is used to set focus in error input field
 * @param dynamicData
 */
export function setFocusInErrorField(dynamicData) {
  let errorFieldRef = { current: '' };
  if (dynamicData.titleError) {
    errorFieldRef = dynamicData.refs.titleRef;
  } else if (dynamicData.dateOfPublicationError) {
    errorFieldRef = dynamicData.refs.dateOfPublicationRef;
  } else if (dynamicData.publicationPeriodError) {
    errorFieldRef = dynamicData.refs.publicationPeriodRef;
  } else if (dynamicData.contentError) {
    if (dynamicData.refs.editorRef.current) dynamicData.refs.editorRef.current.focusEditor();
    return;
  } else if (dynamicData.imageNameError) {
    errorFieldRef = dynamicData.refs.imageNameRef;
  } else if (dynamicData.imageFileError) {
    errorFieldRef = dynamicData.refs.imageFileRef;
  } else if (dynamicData.attachmentNameError) {
    errorFieldRef = dynamicData.refs.attachmentNameRef;
  } else if (dynamicData.attachmentFileError) {
    errorFieldRef = dynamicData.refs.attachmentFileRef;
  }
  if (errorFieldRef.current) errorFieldRef.current.focus();
}

/**
 * initialState is used to build initial state values of notification register or update screen.
 * @param key
 * @param dataSource
 * @param languageList
 * @return initialState
 */
export function initialState(key, dataSource, languageList) {
  return {
    update: key === 'update',
    pageLoading: true,
    showComponent: true,
    dynamicData: initialDynamicDataValue(dataSource, languageList),
    confirm: false,
    dataSource: key === 'update' ? dataSource : initialDataSource(),
    fileDataSource: {},
    headerInformation: notificationHeaderInformation(key),
    uploadedImages: [],
  };
}

/**
 * getPublicationPeriodValue is used to publication period label to be shown in confirm screen
 * @param dateOfPublication
 * @param publicationPeriod
 * @param settingValue
 * @param offset
 * @param noExpiryLabel
 * @param daysLabel
 * @return publicationPeriodValue
 */
export function getPublicationPeriodValue(
  dateOfPublication,
  publicationPeriod,
  settingValue,
  offset,
  noExpiryLabel,
  daysLabel,
) {
  if (publicationPeriod) {
    return `${differenceBetweenTwoDates(
      dateOfPublication ? new Date(dateOfPublication) : getUserTodaysDate(offset),
      new Date(publicationPeriod),
    )} ${daysLabel}`;
  }
  return settingValue ? noExpiryLabel : '';
}

/**
 * INITIAL_INDEX is used to hold initial index of array
 */
export const INITIAL_INDEX = 0;

/**
 * FOURTH_INDEX is used to hold fourth index of array
 */
export const FOURTH_INDEX = 3;

/**
 * changeNoticeListOrder is used to keep the given NoticeId in first of list
 * @param noticeVoList
 * @param noticeId
 * @return noticeList
 */
export function changeNoticeListOrder(noticeVoList, noticeId) {
  let noticeIndex = -1;
  let noticeVo = {};
  const noticeList = noticeVoList;
  noticeVoList.forEach((notice, index) => {
    if (notice.noticeId === noticeId) {
      noticeIndex = index;
      noticeVo = notice;
    }
  });
  noticeList.splice(noticeIndex, 1);
  noticeList.unshift(noticeVo);
  return noticeList;
}

/**
 * renameImageFile is used to rename the given image file to given new name
 * @param originalFile
 * @param newName
 * @return renamedFile
 */
export function renameFile(originalFile, newName) {
  const fileExtension = originalFile.name.includes('.')
    ? originalFile.name.slice(originalFile.name.lastIndexOf('.'))
    : '';
  return new File([originalFile], newName + fileExtension, {
    type: originalFile.type,
    lastModified: originalFile.lastModified,
  });
}
