import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBCheckbox from '../../../../common/component/checkbox/SBCheckbox';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBTable from '../../../../common/component/sbtable/SBTable';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

const resultsPerPageKey ='CA_integration_list';
class CAIntegration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [],
      datasource: [],
    };
  }

  onSearchButtonClick = () => {};

  onCheckBoxChange = () => {};

  onCSVOutputButtonClick = () => {};

  render() {
    const { screenTitleData } = this.props;
    const { datasource } = this.state;
    const { columns } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <SBContentHeader
                title={screenTitleData.title}
                description={screenTitleData.description}
              />
              <div className="contents-main">
                <div className="border p15 mb15">
                  <div className="clearfix mb10">
                    <SBCheckbox
                      name="check"
                      horizontal="true"
                      withColon
                      image
                      filterKey="name"
                      filterValue="name"
                      imageSrcKey="url"
                      onChange={() => this.onCheckBoxChange()}
                      options={OPERATIONMANAGEMENTCONSTANTS.caIntegrationSearchFilterCheckBoxList}
                    />
                  </div>
                  <SBTextbox
                    type="text"
                    name="search_data"
                    size="20"
                    maxlength="100"
                    style={{ marginRight: '10px' }}
                  />
                  <SBButton
                    type="button"
                    name="searchButton"
                    color="green"
                    title="Search"
                    onClick={() => this.onSearchButtonClick()}
                  />
                </div>
              </div>
              <div className="contents-main w100per">
                <div className="mb10">
                  <div className="clearfix">
                    <SBTable
                      columns={columns}
                      dataSource={datasource}
                      loading={false}
                      pagination
                      countInfo={`Search result：${datasource.length} items`}
                      filterList={
                        OPERATIONMANAGEMENTCONSTANTS.antiVirusGridResultsPerPageComboItems
                      }
                      defaultSelected={resultsPerPageKey}
                    />
                  </div>
                  <div className="clearfix mb10">
                    <div className="floatleft">
                      <SBButton
                        type="button"
                        name="csvOutput"
                        title="CSV Output"
                        color="gray"
                        disabled
                        onClick={() => this.onCSVOutputButtonClick()}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(CAIntegration);
