/**
 * PaymentContent is used to load approval content
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import checkLanguage from '../../../language/checkLanguage';
import SBTextbox from '../../../common/component/sbtextbox/SBTextbox';
import SBButton from '../../../common/component/sbbutton/SBButton';

class PaymentContent extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the component
   * @return paymentContentComponent
   */

  render() {
    const { onHandleClick, onHandleChange, paymentSource, errorMessage, reference } = this.props;
    const { approvalNumber } = paymentSource;
    const { approvalRemarks } = paymentSource;
    return (
      <table className="table-tate mb10">
        <tbody>
          <tr>
            <th>{this.languageLabelCheck('approval_number')}</th>
            <td>
              <div className="mb5">
                <SBTextbox
                  type="text"
                  maxLength={20}
                  id="settlement_number"
                  name="settlement_number"
                  key="approvalNumber"
                  ref={reference.paymentNumberRef}
                  canModify
                  value={approvalNumber}
                  onChange={(event, value) => onHandleChange('approvalNumber', event, value)}
                />
              </div>
              <span className="red bold">
                {this.languageLabelCheck(errorMessage.approvalNoError)}
              </span>
            </td>
          </tr>
          <tr>
            <th>{this.languageLabelCheck('payment_content')}</th>
            <td>
              <div>
                <textarea
                  name="approvalRemarks_remarks"
                  rows="4"
                  cols="80"
                  className="w100per mb5"
                  ref={reference.paymentRemarksRef}
                  value={approvalRemarks}
                  onChange={event => onHandleChange('approvalRemarks', event, event.target.value)}
                />
                <span className="red bold">
                  {this.languageLabelCheck(errorMessage.contentError)}
                </span>
              </div>
              <div className="clearfix">
                <div className="floatright">
                  <SBButton
                    type="button"
                    id="btAdd"
                    title="register"
                    color="green"
                    onClick={() => onHandleClick('add')}
                  />
                  <SBButton
                    type="button"
                    id="btDelete"
                    title="delete"
                    noMarginRight
                    onClick={() => onHandleClick('delete')}
                  />
                </div>
              </div>
              <div style={{ marginTop: 5 }}>
                <span className="red bold" />
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

PaymentContent.propTypes = {
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
  onHandleClick: PropTypes.func.isRequired,
  onHandleChange: PropTypes.func.isRequired,
  paymentSource: PropTypes.objectOf(PropTypes.object),
  errorMessage: PropTypes.objectOf(PropTypes.object),
  reference: PropTypes.objectOf(PropTypes.object),
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
PaymentContent.defaultProps = {
  isNotMultiLang: false,
  reference: {},
  paymentSource: {},
  errorMessage: {},
};

export default connect(mapStateToProps)(withRouter(PaymentContent));
