import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import descriptionConverter from '../../utils/descriptionConverter';

class SBContentHeader extends Component {
  setSpanPanel = value => {
    return value.map(data => {
      return typeof data === 'object' ? <span className="red bold">{data.redbold}</span> : data;
    });
  };

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const { title, description, hasTitleDescription } = this.props;
    return (
      <div className="contents-headder clearfix" style={{ height: 'inherit' }}>
        <div className="mb15 clearfix" style={{ display: 'flex', height: 'inherit' }}>
          <div className="contents-title">
            <h1>
              {hasTitleDescription
                ? title && title.map(e => descriptionConverter(this.languageLabelCheck(e)))
                : this.languageLabelCheck(title)}
            </h1>
          </div>
          <div className="contents-description">
            <div>
              {description &&
                description.map(e => descriptionConverter(this.languageLabelCheck(e)))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(SBContentHeader);
