/**
 * LoginContainer is used to logon EMM console
 * @author: Loganathan.P, P.Vinothpandian,V.Vijayaragavan
 */

import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import checkLanguage from '../../language/checkLanguage';
import { post, get } from '../../actions/commonActions';
import APIEndPoints from '../../constants/APIEndPoints';
import logo from '../../common/component/image/bcdm_jp.png';
import btnLogin from '../../common/component/image/btn-isoclogin.png';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import LoadingSpinner from '../../common/component/loadingSpinner/LoadingSpinner';
import FooterLayout from '../../common/component/footerlayout/FooterLayout';
import RouteEndPoints from '../../constants/RouteEndPoints';
import { AuthenticationConstants, loginErrorConstants } from '../AuthenticationConstants';
import AddEnterEventListener from '../../common/CommonListenEvents/CommonListenEvents';
import languageChangerAction from '../../language/languageActions';
import defaultLocale from '../utils/defaultLocale';
import UNDERSCORE from '../../v1/common/utils';
import descriptionConveter from '../../common/utils/descriptionConverter';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

class ISOCLoginContainer extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      message: '',
      currentFocus: 'dmCode',
      param: {},
    };
    this.passwordRef = createRef();
    this.dmCodeRef = createRef();
    this.loginIdRef = createRef();
  }

  /**
   * componentDidMount performs api operation to get status of logged account
   */

  componentDidMount() {
    const validateScreen = async () => {
      await this.validateGlobalAddress();
    };
    validateScreen();
  }

  /**
   * onLoginButtonClick performs login operation
   */

  onLoginButtonClick() {
    const { history, changeLanguage, defaultLang, defaultimeZoneCode, setModified } = this.props;
    const { param } = this.state;
    this.setState({ loading: true });
    const loginValidation = async () => {
      const result = await post(APIEndPoints.LOGINISOC, param);
      const data = await result.json();
      if (data.resetPassword) {
        history.push(RouteEndPoints.CHANGEPASSWORD);
      } else if (data.status) {
        this.setCacheData();
        setModified(false);
        changeLanguage(defaultLang, defaultimeZoneCode, {});
        history.push(RouteEndPoints.DASHBOARD);
      } else {
        param.password = '';
        this.validationResult(data);
        this.setState({ param, loading: false });
        this.bindListenEvents();
        this.setFocus();
      }
    };
    loginValidation();
  }

  /**
   * getLoginStatus performs retrieval of login status
   */

  getLoginStatus = async () => {
    const { history, setModified } = this.props;
    const result = await get(APIEndPoints.LOGINSTATUS);
    const data = await result.json();
    if (data.authStatus && !data.resetPasswordStatus) {
      setModified(false);
      history.push(RouteEndPoints.DASHBOARD);
    } else {
      const languageMap = await defaultLocale(this);
      if (Object.keys(languageMap).length >= 0) {
        this.setState({ loading: false });
        this.clearPasswordField();
        this.setFocus();
        this.bindListenEvents();
        document.title = this.languageLabelCheck('application_title');
      }
    }
  };

  /**
   * setFocus is used to set focus to the error component
   */

  setFocus = () => {
    const { currentFocus } = this.state;
    switch (currentFocus) {
      case 'dmCode':
        this.dmCodeRef.current.focus();
        break;
      case 'loginId':
        this.loginIdRef.current.focus();
        break;
      default:
        this.passwordRef.current.focus();
    }
  };

  /**
   * setValidationErrorMessage is used to set error message
   */

  setValidationErrorMessage(message) {
    this.setState({ message });
  }

  /**
   * validateGlobalAddress is used to validate global ipaddress
   */

  validateGlobalAddress = async () => {
    // const { history } = this.props;
    // const response = await get(APIEndPoints.GLOBALIPADDRESS);
    // const resultSet = await response.json();
    // if (!resultSet.status) {
    //   history.push(RouteEndPoints.LOGIN);
    // } else {
    await this.getLoginStatus();
    // }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : descriptionConveter(checkLanguage(localeData, label));
  };

  handleChange = (event, value) => {
    const { param } = this.state;
    param[event.target.name] = value;
  };

  /**
   * bindListenEvents is used to bind listen events
   */

  bindListenEvents = () => {
    AddEnterEventListener('dm-code-text-input', 'login-button');
    AddEnterEventListener('login-id-text-input', 'login-button');
    AddEnterEventListener('password-text-input', 'login-button');
  };

  /**
   * bindListenEvents is used to clear password fields
   */

  clearPasswordField = () => {
    if (this.passwordRef.current) {
      this.passwordRef.current.value = '';
    }
  };

  /**
   * setCacheData is to set localStorage data
   */
  setCacheData = () => {
    const { deviceFilterCondition } = this.props;
    localStorage.removeItem('idListParam');
    const getStore =
      localStorage.getItem('deviceSearch') && JSON.parse(localStorage.getItem('deviceSearch'));
    if (getStore) {
      localStorage.setItem('deviceSearch', JSON.stringify({ ...getStore }));
    } else {
      localStorage.setItem(
        'deviceSearch',
        JSON.stringify({
          groupSearch: [],
          aDetailMark: '+',
          aPolicyMark: '+',
          departmentSearch: [],
          deviceSearch: { searchKeyword: '', checkBoxKeys: [] },
          multipleConditionSearch: cloneDeep(deviceFilterCondition),
        }),
      );
    }
  };

  /**
   * validationResult is used to get validation data set and make focus to fields
   */

  validationResult(resultData) {
    if (resultData.validationError.dmCode === 'empty') {
      this.setValidationErrorMessage(loginErrorConstants.dmCode.empty);
      this.setState({ currentFocus: 'dmCode' });
      return;
    }
    if (resultData.validationError.loginId === 'empty') {
      this.setValidationErrorMessage(loginErrorConstants.loginId.empty);
      this.setState({ currentFocus: 'loginId' });
      return;
    }
    if (resultData.validationError.password === 'locked') {
      this.setValidationErrorMessage('user_account_locked_login_after_mins');
      this.setState({ currentFocus: 'password' });
      return;
    }
    if (resultData.validationError.password === 'empty') {
      this.setValidationErrorMessage(loginErrorConstants.password.empty);
      this.setState({ currentFocus: 'password' });
      return;
    }
    if (
      !UNDERSCORE.isEmpty(resultData.validationError.dmCode) ||
      !UNDERSCORE.isEmpty(resultData.validationError.loginId) ||
      !UNDERSCORE.isEmpty(resultData.validationError.password)
    ) {
      this.setValidationErrorMessage('the_item_you_entered_is_incorrect');
      this.setState({ currentFocus: 'password' });
      return;
    }
    this.setValidationErrorMessage(resultData.message);
    this.setState({ currentFocus: 'password' });
  }

  /**
   * render is used to render the component
   */

  render() {
    const { message, loading, param } = this.state;
    return (
      <LoadingSpinner loading={loading}>
        <span>
          <div id="login">
            <div className="clearfix">
              <div className="center">
                <p className="logo" />
                <h1 className="hd-logo">
                  <a href="/">
                    <img src={logo} alt="SoftBank" />
                  </a>
                </h1>
              </div>
              <div className="form" style={{ margin: '0 auto', backgroundColor: '#EAF0F6' }}>
                <div style={{ width: 320, fontSize: 24, textAlign: 'center', color: '#4472C4' }}>
                  {this.languageLabelCheck('lbl_isoc')}
                </div>
                <br />
                <dl>
                  <dt>{this.languageLabelCheck('dm_code')}</dt>
                  <dd>
                    <SBTextbox
                      id="dm-code-text-input"
                      width="100%"
                      type="text"
                      name="dmCode"
                      defaultValue={param.dmCode}
                      size={20}
                      maxLength={20}
                      ref={this.dmCodeRef}
                      onChange={(event, content) => this.handleChange(event, content)}
                    />
                  </dd>
                  <dt>{this.languageLabelCheck('login_id')}</dt>
                  <dd>
                    <SBTextbox
                      id="login-id-text-input"
                      width="100%"
                      type="text"
                      name="username"
                      defaultValue={param.username}
                      size={20}
                      maxLength={20}
                      onChange={(event, content) => this.handleChange(event, content)}
                      ref={this.loginIdRef}
                    />
                  </dd>
                  <dt>{this.languageLabelCheck('password')}</dt>
                  <dd>
                    <SBTextbox
                      id="password-text-input"
                      width="100%"
                      type="password"
                      name="password"
                      defaultValue=""
                      size={20}
                      maxLength={20}
                      ref={this.passwordRef}
                      onChange={(event, content) => this.handleChange(event, content)}
                    />
                  </dd>
                </dl>
                <button
                  id="login-button"
                  style={{
                    backgroundColor: 'transparent',
                    borderColor: 'transparent',
                    color: '#036EB7',
                    width: 'auto',
                    padding: 0,
                  }}
                  type="button"
                  className="btn-login mb15"
                  onClick={() => this.onLoginButtonClick()}
                >
                  <img
                    src={btnLogin}
                    style={{
                      margin: 'auto',
                      justifyContent: 'center',
                      alignItems: 'center',
                    }}
                    alt="Login"
                  />
                </button>
              </div>
              <div style={{ width: '355px', margin: '0 auto' }}>
                <span className="red bold">{this.languageLabelCheck(message)}</span>
              </div>
            </div>
          </div>
          <footer id="footer" style={AuthenticationConstants.loginStyles.footer}>
            <FooterLayout />
          </footer>
        </span>
      </LoadingSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    changeLanguage: (language, timezone, languageMap) =>
      dispatch(languageChangerAction(language, timezone, languageMap)),
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
ISOCLoginContainer.propTypes = {
  changeLanguage: PropTypes.func.isRequired,
  defaultLang: PropTypes.string.isRequired,
  defaultimeZoneCode: PropTypes.string.isRequired,
  history: commonPropTypes().history.isRequired,
  setModified: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  deviceFilterCondition: PropTypes.shape({
    deviceSearch: PropTypes.arrayOf(PropTypes.object),
    policySearch: PropTypes.arrayOf(PropTypes.string),
    dummyDeviceSearch: PropTypes.arrayOf(PropTypes.object),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
ISOCLoginContainer.defaultProps = {
  isNotMultiLang: false,
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
    defaultLang: state.language.default,
    deviceFilterCondition: state.device.deviceFilterCondition,
    defaultimeZoneCode: state.language.timezoneCode,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ISOCLoginContainer);
