/**
 * defaultLocale is used to load default local data
 * @author V.Vijayaragavan
 */

import getLanguageData from '../../language/languageUtils';

const defaultLocale = async currentObject => {
  const { changeLanguage, defaultLang, defaultimeZoneCode } = currentObject.props;
  const languageMap = await getLanguageData(defaultLang);
  changeLanguage(defaultLang, defaultimeZoneCode, languageMap);
  return languageMap;
};

export default defaultLocale;
