/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import PropTypes from 'prop-types';

/** SBComponents */
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableHeader from '../../common/component/tableheader/SBTableHeader';
import SBDialog from '../../common/component/sbdialog/SBDialog';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import deviceHeader from '../constants/deviceHeaderConstants';
import { getTargetDeviceList } from '../../actions/GroupAction';
import { getGroupNameList, getUserGroupNameList } from '../../actions/DeviceAction';
import { apiEndPointMapper, serviceMapper } from '../common/deviceCommonFunctions';

/**
 * DeviceGroupSelect is to select group for filter device information
 * @author Aswin Palanisamy
 */
class DeviceGroupSelect extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const {
      screenKey,
      location: getData,
      getGroupNameList: makeGroupData,
      history,
      userGroupList,
    } = this.props;
    const { deviceId, groupId, setBatch, getGroup, parentScreen } = getData.state || {
      deviceId: '',
      groupId: -1,
      setBatch: false,
      getGroup: [],
      parentScreen: '',
    };
    if (!getData.state) {
      history.push(serviceMapper('error'));
    }
    this.state = {
      screenKey,
      setBatch,
      parentScreen,
      groupId: groupId || -1,
      deviceId: deviceId || '',
      disable: true,
      initialList: cloneDeep(parentScreen.includes('devicelist') ? getGroup : userGroupList),
      selectedGroup: [],
      groupDataList: [],
      groupSearch: '',
      header: '',
      isOk: false,
      pageLoading: false,
      gridLoading: false,
      openDialogError: false,
      openDialogConfirmation: false,
      headerData: deviceHeader(screenKey),
    };
    this.groupSearchRef = createRef();
    if (screenKey === 'groupSelect' && parentScreen.includes('devicelist')) {
      const { groupSearch } = !setBatch
        ? JSON.parse(localStorage.getItem('deviceSearch'))
        : { groupSearch: cloneDeep(getGroup) };
      makeGroupData(groupSearch);
    }
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { location } = this.props;
    if (!location.state) this.denyAccess();
    else this.setState({ pageLoading: true }, () => this.getGroup(true));
  }

  /**
   * componentWillUnmount perform onExit operation of groupSelect screen
   */
  componentWillUnmount() {
    const { screenKey, isOk, setBatch, initialList, parentScreen } = this.state;
    const { getGroupNameList: makeGroupData } = this.props;
    if (screenKey === 'groupSelect' && !isOk && parentScreen.includes('devicelist')) {
      const { groupSearch } = !setBatch
        ? JSON.parse(localStorage.getItem('deviceSearch'))
        : { groupSearch: initialList };
      makeGroupData(groupSearch);
    }
  }

  /**
   * denyAccess used to restrict the user from accessing the screen with a prompt.
   */
  denyAccess = () =>
    this.setState({
      header: 'error',
      openDialogError: true,
      message: 'data_does_not_exists',
    });

  /**
   * getGroup is to fetch group information
   * @param onOpen
   */
  getGroup = async (onOpen) => {
    const { groupList, userGroupList, deviceList } = this.props;
    const { screenKey, deviceId, groupSearch, parentScreen } = this.state;
    if (screenKey === 'refineGroup' && isEmpty(deviceList)) {
      this.setState({
        pageLoading: false,
        openDialogError: true,
        message: 'please_select_any_device',
        header: 'information',
      });
    } else {
      const groupName = (groupSearch || '').trim();
      const paramMapper = {
        refineGroup: { groupName },
        groupSelect: { groupName },
        groupChange: { deviceId, search: groupName },
      }[screenKey];
      const response = await post(apiEndPointMapper(screenKey), paramMapper);
      const result = await response.json();
      const { status, message, data: dataList } = result;
      if (status === 'OK') {
        const { selectedGroup, groupId, groupDataList } = this.setGridDataIndex(dataList);
        const updatedGroupList = parentScreen.includes('devicelist') ? groupList : userGroupList;
        this.setState(
          {
            groupId,
            groupDataList,
            disable: isEmpty(screenKey === 'groupSelect' ? selectedGroup : groupDataList),
            groupSearch: groupName,
            selectedGroup: cloneDeep(!isEmpty(selectedGroup) ? selectedGroup : updatedGroupList),
            pageLoading: false,
            gridLoading: false,
          },
          () => {
            if (onOpen && this.groupSearchRef.current) this.groupSearchRef.current.focus();
          },
        );
      } else {
        this.setState({
          openDialogError: true,
          header: 'error',
          message,
          pageLoading: false,
          gridLoading: false,
        });
      }
    }
  };

  /**
   * setGridDataIndex is to set grid row indexes
   * @param gridList
   * @return dataList
   */
  setGridDataIndex = (gridList = []) => {
    let matchIndex = -1;
    const { screenKey, selectedGroup } = this.state;
    let { groupId } = this.state;
    const groupDataList = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;
      if (screenKey === 'refineGroup' && record.groupId === groupId) {
        selectedGroup.push(record.groupName);
        matchIndex = index;
      }
      return record;
    });
    if (matchIndex > -1) groupDataList.unshift(groupDataList.splice(matchIndex, 1)[0]);
    if (screenKey === 'groupChange' && !isEmpty(groupDataList)) {
      const [record] = groupDataList;
      groupId = record.groupId;
      selectedGroup.push(record.groupName);
    }
    return { selectedGroup, groupId, groupDataList };
  };

  /**
   * handleRadioChange handle grid record selection operation
   * @param record
   */
  handleRadioChange = (record) => {
    const { groupId, groupName, scheduledTime } = record;

    if (!isEmpty(scheduledTime)) {
      this.setState({
        message: 'cannot_move_device_to_schedule_group',
        openDialogError: true,
        header: 'error',
        disable: true,
      });
    } else {
      this.setState({ groupId, selectedGroup: [groupName], disable: false });
    }
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = (tableData) => {
    const { screenKey } = this.state;
    let { groupId, selectedGroup } = this.state;
    const [record] = tableData;
    if (screenKey === 'groupChange' && !isEmpty(tableData)) {
      groupId = record.groupId;
      selectedGroup = [record.groupName];
    }
    this.setState({ selectedGroup, groupId });
  };

  /**
   * handleCheckboxChange is to handle grid checkbox events
   * @param selectedRowKeys
   */
  handleCheckboxChange = (selectedRowKeys) => {
    let { selectedGroup } = this.state;
    const { groupDataList, parentScreen } = this.state;
    const { getGroupNameList: makeGroupData, getUserGroupNameList: makeUserGroupData } = this.props;
    selectedGroup = selectedGroup.filter(
      (data) => !groupDataList.some((record) => data === record.groupName),
    );
    selectedRowKeys.forEach((data) => selectedGroup.push(groupDataList[data].groupName));
    if (parentScreen.includes('userlist')) makeUserGroupData(selectedGroup);
    else makeGroupData(selectedGroup);
    this.setState({ selectedGroup, disable: false });
  };

  /**
   * setSelectedRecord is to handle initial grid selection
   * @param groupData
   * @return groupDataList
   */
  setSelectedRecord = (groupData = []) => {
    const { groupDataList, selectedGroup, screenKey } = this.state;
    const groupValidator = !isEmpty(selectedGroup) ? selectedGroup : groupData;
    return screenKey === 'groupChange'
      ? [0]
      : groupDataList
          .filter((data) => groupValidator.indexOf(data.groupName) > -1)
          .map((record) => record.id);
  };

  /**
   * getBacktoPreviousScreen navigates to device/group list screen
   * @param isError
   * @param isCancel
   * @param isGroupSelect
   */
  getBacktoPreviousScreen = (isError, isCancel, isGroupSelect) => {
    const { screenKey, setBatch, initialList, selectedGroup } = this.state;
    const {
      history,
      location: getData,
      group,
      groupList: groupSearch,
      getGroupNameList: makeGroupData,
      getUserGroupNameList: makeUserGroupData,
    } = this.props;
    const { parentScreen: previousScreen } = getData.state || { parentScreen: '' };
    const previousDeviceScreen = previousScreen.includes('devicelist') ? 'deviceList' : 'userList';
    const parentScreen = serviceMapper(group ? 'groupManagement' : previousDeviceScreen);
    const screenName = isError || !previousScreen ? parentScreen : previousScreen;
    if (isCancel && screenKey === 'groupSelect') {
      if (parentScreen.includes('devicelist')) {
        const { groupSearch: groupList } = !setBatch
          ? JSON.parse(localStorage.getItem('deviceSearch'))
          : { groupSearch: initialList };
        makeGroupData(groupList);
      } else {
        makeUserGroupData(initialList);
      }
    } else if (!setBatch && !(isError && isCancel) && screenKey === 'groupSelect') {
      if (parentScreen.includes('devicelist')) {
        const getStore = JSON.parse(localStorage.getItem('deviceSearch'));
        localStorage.setItem('deviceSearch', JSON.stringify({ ...getStore, groupSearch }));
      } else {
        makeUserGroupData(selectedGroup);
      }
    }
    if (!(isError && isGroupSelect)) {
      this.setState({ isOk: !(isError && isCancel) }, () =>
        history.push(screenName, { ...getData.state, parentScreen }),
      );
    }
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, groupSearch) => {
    if (event.keyCode === 13) {
      this.setState({ groupSearch, gridLoading: true }, () => this.getGroup());
    }
  };

  /**
   * handleSearchAction performs group search operation
   * @param key
   * @param value
   */
  handleSearchAction = (key, value) => {
    if (key === 'searchButton')
      this.setState({ groupSearch: value.searchKeyword, gridLoading: true }, () => this.getGroup());
  };

  /**
   * onHandleClick performs button click events in the screen
   * @param screenKey
   * @return buttonAction
   */
  onHandleClick = (screenKey) =>
    ({
      confirm: () => this.getBacktoPreviousScreen(),
      groupChange: () => this.groupChangeClick(),
      cancel: () => this.getBacktoPreviousScreen('', true),
    }[screenKey]());

  /**
   * groupChangeClick prompts confirmation dialog for device migration
   * from one group to other
   */
  groupChangeClick = () => {
    this.setState({ openDialogConfirmation: true });
  };

  /**
   * changeSelectedDeviceGroup is to migrate selected device to the selected group
   */
  changeSelectedDeviceGroup = () => {
    const { deviceId, groupId, screenKey } = this.state;
    const { deviceList, makeDeviceList } = this.props;
    const groupChangeDeviceList = [];
    if (screenKey === 'groupChange') {
      groupChangeDeviceList.push(deviceId);
    }
    this.setState({ pageLoading: true }, async () => {
      const response = await post(apiEndPointMapper('migrateDevice'), {
        deviceList:
          screenKey === 'groupChange'
            ? groupChangeDeviceList
            : deviceList.map((record) => record.deviceId),
        groupId: +groupId,
      });
      const result = await response.json();
      const { status, message } = result;
      if (screenKey === 'refineGroup') makeDeviceList([]);
      this.setState({
        message,
        pageLoading: false,
        openDialogError: true,
        openDialogConfirmation: false,
        header: status === 'OK' ? 'success' : 'error',
      });
    });
  };

  /**
   * render perform groupSelection screen rendering
   * @return DeviceGroupSelect
   */
  render() {
    const {
      header,
      disable,
      message,
      screenKey,
      headerData,
      pageLoading,
      gridLoading,
      groupDataList,
      openDialogError,
      parentScreen,
      openDialogConfirmation,
    } = this.state;
    const { groupList, userGroupList } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <SBTableHeader headerText="group_selection" />
        </div>
        <DeviceSearch
          screenKey="groupSelect"
          reference={this.groupSearchRef}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '70%', left: 'calc(60% - 55px)' }}
        >
          <DeviceGrid
            pagination
            screenKey={screenKey}
            gridList={groupDataList}
            countInfo="select_result_count"
            filterMessage="display_items_colon"
            countInfoContents={groupDataList.length}
            rowKey={screenKey !== 'groupChange' && 'id'}
            type={screenKey === 'groupSelect' && 'checkbox'}
            handleRadioChange={(event) => this.handleRadioChange(event)}
            defaultSelected="deviceGroupSelect"
            handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
            handleCheckboxChange={(event, selectedRowKeys) =>
              this.handleCheckboxChange(selectedRowKeys)
            }
            handleSelectAll={(event, selectedRowKeys) => this.handleCheckboxChange(selectedRowKeys)}
            handleClearAll={(event, selectedRowKeys) => this.handleCheckboxChange(selectedRowKeys)}
            defaultSelectedKeys={this.setSelectedRecord(
              parentScreen.includes('userlist') ? userGroupList : groupList,
            )}
            handleSelectChange={() => this.setState({ gridLoading: true }, () => this.getGroup())}
          />
          <DeviceFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            footer={{
              disable,
              screenKey,
            }}
          />
        </CircularSpinner>
        <SBDialog
          open={openDialogError}
          header={header}
          type={header}
          okButtonTitle="ok"
          content={message}
          onOk={() => {
            this.setState(
              { openDialogError: false },
              this.getBacktoPreviousScreen(header === 'error', false, screenKey === 'groupSelect'),
            );
          }}
          width="400px"
        />
        <SBDialog
          type="confirmation"
          content="group_changes_save"
          header="save_changes"
          open={openDialogConfirmation}
          onCancel={() => {
            this.setState({ openDialogConfirmation: false });
          }}
          onOk={() => this.changeSelectedDeviceGroup()}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps performs data fetch from device/group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { groupList, userGroupList } = state.device;
  const { deviceList } = state.group;
  return { groupList, userGroupList, deviceList };
};

/**
 * mapDispatchToProps performs data setup in device/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    getGroupNameList: (data) => {
      dispatch(getGroupNameList(data));
    },
    getUserGroupNameList: (data) => {
      dispatch(getUserGroupNameList(data));
    },
    makeDeviceList: (data) => {
      dispatch(getTargetDeviceList(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceGroupSelect.propTypes = {
  screenKey: PropTypes.string.isRequired,
  getGroupNameList: PropTypes.func.isRequired,
  getUserGroupNameList: PropTypes.func.isRequired,
  makeDeviceList: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      groupId: PropTypes.number,
      deviceId: PropTypes.string,
      currentPath: PropTypes.string,
      parentScreen: PropTypes.string,
    }),
    pathname: PropTypes.string,
  }).isRequired,
  group: PropTypes.bool,
  groupList: PropTypes.arrayOf(PropTypes.string).isRequired,
  userGroupList: PropTypes.arrayOf(PropTypes.string).isRequired,
  deviceList: PropTypes.arrayOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DeviceGroupSelect.defaultProps = {
  group: false,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceGroupSelect));
