/**
 * ContractFinish is used to load finish page
 * @author: P.Vinothpandian
 *
 */

import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { tableContractView, contractTableHeaderList } from '../Constants/AppFrameConstants';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../Constants/AppManageHeaderConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import AppCRUDFooter from './AppCRUDFooter';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

class ContractFinish extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    let isUpdate = false;
    let isDelete = false;
    if (location.state) {
      param = location.state.dataSource;
      isUpdate = location.state.isUpdate;
      isDelete = location.state.isDelete;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('contractfinish'),
      dataSource: param,
      isUpdate,
      isDelete,
      footer: { primaryVisibile: false },
    };
  }

  /**
   * onHandleClick is used to handle click event
   */

  onHandleClick = () => {
    const { location, history } = this.props;
    const { isUpdate, isDelete } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    }
    if (isDelete) {
      history.push(ROUTEENDPOINTS.APPDELETE, { dmCode: param.dmCode });
    } else {
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTERFINISH, { param, isUpdate });
    }
  };

  /**
   * render is used to render the component
   * @return contractFinishComponent
   */

  render() {
    const { headerData, dataSource, footer } = this.state;
    return (
      <div>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <div className="clearfix mb10">
            <SBTableHeader headerText="contract_information" />
            <CustomTableFrame
              tableHeaderList={contractTableHeaderList}
              tableList={tableContractView}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            />
          </div>
          <div>
            <AppCRUDFooter
              reference={{ backRef: createRef() }}
              footer={footer}
              onHandleClick={(key) => this.onHandleClick(key)}
            />
          </div>
        </div>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */

ContractFinish.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
    }),
  }).isRequired,
};

export default withRouter(ContractFinish);
