import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBButton from '../../../../common/component/sbbutton/SBButton';

class SecurityPolicyFooter extends Component {
  onHandleClick = (event, key) => {
    const { onHandleClick } = this.props;
    if (onHandleClick) {
      onHandleClick(key);
    }
  };

  render() {
    const { okDisable } = this.props;
    return (
      <div className="clearfix">
        <div className="floatright">
          <SBButton
            id="btn_ok"
            color="green"
            type="button"
            title="ok"
            disabled={okDisable}
            onClick={event => this.onHandleClick(event, 'save')}
          />
          <SBButton
            id="btn_cancel"
            noMarginRight
            type="button"
            title="cancel"
            onClick={event => this.onHandleClick(event, 'cancel')}
            canModify
          />
        </div>
      </div>
    );
  }
}

export default withRouter(SecurityPolicyFooter);
