/**
 * IDHeaderConstants is used to load the ID screens header information
 * @author: P.Vinothpandian
 */

/**
 * headerInformation is used to load the ID screens header information
 * @param  key
 */

export default function headerInformation(key) {
  switch (key) {
    case 'registerid':
      return {
        title: 'id_register_header',
        description: ['id_register_desc'],
      };
    case 'registerSuccess':
      return {
        title: 'id_register_header',
        description: ['id_register_desc_success'],
      };
    case 'idlist':
      return {
        title: 'id_list_header',
        description: ['id_list_desc'],
      };
    case 'iddelete':
      return {
        title: 'id_delete_header',
        description: ['id_delete_desc'],
      };
    case 'deletesuccess':
      return {
        title: 'id_delete_header',
        description: ['id_delete_desc_success'],
      };
    case 'updateid':
      return {
        title: 'id_change_header',
        description: ['id_change_desc'],
      };
    case 'updatesuccess':
      return {
        title: 'id_change_header',
        description: ['id_change_desc_success'],
      };
    case 'changepassword':
      return {
        title: 'password_change',
        description: ['user_change_password_desc'],
      };
    case 'changepasswordsuccess':
      return {
        title: 'password_change',
        description: ['user_change_password_success'],
      };
    default:
      return {};
  }
}
