/**
 * IDList is used to load the ID screens
 * @author: P.Vinothpandian
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { isUndefined } from 'util';
import NoResultsFound from '../../../common/component/grid/NoResultsFound';
import UNDERSCORE from '../../../v1/common/utils';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import IDSearchPanel from './IDSearchPanel';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import IDGrid from './IDGrid';
import { post } from '../../../actions/commonActions';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import checkLanguage from '../../../language/checkLanguage';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import headerInformation from '../Constants/IDHeaderConstants';
import {
  isNotUndefinedOrNotEmpty,
  isAlphaOrNumeric,
} from '../../../common/CommonValidations/CommonValidations';
import { errorMessageConstants, setSearchReference } from '../Constants/IDCRUDconstants';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import SBDialog from '../../../common/component/sbdialog/SBDialog';

const resultPerPageKey = 'id_list';
class IDList extends Component {
  /**
   * constructor is used to initalize the state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location } = props;
    let param = {};
    if (!isUndefined(location.state)) {
      param = location.state;
    }
    this.state = {
      pageLoading: false,
      gridLoading: false,
      errorMessage: { dmCodeError: '', loginError: '', noConditionsError: '' },
      userList: [],
      noResults: false,
      openDialog: false,
      selectedRowValue: {},
      searchSource: { dmCode: UNDERSCORE.isEmpty(param) ? '' :
                       param.dmCode, loginId: UNDERSCORE.isEmpty(param) ? '' : param.loginId },
      reference: setSearchReference(),
    };
  }

  /**
   * componentDidMount is load the data in the grid
   */

  componentDidMount() {
    const { searchSource, reference } = this.state;
    if (searchSource.dmCode) this.performSearch();
    this.setState({ pageLoading: false });
    if (reference.dmCodeRef.current) reference.dmCodeRef.current.focus();
  }

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, value) => {
    const { searchSource } = this.state;
    searchSource[key] = value;
    this.setState({ searchSource });
  };

  /**
   * performValidations is used to perform validations
   */

  performValidations = () => {
    const { searchSource, reference } = this.state;
    let { errorMessage } = this.state;
    const dmCodeValidation = this.validateSearchKeyValue(searchSource.dmCode);
    const loginValidation = this.validateSearchKeyValue(searchSource.loginId);
    if (
      (dmCodeValidation.status && loginValidation.status) ||
      (dmCodeValidation.status && loginValidation.error === 'empty') ||
      (loginValidation.status && dmCodeValidation.error === 'empty')
    ) {
      errorMessage = {
        dmCodeError: '',
        loginError: '',
        noConditionsError: '',
      };
      this.setState({ errorMessage });
      return true;
    }
    if (dmCodeValidation.error === 'empty' && loginValidation.error === 'empty') {
      errorMessage = {
        dmCodeError: '',
        loginError: '',
        noConditionsError: errorMessageConstants.searchError.empty,
      };
      reference.dmCodeRef.current.focus();
    }
    if (dmCodeValidation.error === 'alphanumeric' || loginValidation.error === 'alphanumeric') {
      errorMessage = {
        dmCodeError:
          dmCodeValidation.error === 'alphanumeric'
            ? errorMessageConstants.searchError.alphanumeric
            : null,
        loginError:
          loginValidation.error === 'alphanumeric'
            ? errorMessageConstants.searchError.alphanumeric
            : null,
        noConditionsError: '',
      };
      if (dmCodeValidation.error === 'alphanumeric') {
        reference.dmCodeRef.current.focus();
      } else {
        reference.loginIdRef.current.focus();
      }
    }
    this.setState({ errorMessage });
    return false;
  };

  /**
   * performSearch is used to perform search operation
   * @param value
   */

  performSearch = () => {
    const errorValidation = this.performValidations();
    if (errorValidation) {
      this.setState({
        userList: [],
        openDialog: false,
      });
      this.filterDataAPI();
    } else {
      this.setState({
        userList: [],
        noResults: false,
        openDialog: false,
      });
    }
  };

  /**
   * filterDataAPI is used to perform search operation bymaking an API call
   * @param value
   */

  filterDataAPI = () => {
    const { searchSource } = this.state;
    this.setState({ gridLoading: true });
    const getDataList = async () => {
      const data = await post(APIENDPOINTS.ADMINLIST, searchSource);
      const response = await data.json();
      if (response.status) {
        const { idListVoList } = response;
        this.setState({
          userList: idListVoList,
          selectedRowValue: idListVoList.length ? idListVoList[0] : {},
          noResults: !idListVoList.length,
          gridLoading: false,
        });
        localStorage.setItem('idListParam',JSON.stringify(searchSource));
      } else {
        this.setState({
          selectedRowValue: {},
          noResults: true,
          gridLoading: false,
        });
      }
    };
    getDataList();
  };

  /**
   * validateSearchKeyValue is used to validate seacrh key fields
   * @param data
   * @param result
   */

  validateSearchKeyValue = data => {
    const result = { status: true, error: '' };
    if (isNotUndefinedOrNotEmpty(data)) {
      if (isAlphaOrNumeric(data)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'alphanumeric';
      }
    } else {
      result.status = false;
      result.error = 'empty';
    }
    return result;
  };

  /**
   * setValidationErrorMessage is used to error validation message
   * @param validationResultObject
   */

  setValidationErrorMessage = validationResultObject => {
    switch (validationResultObject.error) {
      case 'alphanumeric':
        return errorMessageConstants.searchError.alphanumeric;
      default:
        return '';
    }
  };

  /**
   * handleRadioChange is used to handle radio change event
   * @param value
   */

  handleRadioChange = value => {
    let { selectedRowValue } = this.state;
    selectedRowValue = value;
    this.setState({ selectedRowValue });
  };

  /**
   * handlePaginationClick is used to handle pagination event
   * @param tableData
   */

  handlePaginationClick = tableData => {
    if (tableData.length) {
      this.setState({ selectedRowValue: tableData[0] });
    }
  };

  /**
   * handleSelectChange is used to handle select combo event
   */

  handleSelectChange = () => {
    this.performSearch();
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    if (key === 'search') {
      this.performSearch();
    } else {
      const { selectedRowValue } = this.state;
      const serviceMap = {
        change: ROUTEENDPOINTS.IDUPDATE,
        delete: ROUTEENDPOINTS.IDDELETE,
      };
      this.serviceCall(serviceMap[key], selectedRowValue);
    }
  };

  /**
   * serviceCall is used to make service call
   * @param pathName
   * @param param
   */

  serviceCall = (pathName, param) => {
    const { history } = this.props;
    const getUserDetails = async () => {
      this.setState({ pageLoading: true });
      const data = await post(APIENDPOINTS.IDUSERDETAILS,param);
      const userDetails = await data.json();
      if (userDetails.status) {
        history.push(pathName, { param: userDetails.idListVo });
      } else {
        this.setState({ pageLoading: false, openDialog: true });
      }
    };
    getUserDetails();
  };

  /**
   * onKeyDown is used to perform key down operation
   * @param event
   */

  onKeyDown = event => {
    if (event.keyCode === 13) this.performSearch();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const {
      userList,
      noResults,
      errorMessage,
      pageLoading,
      searchSource,
      gridLoading,
      reference,
      openDialog,
      selectedRowValue,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          title={headerInformation('idlist').title}
          description={headerInformation('idlist').description}
        />
        <IDSearchPanel
          reference={reference}
          searchSource={searchSource}
          errorMessage={errorMessage}
          onHandleClick={key => this.onHandleClick(key)}
          onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, value)}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '60%', left: 'calc(60% - 55px)' }}
        >
          {userList.length ? (
            <IDGrid
              userList={userList}
              defaultSelected={resultPerPageKey}
              onHandleClick={key => this.onHandleClick(key)}
              handleRadioChange={event => this.handleRadioChange(event)}
              handlePaginationClick={tableData => this.handlePaginationClick(tableData)}
              handleSelectChange={() => this.handleSelectChange()}
              selectedRowValue={selectedRowValue}
            />
          ) : (
            ''
          )}
          {noResults ? (
            <>
              <br />
              <NoResultsFound />
            </>
          ) : (
            ''
          )}
        </CircularSpinner>
        <SBDialog
          open={openDialog}
          header="error"
          type="error"
          okButtonTitle="ok"
          content="user_already_deleted"
          onOk={() => this.performSearch()}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

IDList.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(PropTypes.string),
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
IDList.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(IDList));
