import React from 'react';
import { connect } from 'react-redux';
import SBTableLayout from '../../common/component/tableLayout/SBTableLayout';
import SetHeader from './SetHeader';
import checkLanguage from '../../language/checkLanguage';

function TableSwitch(props) {
  const languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  const {
    heading,
    buttonList,
    tableData,
    filterKey,
    filterValue,
    subHeaderList,
    hasSubCaption,
    limitContent,
    addRemoveClick,
    getList,
    currentIndex,
    setPageChange,
  } = props;
  return (
    <div id="contents-main">
      <div>
        {heading && (
          <SetHeader
            caption={languageLabelCheck(heading)}
            setEvent={name => addRemoveClick(name)}
            buttonList={buttonList}
          >
            {buttonList && (
              <SwitchTabButton
                getList={getList}
                currentIndex={currentIndex}
                setPageEvent={getIndex => setPageChange(getIndex)}
              />
            )}
          </SetHeader>
        )}
        {subHeaderList &&
          subHeaderList.map((rowData, idx) => {
            return (
              <div>
                {hasSubCaption && (
                  <h2 className="title-corner relative">{languageLabelCheck(rowData)}</h2>
                )}
                <SBTableLayout
                  tableData={tableData[idx]}
                  filterKey={filterKey}
                  filterValue={filterValue}
                  rowspan="rowspan"
                />
              </div>
            );
          })}
      </div>
      <div className="red bold" style={{ display: limitContent ? 'block' : 'none' }}>
        Wi-Fi can be set up to 10.(E4212)
      </div>
    </div>
  );
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(TableSwitch);

function SwitchTabButton({ getList, currentIndex, setPageEvent }) {
  return (
    <ul className="pagination floatright mr10">
      {getList.length !== 1 &&
        getList.length <= 10 &&
        getList.map((setter, index) => {
          return (
            <li>
              {currentIndex !== index ? (
                <a
                  href
                  style={{
                    color: '#7F7F7F',
                    border: '1px solid #B2B2B2',
                    background: '#fefefe',
                    textDecoration: 'none',
                    display: 'block',
                    padding: '0 8px',
                    borderRadius: 3,
                    height: 23,
                    fontSize: 13,
                    cursor: 'pointer',
                  }}
                  onClick={() => setPageEvent(index)}
                >
                  {index + 1}
                </a>
              ) : (
                <span>{index + 1}</span>
              )}
            </li>
          );
        })}
    </ul>
  );
}
