function mapToFilterData(bcpLanguageList, messageMap) {
  return bcpLanguageList.filter((comboData) =>
    Object.keys(messageMap).some((selected) => selected === comboData.value),
  );
}

/**
 * buildWifiCertPhaseTwoOptions is used to build Phase Two Authentication options based on selected EAP method
 * @param policyData
 * @return phaseTwoAuthenticationOptions
 */
function buildWifiCertPhaseTwoOptions(policyData) {
  if (
    policyData &&
    policyData.wifiCertificateConfiguration &&
    policyData.wifiCertificateConfiguration.openNetworkConfiguration &&
    policyData.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi.EAP
      .Outer === 'EAP-TTLS'
  ) {
    return groupComboData.phaseTwoAuthentication;
  }
  return [
    groupComboData.phaseTwoAuthentication[0],
    groupComboData.phaseTwoAuthentication[3],
    groupComboData.phaseTwoAuthentication[4],
  ];
}

/**
 * checkComponentVisibility is to check whether component can be made visible in screen
 * @param dataSource
 * @param fieldKey
 * @return boolean
 */
function checkComponentVisibility(dataSource, fieldKey) {
  const eapMethod =
    dataSource.wifiCertificateConfiguration &&
    dataSource.wifiCertificateConfiguration.openNetworkConfiguration
      ? dataSource.wifiCertificateConfiguration.openNetworkConfiguration.NetworkConfigurations.WiFi
          .EAP.Outer
      : '';
  switch (fieldKey) {
    case 'phaseTwo':
      return eapMethod === 'PEAP' || eapMethod === 'EAP-TTLS';
    case 'caCertSwitch':
      return true;
    case 'caCert':
      return dataSource.wifiCertificateConfiguration.cacertificate;
    case 'IndividualCSV':
      return eapMethod === 'PEAP' || eapMethod === 'EAP-TTLS';
    case 'userCertSwitch':
      return eapMethod === 'EAP-TLS';
    case 'UserCertificate':
      return eapMethod === 'EAP-TLS' && dataSource.wifiCertificateConfiguration.usercert;
    case 'csvbtn':
      return dataSource.wifiCSV.WifiRadio === 'csv';
    case 'selection_method':
      return dataSource.openNetworkConfiguration.NetworkConfigurations.length > 1;
    default:
      return false;
  }
}

