import React from 'react';
import { connect } from 'react-redux';
import LogoSb from '../image/logo-softbank.png';
import checkLanguage from '../../../language/checkLanguage';

const styles = {
  small: {
    fontSize: 13,
    fontFamily: 'Arial',
    marginLeft: 15,
    lineHeight: '38px',
    color: '#666',
    top: 1,
    position: 'relative',
  },
  version: {
    float: 'right',
    color: '#999',
    marginRight: 20,
    marginLeft: 20,
    fontSize: 13,
    lineHeight: '40px',
  },
  image: {
    position: 'relative',
    top: 7,
  },
};
const FooterLayout = props => {
  const languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };
  const getAnchorComponent = (anchorUrl,anchorContent) => {
    return (
      <a
        href={anchorUrl}
        target="_blank"
        rel="noopener noreferrer"
      >
        {languageLabelCheck(anchorContent)}
      </a>
    );
  }
  return (
    <div>
      <img style={styles.image} src={LogoSb} alt="SoftBank" />
      <small style={styles.small}>Copyright © SoftBank All rights reserved.</small>
      <small style={styles.version}>Ver 12.4</small>
      <div style={{ float: 'right' }}>
        <ul className="submenu">
          <li>
            {getAnchorComponent('http://www.softbanktelecom.co.jp/en/info/public/terms/index.html','contract_term_and_conditions')}
          </li>
          <li>
            {getAnchorComponent('http://www.softbanktelecom.co.jp/en/security/index.html','information_security_policy')}
          </li>
          <li>
            {getAnchorComponent('http://www.softbanktelecom.co.jp/en/privacy/index.html', 'about_personal_information')}
          </li>
        </ul>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  const { data: localeData } = state.language;
  return {
    localeData,
  };
};

export default connect(mapStateToProps)(FooterLayout);