/**
 * getDeviceDetail is to fetch device information for the selected record
 * @param props
 * @param getState
 * @return deviceRecord
 */
export const getDeviceDetail = async (
  { location, tabKey, defaultPolicyData },
  getState,
  button,
) => {
  const { deviceId, operationId } = location.state;
  const methodName = apiEndPointMapper(tabKey);
  const checkApp = tabKey === 'app';
  const { search, enumList } = getState;
  if (methodName) {
    const requestParam = checkApp
      ? { deviceId, search: (search || '').trim(), enumList }
      : { deviceId };
    if (tabKey === 'operationView') requestParam.operationId = operationId;
    if (tabKey === 'device') requestParam.button = button;
    const response = await post(methodName, requestParam);
    const result = await response.json();
    const { status, message } = result;
    let { data: dataSource } = result;
    if (tabKey === 'restriction') {
      dataSource.alwaysOnVpnPackage =
        dataSource.alwaysOnVpnPackageObject || defaultPolicyData.alwaysOnVpnPackageObject;
      dataSource.deviceOwnerLockScreenInfo =
        dataSource.deviceOwnerLockScreenInfoObject ||
        defaultPolicyData.deviceOwnerLockScreenInfoObject;
      dataSource.systemUpdate =
        dataSource.systemUpdateObject || defaultPolicyData.systemUpdateObject;
    }
    if (checkApp) {
      dataSource = dataSource.map((_record) => {
        const record = { ..._record };
        record.installType = record.installType === 'PREINSTALLED' ? '○' : '';
        return record;
      });
    }
    let makeDeviceData = {
      ...getState,
      message,
      gridLoading: false,
      pageLoading: false,
      openErrorDialog: true,
    };
    if (status === 'OK')
      makeDeviceData = {
        ...getState,
        dataSource,
        pageLoading: false,
        gridLoading: false,
        footer: {
          ...getState.footer,
          disable: tabKey === 'operations' || dataSource.lockFlag,
        },
        factoryResetDisable:
          tabKey === 'user' && dataSource.factoryResetDisable !== null
            ? dataSource.factoryResetDisable
            : false,
      };
    return makeDeviceData;
  }
  return { ...getState, pageLoading: false };
};

/**
 * enumMapper is to map selected enum from the specified value
 * @param screenKey
 * @return enumList
 */
export const enumMapper = (screenKey) =>
  ({
    remoteWipe: 'WIPE_EXTERNAL_STORAGE',
    resetPassword: 'LOCK_NOW',
  }[screenKey]);
