/**
 * DashboardDonutChart screen is  a customised donut used to load the dashboard DonutChartdata
 @author Vijayavel Mohan
 */

import React from 'react';
import { connect } from 'react-redux';
import DonutChart from 'react-d3-donut';
import checkLanguage from '../../../language/checkLanguage';


 const DashboardDonutChart =(props) => {
   
  const { localeData} = props;
  const {dataPoints} = props || []; 
  
  
  /**
  * languageLabelCheck is used to get the label data based on lang
  * @param {String} label 
  */
  const  languageLabelCheck = (label) => {
    return checkLanguage(localeData, label);
  };
  
  
  /**
  * generate legendlabel is to generate the legend for the donut chart
  * @param {array} dataArray 
  */
  const generateLegendLabel = (dataArray = []) => {
    const map = {}; 
    dataArray.map(statusData =>  map[statusData.name] =statusData.count);
    const legendData = [
      {
        icon:'device-status-successful',
        label:languageLabelCheck('updated_successfully'),
        value:map[languageLabelCheck('updated_successfully')] || 0,
        style:{
          height:'62px'
        }
      },
      {
        icon:'device-status-updating',
        label:languageLabelCheck('updating'),
        value:map[languageLabelCheck('updating')] || 0,
        style:{
          height:'54px'
        }
      },
      {
        icon:'device-status-safetynet',
        label:languageLabelCheck('safetynet_detection'),
        value:map[languageLabelCheck('safetynet_detection')] || 0,
        style:{
          height:'52px'
        }
      }
    ];
    
    return legendData.map((data) => {
      return(         
        <tr className={data.keyClass} id={data.icon}>
          <td style={data.style}>
            <dl className="clearfix">
              <dt className={data.icon}>
                <font className='dashboard-font-align'>
                  <font className='dashboard-font-align'>{data.label}</font>
                </font>
              </dt>
              <dd>
                <span className="numd-large">{data.value}</span>
                {languageLabelCheck('unit/items')}
              </dd>
            </dl>
          </td>
        </tr>
           );
          });
        }
        
        
		return (
  <>
    <DonutChart
      innerRadius={70}
      outerRadius={80}
      transition
      svgClass="example1"
      pieClass="pie1"
      strokeWidth={3}
      data={dataPoints}
      displayTooltip
    />     
    <div className="dashboard-circular-device-type">
      <font className='dashboard-font-align'><font className='dashboard-font-align'>Android</font></font>
    </div>
    <div className="dashboard-android-device-icon">
      <img src="https://stara.bizconcier-dm.com/wide_img/icon-android-circle.png" alt="" />
    </div>
    <div className="data">
      <table style={{width: '285px'}}>
        <tbody>
          { generateLegendLabel(dataPoints)}
         
        </tbody>
      </table>
    </div>
  </> 
        );
}


/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
 const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    localeData: state.language.data,
    userVo
  };
};

export default  connect(mapStateToProps)(DashboardDonutChart);