/** Bpse Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import cloneDeep from 'lodash/cloneDeep';
import PropTypes from 'prop-types';

import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';

/** SBComponents */
import SBDialog from '../../../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementGrid from '../../../../groupSetting/component/GroupManagementGrid';
import SearchPanel from '../component/SearchPanel';
import GroupManagementFooter from '../../../../groupSetting/component/GroupManagementFooter';
import { groupManagementHeader } from '../../../../groupSetting/constants/groupSettingHeaderConstants';
/** Utility Files */
import {
  getAuthorityList,
  getTargetDeviceList,
  getApplicationList,
} from '../../../../actions/GroupAction';
import { getSecurityPolicyGroupList } from '../../../../actions/securityPolicyAction';
import { serviceMapper } from '../../../../groupSetting/common/GroupSettingCommonFunctions';
import { post } from '../../../../actions/commonActions';
import APIEndPoints from '../../../../constants/APIEndPoints';
import commonChangeAction from '../../../../common/changeReducer/commonChangeAction';
import UNDERSCORE from '../../../../v1/common/utils';

/**
 * SecurityPolicyGroup is to select authority, device and application
 * for the target group
 * @author Aswin Palanisamy
 */
class SecurityPolicyGroup extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, screenKey } = props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    const { mode } = { mode: 1 };
    this.state = {
      mode,
      screenKey,
      message: '',
      disable: true,
      groupAuthority: true,
      defaultList: [],
      gridDataList: [],
      pageLoading: true,
      gridLoading: false,
      isUpdate: false,
      isEdit: mode === 2,
      countInfoContents: 0,
      openErrorDialog: false,
      selectCheckBoxList: param && param.selectedGroupList ? param.selectedGroupList : [],
      headerData: groupManagementHeader(screenKey),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { mode } = this.state;
    this.setState({ gridLoading: true }, () => (!mode ? this.denyAccess() : this.getGridData('')));
  }

  /**
   * denyAccess used to show message when mode not exists.
   */
  denyAccess = () =>
    this.setState({
      gridLoading: false,
      pageLoading: false,
      openErrorDialog: true,
      message: 'data_does_not_exists',
    });

  /**
   * getGridData is to get dataList to be loaded in grid.
   * @param searchName
   * @param isSearch
   */
  getGridData = async (value, isSearch) => {
    const { defaultList, pageLoading } = this.state;
    let { selectCheckBoxList } = this.state;
    const { location } = this.props;
    let securityPolicyId;
    let changeCheck = false;
    if (location.state) {
      securityPolicyId = location.state.param.dataSource.securityPolicyId
        ? location.state.param.dataSource.securityPolicyId
        : 0;
    } else {
      changeCheck = true;
      securityPolicyId = -1;
    }
    const response = await post(APIEndPoints.SECURITY_POLICY_GROUP, {
      groupName: value,
      securityPolicyId,
    });
    const result = await response.json();

    const { status, message, data: responseData } = result;
    if (!status || status === 'OK') {
      const gridList = responseData.dataList;
      const gridDataList = this.setGridDataIndex(gridList || result);
      if (changeCheck && responseData.securityPolicyId > 0) {
        selectCheckBoxList = gridDataList.filter(data => data.checked);
      }
      this.setState({
        gridDataList,
        groupAuthority: responseData.groupAuthority,
        pageLoading: false,
        gridLoading: false,
        selectCheckBoxList,
        isUpdate: responseData.securityPolicyId > 0,
        countInfoContents: gridDataList.length,
        defaultList: cloneDeep(isSearch ? defaultList : gridDataList),
      });
      if (pageLoading && this.searchRef.current) this.searchRef.current.focus();
    } else {
      this.setState({ pageLoading: false, gridLoading: false, openErrorDialog: true, message });
    }
  };

  /**
   * setGridDataIndex is to set index key of grid data list & set griDataList to redux
   * @param gridDataList
   */
  setGridDataIndex = (gridList = []) => {
    const dataList = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;

      return record;
    });
    return dataList;
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, value) => {
    if (event.keyCode === 13) {
      this.setState({ gridLoading: true }, () => this.getGridData(value));
    }
  };

  /**
   * makeFilterCondition is to generate data filter condition
   * @param record
   * @param selectedGridData
   * @param checkerKey
   * @param isList
   * @return boolean
   */
  makeFilterCondition = (record, selectedGridData) => {
    return UNDERSCORE.findIndex(selectedGridData, { groupId: record.groupId }) >= 0;
  };

  /**
   * setSelectedRecord used to return the data index which need to get selected in checkbox.
   * @param selectedGridData
   */
  setSelectedRecord = selectedGridData => {
    const { gridDataList, isUpdate } = this.state;
    const { location } = this.props;
    const { update } = location.state ? location.state.param : { update: isUpdate };
    let resultData = [];
    if (selectedGridData) {
      resultData = gridDataList
        .filter(data => this.makeFilterCondition(data, selectedGridData))
        .map(record => record.id);
    } else if (update) {
      resultData = gridDataList.filter(data => data.checked).map(record => record.id);
    }
    return resultData;
  };

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param value
   */
  handleSearchAction = (key, event, value) => {
    if (key === 'searchButton') {
      this.setState({ gridLoading: true }, () => this.getGridData(value.searchKeyword));
    }
  };

  /**
   * handleCheckboxChange used to handle checkbox operation in grid.
   * @param event
   * @param selectedRowKeys
   */
  handleCheckboxChange = (event, selectedRowKeys) => {
    const selectCheckBoxList = [];
    const { gridDataList } = this.state;
    selectedRowKeys.forEach(data => selectCheckBoxList.push(gridDataList[data]));
    this.setState({
      selectCheckBoxList,
      disable: false,
    });
  };

  /**
   * handleComboboxChange used to handle combobox operation in grid.
   * @param event
   * @param rowIndex
   */
  handleComboboxChange = (event, rowIndex) => {
    const { defaultList, selectCheckBoxList: checkList } = this.state;
    const { value } = event.target;
    let { disable } = this.state;
    defaultList[rowIndex].installType = value;
    const gridDataList = cloneDeep(defaultList);
    const selectCheckBoxList = checkList.map(_record => {
      const record = { ..._record };
      const { packageName } = defaultList[rowIndex];
      record.installType = record.packageName === packageName ? value : record.installType;
      disable = record.packageName === packageName ? false : disable;
      return record;
    });
    this.setState({ gridDataList, defaultList, selectCheckBoxList, disable });
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   * @param screenFunctionMapper
   */
  onHandleClick = key => {
    const { selectCheckBoxList, isUpdate } = this.state;
    const { makeSecurityPolicyGroupList: setGroupListSelected, location, history } = this.props;
    const { update, selectedGroupList } =
      location.state && location.state.param
        ? location.state.param
        : { update: isUpdate, selectedGroupList: selectCheckBoxList };
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    if (key === 'save') {
      param.groupData = selectCheckBoxList;
      param.groupUpdated = !UNDERSCORE.isEqual(selectCheckBoxList, selectedGroupList);
    } else {
      param.groupData = selectedGroupList;
      param.groupUpdated = false;
    }
    if (location.state) setGroupListSelected(param);
    if (update) {
      history.push(ROUTEENDPOINTS.CHANGESECURITYPOLICY, {
        param: {
          securityPolicyId: param.dataSource ? param.dataSource.securityPolicyId : -1,
        },
      });
    } else {
      history.push(ROUTEENDPOINTS.ADDSECURITYPOLICY);
    }
  };

  /**
   * getBackToPreviousScreen used to navigate previous screen.
   * @param screenName
   * @param isConfirm
   */
  getBackToPreviousScreen = (screenName, isConfirm) => {
    const { history, location: getData, setModified } = this.props;
    const { ancestorScreen } = getData.state;
    history.push(screenName || ancestorScreen, {
      ...getData.state,
      parentScreen: getData.pathname,
    });
    if (isConfirm) setModified(true);
  };

  /**
   * navigateDetailScreen used to navigate detail sub screens.
   * @param screenName
   * @param isConfirm
   * @return historyObject
   */
  navigateDetailScreen = (screenName, isConfirm) => {
    return this.getBackToPreviousScreen(screenName, isConfirm);
  };

  /**
   * render performs group management selection screen rendering
   * @return SecurityPolicyGroup
   */
  render() {
    const {
      isEdit,
      message,
      disable,
      screenKey,
      headerData,
      pageLoading,
      gridLoading,
      gridDataList,
      openErrorDialog,
      groupAuthority,
      countInfoContents,
      selectCheckBoxList,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <SearchPanel
            reference={this.searchRef}
            onSearchAction={(key, event, values) => this.handleSearchAction(key, event, values)}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
          />
          <CircularSpinner
            loading={gridLoading}
            css={{ position: 'absolute', top: '60%', left: 'calc(60% - 55px)' }}
          >
            <GroupManagementGrid
              rowKey="id"
              type="checkbox"
              disableAll
              restrictPagination
              columnProps={{ isGroupAuthority: groupAuthority }}
              screenKey={screenKey}
              disableKey="assigned"
              gridList={gridDataList}
              countInfo="select_result_count"
              filterMessage="display_items_colon"
              countInfoContents={countInfoContents}
              handleCheckboxChange={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleSelectAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              handleClearAll={(event, selectedRowKeys) =>
                this.handleCheckboxChange(event, selectedRowKeys)
              }
              defaultSelectedKeys={this.setSelectedRecord(selectCheckBoxList)}
              handleComboboxChange={(event, rowIndex) => this.handleComboboxChange(event, rowIndex)}
            />
            <GroupManagementFooter
              footer={{
                isEdit,
                disable,
                screenKey,
              }}
              onHandleClick={key => this.onHandleClick(key)}
            />
          </CircularSpinner>
        </div>
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              const { history } = this.props;
              history.push(serviceMapper('securityPolicy'));
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps performs data setup in group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = dispatch => {
  return {
    setModified: data => dispatch(commonChangeAction(data)),
    makeAuthority: data => dispatch(getAuthorityList(data)),
    targetListMapper: data => dispatch(getTargetDeviceList(data)),
    makeApplicationList: data => dispatch(getApplicationList(data)),
    makeSecurityPolicyGroupList: data => dispatch(getSecurityPolicyGroupList(data)),
  };
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { department } = state.device;
  const { securityData } = state.securityPolicy;
  const { data } = state.language;
  const { authority, deviceList, applicationListMapper } = state.group;
  return {
    authority,
    deviceList,
    department,
    applicationListMapper,
    securityData,
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
SecurityPolicyGroup.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      mode: PropTypes.number,
      groupId: PropTypes.number,
    }),
  }).isRequired,
  setModified: PropTypes.func.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(SecurityPolicyGroup));
