/**
 * MultipleAdminCountHeader is used to set the count header
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';

class MultipleAdminCountHeader extends React.Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * render is used to render the header count area
   * @return component
   */

  render() {
    const { usersLength, maxUsercount } = this.props;
    return (
      <div className="clearfix mb10">
        <div className="floatleft">
          <span className="lineheight mr15">
            {this.addCountInfo('registered_count_items', usersLength)}
          </span>
          <span className="lineheight mr15">
            {this.addCountInfo('maximum_count_items', maxUsercount)}
          </span>
        </div>
      </div>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = state => {
  const { tenantVo } = state.tenant;
  return {
    tenantVo,
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminCountHeader.propTypes = {
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  maxUsercount: PropTypes.number,
  usersLength: PropTypes.number,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminCountHeader.defaultProps = {
  maxUsercount: 0,
  usersLength: 0,
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(MultipleAdminCountHeader);
