const moment = require('moment');
const dateFormatRequire = require('dateformat');

export function dateFormatWithTime(date) {
  return moment(date).format('YYYY-MM-DD HH:mm:ss Z');
}

export function dateFormat(date) {
  return moment(date).format('YYYY-MM-DD');
}

export const dateFormatWithSlash = 'YYYY/MM/DD';

export function dateFormatSlash(date) {
  return moment(date).format('YYYY/MM/DD');
}
export function dateFormatInvertSlash(date) {
  return moment(date).format('DD/MM/YYYY');
}

export function dateFormatMonth(date) {
  return moment(date).format('DD MMM YYYY');
}

/**
 * dateBasedOnLocale is used to return date format based on given locale
 * @param date
 * @param locale
 * @return formattedDate
 *
 */
export function dateBasedOnLocale(date, locale) {
  switch (locale) {
    case 'ja':
      return moment(date).format('YYYY/MM/DD');
    default:
    case 'en':
      return moment(date).format('DD MMM YYYY');
  }
}

/**
 * dateTimeBasedOnLocale is used to return date Time format based on given locale
 * @param date
 * @param locale
 * @return formattedDate
 *
 */
export function dateTimeBasedOnLocale(date, locale) {
  switch (locale) {
    case 'ja':
      return dateFormatRequire(date, 'yyyy/mm/dd HH:MM');
    default:
    case 'en':
      return dateFormatRequire(date, 'dd mmm yyyy HH:MM');
  }
}

/**
 * dateBasedOnLocaleGeneral is used to return date format based on given locale
 * @param date
 * @return formattedDate
 *
 */
export function dateBasedOnLocaleGeneral(date) {
  return moment(date).format('YYYYMMDD');
}

export function currentDate() {
  return moment().format('YYYY-MM-DD HH:mm:ss');
}

export function currentISODate() {
  return moment().toISOString();
}

export function differenceBetweenTwoDates(date1, date2) {
  const MILLISECONDS_PER_DAY = 1000 * 60 * 60 * 24;
  const timeDiff = Math.abs(date2.getTime() - date1.getTime());
  const diffDays = Math.ceil(timeDiff / MILLISECONDS_PER_DAY);
  return diffDays;
}

/**
 * getUserTodaysDate is used to get user's today date based on user's offset
 * @param offset
 * @return todayDate
 */
export function getUserTodaysDate(offset) {
  const oldOffSet = new Date().getTimezoneOffset();
  const offsetHours = parseInt(offset.split(':')[0], 10);
  const offsetMinutes = parseInt(offset.split(':')[1], 10);
  const newOffsetInTime =
    oldOffSet + (offsetHours * 60 + (offsetHours ? offsetMinutes : -offsetHours));
  return new Date(new Date().getTime() + newOffsetInTime * 60000);
}

/**
 * validateMinMaxDate is validate between min and max dates
 * @param value
 * @return boolean
 */
export function validateMinMaxDate(value) {
  const minDate = new Date('2011/01/01');
  const maxDate = new Date('2050/12/31');
  if (value >= minDate && value <= maxDate) {
    return true;
  }
  return false;
}

/**
 * dateTimeFormatWithoutOffset is return the date without offset
 * @param date
 * @returns dateWithoutOffset
 */
export function dateTimeFormatWithoutOffset(date) {
  return moment(date).format('YYYY/MM/DD HH:mm');
}

export function dateTimeUnderScoreWithoutOffset(date) {
  const localDate = moment.utc(date).toDate();
  const localTime = moment(localDate).format('YYYY-MM-DD HH:mm:ss');
  const tms = moment(localTime).format('X');
  return moment.unix(tms).format('YYYY-MM-DD HH:mm');
}
