import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../common/component/tableLayout/SBTableLayout';
import SBButton from '../../common/component/sbbutton/SBButton';
import SBTextbox from '../../common/component/sbtextbox/SBTextbox';
import SBRadioButton from '../../common/component/sbradio/SBRadio';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { post } from '../../actions/commonActions';
import { currentDate } from '../../common/utils/dateUtils';
import InfoDialog from '../component/InfoDialog';
import SBInfoIcon from '../../common/component/sbicons/SBInfoIcon';
import Utils from '../../constants/Utils';
import APIENDPOINTS from '../../constants/APIEndPoints';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import checkLanguage from '../checkLanguage';
import { CommonConstants } from '../../constants/CommonConstants';
import RouteEndPoints from '../../constants/RouteEndPoints';
import { fetchLanguageList } from '../../actions/FieldsAction';

const { API_URL_ORIGIN } = Utils;
const fileDownload = require('js-file-download');

const styles = {
  errDiv: {
    color: '#bd6363',
    border: '1px solid #bd6363',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    clear: 'both',
    padding: 10,
    top: 12,
    position: 'relative',
    height: 166,
    overflow: 'auto',
  },
  errDivHeader: {
    fontWeight: 'bold',
    paddingBottom: 4,
  },
  errDivContent: {
    padding: 4,
    listStyle: 'inside',
  },
};

/**
 * LanguageAdd is to add or update user language.
 * @author Gobi R
 */
class LanguageAdd extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const getData = props.location.state;
    const selectedRow = getData ? getData.selectedRow : {};
    this.state = {
      mode: getData ? getData.mode : 'ADD',
      code: selectedRow.code || '',
      name: selectedRow.languageName || '',
      enabled: selectedRow.enabled === undefined ? true : selectedRow.enabled,
      isChanged: false,
      file: '',
      open: false,
      isSaved: false,
      errorList: [],
      showErrorCode: false,
      errorMsg: '',
      languageCodeList: [],
      nameErrorMsg: '',
      openInfo: false,
      loading: true,
      modalIsOpen: false,
      langPrefered: false,
      dataNotExist:false,
    };
    this.initialFocus = createRef();
    this.modifyFocus = createRef();
    this.invalidFile = false;
  }

  /**
   * componentDidMount is to fetch already existing language data.
   */
  componentDidMount() {
    const { mode,code } = this.state;
    post(APIENDPOINTS.LANGUAGECODELIST,{})
      .then((response) => response.json())
      .then((data) => {
        if (mode !== 'ADD') {
          const  editingData = data.filter(lang => lang.code === code)[0];
          if(isEmpty(editingData)){
                 this.setState({
                  dataNotExist:true,
                  loading:false
                 });
          }else{
            this.setState(
              {
                name: editingData.languageName || '',
                enabled: editingData.enabled,
                languageCodeList: data.map(lang =>  lang.code),
                loading: false,
              },
              () => {
                if (mode === 'ADD') {
                  this.initialFocus.current.focus();
                } else {
                  this.modifyFocus.current.focus();
                }
              },
            );
          }
        }else{
          this.setState(
            {
              languageCodeList: data.map(lang =>  lang.code),
              loading: false,
            },
            () => {
              if (mode === 'ADD') {
                this.initialFocus.current.focus();
              } else {
                this.modifyFocus.current.focus();
              }
            },
          );
        }
       
      });
  }

  /**
   * getUploadedFile is to fetch currently uploaded file
   * @param event
   */
  getUploadedFile = (event) => {
    const { setModified } = this.props;
    const file = event.target.files[0];
    if (file && file.name.endsWith('.csv')) {
      setModified(true);
      this.setState({ isChanged: true, file });
      this.invalidFile = false;
    } else {
      this.invalidFile = true;
      this.setState({ isChanged: true });
    }
  };

  /**
   * onSaveConfirmation is to open save confirmation dialog.
   */
  onSaveConfirmation = () => {
    this.setState({ modalIsOpen: true });
  };

  /**
   * onSaveButtonClick is to save the language.
   */
  onSaveButtonClick = () => {
    const { file, code, name, enabled, mode } = this.state;
    const { setModified } = this.props;
    const formData = new FormData();
    formData.append('file', file);
    formData.append('code', code);
    formData.append('name', name);
    formData.append('enabled', enabled);
    this.setState({ loading: true, modalIsOpen: false });
    return fetch(`${API_URL_ORIGIN}/api/languages/${mode === 'ADD' ? 'upload' : 'edit'}`, {
      method: 'POST',
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        if(data.status === 'data_not_exists'){
          this.setState({
            dataNotExist:true,
            loading:false
           });
        }else  if (data.status === 'Success') {
          setModified(false);
          this.setState({ open: true, isSaved: true, loading: false, langPrefered: false });
        } else if (data.status === 'lang_prefered') {
          this.setState({ open: true, isSaved: false, langPrefered: true, loading: false });
        } else {
          this.setState({
            open: true,
            isSaved: false,
            errorList: data.errorList,
            loading: false,
            langPrefered: false,
          },() => this.saveButtonDisableCheck());
        }
        this.getAllLanguages();
        this.invalidFile = false;
      });
  };

  getAllLanguages = async () => {
    const { putAllLanguageData } = this.props;
    const languageResponse = await post(APIENDPOINTS.LANGUAGECODELIST, { searchedValue: '' });
    const languageList = await languageResponse.json();

    putAllLanguageData(languageList.filter((lang) => lang.enabled));
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * onCancelButtonClick is to cancel the current process and navigate to previous screen.
   */
  onCancelButtonClick = (changed = false) => {
    const { setModified } = this.props;
    if(changed){
       setModified(false);
    }
    const { history } = this.props;
    history.push(RouteEndPoints.LANGUAGESETTING);
  };

  /**
   * onCodeChange is to valid code field on change.
   * @param event
   */
  onCodeChange = (event) => {
    const { languageCodeList } = this.state;
    const code = event.target.value;
    const regex = /^[a-z]{2}$/g;
    let showErrorCode = false;
    let errorMsg = '';
    if (languageCodeList.includes(code)) {
      showErrorCode = true;
      errorMsg = 'code_already_exists';
    } else if (!regex.test(code)) {
      showErrorCode = true;
      errorMsg = 'please_enter_2_lowercase_letters';
    }
    this.setState({ code, errorMsg, showErrorCode });
  };

  /**
   * setHeader is to change screen header based on mode.
   * @return label
   */
  setHeader = () => {
    const { isSaved, mode, langPrefered } = this.state;
    if (isSaved) {
      return mode === 'ADD' ? 'new_language_add_message' : 'language_updated_sucess';
    }
    if (langPrefered && mode !== 'ADD') {
      return 'failed_to_disable';
    }
    return mode === 'ADD' ? 'failed_to_add' : 'failed_to_update';
  };

  /**
   * onNameChange is to valid Name field on change.
   * @param event
   */
  onNameChange = (event) => {
    const name = event.target.value;
    const regex = /^[a-zA-Z ]*$/g;
    let nameErrorMsg = '';
    if (!regex.test(name)) {
      nameErrorMsg = 'please_enter_only_alphabets';
    }
    this.setState({ name, nameErrorMsg, isChanged: true });
  };

  /**
   * onRadioButtonChange is to handle radio button change.
   * @param event
   * @param data
   */
  onRadioButtonChange = (event, data) => {
    let enabled = true;
    if (data === 'No') {
      enabled = false;
    }
    this.setState({ enabled, isChanged: true });
  };

  /**
   * setDefaultValue is to set default value to enabled field.
   */
  setDefaultValue = () => {
    const { enabled, mode } = this.state;
    if (mode === 'EDIT') {
      return enabled ? 'Yes' : 'No';
    }
    return 'Yes';
  };

  /**
   * openInfoDialog is to open info dialog.
   */
  openInfoDialog = () => {
    this.setState({ openInfo: true });
  };

  /**
   * onkeyDown is to handle enter keydown to trigger save.
   * @param event
   */
  onkeyDown = (event) => {
    if (event.keyCode === 13 && !this.saveButtonDisableCheck()) {
      event.preventDefault();
      this.onSaveConfirmation();
    }
  };

  /**
   * saveButtonDisableCheck is to set condition for save button disable.
   * @returns boolean
   */
  saveButtonDisableCheck = () => {
    if(this.invalidFile) return this.invalidFile;
    const { mode, name, code, file, showErrorCode, nameErrorMsg, isChanged } = this.state;
   return mode === 'EDIT'
    ?  !(name && code && !nameErrorMsg && isChanged)
    :  !(name && code && file && !showErrorCode && !nameErrorMsg );
  };

  /**
   * getTableData is define all fields in add language.
   * @returns array
   */
  getTableData = () => {
    const { mode, showErrorCode, code, name, errorMsg, nameErrorMsg } = this.state;
    return [
      {
        key: ['language_code_bold'],
        value: (
          <div style={{ display: 'inline-grid' }}>
            <SBTextbox
              type="text"
              maxLength="2"
              size="45"
              canModify
              ref={this.initialFocus}
              name="lang_code"
              defaultValue={code}
              disabled={mode === 'EDIT'}
              onKeyDown={this.onkeyDown}
              onChange={(event) => this.onCodeChange(event)}
            />
            {showErrorCode && (
              <font id="lang_code_msg" className="red bold">
                {this.languageLabelCheck(errorMsg)}
              </font>
            )}
          </div>
        ),
      },
      {
        key: ['name_bold'],
        value: (
          <div style={{ display: 'inline-grid' }}>
            <SBTextbox
              type="text"
              maxLength="100"
              size="45"
              name="name"
              ref={this.modifyFocus}
              canModify
              defaultValue={name}
              onKeyDown={this.onkeyDown}
              onChange={(event) => this.onNameChange(event)}
            />
            {nameErrorMsg && (
              <font id="lang_name_msg" className="red bold">
                {this.languageLabelCheck(nameErrorMsg)}
              </font>
            )}
          </div>
        ),
      },
      {
        key: ['bulk_add_language_content_file'],
        value: (
          <div>
            <label
              htmlFor="individual_csvfile"
              className="btn-fileupload mr5"
              style={{ width: '300' }}
              onChange={(event) => {
                this.getUploadedFile(event);
              }}
            >
              <input type="file" accept=".csv" name="individual_csvfile" id="individual_csvfile" />
            </label>
            <SBInfoIcon id="upload_info" onClick={() => this.openInfoDialog()} />
            <SBButton
              style={{margin: '0px 12px'}}
              type="button"
              name="download_template"
              title="download_template"
              onClick={() => this.downloadFile(true)}
            />
          </div>
        ),
      },
      {
        key: ['enable_bold'],
        value: (
          <SBRadioButton
            name="enable"
            id="enable"
            style={{ width: 180 }}
            defaultValue={this.setDefaultValue()}
            options={[
              { label: 'Yes', value: 'yes' },
              { label: 'No', value: 'no' },
            ]}
            filterKey="label"
            canModify
            filterValue="value"
            onChange={(event, value) => this.onRadioButtonChange(event, value)}
          />
        ),
      },
    ];
  };

  /**
   * afterOnOkClick is to handle ok button event
   */
  afterOnOkClick = () => {
    const { isSaved } = this.state;
    this.setState({ open: false });
    if (isSaved) {
      const { history } = this.props;
      history.push(RouteEndPoints.LANGUAGESETTING);
    }
  };





  /**
   * downloadFile is to downloadFile.
   * @param {boolean} templateFile
   */
  downloadFile = (templateFile = false) => {
    if(templateFile){
      let csv = '';
      const data = [['code','label', 'value']];
    data.forEach(function(row) {
            csv += row.join(',');
            csv += "\n";
    });
        fileDownload(csv, `template.csv`);
    }else{
      const { errorList } = this.state;
      fileDownload(errorList.join('\r\n'), `ErrorContent_${currentDate()}.txt`);
    }
  };

  /**
   * closeInfoDialog is to close Information Dialog.
   */
  closeInfoDialog = () => {
    this.setState({ openInfo: false });
  };

  /**
   * render is to load fields.
   * @returns jsx elements
   */
  render() {
    const { mode, open, isSaved, errorList, openInfo, loading, modalIsOpen,dataNotExist } = this.state;
    return (
      <div>
        <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
          <SBContentHeader
            title={mode === 'ADD' ? 'add_language' : 'edit_language'}
            description={
              mode === 'ADD' ? ['start_adding_new_language'] : ['disable_language_and_edit']
            }
          />
          <SBTableLayout tableData={this.getTableData()} filterKey="key" filterValue="value" />
          <div className="floatright">
            <SBButton
              type="submit"
              name="savebutton"
              color="green"
              title="save"
              disabled={this.saveButtonDisableCheck()}
              onClick={() => this.onSaveConfirmation()}
            />
            <SBButton
              type="button"
              name="cancel"
              title="cancel"
              canModify
              noMarginRight
              onClick={() => this.onCancelButtonClick()}
            />
          </div>
          {errorList.length > 0 && (
            <div>
              <div style={styles.errDiv}>
                <div style={styles.errDivHeader}>
                  {this.languageLabelCheck('please_fix_below_errors_document')}
                </div>
                <ul style={{ listStyleType: 'disc' }}>
                  {errorList.map((errMsg, i) => (
                    <li style={styles.errDivContent} key={(i + 1).toString()}>
                      {errMsg}
                    </li>
                  ))}
                </ul>
              </div>
              <br />
              <br />
              <div className="floatright">
                <SBButton
                  type="button"
                  name="csv download"
                  id="csv download"
                  title="download"
                  color="gray"
                  onClick={() => this.downloadFile(false)}
                />
              </div>
            </div>
          )}
          {dataNotExist ? (
            <SBDialog
              open
              header='error'
              type='error'
              content='data_does_not_exists'
              onOk={() => this.onCancelButtonClick(true)}
              width="400px"
              okButtonTitle="ok"
            />
) : (
  <SBDialog
    open={open}
    header={isSaved ? 'save_successful' : 'save_failed'}
    type={isSaved ? 'success' : 'error'}
    content={this.setHeader()}
    onOk={() => this.afterOnOkClick()}
    width="400px"
    okButtonTitle="ok"
  />
        )}
          <SBDialog
            open={modalIsOpen}
            header="save_confirmation"
            type="confirmation"
            content={mode === 'ADD' ? 'do_you_want_add_language' : 'do_you_want_save_changes'}
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={this.onSaveButtonClick}
            width="400px"
          />
          <InfoDialog open={openInfo} closeDialog={() => this.closeInfoDialog()} />
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
LanguageAdd.propTypes = {
  setModified: PropTypes.func.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string),
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    mode: PropTypes.string,
    selectedRow: PropTypes.shape({
      code: PropTypes.string,
      enabled: PropTypes.bool,
      languageName: PropTypes.string,
    }),
  }).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
LanguageAdd.defaultProps = {
  localeData: {},
};

/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

/**
 * mapDispatchToProps is to map store actions to props.
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
    putAllLanguageData: (data) => {
      dispatch(fetchLanguageList(data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LanguageAdd);
