function group(state = initialState, action) {
  const {
    type,
    data,
    wifiList,
    shortMessageList,
    LongMessageList,
    shortDeleteMessageList,
    longDeleteMessageList,
    bcpLanguageList,
    changePolicyWifiDeleteList: currentPolicyWifiDeleteList,
    changePolicyWifiCertDeleteList: currentPolicyWifiCertDeleteList,
  } = action;
  const {
    groupName,
    authority,
    deviceList,
    mainPolicyMapper,
    changePolicyWifiList,
    changePolicyShortMessageList,
    changePolicyLongMessageList,
    changePolicyShortDeleteMessageList,
    changePolicyLongDeleteMessageList,
    changePolicyWifiDeleteList,
    changePolicyWifiCertDeleteList,
    changePolicyMapper,
    bcpLanguageList: defaultBcpLanguageList,
    applicationListMapper,
    managedConfiguration,
    kioskAppSelect,
    kioskBgApps,
    imeApps,
    asmApps,
    kioskMultiApps,
    homeSingleSelect,
  } = initialState;
  switch (type) {
    case FETCH_GROUP_NAME:
      return {
        ...state,
        groupName: data || groupName,
      };
    case FETCH_AUTHORITY_LIST:
      return {
        ...state,
        authority: data || authority,
      };
    case FETCH_TARGET_DEVICE_LIST:
      return {
        ...state,
        deviceList: data || deviceList,
      };
    case FETCH_POLICY_LIST:
      return {
        ...state,
        mainPolicyMapper: data || mainPolicyMapper,
        policyModified: true,
      };
    case FETCH_CHANGE_POLICY_LIST:
      return {
        ...state,
        changePolicyMapper: data || changePolicyMapper,
        changePolicyWifiList: wifiList || changePolicyWifiList,
        changePolicyShortMessageList: shortMessageList || changePolicyShortMessageList,
        changePolicyLongMessageList: LongMessageList || changePolicyLongMessageList,
        changePolicyShortDeleteMessageList:
          shortDeleteMessageList || changePolicyShortDeleteMessageList,
        changePolicyLongDeleteMessageList:
          longDeleteMessageList || changePolicyLongDeleteMessageList,
        changePolicyWifiDeleteList: currentPolicyWifiDeleteList || changePolicyWifiDeleteList,
        changePolicyWifiCertDeleteList:
          currentPolicyWifiCertDeleteList || changePolicyWifiCertDeleteList,
        bcpLanguageList: bcpLanguageList || defaultBcpLanguageList,
      };
    case RESET_CHANGE_POLICY_LIST:
      return {
        ...state,
        changePolicyMapper: initialState.changePolicyMapper,
        changePolicyWifiList: [],
        changePolicyShortMessageList: [],
        changePolicyLongMessageList: [],
        changePolicyShortDeleteMessageList: [],
        changePolicyLongDeleteMessageList: [],
        changePolicyWifiDeleteList: [],
        changePolicyWifiCertDeleteList: [],
        policyModified: false,
      };
    case FETCH_APPILICATION_LIST:
      return {
        ...state,
        applicationListMapper: data || applicationListMapper,
      };
    case FETCH_KIOSK_SELECT:
      return {
        ...state,
        kioskAppSelect: data || kioskAppSelect,
      };
    case FETCH_KIOSK_BG_APPS:
      return {
        ...state,
        kioskBgApps: data || kioskBgApps,
      };
    case FETCH_IME_APPS:
      return {
        ...state,
        imeApps: data || imeApps,
      };
    case FETCH_ASM_APPS:
      return {
        ...state,
        asmApps: data || asmApps,
      };
    case FETCH_KIOSK_MULTI_APPS:
      return {
        ...state,
        kioskMultiApps: data || kioskMultiApps,
      };
    case FETCH_HOME_SINGLE_APP:
      return {
        ...state,
        homeSingleSelect: data || homeSingleSelect,
      };
    case FETCH_MANAGED_CONFIGURATION:
      return {
        ...state,
        managedConfiguration: data || managedConfiguration,
      };
    case FETCH_GROUP_INFORMATION:
      return {
        ...state,
        ...(data || initialState),
      };
    case RESET_GROUP_INFORMATION:
      return {
        ...state,
        ...initialState,
      };
    default:
      return state;
  }
}

export default group;
