/**
 * AppFinishPage is used to load finish page
 * @author: P.Vinothpandian
 *
 */

import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import {
  tableBasicView,
  tableSalesView,
  tableBusinessView,
  tableRemarkOneView,
  tableRemarkTwoView,
  tableDistinguishView,
} from '../Constants/AppFrameConstants';
import { CommonConstants } from '../../../constants/CommonConstants';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import AppCRUDFooter from './AppCRUDFooter';
import SBLinkHeader from '../../../common/component/sblinkheader/SBLinkHeader';
import headerInformation from '../Constants/AppManageHeaderConstants';

class AppFinishPage extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    let isUpdate = false;
    if (location.state) {
      param = location.state.param;
      isUpdate =  location.state.isUpdate;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: isUpdate ? headerInformation('appupdatefinish') : headerInformation('appfinish'),
      dataSource: param,
      isUpdate,
      pageLoading: true,
      footer: { primaryVisibile: false },
    };
  }

  /**
   * componentDidMount is used to change the state of page loading
   */

  componentDidMount() {
    this.setState({ pageLoading: false });
  }

  /**
   * onHandleClick is used to handle click event
   */

  onHandleClick = () => {
    const { history } = this.props;
    const { isUpdate } = this.state;
    if( isUpdate ) {
      history.push(ROUTEENDPOINTS.APPLIST);
    } else {
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * render is used to render the component
   * @return AppfinishPageComponent
   */

  render() {
    const { headerData, dataSource, footer, pageLoading, isUpdate } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <SBTableHeader headerText="basic_information" />
                <CustomTableFrame
                  tableList={tableBasicView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBLinkHeader
                  hyperLinkText="contract_information"
                  hyperLink={ROUTEENDPOINTS.APPCONTRACTFINISH}
                  historyData={{ dataSource, isUpdate }}
                />
              </div>
              <div>
                <SBTableHeader headerText="sales_information" />
                <CustomTableFrame
                  tableList={tableSalesView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBTableHeader headerText="business_information" />
                <CustomTableFrame
                  tableList={tableBusinessView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBLinkHeader
                  hyperLinkText="app_use_information"
                  hyperLink={ROUTEENDPOINTS.APPOPERATIONFINFOFINISH}
                  historyData={{ dataSource, isUpdate }}
                />
              </div>
              <div>
                <SBTableHeader headerText="resales_agency_info" />
                <CustomTableFrame tableList={tableDistinguishView} dataSource={dataSource} />
              </div>
              <div>
                <SBTableHeader headerText="remark_one" />
                <CustomTableFrame
                  tableList={tableRemarkOneView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <SBTableHeader headerText="remark_two" />
                <CustomTableFrame
                  tableList={tableRemarkTwoView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <AppCRUDFooter 
                  reference={{backRef: createRef()}}
                  footer={footer}
                  onHandleClick={key => this.onHandleClick(key)} 
                />
              </div>
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */

AppFinishPage.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
      isUpdate : PropTypes.bool
    }),
  }).isRequired,
};

export default withRouter(AppFinishPage);
