/**
 * AppOperationFinish is used to load finish page
 * @author: P.Vinothpandian
 *
 */

import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import AppCRUDFooter from './AppCRUDFooter';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import { setInfoIndex, setSpecialIndex } from '../Constants/operationalConstants';
import { postWithBlobData } from '../../../actions/commonActions';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import {
  tableInfoListView,
  tableIndentifyView,
  tableSubstituteView,
  tableIndividualView,
  tableSpecialNoteView,
  tableReportView,
} from '../Constants/AppFrameConstants';
import OperationUploadFinish from './OperationUploadFinish';
import { CommonConstants } from '../../../constants/CommonConstants';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../Constants/AppManageHeaderConstants';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

const fileDownload = require('js-file-download');

class AppOperationFinish extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    let isUpdate = false;
    let isDelete = false;
    if (location.state) {
      param = location.state.dataSource;
      isUpdate = location.state.isUpdate;
      isDelete = location.state.isDelete;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('operationfinish'),
      dataSource: param,
      pageLoading: true,
      isUpdate,
      isDelete,
      infoIndex: setInfoIndex(param),
      specialIndex: setSpecialIndex(param),
      footer: { primaryVisibile: false },
    };
  }

  /**
   * componentDidMount is used to change the state of page loading
   */

  componentDidMount() {
    this.setState({ pageLoading: false });
  }

  /**
   * downloadFile is used to handle download button click
   * @param key
   */

  downloadFile = (key) => {
    const { dataSource } = this.state;
    postWithBlobData(APIENDPOINTS.APPLICATIONFILEDOWNLOAD, {
      dmCode: dataSource.dmCode,
      rootPath: key,
      fileName: key === 'AttachFile' ? dataSource.attachFileName : dataSource.individualFileName,
    })
      .then((response) => response.blob())
      .then((blobData) => {
        const printData = new Blob([blobData]);
        fileDownload(
          printData,
          key === 'AttachFile' ? dataSource.attachFileName : dataSource.individualFileName,
        );
      });
  };

  /**
   * onHandleClick is used to handle click event
   */

  onHandleClick = () => {
    const { isUpdate, dataSource, isDelete } = this.state;
    const { history } = this.props;
    if (isDelete) {
      history.push(ROUTEENDPOINTS.APPDELETE, { dmCode: dataSource.dmCode });
    } else {
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTERFINISH, { param: dataSource, isUpdate });
    }
  };

  /**
   * render is used to render the component
   * @return AppfinishPageComponent
   */

  render() {
    const { headerData, infoIndex, dataSource, footer, pageLoading, specialIndex } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="border p15 mb10">
              <div>
                <SBTableHeader headerText="operator_information" />
                {[...Array(infoIndex)].map((data, index) => (
                  <CustomTableFrame
                    tableList={tableInfoListView(index + 1)}
                    dataSource={dataSource}
                    defaultValue={{}}
                  />
                ))}
                <div>
                  <SBTableHeader headerText="operational_identification" />
                  <CustomTableFrame
                    tableList={tableIndentifyView}
                    dataSource={dataSource}
                    defaultValue={{}}
                  />
                </div>
                <div>
                  <SBTableHeader headerText="substitute_operation" />
                  <CustomTableFrame
                    tableList={tableSubstituteView}
                    dataSource={dataSource}
                    defaultValue={{}}
                  />
                </div>
                <div>
                  <SBTableHeader headerText="report_operation" />
                  <CustomTableFrame
                    tableList={tableReportView}
                    dataSource={dataSource}
                    defaultValue={{}}
                  />
                </div>
                <div>
                  <SBTableHeader headerText="operation_attached_data" />
                  {dataSource && dataSource.attachFileName ? (
                    <OperationUploadFinish
                      fileName={dataSource.attachFileName}
                      dateTime={dataSource.attachFileUploadedDate}
                      uploadedBy={dataSource.attachFileUploadedBy}
                      onURLClick={() => this.downloadFile('AttachFile')}
                    />
                  ) : null}
                </div>
                <SBTableHeader headerText="operation_other_individual" />
                <CustomTableFrame
                  tableList={tableIndividualView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
                <CustomTableFrame
                  tableList={tableSpecialNoteView(specialIndex)}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
                {dataSource && dataSource.individualFileUploadedDate ? (
                  <OperationUploadFinish
                    fileName={dataSource.individualFileName}
                    dateTime={dataSource.individualFileUploadedDate}
                    uploadedBy={dataSource.individualFileUploadedBy}
                    onURLClick={() => this.downloadFile('IndividualFile')}
                  />
                ) : null}
              </div>
            </div>
            <div>
              <AppCRUDFooter
                reference={{ backRef: createRef() }}
                footer={footer}
                onHandleClick={(key) => this.onHandleClick(key)}
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */

AppOperationFinish.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
    }),
  }).isRequired,
};

export default withRouter(AppOperationFinish);
