import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import SBButton from '../sbbutton/SBButton';
import checkLanguage from '../../../language/checkLanguage';
import descriptionConveter from '../../utils/descriptionConverter';
import infoIcon from '../../../../css/images/information_icon.png';

const styles = {
  dialog: {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      padding: 0,
      minWidth: 400,
    },
    overlay: {
      zIndex: '6',
    },
  },
};

class SBDialog extends Component {
  constructor(props) {
    super(props);
    this.confirmBtnRef = createRef();
  }

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : descriptionConveter(checkLanguage(localeData, label));
  };

  setFooterContent = type => {
    const { okButtonTitle, cancelButtonTitle, isNotMultiLang } = this.props;
    const footerButtons = [];
    footerButtons.push(
      <SBButton
        ref={this.confirmBtnRef}
        title={okButtonTitle || 'yes'}
        isNotMultiLang={isNotMultiLang}
        key="confirm"
        type="submit"
        name="search"
        color="green"
        onClick={() => this.onOk()}
      />,
    );
    if (type === 'confirmation') {
      footerButtons.push(
        <SBButton
          title={cancelButtonTitle || 'no'}
          key="cancel"
          type="submit"
          isNotMultiLang={isNotMultiLang}
          name="search"
          onClick={() => this.onCancel()}
        />,
      );
    }
    return footerButtons;
  };

  onCancel = () => {
    const { onCancel } = this.props;
    if (onCancel) {
      onCancel();
    }
  };

  onOk = () => {
    const { onOk } = this.props;
    if (onOk) {
      onOk();
    }
  };

  image = type => {
    let src;
    let alt;
    switch (type) {
      case 'success':
        src = 'https://stara.bizconcier-dm.com/wide_img/renew_icon_resposn_comp.png';
        alt = 'info';
        break;
      case 'info':
        src = infoIcon;
        alt = 'info';
        break;
      case 'warning':
        src = 'https://stara.bizconcier-dm.com/wide_img/mdm_del.png';
        alt = 'error';
        break;
      case 'error':
        src = 'https://stara.bizconcier-dm.com/wide_img/renew_icon_policy_violation.png';
        alt = 'warning';
        break;
      default:
        return '';
    }
    return <img src={src} alt={alt} className="icon mr10" height="25" width="25" />;
  };

  setWidth = width => {
    const style = styles.dialog;
    style.content.width = width;
    return style;
  };

  OnAfterOpenCallback = () => {
    if (this.confirmBtnRef.current) this.confirmBtnRef.current.focus();
  };

  render() {
    const { footer, open, header, content, type, width } = this.props;
    return (
      <div>
        <Modal
          isOpen={open}
          style={width ? this.setWidth(width) : styles.dialog}
          ariaHideApp={false}
          onAfterOpen={() => this.OnAfterOpenCallback()}
        >
          <div className="sb-modal-header">
            <b className="sb-modal-title">{this.languageLabelCheck(header)}</b>
          </div>
          <div className="sb-modal-body">
            {type && <div>{this.image(type)}</div>}
            <div>{this.languageLabelCheck(content)}</div>
          </div>
          <div className="sb-modal-footer">{type ? this.setFooterContent(type) : footer}</div>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(SBDialog);
