import React, { createRef, PureComponent } from 'react';

class SBPagination extends PureComponent {
    constructor(props){
        super(props);
        this.state ={
            page: props.defaultPage || 1,
             itemsToBeDisplayed: props.displayCount || 25,
            totalCount:props.totalCount
        }
        this.pagesList = [];
        this.listRefs = {};
    }


     


      onPaginationKeyPress = (event, updatePage) => {
        const {totalCount, itemsToBeDisplayed } = this.state;
        const pageSize = Math.ceil(totalCount / itemsToBeDisplayed);
        if (event.keyCode === 13 && updatePage <= pageSize) {
          this.handlePaginationClick(updatePage);
        }
      };


      handlePaginationClick = (current,position) => {
        const { handlePaginationClick ,displayCount,totalCount} = this.props;
        const {itemsToBeDisplayed} = this.state;
        let pageIndex = 0;
        if (handlePaginationClick) {
          const pageSize = Math.ceil(totalCount / displayCount);
           if(pageSize > 1){
           pageIndex = current > this.pagesList.length ? this.pagesList.length : current;
          handlePaginationClick(this.pagesList[pageIndex - 1][0],this.pagesList[pageIndex - 1][1],pageIndex !== 0 ? pageIndex : current,
            position);
           }else{
            handlePaginationClick(0,totalCount,1,position);
           }
          }
        this.setState({page: pageIndex !== 0 ? pageIndex : current});
        if(itemsToBeDisplayed !== displayCount){
          this.setState({itemsToBeDisplayed:displayCount});
        }
        if(this.listRefs[position]){
          this.listRefs[position][pageIndex - 1].current.focus();
        }
      };

      
      focus = (focus = 0,pos) => {
       this.listRefs[pos][focus].current.focus();
      }

      toggleDisplay = () => {
        const { displayCount } = this.props;
        const {itemsToBeDisplayed,page} = this.state;
        if(parseInt(itemsToBeDisplayed,10) !== parseInt(displayCount,10) ){
          this.handlePaginationClick(page);
        }
      }

      renderpagination(pageSize,position) {
        const { page } = this.state;
        const{ displayCount} =this.props;
        let tempButtonCount = 0;
        let tempStartPage = 0;
        this.pagesList = Array.from(Array(pageSize)).map((x,i) =>   [(displayCount*i),(displayCount * (i+1))]  );
        tempStartPage = page > 3 && pageSize > 5 ? page - 2 : 1;
        tempStartPage = tempStartPage > pageSize - 4 && pageSize >= 5 ? pageSize - 4 : tempStartPage;
        tempButtonCount = pageSize > 5 ? 5 : pageSize;
        this.listRefs[position] = Array.from(Array(tempButtonCount)).map(() =>  createRef() );
        return (
          <div className="floatright">
            <ul className={`pagination floatright ${  position}`}>
              {this.renderArrow(page, 1, 1, '«')}
              {this.renderArrow(page, 1, page - 1, '‹')}
              {[...Array(tempButtonCount)].map((e, i) => (
                <li
                  tabIndex={0}
                  ref={this.listRefs[position][i]}
                  key={(i + tempStartPage).toString()}
                  role="presentation"
                  onClick={() => this.handlePaginationClick(i + tempStartPage,position)}
                  onKeyDown={(event) => this.onPaginationKeyPress(event, i + tempStartPage)}
                >
                  {page === i + tempStartPage ? (
                    <span>{i + tempStartPage}</span>
                  ) : (
                    <div>{i + tempStartPage}</div>
                  )}
                </li>
               
              ))}
              {this.renderArrow(page, pageSize, page + 1, '›')}
              {this.renderArrow(page, pageSize, pageSize, '»')}
            </ul>
          </div>
        );
      }

      renderArrow(currentPage, comparePage, updatePage, icon) {
        return (
          <li
            tabIndex={currentPage === comparePage ? '' : '0'}
            className="arrow"
            role="presentation"
            style={{ pointerEvents: currentPage === comparePage ? 'none' : 'all' }}
            onClick={() => this.handlePaginationClick(updatePage)}
            onKeyDown={(event) => this.onPaginationKeyPress(event, updatePage)}
          >
            {currentPage === comparePage ? <span>{icon}</span> : <div>{icon}</div>}
          </li>
        );
      }

    render() {
        const { totalCount,displayCount,bottomPagination ,children} = this.props;
        const pageSize = Math.ceil(totalCount / displayCount);
       const pagination =  pageSize > 1 && this.renderpagination(pageSize,'top');
        return (
          <div>
            <div className="clearfix mb10" style={{ height: 30 }}>
              {pagination}
            </div>
            {children }
            {bottomPagination && pageSize > 1 && this.renderpagination(pageSize,'bottom')}
            {this.toggleDisplay()}
          </div>
        )
    }

}

export default SBPagination;