import { FETCH_USER_ENTITY, FETCH_GROUP_FILTER } from './ActionTypes';

/**
 * getUserEntity is used fetch the logged in user data.
 * @param data
 */

export default function getUserEntity(data) {
  return {
    type: FETCH_USER_ENTITY,
    data,
  };
}

export function addFilterData(data) {
  return {
    type: FETCH_GROUP_FILTER,
    data,
  };
}
