import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../../../common/component/tableLayout/SBTableLayout';
import SBRadioButton from '../../../../common/component/sbradio/SBRadio';
import SBComboBox from '../../../../common/component/combobox/SBComboBox';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBDatePicker from '../../../../common/component/sbdatepicker/SBDatePicker';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

class ChangeCertificateSetting extends Component {
  basicSettingsTableData = [
    {
      key: ['Settings Name', <span className="red bold">*</span>],
      value: 'Test',
    },
  ];

  certificateDeploymentTableData = [
    {
      key: 'CA Certificate',
      value: 'Off',
    },
    {
      key: 'User Certificate',
      value: 'Off',
    },
    {
      key: 'Device Selection',
      value: 'Device Selection',
      rowspan: '2',
    },
    {
      value: [
        <SBButton
          type="button"
          name="selectdevicebutton"
          title="Select"
          onClick={() => {
            this.onSelectDeviceButtonClick();
          }}
        />,
        'Existing Selected 0 devices.',
      ],
    },
  ];

  scheduleSettingTableData = [
    {
      key: 'Application Schedule',
      value: (
        <SBRadioButton
          name="selectedRadioButton"
          options={OPERATIONMANAGEMENTCONSTANTS.applicationSchedukleOptions}
          filterValue="value"
          filterKey="key"
          defaultValue="immediate"
          onChange={(event, selectedOption) => {
            this.onRadioButtonChange(event, selectedOption);
          }}
        />
      ),
      rowspan: '2',
    },
    {
      value: (
        <div>
          <SBDatePicker id="application-schedule-date-picker" />
          <SBComboBox
            name="scheduletime"
            id="applicaiton-schedule-time"
            options={OPERATIONMANAGEMENTCONSTANTS.timeOptions}
            filterKey="value"
            filterValue="option"
            className="ml10 mr10"
          />
          <SBButton
            type="button"
            name="settingButton"
            title="Setting"
            onClick={() => {
              this.onSettingButtonClick();
            }}
          />
        </div>
      ),
    },
  ];

  otherSettingTableData = [
    {
      key: 'Memo',
      value: (
        <textarea
          name="cert_memo"
          cols="32"
          rows="14"
          style={{ 'ime-mode': 'active', resize: 'vertical' }}
        />
      ),
    },
  ];

  constructor(props) {
    super(props);
    this.state = {
      scheduleSetting: false,
    };
  }

  onRadioButtonChange(event, selectedOption) {
    this.setState({
      scheduleSetting: selectedOption !== 'immediate',
    });
  }

  onOkButtonClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.CHANGECERTIFICATESETTINGCHECK);
  };

  onCancelButtonClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.ANDROIDCERTIFICATELIST);
  };

  onSelectDeviceButtonClick = () => {};

  render() {
    const { screenTitleData } = this.props;
    const { scheduleSetting } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <div style={{ height: '91px' }}>
                <SBContentHeader
                  title={screenTitleData.title}
                  description={screenTitleData.description}
                />
                <div className="contents-main">
                  <h2 className="title-corner">Basic Setting</h2>
                  <SBTableLayout
                    tableData={this.basicSettingsTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <h2 className="title-corner">Certification Deployment Settings</h2>
                  <SBTableLayout
                    tableData={this.certificateDeploymentTableData}
                    filterKey="key"
                    filterValue="value"
                    rowspan="rowspan"
                  />
                  <h2 className="title-corner">Schedule Setting</h2>
                  <SBTableLayout
                    tableData={
                      scheduleSetting
                        ? this.scheduleSettingTableData
                        : this.scheduleSettingTableData.slice(0, 1)
                    }
                    filterKey="key"
                    filterValue="value"
                    rowspan="rowspan"
                  />
                  <h2 className="title-corner">Other Setting</h2>
                  <SBTableLayout
                    tableData={this.otherSettingTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <div className="floatright">
                    <SBButton
                      type="submit"
                      name="okbutton"
                      color="green"
                      title="OK"
                      onClick={() => {
                        this.onOkButtonClick();
                      }}
                    />
                    <SBButton
                      type="button"
                      name="cancel"
                      title="Cancel"
                      noMarginRight
                      onClick={() => {
                        this.onCancelButtonClick();
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(ChangeCertificateSetting);
