import React from 'react';
import { connect } from 'react-redux';
import { Route, Switch, Redirect } from 'react-router-dom';
import LoginContainer from './authentication/container/LoginContainer';
import ISOCLoginContainer from './authentication/container/ISOCLoginContainer';
import ForgotPasswordContainer from './authentication/container/ForgotPasswordContainer';
import ChangePasswordContainer from './authentication/container/ChangePasswordContainer';
import ROUTEENDPOINTS from './constants/RouteEndPoints';
import '../css/skeleton.css';
import '../css/normalize.css';
import '../css/Style.css';
import CommonSBDialog from './common/component/sbdialog/CommonSBDialog';

// V1
import RouteEndPointsV1 from './v1/common/constants/RouteEndPointsV1';
import DeviceWrapperV1 from './v1/common/routes/DeviceWrapperV1';

import SBError from './common/component/sberror/SBError';

// CommonWrapper
import CommonWrapper from './common/CommonWrapper/CommonWrapper';

const App = props => {
  const { open, onOk, onOkEvent, onCancel, onCancelEvent, data } = props;
  return (
    <div style={{ height: 'inherit', overflow: 'hidden' }}>
      <Switch>
        <Route strict exact path="/" component={LoginContainer} />
        <Route strict exact path={ROUTEENDPOINTS.ISOC} component={ISOCLoginContainer} />
        <Route strict exact path={ROUTEENDPOINTS.FORGOT} component={ForgotPasswordContainer} />
        <Route
          strict
          exact
          path={ROUTEENDPOINTS.CHANGEPASSWORD}
          component={ChangePasswordContainer}
        />
        <Route path={ROUTEENDPOINTS.HOME} render={() => <CommonWrapper screenKey="home" />} />

        <Route
          path={ROUTEENDPOINTS.IDMANAGMENT}
          render={() => <CommonWrapper userType="admin" screenKey="idManage" />}
        />
        <Route
          path={ROUTEENDPOINTS.APPMANAGEMENT}
          render={() => <CommonWrapper userType="admin" screenKey="appManage" />}
        />
        <Route
          path={ROUTEENDPOINTS.SYSMANAGEMENT}
          render={() => <CommonWrapper userType="admin" screenKey="systemManage" />}
        />

        <Route
          path={ROUTEENDPOINTS.DEVICE}
          render={() => <CommonWrapper userType="customer" screenKey="device" />}
        />
        <Route
          path={ROUTEENDPOINTS.GROUPSETTING}
          render={() => <CommonWrapper userType="customer" screenKey="groupSetting" />}
        />
        <Route
          path={ROUTEENDPOINTS.OPMANAGEMENT}
          render={() => <CommonWrapper userType="customer" screenKey="opManagement" />}
        />
        <Route path={ROUTEENDPOINTS.SETTING} render={() => <CommonWrapper screenKey="setting" />} />

        <Route path={RouteEndPointsV1.DEVICE} component={DeviceWrapperV1} />
        <Route strict exact path={ROUTEENDPOINTS.ERROR} component={SBError} />
        <Route
          strict
          exact
          path={ROUTEENDPOINTS.PAGENOTFOUND}
          render={() => <SBError messageContent="page_not_found" />}
        />
        <Route
          strict
          exact
          path={ROUTEENDPOINTS.BLOCKED}
          render={() => <SBError messageContent="page_blocked" />}
        />
        <Route>
          <Redirect to={ROUTEENDPOINTS.PAGENOTFOUND} />
        </Route>
      </Switch>
      <CommonSBDialog
        open={open}
        onOk={onOk}
        onCancel={onCancel}
        onOkEvent={onOkEvent}
        onCancelEvent={onCancelEvent}
        data={data}
      />
    </div>
  );
};
const mapStateToProps = state => {
  return {
    open: state.screenModified.open,
    onOk: state.screenModified.onOk,
    onOkEvent: state.screenModified.onOkEvent,
    onCancel: state.screenModified.onCancel,
    onCancelEvent: state.screenModified.onCancelEvent,
    data: state.screenModified.data,
  };
};

export default connect(mapStateToProps)(App);
