/** Base Files */
import React, { Component, createRef } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import cloneDeep from 'lodash/cloneDeep';
import { isEmpty } from 'lodash';
import PropTypes from 'prop-types';

/** SBComponents */
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { post } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import deviceHeader from '../constants/deviceHeaderConstants';
import { getUserInformation } from '../../actions/DeviceAction';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import { serviceMapper } from '../common/deviceCommonFunctions';
import commonChangeAction from '../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

/**
 * UserDeviceSelect is to select device
 * for the target User
 * @author Srinivasan Ravi
 */
class UserDeviceSelect extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, userDeviceMap, location: getData, userVo, history } = props;
    const { mode } = getData.state || { mode: -1 };
    const { groupList } = userVo;
    if (groupList.length || !getData.state) {
      history.push(serviceMapper('error'));
    }
    this.state = {
      screenKey,
      mode,
      disable: isEmpty(cloneDeep(userDeviceMap)),
      pageLoading: true,
      gridLoading: false,
      deviceList: [],
      deviceSearch: '',
      message: '',
      openErrorDialog: false,
      selectedRowValue: cloneDeep(userDeviceMap),
      headerData: deviceHeader(screenKey),
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { mode } = this.state;
    this.setState({ gridLoading: true }, () =>
      mode === -1 ? this.denyAccess() : this.getGridData(),
    );
  }

  /**
   * denyAccess used to show message when mode not exists.
   */
  denyAccess = () =>
    this.setState({
      gridLoading: false,
      pageLoading: false,
      openErrorDialog: true,
      message: 'data_does_not_exists',
    });

  /**
   * getGridData is to get dataList to be loaded in grid.
   */
  getGridData = async (value) => {
    const { deviceSearch } = this.state;
    const deviceId = value || deviceSearch || '';
    const response = await post(APIENDPOINTS.GETUSERDEVICELIST, { deviceId });
    const result = await response.json();
    const { status, data: deviceDataList, message } = result;
    if (status === 'OK') {
      const { deviceList } = this.setGridDataIndex(deviceDataList);
      this.setState({
        deviceList,
        countInfoContents: deviceList.length,
        pageLoading: false,
        gridLoading: false,
        openErrorDialog: false,
      });
    } else {
      this.setState({
        gridLoading: false,
        pageLoading: false,
        openErrorDialog: true,
        message,
      });
    }
    if (!value && this.searchRef.current) this.searchRef.current.focus();
  };

  /**
   * setGridDataIndex is to set index key of grid data list & set griDataList to redux
   * @param gridList
   * @param dataMapper
   * @return dataList
   */
  setGridDataIndex = (gridList = []) => {
    const deviceList = gridList.map((_record, index) => {
      const record = { ..._record };
      record.id = index;
      return record;
    });
    return { deviceList };
  };

  /**
   * setSelectedValue used to return selected group
   * @param deviceList
   * @param selectedRowValue
   */
  setSelectedValue = (deviceList = [], selectedRowValue = {}) => {
    return deviceList
      .filter((data) => data.deviceId === selectedRowValue.deviceId)
      .map((record) => record.id);
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param value
   */
  onKeyDown = (event, deviceSearch) => {
    if (event.keyCode === 13) {
      this.setState({ gridLoading: true, deviceSearch }, () => this.getGridData());
    }
  };

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param event
   * @param value
   */
  handleSearchAction = (key, value) => {
    const searchActionMapper = {
      searchButton: () =>
        this.setState({ gridLoading: true }, () => this.getGridData(value.searchKeyword)),
      searchKeyword: () => this.onSearchText(value[key]),
    };
    return searchActionMapper[key] && searchActionMapper[key]();
  };

  /**
   * onSearchText is to update the search condition text value
   * @param deviceSearch
   */
  onSearchText = (deviceSearch) => this.setState({ deviceSearch });

  /**
   * handleRadioChange performs radio button change operation in grid
   * @param selectedRowValue
   */
  handleRadioChange = (selectedRowValue) => {
    this.setState({ selectedRowValue, disable: false });
  };

  /**
   * handleSelectChange performs user grid rendering
   */
  handleSelectChange = () => {
    this.setState({ gridLoading: true }, () => this.getGridData());
  };

  /**
   * onHandleClick performs CRUD page rendering
   * @param screenKey
   * @param screenFunctionMapper
   */
  onHandleClick = (screenKey) =>
    ({
      cancel: () => this.getBackToPreviousScreen(),
      userDeviceOk: () => this.getBackToPreviousScreen('true'),
    }[screenKey]());

  /**
   * getBackToPreviousScreen used to navigate previous screen.
   * @param screenName
   * @param isConfirm
   * @param packageName
   */
  getBackToPreviousScreen = (isOK) => {
    const { selectedRowValue } = this.state;
    const {
      history,
      location: getData,
      getUserInformationData: makeuserInformation,
      setModified,
    } = this.props;
    if (getData.state) {
      const { dataSource, isModified } = getData.state;
      const makeSourceData = { ...dataSource, userDeviceMap: selectedRowValue };
      if (isOK) makeuserInformation(makeSourceData);
      setModified(isOK || isModified);
      history.push(ROUTEENDPOINTS.ADDUSER, { ...getData.state });
    } else {
      history.push(ROUTEENDPOINTS.USERLIST);
    }
  };

  /**
   * render performs group management selection screen rendering
   * @return UserDeviceSelect
   */
  render() {
    const {
      screenKey,
      disable,
      headerData,
      pageLoading,
      gridLoading,
      deviceList,
      selectedRowValue,
      countInfoContents,
      message,
      openErrorDialog,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div>
          <DeviceSearch
            screenKey={screenKey}
            reference={this.searchRef}
            onKeyDown={(event, value) => this.onKeyDown(event, value)}
            onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
          />
          <CircularSpinner
            loading={gridLoading}
            css={{ position: 'absolute', top: '60%', left: 'calc(60% - 55px)' }}
          >
            <DeviceGrid
              pagination
              rowKey="id"
              screenKey={screenKey}
              gridList={deviceList}
              countInfo="select_result_count"
              filterMessage="display_items_colon"
              defaultSelected="userDeviceList"
              countInfoContents={countInfoContents}
              handleRadioChange={(event) => this.handleRadioChange(event)}
              handleSelectChange={() => this.handleSelectChange()}
              defaultSelectedKeys={this.setSelectedValue(deviceList, selectedRowValue)}
            />
            <DeviceFooter
              footer={{
                screenKey,
                disable,
              }}
              onHandleClick={(key) => this.onHandleClick(key)}
            />
          </CircularSpinner>
        </div>
        <SBDialog
          type="error"
          width="400px"
          header="error"
          okButtonTitle="ok"
          content={message}
          open={openErrorDialog}
          onOk={() => this.setState({ openErrorDialog: false })}
        />
      </CircularSpinner>
    );
  }
}
/**
 * mapStateToProps performs data fetch from device/group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { userInfoInitialState } = state.device;
  const { userVo } = state.user;
  const { userDeviceMap } = userInfoInitialState;
  return { userDeviceMap, userVo };
};

/**
 * mapDispatchToProps performs data setup in device/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (data) => dispatch(commonChangeAction(data)),
    getUserInformationData: (data) => {
      dispatch(getUserInformation(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
UserDeviceSelect.propTypes = {
  setModified: commonPropTypes().setModified.isRequired,
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      groupId: PropTypes.number,
      deviceId: PropTypes.string,
      currentPath: PropTypes.string,
      parentScreen: PropTypes.string,
    }),
    pathname: PropTypes.string,
  }).isRequired,
  getUserInformationData: PropTypes.func.isRequired,
  userDeviceMap: PropTypes.objectOf(PropTypes.string).isRequired,
  userVo: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.number]),
  ).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(UserDeviceSelect));
