import React from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';

class SBTransfer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      whiteList: props.dataList.whiteList,
      blackList: props.dataList.blackList,
      selectList: [{ selectedAppList: [] }, { selectedNegativeList: [] }],
    };
  }

  componentDidUpdate() {
    const { selectList } = this.state;
    window.$('#applist').val(selectList.selectedAppList);
    window.$('#negativeList').val(selectList.selectedNegativeList);
  }

  languageLabelCheck = (label) =>{
    const { isNotMultiLang, localeData } = this.props;
    return (isNotMultiLang) ? label : checkLanguage(localeData, label);
  }

  updateState = (whiteList, blackList) => {
    this.setState({ whiteList, blackList });
  };

  parseData = (selectedList, newRecords, oldRecords, elementID) => {
    let count = 0;
    Object.keys(selectedList).forEach(data => {
      const selectedData = {
        title: selectedList[data].title,
        value: selectedList[data].value,
        name: selectedList[data].text,
      };
      newRecords.push(selectedData);
      oldRecords.splice(Number(selectedList[data].index) - count, 1);
      count += 1;
    });
    this.sortByName(oldRecords);
    this.sortByName(newRecords);
    const selectList = this.getPreviousSelectedList();
    this.setState({ selectList });
    if (elementID === 'applist') {
      this.updateState(oldRecords, newRecords);
    } else {
      this.updateState(newRecords, oldRecords);
    }
  };

  getPreviousSelectedList = () => {
    const selectedAppList = this.generateList(document.getElementById('applist').selectedOptions);
    const selectedNegativeList = this.generateList(
      document.getElementById('negativeList').selectedOptions,
    );
    const selectList = {
      selectedAppList,
      selectedNegativeList,
    };
    return selectList;
  };

  generateList = selectedComboList => {
    const selectList = [];
    Object.keys(selectedComboList).forEach(data => {
      selectList.push(selectedComboList[data].value);
    });
    return selectList;
  };

  sortByName = records => {
    return records.sort(function(x, y) {
      if (x.name < y.name) {
        return -1;
      }
      if (x.name > y.name) {
        return 1;
      }
      return 0;
    });
  };

  addClick = element => {
    const { whiteList, blackList } = this.state;
    let selectedList = [];
    if (element.target.id === 'add') {
      selectedList = document.getElementById('applist').selectedOptions;
    } else {
      selectedList = document.getElementById('applist').options;
    }
    this.parseData(selectedList, blackList, whiteList, 'applist');
  };

  removeClick = element => {
    const { whiteList, blackList } = this.state;
    let selectedList = [];
    if (element.target.id === 'remove') {
      selectedList = document.getElementById('negativeList').selectedOptions;
    } else {
      selectedList = document.getElementById('negativeList').options;
    }

    this.parseData(selectedList, whiteList, blackList, 'negativeList');
  };

  render() {
    const { whiteList, blackList } = this.state;
    const { disabled } = this.props;
    return (
      <table className="table-yoko">
        <tbody>
          <tr>
            <td style={{ width: '46%' }}>
              <select
                id="applist"
                name="applist"
                size="18"
                style={{ width: '100%' }}
                multiple="multiple"
              >
                {whiteList &&
                  whiteList.map(data => (
                    <option title={data.title} value={data.value}>
                      {this.languageLabelCheck(data.name)}
                    </option>
                  ))}
              </select>
            </td>
            <td style={{ width: '8%' }}>
              <input
                type="button"
                id="add"
                name="add"
                value=">"
                className="mb10"
                onClick={this.addClick}
                disabled={disabled}
              />
              <input
                type="button"
                id="remove"
                name="remove"
                value="<"
                className="mb10"
                onClick={this.removeClick}
                disabled={disabled}
              />
              <input
                type="button"
                id="addall"
                name="addall"
                value=">>"
                className="mb10"
                onClick={this.addClick}
                disabled={disabled}
              />
              <input
                type="button"
                id="removeall"
                name="removeall"
                value="<<"
                className="mb10"
                onClick={this.removeClick}
                disabled={disabled}
              />
            </td>

            <td style={{ width: '46%' }}>
              <select
                id="negativeList"
                name="negativeList"
                size="18"
                style={{ width: '100%' }}
                multiple="multiple"
              >
                {blackList &&
                  blackList.map(data => (
                    <option title={data.title} value={data.value}>
                      {this.languageLabelCheck(data.name)}
                    </option>
                  ))}
              </select>
            </td>
          </tr>
        </tbody>
      </table>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps, null)(SBTransfer);
