/**
 * NoticeListGrid is used to generate table component and render columns for list screen
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import SBTable from '../../../../common/component/sbtable/SBTable';
import NoResultsFound from '../../../../common/component/grid/NoResultsFound';
import checkLanguage from '../../../../language/checkLanguage';
import { differenceBetweenTwoDates } from '../../../../common/utils/dateUtils';
import { CommonConstants } from '../../../../constants/CommonConstants';
import commonPropTypes from '../../../../common/commonPropTypes/commonPropTypes';

class NoticeListGrid extends Component {
  /**
   * getColumns is used to generate object of columns used in Notice List Table
   * @return columns
   */
  getColumns = () => {
    return [
      {
        title: '',
        dataIndex: 'render-radio',
        styles: { minWidth: 20, width: 20 },
        textAlign: 'center',
      },
      {
        title: 'title',
        dataIndex: 'notificationTitle',
        rowKey: 'notificationTitle',
        styles: { width: 340 },
        sorter: false,
      },
      {
        title: 'date_publication_email',
        dataIndex: 'dateOfPublicationOrEmailTrans',
        rowKey: 'dateOfPublicationOrEmailTrans',
        styles: { minWidth: 130 },
        sorter: false,
        render: (data) =>
          data.dateOfPublicationOrEmailTrans
            ? data.dateOfPublicationOrEmailTrans
            : this.languageLabelCheck('unpublished'),
      },
      {
        title: 'notice_grid_send_email',
        dataIndex: 'sendEmail',
        rowKey: 'sendEmail',
        textAlign: 'center',
        sorter: false,
        render: (data) => this.languageLabelCheck(data.sendMail ? 'send_mail' : 'dont_send_mail'),
      },
      {
        title: 'publication_period',
        dataIndex: 'publicationPeriod',
        rowKey: 'publicationPeriod',
        textAlign: 'center',
        sorter: false,
        render: (data) => {
          return this.getPublicationPeriodValue(data);
        },
      },
      {
        title: 'language',
        dataIndex: 'languageName',
        rowKey: 'languageName',
        textAlign: 'center',
        sorter: false,
      },
      {
        title: 'updated_date',
        dataIndex: 'updatedDateValue',
        rowKey: 'updatedDateValue',
        sorter: false,
        styles: { minWidth: 130 },
      },
    ];
  };

  /**
   * getPublicationPeriodValue is used to build label of publication period
   * @param data
   * @return publicationPeriodValue
   */
  getPublicationPeriodValue = (data) => {
    if (data.dateOfPublicationOrEmailTrans) {
      return data.publicationPeriod
        ? `${differenceBetweenTwoDates(
            data.dateOfPublication ? new Date(data.dateOfPublication) : new Date(),
            new Date(data.publicationPeriod),
          )} ${this.languageLabelCheck('day_s')}`
        : this.languageLabelCheck('no_expiration_date');
    }
    return '';
  };

  /**
   * languageLabelCheck is used to get label of given code in user locale
   * @param label
   * @return labelContent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the component
   */
  render() {
    const {
      noticeList,
      handleRadioChange,
      handlePaginationClick,
      handleSelectChange,
      defaultSelected,
    } = this.props;
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          {noticeList.length ? (
            <SBTable
              columns={this.getColumns()}
              dataSource={noticeList}
              loading
              pagination
              defaultSelected={defaultSelected}
              filterList={CommonConstants.gridListComboOptions.slice(0, 3)}
              defaultSelectedKeys={[0]}
              countInfoContents={noticeList.length}
              countInfo="search_result_with_count"
              handleRadioChange={(event) => handleRadioChange(event)}
              handlePaginationClick={(tableData) => handlePaginationClick(tableData)}
              handleSelectChange={(tableData) => handleSelectChange(tableData)}
            />
          ) : (
            <NoResultsFound />
          )}
        </div>
      </div>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
NoticeListGrid.propTypes = {
  noticeList: PropTypes.arrayOf(
    PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.array, PropTypes.bool]),
  ).isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
  handleRadioChange: PropTypes.func,
  handlePaginationClick: PropTypes.func,
  handleSelectChange: PropTypes.func,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NoticeListGrid.defaultProps = {
  handleRadioChange: () => {},
  handlePaginationClick: () => {},
  handleSelectChange: () => {},
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(NoticeListGrid);
