/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import cloneDeep from 'lodash/cloneDeep';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBButton from '../../common/component/sbbutton/SBButton';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../common/component/tableheader/SBTableHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** Device Components */
import DeviceGrid from '../component/DeviceGrid';
import DeviceSearch from '../component/DeviceSearch';
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import { Role } from '../../constants/Role';
import { tableView, statusMap } from '../constants/deviceTableConstants';
import { validateTableData } from '../../groupSetting/common/GroupSettingCommonFunctions';
import { searchComponentList, searchFilterCheckBoxList } from '../constants/deviceConstants';
import {
  getDeviceDetail /** , setDataSource */,
  serviceMapper,
} from '../common/deviceCommonFunctions';
import { getUserInformation } from '../../actions/DeviceAction';
import { post } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import checkLanguage from '../../language/checkLanguage';
import SBRadio from '../../common/component/sbradio/SBRadio';
import RoleResolver from '../../common/component/guard/RoleResolver';
import LABELCONSTANTS from '../../constants/LabelConstants';
import descriptionConverter from '../../common/utils/descriptionConverter';
import { buildWifiSettingViewPanelMapper } from '../../groupSetting/constants/groupSettingPolicyConfirmConstants';

/**
 * DeviceDetailTab is to display selected device information based on selected tab
 * @author Aswin Palanisamy
 */
class DeviceDetailTab extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { tabKey, defaultPolicyData } = props;
    this.state = {
      screenKey: tabKey,
      pageLoading: false,
      gridLoading: false,
      openErrorDialog: false,
      search: '',
      enumList: [],
      footer: {
        disable: tabKey === 'operations',
        screenKey: tabKey,
        authorityRoleType: Role.deviceAuthority,
      },
      dataSource: tabKey === 'restriction' ? cloneDeep(defaultPolicyData) : {},
      currentIndex: 0,
      operationId: '',
      openWipeDialog: false,
      wipeFlag: 'no',
      factoryResetDisable: false,
      button: false,
      scheduleProgrss: '',
    };
    this.searchRef = createRef();
  }

  /**
   * componentDidMount perform initial device data fetch operation
   */
  componentDidMount() {
    const { location } = this.props;
    if (location.state) {
      this.getDeviceInformation(false);
    }
  }

  /**
   * componentDidUpdate is to set focus on the app search field
   */
  componentDidUpdate() {
    if (this.searchRef.current) this.searchRef.current.focus();
  }

  /**
   * getDeviceInformation is to fetch selected device information
   */
  getDeviceInformation = async (button) => {
    const { tabKey, reference } = this.props;
    this.setState({ pageLoading: true }, async () => {
      const deviceReport = await getDeviceDetail(this.props, this.state, button);
      this.setState({ ...deviceReport, button });
      if (tabKey === 'device' && reference.refreshRef.current) reference.refreshRef.current.focus();
    });
  };

  /**
   * getDeviceScheduleInformation is to fetch selected device schedule information
   */
  getDeviceScheduleInformation = async (screenKey) => {
    const { operationId } = this.state;
    const { history, location: getData } = this.props;
    const { deviceId } = getData.state;
    this.setState({ pageLoading: true }, async () => {
      const response = await post(APIENDPOINTS.DEVICESCHEDULE, { deviceId });
      const result = await response.json();
      const { status, data: deviceList } = result;
      if (status === 'OK') {
        if (deviceList.scheduleProgress !== 'InProgress') {
          history.push(serviceMapper(screenKey), {
            deviceId,
            operationId,
            wipeFlag: 'yes',
            parentScreen: getData.pathname,
          });
        } else {
          this.setState({
            pageLoading: false,
            openErrorDialog: deviceList.scheduleProgress === 'InProgress',
            message: 'schdeuled_device_cannot_be_changed',
          });
        }
      } else {
        this.setState({
          message: result.message,
          pageLoading: false,
          openErrorDialog: true,
        });
      }
    });
  };

  /**
   * onKeyDown performs key press operation in search text
   * @param event
   * @param deviceSearch
   */
  onKeyDown = (event, search) => {
    if (event.keyCode === 13)
      this.setState({ search, gridLoading: true }, async () => {
        const deviceReport = await getDeviceDetail(this.props, this.state);
        this.setState({ ...deviceReport });
      });
  };

  /**
   * handleSearchAction performs search operation in grid
   * @param key
   * @param values
   */
  handleSearchAction = (key, values) => {
    const { searchKeyword: search, checkBoxKeys: enumList } = values;
    if (key === 'searchButton' || key === 'appSearch')
      this.setState({ search, enumList, gridLoading: true }, async () => {
        const deviceReport = await getDeviceDetail(this.props, this.state);
        this.setState({ ...deviceReport });
      });
  };

  /**
   * navigateDetailScreen is to navigate to the device action screens
   * @param screenKey
   */
  navigateDetailScreen = (screenKey) => {
    const { operationId, factoryResetDisable, dataSource } = this.state;
    const { history, location: getData, getUserInformationData } = this.props;
    const { deviceId } = getData.state;
    if (screenKey !== 'wipe' || !factoryResetDisable) {
      if (screenKey === 'changeGroup') {
        this.getDeviceScheduleInformation(screenKey);
      } else if (screenKey === 'updateUser') {
        getUserInformationData({
          employeeIndex: dataSource.employeeIndex,
        });
        history.push(serviceMapper(screenKey), {
          deviceId,
          parentScreen: getData.pathname,
        });
      } else {
        history.push(serviceMapper(screenKey), {
          deviceId,
          operationId,
          wipeFlag: 'yes',
          parentScreen: getData.pathname,
        });
      }
    } else {
      this.setState({ openWipeDialog: true });
    }
  };

  /**
   * navigateListScreen is to navigate to the deviceList screen
   */
  navigateListScreen = () => {
    const { history } = this.props;
    history.push(serviceMapper('deviceList'));
  };

  /**
   * handleRadioChange method is for getting the operationId
   * @param event
   */
  handleRadioChange = (event) => {
    this.setState({ operationId: event.operationId, footer: { disable: false } });
  };

  /**
   * renderComponent is to set icon image in the device table
   * @param record
   */
  renderComponent = (record) => (
    <span className="mr15">
      <img
        className="icon mr10"
        height="25"
        width="25"
        alt=""
        src={statusMap[parseInt(record.statusId, 10)]}
      />
      {` ${record.updatedDate}`}
    </span>
  );

  /**
   * setVersionViolation is to set policy violation icon image in the device table
   * @param record
   */
  setVersionViolation = (record) => {
    if (
      (record.versionCheckFlag && record.groupAuthority > 0) ||
      (record.allVersionCheckFlag && record.groupAuthority === 0)
    )
      return (
        <span className="mr15">
          <img className="icon mr10" height="25" width="25" alt="" src={statusMap[0]} />
          {` ${record.androidVersion}`}
        </span>
      );
    return <span className="mr15">{` ${record.androidVersion}`}</span>;
  };

  /**
   * setImeiViolation is to set policy violation icon image in the device table
   * @param record
   */
  setImeiViolation = (record) => {
    if (
      (record.imeiCheckFlag && record.groupAuthority > 0) ||
      (record.allImeiCheckFlag && record.groupAuthority === 0)
    )
      return (
        <span className="mr15">
          <img className="icon mr10" height="25" width="25" alt="" src={statusMap[0]} />
          {` ${record.imeiNumber}`}
        </span>
      );
    return <span className="mr15">{` ${record.imeiNumber}`}</span>;
  };

  /**
   * setComboData is to set comboData based on selected value
   * @param comboList
   * @param dataKey
   * @return comboDisplayData
   */
  setComboData = (comboList, dataKey) => {
    return comboList.filter((record) => record.value === dataKey)[0].option;
  };

  /**
   * setFreezePeriods used to form freeze period content in view screen.
   * @param freezePeriodList
   * @returns freezePeriodContent
   */
  setFreezePeriods = (freezePeriodList) => {
    const freezePeriodContentList = [];
    const freezeLimitContent = this.languageLabelCheck('freeze_limit');
    freezePeriodList.forEach((freezePeriod, index) => {
      freezePeriodContentList.push(`${freezeLimitContent} ${index + 1} : ${
        freezePeriod.startDate.month + 1
      } / ${freezePeriod.startDate.day} ${LABELCONSTANTS.TILDE}
       ${freezePeriod.endDate.month + 1} / ${freezePeriod.endDate.day}`);
    });
    return descriptionConverter(freezePeriodContentList.join('[br/]') || '--');
  };

  /**
   * systemUpdateSetting used to validate component for systemUpdate tab
   * @param content
   * @param appliedPolicy
   * @returns validatetableList
   */
  systemUpdateSetting = (content, appliedPolicy) => {
    const validatetableList =
      appliedPolicy.systemUpdate.type !== 'WINDOWED'
        ? content.filter(
            (contentData) =>
              !(contentData.key === 'startMinutes' || contentData.key === 'endMinutes'),
          )
        : content;
    return validatetableList;
  };

  /**
   * validateWifiSetting used to validate component for wifisetting tab
   * @param content
   * @param appliedPolicy
   * @param currentIndex
   * @returns content
   */
  validateWifiSetting = (content, appliedPolicy, currentIndex) => {
    if (appliedPolicy && appliedPolicy.wifiCSV && appliedPolicy.wifiCSV.WifiRadio === 'input') {
      const securityType =
        !isEmpty(appliedPolicy.openNetworkConfiguration.NetworkConfigurations[currentIndex]) &&
        appliedPolicy.openNetworkConfiguration.NetworkConfigurations[currentIndex].WiFi.Security ===
          'None';
      return securityType ? content.slice(0, 4) : content;
    }
    return content;
  };

  /**
   * validateTableList is to hide/show policy components
   * @param header
   * @param content
   * @param appliedPolicy
   * @param currentIndex
   * @return tableList
   */
  validateTableList = (header, content, appliedPolicy, currentIndex) => {
    const tableValidator = {
      passcode_setting: () => validateTableData(content, appliedPolicy, header),
      connectivity_settings: () =>
        appliedPolicy.shareLocationDisabled
          ? [...content.splice(0, 3), ...content.splice(1)]
          : content,
      system_update_settings: () => this.systemUpdateSetting(content, appliedPolicy),
      wifi_settings: () => this.validateWifiSetting(content, appliedPolicy, currentIndex),
    }[header];
    return tableValidator ? tableValidator() : content;
  };

  closeWipeDialog = () => {
    this.setState({ openWipeDialog: false });
    const { operationId, wipeFlag } = this.state;
    const { history, location: getData } = this.props;
    const { deviceId } = getData.state;
    history.push(serviceMapper('wipe'), {
      deviceId,
      operationId,
      wipeFlag,
      parentScreen: getData.pathname,
    });
  };

  onWipeOptionChange = (value) => {
    this.setState({ wipeFlag: value });
  };

  /**
   * languageLabelCheck is to set language label
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  /**
   * render performs device detail tab screen rendering
   * @return DeviceDetailTab
   */
  render() {
    const {
      footer,
      message,
      screenKey,
      dataSource,
      currentIndex,
      gridLoading,
      pageLoading,
      openErrorDialog,
      openWipeDialog,
      button,
    } = this.state;
    const { reference } = this.props;
    const { NetworkConfigurations: wifiList } =
      screenKey === 'restriction' && cloneDeep(dataSource).openNetworkConfiguration;
    const renderSearch = screenKey === 'app';
    const renderGrid =
      screenKey === 'deploymentPolicy' || screenKey === 'operations' || renderSearch;
    const tabMessage =
      screenKey === 'operations' ? 'operation_not_found' : 'deployed_policy_not_found';
    const tableContext = tableView(
      screenKey,
      this.renderComponent,
      this.setVersionViolation,
      this.setImeiViolation,
      this.setComboData,
      this.setFreezePeriods,
      currentIndex,
      this.languageLabelCheck,
    );
    if (isEmpty(wifiList) && screenKey === 'restriction') tableContext.splice(5, 1);
    if (
      dataSource.wifiCertificateConfiguration &&
      dataSource.wifiCertificateConfiguration.openNetworkConfiguration
    ) {
      tableContext.splice(5, 0, {
        header: 'wifi_settings_certificate',
        content: [
          ...buildWifiSettingViewPanelMapper(
            this.setComboData,
            dataSource.wifiCertificateConfiguration,
            dataSource.wifiCertificateConfiguration.wifiCertFileDetails,
            { changePolicyWifiCertDeleteList: [] },
          ).wifiSettingsCertificate,
        ],
      });
    }
    const getComponent = searchComponentList();
    const searchOptions = getComponent[`${screenKey}ListheaderButton`];
    return (
      <div style={pageLoading ? { display: 'absolute', height: '200px', width: '100%' } : {}}>
        {searchOptions && (
          <IconRender
            {...this.props}
            button={button}
            reference={reference}
            refreshBtnAttr={searchOptions}
            reloadDevice={() => this.getDeviceInformation(true)}
            images={searchFilterCheckBoxList('deviceList')}
          />
        )}
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          {tableContext &&
            tableContext.map((record) => [
              record.header && <SBTableHeader headerText={record.header} />,
              record.header === 'wifi_settings' && wifiList && (
                <SwitchTabButton
                  getList={wifiList}
                  currentIndex={currentIndex}
                  setPageEvent={(index) => this.setState({ currentIndex: index })}
                />
              ),
              <CustomTableFrame
                dataSource={dataSource}
                tableList={this.validateTableList(
                  record.header,
                  record.content,
                  dataSource,
                  currentIndex,
                )}
              />,
            ])}
          {renderSearch && (
            <DeviceSearch
              screenKey={screenKey}
              reference={this.searchRef}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
              onSearchAction={(key, event, values) => this.handleSearchAction(key, values)}
            />
          )}
          {renderGrid && (
            <CircularSpinner
              loading={gridLoading}
              css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
            >
              <DeviceGrid
                screenKey={screenKey}
                pagination={renderSearch}
                countInfo="select_result_count"
                filterMessage="display_items_colon"
                nonDataMessage={!renderSearch && tabMessage}
                gridList={Array.isArray(dataSource) ? dataSource : [dataSource]}
                countInfoContents={Array.isArray(dataSource) && dataSource.length}
                handleRadioChange={(event) => this.handleRadioChange(event)}
              />
            </CircularSpinner>
          )}
          <DeviceFooter
            onHandleClick={(key) => this.navigateDetailScreen(key)}
            footer={{ ...footer, screenKey: `${screenKey}Tab` }}
          />
          <SBDialog
            type="error"
            width="400px"
            header="error"
            okButtonTitle="ok"
            content={message}
            open={openErrorDialog}
            onOk={() => this.navigateListScreen()}
          />
          <SBDialog
            width={30}
            type="infoDialog"
            open={openWipeDialog}
            isNotMultiLang="true"
            onOk={() => this.closeWipeDialog()}
            okButtonTitle="OK"
            header={this.languageLabelCheck('device_wipe_option')}
            content={[
              <span>{this.languageLabelCheck('device_factory_reset_confirm')}</span>,
              <SBRadio
                name="wipe_option"
                options={[
                  { key: 'no', value: 'no' },
                  { key: 'yes', value: 'yes' },
                ]}
                defaultValue="no"
                filterKey="key"
                filterValue="value"
                onChange={(event, value) => this.onWipeOptionChange(value)}
              />,
            ]}
          />
        </CircularSpinner>
      </div>
    );
  }
}

/**
 * IconRender is for rendering device status icons
 * @param refreshBtnAttr
 * @param images
 * @param localeData
 * @return IconRender
 */
const IconRender = ({ refreshBtnAttr, reloadDevice, images, localeData, button, reference }) => {
  /**
   * languageLabelCheck is to set language label
   * @param label
   * @return languageLabel
   */
  const languageLabelCheck = (label) => {
    return checkLanguage(localeData, label);
  };

  return (
    <div>
      <RoleResolver authorityRoleType={Role.deviceButtonAuthority}>
        <SBButton
          {...refreshBtnAttr}
          ref={reference ? reference.refreshRef : ''}
          onClick={() => reloadDevice()}
        />
      </RoleResolver>
      <span className="red bold" style={button ? {} : { display: 'none' }}>
        {languageLabelCheck('refresh_message')}
      </span>
      <div className="clearfix mb10" style={{ 'padding-top': 10, height: 30 }}>
        <div className="floatleft">
          {images &&
            Object.keys(images).map((data) => (
              <span className="mr15">
                <img
                  className="icon"
                  height="25"
                  width="25"
                  border="0"
                  src={images[data].url}
                  alt={data}
                />
                {'： '.concat(languageLabelCheck(images[data].name))}
              </span>
            ))}
        </div>
      </div>
    </div>
  );
};

/**
 * SwitchTabButton is to set add/remove wifi panel indicators
 * @param getList
 * @param currentIndex
 * @param setPageEvent
 * @return panelIndicator
 */
const SwitchTabButton = ({ getList, currentIndex, setPageEvent }) => (
  <ul className="pagination floatright mr10">
    {getList.length !== 1 &&
      getList.length <= 10 &&
      getList.map((setter, index) => {
        return (
          <li>
            {currentIndex !== index ? (
              <a
                href
                style={{
                  color: '#7F7F7F',
                  border: '1px solid #B2B2B2',
                  background: '#fefefe',
                  textDecoration: 'none',
                  display: 'block',
                  padding: '0 8px',
                  borderRadius: 3,
                  height: 23,
                  fontSize: 13,
                  cursor: 'pointer',
                }}
                onClick={() => setPageEvent(index)}
              >
                {index + 1}
              </a>
            ) : (
              <span>{index + 1}</span>
            )}
          </li>
        );
      })}
  </ul>
);

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  const { data: localeData } = state.language;
  const { defaultPolicyData } = state.group;
  const { userVo } = state.user;
  return { localeData, defaultPolicyData, userVo };
};

/**
 * mapDispatchToProps performs data setup in device/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    getUserInformationData: (data) => {
      dispatch(getUserInformation(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceDetailTab.propTypes = {
  tabKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      deviceId: PropTypes.string,
    }),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  defaultPolicyData: PropTypes.objectOf(PropTypes.object).isRequired,
};

/**
 * propTypes is used for validation props with its type.
 */
IconRender.propTypes = {
  reloadDevice: PropTypes.func.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  refreshBtnAttr: PropTypes.shape({
    color: PropTypes.string,
    type: PropTypes.string,
    key: PropTypes.string,
    title: PropTypes.string,
    bottomBorder: PropTypes.bool,
    id: PropTypes.string,
  }).isRequired,
  images: PropTypes.arrayOf(
    PropTypes.shape({
      url: PropTypes.string,
      name: PropTypes.string,
      checkBoxKey: PropTypes.string,
    }),
  ).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DeviceDetailTab));
