import React, { Component } from 'react';

class SBInfoIcon extends Component {
  onClick = (event) => {
    const { onClick } = this.props;
    if (onClick) {
      onClick(event);
    }
  };

  handleKeyDown = (event) => {
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.preventDefault();
      this.onClick(event);
    }
  };

  render() {
    const { id, tooltip } = this.props;
    return (
      <span className="sb-tooltip" style={{ marginTop: 3, marginLeft: 3 }}>
        <div
          role="button"
          id={id}
          tabIndex={0}
          className="sb-btn-info"
          aria-label="info"
          onKeyDown={(event) => this.handleKeyDown(event)}
          onClick={(e) => this.onClick(e)}
        />
        {tooltip && <div className="sb-tooltiptext">{tooltip}</div>}
      </span>
    );
  }
}

export default SBInfoIcon;
