import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import SBLayout from '../component/sblayout/SBLayout';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import groupSettingWrapperConstant from '../../Customer/constant/groupSettingWrapperConstant';
import opManagementWrapperConstant from '../../Customer/constant/opManagementWrapperConstant';
import settingWrapperConstant from '../../Customer/constant/settingWrapperConstant';
import deviceWrapperConstant from '../../Customer/constant/deviceWrapperConstant';
import homeWrapperConstant from '../../Home/constant/homeWrapperConstant';
import idManageWrapperConstants from '../../SBAdmin/IDManagement/Constants/idManageWrapperConstants';
import appManageWrapperConstant from '../../SBAdmin/ApplicationManagement/Constants/appManageWrapperConstants';
import systemManageWrapperConstant from '../../SBAdmin/SystemManagement/Constant/systemManageWrapperConstant';

import SBError from '../component/sberror/SBError';
import SBRoute from '../component/sbroute/SBRoute';

/**
 * CommonWrapper used to navigate scereen  based on path
 * @param props
 * @return route
 */
function CommonWrapper(props) {
  const { screenKey, userType } = props;
  const getScreenConstant = (screenName) => {
    const screenConstantMaper = {
      setting: settingWrapperConstant,
      opManagement: opManagementWrapperConstant,
      groupSetting: groupSettingWrapperConstant,
      device: deviceWrapperConstant,
      home: homeWrapperConstant,
      idManage: idManageWrapperConstants,
      appManage: appManageWrapperConstant,
      systemManage: systemManageWrapperConstant,
    }[screenName];
    return screenConstantMaper;
  };
  const screenPathConstant = getScreenConstant(screenKey);
  return (
    <SBLayout userType={userType}>
      <Switch>
        {screenPathConstant.map((data) => (
          <SBRoute
            path={data.path}
            ScreenName={data.screenName}
            attribute={data.attribute ? data.attribute : ''}
          />
        ))}
        <Route
          strict
          exact
          path={ROUTEENDPOINTS.PAGENOTFOUND}
          render={() => <SBError messageContent="page_not_found" />}
        />
        <Route>
          <Redirect to={ROUTEENDPOINTS.PAGENOTFOUND} />
        </Route>
      </Switch>
    </SBLayout>
  );
}

/**
 * propTypes is used for validation props with its type.
 */
CommonWrapper.propTypes = {
  screenKey: PropTypes.string.isRequired,
  userType: PropTypes.string.isRequired,
};

export default CommonWrapper;
