import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBTable from '../../../../common/component/sbtable/SBTable';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

class DeleteCertification extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataSource: [],
    };
  }

  onSearchButtonClick = () => {};

  onSearchByDeptNameButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.DEPARTMENTDEVICESEARCH, {
      parentScreen: location.pathname,
    });
  };

  onSearchByGroupButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.REFINEGROUP, {
      parentScreen: location.pathname,
    });
  };

  onSearchByCSVButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.SEARCHBYCSV, {
      parentScreen: location.pathname,
    });
  };

  onOkButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.ANDROIDCERTIFICATELIST, {
      parentScreen: location.pathname,
    });
  };

  onCancelButtonClick = () => {
    const { history, location } = this.props;
    history.push(ROUTEENDPOINTS.ANDROIDCERTIFICATELIST, {
      parentScreen: location.pathname,
    });
  };

  getColumns = () => {
    return OPERATIONMANAGEMENTCONSTANTS.deleteCertificationGridColumns;
  };

  render() {
    const { screenTitleData } = this.props;
    const columns = this.getColumns();
    const { dataSource } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <div style={{ height: '91px' }}>
                <SBContentHeader
                  title={screenTitleData.title}
                  description={screenTitleData.description}
                />
                <div className="contents-main border p15 mb15">
                  <SBTextbox
                    type="text"
                    name="search_data"
                    maxlength="100"
                    width="142px"
                    className="mr10"
                    style={{ marginRight: '10px' }}
                  />
                  <SBButton
                    type="button"
                    name="antiVirusListSearchButton"
                    color="green"
                    title="Search"
                    onClick={() => this.onSearchButtonClick()}
                  />
                  <SBButton
                    type="button"
                    name="searchByDeptName"
                    title="Search By Dept. Name"
                    onClick={() => this.onSearchByDeptNameButtonClick()}
                  />
                  <SBButton
                    type="button"
                    name="searchByGroupName"
                    title="Search by Group Name"
                    onClick={() => this.onSearchByGroupButtonClick()}
                  />
                  <SBButton
                    type="button"
                    name="searchByCSV"
                    title="Search by CSV"
                    onClick={() => this.onSearchByCSVButtonClick()}
                  />
                </div>
                <div className="clearfix mb10">
                  {OPERATIONMANAGEMENTCONSTANTS.deleteCertificationLegendIcons}
                </div>
                <div className="contents-main mb15 w100per">
                  <SBTable
                    columns={columns}
                    dataSource={dataSource}
                    loading={false}
                    pagination
                    countInfo={`Deployment List Total：${dataSource.length}`}
                    filterList={OPERATIONMANAGEMENTCONSTANTS.securityPolicyGridComboItemsValue}
                    handleRadioChange={data => this.handleRadioChange(data)}
                  />
                  <div className="clearfix mb10">
                    <div className="floatright">
                      <SBButton
                        type="submit"
                        name="okbutton"
                        color="green"
                        title="OK"
                        onClick={() => {
                          this.onOkButtonClick();
                        }}
                      />
                      <SBButton
                        type="button"
                        name="cancel"
                        title="Cancel"
                        noMarginRight
                        onClick={() => {
                          this.onCancelButtonClick();
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(DeleteCertification);
