/**
 * MultipleAdminData is used to load the data
 * @author: P.Vinothpandian ,V.Vijayaragavan
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { CommonConstants } from '../../constants/CommonConstants';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import SBTable from '../../common/component/sbtable/SBTable';
import RoleResolver from '../../common/component/guard/RoleResolver';
import { Role } from '../../constants/Role';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';


class MultipleAdminData extends Component {

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

 
  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const { gridListComboOptions } = CommonConstants;
    const {
      userList,
      tenantVo,
      showColumns,
      countInfoContents,
      handlePaginationClick,
      handleRadioChange,
      handleSelectChange,
      defaultSelected,
    } = this.props;
    return (
      <div>
        <div style={{ marginTop: 10 }}>
          <RoleResolver
            component="sbtable"
            columns={showColumns}
            authorityRoleType={Role.multipleAdminRegisterAndChange}
          >
            <SBTable
              columns={showColumns}
              dataSource={userList}
              loading
              pagination
              defaultSelected={defaultSelected}
              filterList={gridListComboOptions}
              defaultSelectedKeys={[0]}
              countInfoContents={countInfoContents}
              countInfo="registered_count_items"
              maxcountInfo="maximum_count_items"
              maxInfoContent={tenantVo.userCount}
              handleRadioChange={event => handleRadioChange(event)}
              handlePaginationClick={tableData => handlePaginationClick(tableData)}
              handleSelectChange={(tableData, value) => handleSelectChange(value)}
            />
          </RoleResolver>
        </div>
        {userList.length ? null : (
          <div className="clearfix mb10" style={{ marginTop: 10 }}>
            {this.languageLabelCheck('no_results_found')}
          </div>
        )}
      </div>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminData.propTypes = {
  handlePaginationClick: PropTypes.func.isRequired,
  handleRadioChange: PropTypes.func.isRequired,
  handleSelectChange: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  userList: PropTypes.arrayOf(PropTypes.object),
  tenantVo: commonPropTypes().history.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  countInfoContents: PropTypes.number.isRequired,
  defaultSelected: PropTypes.number,
  showColumns : PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminData.defaultProps = {
  isNotMultiLang: false,
  userList: [],
  defaultSelected: 25,
};

export default connect(mapStateToProps)(withRouter(MultipleAdminData));
