import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import checkLanguage from '../checkLanguage';

const styles = {
  divHeader: {
    fontWeight: 'bold',
    paddingBottom: 4,
  },
  divContent: {
    padding: 4,
    listStyle: 'inside',
  },
};

/**
 * InfoDialog is display about requirements to upload language content.
 * @author Gobi R
 */
class InfoDialog extends Component {
  /**
   * onOkClick is to close infoDialog.
   */
  onOkClick = () => {
    const { closeDialog } = this.props;
    closeDialog();
  };

  /**
   * languageLabelCheck is to set labels based on current user's language.
   * @param label
   * @return changed label.
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * content is to show dialog content.
   */
  content = () => {
    return (
      <div>
        <div style={styles.divHeader}>
          {this.languageLabelCheck('successfully_import_csv_rules')}
        </div>
        <ul style={{ listStyleType: 'disc' }}>
          <li style={styles.divContent}>{this.languageLabelCheck('code_label_value')}</li>
          <li style={styles.divContent}>{this.languageLabelCheck('empty_fields_not_allowed')}</li>
          <li style={styles.divContent}>{this.languageLabelCheck('code_field_2_lowercase')}</li>
          <li style={styles.divContent}>{this.languageLabelCheck('label_lower_numb_alpha')}</li>
        </ul>
      </div>
    );
  };

  /**
   * render is to load sub components.
   * @returns jsx elements
   */
  render() {
    const { open } = this.props;
    return (
      <div>
        <SBDialog
          open={open}
          header={this.languageLabelCheck('information')}
          content={this.content()}
          isNotMultiLang
          width="520px"
          type="infoDialog"
          okButtonTitle={this.languageLabelCheck('ok')}
          onOk={() => this.onOkClick()}
        />
      </div>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
InfoDialog.propTypes = {
  localeData: PropTypes.objectOf(PropTypes.string),
  isNotMultiLang: PropTypes.bool,
  open: PropTypes.bool.isRequired,
  closeDialog: PropTypes.func.isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
InfoDialog.defaultProps = {
  localeData: {},
  isNotMultiLang: false,
};

/**
 * mapStateToProps is to map store fields to props.
 * @param state
 */
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(InfoDialog);
