/**
 * MultipleGroupSearch is used to peform group search oepration
 * @author: P.Vinothpandian
 *
 */

import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { post } from '../../actions/commonActions';
import headerInformation from './MultipleAdminHeaderConstants';
import SBButton from '../../common/component/sbbutton/SBButton';
import tableList from './MultipleSearchConstants';
import APIENDPOINTS from '../../constants/APIEndPoints';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import { addFilterData } from '../../actions/UserActions';
import checkLanguage from '../../language/checkLanguage';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';

class MultipleGroupSearch extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location } = props;
    this.state = {
      headerData: headerInformation('search'),
      fieldDataList: [],
      dataSource: { groupName: location.state },
      pageLoading: true,
      comboValues: [{ groupName: '' }],
      status: true,
    };
    this.groupReference = createRef();
    this.backReference = createRef();
  }

  /**
   * componentDidMount is used to load the group names of tenant
   */

  componentDidMount() {
    const { dataSource } = this.state;
    const getGroupData = async () => {
      const reponse = await post(APIENDPOINTS.GETGROUP, { groupName: '' });
      const responseData = await reponse.json();
      if (responseData.status === 'OK') {
        const { comboValues } = this.state;
        const { data: groupDataList } = responseData;
        if (groupDataList) {
          comboValues.push(...groupDataList);
        }
        const { addFilterData: setFilterData } = this.props;
        setFilterData(dataSource.groupName);
        this.setState({
          comboValues,
          pageLoading: false,
          status: true,
        });
        if (this.groupReference.current) this.groupReference.current.focus();
      } else {
        this.setState({
          pageLoading: false,
          dataSource: {
            groupName: '',
          },
          status: false,
        });
        if (this.backReference.current) this.backReference.current.focus();
      }
    };
    getGroupData();
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   *
   */

  onTableFrameEvent = (key, event) => {
    const { dataSource } = this.state;
    dataSource[key] = event.target.value;
    this.setState({ dataSource });
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { history } = this.props;
    switch (key) {
      case 'ok':
        this.onOkClick();
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING);
        break;
      default:
        break;
    }
  };

  /**
   * onOkClick is used to add the group name in filter operation
   */

  onOkClick = () => {
    const { history } = this.props;
    const { dataSource } = this.state;
    const { addFilterData: setFilterData } = this.props;
    setFilterData(dataSource.groupName);
    history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING, { ...dataSource });
  };

  /**
   * render is used to render the component
   */

  render() {
    const { headerData, comboValues, fieldDataList, dataSource, pageLoading, status } = this.state;
    return (
      <>
        <CircularSpinner
          loading={pageLoading}
          css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
        >
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            {status ? (
              <CustomTableFrame
                tableList={
                  fieldDataList.length ? fieldDataList : tableList(comboValues, this.groupReference)
                }
                dataSource={dataSource}
                defaultValue={{}}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              />
            ) : (
              <span>{this.languageLabelCheck('no_results_found_search')}</span>
            )}
            <div className="clearfix">
              <div className="floatright">
                <SBButton
                  id="btn_ok"
                  color="green"
                  type="button"
                  title="ok"
                  disabled={!status}
                  onClick={() => this.onHandleClick('ok')}
                />
                <SBButton
                  id="btn_cancel"
                  type="button"
                  title="cancel"
                  noMarginRight
                  ref={this.backReference}
                  onClick={() => this.onHandleClick('cancel')}
                />
              </div>
            </div>
          </div>
        </CircularSpinner>
      </>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = dispatch => {
  return {
    addFilterData: data => {
      dispatch(addFilterData(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleGroupSearch.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.string,
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  addFilterData: PropTypes.func.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleGroupSearch.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(MultipleGroupSearch));
