/**
 * AppList is used to load the data
 * @author: P.Vinothpandian
 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { isUndefined } from 'util';
import { CommonConstants } from '../../../constants/CommonConstants';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import UNDERSCORE from '../../../v1/common/utils';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import AppSearchPanel from './AppSearchPanel';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import AppGrid from './AppGrid';
import { post, postWithBlobData } from '../../../actions/commonActions';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import checkLanguage from '../../../language/checkLanguage';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import NoResultsFound from '../../../common/component/grid/NoResultsFound';
import headerInformation from '../Constants/AppManageHeaderConstants';
import { validateDate } from '../Constants/AppCommonValidations';
import {
  isNotUndefinedOrNotEmpty,
  isNotContainingSymbols,
  isNotContainingDoubleByteSymbols,
} from '../../../common/CommonValidations/CommonValidations';
import { errorMessageConstants } from '../Constants/AppErrorConstants';
import { setSearchReference } from '../Constants/AppReferenceConstants';

const fileDownload = require('js-file-download');

const resultsPerPageKey = 'app_list';
class AppList extends Component {
  /**
   * constructor is used to initalize the state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location } = props;
    let param = {};
    if (!isUndefined(location.state)) {
      param = location.state;
    }

    this.state = {
      pageLoading: false,
      gridLoading: false,
      errorMessage: { dmCodeError: '' },
      tenantList: [],
      noResults: false,
      selectedRowValue: {},
      searchSource: {
        dmCode: UNDERSCORE.isEmpty(param) ? '' : param.dmCode,
        businessName: '',
        companyNumber: '',
        startDate: '',
        stopDate: '',
        noDataLabel: '',
        dmCodeStatus: 0,
      },
      reference: setSearchReference(),
    };
  }

  /**
   * componentDidMount is load the data in the grid
   */

  componentDidMount() {
    const { searchSource, reference } = this.state;
    if (searchSource.dmCode) this.performSearch();
    this.setState({ pageLoading: false });
    if (reference.dmCodeRef.current) reference.dmCodeRef.current.focus();
  }

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, value) => {
    const { searchSource } = this.state;
    searchSource[key] = value;
    this.setState({ searchSource });
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * downloadFile is used to handle download button click
   */

  downloadFile = (key) => {
    const { searchSource } = this.state;
    const errorMessage = { dmCodeError: '', dateError: '' };
    if (this.performValidations('csv')) {
      const CSVMap = {
        1: APIENDPOINTS.MANAGEMENTINFO,
        4: APIENDPOINTS.GISUNCSVDOWNLOAD,
        5: APIENDPOINTS.PAYMENTINFO,
      };
      const fileNameMap = {
        1: 'app_info_file_name',
        2: 'app_info_diff_file_name',
        4: 'csv_order_number',
        5: 'approval_number',
      };
      postWithBlobData(CSVMap[parseInt(key, 10)], searchSource)
        .then((response) => response.blob())
        .then((blobData) => {
          if (blobData.size) {
            this.setState({
              errorMessage,
            });
            const printData = new Blob([blobData]);
            fileDownload(
              printData,
              `${this.languageLabelCheck(fileNameMap[parseInt(key, 10)])}.csv`,
            );
          } else {
            this.setState({
              errorMessage,
              tenantList: [],
              selectedRowValue: {},
              noResults: true,
              noDataLabel: 'no_csv_output',
            });
          }
        });
    } else {
      this.setState({ noResults: false, noDataLabel: '', tenantList: [], selectedRowValue: {} });
    }
  };

  /**
   * setFocustoErrorField is used to set focus to error field
   * @param dmCodeValidation
   * @param startDateValidation
   * @param stopDateValidation
   */

  setFocustoErrorField = (dmCodeValidation, startDateValidation, stopDateValidation) => {
    const { reference } = this.state;
    const { searchSource } = this.state;
    if (isNotUndefinedOrNotEmpty(searchSource.dmCode) && !dmCodeValidation.status) {
      reference.dmCodeRef.current.focus();
    } else if (!startDateValidation.status) {
      reference.startDateRef.current.focus();
    } else if (!stopDateValidation.status) {
      reference.stopDateRef.current.focus();
    }
  };

  /**
   * setDateErrorMessage is used to set error message to date fields
   * @return errorMessage
   */

  setDateErrorMessage = (errorMessage, startDateValidation, stopDateValidation) => {
    if (stopDateValidation.error === 'invalidformat')
      errorMessage.dateError = errorMessageConstants.firstUseStartDateError.invalidformat;
    else if (stopDateValidation.error === 'invalid')
      errorMessage.dateError = errorMessageConstants.firstUseStartDateError.invalid;
    else if (startDateValidation.error === 'invalidformat')
      errorMessage.dateError = errorMessageConstants.firstUseStartDateError.invalidformat;
    else if (startDateValidation.error === 'invalid')
      errorMessage.dateError = errorMessageConstants.firstUseStartDateError.invalid;
    else if (startDateValidation.error === 'range' || stopDateValidation.error === 'range')
      errorMessage.dateError = errorMessageConstants.firstUseStartDateError.range;
    return errorMessage;
  };

  searchValidation = (
    dmCode,
    dmCodeValidation,
    startDateValidation,
    stopDateValidation,
    errorMessage,
    reference,
  ) => {
    if (isNotUndefinedOrNotEmpty(dmCode) && !dmCodeValidation.status) {
      errorMessage.dmCodeError = errorMessageConstants.searchError.alphanumeric;
    }
    if (!startDateValidation.status || !stopDateValidation.status) {
      errorMessage = this.setDateErrorMessage(
        errorMessage,
        startDateValidation,
        stopDateValidation,
      );
      if (
        (isNotUndefinedOrNotEmpty(dmCode) && !dmCodeValidation.status) ||
        !startDateValidation.status ||
        !stopDateValidation.status
      ) {
        this.setState({ errorMessage });
        this.setFocustoErrorField(dmCodeValidation, startDateValidation, stopDateValidation);
        return false;
      }
    }
    this.setState({ errorMessage });
    if (errorMessage.dmCodeError !== '') {
      reference.dmCodeRef.current.focus();
      return false;
    }
    return true;
  };

  /**
   * performValidations is used to perform validations
   * @return status
   */

  performValidations = (searchKey) => {
    const { searchSource, reference } = this.state;
    let errorMessage = { dmCodeError: '', dateError: '' };
    const doubleByteRegex = /\u3000/g;
    const dmCode = doubleByteRegex.test(searchSource.dmCode)
      ? searchSource.dmCode
      : searchSource.dmCode.trim();
    const businessName = doubleByteRegex.test(searchSource.businessName)
      ? searchSource.businessName
      : searchSource.businessName.trim();
    const companyNumber = doubleByteRegex.test(searchSource.companyNumber)
      ? searchSource.companyNumber
      : searchSource.companyNumber.trim();
    const dmCodeValidation = this.validateSearchKeyValue(searchSource.dmCode);
    const startDateValidation = validateDate(searchSource.startDate, true);
    const stopDateValidation = validateDate(searchSource.stopDate, true);
    if (
      searchKey !== 'csv' &&
      (isNotUndefinedOrNotEmpty(dmCode) ||
        isNotUndefinedOrNotEmpty(businessName) ||
        isNotUndefinedOrNotEmpty(companyNumber))
    ) {
      return this.searchValidation(
        dmCode,
        dmCodeValidation,
        startDateValidation,
        stopDateValidation,
        errorMessage,
        reference,
      );
    }

    if (searchKey === 'csv' && dmCode === '') {
      errorMessage.dmCodeError = errorMessageConstants.searchError.empty;
      this.setState({ errorMessage });
      if (searchSource.startDate !== '' || searchSource.stopDate !== '') {
        errorMessage = this.setDateErrorMessage(
          errorMessage,
          startDateValidation,
          stopDateValidation,
        );
        this.setState({ errorMessage });
      }
      reference.dmCodeRef.current.focus();
      return false;
    }

    if (
      searchKey === 'csv' &&
      (isNotUndefinedOrNotEmpty(dmCode) ||
        isNotUndefinedOrNotEmpty(businessName) ||
        isNotUndefinedOrNotEmpty(companyNumber))
    ) {
      return this.searchValidation(
        dmCode,
        dmCodeValidation,
        startDateValidation,
        stopDateValidation,
        errorMessage,
        reference,
      );
    }

    if (searchSource.startDate !== '' || searchSource.stopDate !== '') {
      if (dmCode === '') errorMessage.dmCodeError = errorMessageConstants.searchError.empty;
      errorMessage = this.setDateErrorMessage(
        errorMessage,
        startDateValidation,
        stopDateValidation,
      );
      this.setState({ errorMessage });
      if (errorMessage.dmCodeError !== '') reference.dmCodeRef.current.focus();
      else this.setFocustoErrorField(dmCodeValidation, startDateValidation, stopDateValidation);
      return false;
    }

    this.setState({ errorMessage: { dmCodeError: errorMessageConstants.searchError.empty } });
    reference.dmCodeRef.current.focus();
    return false;
  };

  /**
   * performSearch is used to perform search operation
   */

  performSearch = () => {
    const errorValidation = this.performValidations();
    if (errorValidation) {
      this.setState({
        tenantList: [],
      });
      this.filterDataAPI();
    } else {
      this.setState({
        tenantList: [],
        noResults: false,
      });
    }
  };

  /**
   * filterDataAPI is used to perform search operation bymaking an API call
   */

  filterDataAPI = () => {
    const { searchSource } = this.state;
    this.setState({ gridLoading: true });
    const getDataList = async () => {
      const data = await post(APIENDPOINTS.APPLICATIONLIST, searchSource);
      const response = await data.json();
      if (response.status) {
        const { applicationRecordVoList } = response;
        this.setState({
          tenantList: applicationRecordVoList,
          selectedRowValue: applicationRecordVoList.length ? applicationRecordVoList[0] : {},
          noResults: !applicationRecordVoList.length,
          gridLoading: false,
          noDataLabel: '',
        });
      } else {
        this.setState({
          selectedRowValue: {},
          noResults: true,
          gridLoading: false,
          noDataLabel: '',
        });
      }
    };
    getDataList();
  };

  /**
   * validateSearchKeyValue is used to validate seacrh key fields
   * @param data
   * @param result
   */

  validateSearchKeyValue = (data) => {
    const result = { status: true, error: '' };
    const doubleByteRegex = /\u3000/g;
    const spaceRegex = /\s/g;
    if (
      !spaceRegex.test(data) &&
      !doubleByteRegex.test(data) &&
      isNotContainingSymbols(data) &&
      isNotContainingDoubleByteSymbols(data)
    ) {
      result.status = true;
    } else {
      result.status = false;
      result.error = 'alphanumeric';
    }
    return result;
  };

  /**
   * setValidationErrorMessage is used to error validation message
   * @param validationResultObject
   */

  setValidationErrorMessage = (validationResultObject) => {
    switch (validationResultObject.error) {
      case 'alphanumeric':
        return errorMessageConstants.searchError.alphanumeric;
      default:
        return '';
    }
  };

  /**
   * handleRadioChange is used to handle radio change event
   * @param value
   */

  handleRadioChange = (value) => {
    let { selectedRowValue } = this.state;
    selectedRowValue = value;
    this.setState({ selectedRowValue });
  };

  /**
   * handlePaginationClick is used to handle pagination event
   * @param tableData
   */

  handlePaginationClick = (tableData) => {
    if (tableData.length) {
      this.setState({ selectedRowValue: tableData[0] });
    }
  };

  /**
   * handleSelectChange is used to handle select combo event
   */

  handleSelectChange = () => {
    this.performSearch();
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   * @param buttonKey
   */

  onHandleClick = (key, buttonKey) => {
    if (key === 'search') {
      this.performSearch();
    } else if (key === 'csvoutput') {
      this.downloadFile(buttonKey);
    } else {
      const { selectedRowValue } = this.state;
      const serviceMap = {
        historycorrection: ROUTEENDPOINTS.HISTORYCORRECTION,
        historyinquiry: ROUTEENDPOINTS.HISTORYINQUIRY,
        notifyinvoice: ROUTEENDPOINTS.REGISTRATIONNOTICE,
        change: ROUTEENDPOINTS.APPUPDATE,
        delete: ROUTEENDPOINTS.APPDELETE,
      };
      this.serviceCall(serviceMap[key], selectedRowValue);
    }
  };

  /**
   * serviceCall is used to make service call
   * @param pathName
   * @param param
   */

  serviceCall = (pathName, param) => {
    const { history } = this.props;
    history.push(pathName, { dmCode: param.dmCode });
  };

  /**
   * onKeyDown is used to perform key down operation
   * @param event
   */

  onKeyDown = (event) => {
    if (event.keyCode === 13) this.performSearch();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const {
      tenantList,
      noResults,
      errorMessage,
      pageLoading,
      searchSource,
      gridLoading,
      reference,
      noDataLabel,
      selectedRowValue,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.gridCircularSpinnerStyle}>
        <SBContentHeader
          title={headerInformation('applist').title}
          description={headerInformation('applist').description}
        />
        <AppSearchPanel
          reference={reference}
          searchSource={searchSource}
          errorMessage={errorMessage}
          onHandleClick={(key, csvKey) => this.onHandleClick(key, csvKey)}
          onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, value)}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '60%', left: 'calc(60% - 55px)' }}
        >
          {tenantList.length > 0 && (
            <AppGrid
              tenantList={tenantList}
              defaultSelected={resultsPerPageKey}
              onHandleClick={(key) => this.onHandleClick(key)}
              handleRadioChange={(event) => this.handleRadioChange(event)}
              handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
              handleSelectChange={() => this.handleSelectChange()}
              selectedRowValue={selectedRowValue}
            />
          )}
          {noResults && (
            <>
              <br />
              <NoResultsFound noDataLabel={noDataLabel} />
            </>
          )}
        </CircularSpinner>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = (state) => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

AppList.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(PropTypes.string),
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
AppList.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(AppList));
