import React from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';

const languageLabelCheck = (label, props) => {
  const { isNotMultiLang, localeData } = props;
  return isNotMultiLang ? label : checkLanguage(localeData, label);
};

const SBTableHeader = props => {
  const { headerText } = props;
  return <h2 className="title-corner">{languageLabelCheck(headerText, props)}</h2>;
};

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(SBTableHeader);
