/**
 * ContractInfo is used to load contract information page
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import {
  tableContractList,
  tableContractUpdateList,
  contractTableHeaderList,
} from '../Constants/AppFrameConstants';
import { validateInputs, setFocusinerrorField } from '../Constants/contractConstants';
import { setAppRegisterEntity, setAppUpdateEntity } from '../../../actions/TenantActions';
import {
  defaultUnitBasicPrice,
  all,
  unitRentalAll,
  historyBasic,
  aPlan,
  bPlan,
  androidPackPlan,
  setContractInitialValues,
} from '../Constants/AppFieldConstants';
import { setContractReference } from '../Constants/AppReferenceConstants';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../Constants/AppManageHeaderConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import AppCRUDFooter from './AppCRUDFooter';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

class contractInfo extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('contractinfo'),
      fieldDataList: [],
      dynamicData: {},
      initialState: true,
      isUpdate: !!location.state.isUpdate,
      contractSource: setContractInitialValues(param),
      reference: setContractReference(),
      modified: false,
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount is used to set initial focus
   */

  componentDidMount() {
    const { reference, isUpdate, dynamicData, contractSource } = this.state;
    this.setState({
      fieldDataList: isUpdate
        ? tableContractUpdateList(dynamicData, reference, contractSource)
        : tableContractList(dynamicData, reference, contractSource),
    });
  }

  /**
   * componentDidUpdate makes focus on first component when screen is loaded
   */

  componentDidUpdate() {
    const { initialState, reference } = this.state;
    if (initialState && reference.contractStatusRef.current) {
      reference.contractStatusRef.current.focus();
      this.setState({ initialState: false });
    }
  }

  /**
   * onHandleClick is used to handle click event
   * @param key
   */

  onHandleClick = key => {
    const {
      location,
      history,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { contractSource, modified, isUpdate } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    }
    if (key === 'save') {
      const errorStatus = this.validateData();
      if (errorStatus) {
        param.contractStatus = contractSource.contractStatus;
        param.firstUseStartDate = contractSource.firstUseStartDate;
        param.effectiveDateChange = contractSource.effectiveDateChange;
        param.cancellationDate = contractSource.cancellationDate;
        param.unitPriceBasic = contractSource.unitPriceBasic;
        param.rentalPlanName = contractSource.rentalPlanName;
        param.unitPriceRental = contractSource.unitPriceRental;
        if (isUpdate) {
          makeAppUpdateEntity({
            param,
            globalModified: modified || location.state.globalModified,
          });
          history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
        } else {
          makeAppRegisterEntity({
            param,
            autoGenertedDmCode: location.state.autoGenertedDmCode,
            globalModified: modified || location.state.globalModified,
          });
          history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
        }
      }
    } else if (modified) {
      this.setState({ modalIsOpen: true });
    } else if (isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: modified || location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: modified || location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * onOkDialog is used to handle ok click event of dialog
   */

  onOkDialog = () => {
    const {
      location,
      history,
      setModified,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { isUpdate } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.dataSource;
    }
    if (isUpdate) {
      setModified(location.state.globalModified);
      makeAppUpdateEntity({
        param,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      setModified(location.state.globalModified);
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  getUnitPriceRentalValue = value => {
    const rentalPlanNameValue = parseInt(value, 10);
    if (rentalPlanNameValue === historyBasic) {
      return 300;
    }
    if (rentalPlanNameValue === aPlan) {
      return 500;
    }
    if (rentalPlanNameValue === bPlan) {
      return 700;
    }
    if (rentalPlanNameValue === androidPackPlan) {
      return 200;
    }
    return '';
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    const { contractSource, reference, isUpdate, dynamicData } = this.state;
    const { setModified } = this.props;
    if (key === 'rentalPlanName') {
      contractSource.unitPriceRental = this.getUnitPriceRentalValue(event.target.value);
    } else if (key === 'contractStatus' && !parseInt(event.target.value, 10)) {
      contractSource.unitPriceBasic = defaultUnitBasicPrice;
      contractSource.cancellationDate = '';
    } else if (key === 'contractStatus' && parseInt(event.target.value, 10) === all) {
      contractSource.rentalPlanName = unitRentalAll;
      contractSource.unitPriceBasic = '';
      contractSource.firstUseStartDate = '';
      contractSource.effectiveDateChange = '';
      contractSource.cancellationDate = '';
      contractSource.unitPriceRental = '';
    } else if (key === 'contractStatus' && parseInt(event.target.value, 10) === 2) {
      contractSource.unitPriceBasic = '';
      contractSource.effectiveDateChange = '';
    } else if (key === 'contractStatus') {
      contractSource.unitPriceBasic = '';
      contractSource.cancellationDate = '';
    }
    contractSource[key] =
      event && event.target && event.target.type === 'select-one' ? event.target.value : value;
    setModified(true);
    this.setState({
      fieldDataList: isUpdate
        ? tableContractUpdateList(dynamicData, reference, contractSource)
        : tableContractList(dynamicData, reference, contractSource),
      contractSource,
      modified: true,
    });
  };

  /**
   * validateData is used to validate data
   * @return validateData
   *
   */

  validateData() {
    const { contractSource, reference, isUpdate } = this.state;
    const validationResult = validateInputs(contractSource, isUpdate);
    this.setState({
      dynamicData: validationResult,
      fieldDataList: isUpdate
        ? tableContractUpdateList(validationResult, reference, contractSource)
        : tableContractList(validationResult, reference, contractSource),
    });
    setFocusinerrorField(validationResult, reference);
    return validationResult.status;
  }

  /**
   * render is used to render the component
   * @return contractInfoComponent
   */

  render() {
    const {
      headerData,
      fieldDataList,
      contractSource,
      modalIsOpen,
      footer,
      reference,
    } = this.state;
    return (
      <div>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <div className="clearfix mb10">
            <SBTableHeader headerText="contract_information" />
            <CustomTableFrame
              tableList={fieldDataList}
              tableHeaderList={contractTableHeaderList}
              dataSource={contractSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
            />
          </div>
          <div>
            <AppCRUDFooter
              reference={reference}
              footer={footer}
              onHandleClick={key => this.onHandleClick(key)}
            />
          </div>
          <SBDialog
            open={modalIsOpen}
            header="cancel_confirmation"
            type="confirmation"
            content="cancel_confirmation_message"
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={() => {
              this.onOkDialog();
            }}
            width="400px"
          />
        </div>
      </div>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    setAppRegisterEntity: data => {
      dispatch(setAppRegisterEntity(data));
    },
    setAppUpdateEntity: data => {
      dispatch(setAppUpdateEntity(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

contractInfo.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dataSource: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
      globalModified: PropTypes.bool.isRequired,
      autoGenertedDmCode: PropTypes.string.isRequired,
    }),
  }).isRequired,
  setAppRegisterEntity: PropTypes.func.isRequired,
  setAppUpdateEntity: PropTypes.func.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(contractInfo));
