/**
 * MultipleAdminHeaderConstant is used to load the header constant data
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

/**
 * headerInformation is used to load the header constant data
 * @param key
 * @return headerList
 *
 */

export default function headerInformation(key) {
  switch (key) {
    case 'add':
      return {
        title: 'register_administrator_info',
        description: ['register_administrator_info_description'],
      };
    case 'update':
      return {
        title: 'change_administrator_information',
        description: ['change_administrator_information_description'],
      };
    case 'updateSuccessfully':
      return {
        title: 'change_administrator_information',
        description: ['administrator_information_edited'],
      };
    case 'delete':
      return {
        title: 'delete_administrator_info',
        description: ['delete_administrator_information_description'],
      };
    case 'deletedSuccessfully':
      return {
        title: 'delete_administrator_info',
        description: ['administrator_information_deleted'],
      };
    case 'setting':
      return {
        title: 'administrator_list',
        description: ['enables_to_search_email_address'],
      };
    case 'continuanceRegistration':
      return {
        title: 'register_administrator_info',
        description: ['register_administrator_info_register'],
      };
    case 'registeredSuccessfully':
      return {
        title: 'register_administrator_info',
        description: ['administrator_information_registered'],
      };
    case 'search':
      return {
        title: 'search_by_group_name',
        description: ['group_refine_search_description'],
      };
    default:
      return {};
  }
}
