import React from 'react';

export default function SetHeader(props) {
  const { caption, buttonList, setEvent, children } = props;
  return (
    <div className="clearfix mb10">
      <h2 className="bold floatleft">{caption}</h2>
      {buttonList &&
        buttonList.map(data => {
          return (
            <div className="floatright">
              <a href onClick={() => setEvent(data.id)}>
                <img className="icon ml10" id={data.id} src={data.src} alt={data.alt} />
              </a>
            </div>
          );
        })}
      {children}
    </div>
  );
}
