import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../language/checkLanguage';

class SBInfoContent extends Component {
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;

    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const { title } = this.props;
    return (
      <p className="attention mb15">
        <font style={{ verticalAlign: 'inherit' }}>
          <font style={{ verticalAlign: 'inherit' }}>
            {title && this.languageLabelCheck(title)}
          </font>
        </font>
      </p>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps, null)(SBInfoContent);
