/**
 * IDGrid is used to load the data
 * @author: P.Vinothpandian
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { CommonConstants } from '../../../constants/CommonConstants';
import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';
import SBTable from '../../../common/component/sbtable/SBTable';
import SBButton from '../../../common/component/sbbutton/SBButton';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import { Role } from '../../../constants/Role';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';

class IDGrid extends Component {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * getColumns is load the column information
   * @return columndetails
   *
   */

  getColumns = () => {
    return [
      {
        title: '',
        dataIndex: 'render-radio',
        styles: { minWidth: 20, width: 20 },
        textAlign: 'center',
      },
      {
        title: 'dm_code',
        dataIndex: 'dmCode',
        rowKey: 'dmCode',
        sorter: false,
        render: record => {
          return (
            <Link to={{ pathname: ROUTEENDPOINTS.APPLIST, state: record }}>
              <u>{record.dmCode}</u>
            </Link>
          );
        },
      },
      {
        title: 'login_id',
        dataIndex: 'loginId',
        rowKey: 'loginId',
        sorter: false,
      },
      {
        title: 'authority',
        dataIndex: 'authorityName',
        rowKey: 'authorityName',
        sorter: false,
      },
      {
        title: 'ip_address_restriction',
        dataIndex: 'ipAddress',
        rowKey: 'ipAddress',
        sorter: false,
        whiteSpace: 'break-spaces',
      },
      {
        title: 'created_date',
        dataIndex: 'createdDateFormat',
        rowKey: 'createdDateFormat',
        sorter: false,
      },
      {
        title: 'updated_date',
        dataIndex: 'updatedDateFormat',
        rowKey: 'updatedDateFormat',
        sorter: false,
      },
    ];
  };

  /**
   * render is used to render the grid
   * @return component
   */

  render() {
    const columns = this.getColumns();
    const { gridListComboOptions } = CommonConstants;
    const {
      userList,
      defaultSelected,
      handleRadioChange,
      handlePaginationClick,
      handleSelectChange,
      onHandleClick,
      userVo,
      selectedRowValue,
    } = this.props;
    return (
      <>
        <div>
          <div style={{ marginTop: 10 }}>
            <RoleResolver
              component="sbtable"
              columns={columns}
              authorityRoleType={Role.AppListUpdateRoles}
            >
              <SBTable
                columns={columns}
                dataSource={userList}
                loading
                pagination
                defaultSelected={defaultSelected}
                filterList={gridListComboOptions}
                defaultSelectedKeys={[0]}
                countInfoContents={userList.length}
                countInfo="search_result_with_count"
                handleRadioChange={event => handleRadioChange(event)}
                handlePaginationClick={tableData => handlePaginationClick(tableData)}
                handleSelectChange={(tableData, value) => handleSelectChange(value)}
              />
            </RoleResolver>
          </div>
        </div>
        <div
          className="clearfix"
          style={!Role.AppListUpdateRoles.includes(userVo.authorityId) ? { display: 'none' } : null}
        >
          <div className="floatright">
            <SBButton
              id="btn_change"
              type="button"
              title="change"
              color="green"
              onClick={() => onHandleClick('change')}
            />
            <SBButton
              id="btn_delete"
              noMarginRight
              type="button"
              title="delete"
              disabled={selectedRowValue.userId === userVo.userId || selectedRowValue.authorityId === 6}
              onClick={() => onHandleClick('delete')}
            />
          </div>
        </div>
      </>
    );
  }
}

/**
 * mapStateToProps is used to map state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    localeData: state.language.data,
    userVo,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
IDGrid.propTypes = {
  handlePaginationClick: PropTypes.func.isRequired,
  handleRadioChange: PropTypes.func.isRequired,
  handleSelectChange: PropTypes.func.isRequired,
  isNotMultiLang: PropTypes.bool,
  userList: PropTypes.arrayOf(PropTypes.object),
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  defaultSelected: PropTypes.number,
  onHandleClick: PropTypes.func.isRequired,
  selectedRowValue: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.number]),
  ).isRequired,
  userVo: commonPropTypes().userVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
IDGrid.defaultProps = {
  isNotMultiLang: false,
  userList: [],
  defaultSelected: 25,
};

export default connect(mapStateToProps)(withRouter(IDGrid));
