/**
 * CommonValidations is functions that can be used to perfom common validation
 * @author V.Vijayaragavan
 */
import UNDERSCORE from '../../v1/common/utils';

/**
 * isNotUndefinedOrNotEmpty is used to check is value is not undefined and not empty
 * @param value
 * @return boolean
 */
export function isNotUndefinedOrNotEmpty(value) {
  return !UNDERSCORE.isEmpty(value);
}

/**
 * isLengthValid is used to check length of value
 * @param value
 * @param minLength
 * @param maxLength
 * @return boolean
 */
export function isLengthValid(value, minLength, maxLength) {
  if (value) {
    const inputLength = value.length;
    return inputLength >= minLength && inputLength <= maxLength;
  }
  return false;
}

/**
 * isTextAreaLengthValid is used to content in text area length is valid
 * @param value
 * @param minLength
 * @param maxLength
 * @return boolean
 */
export function isTextAreaLengthValid(value, minLength, maxLength) {
  const inputValue = value.split('\n').join('  ');
  return isLengthValid(inputValue, minLength, maxLength);
}

/**
 * isAlphaAndNumericOnly is used to check value contains mixed alphabet and numbers
 * @param value
 * @return boolean
 */
export function isAlphaAndNumericOnly(value) {
  const alphaAndNumericRegex = /^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+)$/;
  return alphaAndNumericRegex.test(value);
}

/**
 * isAlphaOrNumeric is used to check value contains only alphabets or numbers
 * @param value
 * @return boolean
 */
export function isAlphaOrNumeric(value) {
  const letterOrNumber = /^[a-zA-Z0-9]+$/;
  return letterOrNumber.test(value);
}

/**
 * isDMCodeRegex is used to check DM Code format given by SB
 * @param value
 * @return boolean
 */
export function isDMCodeRegex(value) {
  const letterOrNumber = /^M[0-9]+$/;
  return letterOrNumber.test(value);
}

/**
 * isNotContainingSpace is used to check value does not contain any space
 * @param value
 * @return boolean
 */
export function isOSVersion(value) {
  if (
    /^[0-9]{1,2}$/.test(value) &&
    (value.toString().length === 1 || value.toString().length === 2)
  ) {
    return true;
  }
  if (/^[0-9][.]([0-9]{1})$/.test(value)) {
    return true;
  }
  if (/^[0-9][0-9][.]([0-9]{1})$/.test(value)) {
    return true;
  }
  if (/^[0-9][.](([0-9]{1})[.]([0-9]{1}))$/.test(value)) {
    return true;
  }
  if (/^[0-9][0-9][.](([0-9]{1})[.]([0-9]{1}))$/.test(value)) {
    return true;
  }
  return false;
}
export function isNotContainingSpace(value) {
  const spaceRegex = /^[^ ]+$/;
  return spaceRegex.test(value);
}

/**
 * isIpValid is used to check is ipvalue given in valid
 * @param ipValue
 * @return boolean
 */

export function isIpValid(ipValue) {
  const ipRegex = new RegExp(
    [
      /^\b(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])\b$/,
    ]
      .map(function(r) {
        return r.source;
      })
      .join(''),
  );

  const ipMaskRegex = new RegExp(
    [
      /^\b(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])/,
      /\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])\/([1-9]|[12][0-9]|3[0-2])\b$/,
    ]
      .map(function(r) {
        return r.source;
      })
      .join(''),
  );
  return ipRegex.test(ipValue) ||  ipMaskRegex.test(ipValue);
}

/**
 * isEmailValid is used to check format of email
 * @param value
 * @return boolean
 */
export function isEmailValid(emailId) {
  const emailRegex = /^[a-zA-Z0-9_!#$%&’*+/=?`{|}~^.-]{1,64}@([a-zA-Z0-9.-]{1,35})$/;
  return emailRegex.test(emailId);
}

/**
 * minOneNumberAndOtherCharacter is used to check is value contains minimum one number and one other character
 * @param value
 * @return boolean
 */
export function minOneNumberAndOtherCharacter(value) {
  return /[0-9]/.test(value) && /[^0-9]/.test(value);
}

/**
 * isNotContainingSymbols is used to validate vlues does not contain any symbols
 * @param value
 * @return boolean
 */
export function isNotContainingSymbols(value) {
  const notAllowSymbolsRegex = /^[^`~!@#$%^&*()_|+\-=?;:'",.<>{}[\]/]+$/;
  return notAllowSymbolsRegex.test(value) && UNDERSCORE.isNull(value.match(/\p{Sc}/gu));
}

/**
 * isNotContainingSymbols is used to validate vlues does not contain any symbols
 * @param value
 * @return boolean
 */
export function isNotContainingDoubleByteSymbols(value) {
  return UNDERSCORE.isNull(value.match(/\p{P}/gu));
}

/**
 * validatePassword is used to validate password
 * @param value
 * @return result
 */
export function validatePassword(value) {
  const minLength = 8;
  const maxLength = 20;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    if (isLengthValid(value, minLength, maxLength)) {
      if (isAlphaAndNumericOnly(value)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'alphanumeric';
      }
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateNameInput is used to validate name inputs like first name last name full name
 * @param value
 * @return result
 */
export function validateNameInput(value) {
  const minLength = 1;
  const maxLength = 100;
  const result = { status: true, error: '' };
  // Regex to test double space and also not to start or end with space
  const singleSpace = /^\S((?!.* {2}).*\S)?$/;
  const nonNumber = /^[^0-9]+$/;
  if (isNotUndefinedOrNotEmpty(value)) {
    if (isLengthValid(value, minLength, maxLength)) {
      if (nonNumber.test(value) && singleSpace.test(value) && isNotContainingSymbols(value)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'alphanumeric';
      }
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateDmCode is used to validate dmcode
 * @param dmCode
 * @return result
 */
export function validateDmCode(dmCode) {
  const maxLength = 8;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(dmCode)) {
    if (isLengthValid(dmCode, maxLength, maxLength)) {
      if (isDMCodeRegex(dmCode)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'alphanumeric';
      }
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateLoginId is used to check loginId
 * @param loginId
 * @return result
 */
export function validateLoginId(loginId) {
  const minLength = 4;
  const maxLength = 20;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(loginId)) {
    if (isLengthValid(loginId, minLength, maxLength)) {
      if (isAlphaOrNumeric(loginId)) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'alphanumeric';
      }
    } else {
      result.status = false;
      result.error = 'length';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateEmail is used to check email address
 * @param emailID
 * @return result
 */
export function validateEmail(emailID) {
  const result = { status: true, error: '' };
  const minLength = 1;
  const maxLength = 100;
  if (isNotUndefinedOrNotEmpty(emailID)) {
    if (emailID.length >= minLength && emailID.length <= maxLength) {
      if (isEmailValid(emailID)) {
        result.error = '';
        result.status = true;
      } else {
        result.error = 'invalidEmail';
        result.status = false;
      }
    } else {
      result.error = 'length';
      result.status = false;
    }
  } else {
    result.error = 'empty';
    result.status = false;
  }
  return result;
}

/**
 * validateMultipleIps is used to validate multiple ips format
 * @param ipAddress
 * @return result
 */
export function validateMultipleIps(ipAddress) {
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(ipAddress)) {
    if (isTextAreaLengthValid(ipAddress, 0, 255)) {
      ipAddress.trim().split('\n').every(function(ip) {
        if (!isIpValid(ip)) {
          result.status = false;
          return false;
        }
        return true;
      });

      if (result.status) {
        result.error = '';
      } else {
        result.error = 'invalidIp';
        result.status = false;
      }
    } else {
      result.error = 'length';
      result.status = false;
    }
  } else {
    result.error = '';
    result.status = true;
  }
  return result;
}

/**
 * validateFileSize is used to validate file size
 * @param file
 * @param maxFileSizeInMB
 * @param minFileSizeInMB
 * @return validationResult
 */
export function validateFileSize(file, maxFileSizeInMB, minFileSizeInMB) {
  const validationResult = { status: true, key: '' };
  if (file) {
    if (file.size === 0 || file.size < minFileSizeInMB) {
      validationResult.status = false;
      validationResult.key = 'file_size_min';
    } else if (Math.ceil(file.size / 1048576) > maxFileSizeInMB) {
      validationResult.status = false;
      validationResult.key = 'file_size_greater';
    }
  }
  return validationResult;
}

/**
 * getLocaleDateFormat is used to get date format based on locale
 * @param languageCode
 * @return datetimeformat
 */

export function getLocaleDateFormat(languageCode) {
  if (languageCode === 'ja') {
    return 'YYYY/MM/DD HH:mm';
  }
  return 'DD/MM/YYYY HH:mm';
}

export default {
  isNotUndefinedOrNotEmpty,
  isLengthValid,
  isAlphaAndNumericOnly,
  isAlphaOrNumeric,
  isIpValid,
  isEmailValid,
  isOSVersion,
  validatePassword,
  validateNameInput,
  validateLoginId,
  validateEmail,
  validateMultipleIps,
  minOneNumberAndOtherCharacter,
  isNotContainingSymbols,
  isNotContainingSpace,
  validateFileSize,
};
