/**
 * MultipleAdminSetting is used to load the list screen
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import checkLanguage from '../../language/checkLanguage';
import { getColumns } from './MultipleAdminCommonFunctions';
import { post, get, postWithBlobData } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import MulipleAdminSearch from './MulipleAdminSearch';
import MulipleAdminData from './MulipleAdminData';
import MultipleAdminFooter from './MultipleAdminFooter';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import { addFilterData } from '../../actions/UserActions';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import { getUserTodaysDate, dateBasedOnLocale } from '../../common/utils/dateUtils';
import { isNotUndefinedOrNotEmpty } from '../../common/CommonValidations/CommonValidations';
import { getTenantEntity } from '../../actions/TenantActions';

const resultPerPageKey = 'multiple_admin_setting';

const fileDownload = require('js-file-download');

class MultipleAdminSetting extends Component {
  /**
   * constructor is used to initalize the state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { groupName } = this.props;
    this.state = {
      searchParam: { id: '', groupName },
      userList: [],
      countInfoContents: 0,
      selectedRowValue: {},
      pageLoading: true,
      gridLoading: false,
      register: false,
      openDialog: false,
      deleteDisable: false,
      searchKeyword: '',
      showColumns: [],
    };
    this.searchBoxRef = createRef();
  }

  /**
   * componentDidMount is load the data in the grid
   */

  componentDidMount() {
    const { searchParam } = this.state;
    const { getTenantEntity: getTenantDetail, history } = this.props;
    const getAdminList = async () => {
      const response = await post(APIENDPOINTS.MULTIPLESETTINGLIST, searchParam);
      const responseData = await response.json();
      const { userVoList } = responseData;
      const { tenantVo } = responseData;
      const { register } = responseData;
      if (isNotUndefinedOrNotEmpty(tenantVo)) {
        getTenantDetail(tenantVo);
        const showColumns = getColumns(tenantVo);
        if (userVoList.length) {
          const { userVo } = this.props;
          const { userId } = userVo;
          let { deleteDisable } = this.state;
          if (userId === userVoList[0].userId) {
            deleteDisable = true;
          } else {
            deleteDisable = false;
          }
          this.setState({ selectedRowValue: userVoList[0], deleteDisable });
        }
        this.setState({
          countInfoContents: responseData.usersLength,
          userList: userVoList,
          pageLoading: false,
          register,
          showColumns,
        });
        if (this.searchBoxRef.current) this.searchBoxRef.current.focus();
      } else {
        history.push(ROUTEENDPOINTS.ERROR);
      }
    };
    getAdminList();
  }

  /**
   * componentWillUnmount is used to clear redux value
   */

  componentWillUnmount() {
    const { addFilterData: setFilterData } = this.props;
    setFilterData('');
  }

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * handleRadioChange is used to handle radio change in the grid
   * @param value
   *
   */

  handleRadioChange = (value) => {
    let { selectedRowValue, deleteDisable } = this.state;
    const { userVo } = this.props;
    const { userId } = userVo;
    if (userId === value.userId) {
      deleteDisable = true;
    } else {
      deleteDisable = false;
    }
    selectedRowValue = value;
    this.setState({ selectedRowValue, deleteDisable });
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   *
   */

  onHandleClick = (key) => {
    const { selectedRowValue } = this.state;
    const serviceMap = {
      registration: ROUTEENDPOINTS.MULTIPLEADMINREGISTER,
      change: ROUTEENDPOINTS.MULTIPLEADMINCHANGE,
      delete: ROUTEENDPOINTS.MULTIPLEADMINDELETE,
      csvBatchOperation: ROUTEENDPOINTS.MULTIPLEADMINBULKREG,
    };
    if (key === 'csvOutput') {
      this.downLoadCSVFile();
    } else {
      this.serviceCall(serviceMap[key], selectedRowValue, key);
    }
  };

  /**
   * downLoadCSVFile is used to fetch all multiple Admin list and download as CSV File.
   *
   */
  downLoadCSVFile = () => {
    const { userVo, currentLanguage, groupName } = this.props;
    const { searchKeyword } = this.state;
    this.setState({ pageLoading: true });
    const searchParam = {
      id: searchKeyword === undefined ? '' : searchKeyword,
      groupName,
    };
    postWithBlobData(APIENDPOINTS.MULTIPLESETTINGCSVLIST, searchParam)
      .then((response) => response.blob())
      .then((blobData) => {
        this.setState({ pageLoading: false });
        if (this.searchBoxRef.current) {
          this.searchBoxRef.current.value = searchKeyword;
        }
        const printData = new Blob([blobData]);
        fileDownload(
          printData,
          `${userVo.dmCode}_${userVo.businessName}_Multiple Administrator List_${dateBasedOnLocale(
            getUserTodaysDate(userVo.timeZoneOffSet),
            currentLanguage,
          )}.csv`,
        );
      });
  };

  /**
   * handlePaginationClick is used to handle pagination click operation
   * @param tableData
   *
   */
  handlePaginationClick = (tableData) => {
    if (tableData.length) {
      const { userVo } = this.props;
      const { userId } = userVo;
      let { deleteDisable } = this.state;
      if (userId === tableData[0].userId) {
        deleteDisable = true;
      } else {
        deleteDisable = false;
      }
      this.setState({ selectedRowValue: tableData[0], deleteDisable });
    }
  };

  /**
   * handleSelectChange is used to handle select change
   *
   */

  handleSelectChange = () => {
    const { searchKeyword } = this.state;
    this.performSearch(searchKeyword);
  };

  /**
   * serviceCall is perform service call
   * @param pathName
   * @param param
   * @param key
   *
   */

  serviceCall = (pathName, param, key) => {
    const { history } = this.props;
    if (key !== 'registration' && key !== 'csvBatchOperation') {
      const getUserDetails = async () => {
        const { userId } = param;
        this.setState({ pageLoading: true });
        const data = await get(`${APIENDPOINTS.USERDETAILS}/${userId}`);
        const userDetails = await data.json();
        if (userDetails.status) {
          history.push(pathName, { param: userDetails.userVo });
        } else {
          this.performSearch();
          this.setState({ pageLoading: false, openDialog: true });
        }
      };
      getUserDetails();
    } else {
      history.push(pathName);
    }
  };

  /**
   * onKeyDown is used to perform key down operations
   * @param event
   * @param value
   *
   */

  onKeyDown = (event, value) => {
    if (event.keyCode === 13) this.performSearch(value);
  };

  /**
   * handleSearchAction is used to handle search operations
   * @param key
   * @param event
   * @param value
   *
   */

  handleSearchAction = (key, event, value) => {
    const { history, groupName: defaultValue } = this.props;
    if (key === 'searchByGroupButton') {
      history.push(ROUTEENDPOINTS.MULTIPLEGROUPSEARCH, defaultValue);
    } else if (key === 'searchButton') {
      this.performSearch(value.searchKeyword);
    }
    this.setState({ searchKeyword: value.searchKeyword ? value.searchKeyword : '' });
  };

  /**
   * performSearch is used to perform search operations
   * @param searchKeyword
   * @param value
   *
   */

  performSearch = (searchKeyword) => {
    const { groupName } = this.props;
    const { getTenantEntity: getTenantDetail } = this.props;
    this.setState({ gridLoading: true });
    const filterData = async () => {
      const searchParam = {
        id: searchKeyword === undefined ? '' : searchKeyword,
        groupName,
      };
      const response = await post(APIENDPOINTS.MULTIPLESETTINGLIST, searchParam);
      const responseData = await response.json();
      const { userVoList } = responseData;
      const { tenantVo } = responseData;
      const { register } = responseData;
      let { deleteDisable } = this.state;
      const showColumns = getColumns(tenantVo);
      if (userVoList.length) {
        const { userVo } = this.props;
        const { userId } = userVo;
        if (userId === userVoList[0].userId) {
          deleteDisable = true;
        } else {
          deleteDisable = false;
        }
        this.setState({ selectedRowValue: userVoList[0], deleteDisable });
      }
      getTenantDetail(tenantVo);
      this.setState({
        countInfoContents: responseData.usersLength,
        userList: userVoList,
        gridLoading: false,
        register,
        showColumns,
      });
    };
    filterData();
  };

  /**
   * render is used to perform render component
   *
   */

  render() {
    const {
      searchParam,
      userList,
      pageLoading,
      gridLoading,
      countInfoContents,
      register,
      openDialog,
      deleteDisable,
      showColumns,
    } = this.state;
    const { tenantVo } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          title="administrator_list"
          description={['enables_to_search_email_address']}
        />
        <MulipleAdminSearch
          reference={this.searchBoxRef}
          onSearchAction={(key, event, values) => this.handleSearchAction(key, event, values)}
          onKeyDown={(event, value) => this.onKeyDown(event, value)}
        />
        <CircularSpinner
          loading={gridLoading}
          css={{ position: 'absolute', top: '60%', left: 'calc(60% - 55px)' }}
        >
          <MulipleAdminData
            searchParam={searchParam}
            userList={userList}
            tenantVo={tenantVo}
            showColumns={showColumns}
            defaultSelected={resultPerPageKey}
            countInfoContents={countInfoContents}
            handleRadioChange={(event) => this.handleRadioChange(event)}
            handlePaginationClick={(tableData) => this.handlePaginationClick(tableData)}
            handleSelectChange={() => this.handleSelectChange()}
          />
          <MultipleAdminFooter
            register={register}
            countInfoContents={countInfoContents}
            userList={userList}
            tenantVo={tenantVo}
            deleteDisable={deleteDisable}
            onHandleClick={(key) => this.onHandleClick(key)}
          />
        </CircularSpinner>
        <SBDialog
          open={openDialog}
          header="error"
          type="error"
          okButtonTitle="ok"
          content="user_already_deleted"
          onOk={() => {
            this.setState({ openDialog: false });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @param state
 *
 */

const mapStateToProps = (state) => {
  const { tenantVo } = state.tenant;
  const { groupName, userVo } = state.user;
  const { data, current } = state.language;
  return {
    userVo,
    tenantVo,
    groupName,
    localeData: data,
    currentLanguage: current,
  };
};

/**
 * mapDispatchToProps is used to dispatch values
 * @param dispatch
 *
 */

const mapDispatchToProps = (dispatch) => {
  return {
    addFilterData: (data) => {
      dispatch(addFilterData(data));
    },
    getTenantEntity: (data) => {
      dispatch(getTenantEntity(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminSetting.propTypes = {
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  userVo: commonPropTypes().userVo.isRequired,
  tenantVo: commonPropTypes().history.isRequired,
  addFilterData: PropTypes.func.isRequired,
  groupName: PropTypes.string,
  currentLanguage: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminSetting.defaultProps = {
  isNotMultiLang: false,
  groupName: '',
  currentLanguage: 'ja',
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(MultipleAdminSetting));
