/**
 * NotificationHeaderConstants is used to hold header values used in Notification screens
 * @author: V.Vijayaragavan
 */
export default function notificationHeaderInformation(key) {
  switch (key) {
    case 'list':
      return {
        title: 'information_from_softbank_list',
        description: ['search_display_announcements'],
      };
    case 'register':
      return {
        title: 'information_from_softbank_registration',
        description: ['information_from_softbank_registration_desc'],
      };
    case 'update':
      return {
        title: 'information_from_softbank_change',
        description: ['information_from_softbank_change_desc'],
      };
    case 'confirm':
      return {
        title: 'information_from_softbank_review',
        description: [],
      };
    case 'registerFinish':
      return {
        title: 'info_softbank_register_finish',
        description: ['info_softbank_register_finish_desc'],
      };
    case 'updateFinish':
      return {
        title: 'info_softbank_update_finish',
        description: ['info_softbank_updated_finish_desc'],
      };
    case 'noticeLookList':
      return {
        title: 'info_softbank_look_list',
        description: [''],
      };
    default:
      return {
        title: '',
        description: [''],
      };
  }
}
