import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { post } from '../../actions/commonActions';
import PLAY_CONSTANTS from '../../constants/GooglePlayConstants';
import APIENDPOINTS from '../../constants/APIEndPoints';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import { serviceMapper } from '../common/GroupSettingCommonFunctions';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import { CommonConstants } from '../../constants/CommonConstants';

/** SBComponents */
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/**
 * ManagedConfiguration is to display list of confirmation
 *
 *  @author Aswin Palanisamy
 */
class ManagedConfiguration extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageLoading: true,
      openErrorDialog: false,
      openSuccessDialog: false,
      message: '',
    };
  }

  /**
   * componentDidMount will perform api operation to fetch, udpate and delete configuration
   */
  async componentDidMount() {
    const { history, location } = this.props;
    const { appMode, packageName, mcmId } = location.state;
    const hostURL = window.location.origin;
    const googleplayURL = hostURL + ROUTEENDPOINTS.MANAGEDCONFIGURATION;
    const thisOperator = this;
    try {
      const response = await post(APIENDPOINTS.WEBTOKEN, { parentURL: googleplayURL });
      const data = await response.json();
      const { value: token } = data;
      let url = `${PLAY_CONSTANTS.MANAGED_CONFIG_ADD}&packageName=${packageName}`;
      if (appMode === 2) {
        url = `${PLAY_CONSTANTS.MANAGED_CONFIG_ADD}&packageName=${packageName}&mcmId=${mcmId}`;
      } else if (appMode === 3) {
        url = `${PLAY_CONSTANTS.MANAGED_CONFIG_ADD}&packageName=${packageName}&mcmId=${mcmId}&canDelete=true`;
      }
      const finalUrl = url.replace('web_token', token);
      this.setState({ pageLoading: false });
      window.gapi.load('gapi.iframes', function googleAPI() {
        const options = {
          url: finalUrl,
          where: document.getElementById('container'),
          attributes: { style: 'height:inherit', scrolling: 'no' },
        };
        const iframe = window.gapi.iframes.getContext().openChild(options);
        iframe.register(
          'onconfigupdated',
          async function prepareApplication(event) {
            const config = event;
            const appConfiguration = {};
            appConfiguration.mcmId = config.mcmId;
            appConfiguration.mcmName = config.name;
            appConfiguration.packageName = packageName;

            try {
              const receiver = await post(APIENDPOINTS.PATCHAPPCONFIG, appConfiguration);
              const result = await receiver.json();
              const { status, message } = result;
              if (status === 'OK') {
                thisOperator.setState({
                  openSuccessDialog: true,
                  message: 'successfully_done',
                });
              } else {
                thisOperator.setState({
                  openErrorDialog: true,
                  message: { message },
                });
              }
            } catch (Exception) {
              history.push('error');
            }
          },
          window.gapi.iframes.CROSS_ORIGIN_IFRAMES_FILTER,
        );
        iframe.register(
          'onconfigdeleted',
          async function prepareApplication(event) {
            const config = event;
            const appConfiguration = {};
            appConfiguration.mcmId = config.mcmId;
            appConfiguration.packageName = packageName;

            try {
              const receiver = await post(APIENDPOINTS.DELETEAPPCONFIG, appConfiguration);
              const result = await receiver.json();
              const { status, message } = result;
              if (status === 'OK') {
                thisOperator.setState({
                  openSuccessDialog: true,
                  message: 'successfully_done',
                });
              } else {
                thisOperator.setState({
                  openErrorDialog: true,
                  message: { message },
                });
              }
            } catch (Exception) {
              history.push('error');
            }
          },
          window.gapi.iframes.CROSS_ORIGIN_IFRAMES_FILTER,
        );
      });
    } catch (Exception) {
      history.push('error');
    }
  }

  render() {
    const { openErrorDialog, openSuccessDialog, pageLoading, message } = this.state;
    const { history, location: getData } = this.props;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div id="container" style={{ height: 'calc(100vh - 165px)' }} />
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              if (this.addButtonRef.current) this.addButtonRef.current.focus();
            });
          }}
          width="400px"
        />
        <SBDialog
          type="success"
          header="success"
          content={message}
          okButtonTitle="ok"
          open={openSuccessDialog}
          onOk={() => {
            this.setState({ openSuccessDialog: false });
            history.push(serviceMapper('applicationList'), {
              ...getData.state,
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

ManagedConfiguration.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
  }).isRequired,
};

export default withRouter(ManagedConfiguration);
