import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';

class SBCheckbox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedValues: this.setSelectedValues(props),
    };
  }

  componentDidUpdate() {
    const { value } = this.props;
    const { selectedValues } = this.state;
    if (value && selectedValues !== value) this.setState({ selectedValues: value });
  }

  setSelectedValues = props => {
    if (props.defaultValue) {
      return props.defaultValue;
    }
    if (props.value) {
      return props.value;
    }
    return [];
  };

  onClick = (event, selectedValue) => {
    const { onChange } = this.props;
    const { selectedValues } = this.state;
    const isChecked = event.target.checked;
    const index = selectedValues.indexOf(selectedValue);
    if (isChecked && index < 0) selectedValues.push(selectedValue);
    else if (!isChecked && index > -1) selectedValues.splice(index, 1);
    this.setState({ selectedValues });
    if (onChange) onChange(event, selectedValues);
  };

  setValue(dataType, data) {
    const { withColon, filterValue } = this.props;
    const label = dataType === 'object' ? data[filterValue] : data;
    const languageChangeLabel = this.languageLabelCheck(label);
    return withColon ? `: ${languageChangeLabel}` : ` ${languageChangeLabel}`;
  }

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const {
      options,
      name,
      id,
      horizontal,
      image,
      defaultValue,
      value,
      filterKey,
      imageSrcKey,
      imageAltKey,
      disabled,
      style,
    } = this.props;
    return (
      <ul className={horizontal ? 'list-float' : 'list-checkbox'} style={style}>
        {options &&
          options.map((data, index) => {
            const dataType = typeof data;
            const dataKey = dataType === 'object' ? data[filterKey] : data;
            const classValue = disabled !== undefined ? 'checkbox mr10 disabled' : 'checkbox mr10';
            return (
              <li key={index.toString()} style={data.styleProperies}>
                <label className={classValue}>
                  <input
                    type="checkbox"
                    name={name}
                    id={id}
                    disabled={disabled}
                    className="checkbox-input"
                    onClick={event => this.onClick(event, dataKey)}
                    {...(value
                      ? { checked: value && value.includes(dataKey) }
                      : { defaultChecked: defaultValue && defaultValue.includes(dataKey) })}
                  />
                  <span className="checkbox-parts">
                    {image && dataType === 'object' && (
                      <img className="icon" src={data[imageSrcKey]} alt={data[imageAltKey]} />
                    )}
                    {this.setValue(dataType, data)}
                  </span>
                </label>
              </li>
            );
          })}
      </ul>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(
  mapStateToProps,
  null,
)(SBCheckbox);
