import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

class RoleResolver extends PureComponent {
  getComponent() {
    const { children, component, columns, hideComponent } = this.props;
    if (this.validateRole()) {
      switch (component) {
        case 'sbtable':
          if (columns[0].dataIndex === 'render-radio') columns.splice(0, 1);
          return React.cloneElement(children, { columns });
        default:
          return hideComponent ? null : React.cloneElement(children, { disabled: true });
      }
    }
    return children;
  }

  validateRole() {
    const { userVo, authorityRoleType } = this.props;
    return authorityRoleType && !authorityRoleType.includes(userVo.authorityId);
  }

  render() {
    return <>{this.getComponent()}</>;
  }
}

const mapStateToProps = state => {
  const { userVo } = state.user;
  return {
    userVo,
  };
};

export default connect(mapStateToProps)(RoleResolver);
