function changeScheduleProgressLabel(scheduleProgress, languageLabelCheck) {
  return languageLabelCheck(
    {
      InProgress: () => 'in_progress',
      NotScheduled: () => 'not_scheduled',
      Completed: () => 'completed',
    }[scheduleProgress](),
  );
}

