/**
 * tableColumnList is to frame device management grid columns
 * @param renderComponent
 * @param screenKey
 * @param languageLabelCheck
 * @return tableColumnList
 */
export const tableColumnList = (renderComponent, screenKey, languageLabelCheck) => {
  const ignoreColumn = [0, 3, 4, 6, 7, 9];
  const deviceList = deviceColumnList(
    renderComponent,
    screenKey === 'deviceList',
    languageLabelCheck,
  );
  return {
    deviceList,
    deviceCsvDetail: deviceCsvColumnList(renderComponent),
    deviceDetail: deviceList.filter((record, index) => ignoreColumn.indexOf(index) < 0),
    app: deviceAppColumnList(renderComponent),
    deploymentPolicy: devicePolicyColumnList(),
    groupSelect: groupList(screenKey),
    groupChange: groupList(screenKey),
    refineGroup: groupList(screenKey),
    operations: deviceOperationColumnList(),
    userList: userColumnList(renderComponent),
    userDeviceSelect: userDeviceColumnList(),
    userCsvList: userColumnList(renderComponent).splice(1, 5),
  }[screenKey];
};

