import React from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import descriptionConverter from '../../utils/descriptionConverter';

const SBTableLayout = props => {
  const { tableData, filterKey, filterValue, rowspan } = props;
  
  const languageLabelCheck = (label) =>{
    const { isNotMultiLang, localeData } = props;
    return (isNotMultiLang) ? label : descriptionConverter(checkLanguage(localeData, label));
  }

  return (
    <table className="table-tate mb10">
      <tbody>
        {tableData &&
          tableData.map((data, index) => {
            return (
              data.visible ? '' : (
                <tr key={index.toString()}>
                  {data[filterKey] && <th rowSpan={data[rowspan]}>{languageLabelCheck(data[filterKey])}</th>}
                  <td>{data[filterValue]}</td>
                </tr>
)
            );
          })}
      </tbody>
    </table>
  );
};

const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps, null)(SBTableLayout);
