/**
 * MultipleAdminDeleteOperation is used to peform delete oepration
 * @author: P.Vinothpandian ,V.Vijayaragavan
 *
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { get, performDelete } from '../../actions/commonActions';
import { tableView } from './MultipleAdminSettingConstants';
import headerInformation from './MultipleAdminHeaderConstants';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import APIENDPOINTS from '../../constants/APIEndPoints';
import ROUTEENDPOINTS from '../../constants/RouteEndPoints';
import CustomTableFrame from '../../v1/common/component/CustomTableFrame';
import MultipleAdminCRUDFooter from './MultipleAdminCRUDFooter';
import SBDialog from '../../common/component/sbdialog/SBDialog';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import MultipleAdminCountHeader from './MultipleAdminCountHeader';
import commonPropTypes from '../../common/commonPropTypes/commonPropTypes';
import { initializeRefs } from './MultipleAdminCommonFunctions';
import getUserEntity from '../../actions/UserActions';
import { getTenantEntity } from '../../actions/TenantActions';

class MultipleAdminDeleteOperation extends Component {
  /**
   * constructor initializes state values
   * @param props
   */

  constructor(props) {
    super(props);
    const { location, history, tenantVo } = props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      headerData: headerInformation('delete'),
      fieldDataList: tableView(tenantVo),
      dataSource: param,
      usersLength: 0,
      pageLoading: true,
      modalIsOpen: false,
      footer: { okDisable: false, primaryVisibile: true },
      errorMessage: '',
      reference: initializeRefs(),
    };
  }

  /**
   * componentDidMount performs api operation to load user length
   */

  componentDidMount() {
    let { usersLength } = this.props;
    const { getUserEntity: getLoggedUser,
            getTenantEntity: getTenantDetail }  = this.props;
    const generateComponent = async () => {
      const response = await get(APIENDPOINTS.USERLENGTH);
      const responseData = await response.text();
      const userDetailResponse = await get(APIENDPOINTS.USERDETAIL);
      const userDetailData = await userDetailResponse.json();
      getLoggedUser(userDetailData.userVo);
      getTenantDetail(userDetailData.tenantVo);
      usersLength = parseInt(responseData, 10);
      this.setState({  fieldDataList: tableView(userDetailData.tenantVo) ,usersLength, pageLoading: false });
    };
    generateComponent();
  }

  /**
   * onOkDialog perform save event on clicking ok button in dialog
   */

  onOkDialog = () => {
    const { location } = this.props;
    const { param } = location.state;
    const { footer, reference } = this.state;
    this.setState({ pageLoading: true });
    const performSave = async () => {
      const response = await performDelete(`${APIENDPOINTS.DELETESETTING}/${param.userId}`);
      const responseData = await response.json();
      if (responseData.status) {
        footer.primaryVisibile = false;
        this.setState({
          headerData: headerInformation('deletedSuccessfully'),
          usersLength: responseData.usersLength,
          modalIsOpen: false,
          footer,
          pageLoading: false,
          dataSource: responseData.userVo,
        });
      } else {
        footer.okDisable = true;
        this.setState({
          modalIsOpen: false,
          errorMessage: responseData.message,
          pageLoading: false,
          footer,
        });
        if (reference.cancelRef.current) reference.cancelRef.current.focus();
      }
    };
    performSave();
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * addCountInfo is used to load label content with count information
   * @param label
   * @param contents
   * @return labelcontent
   *
   */

  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * onHandleClick is used to handle click operation
   * @param key
   */

  onHandleClick = key => {
    const { history } = this.props;
    switch (key) {
      case 'save':
        this.setState({ modalIsOpen: true });
        break;
      case 'cancel':
        history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING);
        break;
      case 'back':
        history.push(ROUTEENDPOINTS.MULTIPLEADMINSETTING);
        break;
      default:
        break;
    }
  };

  /**
   * render is used to render the entire delete screen
   * @return component
   */

  render() {
    const {
      usersLength,
      headerData,
      fieldDataList,
      dataSource,
      modalIsOpen,
      footer,
      pageLoading,
      errorMessage,
      reference,
    } = this.state;
    const { tenantVo } = this.props;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <MultipleAdminCountHeader usersLength={usersLength} maxUsercount={tenantVo.userCount} />
            <div>
              <CustomTableFrame
                tableList={fieldDataList}
                dataSource={dataSource}
                defaultValue={{}}
                onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              />
            </div>
            <div>
              <MultipleAdminCRUDFooter
                footer={footer}
                onHandleClick={key => this.onHandleClick(key)}
                reference={reference}
              />
            </div>
          </div>
          <span className="red bold">{this.languageLabelCheck(errorMessage)}</span>
          <SBDialog
            open={modalIsOpen}
            header="delete_confirmation"
            type="confirmation"
            content="confirm_delete_data"
            onCancel={() => {
              this.setState({ modalIsOpen: false });
            }}
            onOk={() => {
              this.onOkDialog();
            }}
            width="400px"
          />
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is map the state value to props
 * @return state
 *
 */

const mapStateToProps = state => {
  const { tenantVo } = state.tenant;
  return {
    tenantVo,
    localeData: state.language.data,
  };
};


/**
 * mapDispatchToProps is used to dispatch the value to props
 */

const mapDispatchToProps = dispatch => {
  return {
    getUserEntity: data => {
      dispatch(getUserEntity(data));
    },
    getTenantEntity: (data) => {
      dispatch(getTenantEntity(data));
    },
  };
}

/**
 * propTypes is used for validation props with its type.
 */
MultipleAdminDeleteOperation.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: commonPropTypes().userVo.isRequired,
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  tenantVo: commonPropTypes().tenantVo.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
MultipleAdminDeleteOperation.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(MultipleAdminDeleteOperation));
