import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBTableLayout from '../../../../common/component/tableLayout/SBTableLayout';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import SBTextbox from '../../../../common/component/sbtextbox/SBTextbox';
import SBTable from '../../../../common/component/sbtable/SBTable';
import LABELCONSTANTS from '../../../../constants/LabelConstants';
import OPERATIONMANAGEMENTCONSTANTS from '../../../OperationManagementConstants';

function onDateTextInputChange() {}
function onKeywordTextInputChange() {}
const virusDetectionLogTableData = [
  {
    key: 'Term',
    value: [
      <SBTextbox
        id="search_from_date_text_input"
        placeholder="yyyy/mm/dd hh:mm"
        type="text"
        name="search_from_date"
        size="20"
        maxlength="16"
        style={{ marginRight: 10 }}
        onChange={() => onDateTextInputChange()}
      />,
      LABELCONSTANTS.TILDE,
      <SBTextbox
        id="search_to_date_text_input"
        placeholder="yyyy/mm/dd hh:mm"
        type="text"
        name="search_to_date"
        size="20"
        maxlength="16"
        style={{ marginLeft: 10 }}
        onChange={() => onDateTextInputChange()}
      />,
    ],
  },
  {
    key: 'Key Word',
    value: (
      <SBTextbox
        id="search-text-input"
        type="text"
        name="search_text"
        size="20"
        maxlength="20"
        style={{ width: 264 }}
        onChange={() => onKeywordTextInputChange()}
      />
    ),
  },
];

const resultsPerPageKey = 'virus_detection_log';
class VirusDetectionLog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [],
      datasource: [],
    };
  }

  onSearchButtonClick = () => {};

  render() {
    const { screenTitleData } = this.props;
    const { datasource } = this.state;
    const { columns } = this.state;
    return (
      <div id="contents" className="clearfix" style={{ height: '97vh' }}>
        <div style={{ width: '100%', 'padding-bottom': '150px' }}>
          <div id="main">
            <div className="container">
              <SBContentHeader
                title={screenTitleData.title}
                description={screenTitleData.description}
              />
              <div className="contents-main">
                <div className="border p15 mb15">
                  <SBTableLayout
                    tableData={virusDetectionLogTableData}
                    filterKey="key"
                    filterValue="value"
                  />
                  <div className="clearfix">
                    <div className="floatright">
                      <SBButton
                        type="submit"
                        name="search"
                        color="green"
                        title="Search"
                        noMarginRight
                        onClick={() => {
                          this.onSearchButtonClick();
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="contents-main w100per">
                <div className="clearfix mb10">
                  <SBTable
                    columns={columns}
                    dataSource={datasource}
                    loading={false}
                    pagination
                    countInfo={`Search result：${datasource.length} items`}
                    filterList={OPERATIONMANAGEMENTCONSTANTS.securityPolicyGridComboItemsValue}
                    handleRadioChange={data => this.handleRadioChange(data)}
                    defaultSelected={resultsPerPageKey}
                  />
                </div>
                <div className="clearfix">
                  <div className="floatleft mb10">No results found.</div>
                </div>
                <div className="clearfix mb10">
                  <div className="floatleft">
                    <SBButton
                      type="button"
                      name="viresusDetectionLogOutput"
                      color="gray"
                      title="Virus Detection Log Output"
                      disabled
                      onClick={() => this.onSearchButtonClick()}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(VirusDetectionLog);
