/**
 * AppGISUN is used to load GISUN Page
 * @author: P.Vinothpandian
 *
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import SBDialog from '../../../common/component/sbdialog/SBDialog';
import UNDERSCORE from '../../../v1/common/utils';
import { setAppRegisterEntity, setAppUpdateEntity } from '../../../actions/TenantActions';
import {
  isNotUndefinedOrNotEmpty,
  isAlphaOrNumeric,
} from '../../../common/CommonValidations/CommonValidations';
import checkLanguage from '../../../language/checkLanguage';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import { tableDMList, tableGISUNList } from '../Constants/AppFrameConstants';
import { errorMessageConstants } from '../Constants/AppErrorConstants';
import { setGisunReference } from '../Constants/AppReferenceConstants';
import { filterData } from '../Constants/AppFieldConstants';
import headerInformation from '../Constants/AppManageHeaderConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import AppCRUDFooter from './AppCRUDFooter';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';

class CampaignRegistration extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      headerData: headerInformation('gisun'),
      fieldDataList: [],
      dynamicData: {},
      reference: setGisunReference(),
      modified: false,
      footer: { okDisable: false, primaryVisibile: true },
    };
  }

  /**
   * componentDidMount is used to set initial focus
   */

  componentDidMount() {
    const { reference } = this.state;
    if (reference.textGisunRef.current) reference.textGisunRef.current.focus();
  }

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    if (key === 'addgisun') {
      this.createList();
    } else if (key === 'del_gisun') {
      this.removeFromList();
    } else {
      const { gisunSource } = this.state;
      gisunSource[key] = event.target.type === 'text' ? value : event.target.value;
      this.setState({ gisunSource, modified: true });
    }
  };

  /**
   * onKeyDown is used to handle key down operation in component
   * @param event
   *
   */

  onKeyDown = event => {
    const { footer } = this.state;
    const { okDisable } = footer;
    if (event.keyCode === 13 && !okDisable) {
      event.preventDefault();
      this.createList();
    }
  };

  /**
   * setValidationErrorMessage is used to set error validation message
   *
   * @param key
   * @param validationResultObject
   *
   */

  setValidationErrorMessage(key, validationResultObject) {
    let { dynamicData } = this.state;
    const { reference } = this.state;
    dynamicData = {};
    dynamicData[key] = errorMessageConstants[key][validationResultObject.error];
    this.setState({ dynamicData });
    if (dynamicData.gisunOrderNumberError && dynamicData.gisunOrderNumberError !== '') {
      reference.textGisunRef.current.focus();
    }
  }

  /**
   * onHandleClick is used to handle click event
   * @param key
   */

  onHandleClick = key => {
    switch (key) {
      case 'save':
      case 'cancel':
        this.callService(key);
        break;
      default:
        break;
    }
  };

  /**
   * callService is used to call the service based on the key
   * @param key
   */

  callService = key => {
    const {
      location,
      history,
      setModified,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    const { gisunSource, modified } = this.state;
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    if (key === 'save') {
      param.gisun = gisunSource.gisun;
      if (location.state.isUpdate) {
        makeAppUpdateEntity({
          param,
          globalModified: modified || location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
      } else {
        makeAppRegisterEntity({
          param,
          autoGenertedDmCode: location.state.autoGenertedDmCode,
          globalModified: modified || location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
      }
    } else if (modified) {
      this.setState({ modalIsOpen: true });
    } else {
      setModified(location.state.globalModified);
      if (location.state.isUpdate) {
        makeAppUpdateEntity({
          param,
          globalModified: location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
      } else {
        makeAppRegisterEntity({
          param,
          autoGenertedDmCode: location.state.autoGenertedDmCode,
          globalModified: location.state.globalModified,
        });
        history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
      }
    }
  };

  /**
   * onOkDialog is used to handle event on ok click of dialog
   */

  onOkDialog = () => {
    const {
      location,
      history,
      setAppRegisterEntity: makeAppRegisterEntity,
      setAppUpdateEntity: makeAppUpdateEntity,
    } = this.props;
    let param = {};
    if (location.state) {
      param = location.state.param;
    }
    if (location.state.isUpdate) {
      makeAppUpdateEntity({
        param,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPUPDATE, { dmCode: param.dmCode });
    } else {
      makeAppRegisterEntity({
        param,
        autoGenertedDmCode: location.state.autoGenertedDmCode,
        globalModified: location.state.globalModified,
      });
      history.push(ROUTEENDPOINTS.APPLICATIONREGISTER);
    }
  };

  /**
   * removeFromList is used to remove data from gisun list
   */

  removeFromList = () => {
    const { reference, gisunSource } = this.state;
    const key = 'gisunError';
    let { dynamicData } = this.state;
    dynamicData = {};
    if (
      gisunSource &&
      (!UNDERSCORE.isUndefined(gisunSource.selectgisun) ||
        !UNDERSCORE.isEmpty(gisunSource.selectgisun))
    ) {
      dynamicData = {};
      gisunSource.gisun = UNDERSCORE.without(gisunSource.gisun, gisunSource.selectgisun);
      gisunSource.selectgisun =
        gisunSource && gisunSource.gisun && gisunSource.gisun.length ? gisunSource.gisun[0] : '';
      this.setState({
        dynamicData,
        gisunSource,
        modified: true,
        fieldDataList: tableGISUNList(dynamicData, reference),
      });
      if (gisunSource.gisun.length === 0 && reference.textGisunRef.current)
        reference.textGisunRef.current.focus();
    } else {
      dynamicData = { gisunError: errorMessageConstants[key].selectData };
      this.setState({ dynamicData, fieldDataList: tableGISUNList(dynamicData, reference) });
    }
  };

  /**
   * validateGISUN is used to validate GISUN order number
   * @param gisun
   * @param gisunList
   * @return result
   */

  validateGISUN = (gisun, gisunList) => {
    const minLength = 16;
    const maxLength = 16;
    const listLength = 1000;
    const result = { status: true, error: '' };
    if (gisunList.length < listLength) {
      if (isNotUndefinedOrNotEmpty(gisun)) {
        const gisunLength = gisun.length;
        if (gisunLength >= minLength && gisunLength <= maxLength) {
          if (isAlphaOrNumeric(gisun)) {
            if (UNDERSCORE.contains(gisunList, gisun)) {
              result.status = false;
              result.error = 'samecontent';
            } else {
              result.status = true;
            }
          } else {
            result.status = false;
            result.error = 'alphanumeric';
          }
        } else {
          result.status = false;
          result.error = 'length';
        }
      } else {
        result.status = false;
        result.error = 'empty';
      }
    } else {
      result.status = false;
      result.error = 'listlength';
    }
    return result;
  };

  /**
   * performValidations is used to perfrom validations
   * @return status
   */

  performValidations = () => {
    const { gisunSource } = this.state;
    const gisunValidation = this.validateGISUN(gisunSource.gisunOrderNumber, gisunSource.gisun);
    this.setValidationErrorMessage('gisunOrderNumberError', gisunValidation);
    return gisunValidation.status;
  };

  /**
   * createList is used to create GISUN list
   */

  createList = () => {
    let { gisunSource } = this.state;
    const { gisun: gisunList } = gisunSource;
    const { reference } = this.state;
    const inputStatus = this.performValidations();
    if (inputStatus) {
      gisunList.push(gisunSource.gisunOrderNumber);
      gisunSource = {
        gisunOrderNumber: '',
        gisun: gisunList,
        selectgisun: gisunSource.gisunOrderNumber,
      };
      this.setState({
        dynamicData: {},
        gisunSource,
        fieldDataList: tableGISUNList({}, reference),
      });
    } else {
      this.setState({ fieldDataList: [] });
    }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * generateComboValues is used to load the combo values
   *
   */

  generateComboValues = () => {
    const { gisunSource } = this.state;
    const { gisun } = gisunSource;
    filterData.gisun.splice(0, filterData.gisun.length);
    gisun.map(data => filterData.gisun.push({ value: data, option: data }));
  };

  /**
   * render is used to render the component
   * @return AppGISUNComponent
   */

  render() {
    const {
      headerData,
      fieldDataList,
      dmSource,
      gisunSource,
      modalIsOpen,
      footer,
      dynamicData,
      reference,
    } = this.state;
    this.generateComboValues();
    return (
      <div>
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <div className="contents-main">
          <div className="clearfix mb10">
            <div className="mb15">
              <CustomTableFrame tableList={tableDMList} dataSource={dmSource} defaultValue={{}} />
            </div>
            <CustomTableFrame
              tableList={
                fieldDataList.length ? fieldDataList : tableGISUNList(dynamicData, reference)
              }
              dataSource={gisunSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <div>
              <AppCRUDFooter
                reference={reference}
                footer={footer}
                onHandleClick={key => this.onHandleClick(key)}
              />
            </div>
            <SBDialog
              open={modalIsOpen}
              header="cancel_confirmation"
              type="confirmation"
              content="cancel_confirmation_message"
              onCancel={() => {
                this.setState({ modalIsOpen: false });
              }}
              onOk={() => {
                this.onOkDialog();
              }}
              width="400px"
            />
            <div />
          </div>
        </div>
      </div>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */

const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    setAppRegisterEntity: data => {
      dispatch(setAppRegisterEntity(data));
    },
    setAppUpdateEntity: data => {
      dispatch(setAppUpdateEntity(data));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */

CampaignRegistration.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      param: PropTypes.objectOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.bool,
          PropTypes.number,
          PropTypes.null,
          PropTypes.any,
        ]),
      ),
      globalModified: PropTypes.bool.isRequired,
      autoGenertedDmCode: PropTypes.string.isRequired,
    }),
  }).isRequired,
  setAppRegisterEntity: PropTypes.func.isRequired,
  setModified: commonPropTypes().setModified.isRequired,
  localeData: commonPropTypes().localeData.isRequired,
  isNotMultiLang: PropTypes.bool,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */

CampaignRegistration.defaultProps = {
  isNotMultiLang: false,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(CampaignRegistration));
