/**
 * tableSpecialNoteView constant is used to view the special note information of saved data
 */

export function tableSpecialNoteView(range) {
  const specialNote = [];
  [...Array(range)].map((data, index) =>
    specialNote.push({
      label: `special_notes_${index + 1}`,
      component: 'span',
      key: `operationMemo${indexMap[index + 1]}`,
      style: { whiteSpace: 'break-spaces' },
      trStyle: value => (value.existenceSpecialNotes ? {} : { display: 'none' }),
    }),
  );
  return specialNote;
}

