/**
 * SecurityPolicyDetail is used to load the data
 * @author: Loganathan R P
 */
import React, { createRef, Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBButton from '../../../../common/component/sbbutton/SBButton';
import headerInformation from '../constants/HeaderConstant';
import { get } from '../../../../actions/commonActions';
import APIENDPOINTS from '../../../../constants/APIEndPoints';
import CircularSpinner from '../../../../common/component/loadingSpinner/CircularSpinner';
import CustomTableFrame from '../../../../v1/common/component/CustomTableFrame';
import { detailView } from '../constants/TableConstant';
import RouteEndPoints from '../../../../constants/RouteEndPoints';

class SecurityPolicyDetail extends Component {
  constructor(props) {
    super(props);
    const { location, history } = props;
    let param = {};
    if (location.state) {
      param = location.state;
    } else {
      history.push('error');
    }
    this.state = {
      pageLoading: true,
      dataSource: param,
      securityPolicyDetail: {},
      ref: { backBtnRef: createRef() },
    };
  }

  componentDidMount() {
    const { dataSource, ref } = this.state;
    const getSecurityPolicyDetail = async () => {
      const response = await get(
        `${APIENDPOINTS.SECURITY_POLICY_DETAIL}/${dataSource.securityPolicyId}`,
      );
      const responseData = await response.json();
      responseData.assignTarget =
        responseData.groupList && responseData.groupList.length
          ? responseData.groupList.join(',')
          : '';

      this.setState(
        {
          securityPolicyDetail: responseData,
          pageLoading: false,
          ref,
        },
        () => {
          if (ref.backBtnRef.current) ref.backBtnRef.current.focus();
        },
      );
    };
    getSecurityPolicyDetail();
  }

  onHandleClick = () => {
    const { history } = this.props;
    history.push(RouteEndPoints.SECURITYPOLICY, { isSearch: true });
  };

  render() {
    const { pageLoading, securityPolicyDetail, ref } = this.state;
    const { isCheck, localeData } = this.props;
    const headerKey = isCheck ? 'security_policy_register_header' : 'securityPolicyDetail';
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          hasTitleDescription
          title={headerInformation(headerKey).title}
          description={headerInformation(headerKey).description}
        />
        <div className="contents-main">
          <CustomTableFrame
            tableList={detailView().securityPolicy}
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />
          {!isCheck && (
            <CustomTableFrame
              tableList={detailView().createdPolicyAccount}
              dataSource={securityPolicyDetail}
              defaultValue={{}}
            />
          )}
          <CustomTableFrame
            tableList={detailView().androidOS}
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />

          <CustomTableFrame
            tableList={
              isCheck ? detailView(localeData).applicationNo : detailView(localeData).application
            }
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />

          <CustomTableFrame
            tableList={isCheck ? detailView(localeData).imeiNo : detailView().imei}
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />
          <CustomTableFrame
            tableList={detailView().antiVirus}
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />
          <CustomTableFrame
            tableList={detailView().assignTarget}
            dataSource={securityPolicyDetail}
            defaultValue={{}}
          />
          <div className="clearfix">
            <div className="floatright">
              <SBButton
                id="btn_back"
                noMarginRight
                type="button"
                title="back"
                ref={ref.backBtnRef}
                style={{ display: 'block' }}
                onClick={event => this.onHandleClick(event)}
              />
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}
/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */

const mapStateToProps = state => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

export default connect(mapStateToProps)(withRouter(SecurityPolicyDetail));
