/**
 * DeviceActions is used fetch the device data.
 * @author Aswin Palanisamy
 */
import {
  FETCH_GROUP_NAME_LIST,
  FETCH_USER_GROUP_NAME_LIST,
  RESET_USER_INFORMATION,
  FETCH_DEPARTMENT,
  FETCH_DEVICE_FILTER,
  FETCH_USER_INFORMATION,
  FETCH_USER_SEARCH_DATA,
} from './ActionTypes';

/**
 * getGroupNameList is used fetch the selected list of groupnames from the user.
 * @param data
 */
export function getGroupNameList(data) {
  return {
    type: FETCH_GROUP_NAME_LIST,
    data,
  };
}

/**
 * getUserGroupNameList is used fetch the user information.
 * @param data
 */
export function getUserInformation(data) {
  return {
    type: FETCH_USER_INFORMATION,
    data,
  };
}

/**
 * resetUserInformation is used reset the user information.
 * @param data
 */
export function resetUserInformation() {
  return {
    type: RESET_USER_INFORMATION,
  };
}

/**
 * getUserGroupNameList is used fetch the selected list of groupnames from the user.
 * @param data
 */
export function getUserGroupNameList(data) {
  return {
    type: FETCH_USER_GROUP_NAME_LIST,
    data,
  };
}

/**
 * getUserSearchData is used fetch the search data for user list
 * @param data
 */
export function getUserSearchData(userStatus, userSearchText) {
  return {
    type: FETCH_USER_SEARCH_DATA,
    userStatus,
    userSearchText,
  };
}

/**
 * getDepartment is used fetch the department name from the user.
 * @param data
 */
export function getDepartment(data) {
  return {
    type: FETCH_DEPARTMENT,
    data,
  };
}

/**
 * getDeviceFilter is used fetch the device filter condition name from the user.
 * @param data
 */
export function getDeviceFilter(data) {
  return {
    type: FETCH_DEVICE_FILTER,
    data,
  };
}
