/**
 * NotificationSetting is used to display the Email Notification Setting screen
 * @author: P.Vinothpandian, A.Aakash
 *
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import SBTableHeader from '../../../common/component/tableheader/SBTableHeader';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import headerInformation from '../constants/HeaderConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import { emptyDataSource, setTableView } from '../constants/FieldContants';
import setRegisterReference from '../constants/ReferenceConstants';
import CRUDFooter from '../constants/CRUDFooter';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIEndPoints from '../../../constants/APIEndPoints';
import { get, put } from '../../../actions/commonActions';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import { CommonConstants } from '../../../constants/CommonConstants';
import { Role } from '../../../constants/Role';
import checkLanguage from '../../../language/checkLanguage';

class NotificationSetting extends Component {
  constructor(props) {
    super(props);
    const { userVo } = props;
    const { authorityId } = userVo;
    const isAuthority = Role.emailNotificationAuthority.includes(authorityId);
    this.state = {
      loading: true,
      headerInfo: headerInformation('notifysetting'),
      tableView: setTableView('view', {}, setRegisterReference()),
      dataSource: emptyDataSource(),
      reference: setRegisterReference(),
      footer: { isChangeDisabled: !isAuthority, okDisable: true, primaryVisibile: false },
    };
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const generateComponent = async () => {
      await this.getEmailSettings();
    };
    generateComponent();
  }

  /**
   * getEmailSettings is used to get Email settings
   */
  getEmailSettings = async () => {
    const { history } = this.props;
    this.setState({ loading: true });
    const response = await get(`${APIEndPoints.GET_EMAIL_SETTING}`);
    const responseData = await response.json();
    if (responseData.status) {
      if (responseData.emailNotificationSettingVo.groupAdmin) {
        this.setState({ footer: { isChangeDisabled: true } });
      }
      this.setState({ loading: false, dataSource: this.makeDataSource(responseData) });
      this.onModeChange('view', false);
    } else {
      this.setState({ loading: false });
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * makeDataSource is used to make data source from responseData
   */
  makeDataSource = (responseData) => {
    const { emailNotificationSettingVo } = responseData
    return {
      mailNotifyDeviceWaiting: emailNotificationSettingVo.mailNotifyDeviceWaiting,
      mailNotifyDeviceWaitingDays: emailNotificationSettingVo.mailNotifyDeviceWaitingDays,
      mailNotifyPolicyViolation: emailNotificationSettingVo.mailNotifyPolicyViolation,
      mailNotifyPolicyViolationGroup: emailNotificationSettingVo.mailNotifyPolicyViolationGroup,
      mailNotifyAppRelatedSetting: emailNotificationSettingVo.mailNotifyAppRelatedSetting,
    };
  }

  /**
   * saveEmailSettings is used to save email settings
   */
  saveEmailSettings = async () => {
    const { setModified, history } = this.props;
    const { dataSource } = this.state;
    this.setState({ loading: true });
    const response = await put(APIEndPoints.SAVE_EMAIL_SETTING, dataSource);
    const responseData = await response.json();
    setModified(false);
    this.setState({ loading: false });
    if (responseData.status) {
      this.onModeChange('view', false);
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * onModeChange is called when the change operation is performed
   */
  onModeChange = (mode, primaryVisibile) => {
    const { reference, footer, dataSource } = this.state;
    let { headerInfo } = this.state;
    footer.primaryVisibile = primaryVisibile;
    footer.okDisable = mode === 'update';
    if (primaryVisibile) {
      headerInfo = headerInformation('changenotifysetting');
    } else {
      headerInfo = headerInformation('notifysetting');
    }
    this.setState({ headerInfo, tableView: setTableView(mode, {}, reference, dataSource), footer });
  };

  /**
  * onHandleClick is called when button is click is performed
  */
  onHandleClick = (key) => {
    const { setModified } = this.props;
    switch (key) {
      case 'change':
        this.onModeChange('update', true);
        break;
      case 'save':
        this.saveEmailSettings();
        break;
      case 'cancel':
        setModified(false);
        this.getEmailSettings();
        this.onModeChange('view', false);
        break;
      default:
        break;
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param value
   *
   */
  onTableFrameEvent = (key, value) => {
    const { dataSource, footer } = this.state;
    dataSource[key] = value;
    footer.okDisable = false;
    this.setState({ dataSource, footer });
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  render() {
    const { loading, headerInfo, dataSource, tableView, reference, footer } = this.state;
    return (
      <CircularSpinner loading={loading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <>
          <SBContentHeader title={headerInfo.title} description={headerInfo.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <SBTableHeader headerText="basic_setting" />
                <CustomTableFrame
                  tableList={tableView.deviceList}
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, value)}
                // onKeyDown={(event, value) => this.onKeyDown(event, value)}
                />
              </div>
              <div>
                <SBTableHeader headerText="notification_security_policy_violation" />
                <CustomTableFrame
                  tableList={tableView.policyList}
                  dataSource={dataSource}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, value)}
                />
              </div>
              <div>
                <CRUDFooter
                  reference={reference}
                  footer={footer}
                  onHandleClick={(key) => this.onHandleClick(key)}
                />
              </div>
            </div>
          </div>
        </>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { userVo } = state.user;
  const { tenantVo } = state.tenant;
  const { data: localeData, current: currentLanguage } = state.language;
  const { isModified } = state.screenModified;
  return {
    tenantVo,
    userVo,
    localeData,
    currentLanguage,
    isModified,
  };
};

/**
* mapDispatchToProps is used to dispatch the value to props
* @param dispatch
*/
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * defaultProps is used to initialise non required props initial values
 */
NotificationSetting.defaultProps = {
  localeData: {},
  isNotMultiLang: false,
};

/**
 * propTypes is used for validation props with its type.
 */
NotificationSetting.propTypes = {
  isNotMultiLang: PropTypes.bool,
  history: commonPropTypes().history.isRequired,
  setModified: PropTypes.func.isRequired,
  localeData: PropTypes.objectOf(PropTypes.string),
};

export default connect(mapStateToProps, mapDispatchToProps)(NotificationSetting);
