export const authorityValues = {
  1: 'system_administrator',
  3: 'operator',
  4: 'system_administrator_isoc',
  5: 'system_administrator_sbtm',
};

export const authorityValuesUpdate = {
  1: 'system_administrator',
  3: 'operator',
  5: 'system_administrator_sbtm',
};

export const userAuthority = [4, 6, 7, 8, 9];

function mapToFilterData(map) {
  const filterData = [];
  Object.keys(map).forEach(function(mapKey) {
    const tempMap = { value: mapKey, option: map[mapKey] };
    filterData.push(tempMap);
  });
  return filterData;
}

export const filterData = {
  authority: mapToFilterData(authorityValues),
  authorityUpdate: mapToFilterData(authorityValuesUpdate),
  report: [{ filterKey: 1, filterValue: 'display' }, { filterKey: 0, filterValue: 'hide' }],
};

