export function emptyDataSource(userVo) {
  return {
    dmCode: userVo.masterCode,
    loginId: '',
    password: '',
    confirmpass: '',
    authorityId: '1',
    ipAddress: '',
  };
}

export function setSearchReference() {
  return {
    dmCodeRef: createRef(),
    loginIdRef: createRef(),
  };
}

export function setReference() {
  return {
    dmCodeRef: createRef(),
    loginIdRef: createRef(),
    passwordRef: createRef(),
    confPassRef: createRef(),
    authorityRef: createRef(),
    ipAddressRef: createRef(),
    okRef: createRef(),
    cancelRef: createRef(),
    backRef: createRef(),
  };
}

/**
 * loginIdRegex is used to validate loginId regex
 * @param value
 * @return boolean
 */

export function loginIdRegex(loginId, regex) {
  return regex.test(loginId);
}
