/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';

/** SBComponents */
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBDialog from '../../common/component/sbdialog/SBDialog';

/** Device Components */
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import deviceHeader from '../constants/deviceHeaderConstants';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import { serviceMapper } from '../common/deviceCommonFunctions';
import RouteEndPoints from '../../constants/RouteEndPoints';
import { getUserGroupNameList } from '../../actions/DeviceAction';

/**
 * UserCsvComplete is to show bulk delete completion report
 * @author Srinivasan Ravi
 */
class UserCsvComplete extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { location } = props;
    let { screenKey } = props;
    const { deletedCount, alreadyDeletedCount } = location.state;
    if (deletedCount !== 0 && !isEmpty(alreadyDeletedCount)) {
      screenKey = 'userCsvCompleteWithTwoStatus';
    } else if (deletedCount === 0 && !isEmpty(alreadyDeletedCount)) {
      screenKey = 'userCsvCompletewithAlreadyDeleted';
    } else if (deletedCount !== 0 && isEmpty(alreadyDeletedCount)) {
      screenKey = 'userCsvCompletewithDeleted';
    }
    this.state = {
      deletedCount,
      screenKey,
      alreadyDeletedCount,
      pageLoading: false,
      headerData: deviceHeader(screenKey),
    };
    this.backButtonRef = createRef();
  }

  /**
   * componentDidMount render bulk delete complete screen
   */
  componentDidMount() {
    const { history, location } = this.props;
    if (!location.state) history.push(RouteEndPoints.ERROR);
    else {
      this.setState({ pageLoading: false }, () => {
        if (this.backButtonRef.current) this.backButtonRef.current.focus();
      });
    }
  }

  /**
   * getBacktoPreviousScreen navigates to bulk user delete screen
   */
  getBacktoPreviousScreen = () => {
    const { history, makeUserGroupList } = this.props;
    makeUserGroupList();
    history.push(serviceMapper('userList'));
  };

  /**
   * onHandleClick performs button click events in the screen
   * @param screenKey
   * @return buttonAction
   */
  onHandleClick = (screenKey) =>
    ({
      back: () => this.getBacktoPreviousScreen(),
    }[screenKey]());

  /**
   * addCountInfo is to set grid data count in screen header label
   * @param label
   * @param  contents
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * addTwoStatusInfo is to set deleted & already deleted count in screen header label
   * @param label
   * @param  contents
   */
  addTwoStatusInfo = (label, contentOne, contentTwo) => {
    return addDynamicContent(this.languageLabelCheck(label), contentOne, contentTwo);
  };

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * getDescriptionContent used to get description content based on deleted count & already deleted count
   */
  getDescriptionContent = () => {
    const { screenKey, headerData, deletedCount, alreadyDeletedCount } = this.state;
    switch (screenKey) {
      case 'userCsvCompleteWithTwoStatus':
        return this.addTwoStatusInfo(headerData.description, deletedCount, alreadyDeletedCount);
      case 'userCsvCompletewithAlreadyDeleted':
        return this.addCountInfo(headerData.description, alreadyDeletedCount);
      default:
        return this.addCountInfo(headerData.description, deletedCount);
    }
  };

  /**
   * render perform user bulk delete finish screen rendering
   * @return UsercsvComplete
   */
  render() {
    const { headerData, pageLoading, openDialogError } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader
          isNotMultiLang
          title={this.languageLabelCheck(headerData.title)}
          description={[this.getDescriptionContent()]}
        />
        <div className="contents-main">
          <DeviceFooter
            onHandleClick={(key) => this.onHandleClick(key)}
            footer={{
              screenKey: 'userConfirmation',
              reference: {
                backButtonRef: this.backButtonRef,
              },
            }}
          />
        </div>
        <SBDialog
          type="error"
          header="error"
          content="data_does_not_exists"
          okButtonTitle="ok"
          open={openDialogError}
          onOk={() => {
            this.setState({ openDialogError: false }, this.getBacktoPreviousScreen());
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps perform data linking from the redux store
 * @param state
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  return { localeData: data };
};

/**
 * mapDispatchToProps performs data setup in device/group redux state
 * @param dispatch
 * @return reduxAction
 */
const mapDispatchToProps = (dispatch) => {
  return {
    makeUserGroupList: (data) => {
      dispatch(getUserGroupNameList(data));
    },
  };
};

/**
 * propTypes is used for validation props with its type.
 */
UserCsvComplete.propTypes = {
  isNotMultiLang: PropTypes.bool.isRequired,
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      deletedCount: PropTypes.number,
      alreadyDeletedCount: PropTypes.string,
    }),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
  makeUserGroupList: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(UserCsvComplete));
