/**
 * SecurityPolicyActions is used to map the response data in redux
 * @author P.Vinothpandian
 */

import {
  FETCH_SECURITY_GROUP_LIST,
  SECURITY_POLICY_SEARCH_PARAM,
  SECURITY_POLICY_DETAIL,
} from '../../../actions/ActionTypes';

const initialState = {
  securityData: {},
};

function securityPolicy(state = initialState, action) {
  const { type, data } = action;
  switch (type) {
    case FETCH_SECURITY_GROUP_LIST:
      return {
        ...state,
        securityData: data || {},
      };
    case SECURITY_POLICY_SEARCH_PARAM:
      return {
        ...state,
        securityPolicySearch: data || '',
      };
    case SECURITY_POLICY_DETAIL:
      return {
        ...state,
        securityPolicyDetail: data || {},
      };
    default:
      return state;
  }
}

export default securityPolicy;
