import React from 'react';
import { connect } from 'react-redux';
import commonChangeAction from '../../changeReducer/commonChangeAction';

class SBTextArea extends React.Component {
  changeModified = () => {
    const { setModified, canModify } = this.props;
    if (canModify) {
      setModified(true);
    }
  };

  onChange = (event) => {
    const { onChange } = this.props;
    this.changeModified();
    if (onChange) {
      onChange(event);
    }
  };

  render() {
    const {
      id,
      name,
      disabled,
      rows,
      style,
      placeholder,
      defaultValue,
      value,
      forwadedRef,
      cols,
      classes,
    } = this.props;

    return (
      <textarea
        id={id}
        rows={rows}
        className={classes}
        name={name}
        cols={cols}
        placeholder={placeholder}
        disabled={disabled}
        ref={forwadedRef}
        onChange={(event) => this.onChange(event)}
        style={style}
        {...(defaultValue ? { defaultValue } : { value })}
      />
    );
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(null, mapDispatchToProps)(SBTextArea);
