/** userReducer is used to map the repose data for the group to the redux.
 * @author P.Vinothpandian
 */
import { FETCH_USER_ENTITY, FETCH_GROUP_FILTER } from '../../../../actions/ActionTypes';

const initialState = {
  userVo: {},
  groupName: '',
};

function user(state = initialState, action) {
  const { type, data } = action;
  switch (type) {
    case FETCH_USER_ENTITY:
      return {
        ...state,
        userVo: data || {},
      };
    case FETCH_GROUP_FILTER:
      return {
        ...state,
        groupName: data || '',
      };
    default:
      return state;
  }
}

export default user;
