/**
 * HistoryInquiry is used to Show Inquiry of  History
 * @author: Rajeshkumar.R
 */

import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBTable from '../../../common/component/sbtable/SBTable';
import { CommonConstants } from '../../../constants/CommonConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import { Role } from '../../../constants/Role';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import NoResultsFound from '../../../common/component/grid/NoResultsFound';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIEndPoints from '../../../constants/APIEndPoints';
import { get } from '../../../actions/commonActions';
import SBButton from '../../../common/component/sbbutton/SBButton';
import checkLanguage from '../../../language/checkLanguage';

class HistoryInquiry extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      dmCode: '',
      historyList: [],
      pageLoading: true,
    };
    this.initialFocus = createRef();
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const { location, history } = this.props;
    if (location.state && location.state.dmCode) {
      const generateComponent = async () => {
        await this.getInquiryDetails();
      };
      generateComponent();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  }

  /**
   * getHistoryDetails is used to get History records
   */
  getInquiryDetails = async () => {
    const { location, history } = this.props;
    const response = await get(`${APIEndPoints.HISTORYINQUIRYDETAILS}/${location.state.dmCode}`);
    const responseData = await response.json();

    if (responseData.status) {
      const { historyList, firstUseStartDate } = responseData;
      this.setState(
        {
          dmCode: location.state.dmCode,
          historyList: this.prepareInquiryData(historyList, firstUseStartDate),
          pageLoading: false,
        },
        () => {
          this.initialFocus.current.focus();
        },
      );
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * prepareInquiryData is used to prepare History map
   * @param historyList
   * @return historyList
   *
   */
  prepareInquiryData = (historyList, firstUseStartDate) => {
    const inquiryRecordList = [
      {
        date: this.languageLabelCheck('first_use_start_date'),
        deviceCount: firstUseStartDate ? firstUseStartDate.replace(/-/g, '/') : '',
        remarks: '',
        backgroundColor: '#EDF4CC',
      },
    ];
    return historyList.length ? inquiryRecordList.concat(historyList) : historyList;
  };

  /**
   * onHandleClick is used to handle click operation of back button.
   *
   */
  onHandleClick = () => {
    const { history } = this.props;
    history.push(RouteEndPoints.APPLIST);
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * getDmCodeViewList is load the customcolumn information
   * @return customcolumndetails
   *
   */
  getDmCodeViewList = () => {
    return [
      {
        label: 'dm_code',
        component: 'span',
        key: 'dmCode',
      },
    ];
  };

  /**
   * getHistoryColumnList is load the column information
   * @return columndetails
   *
   */
  getInquiryColumnList = () => {
    return [
      {
        title: 'history_date',
        dataIndex: 'date',
        rowKey: 'date',
        sorter: false,
        textAlign: 'center',
        render: (value) => (value && value.date ? value.date.replace(/-/g, '/') : ''),
      },
      {
        dataIndex: 'deviceCount',
        rowKey: 'deviceCount',
        sorter: false,
        textAlign: 'center',
        styles: { padding: '0' },
        headerColumnList: [
          { title: 'numer_of_application_device' },
          { title: 'history_basic' },
          { title: 'android' },
        ],
      },
      {
        title: 'remarks',
        dataIndex: 'remarks',
        rowKey: 'remarks',
        sorter: false,
        textAlign: 'center',
        whiteSpace: 'pre-line',
      },
    ];
  };

  /**
   * render is used to render the component
   * @return HistoryCorrectionComponent
   */
  render() {
    const dmView = this.getDmCodeViewList();
    const columns = this.getInquiryColumnList();
    const { dmCode, historyList, pageLoading } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader title="history_inquiry_title" description={['history_inquiry_desc']} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame tableList={dmView} dataSource={{ dmCode }} defaultValue={{}} />
              </div>
              <div>
                {historyList.length ? (
                  <div style={{ marginTop: 10 }}>
                    <RoleResolver
                      component="sbtable"
                      columns={columns}
                      authorityRoleType={Role.AppListUpdateRoles}
                    >
                      <SBTable
                        rowKey="sequenceNo"
                        columns={columns}
                        dataSource={historyList}
                        loading
                      />
                    </RoleResolver>
                  </div>
                ) : (
                  <>
                    <br />
                    <NoResultsFound noDataLabel="no_history_change" />
                  </>
                )}
                <div className="clearfix">
                  <div className="floatright">
                    <SBButton
                      id="btn_back"
                      noMarginRight
                      type="button"
                      title="return"
                      ref={this.initialFocus}
                      onClick={() => this.onHandleClick()}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
HistoryInquiry.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dmCode: PropTypes.string,
    }),
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
HistoryInquiry.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(HistoryInquiry));
