import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import fileDownload from 'js-file-download';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import { CommonConstants } from '../../../constants/CommonConstants';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import SBButton from '../../../common/component/sbbutton/SBButton';
import { get, postWithBlobData } from '../../../actions/commonActions';
import APIEndPoints from '../../../constants/APIEndPoints';
import NoResultsFound from '../../../common/component/grid/NoResultsFound';
import checkLanguage from '../../../language/checkLanguage';
import { getDmCodeViewList, SetErrorAndFocus } from '../Constants/RegisterNoticeConstants';

class RegistrationNotification extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      dmCode: '',
      businessName: '',
      pageLoading: true,
      noticeType: 0,
      fileNameDate: '',
      dynamicData: {
        fileNameDateError: '',
        filenameRef: createRef(),
      },
      loginId: '',
      isDownloadFailed: false,
      isCreateDisable: false,
    };
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const { location, history } = this.props;
    if (location.state && location.state.dmCode) {
      this.getNoticeDetails();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  }

  /**
   * getNoticeDetails is used to get application details
   */
  getNoticeDetails = async () => {
    const { location, history } = this.props;
    const response = await get(`${APIEndPoints.TENANTDETAILS}/${location.state.dmCode}`);
    const responseData = await response.json();
    if (responseData.status) {
      const { applicationVo } = responseData;
      this.setState({
        dmCode: location.state.dmCode,
        businessName: applicationVo.businessName,
        loginId: applicationVo.loginId,
        pageLoading: false,
      });
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * onTableFrameEvent is used to handle operation performed on the component
   * @param key
   * @param event
   * @param value
   *
   */

  onTableFrameEvent = (key, event, value) => {
    if (key === 'registerNoticeType') {
      this.setState({ noticeType: value });
    }
    if (key === 'fileNameDate') {
      this.setState({ fileNameDate: value.replaceAll('/', '') });
    }
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onHandleClick is used to handle click operation based on key
   * @param key
   *
   */
  onHandleClick = (key) => {
    const { history } = this.props;
    if (key === 'create') {
      this.createRegistrationNoticePdf();
    } else {
      history.push(RouteEndPoints.APPLIST);
    }
  };

  /**
   * createRegistrationNoticePdf is used to export pdf.
   */
  createRegistrationNoticePdf = () => {
    const { dmCode, fileNameDate, noticeType, dynamicData, loginId } = this.state;
    if (fileNameDate) {
      this.setState({ dynamicData: { fileNameDateError: '' }, isCreateDisable: true });
      postWithBlobData(APIEndPoints.CREATEREGISTERNOTICE, {
        dmCode,
        fileNameDate,
        noticeType,
        loginId,
      })
        .then((response) => response.blob())
        .then((blobData) => {
          const printData = new Blob([blobData]);
          if (printData.size) {
            const noticeFileName = this.createFileName();
            fileDownload(printData, noticeFileName);
            this.setState({ isCreateDisable: false });
          } else {
            this.setState({ isDownloadFailed: true });
          }
        });
    } else {
      this.setState({
        dynamicData: SetErrorAndFocus(dynamicData),
      });
    }
  };

  /**
   * createFileName is used to generate file name based on Language
   */
  createFileName = () => {
    const { dmCode, fileNameDate, businessName } = this.state;

    return fileNameDate.concat(
      '_',
      businessName,
      ' ',
      this.languageLabelCheck('register_notice_mr'),
      '_',
      this.languageLabelCheck('register_notice_export_name'),
      ' ',
      '(',
      dmCode,
      ')',
      '.pdf',
    );
  };

  /**
   * render is used to render the component
   * @return HistoryCorrectionComponent
   */
  render() {
    const {
      dmCode,
      businessName,
      pageLoading,
      fileNameDate,
      isDownloadFailed,
      dynamicData,
      isCreateDisable,
    } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader
            title="registration_notification_title"
            description={['registration_notification_desc']}
          />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame
                  tableList={getDmCodeViewList(dynamicData)}
                  dataSource={{ dmCode, businessName, fileNameDate }}
                  defaultValue={{}}
                  onTableFrameEvent={(key, event, value) =>
                    this.onTableFrameEvent(key, event, value)
                  }
                />
              </div>
              <div className="clearfix">
                <div className="floatright">
                  <SBButton
                    id="btn_change"
                    color="green"
                    type="button"
                    title="create"
                    disabled={isCreateDisable}
                    onClick={() => this.onHandleClick('create')}
                  />
                  <SBButton
                    id="btn_cancel"
                    noMarginRight
                    type="button"
                    title="cancel"
                    onClick={() => this.onHandleClick('cancel')}
                  />
                </div>
              </div>
              {isDownloadFailed && (
                <div>
                  <br />
                  <NoResultsFound noDataLabel="user_already_deleted" />
                </div>
              )}
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
RegistrationNotification.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dmCode: PropTypes.string,
    }),
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
RegistrationNotification.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps)(withRouter(RegistrationNotification));
