/** Base Files */
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/** Utility File */
import checkLanguage from '../../language/checkLanguage';

/**
 * DeviceIndicator is to display device information
 * @param deviceList
 * @return DeviceIndicator
 */
const DeviceIndicator = ({ deviceList, isNotMultiLang, localeData }) => {
  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  const languageLabelCheck = (label) => {
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  return [
    deviceList.length < 11
      ? deviceList.map((record, index) => (
        <li key={index.toString()} style={{ padding: 1 }}>
          {`${record.deviceName} (${record.deviceModel})`}
        </li>
        ))
      : languageLabelCheck('eleven_devices_or_more'),
  ];
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = (state) => {
  return { localeData: state.language.data };
};

/**
 * propTypes is used for validating props with its type.
 */
DeviceIndicator.propTypes = {
  isNotMultiLang: PropTypes.bool,
  localeData: PropTypes.objectOf(PropTypes.string),
  deviceList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

/**
 * defaultProps is used to initialise non required props initial values
 */
DeviceIndicator.defaultProps = { localeData: {}, isNotMultiLang: false };

export default connect(mapStateToProps)(DeviceIndicator);
