/**
 * ConfirmationFrame is used to create footer components
 * @author:  P.Vinothpandian
 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBTextbox from '../../../common/component/sbtextbox/SBTextbox';
import SBComboBox from '../../../common/component/combobox/SBComboBox';
import SBRadioButton from '../../../common/component/sbradio/SBRadio';
import SBCheckbox from '../../../common/component/checkbox/SBCheckbox';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import descriptionConveter from '../../../common/utils/descriptionConverter';

class ConfirmationFrame extends Component {
  renderList = floatList => {
    return floatList.map(data => this.renderPanel(data));
  };

  onClick = (event, value) => {
    const { onConfirmationFrameEvent } = this.props;
    if (onConfirmationFrameEvent) {
      onConfirmationFrameEvent(event, value);
    }
  };

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : descriptionConveter(checkLanguage(localeData, label));
  };

  renderPanel = values => {
    switch (values.component) {
      case 'textbox':
        return (
          <SBTextbox
            placeholder={values.placeholder}
            type={values.type}
            maxLength={values.maxLength}
            name={values.name}
            disabled={values.disabled}
            size={values.size}
            style={values.style}
            defaultValue={values.defaultValue}
            value={values.value}
            ref={values.ref}
            id={values.id}
            onChange={(event, value) => this.onClick(event, value)}
          />
        );
      case 'combobox':
        return (
          <SBComboBox
            options={values.options}
            name={values.name}
            id={values.id}
            defaultValue={values.defaultValue}
            value={values.value}
            filterKey={values.filterKey}
            filterValue={values.filterValue}
            className={values.className}
            size={values.size}
            disabled={values.disabled}
            onChange={(event, value) => this.onClick(event, value)}
            style={{ marginRight: 10, ...values.style }}
          />
        );
      case 'radio':
        return (
          <SBRadioButton
            name={values.name}
            options={values.options}
            value={values.value}
            defaultValue={values.defaultValue}
            filterKey={values.filterKey}
            filterValue={values.filterValue}
            buttonStyle={values.buttonStyle}
            vertical={values.vertical}
            disabled={values.disabled}
            onChange={(event, value) => this.onClick(event, value)}
          />
        );
      case 'checkbox':
        return (
          <SBCheckbox
            options={values.options}
            name={values.name}
            id={values.id}
            horizontal={values.horizontal}
            image={values.image}
            defaultValue={values.defaultValue}
            value={values.value}
            filterKey={values.filterKey}
            imageSrcKey={values.imageSrcKey}
            imageAltKey={values.imageAltKey}
            disabled={values.disabled}
            onChange={(event, value) => this.onClick(event, value)}
          />
        );
      case 'button':
        return (
          <RoleResolver authorityRoleType={values.authorityRoleType}>
            <SBButton
              add={values.add}
              type={values.type}
              title={values.title}
              noClassName={values.noClassName}
              name={values.name}
              color={values.color}
              style={values.style}
              disabled={values.disabled}
              noMarginRight={values.noMarginRight}
              ref={values.ref}
              id={values.id}
              onClick={event => this.onClick(event)}
            />
          </RoleResolver>
        );
      case 'div':
        return (
          <div id={values.id} className={values.class} style={values.style}>
            {this.renderInnerComponents(values.innerComponents)}
          </div>
        );
      case 'break':
        return <br />;
      case 'span':
        return (
          <span className={values.class} style={values.style}>
            {this.renderInnerComponents(values.innerComponents)}
          </span>
        );
      default:
        return this.languageLabelCheck(values.text);
    }
  };

  renderInnerComponents = data => {
    return data.map(values => this.renderPanel(values));
  };

  renderFloatLeftList = floatLeftList => {
    return <div className="floatleft">{this.renderList(floatLeftList)}</div>;
  };

  renderFloatRightList = floatRightList => {
    return <div className="floatright">{this.renderList(floatRightList)}</div>;
  };

  render() {
    const { floatLeftList, floatRightList } = this.props;
    return (
      <div className="clearfix">
        {floatLeftList ? this.renderFloatLeftList(floatLeftList) : ''}
        {floatRightList ? this.renderFloatRightList(floatRightList) : ''}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    localeData: state.language.data,
  };
};
export default connect(mapStateToProps)(ConfirmationFrame);
