/**
 * NoticeDetail is used to view detail of notification from softbank
 * @author: V.Vijayaragavan
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import notificationHeaderInformation from '../constants/NotificationHeaderConstants';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get, postWithBlobData } from '../../../actions/commonActions';
import NotificationView from '../component/NotificationRegistration/NotificationView';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import { dateBasedOnLocale } from '../../../common/utils/dateUtils';
import { CommonConstants } from '../../../constants/CommonConstants';
import SBButton from '../../../common/component/sbbutton/SBButton';

const fileDownload = require('js-file-download');

class NoticeDetail extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history } = this.props;
    this.state = {
      pageLoading: true,
      notificationVo: {},
    };
    if (location && location.state && location.state.noticeId) {
      this.state = {
        noticeId: location.state.noticeId,
        pageLoading: true,
        notificationVo: {},
      };
    } else {
      history.push(ROUTEENDPOINTS.ERROR);
    }
  }

  /**
   * componentDidMount performs api operation to fetch notification detail
   */
  componentDidMount() {
    const { history } = this.props;
    const { noticeId } = this.state;
    const getNotificationDetail = async () => {
      this.setState({
        pageLoading: true,
      });
      const response = await get(`${APIENDPOINTS.NOTICEDETAIL}/${noticeId}`);
      const responseVo = await response.json();
      if (responseVo.status) {
        const { notificationVo } = responseVo;
        notificationVo.attachmentFile = notificationVo.attachmentName;
        if (notificationVo.imageName) {
          this.getImageFiles(notificationVo);
        } else {
          this.setState({
            notificationVo,
            pageLoading: false,
          });
        }
      } else {
        history.push(ROUTEENDPOINTS.ERROR);
      }
    };
    getNotificationDetail();
  }

  /**
   * getImageFiles is used to download the image files
   * @param notificationVo
   */
  getImageFiles = (notificationVo) => {
    const { noticeId } = this.state;
    let htmlContent = notificationVo.content;
    let imageIndex = 0;
    const imageNames = notificationVo.imageName.split(',');
    const getIndividualImage = () => {
      postWithBlobData(
        `${APIENDPOINTS.NOTICEFILEDOWNLOAD}/${noticeId}/images/${imageNames[imageIndex]}`,
      )
        .then((response) => response.blob())
        .then((blobData) => {
          const printData = new Blob([blobData], { type: 'image/*' });
          htmlContent = htmlContent.replace(
            `cid:image${imageIndex}`,
            URL.createObjectURL(printData),
          );
          notificationVo.content = htmlContent;
          if (imageNames.length - 1 > imageIndex) {
            imageIndex += 1;
            getIndividualImage();
          } else {
            this.setState({
              notificationVo,
              pageLoading: false,
            });
          }
        });
    };
    getIndividualImage();
  };

  /**
   * downloadAttachment is used to download the attachment file
   */
  downloadAttachment = () => {
    const { noticeId, notificationVo } = this.state;
    postWithBlobData(
      `${APIENDPOINTS.NOTICEFILEDOWNLOAD}/${noticeId}/attachment/${notificationVo.attachmentName}`,
    )
      .then((response) => response.blob())
      .then((blobData) => {
        const printData = new Blob([blobData]);
        fileDownload(printData, notificationVo.attachmentName);
      });
  };

  /**
   * backButtonClick is used to navigate user back to Notice list screen
   */
  backButtonClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.NOTICELOOKLIST);
  };

  /**
   * render is used to render the component
   */
  render() {
    const { currentLanguage } = this.props;
    const { pageLoading, notificationVo } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <SBContentHeader
          title={notificationHeaderInformation('noticeLookList').title}
          description={notificationHeaderInformation('noticeLookList').description}
        />
        <div className="contents-main">
          <h2 className="title-corner mb0">
            {dateBasedOnLocale(new Date(notificationVo.dateOfPublication), currentLanguage)}
            <br />
            {notificationVo.notificationTitle}
          </h2>
          <NotificationView
            showDownload={Boolean(notificationVo.attachmentFile)}
            imageFileURL={
              notificationVo.imageFile ? URL.createObjectURL(notificationVo.imageFile) : ''
            }
            onDownloadButtonClick={() => this.downloadAttachment()}
            htmlContent={notificationVo.content || ''}
            removeMinHeight
          />
          <div className="clearfix floatright">
            <SBButton
              id="btn_back"
              type="button"
              title="back"
              noMarginRight
              onClick={() => this.backButtonClick()}
            />
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
NoticeDetail.propTypes = {
  location: PropTypes.shape({
    state: PropTypes.shape({
      noticeId: PropTypes.string.isRequired,
    }),
  }).isRequired,
  history: commonPropTypes().history.isRequired,
  currentLanguage: PropTypes.string,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
NoticeDetail.defaultProps = {
  currentLanguage: 'ja',
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { current } = state.language;
  return {
    currentLanguage: current,
  };
};

export default connect(mapStateToProps)(NoticeDetail);
