import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { isUndefined } from 'util';
import ROUTEENDPOINTS from '../../../constants/RouteEndPoints';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import AppCRUDFooter from './AppCRUDFooter';
import headerInformation from '../Constants/AppManageHeaderConstants';
import APIENDPOINTS from '../../../constants/APIEndPoints';
import { get } from '../../../actions/commonActions';
import { AppInfoView } from '../Constants/AppFrameConstants';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';

class AppInfo extends Component {
  /**
   * constructor is used to initalize the state values
   * @param props
   */
  constructor(props) {
    super(props);
    const { location, history } = props;
    if (isUndefined(location.state)) {
      history.push(ROUTEENDPOINTS.ERROR);
    }
    this.state = {
      pageLoading: true,
      headerData: headerInformation('appinfo'),
      dataSource: {},
      footer: { okDisable: true, primaryVisibile: false },
    };
  }

  /**
   * componentDidMount is load the data in the grid
   */

  componentDidMount() {
    const { location, history } = this.props;
    if (!isUndefined(location.state)) {
      const param = location.state;
      const userStatus = async () => {
        const userDetailResponse = await get(
          `${APIENDPOINTS.APPLICATIONUSERDETAILS}/${param.dmCode}`,
        );
        const userDetailData = await userDetailResponse.json();
        if (userDetailData.status) {
          this.setState({ dataSource: userDetailData.userVo, pageLoading: false });
        } else {
          history.push(ROUTEENDPOINTS.ERROR);
        }
      };
      userStatus();
    }
  }

  onHandleClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.APPLIST);
  };

  render() {
    const { pageLoading, headerData, dataSource, footer } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div>
          <SBContentHeader title={headerData.title} description={headerData.description} />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame
                  tableList={AppInfoView}
                  dataSource={dataSource}
                  defaultValue={{}}
                />
              </div>
              <div>
                <AppCRUDFooter 
                  reference={{backRef: createRef()}}
                  footer={footer} 
                  onHandleClick={() => this.onHandleClick()}
                />
              </div>
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

export default withRouter(AppInfo);
