/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

/** SBComponents */
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import SBDialog from '../../common/component/sbdialog/SBDialog';

/** Device Components */
import DeviceFooter from '../component/DeviceFooter';

/** Utility Files */
import deviceHeader from '../constants/deviceHeaderConstants';
import checkLanguage from '../../language/checkLanguage';
import addDynamicContent from '../../language/addDynamicContent';
import { serviceMapper } from '../common/deviceCommonFunctions';
import RouteEndPoints from '../../constants/RouteEndPoints';

/**
 * DeviceCsvComplete is to show csv batch operation completion report
 * @author Aswin Palanisamy
 */
class DeviceCsvComplete extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey, operationType } = props;
    this.state = {
      screenKey,
      counter: 0,
      pageLoading: false,
      headerData: deviceHeader(`${operationType}BatchCompletion`),
    };
    this.backRef = createRef();
  }
  
  /**
   * componentDidMount perform initial grid load operation
   */
  componentDidMount() {
    const { history, location } = this.props;
    if(!location.state) 
      history.push(RouteEndPoints.ERROR);
    else {
      this.setState({ pageLoading: false, counter: location.state.counter }, () => {
        if(this.backRef.current) this.backRef.current.focus();
      })
    }
  }

  /**
   * getBacktoPreviousScreen navigates to csvBatchOperation screen
   */
  getBacktoPreviousScreen = () => {
    const { history } = this.props;
    history.push(serviceMapper('csvBatchOperation'), {
      parentScreen: serviceMapper('deviceList'),
    });
  };

  /**
   * onHandleClick performs button click events in the screen
   * @param screenKey
   * @return buttonAction
   */
  onHandleClick = screenKey =>
    ({
      back: () => this.getBacktoPreviousScreen(),
    }[screenKey]());

  /**
   * addCountInfo is to set grid data count in screen header label
   * @param label 
   * @param  contents 
   */
  addCountInfo = (label, ...contents) => {
    return addDynamicContent(this.languageLabelCheck(label), contents);
  };

  /**
   * languageLabelCheck is to set language label data
   * @param label
   * @return languageLabel
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * render perform groupSelection screen rendering
   * @return DeviceGroupSelect
   */
  render() {
    const {
      counter,
      screenKey,
      headerData,
      pageLoading,
      openDialogError,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader 
          isNotMultiLang 
          title={this.languageLabelCheck(headerData.title)}
          description={[this.addCountInfo(headerData.description, counter)]} 
        />
        <div className="contents-main">
          <DeviceFooter
            onHandleClick={key => this.onHandleClick(key)}
            footer={{
              screenKey,
              reference: this.backRef,
            }}
          />
        </div>
        <SBDialog
          type="error"
          header="error"
          content="data_does_not_exists"
          okButtonTitle="ok"
          open={openDialogError}
          onOk={() => {
            this.setState(
              { openDialogError: false },
              this.getBacktoPreviousScreen(),
            );
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * mapStateToProps perform data linking from the redux store
 * @param state 
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  return { localeData: data };
};

/**
 * propTypes is used for validation props with its type.
 */
DeviceCsvComplete.propTypes = {
  isNotMultiLang: PropTypes.bool.isRequired,
  operationType: PropTypes.string.isRequired,
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      counter: PropTypes.number,
    }),
  }).isRequired,
  localeData: PropTypes.objectOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps)(withRouter(DeviceCsvComplete));
