/**
 * CommonFrame is used to create table components
 * @author:  P.Vinothpandian
 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import checkLanguage from '../../../language/checkLanguage';
import SBTextbox from '../../../common/component/sbtextbox/SBTextbox';
import SBButton from '../../../common/component/sbbutton/SBButton';
import SBComboBox from '../../../common/component/combobox/SBComboBox';
import SBRadioButton from '../../../common/component/sbradio/SBRadio';
import SBCheckbox from '../../../common/component/checkbox/SBCheckbox';
import SBDatePicker from '../../../common/component/sbdatepicker/SBDatePicker';
import LABELCONSTANTS from '../../../constants/LabelConstants';

const bottomStyle = { marginBottom: 5 };

class TableFrame extends Component {
  generateTableStructue = (tableObj, dataSource) => {
    return (
      <tr>
        {(tableObj.label || tableObj.required) && (
          <th rowSpan={tableObj.rowSpan}>
            {this.languageLabelCheck(tableObj.label)}
            {tableObj.required ? <span className="red bold">*</span> : ''}
          </th>
        )}
        <td style={tableObj.tdStyle}>
          {tableObj.componentList.map((componentObj) => {
            return (
              <span>
                {this.setTableDesc(componentObj, dataSource)}
                <span id={`${componentObj.key}_errormsg`} className="red bold">
                  {this.languageLabelCheck(componentObj.errorMessage)}
                </span>
              </span>
            );
          })}
        </td>
      </tr>
    );
  };

  onClick = (key, event, value) => {
    const { onTableFrameEvent } = this.props;
    onTableFrameEvent(key, event, value);
  };

  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  onChange = (key, event, value) => {
    const { onTableFrameEvent } = this.props;
    onTableFrameEvent(key, event, value);
  };

  onDateSelect = (key, dateText, instance) => {
    const { onTableFrameEvent } = this.props;
    onTableFrameEvent(key, dateText, instance);
  };

  setTableDesc = (componentObj, dataSource) => {
    switch (componentObj.component) {
      case 'textbox':
        return (
          <div style={bottomStyle}>
            <SBTextbox
              placeholder={componentObj.placeholder}
              type={componentObj.type}
              maxLength={componentObj.maxLength}
              name={componentObj.name}
              readOnly={componentObj.readOnly}
              disabled={componentObj.disabled}
              size={componentObj.size}
              style={componentObj.style}
              defaultValue={componentObj.defaultValue}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              ref={componentObj.ref}
              id={componentObj.id}
              key={componentObj.key}
              onChange={(event, content) => this.onChange(componentObj.key, event, content)}
            />
          </div>
        );
      case 'textarea':
        return (
          <div style={bottomStyle}>
            <textarea
              id={componentObj.id}
              name={componentObj.name}
              cols={componentObj.cols}
              rows={componentObj.rows}
              className={componentObj.class}
              style={componentObj.style}
              key={componentObj.key}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              onChange={(event, value) => this.onChange(componentObj.key, event, value)}
            />
          </div>
        );
      case 'combobox':
        return (
          <div style={bottomStyle}>
            <SBComboBox
              options={componentObj.options}
              name={componentObj.name}
              style={componentObj.style}
              id={componentObj.id}
              defaultValue={componentObj.defaultValue}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              filterKey={componentObj.filterKey}
              filterValue={componentObj.filterValue}
              className={componentObj.className}
              size={componentObj.size}
              disabled={componentObj.disabled}
              key={componentObj.key}
              onChange={(event, value) => this.onChange(componentObj.key, event, value)}
              isNotMultiLang={componentObj.isNotMultiLang}
            />
          </div>
        );
      case 'radio':
        return (
          <div style={bottomStyle}>
            <SBRadioButton
              name={componentObj.name}
              options={componentObj.options}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              defaultValue={componentObj.defaultValue}
              filterKey={componentObj.filterKey}
              filterValue={componentObj.filterValue}
              buttonStyle={componentObj.buttonStyle}
              vertical={componentObj.vertical}
              disabled={componentObj.disabled}
              key={componentObj.key}
              onChange={(event, value) => this.onChange(componentObj.key, event, value)}
            />
          </div>
        );
      case 'checkbox':
        return (
          <div style={bottomStyle}>
            <SBCheckbox
              options={componentObj.options}
              name={componentObj.name}
              id={componentObj.id}
              horizontal={componentObj.horizontal}
              image={componentObj.image}
              defaultValue={componentObj.defaultValue}
              value={
                componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]
              }
              filterKey={componentObj.filterKey}
              filterValue={componentObj.filterValue}
              imageSrcKey={componentObj.imageSrcKey}
              imageAltKey={componentObj.imageAltKey}
              disabled={componentObj.disabled}
              key={componentObj.key}
              onChange={(event, value) => this.onChange(componentObj.key, event, value)}
            />
          </div>
        );
      case 'button':
        return (
          <div style={bottomStyle}>
            <SBButton
              add={componentObj.add}
              type={componentObj.type}
              title={componentObj.title}
              noClassName={componentObj.noClassName}
              name={componentObj.name}
              color={componentObj.color}
              style={componentObj.style}
              disabled={componentObj.disabled}
              noMarginRight={componentObj.noMarginRight}
              id={componentObj.id}
              key={componentObj.key}
              onClick={(event) => this.onClick(componentObj.key, event)}
            />
          </div>
        );

      case 'datepicker':
        return (
          <div style={bottomStyle}>
            <SBDatePicker
              id={componentObj.id}
              name={componentObj.name}
              width={componentObj.width}
              className={componentObj.className}
              onSelect={(dateText, instance) =>
                this.onDateSelect(componentObj.startDateKey, dateText, instance)
              }
              onChange={(event) =>
                this.onDateSelect(componentObj.startDateKey, event.target.value, event)
              }
              value={componentObj.value}
              format={componentObj.format}
              minDate={componentObj.minDate}
              maxDate={componentObj.maxDate}
            />
          </div>
        );

      case 'daterange':
        return (
          <div style={bottomStyle}>
            <SBDatePicker
              id={`${componentObj.id}_start_date`}
              name={componentObj.name}
              width={componentObj.width}
              className="w140px mr10"
              onSelect={(dateText, instance) =>
                this.onDateSelect(componentObj.startDateKey, dateText, instance)
              }
              onChange={(event) =>
                this.onDateSelect(componentObj.startDateKey, event.target.value, event)
              }
              value={componentObj.defaultStartDate}
              format={componentObj.startDateFormat}
              minDate={componentObj.startDateMinValue}
              maxDate={componentObj.startDateMaxValue}
            />
            {LABELCONSTANTS.TILDE}
            <SBDatePicker
              id={`${componentObj.id}_endDate`}
              name={componentObj.name}
              width={componentObj.width}
              className="w140px ml10"
              onSelect={(dateText, instance) =>
                this.onDateSelect(componentObj.endDateKey, dateText, instance)
              }
              onChange={(event) =>
                this.onDateSelect(componentObj.endDateKey, event.target.value, event)
              }
              value={componentObj.defaultEndDate}
              format={componentObj.endDateFormat}
              minDate={componentObj.endDateMinValue}
              maxDate={componentObj.endDateMaxValue}
            />
          </div>
        );
      case 'div':
        return (
          <div id={componentObj.id} className={componentObj.class} style={bottomStyle}>
            {componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]}
          </div>
        );
      case 'break':
        return <br />;
      case 'input':
        return (
          <input type={componentObj.type} name={componentObj.name} value={componentObj.value} />
        );
      case 'paragraph':
        return <p className="mt10">{componentObj.text}</p>;
      case 'span':
        return (
          <span style={componentObj.style} className={componentObj.class}>
            {componentObj.render ? componentObj.render(dataSource) : dataSource[componentObj.key]}
          </span>
        );
      case 'fileupload':
        return (
          <div style={bottomStyle}>
            <label
              htmlFor={componentObj.id}
              className="btn-fileupload mr5"
              onChange={(event) => {
                this.onChange(componentObj.key, event);
              }}
            >
              <input
                type="file"
                id={componentObj.id}
                name={componentObj.id}
                size="40"
                style={{ width: '100%' }}
                accept={componentObj.accept}
              />
            </label>
          </div>
        );
      default:
        return componentObj.text;
    }
  };

  render() {
    const { tableList, dataSource, className, tableHeaderList } = this.props;
    const emptyText = '';
    return (
      <table className={className || 'table-tate mb10'}>
        <tbody>
          {tableHeaderList ? (
            <tr>
              <th style={{ background: '#FFF', border: 'none' }}>{emptyText}</th>
              {tableHeaderList.map((subHeader) => (
                <th className="center">{this.languageLabelCheck(subHeader)}</th>
              ))}
            </tr>
          ) : (
            ''
          )}
          {tableList &&
            tableList.map((tableObj) => {
              return this.generateTableStructue(tableObj, dataSource);
            })}
        </tbody>
      </table>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(TableFrame);
