/**
 * HistoryCorrection is used to Update Aplication History
 * @author: Rajeshkumar.R
 */

import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import APIEndPoints from '../../../constants/APIEndPoints';
import SBContentHeader from '../../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../../common/component/loadingSpinner/CircularSpinner';
import SBTable from '../../../common/component/sbtable/SBTable';
import { CommonConstants } from '../../../constants/CommonConstants';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import CustomTableFrame from '../../../v1/common/component/CustomTableFrame';
import RoleResolver from '../../../common/component/guard/RoleResolver';
import { Role } from '../../../constants/Role';
import commonPropTypes from '../../../common/commonPropTypes/commonPropTypes';
import SBButton from '../../../common/component/sbbutton/SBButton';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction from '../../../common/changeReducer/commonChangeAction';
import NoResultsFound from '../../../common/component/grid/NoResultsFound';
import { get, post } from '../../../actions/commonActions';

class HistoryCorrection extends Component {
  /**
   * constructor initializes state values
   * @param props
   */
  constructor(props) {
    super(props);
    this.state = {
      dataSource: {},
      dmCode: '',
      historyList: [],
      pageLoading: true,
      changedComboList: {},
      disableChange: true,
      messageContent: '',
    };
  }

  /**
   * componentDidMount is used to load initial data
   */
  componentDidMount() {
    const { location, history } = this.props;
    if (location.state && location.state.dmCode) {
      const generateComponent = async () => {
        await this.getHistoryDetails();
      };
      generateComponent();
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  }

  /**
   * getHistoryDetails is used to get History records
   */
  getHistoryDetails = async () => {
    const { location, history } = this.props;
    const response = await get(`${APIEndPoints.HISTORYDETAILS}/${location.state.dmCode}`);
    const responseData = await response.json();

    if (responseData.status) {
      const { historyList } = responseData;
      this.setState({
        dataSource: this.prepareDataSourceMap(historyList),
        dmCode: location.state.dmCode,
        historyList,
        pageLoading: false,
      });
    } else {
      history.push(RouteEndPoints.ERROR);
    }
  };

  /**
   * prepareDataSourceMap is used to prepare History map
   * @param historyList
   * @return dataSource
   *
   */
  prepareDataSourceMap = (historyList) => {
    const dataSource = {};
    historyList.forEach((historyData) => {
      dataSource[historyData.sequenceNo.toString()] = historyData;
    });
    return dataSource;
  };

  /**
   * mapTochangedComboList is used to convert changed map to list
   * @param map
   * @return changedComboList
   *
   */
  mapTochangedComboList = (map) => {
    const changedComboList = [];
    if (map) {
      Object.keys(map).forEach((mapKey) => {
        changedComboList.push(map[mapKey]);
      });
    }
    return changedComboList;
  };

  /**
   * getDmCodeViewList is load the customcolumn information
   * @return customcolumndetails
   *
   */
  getDmCodeViewList = () => {
    return [
      {
        label: 'dm_code',
        component: 'span',
        key: 'dmCode',
      },
    ];
  };

  /**
   * getHistoryColumnList is load the column information
   * @return columndetails
   *
   */
  getHistoryColumnList = () => {
    return [
      {
        title: 'enable_combo',
        dataIndex: 'render-combobox',
        rowKey: 'enabled',
        textAlign: 'left',
        comboDataList: [
          { key: '0', value: 'option_effective' },
          { key: '1', value: 'option_invalid' },
        ],
        sorter: false,
        autoFocusIndex: 0,
      },
      {
        title: 'history_date',
        dataIndex: 'date',
        rowKey: 'date',
        sorter: false,
        render: (value) => (value && value.date ? value.date.replace(/-/g, '/') : ''),
      },
      {
        title: 'device',
        dataIndex: 'device',
        rowKey: 'device',
        sorter: false,
        render: () => this.languageLabelCheck('android'),
      },
      {
        title: 'history_service',
        dataIndex: 'service',
        rowKey: 'service',
        sorter: false,
        render: () => this.languageLabelCheck('history_basic'),
      },
      {
        title: 'numer_of_application_device',
        dataIndex: 'deviceCount',
        rowKey: 'deviceCount',
        sorter: false,
        textAlign: 'center',
      },
      {
        title: 'remarks',
        dataIndex: 'remarks',
        rowKey: 'remarks',
        sorter: false,
        style: { whiteSpace: 'break-spaces' },
      },
    ];
  };

  /**
   * handleComboboxChange is used to handle combo change event
   * @param event
   * @param rowIndex
   *
   */
  handleComboboxChange = (event, rowIndex) => {
    const { setModified } = this.props;
    const { dataSource, changedComboList } = this.state;
    const { value } = event.target;
    dataSource[rowIndex].enabled = value;
    changedComboList[rowIndex] = dataSource[rowIndex];
    setModified(true);
    this.setState({
      changedComboList,
      disableChange: false,
      messageContent: '',
    });
  };

  /**
   * languageLabelCheck is used to load label content
   * @param label
   * @return labelcontent
   */
  languageLabelCheck = (label) => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  /**
   * onHandleClick is used to handle click operation based on key
   * @param key
   *
   */
  onHandleClick = (key) => {
    const { history } = this.props;
    if (key === 'save') {
      this.performUpdate();
    } else {
      history.push(RouteEndPoints.APPLIST);
    }
  };

  /**
   * performUpdate is used to updated changed history record
   */
  performUpdate = async () => {
    const { history, setModified } = this.props;
    const { changedComboList } = this.state;
    const updatedList = this.mapTochangedComboList(changedComboList);
    if (updatedList.length > 0) {
      const historyResponse = await post(APIEndPoints.HISTORYUPDATE, updatedList);
      const result = await historyResponse.json();
      if (result) {
        setModified(false);
        this.setState({
          changedComboList: {},
          disableChange: true,
          messageContent: 'history_update_success',
        });
      } else {
        history.push(RouteEndPoints.ERROR);
      }
    }
  };

  /**
   * render is used to render the component
   * @return HistoryCorrectionComponent
   */
  render() {
    const dmView = this.getDmCodeViewList();
    const columns = this.getHistoryColumnList();
    const { dmCode, historyList, pageLoading, disableChange, messageContent } = this.state;
    return (
      <CircularSpinner loading={pageLoading} css={CommonConstants.circularSpinnerDefaultStyle}>
        <div>
          <SBContentHeader
            title="history_correction_title"
            description={['history_correction_desc']}
          />
          <div className="contents-main">
            <div className="clearfix mb10">
              <div>
                <CustomTableFrame tableList={dmView} dataSource={{ dmCode }} defaultValue={{}} />
              </div>
              <div>
                {historyList.length > 0 && (
                  <div style={{ marginTop: 10 }}>
                    <RoleResolver
                      component="sbtable"
                      columns={columns}
                      authorityRoleType={Role.AppListUpdateRoles}
                    >
                      <SBTable
                        rowKey="sequenceNo"
                        columns={columns}
                        dataSource={historyList}
                        loading
                        handleComboboxChange={(event, rowIndex) =>
                          this.handleComboboxChange(event, rowIndex)
                        }
                      />
                    </RoleResolver>
                  </div>
                )}
                {historyList.length === 0 && (
                  <>
                    <br />
                    <NoResultsFound noDataLabel="no_history_change" />
                  </>
                )}
                <div className="clearfix">
                  <div className="floatright">
                    <SBButton
                      id="btn_change"
                      color="green"
                      type="button"
                      title="change"
                      disabled={disableChange}
                      onClick={() => this.onHandleClick('save')}
                    />
                    <SBButton
                      id="btn_cancel"
                      noMarginRight
                      type="button"
                      title="cancel"
                      onClick={() => this.onHandleClick('cancel')}
                    />
                  </div>
                </div>
                <span className="red bold">{this.languageLabelCheck(messageContent)}</span>
              </div>
            </div>
          </div>
        </div>
      </CircularSpinner>
    );
  }
}

/**
 * mapDispatchToProps is used to dispatch the value to props
 * @param dispatch
 */
const mapDispatchToProps = (dispatch) => {
  return {
    setModified: (isModified) => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

/**
 * mapStateToProps is used to map the state value to props
 * @param state
 */
const mapStateToProps = (state) => {
  const { data } = state.language;
  return {
    localeData: data,
  };
};

/**
 * propTypes is used for validation props with its type.
 */
HistoryCorrection.propTypes = {
  history: commonPropTypes().history.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      dmCode: PropTypes.string,
    }),
  }).isRequired,
  isNotMultiLang: PropTypes.bool,
  localeData: commonPropTypes().localeData.isRequired,
};

/**
 * defaultProps is used to initialise non requiered props initial values
 */
HistoryCorrection.defaultProps = {
  isNotMultiLang: false,
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(HistoryCorrection));
