/**
 * buttonList sets the group setting button components
 * @param buttonKey
 * @return buttonList
 */
export const buttonList = (buttonKey, mainPolicyMapper) => {
  if (
    buttonKey === 'wifi_settings' &&
    mainPolicyMapper.wifiCSV &&
    mainPolicyMapper.wifiCSV.WifiRadio === 'input'
  ) {
    return [
      { id: 'buttonRemove', src: BackgroundRemoveImage, alt: '' },
      { id: 'buttonAdd', src: BackgroundAddImage, alt: '' },
    ];
  }
  return [{ id: 'buttonRemove', src: BackgroundRemoveImage, alt: '' }];
};

