/**
 * makeApplicationSelect fetches grid columns for application selection
 * @param applicationSelect
 * @return gridColumnList
 */
const makeApplicationSelect = (applicationSelect) => [
  ...applicationSelect.splice(0, 1),
  ...applicationSelect.splice(1, 11),
];

function getsecurityPolicyGroupColumns(columnParams) {
  const { isGroupAuthority } = columnParams || {};
  const columns = [
    {
      title: '',
      textAlign: 'center',
      dataIndex: 'render-checkbox',
      styles: { minWidth: 20, width: 20 },
    },
    {
      sorter: false,
      title: 'assign_group_name',
      rowKey: 'groupName',
      dataIndex: 'groupName',
    },
  ];
  if (isGroupAuthority)
    columns.push({
      sorter: false,
      title: 'authority',
      rowKey: 'authority',
      dataIndex: 'authority',
    });
  return columns;
}

