import React from 'react';
import { connect } from 'react-redux';
import SBButton from '../sbbutton/SBButton';
import UNDERSCORE from '../../../v1/common/utils';
import checkLanguage from '../../../language/checkLanguage';
import addDynamicContent from '../../../language/addDynamicContent';

const styles = {
  div: {
    overflowX: 'scroll',
    overflowY: 'hidden',
  },
  link: {
    cursor: 'pointer',
  },
};
class SBTable extends React.Component {
  constructor(props) {
    super(props);
    this.defaultComboValue = this.getResultsPerPage(props.defaultSelected);
    this.state = {
      isAscending: !props.isAscending,
      tableData: this.setData(props),
      enablePagination: props.dataSource.length > this.defaultComboValue,
      dataCount: this.defaultComboValue,
      page: props.defaultPage || 1,
      sortColumnName: props.defaultSortColumn,
      selectedRowKeys: props.defaultSelectedKeys ? props.defaultSelectedKeys : [],
    };
  }

  componentDidUpdate(prevProps) {
    const { dataSource, defaultPage, defaultSelectedKeys, screenKey } = this.props;
    const { page, dataCount, selectedRowKeys } = this.state;
    if (!UNDERSCORE.isEqual(dataSource, prevProps.dataSource)) {
      this.setState({
        tableData: this.setData(this.props),
        enablePagination: dataSource.length > dataCount,
      });
    }
    if (defaultPage && page !== defaultPage) {
      this.setState({ page: defaultPage });
    }
    if (selectedRowKeys !== defaultSelectedKeys && screenKey === 'selectTarget') {
      this.setState({ selectedRowKeys: defaultSelectedKeys });
    }
  }

  setData = (props) => {
    let data = [];
    if (props.filterList && this.defaultComboValue && this.defaultComboValue !== 'all') {
      data = props.dataSource.slice(0, this.defaultComboValue);
    } else {
      data = props.dataSource;
    }
    if (!props.pagination) {
      data = props.dataSource;
    }
    if (props.defaultSortColumn) {
      this.applySort(props.defaultSortColumn, props.isAscending, data);
    }
    return data;
  };

  addCountInfo = (label, contentOne, contentTwo) => {
    return addDynamicContent(this.languageLabelCheck(label), contentOne, contentTwo);
  };

  languageLabelCheck = (label) => {
    const { localeData } = this.props;
    return checkLanguage(localeData, label);
  };

  renderColumns = (columns) => {
    const { isAscending, sortColumnName } = this.state;
    const columnData = [];
    columns.forEach((column, index) => {
      columnData.push(
        <th key={index.toString()} style={column.styles}>
          {column.sorter ? (
            <u role="presentation" style={styles.link} onClick={() => this.handleSort(column)}>
              {this.languageLabelCheck(column.title)}
            </u>
          ) : (
            this.languageLabelCheck(column.title)
          )}
          {column.sorter && sortColumnName === column.dataIndex && (
            <span
              role="presentation"
              style={styles.link}
              onClick={() => this.handleSort(column)}
              className={`ml5 arrow-square arrow ${isAscending ? 'down' : 'up'}`}
            />
          )}
          {!column.title && column.headerColumnList && (
            <table className="table-yoko">
              <tbody>{this.renderHeaderColumnList(column.headerColumnList)}</tbody>
            </table>
          )}
        </th>,
      );
    });
    return columnData;
  };

  renderHeaderColumnList = (headerColumnList) => {
    const headerColumnListData = [];
    headerColumnList.forEach((headerColumn, headerIndex) => {
      headerColumnListData.push(
        <tr key={headerIndex.toString()}>
          <th
            key={headerIndex.toString()}
            style={
              headerIndex === 0
                ? { border: 'none' }
                : {
                    borderLeft: 'none',
                    borderRight: 'none',
                    borderBottom: 'none',
                  }
            }
          >
            {this.languageLabelCheck(headerColumn.title)}
          </th>
        </tr>,
      );
    });
    return headerColumnListData;
  };

  handleSort = (column) => {
    const { tableData, sortColumnName } = this.state;
    const { isAscending: sortAscending, handleSort } = this.props;
    let { isAscending } = this.state;
    if (sortColumnName !== column.dataIndex) {
      isAscending = sortAscending === undefined ? true : sortAscending;
    }
    this.setState(
      {
        isAscending: !isAscending,
        tableData: this.applySort(column.dataIndex, isAscending, tableData),
        sortColumnName: column.dataIndex,
      },
      () => {
        if (handleSort) {
          handleSort(tableData);
        }
      },
    );
  };

  sortData = (valueOne, valueTwo) => {
    switch (typeof valueOne) {
      case 'number':
        return valueOne - valueTwo;
      case 'string':
        if (valueOne.toLowerCase() > valueTwo.toLowerCase()) {
          return 1;
        }
        if (valueOne.toLowerCase() < valueTwo.toLowerCase()) {
          return -1;
        }
        return 0;
      default:
        break;
    }
    return '';
  };

  renderData = (data, column, index) => {
    const { selectedRowKeys } = this.state;
    const { rowKey, disableKey } = this.props;
    const setIndex = rowKey ? data[rowKey] : index;
    if (column.dataIndex === 'render-radio') {
      return [
        <input
          type="radio"
          name="tableRadioButton"
          id={`tableRadio${setIndex}`}
          key={`tableRadio${setIndex}`}
          checked={selectedRowKeys.includes(setIndex)}
          onClick={(event) => this.onRadioChange(event, data, setIndex)}
        />,
        <label className="radiobtn" key={`label${setIndex}`} htmlFor={`tableRadio${setIndex}`} />,
      ];
    }
    if (column.dataIndex === 'render-checkbox') {
      const className = disableKey && data[disableKey] ? 'checkbox disabled' : 'checkbox';
      return (
        <label className={className}>
          <input
            type="checkbox"
            name="tableCheckBox"
            className="checkbox-input"
            id={`tableCheck${setIndex}`}
            key={`tableCheck${setIndex}`}
            onChange={(event) => this.onCheckBoxChange(event, setIndex)}
            checked={selectedRowKeys.includes(setIndex)}
            disabled={disableKey && data[disableKey]}
          />
          <span className="checkbox-parts" />
        </label>
      );
    }
    if (column.dataIndex === 'render-combobox') {
      return (
        column.comboDataList && (
          <span className="select-box01">
            <select
              name="tableComboBox"
              id={`tableCombo${setIndex}`}
              key={`tableCombo${setIndex}`}
              defaultValue={data[column.rowKey].toLowerCase()}
              onChange={(event) => this.onComboBoxChange(event, setIndex)}
              disabled={!!column.disabled}
              {...{ autoFocus: column.autoFocusIndex === index }}
            >
              {column.comboDataList.map((comboData, row) => (
                <option key={row.toString()} value={comboData.key}>
                  {this.languageLabelCheck(comboData.value)}
                </option>
              ))}
            </select>
          </span>
        )
      );
    }
    if (column.dataIndex === 'render-button') {
      return (
        <input
          id={`configuration_id${index}`}
          name={`${index}`}
          className="btn-green"
          type="button"
          value={this.languageLabelCheck('set_configuration')}
          onClick={(event) => this.onButtonHandleClick(event)}
        />
      );
    }
    if (column.dataIndex === 'render-dynamicMultiSelectCombobox') {
      return (
        data[column.comboListKey] && (
          <span>
            <select
              size={column.size}
              multiple="true"
              className="w100per border-gray"
              name="tableMultiSelectComboBox"
              id={`tableMultiSelectCombo${setIndex}`}
              key={`tableMultiSelectCombo${setIndex}`}
              defaultValue={data[column.rowKey]}
              disabled={data[column.comboListKey].length === 0}
              onChange={(event) => this.onMultiComboBoxChange(event, setIndex)}
            >
              {data[column.comboListKey].map((multiCombo, row) => (
                <option
                  key={row.toString()}
                  value={data[column.comboListKey][row][column.filterKey]}
                >
                  {data[column.comboListKey][row][column.filterValue]}
                </option>
              ))}
            </select>
          </span>
        )
      );
    }

    if (column.render) {
      return column.render(data[column.dataIndex], data);
    }
    return data[column.dataIndex];
  };

  onButtonHandleClick = (event) => {
    const { handleButtonHandleClick } = this.props;
    if (handleButtonHandleClick) {
      handleButtonHandleClick(event);
    }
  };

  onRadioChange = (event, data, selectedRowKey) => {
    const { handleRadioChange } = this.props;
    const selectedRowKeys = [];
    if (event.target.checked) {
      selectedRowKeys.push(selectedRowKey);
    }
    this.setState({ selectedRowKeys });
    if (handleRadioChange) {
      handleRadioChange(data, selectedRowKeys[0]);
    }
  };

  onCheckBoxChange = (event, selectedRowKey) => {
    const { dataSource, handleCheckboxChange, disableKey } = this.props;
    const { selectedRowKeys } = this.state;
    if (!(disableKey && dataSource[parseInt(selectedRowKey, 10)][disableKey])) {
      if (event.target.checked) {
        selectedRowKeys.push(selectedRowKey);
      } else {
        const index = selectedRowKeys.indexOf(selectedRowKey);
        if (index !== -1) {
          selectedRowKeys.splice(index, 1);
        }
      }
      this.setState({ selectedRowKeys });
      if (handleCheckboxChange) {
        handleCheckboxChange(event, selectedRowKeys);
      }
    }
  };

  onComboBoxChange = (event, rowIndex) => {
    const { handleComboboxChange } = this.props;
    if (handleComboboxChange) handleComboboxChange(event, rowIndex);
  };

  handleSelectChange = (value) => {
    const { dataSource, defaultSelectedKeys, handleSelectChange, defaultSelected } = this.props;
    if (value === 'all') {
      this.setState({
        tableData: dataSource,
        enablePagination: false,
        dataCount: value,
        selectedRowKeys: defaultSelectedKeys || [],
      });
    } else {
      this.setState(
        {
          tableData: dataSource.slice(0, value),
          enablePagination: true,
          dataCount: value,
          page: 1,
          selectedRowKeys: defaultSelectedKeys || [],
        },
        () => this.pageChangeHandler(1),
      );
    }
    if (defaultSelected) {
      localStorage.setItem(defaultSelected, value);
    }
    if (handleSelectChange) {
      handleSelectChange(dataSource, value);
    }
  };

  onMultiComboBoxChange = (event, rowIndex) => {
    const { handleMultiComboboxChange } = this.props;
    if (handleMultiComboboxChange) handleMultiComboboxChange(event, rowIndex);
  };

  pageChangeHandler = (newPage) => {
    const { onPageChange } = this.props;
    if (onPageChange) {
      onPageChange(newPage);
    }
  };

  handlePaginationClick = (page) => {
    const { dataSource, defaultSelectedKeys, handlePaginationClick } = this.props;
    const { dataCount } = this.state;
    const tableData = this.sort(dataSource.slice(dataCount * (page - 1), dataCount * page));
    this.setState({
      tableData,
      selectedRowKeys: defaultSelectedKeys || [],
      page,
    });
    this.pageChangeHandler(page);
    if (handlePaginationClick) {
      handlePaginationClick(tableData);
    }
  };

  sort = (tableData) => {
    const { sortColumnName, isAscending } = this.state;
    if (sortColumnName) {
      return this.applySort(sortColumnName, !isAscending, tableData);
    }
    return tableData;
  };

  onClearAll = (event) => {
    const { selectedRowKeys: selectedKeys, tableData } = this.state;
    const { handleClearAll, rowKey } = this.props;
    const selectedRowKeys = selectedKeys.filter(
      (data) => !tableData.some((record) => record[rowKey] === data),
    );
    this.setState({ selectedRowKeys });
    if (handleClearAll) handleClearAll(event, selectedRowKeys);
  };

  onSelectAll = (event) => {
    const { tableData, selectedRowKeys } = this.state;
    const { rowKey, handleSelectAll, disableKey } = this.props;
    tableData.forEach((data) => {
      if (!(disableKey && data[disableKey]) && selectedRowKeys.indexOf(data[rowKey]) < 0)
        selectedRowKeys.push(data[rowKey]);
    });
    this.setState({ selectedRowKeys });
    if (handleSelectAll) handleSelectAll(event, selectedRowKeys);
  };

  onPaginationKeyPress = (event, updatePage) => {
    const { dataSource } = this.props;
    const { dataCount } = this.state;
    const pageSize = Math.ceil(dataSource.length / dataCount);
    if (event.keyCode === 13 && updatePage <= pageSize) {
      this.handlePaginationClick(updatePage);
    }
  };

  applySort = (columnName, isAscending, tableData) => {
    if (isAscending) {
      return tableData.sort((a, b) => this.sortData(a[columnName], b[columnName]));
    }
    return tableData.sort((a, b) => this.sortData(b[columnName], a[columnName]));
  };

  getResultsPerPage = (key) => {
    if (key in localStorage) {
      return localStorage.getItem(key);
    }
    if (key) {
      localStorage.setItem(key, 25);
    }
    return 25;
  };

  renderArrow(currentPage, comparePage, updatePage, icon) {
    return (
      <li
        tabIndex={currentPage === comparePage ? '' : '0'}
        className="arrow"
        role="presentation"
        style={{ pointerEvents: currentPage === comparePage ? 'none' : 'all' }}
        onClick={() => this.handlePaginationClick(updatePage)}
        onKeyDown={(event) => this.onPaginationKeyPress(event, updatePage)}
      >
        {currentPage === comparePage ? <span>{icon}</span> : <div>{icon}</div>}
      </li>
    );
  }

  renderpagination(pageSize) {
    const { page } = this.state;
    let tempButtonCount = 0;
    let tempStartPage = 0;
    tempStartPage = page > 3 && pageSize > 5 ? page - 2 : 1;
    tempStartPage = tempStartPage > pageSize - 4 && pageSize >= 5 ? pageSize - 4 : tempStartPage;
    tempButtonCount = pageSize > 5 ? 5 : pageSize;
    return (
      <div className="floatright">
        <ul className="pagination floatright">
          {this.renderArrow(page, 1, 1, '«')}
          {this.renderArrow(page, 1, page - 1, '‹')}
          {[...Array(tempButtonCount)].map((e, i) => (
            <li
              tabIndex={0}
              key={(i + tempStartPage).toString()}
              role="presentation"
              onClick={() => this.handlePaginationClick(i + tempStartPage)}
              onKeyDown={(event) => this.onPaginationKeyPress(event, i + tempStartPage)}
            >
              {page === i + tempStartPage ? (
                <span>{i + tempStartPage}</span>
              ) : (
                <div>{i + tempStartPage}</div>
              )}
            </li>
          ))}
          {this.renderArrow(page, pageSize, page + 1, '›')}
          {this.renderArrow(page, pageSize, pageSize, '»')}
        </ul>
      </div>
    );
  }

  renderSelect() {
    const {
      countInfo,
      maxcountInfo,
      filterList,
      type,
      filterMessage,
      countInfoContents,
      countInfoTotalContents,
      maxInfoContent,
      disableAll,
    } = this.props;
    return (
      <div className="floatleft mr15">
        <span className="select-box01">
          {countInfo && (
            <span className="lineheight mr15">
              {this.addCountInfo(countInfo, countInfoContents, countInfoTotalContents || 0)}
            </span>
          )}
          {maxcountInfo && (
            <span className="lineheight mr15">
              {this.addCountInfo(maxcountInfo, maxInfoContent)}
            </span>
          )}
          {type === 'checkbox' &&
            !disableAll && [
              <SBButton
                title="select_all"
                type="submit"
                name="selectAll"
                disabled={!countInfoContents}
                onClick={(event) => this.onSelectAll(event)}
              />,
              <SBButton
                title="clear_all"
                type="submit"
                name="clearAll"
                disabled={!countInfoContents}
                onClick={(event) => this.onClearAll(event)}
              />,
            ]}
          {filterList && [
            <span className="lineheight mr5">
              {this.languageLabelCheck(filterMessage || 'results_per_page')}
            </span>,
            <select
              name="cnt"
              size="1"
              defaultValue={this.defaultComboValue}
              onChange={(event) => this.handleSelectChange(event.target.value)}
            >
              {filterList.map((data, i) => (
                <option key={i.toString()} value={data.key}>
                  {this.languageLabelCheck(data.value)}
                </option>
              ))}
            </select>,
          ]}
        </span>
      </div>
    );
  }

  render() {
    const { columns, pagination, dataSource, hasHeightScroll, gridHeight } = this.props;
    const { tableData, enablePagination, dataCount } = this.state;
    const columnData = this.renderColumns(columns);
    const pageSize = Math.ceil(dataSource.length / dataCount);
    return (
      <div>
        {pagination && (
          <div className="clearfix mb10" style={{ height: 30 }}>
            {this.renderSelect()}
            {enablePagination && pageSize > 1 && this.renderpagination(pageSize)}
          </div>
        )}
        {dataSource.length ? (
          <div
            className="mb10"
            style={{
              ...styles.div,
              ...(hasHeightScroll ? { overflowY: 'scroll', height: gridHeight } : {}),
            }}
          >
            <table className="table-yoko">
              <tbody>
                <tr>{columnData}</tr>
                {tableData &&
                  tableData.map((data, i) => {
                    return (
                      <tr
                        key={i.toString()}
                        style={data.backgroundColor && { backgroundColor: data.backgroundColor }}
                      >
                        {columns.map((column, index) => {
                          return (
                            <td
                              key={index.toString()}
                              style={{ textAlign: column.textAlign, whiteSpace: column.whiteSpace }}
                            >
                              {column.render
                                ? column.render(data)
                                : this.renderData(data, column, i)}
                            </td>
                          );
                        })}
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
        ) : (
          ''
        )}
        {pagination && (
          <div className="clearfix mb10">
            {enablePagination && pageSize > 1 && this.renderpagination(pageSize)}
          </div>
        )}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    localeData: state.language.data,
  };
};

export default connect(mapStateToProps)(SBTable);
