import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { PropTypes } from 'prop-types';
import BcdmEn from '../../image/bcdm_en.png';
import LogOut from '../../image/hd_icon_logout.png';
import { get } from '../../../actions/commonActions';
import RouteEndPoints from '../../../constants/RouteEndPoints';
import APIEndPoints from '../../../constants/APIEndPoints';
import checkLanguage from '../../../language/checkLanguage';
import commonChangeAction, { commonOnChangeMenu } from '../../changeReducer/commonChangeAction';

const styles = {
  logout: {
    float: 'right',
    padding: '17px 15px 15px 20px',
  },
  image: {
    padding: '23px 15px 15px 15px',
  },
  info: {
    float: 'right',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    textAlign: 'right',
    fontSize: '93%',
    color: '#666',
  },
  button: {
    cursor: 'pointer',
    background: 'none',
    padding: 0,
    border: 'none',
    minWidth: 48,
    letterSpacing: 0,
  },
  row: {
    display: 'inline-grid',
    lineHeight: '18px',
    paddingTop: 18,
  },
};

class HeaderLayout extends Component {
  /**
   * handleLogout Method is used to sign out from EMM console
   */
  handleLogout = () => {
    const { history, userVo, setModified } = this.props;
    get(APIEndPoints.LOGOUT).then(() => {
      this.clearCacheData();
      setModified(false);
      if (userVo.authorityId === 4) history.push(RouteEndPoints.ISOC);
      else history.push(RouteEndPoints.LOGIN);
    });
  };

  /**
   * clearCacheData is to clear localStorage data
   */
  clearCacheData = () => {
    localStorage.removeItem('deviceSearch');
    localStorage.removeItem('idListParam');
  };

  languageLabelCheck = label => {
    const { isNotMultiLang, localeData } = this.props;
    return isNotMultiLang ? label : checkLanguage(localeData, label);
  };

  loadMainMenuClick = () => {
    const { onChangeHandler, isModified, history } = this.props;
    localStorage.removeItem('idListParam');
    if (isModified) {
      onChangeHandler({ open: true, onOk: () => history.push(RouteEndPoints.DASHBOARD) });
    } else {
      history.push(RouteEndPoints.DASHBOARD);
    }
  };

  handleKeyDown = event => {
    if (event.keyCode === 13 || event.keyCode === 32) {
      event.preventDefault();
      this.loadMainMenuClick();
    }
  };

  render() {
    const { noLogoutdisplay, userVo } = this.props;
    return (
      <div>
        <span
          style={{ cursor: 'pointer' }}
          tabIndex={0}
          role="button"
          onClick={() => this.loadMainMenuClick()}
          onKeyDown={this.handleKeyDown}
        >
          <img style={styles.image} src={BcdmEn} alt="bcdm_en" />
        </span>
        {!noLogoutdisplay && (
          <span>
            <span style={styles.logout}>
              <button
                type="button"
                id="logout"
                style={styles.button}
                onClick={() => this.handleLogout()}
              >
                {/* <a href="/" onClick={() => this.handleLogout()}> */}
                <img role="presentation" style={{ cursor: 'pointer' }} src={LogOut} alt="logout" />
                {/* </a> */}
              </button>
            </span>
            <span style={{ float: 'right' }}>
              <table>
                <tbody>
                  <tr style={styles.row}>
                    <td style={styles.info}>{`Dear ${userVo.businessName}`}</td>
                    <td style={{ ...styles.info, fontSize: '74.3%' }}>
                      {userVo.lastLoginTime
                        ? `${this.languageLabelCheck('last_login')} ${userVo.lastLoginTime}`
                        : ''}
                    </td>
                  </tr>
                </tbody>
              </table>
            </span>
          </span>
        )}
      </div>
    );
  }
}

HeaderLayout.contextTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }),
};

const mapPropsFromState = state => {
  const { userVo } = state.user;
  return {
    userVo,
    localeData: state.language.data,
    isModified: state.screenModified.isModified,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onChangeHandler: payload => dispatch(commonOnChangeMenu(payload)),
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
  };
};

export default connect(
  mapPropsFromState,
  mapDispatchToProps,
)(withRouter(HeaderLayout));
