import { isEmpty } from 'lodash';
/** Utility Files */
import { deviceComboData } from './deviceConstants';

function mapToFilterData(departmentList) {
  const filterData = [];
  filterData.push({});
  departmentList.forEach(function (data) {
    if (!isEmpty(data)) {
      const tempMap = { value: data, option: data };
      filterData.push(tempMap);
    }
  });
  return filterData;
}

