/**
 * contractConstants is used to validate contract screen fields
 * @author: P.Vinothpandian
 */

import { isNotUndefinedOrNotEmpty } from '../../../common/CommonValidations/CommonValidations';
import { validateMinMaxDate } from '../../../common/utils/dateUtils';
import { setValidationErrorMessage } from './AppErrorConstants';
import { all, underContract, trialend, cancellation } from './AppFieldConstants';

/**
 * validateFirstUseStartDate is used to validate firt use start date values
 * @param value
 * @return status
 */

function validateContractDate(value) {
  const dateRegex = /([12]\d{3}[/](0[1-9]|1[0-2])[/](0[1-9]|[12]\d|3[01]))/;
  const yearMonthRegex = /^(([12]\d{3}[/]([1-9]{1}))|([12]\d{3}[/](0[1-9]|1[0-2])))$/;
  const yearMonthRegexWithSlash = /^(([12]\d{3}[/]([1-9]{1})[/])|([12]\d{3}[/](0[1-9]|1[0-2])[/]))$/;
  const monthSingleRegex = /^([12]\d{3}[/]([1-9]{1})[/](0[1-9]|[12]\d|3[01]))$/;
  const dateSingleRegex = /^([12]\d{3}[/](0[1-9]|1[0-2])[/]([1-9]{1}))$/;
  const monthDateSingleRegex = /^([12]\d{3}[/]([1-9]{1})[/]([1-9]{1}))$/;
  const dateNumberRegex = /([12]\d{3}(0[1-9]|1[0-2])(0[1-9]|[12]\d|3[01]))/;
  const numberRegex = /^[0-9]+$/;
  const result = { status: true, error: '' };
  if (isNotUndefinedOrNotEmpty(value)) {
    if (
      (numberRegex.test(value) && dateNumberRegex.test(value.slice(0, 8))) ||
      monthSingleRegex.test(value) ||
      dateSingleRegex.test(value) ||
      yearMonthRegex.test(value) ||
      monthDateSingleRegex.test(value) ||
      yearMonthRegexWithSlash.test(value)
    ) {
      result.status = false;
      result.error = 'invalidformat';
    } else if (dateRegex.test(value)) {
      if (validateMinMaxDate(new Date(value))) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'range';
      }
    } else {
      result.status = false;
      result.error = 'invalid';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateUnitPriceBasic is used to validate unit basic price values
 * @param value
 * @return status
 */

function validateUnitPriceBasic(value) {
  const numberRegex = /^[0-9]+$/;
  const minLength = 0;
  const maxLength = 7;
  const result = { status: true, error: '' };
  if (value) {
    if (numberRegex.test(value)) {
      const valueLength = value.toString().length;
      if (valueLength >= minLength && valueLength <= maxLength) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'alpha';
    }
  } else {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateUnitPriceRental is used to validate unit basic rental values
 * @param value
 * @return status
 */

function validateUnitPriceRental(rentalPlanName, unitPriceRental) {
  const numberRegex = /^[0-9]+$/;
  const minLength = 0;
  const maxLength = 10;
  const result = { status: true, error: '' };
  if (unitPriceRental) {
    if (numberRegex.test(unitPriceRental)) {
      const valueLength = unitPriceRental.toString().length;
      if (valueLength >= minLength && valueLength <= maxLength) {
        result.status = true;
      } else {
        result.status = false;
        result.error = 'length';
      }
    } else {
      result.status = false;
      result.error = 'alpha';
    }
  } else if (rentalPlanName) {
    result.status = false;
    result.error = 'empty';
  }
  return result;
}

/**
 * validateInputs is used to validate maximum and minimum length validations
 * @param dataSource
 * @return tempDynamicData
 */

export function validateInputs(dataSource, isUpdate) {
  let tempDynamicData = {};
  const contractStatus = parseInt(dataSource.contractStatus, 10);
  if (contractStatus === all) {
    tempDynamicData.status = true;
    return tempDynamicData;
  }

  if (
    dataSource.androidDeviceCount !== null &&
    dataSource.androidDeviceCount !== undefined &&
    dataSource.androidDeviceCount !== '' &&
    parseInt(dataSource.androidDeviceCount, 10)
  ) {
    if (contractStatus !== all) {
      const firstUseStartDateValidation = validateContractDate(dataSource.firstUseStartDate);
      tempDynamicData = setValidationErrorMessage(
        'firstUseStartDateError',
        firstUseStartDateValidation,
        tempDynamicData,
      );

      const unitPriceBasicValidation =
        contractStatus === underContract
          ? validateUnitPriceBasic(dataSource.unitPriceBasic)
          : { status: true, error: '' };
      tempDynamicData = setValidationErrorMessage(
        'unitPriceBasicError',
        unitPriceBasicValidation,
        tempDynamicData,
      );
      const effectiveDateChangeValidation =
        contractStatus !== cancellation && isUpdate
          ? validateContractDate(dataSource.effectiveDateChange)
          : { status: true, error: '' };
      tempDynamicData = setValidationErrorMessage(
        'effectiveDateChangeError',
        effectiveDateChangeValidation,
        tempDynamicData,
      );

      const cancellationDateValidation =
        contractStatus === cancellation && isUpdate
          ? validateContractDate(dataSource.cancellationDate)
          : { status: true, error: '' };
      tempDynamicData = setValidationErrorMessage(
        'cancellationDateError',
        cancellationDateValidation,
        tempDynamicData,
      );

      const unitPriceRentalValidation = validateUnitPriceRental(
        dataSource.rentalPlanName,
        dataSource.unitPriceRental,
      );
      tempDynamicData = setValidationErrorMessage(
        'unitPriceRentalError',
        unitPriceRentalValidation,
        tempDynamicData,
      );

      tempDynamicData.status =
        firstUseStartDateValidation.status &&
        unitPriceBasicValidation.status &&
        effectiveDateChangeValidation.status &&
        cancellationDateValidation.status &&
        unitPriceRentalValidation.status;
      return tempDynamicData;
    }
    tempDynamicData.status = true;
    return tempDynamicData;
  }

  if (
    (dataSource.androidDeviceCount === '' || !parseInt(dataSource.androidDeviceCount, 10)) &&
    (contractStatus === trialend || contractStatus === cancellation)
  ) {
    const firstUseStartDateValidation = validateContractDate(dataSource.firstUseStartDate);
    tempDynamicData = setValidationErrorMessage(
      'firstUseStartDateError',
      firstUseStartDateValidation,
      tempDynamicData,
    );

    const effectiveDateChangeValidation =
      contractStatus !== cancellation && isUpdate
        ? validateContractDate(dataSource.effectiveDateChange)
        : { status: true, error: '' };
    tempDynamicData = setValidationErrorMessage(
      'effectiveDateChangeError',
      effectiveDateChangeValidation,
      tempDynamicData,
    );

    const cancellationDateValidation =
      contractStatus === cancellation && isUpdate
        ? validateContractDate(dataSource.cancellationDate)
        : { status: true, error: '' };
    tempDynamicData = setValidationErrorMessage(
      'cancellationDateError',
      cancellationDateValidation,
      tempDynamicData,
    );
    const unitPriceRentalValidation = validateUnitPriceRental(
      dataSource.rentalPlanName,
      dataSource.unitPriceRental,
    );
    tempDynamicData = setValidationErrorMessage(
      'unitPriceRentalError',
      unitPriceRentalValidation,
      tempDynamicData,
    );
    tempDynamicData.status =
      firstUseStartDateValidation.status &&
      effectiveDateChangeValidation.status &&
      cancellationDateValidation.status &&
      unitPriceRentalValidation.status;
    return tempDynamicData;
  }
  if (contractStatus !== all) {
    tempDynamicData = setValidationErrorMessage(
      'contractStatusError',
      { status: false, error: 'invalid' },
      tempDynamicData,
    );
    const firstUseStartDateValidation = validateContractDate(dataSource.firstUseStartDate);
    tempDynamicData = setValidationErrorMessage(
      'firstUseStartDateError',
      firstUseStartDateValidation,
      tempDynamicData,
    );
    const unitPriceBasicValidation =
      contractStatus === underContract
        ? validateUnitPriceBasic(dataSource.unitPriceBasic, 10)
        : { status: true, error: '' };
    tempDynamicData = setValidationErrorMessage(
      'unitPriceBasicError',
      unitPriceBasicValidation,
      tempDynamicData,
    );

    const effectiveDateChangeValidation =
      contractStatus !== cancellation && isUpdate
        ? validateContractDate(dataSource.effectiveDateChange)
        : { status: true, error: '' };
    tempDynamicData = setValidationErrorMessage(
      'effectiveDateChangeError',
      effectiveDateChangeValidation,
      tempDynamicData,
    );

    const cancellationDateValidation =
      contractStatus === cancellation && isUpdate
        ? validateContractDate(dataSource.cancellationDate)
        : { status: true, error: '' };
    tempDynamicData = setValidationErrorMessage(
      'cancellationDateError',
      cancellationDateValidation,
      tempDynamicData,
    );
    const unitPriceRentalValidation = validateUnitPriceRental(
      dataSource.rentalPlanName,
      dataSource.unitPriceRental,
    );
    tempDynamicData = setValidationErrorMessage(
      'unitPriceRentalError',
      unitPriceRentalValidation,
      tempDynamicData,
    );

    tempDynamicData.status = false;
    return tempDynamicData;
  }
  tempDynamicData.status = true;
  return tempDynamicData;
}

/**
 * setFocusinerrorField is used to set focus in error fields
 * @param dynamicData
 * @param reference
 */

export function setFocusinerrorField(dynamicData, reference) {
  if (dynamicData.contractStatusError && dynamicData.contractStatusError !== '') {
    reference.contractStatusRef.current.focus();
  } else if (dynamicData.firstUseStartDateError && dynamicData.firstUseStartDateError !== '') {
    reference.firstUseStartDateRef.current.focus();
  } else if (dynamicData.unitPriceBasicError && dynamicData.unitPriceBasicError !== '') {
    reference.unitPriceBasicRef.current.focus();
  }
}
