import { notification } from 'antd';

function setMessageType() {
  notification.config({
    placement: 'topRight',
    top: 80,
  });
}

export function showSuccessMessage(text, title = 'Success') {
  setMessageType();
  notification.success({
    message: title,
    description: text,
  });
}

export function showErrorMessage(text, title = 'Errors') {
  setMessageType();
  notification.error({
    message: title,
    description: text,
  });
}

export function showErrorMessageByNetworkResponse(error) {
  if (error && error.response) {
    const {response} = error;
    if (response.bodyUsed) {
      showErrorMessage(error.message);
    } else {
      response.json().then(errorResponse => {
        showErrorMessage(errorResponse.message);
      });
    }
  }
}
