/** Base Files */
import React, { Component, createRef } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';

/** SBComponents */
import SBDialog from '../../common/component/sbdialog/SBDialog';
import SBContentHeader from '../../common/component/contentheader/SBContentHeader';
import CircularSpinner from '../../common/component/loadingSpinner/CircularSpinner';

/** GroupManagement Components */
import GroupManagementAdd from '../component/GroupManagementAdd';
import GroupManagementGrid from '../component/GroupManagementGrid';
import GroupManagementFooter from '../component/GroupManagementFooter';

/** Utility Files */
import { get } from '../../actions/commonActions';
import APIENDPOINTS from '../../constants/APIEndPoints';
import { applicationListHeader } from '../constants/groupSettingHeaderConstants';
import { serviceMapper } from '../common/GroupSettingCommonFunctions';
import { Role } from '../../constants/Role';

/**
 * ApplicationList is to display list of application information
 * @author Aswin Palanisamy
 */

const resultsPerPageKey = 'application_list';
class ApplicationList extends Component {
  /**
   * constructor performs component state and ref initialization
   * @param props
   */
  constructor(props) {
    super(props);
    const { screenKey } = props;
    this.state = {
      screenKey,
      message: '',
      disable: false,
      addDisable: false,
      pageLoading: true,
      selectedRows: [],
      applicationList: [],
      countInfoContents: 0,
      openErrorDialog: false,
      headerData: applicationListHeader(screenKey),
      authorityRoleType: Role.group1,
    };
    this.addButtonRef = createRef();
  }

  /**
   * componentDidMount is to load list of approved application records
   */
  async componentDidMount() {
    const response = await get(APIENDPOINTS.APPLICATIONDATALIST);
    const result = await response.json();
    const { status, message, data: applicationList } = result;
    if (status === 'OK') {
      this.setState(
        {
          applicationList,
          selectedRows: !isEmpty(applicationList) ? applicationList[0] : [{}],
          pageLoading: false,
          countInfoContents: applicationList.length,
        },
        () => {
          if (this.addButtonRef.current) this.addButtonRef.current.focus();
        },
      );
    } else {
      this.setState({
        message,
        disable: true,
        addDisable: true,
        pageLoading: false,
        openErrorDialog: true,
      });
    }
  }

  /**
   * handleRadioChange performs to list the propert of each package
   * @param event
   */
  handleRadioChange = event => {
    this.setState({ selectedRows: event, disable: false });
  };

  /**
   * setSelectedRecord used to return the data index which need to get selected in checkbox.
   * @param selectedGridData
   */
  setSelectedRecord = () => {
    return [0];
  };

  /**
   * onHandleClick performs application approval page navigation
   * @param key
   */
  onHandleClick = key => {
    const { history, location: getData } = this.props;
    const { selectedRows } = this.state;
    history.push(serviceMapper(key), { ...getData.state, packageName: selectedRows.packageName });
  };

  /**
   * handlePaginationClick performs page change operation in grid
   * @param tableData
   */
  handlePaginationClick = tableData => {
    const { selectedRows } = this.state;
    const [record] = tableData;
    this.setState({ selectedRows: !isEmpty(tableData) ? record : selectedRows });
  };

  /**
   * render performs group management screen rendering
   * @return GroupManagement
   */
  render() {
    const {
      disable,
      message,
      screenKey,
      headerData,
      addDisable,
      pageLoading,
      openErrorDialog,
      applicationList,
      countInfoContents,
      authorityRoleType,
    } = this.state;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <SBContentHeader title={headerData.title} description={headerData.description} />
        <GroupManagementAdd
          disabled={addDisable}
          reference={this.addButtonRef}
          buttonKey="registerApplication"
          title="app_enrollment_and_deletion"
          onHandleClick={key => this.onHandleClick(key)}
        />
        <GroupManagementGrid
          screenKey={screenKey}
          gridList={applicationList}
          countInfo="registered_count_items"
          countInfoContents={countInfoContents}
          defaultSelectedKeys={this.setSelectedRecord()}
          handleRadioChange={(event, selectedRowKeys) =>
            this.handleRadioChange(event, selectedRowKeys)
          }
          handlePaginationClick={tableData => this.handlePaginationClick(tableData)}
          defaultSelected={resultsPerPageKey}
        />
        <GroupManagementFooter
          footer={{
            disable,
            screenKey,
            authorityRoleType,
          }}
          onHandleClick={key => this.onHandleClick(key)}
        />
        <SBDialog
          type="error"
          header="error"
          content={message}
          okButtonTitle="ok"
          open={openErrorDialog}
          onOk={() => {
            this.setState({ openErrorDialog: false }, () => {
              if (!addDisable && this.addButtonRef.current) this.addButtonRef.current.focus();
            });
          }}
          width="400px"
        />
      </CircularSpinner>
    );
  }
}

/**
 * propTypes is used for validation props with its type.
 */
ApplicationList.propTypes = {
  screenKey: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string,
    state: PropTypes.shape({
      mode: PropTypes.number,
      groupId: PropTypes.number,
      packageName: PropTypes.string,
    }),
  }).isRequired,
};

export default withRouter(ApplicationList);
