/**
 * validateFileSize is used to validate file size
 * @param file
 * @param maxFileSize
 * @return validationResult
 */

function validateFileSize(file, maxFileSize) {
  const validationResult = { status: true, error: '' };
  if (Math.ceil(file.size / 1048576) > maxFileSize) {
    validationResult.status = false;
    validationResult.error = 'fileSizeGreater';
  }
  return validationResult;
}

/**
 * validateFileExtention is used to validate file extension
 * @param fileName
 * @return validationResult
 */

function validateFileExtention(fileName) {
  const validationResult = { status: true, error: '' };
  const lowerCasedFileName = fileName.toLowerCase();
  if (
    lowerCasedFileName.endsWith('.xlsx') ||
    lowerCasedFileName.endsWith('.pptx') ||
    lowerCasedFileName.endsWith('.doc') ||
    lowerCasedFileName.endsWith('.csv') ||
    lowerCasedFileName.endsWith('.txt') ||
    lowerCasedFileName.endsWith('.pdf')
  ) {
    validationResult.status = true;
  } else {
    validationResult.status = false;
    validationResult.error = 'fileExtension';
  }
  return validationResult;
}

/**
 * validateFile is used to validate file and all other params
 * @param file
 * @param maxFileSize
 * @return validationResult
 */

export function validateFile(file, maxFileSize) {
  const validationResult = { status: true, error: '' };
  if (file) {
    const fileTypeValidation = validateFileExtention(file.name);
    if (!fileTypeValidation.status) {
      return fileTypeValidation;
    }
    const sizeValidationResult = validateFileSize(file, maxFileSize);
    if (!sizeValidationResult.status) {
      return sizeValidationResult;
    }
    if (file.name.length > 80) {
      validationResult.status = false;
      validationResult.error = 'fileNameLength';
      return validationResult;
    }
  } else {
    validationResult.status = false;
    validationResult.error = 'nofile';
  }
  return validationResult;
}

/**
 * validateInputs is used to validate fields
 * @param dataSource
 * @return tempDynamicData
 */

export function validateInputs(dataSource) {
  let tempDynamicData = {};
  const fullNameOneValidation = validateLength(dataSource.fullNameOne, 100);
  const fullNameTwoValidation = validateLength(dataSource.fullNameTwo, 100);
  const fullNameThreeValidation = validateLength(dataSource.fullNameThree, 100);
  const contractNameOneValidation = validateLength(dataSource.contactInfoOne, 100);
  const contractNameTwoValidation = validateLength(dataSource.contactInfoTwo, 100);
  const contractNameThreeValidation = validateLength(dataSource.contactInfoThree, 100);
  const emailIdOneValidation = validateEmailId(dataSource.emailOne);
  const emailIdTwoValidation = validateEmailId(dataSource.emailTwo);
  const emailIdThreeValidation = validateEmailId(dataSource.emailThree);
  const departNameOneValidation = validateLength(dataSource.departmentNameOne, 100);
  const departNameTwoValidation = validateLength(dataSource.departmentNameTwo, 100);
  const departNameThreeValidation = validateLength(dataSource.departmentNameThree, 100);
  tempDynamicData = setValidationErrorMessage(
    'fullName1Error',
    fullNameOneValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'fullName2Error',
    fullNameTwoValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'fullName3Error',
    fullNameThreeValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'contactInfo1Error',
    contractNameOneValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'contactInfo2Error',
    contractNameTwoValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'contactInfo3Error',
    contractNameThreeValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage('email1Error', emailIdOneValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage('email2Error', emailIdTwoValidation, tempDynamicData);
  tempDynamicData = setValidationErrorMessage(
    'email3Error',
    emailIdThreeValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'departmentName1Error',
    departNameOneValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'departmentName2Error',
    departNameTwoValidation,
    tempDynamicData,
  );
  tempDynamicData = setValidationErrorMessage(
    'departmentName3Error',
    departNameThreeValidation,
    tempDynamicData,
  );

  let companiesDeclaredValidation = { status: true };
  let identificationRemarksValidation = { status: true };
  if (dataSource.declarationNonContractor) {
    companiesDeclaredValidation = remarksValidation(dataSource.companiesDeclared, 500);
    identificationRemarksValidation = remarksValidation(dataSource.identificationRemarks, 500);
    tempDynamicData = setValidationErrorMessage(
      'companiesDeclaredError',
      companiesDeclaredValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'identificationRemarksError',
      identificationRemarksValidation,
      tempDynamicData,
    );
  }
  let companyNameValidation = { status: true };
  let postalCodeValidation = { status: true };
  let streetAddressValidation = { status: true };
  let phoneNumberValidation = { status: true };
  let personInChargeValidation = { status: true };
  let contactNameValidation = { status: true };
  let alternativeDeviceRemarksValidation = { status: true };
  if (dataSource.deviceDestinationFixed) {
    companyNameValidation = validateLength(dataSource.companyName, 200);
    postalCodeValidation = validatePhoneNumberorPostalCode(dataSource.postalCode, 10);
    streetAddressValidation = validateLength(dataSource.streetAddress, 200);
    phoneNumberValidation = validatePhoneNumberorPostalCode(dataSource.phoneNumber, 20);
    personInChargeValidation = validateLength(dataSource.personInChargeDeptName, 200);
    contactNameValidation = validateLength(dataSource.contactName, 50);
    alternativeDeviceRemarksValidation = remarksValidation(
      dataSource.alternativeDeviceRemarks,
      500,
    );
    tempDynamicData = setValidationErrorMessage(
      'companyNameError',
      companyNameValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'postalCodeError',
      postalCodeValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'streetAddressError',
      streetAddressValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'phoneNumberError',
      phoneNumberValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'personInchargeNameError',
      personInChargeValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'contactNameError',
      contactNameValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'alternativeDeviceRemarksError',
      alternativeDeviceRemarksValidation,
      tempDynamicData,
    );
  }

  let receptionistInformationValidation = { status: true };
  let receptionRemarksValidation = { status: true };
  if (dataSource.alternativeReceptionFixed) {
    receptionistInformationValidation = remarksValidation(dataSource.receptionistInformation, 500);
    tempDynamicData = setValidationErrorMessage(
      'receptionistInformationError',
      receptionistInformationValidation,
      tempDynamicData,
    );
    receptionRemarksValidation = remarksValidation(dataSource.receptionRemarks, 500);
    tempDynamicData = setValidationErrorMessage(
      'receptionRemarksError',
      receptionRemarksValidation,
      tempDynamicData,
    );
  }
  let operationMemoOneValidation = { status: true };
  let operationMemoTwoValidation = { status: true };
  let operationMemoThreeValidation = { status: true };
  let operationMemoFourValidation = { status: true };
  let operationMemoFiveValidation = { status: true };
  if (dataSource.existenceSpecialNotes) {
    operationMemoOneValidation = remarksValidation(dataSource.operationMemoOne, 500);
    operationMemoTwoValidation = remarksValidation(dataSource.operationMemoTwo, 500);
    operationMemoThreeValidation = remarksValidation(dataSource.operationMemoThree, 500);
    operationMemoFourValidation = remarksValidation(dataSource.operationMemoFour, 500);
    operationMemoFiveValidation = remarksValidation(dataSource.operationMemoFive, 500);
    tempDynamicData = setValidationErrorMessage(
      'operationMemo1Error',
      operationMemoOneValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'operationMemo2Error',
      operationMemoTwoValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'operationMemo3Error',
      operationMemoThreeValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'operationMemo4Error',
      operationMemoFourValidation,
      tempDynamicData,
    );
    tempDynamicData = setValidationErrorMessage(
      'operationMemo5Error',
      operationMemoFiveValidation,
      tempDynamicData,
    );
  }

  tempDynamicData.status =
    fullNameOneValidation.status &&
    fullNameTwoValidation.status &&
    fullNameThreeValidation.status &&
    contractNameOneValidation.status &&
    contractNameTwoValidation.status &&
    contractNameThreeValidation.status &&
    emailIdOneValidation.status &&
    emailIdTwoValidation.status &&
    emailIdThreeValidation.status &&
    departNameOneValidation.status &&
    departNameTwoValidation.status &&
    departNameThreeValidation.status &&
    companiesDeclaredValidation.status &&
    identificationRemarksValidation.status &&
    companyNameValidation.status &&
    postalCodeValidation.status &&
    streetAddressValidation.status &&
    phoneNumberValidation.status &&
    personInChargeValidation.status &&
    contactNameValidation.status &&
    alternativeDeviceRemarksValidation.status &&
    receptionistInformationValidation.status &&
    receptionRemarksValidation.status &&
    operationMemoOneValidation.status &&
    operationMemoTwoValidation.status &&
    operationMemoThreeValidation.status &&
    operationMemoFourValidation.status &&
    operationMemoFiveValidation.status;
  return tempDynamicData;
}
