import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {
  getSecurityPolicyGroupList,
  setSecurityPolicyDetail,
} from '../../../../actions/securityPolicyAction';
import commonChangeAction from '../../../../common/changeReducer/commonChangeAction';
import SBContentHeader from '../../../../common/component/contentheader/SBContentHeader';
import SBDialog from '../../../../common/component/sbdialog/SBDialog';
import headerInformation from '../constants/HeaderConstant';
import CircularSpinner from '../../../../common/component/loadingSpinner/CircularSpinner';
import CustomTableFrame from '../../../../v1/common/component/CustomTableFrame';
import SecurityPolicyFooter from '../component/SecurityPolicyFooter';
import {
  secuirtyPolicyName,
  androidOsVersion,
  androidApplication,
  setRegisterReference,
  osVersionFilter,
  applicationFilter,
  antiVirusFrame,
  assignTarget,
  imeiTableList,
} from '../constants/TableConstant';
import { errorMessageConstants } from '../constants/MessageConstant';
import {
  setFocusinerrorField,
  validateInputs,
  setOsVersionList,
  setApplicationList,
  validateList,
  validateAppList,
  validateFile,
  validateFileCheck,
} from '../constants/InputValidation';
import { postWithFormData, get } from '../../../../actions/commonActions';
import UNDERSCORE from '../../../../v1/common/utils';
import ROUTEENDPOINTS from '../../../../constants/RouteEndPoints';
import APIEndPoints from '../../../../constants/APIEndPoints';
import SBInfoContent from '../../../../common/component/SBInfoContent';
import { isNotUndefinedOrNotEmpty } from '../../../../common/CommonValidations/CommonValidations';

const fileDownload = require('js-file-download');

class SecurityPolicyRegistration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageLoading: true,
      dynamicData: {},
      openDialogError: false,
      dataSource: { antivirusCheck: 1 },
      osFieldDataList: [],
      appFieldDataList: [],
      selectedGroupList: [],
      fileDataSource: null,
      disabled: false,
      groupAssigned: false,
      osVersion: setOsVersionList({}),
      applicationList: setApplicationList({}),
      reference: setRegisterReference(),
    };
  }

  componentDidMount() {
    const { reference } = this.state;
    let dataSource = this.state;
    const {
      securityData,
      setModified,
      update,
      location,
      isModified,
      makeSecurityPolicyDetail,
    } = this.props;
    const { groupData, groupUpdated } = securityData;
    if (update && !isNotUndefinedOrNotEmpty(securityData) && !isNotUndefinedOrNotEmpty(groupData)) {
      const { param } = location && location.state ? location.state : {};
      const securityPolicyId = param && param.securityPolicyId ? param.securityPolicyId : -1;

      const getSecurityPolicyDetail = async () => {
        const response = await get(`${APIEndPoints.SECURITY_POLICY_DETAIL}/${securityPolicyId}`);
        const responseData = await response.json();
        responseData.applicationList.map(value => ({
          checkMethod: value.applicationCheckMethod,
          applicationName: value.applicationName,
        }));
        await this.setResponseData(responseData);
        await makeSecurityPolicyDetail(responseData);
      };
      getSecurityPolicyDetail();
    }
    if (isNotUndefinedOrNotEmpty(securityData) && isNotUndefinedOrNotEmpty(groupData)) {
      const { securityPolicyDetail, userVo } = this.props;
      const assignData = [];
      setModified(groupUpdated || isModified);
      const allGroup = groupData.filter(data => data.groupId === 0);
      groupData.sort((a, b) => (a.groupName > b.groupName ? 1 : -1));
      groupData.filter(data => data.groupId !== 0).forEach(data => assignData.push(data.groupName));
      dataSource = securityData.dataSource;
      if (isNotUndefinedOrNotEmpty(allGroup)) {
        dataSource.assignTarget = allGroup[0].groupName
          .concat(assignData.length ? ',' : '')
          .concat(assignData.join(','));
      } else {
        dataSource.assignTarget = assignData.join(',');
      }
      dataSource.allGroup = !!allGroup.length;
      this.setState({
        dataSource,
        pageLoading: false,
        disabled:
          securityPolicyDetail && securityPolicyDetail.groupAssigned
            ? userVo.loginId !== securityPolicyDetail.createdBy
            : false,
        selectedGroupList: groupData,
        osVersion: securityData.osVersion,
        applicationList: securityData.applicationList,
      });
    } else if (isNotUndefinedOrNotEmpty(securityData) && isNotUndefinedOrNotEmpty(dataSource)) {
      const { securityPolicyDetail, userVo } = this.props;
      dataSource = securityData.dataSource;
      dataSource.allGroup = false;
      dataSource.assignTarget = '';
      setModified(groupUpdated || isModified);
      this.setState({
        dataSource,
        pageLoading: false,
        osVersion: securityData.osVersion,
        applicationList: securityData.applicationList,
        disabled:
          securityPolicyDetail && securityPolicyDetail.groupAssigned
            ? userVo.loginId !== securityPolicyDetail.createdBy
            : false,
      });
    } else if (!update) {
      this.setState(
        {
          pageLoading: false,
        },
        () => {
          if (reference.securityPolicyNameRef.current)
            reference.securityPolicyNameRef.current.focus();
        },
      );
    }
    if (reference.securityPolicyNameRef.current) reference.securityPolicyNameRef.current.focus();
  }

  componentWillUnmount() {
    const { makeSecurityPolicyGroupList: setGroupListSelected } = this.props;
    setGroupListSelected({});
  }

  onHandleClick = key => {
    switch (key) {
      case 'save':
        if (this.validateData()) {
          this.onOkClick();
        }
        break;
      case 'cancel':
        this.onCancelClick();
        break;
      default:
        break;
    }
  };

  onKeyDown = event => {
    const { isModified } = this.props;
    if (event.keyCode === 13 && isModified) {
      event.preventDefault();
      this.onHandleClick('save');
    }
  };

  /**
   * getUploadedFile is used to get the uploaded file from input field
   * @param key
   * @param event
   */
  getUploadedFile = event => {
    let { fileDataSource } = this.state;
    const { setModified } = this.props;
    setModified(true);
    [fileDataSource] = event.target.files;
    this.setState({ fileDataSource });
  };

  setResponseData = responseData => {
    const { reference } = this.state;
    const { userVo } = this.props;
    let dataSource = this.state;
    if (responseData && responseData.policyId) {
      const targetGroupName =
        responseData.groupList && responseData.groupList.length
          ? responseData.groupList.join(',')
          : '';
      const osVersion = {
        androidOSVersionCheck: responseData.osVersionCheck,
        androidOSList: responseData.androidOSVersionList,
      };
      const checkValue = responseData.checkMethod === 'WhiteList';
      const selectApplicationData =
        responseData &&
        responseData.applicationList &&
        responseData.applicationList.filter(
          appInfo => appInfo.applicationCheckMethod === checkValue,
        ).length
          ? responseData.applicationList.filter(
              appInfo => appInfo.applicationCheckMethod === checkValue,
            )[0]
          : {};
      const applicationList = {
        androidApplicationCheck: responseData.applicationCheck,
        androidApplicationCheckMethod: responseData.checkMethod,
        androidApplicationList: responseData.applicationList,
        selectApplication: selectApplicationData.applicationName ? selectApplicationData : '',
        selectApplicationData,
      };

      dataSource = {
        allGroup: responseData.allGroup,
        antivirusCheck: responseData.antivirusCheck,
        assignTarget: targetGroupName,
        imeiCheck: responseData.imeiCheck,
        imeiList: responseData.imeiList,
        imeiFileName:
          responseData.imeiList && responseData.imeiFileName ? responseData.imeiFileName : '',
        imeiFile: 'IMEI'.concat('\r\n').concat(responseData.imeiList.join('\r\n')),
        securityPolicyName: responseData.policyName,
        securityPolicyId: responseData.policyId,
      };
      this.setState(
        {
          dataSource,
          pageLoading: false,
          osVersion,
          groupAssigned: responseData.groupAssigned,
          disabled: responseData.groupAssigned ? userVo.loginId !== responseData.createdBy : false,
          selectedGroupList: responseData.selectedGroup,
          applicationList,
        },
        () => {
          if (reference.securityPolicyNameRef.current)
            reference.securityPolicyNameRef.current.focus();
        },
      );
    } else {
      this.setState(
        {
          pageLoading: false,
          openDialogError: true,
        },
        () => {
          if (reference.securityPolicyNameRef.current)
            reference.securityPolicyNameRef.current.focus();
        },
      );
    }
  };
  /**
   * onUploadClick is used to make the data ready to save in database
   */

  onUploadClick = () => {
    const { dataSource, reference } = this.state;
    let { fileDataSource } = this.state;
    let { dynamicData } = this.state;
    dynamicData = {};
    const attachmentFileResult = validateFile(fileDataSource);
    if (attachmentFileResult.status) {
      this.setState({ pageLoading: true });
      const validateFileContent = async () => {
        const formData = new FormData();
        formData.append('imeiFile', fileDataSource);
        const response = await postWithFormData(`${APIEndPoints.SECURITY_POLICY_UPLOAD}`, formData);
        const responseData = await response.json();
        if (responseData.status) {
          dataSource.imeiFile = fileDataSource;
          dataSource.imeiFileName = fileDataSource.name;
          dataSource.imeiList = responseData.imeiList;
          fileDataSource = null;
          this.setState({
            dynamicData,
            dataSource,
            fileDataSource,
            pageLoading: false,
          });
        } else {
          fileDataSource = null;
          dynamicData.uploadError = responseData.message;
          this.setState({ fileDataSource, pageLoading: false, dynamicData });
        }
        if (reference.uploadRef.current) reference.uploadRef.current.focus();
      };
      validateFileContent();
    } else {
      fileDataSource = null;
      dynamicData = this.setValidationErrorMessage(
        'uploadError',
        attachmentFileResult,
        'uploadRef',
      );
      this.setState({ fileDataSource });
    }
    if (reference.uploadRef.current) {
      reference.uploadRef.current.value = '';
    }
  };

  onTableFrameEvent = (key, event, value) => {
    if (key === 'btnAssignTarget') {
      this.callService(key);
    } else if (key === 'imeiFile') {
      this.getUploadedFile(event);
    } else if (key === 'imeiUpload') {
      this.onUploadClick();
    } else if (key === 'imeidownload') {
      this.downloadFile();
    } else if (key === 'addOsVersion') {
      this.createOSList();
    } else if (key === 'addApplication') {
      this.createAppList();
    } else if (key === 'del_os_version') {
      this.removeOSFromList();
    } else if (key === 'del_application') {
      this.removeAppFromList();
    } else if (key === 'delall_application') {
      this.removeAllAppList();
    } else if (key === 'androidOSVersion') {
      const { osVersion } = this.state;
      osVersion[key] = event.target.type === 'text' ? value : event.target.value;
      this.setState({ osVersion });
    } else if (key === 'androidApplication') {
      const { applicationList } = this.state;
      applicationList[key] = event.target.type === 'text' ? value : event.target.value;
      this.setState({ applicationList });
    } else if (key === 'androidOSVersionCheck') {
      const { osVersion } = this.state;
      osVersion[key] = value;
      this.setState({ osVersion });
    } else if (key === 'androidApplicationCheckMethod' || key === 'androidApplicationCheck') {
      const { applicationList } = this.state;
      const checkValue = value === 'WhiteList';
      const selectApplicationData =
        applicationList &&
        applicationList.androidApplicationList &&
        applicationList.androidApplicationList.filter(
          appInfo => appInfo.applicationCheckMethod === checkValue,
        ).length
          ? applicationList.androidApplicationList.filter(
              appInfo => appInfo.applicationCheckMethod === checkValue,
            )[0]
          : {};
      applicationList.selectApplication = selectApplicationData.applicationName
        ? selectApplicationData
        : '';
      applicationList.selectApplicationData = selectApplicationData;
      applicationList[key] = value;
      this.setState({ applicationList });
    } else if (key === 'selectOSVersion') {
      const { osVersion } = this.state;
      osVersion[key] = event.target.value;
      this.setState({ osVersion });
    } else if (key === 'selectApplication') {
      const { applicationList } = this.state;
      const checkMethodStatus = applicationList.androidApplicationCheckMethod === 'WhiteList';
      const selectRecord = applicationList.androidApplicationList.filter(data => {
        if (checkMethodStatus === data.applicationCheckMethod)
          return data.applicationName === event.target.value;
        return false;
      })[0];
      applicationList[key] = selectRecord.applicationName;
      applicationList.selectApplicationData = selectRecord;
      this.setState({ applicationList });
    } else if (key === 'imeiCheck' || key === 'antivirusCheck') {
      const { dataSource } = this.state;
      dataSource[key] = value;
      this.setState({ dataSource });
    } else {
      const { dataSource } = this.state;
      dataSource[key] = event.target.type === 'text' ? value : event.target.value;
      this.setState({ dataSource });
    }
  };

  onRadioChange = (radioKey, key, value, defaultValue) => {
    const { dataSource } = this.state;
    if (value === 0) dataSource[key] = defaultValue;
    else dataSource[key] = '';
    dataSource[radioKey] = value;
    this.setState({ dataSource });
  };

  setValidationErrorMessage(key, validationResultObject, refKey) {
    let { dynamicData } = this.state;
    const { reference } = this.state;
    dynamicData = {};
    dynamicData[key] = errorMessageConstants[key][validationResultObject.error];
    this.setState({ dynamicData });
    if (dynamicData[key] && dynamicData[key] !== '') {
      reference[refKey].current.focus();
    }
  }

  /**
   * downloadFile is used to handle download button click
   * @param key
   */

  downloadFile = () => {
    const { dataSource } = this.state;
    const fileToDownload = dataSource.imeiFile;
    const fileName = fileToDownload.name ? fileToDownload.name : dataSource.imeiFileName;
    fileDownload(fileToDownload, fileName);
  };

  validateData = () => {
    const { dynamicData, dataSource, fileDataSource, reference } = this.state;
    const validationResult = validateInputs(dataSource, fileDataSource, dynamicData);
    this.setState({
      fileDataSource: null,
      dynamicData: validationResult,
    });
    setFocusinerrorField(validationResult, reference);
    return validationResult.status;
  };

  validateFileData = () => {
    const { dynamicData, fileDataSource, reference } = this.state;
    const validationResult = validateFileCheck(fileDataSource, dynamicData);
    this.setState({
      fileDataSource: null,
      dynamicData: validationResult,
    });
    setFocusinerrorField(validationResult, reference);
    return validationResult.status;
  };

  performOSValidations = () => {
    const { osVersion } = this.state;
    const androidOSValidation = validateList(osVersion.androidOSVersion, osVersion.androidOSList);
    return androidOSValidation;
  };

  callService = () => {
    const { history, securityData, update } = this.props;
    const { groupData } = securityData;
    const { dataSource, osVersion, applicationList, selectedGroupList } = this.state;
    applicationList.androidApplication = '';
    osVersion.androidOSVersion = '';
    this.setState({ applicationList, osVersion });
    if (this.validateFileData()) {
      history.push(ROUTEENDPOINTS.SECURITYPOLICYGROUPTARGET, {
        param: { dataSource, osVersion, applicationList, groupData, update, selectedGroupList },
      });
    }
  };

  performAppValidations = () => {
    const { applicationList } = this.state;
    const appNameList = [];
    applicationList.androidApplicationList
      .filter(appInfo =>
        applicationList.androidApplicationCheckMethod === 'WhiteList'
          ? appInfo.applicationCheckMethod
          : !appInfo.applicationCheckMethod,
      )
      .forEach(data => appNameList.push(data.applicationName));
    const androidAppValidation = validateAppList(applicationList.androidApplication, appNameList);
    return androidAppValidation;
  };

  createAppList = () => {
    let { applicationList } = this.state;
    const { androidApplicationList: dataList } = applicationList;
    const { reference } = this.state;
    const inputStatus = this.performAppValidations();
    if (inputStatus.status) {
      const checkStatus = applicationList.androidApplicationCheckMethod === 'WhiteList';
      dataList.push({
        applicationName: applicationList.androidApplication,
        applicationCheckMethod: checkStatus,
      });
      const selectApplication = dataList.filter(
        data => data.applicationCheckMethod === checkStatus,
      );
      applicationList = {
        androidApplication: '',
        androidApplicationList: dataList,
        selectApplication: selectApplication.length ? selectApplication[0].applicationName : '',
        selectApplicationData: selectApplication.length ? selectApplication[0] : {},
        androidApplicationCheckMethod: applicationList.androidApplicationCheckMethod,
        androidApplicationCheck: applicationList.androidApplicationCheck,
      };
      this.setState({
        dynamicData: {},
        applicationList,
        appFieldDataList: androidApplication({}, reference),
      });
    } else {
      const dynamicData = {};
      dynamicData.androidApplicationError =
        errorMessageConstants.androidApplicationError[inputStatus.error];
      if (inputStatus.error === 'samecontent') {
        applicationList.selectApplication = applicationList.androidApplication;
        applicationList.selectApplicationData = applicationList.androidApplication;
        this.setState({ applicationList });
      }
      this.setState({
        appFieldDataList: androidApplication(dynamicData, reference),
        dynamicData,
      });
      reference.textApplicationRef.current.focus();
    }
  };

  createOSList = () => {
    let { osVersion } = this.state;
    const { androidOSList: dataList } = osVersion;
    const { reference } = this.state;
    const inputStatus = this.performOSValidations();
    if (inputStatus.status) {
      dataList.push(osVersion.androidOSVersion);
      osVersion = {
        androidOSVersion: '',
        androidOSList: dataList,
        selectOSVersion: osVersion.androidOSVersion,
        androidOSVersionCheck: osVersion.androidOSVersionCheck,
      };
      this.setState({
        dynamicData: {},
        osVersion,
        osFieldDataList: androidOsVersion({}, reference),
      });
      this.setState({ osFieldDataList: [] });
    } else {
      const dynamicData = {};
      dynamicData.androidOSVersionValueError =
        errorMessageConstants.androidOSVersionValueError[inputStatus.error];
      if (inputStatus.error === 'samecontent') {
        osVersion.selectOSVersion = osVersion.androidOSVersion;
        this.setState({ osVersion });
      }
      this.setState({ dynamicData, osFieldDataList: androidOsVersion(dynamicData, reference) });
      reference.textOSVersionRef.current.focus();
    }
  };

  loadConfirmationScreen = saveData => {
    this.setState({ pageLoading: true });
    const { reference, dynamicData } = this.state;
    const { history, update, setModified } = this.props;
    const showData = saveData;
    const formData = new FormData();
    formData.append('imeiFile', saveData.imeiFile);
    formData.append('policyName', saveData.policyName);
    formData.append('policyId', saveData.policyId);
    const checkData = async () => {
      const response = await postWithFormData(APIEndPoints.SECURITY_POLICY_CHECK, formData);
      const resultSet = await response.json();
      if (resultSet.status) {
        const { dataSource, osVersion, applicationList, groupAssigned } = this.state;
        showData.imeiList = dataSource.imeiList ? dataSource.imeiList : [];
        showData.imeiFileName = dataSource.imeiList ? dataSource.imeiFileName : '';
        const { selectedGroupList } = this.state;
        applicationList.androidApplication = '';
        osVersion.androidOSVersion = '';
        this.setState({ applicationList, osVersion });
        history.push(ROUTEENDPOINTS.SECURITYPOLICYVIEW, {
          param: {
            dataSource,
            osVersion,
            applicationList,
            groupData: selectedGroupList,
            update,
            groupAssigned,
          },
          showData,
        });
      } else if (resultSet.message === 'data_exist') {
        dynamicData.securityPolicyNameError =
          errorMessageConstants.securityPolicyNameError.samecontent;
        this.setState({
          pageLoading: false,
          dynamicData,
        });
        reference.securityPolicyNameRef.current.focus();
      } else if (resultSet.message === 'data_not_exist') {
        setModified(false);
        this.setState({
          pageLoading: false,
          openDialogError: true,
        });
      }
    };

    checkData();
  };

  onOkClick = () => {
    const { dataSource, osVersion, applicationList } = this.state;
    const saveData = {};

    saveData.policyId = dataSource.securityPolicyId ? dataSource.securityPolicyId : -1;
    saveData.policyName = dataSource.securityPolicyName;
    saveData.osVersionCheck = !!osVersion.androidOSVersionCheck;
    saveData.androidOSVersionList = osVersion.androidOSList ? osVersion.androidOSList : [];
    saveData.applicationCheck = !!applicationList.androidApplicationCheck;
    saveData.applicationList = applicationList.androidApplicationList
      ? applicationList.androidApplicationList
      : [];
    saveData.checkMethod = applicationList.androidApplicationCheckMethod
      ? applicationList.androidApplicationCheckMethod
      : 'WhiteList';
    saveData.imeiCheck = !!dataSource.imeiCheck;
    saveData.imeiFile = dataSource.imeiFile;
    saveData.assignTarget = dataSource.assignTarget;
    saveData.antivirusCheck = dataSource.antivirusCheck;
    saveData.allGroup = dataSource.allGroup;
    this.loadConfirmationScreen(saveData);
  };

  onCancelClick = () => {
    const { history } = this.props;
    history.push(ROUTEENDPOINTS.SECURITYPOLICY);
  };

  removeOSFromList = () => {
    const { reference, osVersion } = this.state;
    const { setModified } = this.props;
    const key = 'androidOSVersionError';
    let { dynamicData } = this.state;
    dynamicData = {};
    if (
      osVersion &&
      (!UNDERSCORE.isUndefined(osVersion.selectOSVersion) ||
        !UNDERSCORE.isEmpty(osVersion.selectOSVersion))
    ) {
      dynamicData = {};
      osVersion.androidOSList = UNDERSCORE.without(
        osVersion.androidOSList,
        osVersion.selectOSVersion,
      );
      osVersion.selectOSVersion =
        osVersion && osVersion.androidOSList && osVersion.androidOSList.length
          ? osVersion.androidOSList[0]
          : '';
      this.setState({
        dynamicData,
        osVersion,
        osFieldDataList: androidOsVersion(dynamicData, reference),
      });
      setModified(true);
      if (osVersion.androidOSList.length === 0 && reference.textOSVersionRef.current)
        reference.textOSVersionRef.current.focus();
    } else {
      dynamicData = {
        androidApplicatoinSelError: '',
        androidOSVersionError: errorMessageConstants[key].selectData,
      };
      this.setState({
        dynamicData,
        appFieldDataList: androidApplication(dynamicData, reference),
        osFieldDataList: androidOsVersion(dynamicData, reference),
      });
    }
  };

  removeAllAppList = () => {
    const { reference, applicationList } = this.state;
    const { setModified } = this.props;
    const dynamicData = {};
    const checkValue = applicationList.androidApplicationCheckMethod === 'WhiteList';
    applicationList.androidApplicationList = applicationList.androidApplicationList.filter(
      value => value.applicationCheckMethod !== checkValue,
    );
    this.setState({
      dynamicData,
      applicationList,
      appFieldDataList: androidApplication(dynamicData, reference),
    });
    setModified(true);
    if (reference.textApplicationRef.current) reference.textApplicationRef.current.focus();
  };

  removeAppFromList = () => {
    const { reference, applicationList } = this.state;
    const { setModified } = this.props;
    const key = 'androidApplicatoinSelError';
    let { dynamicData } = this.state;
    dynamicData = {};
    if (
      applicationList &&
      (!UNDERSCORE.isUndefined(applicationList.selectApplication) ||
        !UNDERSCORE.isEmpty(applicationList.selectApplication))
    ) {
      dynamicData = {};
      applicationList.androidApplicationList = applicationList.androidApplicationList.filter(
        value => {
          return !UNDERSCORE.isEqual(value, applicationList.selectApplicationData);
        },
      );
      const checkMethodStatus = applicationList.androidApplicationCheckMethod === 'WhiteList';
      applicationList.selectApplication =
        applicationList &&
        applicationList.androidApplicationList &&
        applicationList.androidApplicationList.filter(
          value => value.applicationCheckMethod === checkMethodStatus,
        ).length
          ? applicationList.androidApplicationList.filter(
              value => value.applicationCheckMethod === checkMethodStatus,
            )[0]
          : '';
      applicationList.selectApplicationData =
        applicationList &&
        applicationList.androidApplicationList &&
        applicationList.androidApplicationList.filter(
          value => value.applicationCheckMethod === checkMethodStatus,
        ).length
          ? applicationList.androidApplicationList.filter(
              value => value.applicationCheckMethod === checkMethodStatus,
            )[0]
          : {};
      this.setState({
        dynamicData,
        applicationList,
        appFieldDataList: androidApplication(dynamicData, reference),
      });
      setModified(true);
      if (
        applicationList.androidApplicationList.length === 0 &&
        reference.textApplicationRef.current
      )
        reference.textApplicationRef.current.focus();
    } else {
      dynamicData = {
        androidApplicatoinSelError: errorMessageConstants[key].selectData,
        androidOSVersionError: '',
      };
      this.setState({
        dynamicData,
        appFieldDataList: androidApplication(dynamicData, reference),
        osFieldDataList: androidOsVersion(dynamicData, reference),
      });
    }
  };

  generateComboValues = () => {
    const { osVersion, applicationList } = this.state;
    const { androidOSList } = osVersion;
    const { androidApplicationList } = applicationList;
    osVersionFilter.splice(0, osVersionFilter.length);
    androidOSList.map(data => osVersionFilter.push({ value: data, option: data }));
    applicationFilter.splice(0, applicationFilter.length);

    androidApplicationList
      .filter(appInfo =>
        applicationList.androidApplicationCheckMethod === 'WhiteList'
          ? appInfo.applicationCheckMethod
          : !appInfo.applicationCheckMethod,
      )
      .map(data =>
        applicationFilter.push({ value: data.applicationName, option: data.applicationName }),
      );
  };

  render() {
    const { isModified, screenKey } = this.props;
    const {
      pageLoading,
      dynamicData,
      osVersion,
      reference,
      disabled,
      osFieldDataList,
      appFieldDataList,
      dataSource,
      openDialogError,
      applicationList,
    } = this.state;
    this.generateComboValues();
    dynamicData.disabled = disabled;
    return (
      <CircularSpinner
        loading={pageLoading}
        css={{ position: 'absolute', top: '50%', left: 'calc(60% - 55px)' }}
      >
        <div className="contents-main">
          <div className="clearfix mb10">
            <SBContentHeader
              hasTitleDescription
              title={headerInformation(screenKey).title}
              description={headerInformation(screenKey).description}
            />
            <CustomTableFrame
              tableList={secuirtyPolicyName(dynamicData, reference)}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <CustomTableFrame
              tableList={
                osFieldDataList.length ? osFieldDataList : androidOsVersion(dynamicData, reference)
              }
              dataSource={osVersion}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <SBInfoContent title="security_policy_os_info_content" />
            <CustomTableFrame
              tableList={
                appFieldDataList.length
                  ? appFieldDataList
                  : androidApplication(dynamicData, reference)
              }
              dataSource={applicationList}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <SBInfoContent title="security_policy_app_info_content" />
            <CustomTableFrame
              tableList={imeiTableList(dynamicData, reference)}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <CustomTableFrame
              tableList={antiVirusFrame(dynamicData, reference)}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />

            <CustomTableFrame
              tableList={assignTarget(dynamicData, reference)}
              dataSource={dataSource}
              defaultValue={{}}
              onTableFrameEvent={(key, event, value) => this.onTableFrameEvent(key, event, value)}
              onKeyDown={(event, value) => this.onKeyDown(event, value)}
            />
            <SBDialog
              type="error"
              header="error"
              content="data_does_not_exists"
              okButtonTitle="ok"
              open={openDialogError}
              onOk={() => {
                this.setState({ openDialogError: false }, this.onCancelClick());
              }}
              width="400px"
            />
            <SecurityPolicyFooter
              okDisable={!isModified}
              onHandleClick={key => this.onHandleClick(key)}
            />
          </div>
        </div>
      </CircularSpinner>
    );
  }
}
const mapDispatchToProps = dispatch => {
  return {
    setModified: isModified => {
      dispatch(commonChangeAction(isModified));
    },
    makeSecurityPolicyGroupList: data => dispatch(getSecurityPolicyGroupList(data)),
    makeSecurityPolicyDetail: data => dispatch(setSecurityPolicyDetail(data)),
  };
};

/**
 * mapStateToProps performs data fetch from group redux state
 * @param state
 * @return reduxState
 */
const mapStateToProps = state => {
  const { securityData } = state.securityPolicy;
  const { isModified } = state.screenModified;
  const { userVo } = state.user;
  const { securityPolicyDetail } = state.securityPolicy;
  return { securityData, isModified, userVo, securityPolicyDetail };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(SecurityPolicyRegistration));
